import React, {ReactElement, useState} from 'react';
import {Viewer} from '@react-pdf-viewer/core';
import {defaultLayoutPlugin, ToolbarProps} from '@react-pdf-viewer/default-layout';
import {toolbarPlugin, ToolbarSlot, TransformToolbarSlot} from '@react-pdf-viewer/toolbar';
import {SelectionMode, selectionModePlugin} from '@react-pdf-viewer/selection-mode';
import tr_TR from './tr_TR.json';
import '@react-pdf-viewer/default-layout/lib/styles/index.css';
import '@react-pdf-viewer/core/lib/styles/index.css';
import '@react-pdf-viewer/toolbar/lib/styles/index.css';
import '@react-pdf-viewer/selection-mode/lib/styles/index.css';
import {UncontrolledTooltip} from "reactstrap";

const PdfViewer = (props) => {
    const [isThemeDark,setIsthemeDark] = useState(null);
    const [isMobile,setIsMobile] = useState(window.innerWidth > 991 ? false : true);

    const customButtonStyles = {
        border:'none',
        background:'transparent',
        margin:'0px 10px',
        padding:'3px'
    };

    const iconStyles = {
        fontSize:'17px',
        color: isThemeDark ? 'white' : '#1657bb'
    };

    const toolTipStyles = {
        background: isThemeDark ? '#363636' : 'black',
        color:'white',
        height:'40px'
    };


    const transform: TransformToolbarSlot = (slot: ToolbarSlot) => ({
        ...slot,
        Open: () => <></>,
    });

    const toolbarPluginInstance = toolbarPlugin();
    const {renderDefaultToolbar} = toolbarPluginInstance;

    const renderToolbar = (Toolbar: (props: ToolbarProps) => ReactElement) => (
        <>
            <Toolbar>{renderDefaultToolbar(transform)}</Toolbar>
            {props.showCustomButtons && <div>
                <button id="showFormButton" onClick={()=>props.showForm()} style={customButtonStyles}><i style={iconStyles} className="fas fa-eye"/></button>
                <UncontrolledTooltip style={toolTipStyles} placement="bottom" target="showFormButton" hideArrow={true}>
                    Formu Gster
                </UncontrolledTooltip>
            </div>}

            {props.showCustomButtons && <div>
                <button id="showBarcodesButton" onClick={()=>props.showBarcodes()} style={customButtonStyles}><i style={iconStyles} className="fas fa-qrcode"/></button>
                <UncontrolledTooltip style={toolTipStyles} placement="bottom" target="showBarcodesButton" hideArrow={true}>
                    Barkodlar Gster
                </UncontrolledTooltip>
            </div>
            }

        </>
    );

    const defaultLayoutPluginInstance = defaultLayoutPlugin({
        renderToolbar,
        sidebarTabs: (defaultTabs) => !isMobile ? [defaultTabs[0],] : [],
        toolbarPlugin: {
            selectionModePlugin: {
                selectionMode: SelectionMode.Hand,
            }
        }
    });

    const renderError = (error) => {
        let message = '';
        switch (error.name) {
            case 'InvalidPDFException':
                message = 'Pdf dokman hatal veya hasar grm';
                break;
            case 'MissingPDFException':
                message = 'Pdf dokman kayp';
                break;
            case 'UnexpectedResponseException':
                message = 'Beklenmedik sunucu yant';
                break;
            default:
                message = 'Dokman yklenemedi';
                break;
        }

        return (
            <div
                style={{
                    alignItems: 'center',
                    display: 'flex',
                    height: '100%',
                    justifyContent: 'center',
                }}
            >
                <div
                    style={{
                        backgroundColor: '#e53e3e',
                        borderRadius: '0.25rem',
                        color: '#fff',
                        padding: '0.5rem',
                    }}
                >
                    {message}
                </div>
            </div>
        );
    };


    const handleSwitchTheme = (theme: string) => {
        if (theme === 'dark') {
            setIsthemeDark(true);
        } else {
            setIsthemeDark(false);
        }
    };

    return (
        <div style={{height:'700px',maxHeight:'700px',width:'100%',overflow:'hidden'}}>
            {props.url &&
                <Viewer
                    renderError={renderError}
                    fileUrl={props.url}
                    plugins={[defaultLayoutPluginInstance]}
                    theme="auto"
                    defaultScale={isMobile ? 0.3 : props.scale ? props.scale : 0.9}
                    onSwitchTheme={handleSwitchTheme}
                    localization={tr_TR} as unknown as LocalizationMap
                />
            }

            {!props.url && <div>Ykleniyor...</div>}
        </div>
    );
};

export default PdfViewer;