import React, {Component} from 'react';
import {Button} from "reactstrap";

class SquareButtonX extends Component {

	constructor(props) {
		super(props);

		this.state = {};
	}

	render() {

		let cloneProps = Object.assign({}, this.props);

		if (cloneProps.title) {
			cloneProps.toggle = "tooltip";
			cloneProps.placement = "top";
			cloneProps.rel = "tooltip";
		}

		return (
			<div>
				<Button {...cloneProps} style={{ borderRight: 'none'}} className="BtnZkn">
					<img className="simple-text logo-mini"
						 src={process.env.REACT_APP_CONTEXT_NAME + "/image/" + this.props.img}
						 style={{marginRight: '10px'}}
					/>
					<span style={{fontSize: 'medium', color: 'black'}}></span>
					{this.props.label}
				</Button>
			</div>
		)
	}
}

export default SquareButtonX;