import React, {Component} from 'react';
import Form from "react-jsonschema-form";
import {requestK} from "../../../helper/APIUtils";
import {showAxiosError,create_UUID} from "../../../helper/helper";
import Select from "../../../local_node_modules/react-select";
import StickButton from "../../../components/StickButton";
import Swal from "sweetalert2";
import SweetAlert from "react-bootstrap-sweetalert";
import ReactLoading from "react-loading";
import moment from "moment";
import {Col, Modal, ModalBody, ModalFooter, ModalHeader, Row} from "reactstrap";
import "./survey.css";
import ImageUpload from "../formbuilder/ImageUpload";
import LinkWidget from "../formbuilder/LinkWidget";
import FileChooserWidget from "../formbuilder/FileChooserWidget";
import Notify from "react-notification-alert";
import SweetAlertExt from "../../../components/SweetAlertExt";
import TextOverFlowTooltip from "../../../components/TextOverFlowTooltip";
import PdfViewer from "../approval/PdfViewer/PdfViewer";
import TimeWidget from "../formbuilder/TimeWidget";

const defaultSchema = {
    "type": "object"
};
const defaultUISchema = {};

const imageFileChooserWidget = ImageUpload;
const linkWidget = LinkWidget;
const fileChooserWidget = FileChooserWidget;
const timeWidget = TimeWidget;
const widgets = {imageFileChooserWidget, linkWidget, fileChooserWidget, timeWidget };

class SurveyForm extends Component {
    isExistRequiredField = false;
    statesEnum = {
        SAVED: 'KAYDEDLD',
        INAPPROVABLE: 'ONAYDA BEKLYOR',
        APPROVED: 'ONAYLANDI',
        REJECTED: 'REDDEDLD',
        REFORMATION: 'YENDEN DZENLE',
        WAITING: "GNDERLMEY BEKLYOR"
    };
    emptyResponse = {
        responseId: '',
        surveyId: '',
        userId: '',
        date: '',
        response: '',
        state: '',
        explanation: ''
    };

    constructor(props) {
        super(props);
        this.state = {
            options: [],
            selectedForm: null,
            state: '',
            formResponse: this.props.selectedResponse !== null ? this.props.selectedResponse : this.emptyResponse,
            sentApprovalAlert: null,
            approveAlert: null,
            isChanged: false,
            rejectAlert: null,
            isLoading: false,
            isPersonalInfo: this.props.isPersonalInfo,
            alert: null,
            response: {},
            isAllValid: null,
            openFormPreview: false,
            formSelected: false,
            uuid: null,
            formNumberProps: []
        };

        this.saveForm = this.saveForm.bind(this);
        this.getSurveyForms = this.getSurveyForms.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.sentToApproveConfirm = this.sentToApproveConfirm.bind(this);
        this.delete = this.delete.bind(this);
        this.onChangeTextArea = this.onChangeTextArea.bind(this);
        this.onChangeForm = this.onChangeForm.bind(this);
        this.getEmployee = this.getEmployee.bind(this);
        this.rearrange = this.rearrange.bind(this);
        // this.transformErrors = this.transformErrors.bind(this);
        this.saveFormWithApprove = this.saveFormWithApprove.bind(this);
        this.getSurveyDocuments = this.getSurveyDocuments.bind(this);
        this.getUpdatesFormValues = this.getUpdatesFormValues.bind(this);
        this.getUpdatesFormValuesForFormPreview = this.getUpdatesFormValuesForFormPreview.bind(this);

    }

    componentWillMount() {
        this.getSurveyForms();
        if (this.state.formResponse.responseId !== '')
            this.getSurveyDocuments();
    }
    componentDidMount() {
        this.state.uuid=create_UUID();
    }

    componentDidUpdate(prevProps, prevState) {
        if (this.state.isAllValid === null || prevState.isAllValid === this.state.isAllValid)
            return;
        //console.log("VALIDITY CHANGED ! " + this.state.isAllValid);
        if (this.state.selectedForm === null) {
            Swal({
                title: 'Uyar ! ',
                text: (this.state.isPersonalInfo === 1 ? "Ltfen bir zlk Formu seiniz." : "Ltfen bir Talep Formu seiniz."),
                confirmButtonText: 'Tamam'
            }).then(() => {
                this.setState({isAllValid: null});
            });
        } else if (this.state.isAllValid === false) {
            this.setState({
                alert: (
                    <SweetAlert warning
                                confirmBtnText="Tamam"
                                confirmBtnBsStyle="warning"
                                onConfirm={() => this.setState({alert: null, isAllValid: null})}
                    >
                        Ltfen zorunlu alanlar doldurunuz!
                    </SweetAlert>
                )
            });
        } else if (!this.numberValidControl()) {
            Swal({
                title: 'Uyar ! ',
                text: "Ltfen say alanlarn kontrol ediniz..",
                confirmButtonText: 'Tamam'
            }).then(() => {
                this.setState({isAllValid: null});
            });
        } else {
            this.setState({
                sentApprovalAlert: (
                    <SweetAlert showCancel warning
                                confirmBtnText="Evet"
                                cancelBtnText="Hayr"
                                confirmBtnBsStyle="success"
                                cancelBtnBsStyle="primary"
                                title=""
                                onConfirm={() => {
                                    this.sentToApproveConfirm()
                                    this.setState({isAllValid: null})
                                }}
                                onCancel={() => this.setState({sentApprovalAlert: null, isAllValid: null})}>
                        {(this.props.isPersonalInfo === true ? "zlk Formu" : "Talebiniz") + " gnderilecek onaylyor musunuz?"}
                    </SweetAlert>
                )
            });
        }
    }

    numberValidControl() {
        let self = this;
        let params = JSON.parse(self.state.selectedForm.form.jsonContent)['schema']['properties'];
        for (let key in params) {
            if(params[key]["type"] === "number" && self.state.response[key] === undefined) {
                return false;
            }
        }
        return true;
    }

    // transformErrors(errors) {
    //     let self = this;
    //     self.isExistRequiredField = false;
    //     errors = errors.map(error => {
    //         if (error.name === "required") {
    //             error.message = `*`
    //             self.isExistRequiredField = true;
    //         }
    //         return error;
    //     });
    //     errors = errors.filter(error => error.name !== "type");
    //     return errors;
    // }

    isCandidate = () => {
        return this.props.currentUser && !!this.props.currentUser.statusType && this.props.currentUser.statusType === "3";
    };

    getSurveyForms() {
        let self = this;
        self.setState({ isLoading: true });
        let params = {
            url: `/survey/getSurveyForms`,
            method: 'post',
            data: String(self.state.isPersonalInfo)
        };
        requestK(params)
            .then(function (response) {
                let optionItems = [];
                response.data.map((form) => {
                    if (!self.isCandidate()) {
                        optionItems.push({value: form.id, label: form.name, form: form});
                    } else {
                        if (form.candidateCanDisplay === null || form.candidateCanDisplay === true) {
                            optionItems.push({value: form.id, label: form.name, form: form});
                        }
                    }
                });
                self.setState({options: optionItems});
                if (self.state.formResponse.responseId !== '') {
                    let item = optionItems.find(item => item.value === self.state.formResponse.surveyId);
                    self.setState({selectedForm: item, formSelected: true});
                } else {
                    self.setState({ isLoading: false });
                }
                self.getEmployee();
            }).catch(function (error) {
            self.setState({ isLoading: false });
            showAxiosError(error);
        });
    }

    rearrange() {
        let self = this;
        let item = {...self.state.formResponse};
        item['state'] = self.statesEnum.REFORMATION;
        item['formId'] = null;
        item['explanation'] = "";
        self.setState({formResponse: item});
    }

    getEmployee() {
        let self = this;
        let params = {
            url: `/survey/getEmployeeByIdForSurveys`,
            method: "post",
            data: self.props.currentUser.employeeId ? self.props.currentUser.employeeId : -1
        };
        requestK(params)
            .then(function (response) {
                self.setState({ employee: response.data });
            }).catch(function (error) {
            showAxiosError(error);
        });
    }

    getSurveyDocuments() {
        let self = this;
        let params = {
            url: `/survey/getSurveyDocuments`,
            method: 'post',
            data: self.state.formResponse
        };
        requestK(params)
            .then(function (response) {
                let content = Object.assign(JSON.parse(self.state.formResponse.response), response.data);
                self.setState({response: content, isLoading: false});
            }).catch(function (error) {
            self.setState({ isLoading: false });
            showAxiosError(error);
        });
    }

    handleChange = selectedForm => {
        const formNumberProps = this.filteredObjectKeys(JSON.parse(selectedForm.form.jsonContent)['schema']['properties']);
        if (formNumberProps.length > 0) {
            let responseData = this.state.response;
            let isNumbersLoaded = false;
            for (let key of formNumberProps) {
                if(responseData[key] === undefined) {
                    isNumbersLoaded = true;
                    responseData[key] = "0";
                }
            }
            if (isNumbersLoaded)
                this.setState({selectedForm, formSelected: true, formNumberProps, response: responseData});
            else
                this.setState({selectedForm, formSelected: true, formNumberProps});
        }
        else
            this.setState({selectedForm, formSelected: true, formNumberProps});
    };
    filteredObjectKeys = (obj) => {
        return Object.keys(obj).filter( key => obj[key]["type"] === "number");
    }

    onChangeForm = ({formData}) => {
        const uiSchemas = this.state.selectedForm !== null ? JSON.parse(this.state.selectedForm.form.jsonContent)['uiSchema'] : defaultUISchema;
        if (this.state.selectedForm == null) {
            this.setState({isChanged: true, response: formData})
            console.log("Log111-selected form is empty!!!!");
            return;
        }
        let formNumberProps;
        if (this.state.formNumberProps != null)
            formNumberProps = this.state.formNumberProps;
        else {
            formNumberProps = this.filteredObjectKeys(JSON.parse(this.state.selectedForm.form.jsonContent)['schema']['properties']);
            this.setState({formNumberProps});
        }
        if (formNumberProps.length > 0) {
            let showAlertOnce = true;
            for (let key of formNumberProps) {
                if(formData[key] === undefined) {
                    if (showAlertOnce)
                        Swal({
                            title: 'Uyar ! ',
                            text: "Ltfen say alanna rakam giriniz..",
                            confirmButtonText: 'Tamam'
                        }).then(() => { showAlertOnce = false;});
                    if (document.getElementById('root_' + key) != null)
                        document.getElementById('root_' + key).style["background"] = "#f39d9d";
                }
                else if (document.getElementById('root_' + key) != null)
                    document.getElementById('root_' + key).style["background"] = "#ffffff";
            }
        }
        Object.keys(uiSchemas).forEach(fieldName => {
            const fieldOptions = uiSchemas[fieldName]?.["ui:options"];
            const inputElement = document.getElementById(`root_${fieldName}`);
            if (fieldOptions && fieldOptions.inputType === "tel" ) {
                let fieldValue = formData[fieldName];

                if (fieldValue !== undefined && fieldValue !== null) {
                    fieldValue = fieldValue.replace(/\s/g, '');
                    inputElement.value = fieldValue;

                    if (!/^\d+$/.test(fieldValue)) {
                        this.showAlert("Uyar ! ", "Ltfen telefon alanna sadece say giriniz.");
                        fieldValue = "";
                        inputElement.value = "";

                    } else if (fieldValue.trim().length > 10 || fieldValue.trim().charAt(0) === '0') {

                        if (fieldValue.trim().charAt(0) === '0'){
                            this.showAlert("Uyar ! ", "Ltfen telefon numaranzn banda sfr (0) olmadan giriniz.");
                            fieldValue = fieldValue.substring(1);
                            inputElement.value = fieldValue;
                        }
                        else if (fieldValue.trim().length > 10){
                            this.showAlert("Uyar ! ", "Ltfen telefon numaranz 10 hane olarak giriniz.");
                            fieldValue = fieldValue.substring(0,fieldValue.length - 1);
                            inputElement.value = fieldValue;
                        }


                    }
                }
                formData[fieldName] = fieldValue;

            }
        });
        this.setState({isChanged: true, response: formData})
    };
    showAlert = (title, text) =>{
        Swal({
            title: title,
            text: text,
            confirmButtonText: "Tamam"
        });
    }
    validateRequiredFields = (formData) => {
        let requiredFields, formResponse, isAllValid;
        requiredFields = this.state.selectedForm !== null ? JSON.parse(this.state.selectedForm.form.jsonContent)['schema'].required : defaultSchema;
        formResponse = Object.keys(formData);

        if (requiredFields && requiredFields.length > 0) {
            if (requiredFields.every(value => formResponse.includes(value))) {
                let fieldValue;
                isAllValid = requiredFields.every(field => {
                    fieldValue = formData[`${field}`];
                    switch (typeof fieldValue) {
                        case "object":
                            return fieldValue.length > 0;//break;
                        case "string":
                        case "number":
                            return fieldValue !== "" && fieldValue !== 'undefined';
                        default:
                            return fieldValue !== undefined;
                    }
                })
                isAllValid ? this.setState({isAllValid: true}) : this.setState({isAllValid: false})
            } else {
                this.setState({isAllValid: false});
            }
        } else {
            this.setState({isAllValid: true})
        }

    }

    getUpdatesFormValuesForFormPreview() {
        let self = this;
        let formResponse = {...self.state.formResponse};
        let formValues = {};
        let params = self.state.selectedForm !== null ? JSON.parse(self.state.selectedForm.form.jsonContent)['schema']['properties'] : defaultSchema;
        for (let key in params) {
            let content = self.state.response[key];
            if (content !== undefined && content !== null) {
                formValues[key] = content;
            }
        }
        formResponse.response = JSON.stringify(formValues);
        self.setState({formResponse});
        return formResponse;
    }

    getUpdatesFormValues() {
        let self = this;
        let documentSet = self.state.formResponse.documentSet !== undefined ? self.state.formResponse.documentSet : [];
        let formResponse = {...self.state.formResponse};
        let formValues = {};
        let params = self.state.selectedForm !== null ? JSON.parse(self.state.selectedForm.form.jsonContent)['schema']['properties'] : defaultSchema;
        for (let i in params) {
            let key = i;
            let val = params[i];
            if (val !== null && val['format'] !== undefined && val['format'] === 'data-url') {
                let content = self.state.response[key];
                if (content !== undefined && Array.isArray(content)) {
                    documentSet.forEach(surveyDocument => {
                        if (surveyDocument.paramName === key) {
                            surveyDocument.deletedAt = new Date();
                        }
                    });
                    content.forEach(content => {
                        let document = {};
                        document['paramName'] = key;
                        document['content'] = content;
                        documentSet.push(document);
                    });
                } else if (content !== undefined) {
                    documentSet.forEach(surveyDocument => {
                        if (surveyDocument.paramName === key) {
                            surveyDocument.deletedAt = new Date();
                        }
                    });
                    let document = {};
                    document['paramName'] = key;
                    document['content'] = content;
                    documentSet.push(document);
                } else {
                    documentSet.forEach(surveyDocument => {
                        if (surveyDocument.paramName === key) {
                            surveyDocument.deletedAt = new Date();
                        }
                    });
                }
            } else {
                let content = self.state.response[key];
                if (content !== undefined && content !== null) {
                    formValues[key] = content;
                }
            }
        }
        formResponse.response = JSON.stringify(formValues);
        formResponse.documentSet = documentSet;
        self.setState({formResponse, isChanged: true});
        return formResponse;
    }

    saveForm() {
        let self = this;
        if (self.state.selectedForm === null) {
            Swal({
                title: 'Uyar ! ',
                text: ("Ltfen bir Talep Formu seiniz."),
                type: 'warning',
                confirmButtonText: 'Tamam'
            }).then(() => {
                //self.fetchData();
            });
            return;
        }
        let item = self.getUpdatesFormValues();
        item['state'] = this.statesEnum.SAVED;
        item['surveyId'] = self.state.selectedForm.form.id;
        item['surveyName'] = self.state.selectedForm.form.name;
        self.setState({isLoading: true});
        let params = {
            url: '/survey/saveSurveyFormResponse',
            method: "post",
            data: item
        };
        requestK(params)
            .then(function (response) {
                self.setState({formResponse: response.data, isChanged: false, isLoading: false});
                Swal({
                    title: 'Baarl ! ',
                    text: ("Kaydetme ilemi baaryla gerekletirildi."),
                    type: 'success',
                    confirmButtonText: 'Tamam'
                }).then(() => {
                    //self.fetchData();
                });
            }).catch(function (error) {
            self.setState({isLoading: false});
            showAxiosError(error);
        });
    };

    saveFormWithApprove() {
        if (this.state.selectedForm) {
            this.validateRequiredFields(this.state.response);
        } else {
            this.setState({isAllValid: false})
        }

    };

    openFormPreview = () => {
        this.setState({openFormPreview: !this.state.openFormPreview}, () => this.downloadSurveyFormPreview());
    }

    downloadSurveyFormPreview = () => {
        if (this.state.openFormPreview === true) this.previewFormContent()
    }


    previewFormContent = () => {
        let self = this;
        let surveyForm = self.getUpdatesFormValuesForFormPreview();
        let parsedJSON = {};
        for (let prop in self.state.response) {
            if (self.state.response[prop] !== undefined) if (self.state.response[prop].toString().includes("data:application/")||self.state.response[prop].toString().includes("data:image/")) {
                continue;
            } else {
                if (isNaN(self.state.response[prop])) {
                    parsedJSON[prop] = moment(self.state.response[prop].toString().replace(/\s+/g, ''), "YYYY-MM-DD", true).isValid() ? moment(self.state.response[prop]).format("DD.MM.YYYY") : self.state.response[prop];
                } else {
                    parsedJSON[prop] = self.state.response[prop];
                }
            }
        }
        surveyForm['surveyId'] = self.state.selectedForm.form.id;
        surveyForm['surveyName'] = self.state.selectedForm.form.name;
        surveyForm['formParamList'] = JSON.stringify(parsedJSON);
        let params = {
            url: `/survey/surveyFormPreview`, responseType: 'arraybuffer', method: "post", data: surveyForm
        };
        requestK(params)
            .then(function (response) {
                self.setState({mediaModal: true});
                let blob = new Blob([response.data], {type: response.headers["content-type"]});
                let base64Data = "";
                if (blob !== undefined) {
                    let reader = new FileReader();
                    reader.readAsDataURL(blob);
                    reader.onloadend = function () {
                        base64Data = reader.result;
                        self.setState({url: base64Data});
                    }
                }
            }).catch(function (error) {
            showAxiosError(error);
        });
    }

    sentToApproveConfirm() {
        let self = this;
        const uuid=this.state.uuid;
        let preFormResponse = self.state.formResponse;
        let item = self.getUpdatesFormValues();
        let parsedJSON = {};
        for (let prop in self.state.response) {
            if (self.state.response[prop] !== undefined) if (self.state.response[prop].toString().includes("data:application/")||self.state.response[prop].toString().includes("data:image/")) {
                continue;
            } else {
                if (isNaN(self.state.response[prop])) {
                    parsedJSON[prop] = moment(self.state.response[prop].toString().replace(/\s+/g, ''), "YYYY-MM-DD", true).isValid() ? moment(self.state.response[prop]).format("DD.MM.YYYY") : self.state.response[prop];
                } else {
                    parsedJSON[prop] = self.state.response[prop];
                }
            }
        }
        item['state'] = this.statesEnum.INAPPROVABLE;
        item['surveyId'] = self.state.selectedForm.form.id;
        item['surveyName'] = self.state.selectedForm.form.name;
        item['formParamList'] = JSON.stringify(parsedJSON);
        self.setState({isLoading: true, sentApprovalAlert: false});

        let params = {
            url: '/survey/saveAndSendSurveyFormResponseForWeb',
            method: "post",
            data: {item:item,
            uuid:uuid}
        };
        requestK(params)
            .then(function (response) {
                self.setState({formResponse: response.data, isChanged: false, isLoading: false});
                Swal({
                    title: 'Baarl ! ',
                    text: (self.state.isPersonalInfo === true ? "zlk Formu" : "Talep Formu") + " Onaya Gnderildi.",
                    type: 'success',
                    confirmButtonText: 'Tamam'
                }).then(() => {
                    //self.fetchData();
                    self.props.cancelProcess();
                });
            }).catch(function (error) {
            let formResponse = self.state.formResponse;
            formResponse['state'] = self.statesEnum.WAITING;
            self.setState({isLoading: false, isChanged: false, formResponse: preFormResponse});
            if (error.response.data.includes("Ykleme ilemi srasnda bir hata olutu. Yklenen dosyalar bozuk olabilir.") || error.response.data.includes("Atanm Organizasyon Yetkilisi bulunmamaktadr. Ltfen nsan Kaynaklar yneticiniz ile grnz.")) {
                Swal({
                    title: 'Hata!',
                    text: error.response.data,
                    type: 'error',
                    confirmButtonText: 'Tamam'
                }).then(() => {
                    self.props.cancelProcess();
                });
            }else{
                self.props.cancelProcess();
                showAxiosError(error);
            }
        });
    }

    delete() {
        Swal.fire({
            title: 'Silinsin mi?',
            text: (this.state.isPersonalInfo === 1 ? "zlk Formunu" : "Talebi ") + ' silmek istiyor musunuz?',
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: 'Evet',
            cancelButtonText: 'Hayr',
            showClose: true
        }).then((isConfirm) => {
            if (isConfirm.value) {
                let self = this;
                let item = {...self.state.formResponse};
                let params = {
                    url: '/survey/deleteSurveyFormResponse',
                    method: "post",
                    data: item
                };
                requestK(params)
                    .then(function () {
                        self.setState({
                            formResponse: {
                                responseId: '',
                                surveyId: '',
                                userId: '',
                                date: '',
                                response: '',
                                state: '',
                                explanation: ''
                            }
                        });
                        Swal({
                            title: 'Baarl ! ',
                            text: ("Silme ilemi baaryla gerekletirildi."),
                            type: 'success',
                            confirmButtonText: 'Tamam'
                        }).then(() => {
                            //self.fetchData();
                            self.props.cancelProcess();
                        });
                    }).catch(function (error) {
                    showAxiosError(error);
                });
            }
        });
    }

    onChangeTextArea = (evt) => {
        let self = this;
        let item = {...self.state.formResponse};
        item['explanation'] = evt.target.value;
        self.setState({
            formResponse: {...item}
        });
    };

    toggle = () => {
        if (this.state.isChanged) {
            this.setState({
                alert: (
                    <SweetAlertExt showCancel info
                                   confirmBtnText="Evet"
                                   cancelBtnText="Hayr"
                                   confirmBtnBsStyle="info"
                                   cancelBtnBsStyle="default"
                                   title="UYARI"
                                   onConfirm={() => this.setState({alert: null}, () => {
                                       this.props.cancelProcess();
                                   })}
                                   onCancel={() => this.setState({alert: null})}
                    >
                        {"Formda kaydedilmemi veriler vardr. Sayfay kapatrsanz bu veriler kaybolacaktr. Onaylyor musunuz?"}
                    </SweetAlertExt>)
            });
        } else {
            this.props.cancelProcess();
        }
    }

    renderSurveyForm() {
        const {selectedForm, isChanged} = this.state;
        const schemas = this.state.selectedForm !== null ? JSON.parse(this.state.selectedForm.form.jsonContent)['schema'] : defaultSchema;
        const uiSchemas = this.state.selectedForm !== null ? JSON.parse(this.state.selectedForm.form.jsonContent)['uiSchema'] : defaultUISchema;
        return <Modal isOpen={this.props.createDialog} size="xl"
                      toggle={() => this.toggle()}>
            <ModalHeader className="justify-content-center"
                         toggle={() => this.toggle()}>
                {this.state.isPersonalInfo ? "zlk Formu" : "Talep Formu"}
            </ModalHeader>
            <ModalBody>
                {this.state.createDialog && <Notify ref={this.notify}/>}
                <div className="FormRenk col-sm-12" style={{margin: '16px 0 16px 0'}}>
                    <div className='col-sm-12 row' style={{margin: '0 0 16px -16px'}}>
                        <div className="col-sm-2">
                            <label
                                className='col-form-label'>{this.state.isPersonalInfo === true ? "zlk Formlar" : "Talep Formu"} *</label>
                        </div>
                        <div id="select-area" className='col-sm-10'
                             style={{margin: '0 0 0 -4px', padding: '0 70px 0 35px'}}>
                            <Select value={selectedForm !== null ? selectedForm.value : ""}
                                    clearable={false}
                                    noResultsText="Tanmlanm form bulunamad"
                                    clearValueText="Temizle"
                                    placeholder="Seiniz..."
                                    onChange={this.handleChange}
                                    options={this.state.options}
                                    disabled={(this.state.formResponse.responseId !== "") || this.state.isPersonalInfo}/>
                        </div>
                    </div>
                </div>
                <div className="FormRenk col-sm-12">
                    <div className='col-sm-12 row'>
                        <div className="container">
                            <Row>
                                <Col md={2}>
                                    <h6>{this.state.isPersonalInfo === true ? "Form No:" : "Talep No:"}</h6>
                                </Col>
                                <Col md={10}>
                                    <b>{this.state.formResponse.responseId}</b>
                                </Col>
                            </Row>
                            <Row>
                                <Col md={2}>
                                    <h6>{this.state.isPersonalInfo === true ? "Form Tarihi:" : "T. Tarihi:"}</h6>
                                </Col>
                                <Col md={10}>
                                    <b>{
                                        moment(this.state.formResponse.date).isValid() ? moment(this.state.formResponse.date).format("DD-MM-YYYY HH:mm:ss") : ''
                                    }</b>
                                </Col>
                            </Row>
                            <Row>
                                <Col md={2}>
                                    <h6>Durum:</h6>
                                </Col>
                                <Col md={10}>
                                    {this.state.formResponse.state === this.statesEnum.SAVED ?
                                        <h6
                                            className="text-muted">{this.statesEnum.SAVED}</h6> : this.state.formResponse.state === this.statesEnum.INAPPROVABLE ?
                                            <h6
                                                className="text-warning">{this.statesEnum.INAPPROVABLE}</h6> : this.state.formResponse.state === this.statesEnum.WAITING ?
                                                <h6
                                                    className="text-warning">{this.statesEnum.WAITING}</h6> : this.state.formResponse.state === this.statesEnum.REJECTED ?
                                                    <h6
                                                        className="text-danger">{this.statesEnum.REJECTED}</h6> : this.state.formResponse.state === this.statesEnum.REFORMATION ?
                                                        <h6
                                                            className="text-muted">{this.statesEnum.REFORMATION}</h6> : this.state.formResponse.state === this.statesEnum.APPROVED ?
                                                            <h6 className="text-success">{this.statesEnum.APPROVED}</h6> : ''}
                                </Col>
                            </Row>
                            <Row>
                                <Col md={2}>
                                    <label htmlFor="formControlTextarea1">Aklama:</label>
                                </Col>
                                <Col md={10}>
                                    <TextOverFlowTooltip
                                        text={this.state.formResponse.explanation}
                                        maxLength={50}/>
                                </Col>
                            </Row>
                            <Row>
                                {this.props.isEmployee && <StickButton position="rightSide" className="zocial Goruntule"
                                                                       style={{
                                                                           fontSize: 'x-small',
                                                                           backgroundColor: 'forestgreen'
                                                                       }}
                                                                       label="Formu nizle"
                                                                       title="Gnderilecek Formu Grntle"
                                                                       onClick={this.openFormPreview}
                                                                       disabled={!this.state.formSelected}
                                />}
                            </Row>
                            <Row>
                                <label className='col-form-label' style={{fontSize: '12px', color: 'grey'}}>Doldurmak
                                    zere olduunuz formun ilk halini veya parametreler doldurulduktan sonraki halini
                                    'Form nizle' butonuna tklayarak grntleyebilirsiniz. Form nizlemesini kapatmak
                                    iin tekrar tklaynz.</label>
                            </Row>
                        </div>
                    </div>
                </div>
                {this.state.openFormPreview && <div className="FormRenk col-sm-12">
                    <div className='col-sm-12 row'>
                        <div className="container" style={{overflowY: 'scroll', height: '500px'}}>
                            {this.state.mediaModal && <PdfViewer url={this.state.url} scale={0.7}/>}
                            {!this.state.url && <ReactLoading type="spinningBubbles" style={{
                                width: "70px", height: "70px", margin: "20px auto 0 auto", fill: "#888"
                            }}/>}
                        </div>
                    </div>
                </div>}
                <div className="card">
                    <div className="card-body" id='surveyFormCardBody'>
                        <Form schema={schemas}
                              uiSchema={uiSchemas}
                              onChange={this.onChangeForm}
                              formData={this.state.response}
                              ref={(form) => {
                                  this.submitForm = form
                              }}
                              disabled={this.state.formResponse.state === this.statesEnum.REJECTED ||
                                  this.state.formResponse.state === this.statesEnum.APPROVED ||
                                  this.state.formResponse.state === this.statesEnum.INAPPROVABLE ||
                                  this.state.isLoading}
                              noValidate={true}
                              widgets={widgets}
                        >
                            {this.state.isLoading && <ReactLoading type="spinningBubbles" style={{
                                width: "60px",
                                height: "60px",
                                margin: "20px auto 0 auto",
                                fill: "#888"
                            }}/>}
                            <div className="container">
                                <div className="row">
                                    <div className='col-12 pl-0'>
                                        {this.state.response && this.state.response["Onay Alan"] &&
                                            <h6 style={{ textTransform: 'none' }}>Kabul Ediyorum seeneini semeden Kaydet ve Gnder ilemini gerekletiremezsiniz.</h6>
                                        }
                                        <h6 style={{textTransform: 'lowercase'}}><span
                                            style={{color: 'red'}}>*</span> ile iaretli alanlarn doldurulmas
                                            zorunludur.</h6>
                                    </div>
                                    <div className="col-12 col-md-6 d-flex">
                                        {this.props.isEmployee &&
                                            <StickButton position="rightSide" className="zocial Sil"
                                                         style={{fontSize: 'x-small', backgroundColor: 'red'}}
                                                         label="Sil" title="Kaytl Formu Sil" onClick={this.delete}
                                                         disabled={this.state.formResponse.state !== this.statesEnum.SAVED || this.state.isLoading}
                                            />}
                                        {this.props.isEmployee &&
                                            <StickButton position="rightSide" className="zocial Red"
                                                         style={{fontSize: 'x-small', backgroundColor: 'forestgreen'}}
                                                         label="Dzenle" title="Yeniden Dzenle"
                                                         onClick={this.rearrange}
                                                         disabled={this.state.formResponse.state !== this.statesEnum.REJECTED || this.state.isLoading}
                                            />}
                                    </div>

                                    <div className='col-12 col-md-6 d-flex justify-content-end'>
                                        {this.props.isEmployee &&
                                            <StickButton position="rightSide" className="zocial Kaydet"
                                                         style={{fontSize: 'x-small', backgroundColor: 'blue'}}
                                                         label="Tasla Kaydet" title="Taslak Olarak Kaydet"
                                                         onClick={this.saveForm}
                                                         disabled={isChanged === false ||
                                                             this.state.formResponse.state === this.statesEnum.INAPPROVABLE ||
                                                             this.state.formResponse.state === this.statesEnum.APPROVED ||
                                                             this.state.formResponse.state === this.statesEnum.REJECTED ||
                                                             this.state.isLoading}
                                            />}
                                        {this.props.isEmployee &&
                                            <StickButton position="rightSide" className="zocial Onay"
                                                         style={{fontSize: 'x-small', backgroundColor: 'forestgreen'}}
                                                         label="Kaydet ve Gnder" title="Kaydet ve Gnder"
                                                         onClick={this.saveFormWithApprove}
                                                         disabled={this.state.formResponse.state === this.statesEnum.INAPPROVABLE ||
                                                             this.state.formResponse.state === this.statesEnum.APPROVED ||
                                                             this.state.isLoading || this.state.response["Onay Alan"]?.Selection === "Kabul Etmiyorum" || JSON.stringify(this.state.response["Onay Alan"]) === '{}' ||
                                            (Object.keys(uiSchemas).some(fieldName => {
                                            const fieldOptions = uiSchemas[fieldName]?.["ui:options"];
                                            return fieldOptions && fieldOptions.inputType === "tel" &&
                                            this.state.response[fieldName] && this.state.response[fieldName].replace(/\s/g, '').length < 10;}))}
                                            />}
                                    </div>
                                </div>
                            </div>
                        </Form>
                    </div>
                </div>
            </ModalBody>
            <ModalFooter/>
        </Modal>
    }

    render() {
        return (<div style={{paddingLeft: 20, paddingRight: 20}}>
            {this.state.sentApprovalAlert}
            {this.state.approveAlert}
            {this.state.rejectAlert}
            {this.state.alert}
            {this.renderSurveyForm()}
        </div>)
    }
}

export default SurveyForm;