import React, {Component} from 'react';
import {UncontrolledTooltip} from "reactstrap";
import SquareButton from "../../../components/SquareButton";
import {validateComponent, validateField} from "../../../common/validation";
import {deepCopyObject} from "../../../helper/helper";
import Alert from "react-s-alert";
import _ from 'lodash';
import EmployeeDetails from "../../kepik/employee/component/EmployeeDetails";
import {isDepartmentMandatory} from "../../kepik/common/PeykHelper";
import {requestK} from "../../../helper/APIUtils";

class UserGeneralInfo extends Component {

  constructor(props) {
    super(props);

    this.state = {
      mode: 'generalBrowse',
			error: {}
    };

    this.validateClass = this.validateClass.bind(this);
    this.validateComponent = this.validateComponent.bind(this);
    this.validateField = this.validateField.bind(this);
    this.validateMessage = this.validateMessage.bind(this);
    this.switchToEditMode = this.switchToEditMode.bind(this);
    this.saveUser = this.saveUser.bind(this);
  }

  componentDidMount() {
    this.readTitleAll();
  }

  readTitleAll = () => {
    let self = this;
    let params = {
      url: `/employeecustom/readTitleAll`,
      method: "get"
    };
    requestK(params)
      .then(function (response) {
        self.setState({titleList: response.data});
      })
      .catch(function (error) {
      });
  };

  validateField(e) {
    let id = e.target.id;
    let rule = e.target.dataset.vdata;
    let boundary = e.target.dataset.vlength;
    let result = validateField(rule, boundary, e.target.value);
    let error = this.state.error;
    error = Object.assign(error, {[id]: result});
    this.setState({error});
  }

  validateClass(id) {
    let error = this.state.error;
    return (error[id] === undefined ? "" : (error[id].valid ? " has-success" : " has-danger"));
  }

  validateMessage(id) {
    let error = this.state.error;
    return (error[id] === undefined ? '' : (error[id].message));
  }

  validateComponent() {
    let result = validateComponent(this);
    this.setState({
      error: result.error
    });
    return result
  }

  saveUser() {
    let generalInfoValidity = this.validateComponent();
    if (generalInfoValidity.valid) {
      let completeUser = deepCopyObject(this.props.completeUser);

      let finalState = {};
      finalState.firstName = completeUser.firstName;
      finalState.lastName = completeUser.lastName;
      finalState.username = completeUser.username;
      finalState.email = completeUser.email;

      if (!_.isEqual(this.props.initState, finalState)) {
        this.props.handleSubmit();
        this.setState({mode: 'generalBrowse'})
      } else {
        Alert.warning('Herhangi bir deiiklik yapmadnz', {
          html: true,
          position: 'top-right',
          effect: 'stackslide',
          timeout: 5000
        });
        this.setState({mode: 'generalBrowse'})
      }
    } else {
      Alert.error('Zorunlu alanlari doldurunuz', {
        html: true,
        position: 'top-right',
        effect: 'stackslide',
        timeout: 5000
      })
    }
  }

  switchToEditMode() {
    this.setState({mode: 'generalEdit'})
  }

  render() {
    return (
      <div>

        {/*Customer Vkn/Tckn Row*/}
        {!this.props.isEmployee &&
        <div className="col-sm-12 row" style={{margin: '30px 0 30px 0'}}>
          <div className="col-sm-6 row">
            <div className="col-sm-5">
              <label className="col-form-label" style={{fontWeight: 'bold'}}>Vkn/Tckn: </label>
            </div>
            <div className="col-sm-7">
              <label
                className="col-form-label">{this.props.currentCustomer && this.props.currentCustomer.vknTckn}</label>
            </div>
          </div>

          <div className="col-sm-6 row">
            <div className="col-sm-5">
              <label className="col-form-label" style={{fontWeight: 'bold'}}>Unvan: </label>
            </div>
            <div className="col-sm-7">
              <label
                className="col-form-label">{this.props.currentCustomer && this.props.currentCustomer.formattedName}</label>
            </div>
          </div>
        </div>}
        {/*End of Customer Vkn/Tckn Row*/}
        {!this.props.isEmployee &&
        <div style={{padding: '0 15px 0 15px'}}>
          <hr/>
        </div>
        }
        {/*Edit Button Row*/}
        {!this.props.isEmployee &&
        <div className="col-sm-12" style={{textAlign: 'right'}}>
          <SquareButton title={this.state.mode === 'generalEdit' ? 'Kaydet' : 'Dzenle'}
                        img={this.props.loading ? 'sandwatch.png' :  (this.state.mode === 'generalEdit' ? 'save.png' : 'edit.png')}
                        style={this.state.mode === 'generalEdit' ? {background: 'linear-gradient(rgba(135, 226, 146, 0.65), #3abf44)'} :
                          {background: 'linear-gradient(rgba(154, 193, 218, 0.61), #6e8da2)'}}
                        onClick={()=>this.state.mode === 'generalEdit' ? this.saveUser() : this.switchToEditMode()}/>
        </div>
        }
        {this.props.isEmployee && <div className="col-sm-12" style={{textAlign: 'right'}}><br/></div>}
        <div className="col-sm-12 row" style={{margin: '0 0 10px 0'}}>
          <div className="col-sm-6 row">
            <div className="col-sm-5">
              <label className="col-form-label" style={{fontWeight: 'bold'}}>Ad: </label>
            </div>
            <div className="col-sm-7">
              {this.state.mode === 'generalBrowse' &&
              <label className="col-form-label">{this.props.completeUser && this.props.completeUser.firstName}</label>}

              {this.state.mode === 'generalEdit' &&
              <div className={"form-group" + this.validateClass('tFirstName')}>
                <input className="form-control"
                       type="text"
                       name="firstName"
                       id="tFirstName"
                       data-vlength="1,255"
                       data-vdata="required"
                       onBlur={this.validateField}
                       ref="tFirstName"
                       value={this.props.completeUser && this.props.completeUser.firstName}
                       onChange={(e) => this.props.onChangeGeneralInfo(e)}/>
                {this.validateMessage('tFirstName') !== "" &&
                <UncontrolledTooltip placement="right" target="tFirstName" delay={0}>
                  {this.validateMessage("tFirstName")}
                </UncontrolledTooltip>}
              </div>}
            </div>
          </div>

          <div className="col-sm-6 row">
            <div className="col-sm-5">
              <label className="col-form-label" style={{fontWeight: 'bold'}}>Soyad: </label>
            </div>
            <div className="col-sm-7">
              {this.state.mode === 'generalBrowse' &&
              <label className="col-form-label">{this.props.completeUser && this.props.completeUser.lastName}</label>}

              {this.state.mode === 'generalEdit' &&
              <div className={"form-group" + this.validateClass('tLastName')}>
                <input className="form-control"
                       type="text"
                       name="lastName"
                       id="tLastName"
                       data-vlength="1,255"
                       data-vdata="required"
                       onBlur={this.validateField}
                       ref="tLastName"
                       value={this.props.completeUser && this.props.completeUser.lastName}
                       onChange={(e) => this.props.onChangeGeneralInfo(e)}/>
                {this.validateMessage('tLastName') !== "" &&
                <UncontrolledTooltip placement="right" target="tLastName" delay={0}>
                  {this.validateMessage("tLastName")}
                </UncontrolledTooltip>}
              </div>}
            </div>
          </div>
        </div>
        <div className="col-sm-12 row" style={{margin: '0 0 10px 0'}}>
          <div className="col-sm-6 row">
            <div className="col-sm-5">
              <label className="col-form-label" style={{fontWeight: 'bold'}}>Kullanc Ad: </label>
            </div>
            <div className="col-sm-7">
              {this.state.mode === 'generalBrowse' &&
              <label className="col-form-label">{this.props.completeUser && this.props.completeUser.username}</label>}

              {this.state.mode === 'generalEdit' &&
              <div className={"form-group" + this.validateClass('tUsername')}>
                <input className="form-control"
                       type="text"
                       name="username"
                       id="tUsername"
                       disabled={!!this.props.isEmployee}
                       data-vlength="1,255"
                       data-vdata="required"
                       onBlur={this.validateField}
                       ref="tUsername"
                       value={this.props.completeUser && this.props.completeUser.username}
                       onChange={(e) => this.props.onChangeGeneralInfo(e)}/>
                {this.validateMessage('tUsername') !== "" &&
                <UncontrolledTooltip placement="right" target="tUsername" delay={0}>
                  {this.validateMessage("tUsername")}
                </UncontrolledTooltip>}
              </div>}
            </div>
          </div>
          {!this.props.isEmployee &&
          <div className="col-sm-6 row">
            <div className="col-sm-5">
              <label className="col-form-label" style={{fontWeight: 'bold'}}>e-Posta: </label>
            </div>
            <div className="col-sm-7">
              {this.state.mode === 'generalBrowse' &&
              <label className="col-form-label">{this.props.completeUser && this.props.completeUser.email}</label>}

              {this.state.mode === 'generalEdit' &&
              <div className={"form-group" + this.validateClass('tEmail')}>
                <input className="form-control"
                       type="text"
                       name="email"
                       id="tEmail"
                       data-vlength="1,255"
                       data-vdata="email"
                       onBlur={this.validateField}
                       ref="tEmail"
                       value={this.props.completeUser && this.props.completeUser.email}
                       onChange={(e) => this.props.onChangeGeneralInfo(e)}/>
                {this.validateMessage('tEmail') !== "" &&
                <UncontrolledTooltip placement="right" target="tEmail" delay={0}>
                  {this.validateMessage("tEmail")}
                </UncontrolledTooltip>}
              </div>}
            </div>
          </div>}
        </div>
        {/* End of 2nd Row*/}

        <div className="col-sm-12 row" style={{margin: '0 0 15px 0'}}>
          <div className="col-sm-6 row" style={{paddingLeft: '30px'}}>
            {!this.props.impersonate && <button className='TekBtn edit'
                    onClick={this.props.openPreWarningForPasswordChange}> ifre Deitir
            </button>}
          </div>
          <div className="col-sm-6 row" style={{paddingLeft: '30px'}}>
            {this.props.isEmployee &&
            <button className='TekBtn detay genislik'
                    style={{ background: 'linear-gradient(rgba(125, 125, 125, 0.57), rgb(111, 111, 111))'}}
                    onClick={() => {
                      this.setState({displayDetailDialog: true})
                    }}> Personel Kartnz
            </button>
            }
          </div>
        </div>


        {this.state.displayDetailDialog && this.props.completeUser &&
        <EmployeeDetails selectedId={this.props.completeUser.employeeId} isOpen={this.state.displayDetailDialog}
                         propsHistory={this.props.history} titleList={this.state.titleList}
                         isDepartmentOwner={isDepartmentMandatory(this)}
                         isBordroOwner={false} isFormOwner={false}
                         isKepOwner={false} readOnly={true}
                         hasGrant={this.props.hasGrant}
                         closeDetailDialog={() => this.setState({displayDetailDialog: false})}/>
        }

      </div>
    )
  }
}

export default UserGeneralInfo;