import React from "react";

class LinkWidget extends React.Component {
  constructor(props) {
    super(props);
    this.state = {link: props.schema.default, label: props.label};
  }

  render() {
    return (
      <div>
        <a href={(this.state.link.startsWith("http")||this.state.link.startsWith("https"))? this.state.link : "http://" + this.state.link}
           target="_blank">{"Tklaynz..."}</a>
      </div>
    )
  }
}

export default LinkWidget;
