    static toBeDisplayed(state, isAnswerable) {
        if (isAnswerable !== 1 && stateCompare(state, "<", "APPROVED_BY_EMPLOYEE")) {
            return true;
        } else return !!(isAnswerable === 1 && stateCompare(state, "<", "ANSWERED_BY_EMPLOYEE"));
    }

