    static previewButtonCondition(row) {
        return row.document &&
        row.document.formTemplate ?
            ApprovalPage.toBeDisplayed(row.document.state, row.document.formTemplate.isAnswerable) : true;
    }

    documentApprovalCheck = () => {
        const params = {
            url: `/approval/documentApprovalCheck`,
            method: "post",
            data: this.state.selection
        };

        requestK(params)
            .then((response) => {
                const data = response.data;
                this.setState(
                    {
                        isSignorApprove: data.isSignorApprove,
                        allowDecline: data.isDeclineAllowed
                    },
                    () => {
                        this.setState({alert: this.getApprovalAlert()});                    }
                );
            })
            .catch((error) => {
                showAxiosError(error);
            });
    };




    approveSelected() {
        this.approveList(this.state.selection);
    }

    approveList(list) {
        const approveRequestData = list.map(id => ({
            id: id,
            approveStatus: "CONFIRMED"
        }));

        const approveRequest = {
            url: `/approval/approveAll`,
            method: "post",
            data: approveRequestData
        };

        this.setState({ isLoading: true, disabledApproveBtn: true, alert: null });

        requestK(approveRequest)
            .then(response => {
                Alert.success(response.data, {
                    position: 'top-right',
                    effect: 'stackslide',
                    timeout: 5000
                });
                this.setState({ isLoading:false})
                if (!this.state.selected.answerable || this.state.selected.documentType === "BORDRO") {
                    this.refreshTable();
                } else {
                    this.setState({ isAnswered: false, alert: null, selection: [] });
                }
            })
            .catch(error => {
                this.setState({ isLoading:false})
                this.refreshTable();
                showAxiosError(error);
            })
            .finally(() => {
                this.setState({
                    isLoading: false,
                    disabledApproveBtn: false,
                    alert: null,
                    selection: []
                });
            });
    }


    getApprovalAlert() {
        const additionalMessage = (
            <>
                {(this.state.isSignorApprove || this.state.allowDecline) && (
                    <p>
                        Toplu onay ileminde semi olduunuz dokman listesi ierisinde
                    </p>
                )}
                {this.state.isSignorApprove && (
                    <p>
                        <strong>'E-imza veya Onaylama'</strong> gerekli dokmannz bulunmaktadr.
                    </p>
                )}
                {this.state.allowDecline && (
                    <p>
                        <strong>'Reddedilebilir'</strong> bir dokmannz bulunmaktadr.
                    </p>
                )}
            </>
        );

        return (
            <SweetAlert showCancel success
                        className="custom-sweetalert"
                        confirmBtnText="Evet"
                        cancelBtnText="Hayr"
                        confirmBtnBsStyle="success"
                        cancelBtnBsStyle="default"
                        onConfirm={this.approveSelected}
                        onCancel={() => this.setState({alert: null})}
            >
                {additionalMessage}
                leminiz, seilen dokman saysna bal olarak uzun srebilir. Ltfen bu sre iersinde taraycy kapatmaynz.
                Yine de seilenleri onaylamak istiyorsanz,<br />
                Dokmanlar onaylanacak ve onaynz Tbitak zaman damgas ile
                mhrlenerek saklanacaktr.
            </SweetAlert>
        );
    }

