import React, {Component} from 'react';
import 'react-table/react-table.css';
import {Button, Col, Form, FormGroup, Input, Label, Modal, ModalBody, ModalFooter, ModalHeader, Nav} from 'reactstrap';
import 'moment/locale/tr';
import StickButton from "../../../components/StickButton";
import { Alert } from 'react-bootstrap';
import moment from "moment/moment";
import {renewListForPrivileges} from "../common/PeykHelper";
import Table from "../../../components/Table/Table";
import {requestK} from "../../../helper/APIUtils";
import {showAxiosError} from "../../../helper/helper";
import TextOverFlowTooltip from "../../../components/TextOverFlowTooltip";


class SmsPaymentInformationPage extends Component {

    constructor(props) {
        super(props);
        this.state = {
            alert: null,
            loading: false,
            showModal: false,
            displayDetailDialog: false,
            valid: {},
            manual: false,
            list: [],
            totalIdList: [],
            total: 0,
            searching: false,
            searchQuery: undefined,
            page: 0,
            size: 10,
            selection: [],
            selectedAll: false,
            isKepOwner: false,
            options: [],
            types: [],
            isLoading: false,
            pagePurchase:0,
            sizePurchase: 10,
            totalPurchase: 0,
            listPurchase:[],
            loadingPurchase: false,
            searchingPurchase: false,
            manualPurchase: false,
            content: [],
        };

        this.fetchDataPurchase = this.fetchDataPurchase.bind(this);
        this.getTrStyles = this.getTrStyles.bind(this);
    }

    findCreditLog = () => {
        let self = this;
        let params = {
            url: `/credit/findCreditLog`,
            method: "get"
        };
        requestK(params)
            .then(function (response) {
            }).catch(function (error) {
            showAxiosError(error);
        });
    };

    componentDidMount() {
        this.findCreditLog();
    }

    fetchDataPurchase(accessor, direction, page, size) {
        let self = this;
        let sortStr = accessor !== undefined ? ('&sort=' + accessor + ',' + direction) : '&sort=id,desc';
        let searchQuery = !self.state.searchQuery ? '' : '&' + self.state.searchQuery;
        let params = {
            url: '/credit/customerPurchase?page=' + page + '&size=' + size + searchQuery + sortStr,
            method: "get",
        };
        self.setState({loadingPurchase: true, searchingPurchase: true, manualPurchase: self.manualPurchase === true ? false : self.manualPurchase});
        requestK(params).then(function (response) {
            self.setState({
                pagePurchase: page,
                sizePurchase: size,
                totalPurchase: response.data.totalElements,
                loadingPurchase: false,
                searchingPurchase: false,
                content: response.data.content,
            });
        }).catch(function (error) {
            self.setState({loadingPurchase: false, searching: false});
            showAxiosError(error);
        });
    }

    renderTables() {
        let columnsList = [
            {
                Header: "Oluturulan Tarih",
                accessor: "createdAt",
                sortable: false,
                Cell: row => (
                    <div style={{margin: 0}}>
                        {moment(row.createdAt).isValid() ? moment(row.createdAt).format("DD-MM-YYYY HH:mm:ss") : ''}
                    </div>
                )
            },
            {
                Header: "Satn Alnan Tarih",
                accessor: "purchasedAt",
                sortable: false,
                Cell: row => (
                    <div style={{margin: 0}}>
                        {moment(row.purchasedAt).isValid() ? moment(row.purchasedAt).format("DD-MM-YYYY HH:mm:ss") : ''}
                    </div>
                )
            },
            {
                Header: "Kredi",
                accessor: "credit",
                sortable: false,

            },
            {
                Header: "Kredi Tr",
                accessor: "creditType",
                sortable: false,
            },
            {
                Header: "Fiyat",
                accessor: "price",
                sortable: false,
            },
            {
                Header: "Satn Alan",
                accessor: "firstName",
                sortable: false,
                Cell: row => (<div style={{margin:0}}><TextOverFlowTooltip text={row.firstName + " " + row.lastName} maxLength={30}/></div>)
            },
            {
                Header: "Aklama",
                accessor: "explanation",
                sortable: false
            },
            {
                Header: "Referans Kodu",
                accessor: "referenceCode",
                sortable: false,
            },
            {
                Header: "Durumu",
                accessor: "status",
                sortable: false,
            }
        ];
        let columns = renewListForPrivileges(this, columnsList, "accessor", this.state.isKepOwner);
        return <Table columns={columns}
                      content={this.state.content}
                      loading={this.state.loadingPurchase}
                      loadingText="Kaytlar getiriliyor..."
                      total={this.state.totalPurchase}
                      page={this.state.pagePurchase}
                      size={this.state.sizePurchase}
                      manual={this.state.manualPurchase}
                      options={["5", "10", "20", "50", "100"]}
                      getTrStyles={this.getTrStyles}
                      fetchData={this.fetchDataPurchase}/>
    }
    getTrStyles(item) {
        return (item.statusType && item.statusType !== 'ACTIVE') ? {
            backgroundColor: 'lightgray',
            webkitTextFillColor: 'darkgray'
        } : {};
    }

    render() {
        return <div className="row baseBackground">
            <Nav className="topbar" style={{paddingLeft: '14px', height: '53px'}}>
                <StickButton position="leftSide" className="zocial Yenile" style={{fontSize: 'x-small'}}
                             label="Sonu Yenile" title="Yenile"
                             disabled={this.state.disabledBtn}
                             onClick={() => {
                                 this.setState({manualPurchase: true})
                             }}/>
            </Nav>
            <div className="row">
                <div className="col-sm-12">
                    <div className="col-sm-12 satirBasi">
                        <br></br>
                        <Alert variant="success" style={{backgroundColor: 'orange'}}>
                            {/*<MdCheckCircle style={{marginRight: '5px'}} />*/}
                            deme ilemlerinin tamamlanmas sonras satn aldnz kontr PEYK sistemine yklenecektir. deme ilemlerinize ait son durumu aadaki listeden takip edebilirsiniz.
                        </Alert>
                        <div className="col-sm-12 satirBasi">
                            <div className="card-body">
                                {this.renderTables()}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div style={{ display: 'flex', justifyContent: 'center' }}>
                <StickButton position="Side"
                             className="zocial GeriDon"
                             style={{fontSize: 'small'}}
                             label="Sms Sayfasna Dn" title="Sms Sayfasna Git"
                             disabled={this.state.disabledBtn}
                             onClick={() => {this.props.history.push({pathname: `/${this.props.peykName}/smsInformation/`});}}/>
            </div>
        </div>

    }
}

export default SmsPaymentInformationPage;
