import React, {useEffect, useState} from "react";
import PropTypes from "prop-types";
import * as dropEffects from "./dropEffects";
import {Col, Label} from "reactstrap";
import Select from "../../../local_node_modules/react-select";
import {requestK} from "../../../helper/APIUtils";
import {showAxiosError} from "../../../helper/helper";
import Row from "react-bootstrap/Row";

const insideStyle = {
    backgroundColor: "#cccccc",
    opacity: 0.5,
};

const DropTarget = props => {
    const [isOver, setIsOver] = useState(false);
    const [groupList, setGroupList] = useState([]);
    const [surveyAnswerWays, setSurveyAnswerWays] = useState([]);
    const [selectedGroupList, setSelectedGroupList] = useState([]);
    const [personalInfo, setPersonalInfo] = useState(false);
    const [answerWay, setAnswerWay] = useState(null);
    const [surveyAnswerGroups, setSurveyAnswerGroups] = useState([]);


    const dragOver = ev => {
        ev.preventDefault();
        ev.dataTransfer.dropEffect = props.dropEffect;
    };

    const drop = ev => {
        const droppedItem = ev.dataTransfer.getData("drag-item");
        if (droppedItem) {
            props.onItemDropped(droppedItem);
        }
        setIsOver(false);
    };

    const dragEnter = ev => {
        ev.dataTransfer.dropEffect = props.dropEffect;
        setIsOver(true);
    };

    const dragLeave = () => setIsOver(false);

    const onHandlerChange = (e) => {
        setPersonalInfo(e.target.checked);
        if (!e.target.checked)
            setAnswerWay(null);
    }

    const onChangeGroupList = (e) => {
        let selectedGroupList = [];
        let selectedSurveyAnswerGroupList = [];
        e.forEach(selected => {
            let existSurveyAnswerGroup = props.surveyAnswerGroups?.find(surveyAnswerGroup => (surveyAnswerGroup.group !== null && surveyAnswerGroup.group.id === selected.group.id));
            if (existSurveyAnswerGroup) {
                selectedSurveyAnswerGroupList.push(existSurveyAnswerGroup);
            } else {
                selectedSurveyAnswerGroupList.push({id: null, "group": selected.group})
            }

        });
        setSurveyAnswerGroups(selectedSurveyAnswerGroupList);
        selectedSurveyAnswerGroupList.map(selected => (selectedGroupList.push({
            value: selected.group.id,
            label: selected.group.name,
            group: selected.group
        })));
        setSelectedGroupList(selectedGroupList);
    }
    const onChangeAnswerWay = (e) => {
        if (e == null) {
            setAnswerWay(null);
        } else {
            setAnswerWay(e.value);
        }
    }

    const getGroupList = () => {
        let params = {
            url: '/employee/getGroups',
            method: "get"
        };
        requestK(params)
            .then(function (response) {
                let groupList = [];
                response.data.map((group) => {
                    groupList.push({value: group.id, label: group.name, group: group})
                });
                setGroupList(groupList);
            }).catch(function (error) {
            showAxiosError(error);
        });

    }
    const getSurveyAnswerWays = () => {
        let params = {
            url: '/employee/getSurveyAnswerWays',
            method: "get"
        };
        requestK(params)
            .then(function (response) {
                let surveyAnswerWays = [];
                response.data.map((answerWay) => {
                    surveyAnswerWays.push(answerWay)
                });
                setSurveyAnswerWays(surveyAnswerWays);
            }).catch(function (error) {
            showAxiosError(error);
        });

    }
    useEffect(() => {
        getGroupList();
        getSurveyAnswerWays();
        setPersonalInfo(props.personalInfo);
        setAnswerWay(props.answerWay !== undefined ? props.answerWay : null);
        setSurveyAnswerGroups(props.surveyAnswerGroups !== undefined ? props.surveyAnswerGroups : []);
        let selectedGroupList = [];
        if (props.surveyAnswerGroups != undefined && props.surveyAnswerGroups != null) {
            props.surveyAnswerGroups.forEach(selected => {
                if (selected.group != null) {
                    selectedGroupList.push({
                        value: selected.group.id,
                        label: selected.group.name,
                        group: selected.group
                    });
                }
            });
            setSelectedGroupList(selectedGroupList);
        }
    }, [props.personalInfo, props.surveyAnswerGroups, props.answerWay])

    return (
        <>
            <div className='drop-list-header'>

                <div style={{position: 'absolute'}}>
                    {props.alert &&
                        <div className='alert-box fade-out'>
                            {props.alert.message}
                        </div>}
                </div>

                <div style={{width: '100%'}}>
                    <div className="form-group row" style={{marginTop: '35px', marginBottom: '30px'}}>
                        <div className="col-md-2">
                            <Row>
                                <Col className="col-md-8">
                                    <label htmlFor="personalInfo">zlk formu mu?</label>
                                </Col>
                                <Col className="align-items-center">
                                    <input
                                        type='checkbox'
                                        id="personalInfo"
                                        checked={personalInfo}
                                        onChange={onHandlerChange}
                                    />
                                </Col>
                            </Row>
                        </div>
                        <div className="col-md-4">
                            <Row>
                                <Col className="col-md-3">
                                    <Label id="answerWays">Cevap ekli</Label>
                                </Col>
                                <Col className="drop-list-header-select">
                                    <Select
                                        className="select-control"
                                        for="answerWays"
                                        disabled={!personalInfo}
                                        placeholder="Seiniz..."
                                        value={answerWay}
                                        onChange={(e) => onChangeAnswerWay(e)}
                                        options={surveyAnswerWays}
                                    />
                                </Col>
                            </Row>
                        </div>
                        <div className="col-md-4">
                            <Row>
                                <Col className="col-md-3" style={{alignItems: "center"}}>
                                    <Label id="group">lgili Gruplar</Label>
                                </Col>
                                <Col className="drop-list-header-select">
                                    <Select
                                        className="select-control"
                                        for="group"
                                        multi={true}
                                        placeholder="Seiniz..."
                                        value={selectedGroupList}
                                        onChange={(e) => e && onChangeGroupList(e)}
                                        options={groupList}
                                    />
                                </Col>
                            </Row>
                        </div>
                        <div className="col-md-2 drop-list-header-save-btn-container">

                            <div className="d-flex align-items-center pr-3" onClick={() => props.resetEditor()}>
                                <i className="fas fa-trash-alt" id="resetEditor" title="Temizle"/>
                            </div>

                            <button className='saveButton' disabled={props.loading}
                                    onClick={() => props.onSubmitForm(personalInfo, surveyAnswerGroups, answerWay)}>Kaydet
                            </button>
                        </div>
                    </div>
                </div>
            </div>

            <div
                onDragOver={dragOver}
                onDrop={drop}
                onDragEnter={dragEnter}
                onDragLeave={dragLeave}
                style={{width: "100%", height: "100%", ...(isOver ? insideStyle : {})}}
            >
                {props.children}
            </div>
        </>
    );
};

DropTarget.propTypes = {
    onItemDropped: PropTypes.func.isRequired,
    dropEffect: PropTypes.string,
    children: PropTypes.oneOfType([PropTypes.arrayOf(PropTypes.node), PropTypes.node]).isRequired,
};

DropTarget.defaultProps = {
    dropEffect: dropEffects.All,
};

export default DropTarget;