import React, {Component} from "react";
import {Button, Col, Form, FormGroup, Input, Label, Row, UncontrolledTooltip} from "reactstrap";
import {ACCESS_TOKEN, KEP_IK_API_BASE_URL} from "./constants";
import {encrypt, login, requestK} from "./helper/APIUtils";
import {getCaptchaCode, showAxiosError} from "./helper/helper";
import Alert from "react-s-alert";
import Timer from "react-compound-timer";
import Joyride from "react-joyride";
import VideoModal from "./components/VideoModal";
import ReCAPTCHA from "react-google-recaptcha";
import ApproveDocumentViewer from "./accounting/kepik/approval/helper/ApproveDocumentViewer";

const recaptchaRef = React.createRef();

class TwoStepVerification extends Component {

  videos = [
    {title: "ifremi Nasl Yenilerim?", id: "an-v6rIDvPA"},
  ];

  switchVideo = (video) => {
    this.setState({video: video, showEczaciHelpModal: true})
  };

  constructor(props) {
    super(props);
    this.state = {
      verificationCodeSent: false,
      sending: false,
      success: false,
      isEmployee: false,
      passwordError: '',
      passwordRepeatError: '',
      selected: {
        password: "",
        passwordRepeat: "",
        verificationCode: ""
      },
      steps: [
        {
          target: '.login-panel',
          content: "Yeni ifrenizi bu ekranda oluturacaksnz. Ekran aldnda 6 haneli dorulama kodu telefonunuza SMS ile gnderilmektedir. ",
          placement: "center",
        },
        {
          target: '.input-group:nth-child(1)',
          content: "Yeni bir ifre giriniz.",
          placement: "auto",
        },
        {
          target: '.input-group:nth-child(3)',
          content: "Yeni ifrenizi tekrar giriniz.",
          placement: "auto",
        },
        {
          target: '.input-group:nth-child(5)',
          content: "Telefonunuza gelen dorulama kodunu giriniz.",
          placement: "auto",
        },
        {
          target: '.login-button:nth-child(1)',
          content: "'Dorulama kodu sreniz 5 dakikadr. Bu sre dolduunda tekrar Dorulama Kodu ste' butonuna tklayarak yeni bir dorulama kodu edinebilirsiniz.",
          placement: "auto",
        },
        {
          target: '#savePassword',
          content: "Yeni ifrenizi kaydedin.",
          placement: "auto",
        },
      ],
      locale: {
        back: 'Geri',
        close: 'Kapat',
        last: 'Son',
        next: 'leri',
        skip: 'Atla',
      },
      joyrideActive: false,
      shouldRun: true,
      showPasswordIcon: false,
      showPasswordIcon2: false,
      showEczaciHelpModal: false,
      showPopupNotification: true,
      savePassword: false,
      isExistPasswordPolicy: false,
      isUpperCase: false,
      isLowerCase: false,
      isOneNumber: false,
      isOneSymbol: false,
      isMaxLength: null,
      isMinLength: null,
      isPasswordHistoryScann: null,
      isReplacementFrequency: null,
      upperCase: false,
      lowerCase: false,
      oneNumber: false,
      oneSymbol: false,
      maxLength: null,
      minLength: null,
      passwordHistoryScann: null,
      replacementFrequency: null,
      existFileList: [],
      mediaModal: false,
      checkKvkkDocument: false
    };
  }


  componentDidMount() {
    getCaptchaCode(this);
    this.verificationRequest();
    this.isEmployee();
    this.checkCaptcha();
    this.getPasswordPolicy();
    this.getKvkkDocuments();
  }

  verificationRequest = () => {
    let self = this;
    let match = this.props.match;
    let token = match.params.token;
    this.setState({sending: true});
    requestK({
      url: `/passwordCreation/verificationCodeRequest`,
      method: "post",
      data: token
    }).then(function (response) {
      Alert.success(response.data, {
        position: 'top-right',
        effect: 'stackslide',
        timeout: 10000
      });
      self.setState({verificationCodeSent: true, sending: false});
    }).catch(function (error) {
      showAxiosError(error);
      self.setState({verificationCodeSent: false, sending: false});
      self.props.history.push("/login");
      // if (error.response && (error.response.data === "Linkin geerlilik sresi doldu!" || error.response.data === "Bu linkle ilem yaplmtr, ifre olutur sayfasndan ifre ileminizi gerekletirebilirsiniz!"))
      //   setTimeout(self.props.history.push("/login"), 3000);
    })
  };

  checkCaptcha = () => {
    let self = this;
    let match = this.props.match;
    let token = match.params.token;
    requestK({
      url: `/passwordCreation/checkIncorrectEntryCount`,
      method: "post",
      data: token
    }).then(function (response) {
      if (response.data && parseInt(response.data) > 3) {
        self.setState({showCaptcha: true});
      }
    }).catch(function (error) {
      // showAxiosError(error);
    })
  };

  loginScreen = () => {
    this.props.history.push("/login");
  };

  handleCaptcha = (key) => {
    this.setState({
      captchaStatus: true,
      'g_recaptcha_response': key
    })
  };

  activateJoyride = (e) => {
    if (e) {
      e.preventDefault();
    }
    if (this.state.joyrideActive === false) {
      this.setState({flag: 0});
      this.setState({joyrideActive: true});
      if (this.state.width < 1000) {
        this.setState({flag: 1});
      }
    }
  };

  callback = (data, e) => {
    if (data.action === 'close' && data.type === 'step:after') {
      this.setState({shouldRun: false});
      this.setState({flag: 0});
    }
    if (data.action === 'stop' || data.action === 'reset' || data.action === 'skip') {
      this.setState({shouldRun: true});
      this.setState({joyrideActive: false});
      this.setState({flag: 0});
      this.activateJoyride(e);
    }
  };

  showPassword = (event) => {
    if (event.target.id === 'passwordIcon') {
      let password = document.getElementById('password');
      if (password.value !== '') {
        this.setState({showPasswordIcon: !this.state.showPasswordIcon});
        if (password.type === 'password') {
          password.type = 'text';
        } else {
          password.type = 'password';
        }
      }
    }

    if (event.target.id === 'repeatPasswordIcon') {
      let repeatPassword = document.getElementById('passwordRepeat');
      if (repeatPassword.value !== '') {
        this.setState({showPasswordIcon2: !this.state.showPasswordIcon2});
        if (repeatPassword.type === 'password') {
          repeatPassword.type = 'text';
        } else {
          repeatPassword.type = 'password';
        }
      }
    }
  };


  isEmployee = () => {
    let self = this;
    let match = this.props.match;
    let token = match.params.token;
    this.setState({sending: true});
    requestK({
      url: `/passwordCreation/isEmployee`,
      method: "post",
      data: token
    }).then(function (response) {
      self.setState({isEmployee: response.data, sending: false});
    }).catch(function () {
      self.setState({isEmployee: false, sending: false});
    })
  };

  getPasswordPolicy = () => {
    let self = this;

    if (self.props.location && self.props.location.policy) {
      self.setState({
        isExistPasswordPolicy: true,
        oneNumber: self.props.location.policy.oneNumber,
        oneSymbol: self.props.location.policy.oneSymbol,
        upperCase: self.props.location.policy.upperCase,
        lowerCase: self.props.location.policy.lowerCase,
        maxLength: self.props.location.policy.maxLength,
        minLength: self.props.location.policy.minLength,
        passwordHistoryScann: self.props.location.policy.passwordHistoryScanns,
        replacementFrequency: self.props.location.policy.replacementFrequency
      });
    } else {

      let match = this.props.match;
      let token = match.params.token;
      let params = {
        url: `/passwordCreation/getPasswordPolicy`,
        method: "post",
        data: token
      };
      requestK(params)
          .then(function (response) {
            if (response.data != null && response.data != "") {
              self.setState({
                isExistPasswordPolicy: true,
                oneNumber: response.data.oneNumber,
                oneSymbol: response.data.oneSymbol,
                upperCase: response.data.upperCase,
                lowerCase: response.data.lowerCase,
                maxLength: response.data.maxLength,
                minLength: response.data.minLength,
                passwordHistoryScann: response.data.passwordHistoryScanns,
                replacementFrequency: response.data.replacementFrequency
              });
            }
          }).catch(function () {
      })
    }
  };

  showPreview = () => {
    let self = this;
    if (self.state.selectedForm) {
      let formTemplate = Object.assign({}, self.state.selectedForm);
      let valid = formTemplate.formContent;
      if (valid) {
        let params = {
          url: `/formtemplate/previewContent`,
          responseType: 'arraybuffer',
          method: "post",
          data: formTemplate
        };
        self.setState({mediaModal: true});
        requestK(params)
            .then(function (response) {
              let blob = new Blob([response.data], {type: response.headers["content-type"]});
              let urlCreator = window.URL || window.webkitURL;
              let url = urlCreator.createObjectURL(blob);
              self.setState({url});
            }).catch(function (error) {
          showAxiosError(error);
          self.setState({mediaModal: false, title: null});
        });
      } else {
        Alert.error("Form ablon erii grntlenemedi.", {
          position: 'top-right',
          effect: 'stackslide',
          timeout: 8000
        });
      }
    }
  };

  setMediaViewer = () => {
    this.setState({url: null, mediaModal: false});
  }

  setDocument = async (document) => {
    await this.setState({selectedForm: document});
    await this.showPreview()
  }

  render() {
    const steps = this.state.steps;
    const locale = this.state.locale;
    return (
        <div>
          {this.state.mediaModal &&
              <ApproveDocumentViewer modal={this.state.mediaModal}
                                     url={this.state.url}
                                     closeModal={this.setMediaViewer}
                                     title="Form ablonu nizleme"
                                     listener={this.setMediaViewer}
              />
          }
          <Form>
            <Row style={{
              height: '100vh',
              backgroundImage: 'url(' + KEP_IK_API_BASE_URL + "/virtual/resource/loginBackground.png" + ')'
            }}>
              {this.state.joyrideActive &&
                  <Joyride
                      disableBeacon={false}
                      autoStart
                      locale={locale}
                      steps={steps}
                      continuous={true}
                      run={this.state.shouldRun}
                      scrollToFirstStep={true}
                      showProgress={true}
                      showSkipButton={true}
                      scrollToSteps={true}
                      disableCloseOnEsc={true}
                      disableOverlay={false}
                      disableOverlayClicks={false}
                      disableScrollParentFix={true}
                      keyboardNavigation={true}
                      hideBackButton={false}
                      disableOverlayClose={true}
                      callback={this.callback}
                      holePadding={0}
                      shouldRun={this.state.shouldRun}
                      styles={{
                        options: {
                          zIndex: 100000,
                          primaryColor: "#50a8e2"   // "#fc9636"
                        }
                      }}/>
              }
              {this.state.showEczaciHelpModal &&
                  <VideoModal video={this.state.video} isOpen={this.state.showEczaciHelpModal}
                              close={() => this.setState({showEczaciHelpModal: false})}/>
              }
              <Col sm='12' md='12' lg='12' className='login-panel-container'>
                <Row className='login-panel'>
                  <Col xs='12' style={{paddingBottom: "20px"}}>
                    <div className="listOfIcons" style={{position: "absolute", right: "4%"}}>
                      <div>
                        <button id="UncontrolledTooltipExample" className="login-button-icon"
                                onClick={this.loginScreen}>
                          <i className="fas fa-user-circle loginIcon" style={{fontSize: "20px"}}/>
                        </button>
                        <UncontrolledTooltip placement="auto" target="UncontrolledTooltipExample">
                          Giri Yap
                        </UncontrolledTooltip>
                      </div>
                      <div>
                        <button id="UncontrolledTooltipExample2" className="helpButton"
                                onClick={this.activateJoyride}>
                          <i className="far fa-question-circle helpButtonIcon"/>
                        </button>
                        <UncontrolledTooltip placement="auto" target="UncontrolledTooltipExample2">
                          Size yardmc olabilirim
                        </UncontrolledTooltip>
                      </div>
                      <div className="nav-item dropdown  videoIconContainer">
                        <button className="nav-link  videoIconButton" data-toggle="dropdown"
                                aria-haspopup="true" id="navbarDropdownMenuLink videoIconContainer"
                                aria-expanded="false"
                                style={{padding: 0}}>
                          <i className="fab fa-youtube videoIcon" id='youtubeIcon'/>
                        </button>

                        <Col className='notification-box'
                             onClick={() => this.setState({showVideoNotification: !this.state.showVideoNotification})}
                             style={{cursor: 'pointer'}}>
                        <span className="notification-count"><i className="fab fa-youtube videoIcon" id='youtubeIcon'
                                                                style={{color: 'white'}}/></span>
                          <div className="notification-bell">
                            <span className="bell-top"/>
                            <span className="bell-middle"/>
                            <span className="bell-bottom"/>
                            <span className="bell-rad"/>
                          </div>
                        </Col>
                        {this.state.showVideoNotification &&
                            <Col sm='12' className='notificationDescription'>
                              <label>ifre deitirme yardmc videomuzu aadaki 'youtube' ikonuna
                                tklayarak
                                izyelebilirsiniz.</label>
                            </Col>
                        }
                        <div className="dropdown-menu dropdown-menu-right" id="showVideos"
                             aria-labelledby="videosLink">
                          {
                            this.videos.map((video, i) =>
                                <a key={i} className="dropdown-item"
                                   onClick={() => this.switchVideo(video)}>{video.title}</a>)
                          }
                        </div>
                      </div>
                    </div>
                  </Col>
                  <Col xs='12' sm='12' md='4' lg='4' style={{textAlign: 'center', padding: '2% 3% 3% 3%'}}>
                    <img src={KEP_IK_API_BASE_URL + "/virtual/resource/login.png"} alt="now-logo"/>
                  </Col>
                  <Col xs='12' sm='12' md='8' lg='8' style={{padding: '2% 2% 1% 2%'}}>
                    <Col sm='12' md='12' lg='12' className="no-border  input-group input-group-lg">

								<span className="input-group-addon">
									<i style={{fontSize: '15px', color: 'white'}} className="fa fa-lock"
                                       aria-hidden="true"/>
								</span>
                      <input
                          placeholder={!this.state.validate ? "Yeni ifreyi giriniz." : 'ifreniz ifre politikasna uygun deil!'}
                          className={this.state.validate ? 'validatePassword' : 'red'}
                          autoComplete="off"
                          autoFocus
                          id="password"
                          name="password"
                          value={this.state.selected.password}
                          style={{
                            borderLeft: '1px solid #c3cdd4',
                            background: '#b5bdc6',
                            color: 'white',
                            fontSize: '14px',
                            fontWeight: 'bold',
                            borderRadius: 0
                          }}
                          onChange={this.editHandleInputChange}
                          onKeyDown={(event) => {
                            if (event.key === "Enter") {
                              event.preventDefault(); // Formun otomatik olarak gnderilmesini engeller
                            }
                          }}
                          className={`loginText form-control ${this.state.passwordError ? 'is-invalid' : ''}`}
                          type="password"/>
                      <span className="input-group-addon"
                            id='passwordToggle'
                            style={{
                              backgroundColor: 'rgba(111, 111, 111, 0.3)',
                              borderLeft: '1px solid rgb(195, 205, 212)',
                              padding: '11px 15px 11px 15px'
                            }}>
									<i style={{
                                      fontSize: '15px',
                                      color: 'rgba(108, 108, 108, 0.7)',
                                      paddingLeft: '0px',
                                      cursor: 'pointer'
                                    }}
                                       id='passwordIcon'
                                       className={this.state.showPasswordIcon ? "fa fa-eye" : "fa fa-eye-slash"}
                                       data-toggle='toggle'
                                       title='ifreyi Gster'
                                       onClick={(e) => this.showPassword(e)}
                                       aria-hidden="true"/>
								</span>
                    </Col>
                    <div className='' style={{paddingLeft: '15px'}}>{this.state.passwordError}</div>

                    <Col sm='12' md='12' lg='12' className="no-border  input-group input-group-lg">
								<span className="input-group-addon">
									<i style={{fontSize: '15px', color: 'white'}} className="fa fa-lock"
                                       aria-hidden="true"/>
								</span>

                      <input
                          placeholder={!this.state.validate ? "Yeni ifreyi tekrar giriniz." : 'ifreniz ifre politikasna uygun deil!'}
                          className={!this.state.validate ? '' : 'validatePassword'}
                          autoComplete="off"
                          id="passwordRepeat"
                          name="passwordRepeat"
                          value={this.state.selected.passwordRepeat}
                          style={{
                            borderLeft: '1px solid #c3cdd4',
                            background: '#b5bdc6',
                            color: 'white',
                            fontSize: '14px',
                            fontWeight: 'bold',
                            borderRadius: 0
                          }}
                          className={`loginText form-control ${this.state.passwordRepeatError ? 'is-invalid' : ''}`}
                          onChange={this.editHandleInputChange}
                          onKeyDown={(event) => {
                            if (event.key === "Enter") {
                              event.preventDefault(); // Formun otomatik olarak gnderilmesini engeller
                            }
                          }}
                          type="password"/>
                      <span className="input-group-addon"
                            id='repeatPassword'
                            style={{
                              backgroundColor: 'rgba(111, 111, 111, 0.3)',
                              borderLeft: '1px solid rgb(195, 205, 212)',
                              padding: '11px 15px 11px 15px'
                            }}>
									<i style={{
                                      fontSize: '15px',
                                      color: 'rgba(108, 108, 108, 0.7)',
                                      paddingLeft: '0px',
                                      cursor: 'pointer'
                                    }}
                                       id='repeatPasswordIcon'
                                       className={this.state.showPasswordIcon2 ? "fa fa-eye" : "fa fa-eye-slash"}
                                       aria-hidden="true"
                                       data-toggle='toggle'
                                       title='ifreyi Gster'
                                       onClick={(e) => this.showPassword(e)}/>
								</span>
                    </Col>
                    <div className='' style={{paddingLeft: '15px'}}>{this.state.passwordRepeatError}</div>
                    <Col sm='12' md='12' lg='12' className="no-border  input-group input-group-lg">
								<span className="input-group-addon">
									<i style={{fontSize: '15px', color: 'white'}}
                                       className="fa fa-mobile"/>
								</span>

                      <input placeholder="Dorulama Kodu"
                             autoComplete="off"
                             id="verificationCode"
                             name="verificationCode"
                             value={this.state.selected.verificationCode}
                             style={{
                               borderLeft: '1px solid #c3cdd4',
                               background: '#b5bdc6',
                               color: 'white',
                               fontSize: '14px',
                               fontWeight: 'bold'
                             }}
                             onChange={this.editHandleInputChange}
                             onKeyDown={(event) => {
                               if (event.key === "Enter") {
                                 event.preventDefault(); // Formun otomatik olarak gnderilmesini engeller
                               }
                             }}
                             className="loginText form-control"
                             type="text"/>
                    </Col>
                    {this.state.showCaptcha && <Col>
                      <ReCAPTCHA
                          ref={recaptchaRef}
                          sitekey={this.state.captchaCode}
                          onChange={this.handleCaptcha}/>
                    </Col>}
                  </Col>
                  {this.state.existFileList && this.state.isEmployee &&this.state.existFileList.length !== 0 &&
                      <Col xs='12' sm='12' md='12' lg='12'
                           className='login-button-container  d-flex align-items-center justify-content-end' style={{padding: '0 5% 0 0'}}>
                        <FormGroup row className="PasswordType">
                          <Row className='align-items-center'>
                            <Col xs={1} style={{top: '-5px', marginRight: '15px'}}>
                              <Input type="checkbox" name="lowerCase"
                                     style={{marginLeft: '10px', marginTop: '10px'}}
                                     checked={this.state.checkKvkkDocument}
                                     onChange={(e) => {
                                       this.setState({
                                         checkKvkkDocument: e.target.checked,
                                       });
                                     }}/>
                            </Col>
                            {this.state.existFileList && this.state.existFileList.map((document, index) =>
                                <span className='mr-1'><a href="#" onClick={() => {
                                  this.setDocument(document);
                                }}>{document.formName},</a> </span>
                            )}
                            <div className='ml-1'>
                              okudum, anladm.
                            </div>
                          </Row>
                        </FormGroup>
                      </Col>}
                  <Col xs='12' sm='12' md='12' lg='12' className='login-button-container'>
                    {this.renderVerificationCodeRequestButton()}
                  </Col>
                  {this.state.success ?
                      <Col xs='12' sm='12' md='12' lg='12' className='login-button-container'>
                        <Button color='primary' className='login-button-success' id="succes">
                          <div><i className="fa fa-check "/> ifreniz yenilendi, giri sayfasna ynlendiriliyor</div>
                        </Button>
                      </Col>
                      : <>

                        <Col xs='12' sm='12' md='12' lg='12' className='login-button-container'>
                          <Button color='primary' className='login-button' id="savePassword"
                                  disabled={!this.state.checkKvkkDocument && this.state.isEmployee && (this.state.existFileList && this.state.existFileList.length !== 0)}
                                  onClick={this.savePassword}>
                            {this.state.sending ?
                                <div><i className="fa fa-spinner fa-spin"/> Ltfen bekleyiniz</div>  : 'ifreyi Kaydet'}
                          </Button>
                        </Col>
                      </>}
                  <Col style={{textAlign: 'center'}}>
                    <Label style={{
                      width: '100%',
                      fontSize: '12px',
                      letterSpacing: '1px',
                      fontWeight: '600'
                    }}> ifre{this.state.oneNumber ? ' bir rakam' : ''}
                      {this.state.lowerCase ? ' bir kk harf' : ''} {this.state.upperCase ? ' bir byk harf' : ''}
                      {this.state.oneSymbol ? ' bir zel karakter' : ''}
                      {this.state.oneNumber || this.state.lowerCase || this.state.upperCase || this.state.oneSymbol ? ' iermelidir ' : ''}
                      Karakter says {this.state.minLength}- {this.state.maxLength} aras olmaldr ve
                      Son {this.state.passwordHistoryScann} ifreden farkl olmaldr. </Label>
                  </Col>

                </Row>
              </Col>
            </Row>

          </Form>
        </div>

    )
        ;
  }

  keyPress = (e) => {
    if (e.keyCode === 13 && this.state.checkKvkkDocument) {
      this.savePassword();
    }
  };

  renderVerificationCodeRequestButton = () => {
    if (this.state.verificationCodeSent) {
      return (
          <Timer id={"timer"} initialTime={300000} direction="backward" checkpoints={
            [
              {
                time: 300,
                callback: this.onTimerEnd,
              }
            ]
          }>
            <React.Fragment>
              <Button color="primary" className='login-button' onClick={this.verificationRequest}
                      disabled={this.state.verificationCodeSent || this.state.sending}>
                Tekrar Dorulama Kodu Iste (<Timer.Minutes/> d {" "}<Timer.Seconds/> s)
              </Button>
            </React.Fragment>
          </Timer>
      );
    } else {
      return (
          <Button color="primary" className='login-button' onClick={this.verificationRequest}
                  disabled={this.state.verificationCodeSent || this.state.sending}>
            Tekrar Dorulama Kodu Iste
          </Button>
      );
    }
  };

  onTimerEnd = () => {
    this.setState({verificationCodeSent: false});
  };

  isValid = () => {
    Alert.error("ifre Politikanz bulunmamaktadr, Sistem salaycnz ile irtibat kurunuz", {
      position: 'top-right',
      effect: 'stackslide',
      html: true,
      timeout: 1000
    });
    return false;
  };

  policyCheck = () => {
    let status = this.updatePasswordPolicyStatus(this.state.selected.password);
    if ((this.state.upperCase && status.isUpperCase !== true)) {
      Alert.error("ifrenizde Byk harf gerekmektedir.", {
        position: 'top-right',
        effect: 'stackslide',
        html: true,
        timeout: 5000
      });
    }
    if ((this.state.lowerCase && status.isLowerCase !== true)) {
      Alert.error("ifrenizde Kk harf gerekmektedir.", {
        position: 'top-right',
        effect: 'stackslide',
        html: true,
        timeout: 5000
      });
    }
    if ((this.state.oneNumber && status.isOneNumber !== true)) {
      Alert.error("ifrenizde Rakam gerekmektedir.", {
        position: 'top-right',
        effect: 'stackslide',
        html: true,
        timeout: 5000
      });
    }
    if ((this.state.oneSymbol && status.isOneSymbol !== true)) {
      Alert.error("ifrenizde Sembol gerekmektedir.", {
        position: 'top-right',
        effect: 'stackslide',
        html: true,
        timeout: 5000
      });
    }
    if ((this.state.selected.password.length > this.state.maxLength) || (this.state.selected.password.length < this.state.minLength)) {
      Alert.error("ifrenizin karakter saysn kontrol ediniz.", {
        position: 'top-right',
        effect: 'stackslide',
        html: true,
        timeout: 5000
      });
    }
    if ((this.state.selected.password !== this.state.selected.passwordRepeat)) {
      Alert.error("ifreniz tekrar ile uyumsuz.", {
        position: 'top-right',
        effect: 'stackslide',
        html: true,
        timeout: 5000
      });
    }
    if ((this.state.selected.verificationCode.length !== 6)) {
      Alert.error("Dorulama kodunuz 6 haneden olumaldr.", {
        position: 'top-right',
        effect: 'stackslide',
        html: true,
        timeout: 5000
      });
    }
    return (this.state.selected.password === this.state.selected.passwordRepeat
        && this.state.selected.password.length >= this.state.minLength
        // && !isNaN(this.state.selected.password)
        && this.state.selected.password.length <= this.state.maxLength
        && this.state.selected.verificationCode.length === 6
        && !this.state.sending && (!this.state.showCaptcha || this.state.captchaStatus)
        && ((this.state.upperCase && status.isUpperCase) || (!this.state.upperCase && !status.isUpperCase))
        && ((this.state.lowerCase && status.isLowerCase) || (!this.state.lowerCase && !status.isLowerCase))
        && ((this.state.oneNumber && status.isOneNumber) || (!this.state.oneNumber && !status.isOneNumber))
        && ((this.state.oneSymbol && status.isOneSymbol) || (!this.state.oneSymbol && !status.isOneSymbol))
    )
  };

  savePassword = () => {
    if (this.state.isExistPasswordPolicy ? this.policyCheck() : this.isValid()) {
      this.state.showCaptcha ? this.sendCodeWithCaptcha() : this.sendCode();
    } else {
      Alert.error("ifre Politikanza uygun deil", {
        position: 'top-right',
        effect: 'stackslide',
        html: true,
        timeout: 5000
      });
      this.resetPasswordPolicyStatus();
    }
  }

  sendCode = () => {
    if ((this.state.selected.password === this.state.selected.passwordRepeat) && this.state.selected.password.length > 0) {
      let self = this;
      let password = self.state.selected.password;
      let data = {
        "password": encrypt(password),
        "token": self.props.match.params.token,
        "code": self.state.selected.verificationCode,
        "captcha": null,
        "replacementFrequency": self.state.replacementFrequency,
        "passwordHistoryScann": self.state.passwordHistoryScann
      }
      this.setState({sending: true});
      requestK({
        url: `/passwordCreation/reset`,
        method: "post",
        data: data
      }).then(response => {
        this.setState({success:true});
        Alert.success(response.data, {
          position: 'top-right',
          effect: 'stackslide',
          timeout: 3000
        });
        setTimeout(() => {
          this.setState({success:false});
          self.props.history.push("/login");
        }, "3000");
      }).catch(error => {
        Alert.error(error.response.data, {
          position: 'top-right',
          effect: 'stackslide',
          timeout: 5000
        });
        this.resetPasswordPolicyStatus();
        this.setState({sending: false, captchaStatus: false});
        this.checkCaptcha();
      });
    } else {
      Alert.error("Sifreler uyusmuyor.");
    }
  };

  sendCodeWithCaptcha = () => {
    if (!this.state.captchaStatus) {
      Alert.error("Ltfen gvenlik dorulamasn tamamlaynz.");
    } else if ((this.state.selected.password === this.state.selected.passwordRepeat) && this.state.selected.password.length > 0) {
      let self = this;
      let password = self.state.selected.password;
      let payload = {
        "password": encrypt(password),
        "token": self.props.match.params.token,
        "code": self.state.selected.verificationCode,
        "captcha": self.state.g_recaptcha_response,
        "replacementFrequency": self.state.replacementFrequency,
        "passwordHistoryScann": self.state.passwordHistoryScann
      }
      this.setState({sending: true});
      requestK({
        url: `/passwordCreation/resetWithCaptcha`,
        method: "post",
        data: payload
      }).then(response => {
        Alert.success(response.data);
        this.setState({success:false});
        self.props.history.push("/login");
      }).catch(error => {
        Alert.error(error.response.data, {
          position: 'top-right',
          effect: 'stackslide',
          timeout: 5000
        });
        this.setState({sending: false, captchaStatus: false});
        this.checkCaptcha();
      });
      recaptchaRef.current.reset();
    } else {
      Alert.error("Sifreler uyusmuyor.");
    }
  };

  readUser = (token, password) => {
    let self = this;
    if (!!token) {
      requestK({
        url: `/passwordCreation/getUserName`,
        method: "post",
        data: token
      }).then(function (response) {
        let username = response.data;
        self.loginOperation(username, password);
      }).catch(function () {
        this.setState({success:false});
        self.props.history.push("/login");
      })
    } else {
      this.setState({success:false});
      self.props.history.push("/login");
    }
  };

  loginOperation = (username, password) => {
    let self = this;
    if (!!username && !!password) {
      const loginRequest =
          {
            'username': encrypt(username.toString()),
            'password': encrypt(password)
          };
      login(loginRequest)
          .then(response => {
            Alert.closeAll();
            sessionStorage.setItem(ACCESS_TOKEN, response.data.accessToken);
            self.props.onLogin();
          }).catch(() => {
        this.setState({success:false});
        self.props.history.push("/login");
      });
    } else {
      this.setState({success:false});
      self.props.history.push("/login");
    }
  };

  onlyInputNumber = (event) => {
    let ch = String.fromCharCode(event.which);
    if (!(/[0-9]/.test(ch))) {
      event.preventDefault();
      event.target.reportValidity();
      this.setState({validate: true});
    } else if (event.target.value.length > 7) {
      event.preventDefault();
      event.target.setCustomValidity('ifreniz en fazla 8 rakamdan oluabilir!');
      event.target.reportValidity();
    } else {
      event.target.setCustomValidity("");
      event.target.reportValidity();
      this.setState({validate: false});
    }
  };

  resetPasswordPolicyStatus = () => {
    this.setState(
        {
          isUpperCase: false,
          isLowerCase: false,
          isOneNumber: false,
          isOneSymbol: false,
          isMaxLength: null,
          isMinLength: null,
        }
    )
  }

  updatePasswordPolicyStatus = (password) => {

    let isUpperCase, isLowerCase, isOneNumber, isOneSymbol = false;
    for (let i = 0; i < password.length; i++) {
      let ch = password.substring(i, i + 1);

      if (this.state.upperCase && !this.state.isUpperCase && (/[A-Z]/.test(ch))) { // Byk harf art var ve daha nce hi Byk harf gelmedi
        isUpperCase = true;
        this.setState({isUpperCase, validate: false})
      } else if (this.state.lowerCase && !this.state.isLowerCase && (/[a-z]/.test(ch))) {
        isLowerCase = true;
        this.setState({isLowerCase, validate: false})
      } else if (this.state.oneNumber && !this.state.isOneNumber && (/[0-9]/.test(ch))) {
        isOneNumber = true;
        this.setState({isOneNumber, validate: false})
      } else if (this.state.oneSymbol && !this.state.isOneSymbol && (/[-!$%^&*()_+|~=`{}\[\]:";'<>?,.\/]/.test(ch))) {
        isOneSymbol = true;
        this.setState({isOneSymbol, validate: false})
      }

    }
    return {isUpperCase, isLowerCase, isOneNumber, isOneSymbol};
  };

  editHandleInputChange = (event) => {
    const target = event.target;
    let value = target.value;
    let name = target.name;
    value = value.replace(" ", "");
    this.setState((prevState) => {
      prevState.selected[name] = value;
      return {
        selected: prevState.selected,
      };
    });
  };

  getKvkkDocuments = () => {
    let self = this;
    let match = this.props.match;
    let token = match.params.token;
    let params = {
      url: `/formtemplate/readKvkkDocuments`,
      method: "post",
      data: token
    };
    requestK(params)
        .then(function (response) {
          self.setState({
            existFileList: response.data
          });
        }).catch(function (error) {
      showAxiosError(error);
    });
  }

}

export default TwoStepVerification;
