import React from 'react';
import Swal from "sweetalert2";

export default class FileBase64 extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            files: [],
            fileLimitExceed: ""
        };
    }
    addNameToDataURL(dataURL, name) {
        return dataURL.replace(";base64", ";name=".concat(unescape(encodeURIComponent(name)), ";base64"));
    }
    handleChange(e) {
        let self = this;
        self.setState({fileLimitExceed: ""});
        // get the files
        let files = e.target.files;

        // Process each file
        var allFiles = [];
        for (var i = 0; i < files.length; i++) {

            let file = files[i];

            // Make new FileReader
            let reader = new FileReader();

            // Convert the file to base64 text
            reader.readAsDataURL(file);
            // on reader load somthing...
            reader.onload = () => {
                let content = self.addNameToDataURL(reader.result, file.name)
                // Make a fileInfo Object
                let fileInfo = {
                    name: file.name,
                    type: file.type,
                    size: Math.round(file.size / 1000) + ' kB',
                    base64: content ,
                    file: file,
                };

                // Push it to the state
                if (fileInfo.type.toUpperCase() == "APPLICATION/PDF" && file.size > 8 * 1024 * 1024) {
                    allFiles = [];
                    self.setState({fileLimitExceed: "PDF dosya boyutlar 8MB'tan  kk olmaldr"});
                    return;
                }
                if ((fileInfo.type.toLowerCase() == "image/jpeg" || fileInfo.type.toUpperCase() == "image/jpg") && file.size > 8 * 1024 * 1024) {
                    allFiles = [];
                    self.setState({fileLimitExceed: "Resim dosya boyutlar 8MB'tan  kk olmaldr."});
                    return;
                }
                if ((fileInfo.type.toLowerCase() !== "image/jpeg" && fileInfo.type.toUpperCase() !== "image/jpg" && fileInfo.type.toUpperCase() !== "APPLICATION/PDF" )) {
                    allFiles = [];
                    self.setState({fileLimitExceed: "Dosya tipi PDF,JPG yada JPEG olmaldr."});
                    return;
                }
                allFiles.push(fileInfo);

                // If all files have been proceed
                if (allFiles.length == files.length) {
                    // Apply Callback function
                    if (self.props.multiple) self.props.onDone(allFiles);
                    else self.props.onDone(allFiles[0]);
                }

            } // reader.onload

        } // for

    }

    render() {
        return (
            <div>
                <div>
                    <label htmlFor="files" className="btn">Dosyalar Se</label>
                    <input
                        id="files"
                        type="file"
                        onChange={this.handleChange.bind(this)}
                        multiple={this.props.multiple}
                        style={{visibility: "hidden"}}
                        accept="application/pdf , image/jpeg, image/jpg"
                    />
                </div>
                <div>
                    {this.state.fileLimitExceed !== "" && <p style={{color: 'red'}}>{this.state.fileLimitExceed}</p>}
                </div>
            </div>
        );
    }
}

FileBase64.defaultProps = {
    multiple: false,
};
