import React, {Component} from 'react';
import {requestK} from "../helper/APIUtils";
import {showAxiosError} from "../helper/helper";
import {SERVLET_PATH, VirtualHost} from "../constants";
import {Link} from "react-router-dom";
import OngoingProcesses from "./component/OngoingProcesses";
import VideoModal from "../components/VideoModal";
import SwitchCompanyModal from "../components/SwitchCompanyModal";
import "./Header.css";
import DownloadRequestModal from "../components/DownloadRequestModal";

class Header extends Component {

  videos = [
    {prefix: "eczacikart", title: "C grubu kan rnleri faturas SGKya nasl gnderilir?", id: "lTjRaQZI4_Y"},
    {prefix: "eczacikart", title: "Eczackart Portal Giris ve Aktivasyon lemleri", id: "zGgh_nCEWAI"},
    {prefix: "eczacikart", title: "la depolarna ade Fatura Nasl Kesilir?", id: "KRUBAJDI04A"},
    {prefix: "eczacikart", title: "Ticari Fatura nedir?", id: "vRaOsVQZUfE"},
    {prefix: "eczacikart", title: "Temel Faturay Nasl ade Ederim?", id: "rP2nGVIJ7Cc"},
    {prefix: "eczacikart", title: "Giden Faturalarm nasl grntlerim?", id: "6S-xxKsFqTE"},
    {prefix: "eczacikart", title: "Gelen Faturalarm nasl grntlerim?", id: "hUQfIQqg5ZQ"},
    {prefix: "eczacikart", title: "E-Ariv fatura nasl grntlerim?", id: "SqzfA_DonEc"},
    {prefix: "eczacikart", title: "E-Ariv fatura nasl oluturulur?", id: "QwOrggqbSPw"},
    {prefix: "eczacikart", title: "G daresine E-Fatura Nasl Kesilir?", id: "p744jl02sGw"},
    {prefix: "eczacikart", title: "E-Fatura'ya Logo Kae mza Nasl Eklenir?", id: "sdq4E3nHwjU"},
    {prefix: "eczacikart", title: "Hastalara E-Ariv Fatura Nasl Gnderilir?", id: "6k0VRjm62W4"},
    {prefix: "eczacikart", title: "SGK'ya Gnderilen Tm Faturalar Grntleme", id: "5zB7CHCp03g"},
    {prefix: "eczacikart", title: "Gelen E-Fatura'larda Firma Bazl Arama Nasl Yaplr?", id: "eGMwhlxnl1M"},
    {prefix: "eczacikart", title: "Gelen-Giden E-Fatura'lar Arive Nasl Kaldrlr?", id: "2huSAiPzOLo"},
    {prefix: "eczacikart", title: "SGK'ya E-Fatura nasil gonderilir?", id: "VVjrKDDt5p8"},
    {prefix: "eczacikart", title: "Kontr Nasl Yklenir?", id: "T3-g_IoLqBM"},
    {prefix: "eczacikart", title: "Medikal Market Faturas Nasl Oluturulur?", id: "hbXoEjAihsg"},
    {prefix: "eczacikart", title: "Kooplara ade Faturas Nasl Kesilir?", id: "AsLJJmPVIWU"},
    {prefix: "togb", title: "SGK'ya fatura nasl kesilir?", id: "8-fkvL4LtxQ"},
    {prefix: "togb", title: "Cari kart nasl oluturulur?", id: "wC9jvG4j7qs"},
    {prefix: "togb", title: "Efatura nasil oluturulur?", id: "NHHVeozTJLA"},
    {prefix: "togb", title: "Gercek kisilere efatura olusturma?", id: "i9h9g6yq2ss"},
    {prefix: "togb", title: "Efaturaya kae imza logo nasl eklenir?", id: "up8nffCAh2U"},
    {prefix: "togb", title: "Ticari ve temel fatura nedir?", id: "xkZTaDuT7Jo"},
  ];

  switchVideo = (e, video) => {
    this.setState({video: video, showHelpVideoModal: true});
    window.removeEventListener('resize', this.updateDimensions);
  };

  showHelpVideos = () => {
    return ["eczacikart", "togb"].includes(VirtualHost.conf.resourcePrefix);
  };

  constructor(props) {
    super(props);

    this.state = {
      height: window.innerHeight,
      width: window.innerWidth,
      branches: [],
      branchIndex: 0,
      key: Math.random().toString(36),
      pbTopMargin: '-26px 20px',
      pmHeight: '0',
      checkBoxState: false,
      showHelpVideoModal: false,
      video: null,
      multiCompany: false,
      companyList: [],
      showChangeCompany: false,
      top: '-1800px',
      alternativeName: null
    };

    this.isPeyk = this.isPeyk.bind(this);
    this.showReload = this.showReload.bind(this);
    this.switchBranch = this.switchBranch.bind(this);
    this.updateDimensions = this.updateDimensions.bind(this);
    this.readWorkingCompanyList = this.readWorkingCompanyList.bind(this);
    this.isEmployeeOnMultiCompanies = this.isEmployeeOnMultiCompanies.bind(this);
    this.defineProfileMenuHeightOrButtonTopMargin = this.defineProfileMenuHeightOrButtonTopMargin.bind(this);
    this.closeVideoModal = this.closeVideoModal.bind(this);
    this.openDownloadRequestModal = this.openDownloadRequestModal.bind(this);
    this.closeDownloadRequestModal = this.closeDownloadRequestModal.bind(this);
  }

  componentDidMount() {
    let currentUser = this.props.currentUser;
    let activeBranch = currentUser.branches[currentUser.activeBranchIndex];
    this.setState({
      branches: currentUser.branches,
      activeBranch: activeBranch,
      branchIndex: currentUser.activeBranchIndex,
      formattedName: currentUser.formattedName,
      name: currentUser.name, // User.firstName

    });
    window.addEventListener("resize", this.updateDimensions);
    window.addEventListener('resize', this.defineProfileMenuHeightOrButtonTopMargin);
    if (this.isPeyk()) {
      this.readWorkingCompanyList();
    }
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.updateDimensions);
    window.removeEventListener('resize', this.defineProfileMenuHeightOrButtonTopMargin)
  }

  updateDimensions(event) {
    let currentHeight = this.state.height;
    let currentWidth = this.state.width;

    if (currentHeight !== window.innerHeight || currentWidth !== window.innerWidth) {
      this.setState({
        height: window.innerHeight,
        width: window.innerWidth,
        key: Math.random().toString(36)
      });
    }
  }

  switchBranch(index) {
    if (this.state.branchIndex === index) return;

    let self = this;
    let formData = new FormData();
    formData.append("index", index);

    let options = {
      method: 'post',
      url: `/user/switchBranch`,
      data: formData,
    };

    requestK(options)
      .then(response => {
        let {branches} = self.state;

        self.setState({
          activeBranch: branches[index],
          branchIndex: index,
        });
        setTimeout(function () {
          window.location.href = SERVLET_PATH + "/";
        }, 100)
      })
      .catch(function (error) {
        showAxiosError(error);
      });
  }

  showReload() {
    return (this.props.location && !(this.props.location.indexOf("Create") !== -1 ||
      this.props.location.indexOf("Edit") !== -1 ||
      this.props.location.indexOf("homePage") !== -1 ||
      this.props.location.indexOf("kullaniciOlustur") !== -1 ||
      this.props.location === "/"));
  }

  isPeyk() {
    return this.props.currentUser && this.props.currentUser.holder.accounting && this.props.currentUser.holder.accounting.KEPIK;
  }

  isEmployee() {
    return this.props.currentUser && this.props.currentUser.employeeId;
  }

  isEmployeeOnMultiCompanies() {
    return this.state.multiCompany;
  }

  readWorkingCompanyList() {
    let self = this;
    if (self.props.currentUser.employeeId) {
      let params = {
        url: `/employee/companyList?employeeId=${self.props.currentUser.employeeId}`,
        method: "get"
      };
      requestK(params)
        .then(function (response) {
          let companyList = response.data;
          let values= self.props.currentUser.id;
          for(let i=0; i< companyList.length; i++){
            if(companyList[i].value === values){
              self.setState({alternativeName: companyList[i].label});
            }
          }

          if (companyList && companyList.length > 1) {
            self.setState({companyList, multiCompany: true});
          } else {
            self.setState({multiCompany: false});
          }

        }).catch(function (error) {
        self.setState({multiCompany: false});
      });
    } else {
      self.setState({multiCompany: false});
    }
  }

  defineProfileMenuHeightOrButtonTopMargin() {

    let pmlHeight = document.getElementById('profileMenuList') ? document.getElementById('profileMenuList').offsetHeight : 0;

    let totalHeight;
    let margin;

    if (window.innerWidth < 992) {
      totalHeight = (pmlHeight + 32).toString() + 'px';

    } else {
      totalHeight = '0';
    }

    if (document.getElementById('profileMenuButton')) {
      if (document.getElementById('profileMenuButton').checked) {
        margin = (pmlHeight + 5).toString() + 'px' + ' ' + '20px';

      } else {
        margin = '-26px 20px';
      }

    } else {
      margin = '0'
    }

    return (
      this.setState({
        pmHeight: totalHeight,
        pbTopMargin: margin,
        top: '-' + totalHeight
      })
    )
  }

  setCheckboxState() {

    let currentCheckBoxState = this.state.checkBoxState;

    if ((document.getElementById('profileMenuButton').checked) !== currentCheckBoxState) {
      this.defineProfileMenuHeightOrButtonTopMargin()
    }

    this.setState({
      checkBoxState: document.getElementById('profileMenuButton').checked
    })

  }

  closeVideoModal() {
    window.addEventListener('resize', this.updateDimensions);
    this.setState({showHelpVideoModal: false})
  }

  openDownloadRequestModal() {
    window.removeEventListener('resize', this.updateDimensions);
    this.setState({downloadRequestModal: true})
  }

  closeDownloadRequestModal() {
    window.addEventListener('resize', this.updateDimensions);
    this.setState({downloadRequestModal: false})
  }

  render() {

    let right = this.state.width > 991 ? (this.props.offsetWidth === null || this.props.offsetWidth === 24) ? {
      float: 'right',
      marginRight: '17em'
    } : {
      float: 'right',
      marginRight: '3em'
    } : {float: 'right'};

    let employeeName = this.isEmployee() ? this.props.currentUser.name : "";
    let companyName = (this.state.formattedName || this.state.alternativeName ) ; //+ "-" + this.state.activeBranch;

    return !this.props.isAuthenticated ?
      <div/> :
      (
        <div key={this.state.key}
             className="topbar3 col-sm-12 navbar navbar-expand-lg navbar-transparent  navbar-absolute bg-primary"
             id="merdingen">

          <input type="checkbox" name="toggle" id="profileMenuButton" onClick={() => this.setCheckboxState()}/>
          <label htmlFor="profileMenuButton" style={{margin: this.state.pbTopMargin}}/>

          <div className="container-fluid">
            <div className="navbar-wrapper col-sm-7">
              <div className="navbar-toggle">
                <button type="button" className="navbar-toggler">
                  <span className="navbar-toggler-bar bar1"/>
                  <span className="navbar-toggler-bar bar2"/>
                  <span className="navbar-toggler-bar bar3"/>
                </button>
              </div>
              <div className="col-sm-12 navbar-brand" style={{color: '#404040', paddingLeft: 0}}>
                <div className='col-sm-12' style={{display: 'flex', paddingLeft: 0}}>
                  <div className='col-sm-6 col-md-4' id='employee-name'>
                    {this.isEmployee() && !this.isEmployeeOnMultiCompanies() && <label>{employeeName} - {companyName}</label> }
                    {this.isEmployeeOnMultiCompanies() && <label>{employeeName}</label> }
                    {!this.isEmployee() && !this.isEmployeeOnMultiCompanies() &&
                    <span>{companyName}</span>
                    }
                  </div>
                  {this.isEmployee() && this.isEmployeeOnMultiCompanies() &&
                  <div className='col-sm-6 col-md-8'>
                    <div onClick={() => this.setState({showChangeCompany: true})} style={{cursor: 'pointer'}}>
                      <label id='icon-content'>Firma Deitir: </label>
                      <i className="fas fa-exchange-alt" id='switchLogo' title={"Firma Deitir"}/>
                    </div>
                  </div>}
                </div>
                {this.isEmployee() && this.isEmployeeOnMultiCompanies() &&
                <div className="col-sm-12 navbar-company-list">
                  <div id='switch-company-name'>
                    {companyName}
                  </div>
                </div>}
              </div>
            </div>

            <div className="navbar-collapse justify-content-end pull-right profileMenu"
                 style={{height: this.state.pmHeight, top: this.state.checkBoxState ? '0' : this.state.top}}
                 id="navigation">

              <div style={right}>
                <ul id='profileMenuList' className="navbar-nav" style={{border: 'none'}}>


                  {this.props.showOngoingProcesses && !this.isEmployee() &&
                  <OngoingProcesses {...this.props}/>
                  }

                  {this.showHelpVideos() &&
                  <li className="nav-item dropdown" id='eczaciHelpButton'>
                    <button className="nav-link"
                            id="videosLink" data-toggle="dropdown" aria-haspopup="true"
                            aria-expanded="false"
                            title='Yardmc Videolar'
                            style={{
                              color: window.innerWidth < 992 ? "white" : '#525252',
                              background: 'transparent',
                              border: 'none',
                              outline: 'none'
                            }}>
                      Yardmc Videolar
                    </button>
                    <div className="dropdown-menu dropdown-menu-right dropdown-scroll" aria-labelledby="videosLink">
                      {
                        this.videos.filter(video => video.prefix === VirtualHost.conf.resourcePrefix).map((video, i) =>
                          <a key={i} className="dropdown-item"
                             onClick={(e) => this.switchVideo(e, video)}>{video.title}</a>)
                      }
                    </div>
                  </li>
                  }

                  {this.showReload() &&
                  <li id='reloadButton'>
                    <button type="button" id='refreshButton' data-toggle="tooltip" style={{
                      marginLeft: '8px',
                      border: 'none',
                      cursor: 'pointer',
                      background: "none",
                      color: window.innerWidth < 992 ? "white" : '#525252'
                    }} className=" pull-left"
                            onClick={(e) => this.props.reload(true, "header")}
                            rel="tooltip">
                      <i className="fas fa-sync" style={{marginTop: '15px', marginRight: '1em'}}/>
                    </button>
                  </li>
                  }
                  {this.isPeyk() &&
                  <li className="nav-item dropdown" href="#" id='downloadRequestButton'>
                    <a className="nav-link"
                       onClick={this.openDownloadRequestModal}
                       data-toggle="tooltip" rel='tooltip'
                       title='Bekleyen ndirmeler' id="downloadRequests"
                       style={{color: window.innerWidth < 992 ? "white" : '#525252'}}>
                      <i className="fas fa-download"/>
                    </a>
                  </li>
                  }
                  <li className="nav-item dropdown" href="#" id='branchButton'>
                    <button className="nav-link dropdown-toggle"
                            id="branchesLink" data-toggle="dropdown" aria-haspopup="true"
                            aria-expanded="false"
                            style={{
                              color: window.innerWidth < 992 ? "white" : '#525252',
                              background: 'transparent',
                              border: 'none',
                              outline: 'none'
                            }}>
                      <i className="fa fa-building"/>
                      <p>
                        <span className="d-lg-none d-md-block">ubeler</span>
                      </p>
                    </button>
                    {this.state.branches.length > 0 && <div className="dropdown-menu dropdown-menu-right dropdown-scroll"
                         aria-labelledby="branchesLink">
                      {
                        this.state.branches.map((branch, i) =>
                          <a key={i} className="dropdown-item"
                             style={this.state.branchIndex === i ? {background: 'lightblue'} : {}}
                             onClick={() => this.switchBranch(i)}>{branch}</a>
                        )
                      }
                    </div>}
                  </li>

                  <li className="nav-item dropdown" href="#" id='profileButton'>
                    <button className="nav-link dropdown-toggle" data-toggle="dropdown"
                            aria-haspopup="true" id="navbarDropdownMenuLink" aria-expanded="false"
                            style={{
                              color: window.innerWidth < 992 ? "white" : '#525252',
                              background: 'transparent',
                              border: 'none',
                              outline: 'none'
                            }}>
                      <i className={"fa fa-user" + (this.props.impersonate ? "-secret" : "")}/>
                      <p>
                        <span className="d-lg-none d-md-block">Profil</span>

                      </p>
                    </button>
                    <div className="dropdown-menu dropdown-menu-right"
                         aria-labelledby="navbarDropdownMenuLink">
                      <Link to="/userProfile" style={{textDecoration: 'none'}}>
                        <div className="dropdown-item">
                          <span>{this.props.currentUser.name ? this.props.currentUser.name : this.props.currentUser.username}</span>
                        </div>
                      </Link>
                      <a className="dropdown-item" onClick={this.props.onLogout}>
                        <span>k</span>
                      </a>
                      {this.props.impersonate &&
                      <a className="dropdown-item" onClick={() => this.props.onImpersonate(false)}>
                        <span>Kullanc Ekranndan k</span>
                      </a>
                      }
                    </div>
                  </li>
                </ul>
              </div>
            </div>
          </div>
          {this.state.downloadRequestModal &&
          <DownloadRequestModal isOpen={this.state.downloadRequestModal} close={this.closeDownloadRequestModal}/>
          }
          {this.state.showHelpVideoModal &&
          <VideoModal video={this.state.video} isOpen={this.state.showHelpVideoModal} close={this.closeVideoModal}/>
          }
          {this.state.showChangeCompany &&
          <SwitchCompanyModal isOpen={this.state.showChangeCompany}
                              close={() => this.setState({showChangeCompany: false})}
                              companyList={this.state.companyList} onLogin={this.props.onLogin}/>
          }
        </div>
      );


  }
}

Header.defaultProps = {
  showOngoingProcesses: false
};

export default Header;