import React, {Component} from 'react';
import {Modal, ModalBody, ModalFooter, ModalHeader, UncontrolledTooltip} from "reactstrap";
import Select from"../../../local_node_modules/react-select"
import {validateComponent, validateField} from "../../../common/validation";
import Table from "../../../components/Table/Table";
import {deepCopyObject} from "../../../helper/helper";
import Notify from "react-notification-alert";
import MaskedInput from "react-text-mask";
import emailMask from "text-mask-addons/dist/emailMask";

class CustomerBranchInfo extends Component {
	constructor(props) {
		super(props);

		this.notifyRef = React.createRef();

		this.state = {
			error: {},
			initCustAddData: '',
			deleteModal: false,
			branchDetailModal: false,
			branchList: [],
			page: 0,
			size: 10
		};

		this.validateField = this.validateField.bind(this);
		this.validateClass = this.validateClass.bind(this);
		this.validateMessage = this.validateMessage.bind(this);
		this.validateComponent = this.validateComponent.bind(this);
		this.isChanged = this.isChanged.bind(this);
		this.toggle = this.toggle.bind(this);
		this.remoteSwitchBranchDetailModal = this.remoteSwitchBranchDetailModal.bind(this);
		this.arrangeData = this.arrangeData.bind(this);
		this.cancelCurrentBranch = this.cancelCurrentBranch.bind(this);
		this.handleEdit = this.handleEdit.bind(this);
		this.showErrorMessage = this.showErrorMessage.bind(this);
	}

	componentDidMount() {
		this.setState({branchList: this.props.branchList});
	}

	componentWillReceiveProps(nextProps) {
		this.setState({branchList: this.props.branchList});
	}

	validateField(e) {
		let id = e.target.id;
		let rule = e.target.dataset.vdata;
		let boundary = e.target.dataset.vlength;
		let value = e.target.value;
		let result = validateField(rule, boundary, value);
		let error = this.state.error;
		error = Object.assign(error, {[id]: result});
		this.setState({error});
	}

	validateClass(id) {
		let error = this.state.error;
		return (error[id] === undefined ? "" : (error[id].valid ? " has-success" : " has-danger"));
	}

	validateMessage(id) {
		let error = this.state.error;
		return (error[id] === undefined ? '' : (error[id].message));
	}

	validateComponent() {
		let result = validateComponent(this);
		this.setState({error: result.error});
		return result
	}

	handleEdit(data) {
		let index = this.state.branchList.indexOf(data);
		this.props.fillAddressForm(data, index);
		this.setState({
			initCustBranchData: data,
			selectedBranch: data
		});
		this.openBranchDetailModal();
	}

	remoteSwitchBranchDetailModal() {
		if (this.state.branchDetailModal === false) {
			this.setState({
				branchDetailModal: true
			})
		} else {
			this.setState({
				branchDetailModal: false
			})
		}
	}

	isChanged(initState, input) {
		if (JSON.stringify(initState) === JSON.stringify(input)) {
			return false
		} else {
			return true
		}
	}

	openBranchDetailModal() {
		this.setState({branchDetailModal: true});
	}

	toggle() {
		this.setState({branchDetailModal: !this.state.modal});
	}

	arrangeData(page, size) {
		this.setState({
			page: page,
			size: size,
			loading: false
		});
	}

	cancelCurrentBranch() {
		this.props.clearCustomerBranchAddress();
		this.setState({branchDetailModal: false});
	}

	showErrorMessage() {
		let notificationAlertOptions = {
			place: 'tr',
			message: (
				<div>
					<div>
						Hatali alanlar var.
					</div>
				</div>
			),
			type: "danger",
			autoDismiss: 'none'
		};

		this.notifyRef.current.notificationAlert(notificationAlertOptions)
	}

	selectStyle(item) {
		let error = deepCopyObject(this.state.error);
		if (error[item] === undefined || error[item].valid) {
			return null;
		} else {
			return {backgroundColor: '#f8f8f8', borderColor: '#ffcfcf'}
		}
	}

	render() {
		let customerBranchAddress = this.props.customerBranchAddress;

		const columns = [
			{
				Header: "ube Ad",
				accessor: "branchName",
				sortable: false
			},
			{
				Header: "ehir",
				accessor: 'city',
				sortable: false
			},
			{
				Header: "le",
				accessor: 'citySubdivisionName',
				sortable: false
			},
			{
				Header: "Telefon",
				accessor: 'phone',
				sortable: false
			},
			{
				Header: "E-posta",
				accessor: 'email',
				sortable: false
			},
			{
				Header: "Web Sitesi",
				accessor: 'webSite',
				sortable: false
			},
			{
				Header: "",
				Cell: row => (
					<div>
						<button type="button" onClick={() => this.handleEdit(row)} rel="tooltip"
								className="btn btn-info btn-sm btn-icon "
								style={{margin: "0px", background: 'linear-gradient(rgb(120, 177, 215), rgb(120, 177, 215))'}}>
							<i className="fa fa-edit"></i>
						</button>
					</div>
				)
			}
		];
		return (
			<div>
				<div className="col-sm-12 " style={{marginBottom: '20px'}}>
					<nav className="">
						<button type="button" data-toggle="tooltip" data-placement="bottom"
								style={{background: 'linear-gradient(rgba(159, 208, 55, 0.6), #9fd037)'}}
								rel="tooltip" className="TekBtn ekle btn-icon"
								onClick={() => {
									this.openBranchDetailModal()
								}}>ube Ekle
						</button>
					</nav>
				</div>

				<div className="card-body">
					<Table
						columns={columns}
						content={this.state.branchList}
						total={this.state.branchList.length}
						paginationEnabled={false}
					/>
				</div>

				<Modal isOpen={this.state.branchDetailModal} size='lg'>
					<ModalHeader toggle={this.cancelCurrentBranch}>ube Detaylar</ModalHeader>
					<ModalBody>
						<div>
							<Notify ref={this.notifyRef} className="modalAlert"/>
							<div className="col-sm-12 row form-row">
								<div className="col-sm-6 row">
									<div className="col-sm-6">
										<label className="col-form-label">ube Ad *</label>
									</div>
									<div className="col-md-6">
										<div className={"form-group" + this.validateClass('tBranchName')}>
											<input className="form-control"
												   type="text"
												   name="branchName"
												   id="tBranchName"
												   data-vlength="1,45"
												   onBlur={this.validateField}
												   ref="tBranchName"
												   value={customerBranchAddress.branchName}
												   onChange={(e) => this.props.onChangeCustomerBranchAddress(e)}/>
											{this.validateMessage('tBranchName') !== "" &&
											<UncontrolledTooltip placement="right" target="tBranchName" delay={0}>
												{this.validateMessage("tBranchName")}
											</UncontrolledTooltip>}
										</div>
									</div>
								</div>
								<div className="col-md-6 row">
									<div className="col-md-6">
										<label className="col-form-label">lke *</label>
									</div>
									<div className="col-md-6">
										<div className={"form-group" + this.validateClass('tCountry')}>
											<input className="form-control"
												   type="text"
												   name="country"
												   id="tCountry"
												   data-vlength="1,45"
												   onBlur={this.validateField}
												   ref="tCountry"
												   value={customerBranchAddress.country}
												   onChange={(e) => this.props.onChangeCustomerBranchAddress(e)}/>
											{this.validateMessage('tCountry') !== "" &&
											<UncontrolledTooltip placement="right" target="tCountry" delay={0}>
												{this.validateMessage("tCountry")}
											</UncontrolledTooltip>}
										</div>
									</div>
								</div>
							</div>

							<div className="col-md-12 row form-row">
								<div className="col-md-6 row">
									<div className="col-md-6">
										<label className="col-form-label">ehir *</label>
									</div>
									<div className="col-sm-6">
										<div className={"form-group" + this.validateClass('tCity')}>
											<Select
												clearable={false}
												className="select-control"
												name="city"
												id="tCity"
												onBlur={() => this.validateField({
													target: {
														id: "tCity",
														dataset: {vdata: "required"},
														value: customerBranchAddress.city
													}
												})}
												inputProps={{'vdata': "city", 'vlength': "1,45"}}
												ref="tCity"
												value={customerBranchAddress.city}
												placeholder="Seiniz..."
												style={this.selectStyle('tCity')}
												onChange={(e) => this.props.onCityChange(e)}
												options={this.props.cities.map(city => ({
													value: city.cityName,
													label: city.cityName
												}))}
											/>
											{this.validateMessage('tCity') !== "" &&
											<UncontrolledTooltip placement="right" target="tCity" delay={0}>
												{this.validateMessage("tCity")}
											</UncontrolledTooltip>}
										</div>
									</div>
								</div>

								<div className="col-md-6 row">
									<div className="col-md-6">
										<label className="col-form-label">le *</label>
									</div>
									<div className="col-sm-6">
										<div className={"form-group" + this.validateClass('tCitySubdivisionName')}>
											<Select
												clearable={false}
												className="select-control"
												name="citySubdivisionName"
												id="tCitySubdivisionName"
												onBlur={() => this.validateField({
													target: {
														id: "tCitySubdivisionName",
														dataset: {vdata: "required"},
														value: customerBranchAddress.citySubdivisionName
													}
												})}
												inputProps={{'vdata': "citySubdivisionName", 'vlength': "1,45"}}
												ref="tCitySubdivisionName"
												value={customerBranchAddress.citySubdivisionName}
												placeholder="Seiniz..."
												style={this.selectStyle('tCitySubdivisionName')}
												onChange={(e) => this.props.onCitySubdivisionChange(e)}
												options={this.props.getSubdivisions(customerBranchAddress.city)}
											/>
											{this.validateMessage('tCitySubdivisionName') !== "" &&
											<UncontrolledTooltip placement="right" target="tCitySubdivisionName"
																 delay={0}>
												{this.validateMessage("tCitySubdivisionName")}
											</UncontrolledTooltip>}
										</div>
									</div>
								</div>
							</div>

							<div className="col-md-12 row form-row">
								<div className="col-md-6 row">
									<div className="col-md-6">
										<label className="col-form-label">Web Sitesi </label>
									</div>
									<div className="col-md-6 form-group">
										<input className="form-control"
											   type="text"
											   name="webSite"
											   value={customerBranchAddress.webSite}
											   onChange={(e) => this.props.onChangeCustomerBranchAddress(e)}/>
									</div>
								</div>

								<div className="col-md-6 row">
									<div className="col-md-6">
										<label className="col-form-label">E-Posta *</label>
									</div>
									<div className="col-md-6">
										<div className={"form-group" + this.validateClass('tEmail')}>
											<MaskedInput className="form-control"
														 mask={emailMask}
														 placeholder="_________@______.___"
														 type="text"
														 name="email"
														 inputProps={{vdata:"email", vlength:'1,255'}}
														 id="tEmail"
														 ref="tEmail"
														 onBlur={() => this.validateField({
															 target: {
																 id: "tEmail",
																 dataset: {vdata: "email", vlength:'1,255'},
																 value: customerBranchAddress.email
															 }
														 })}
														 value={customerBranchAddress.email}
														 onChange={(e) => this.props.onChangeCustomerBranchAddress(e)}/>
											{this.validateMessage('tEmail') !== "" &&
											<UncontrolledTooltip placement="right" target="tEmail" delay={0}>
												{this.validateMessage("tEmail")}
											</UncontrolledTooltip>}
										</div>
									</div>
								</div>

								<div className="col-md-6 row">
									<div className="col-md-6">
										<label className="col-form-label">Telefon </label>
									</div>
									<div className="col-md-6 form-group">
										<MaskedInput className="form-control"
													 mask={['0','(', /[1-9]/, /\d/, /\d/, ')', ' ', /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/]}
													 placeholder="0(___) ___-____"
													 type="text"
													 name="phone"
													 value={customerBranchAddress.phone}
													 onChange={(e) => this.props.onChangeCustomerBranchAddress(e)}/>
									</div>
								</div>

								<div className="col-md-6 row">
									<div className="col-md-6">
										<label className="col-form-label">Faks </label>
									</div>
									<div className="col-md-6 form-group">
										<input className="form-control"
											   type="text"
											   name="fax"
											   value={customerBranchAddress.fax}
											   onChange={(e) => this.props.onChangeCustomerBranchAddress(e)}/>
									</div>
								</div>
							</div>

							<div className="col-md-12 row form-row">


								<div className="col-md-6 row">
									<div className="col-md-6">
										<label className="col-form-label">Sokak </label>
									</div>
									<div className="col-md-6 form-group">
										<input className="form-control"
											   type="text"
											   name="street"
											   value={customerBranchAddress.street}
											   onChange={(e) => this.props.onChangeCustomerBranchAddress(e)}/>
									</div>
								</div>

								<div className="col-md-6 row">
									<div className="col-md-6">
										<label className="col-form-label">Bina Ad </label>
									</div>
									<div className="col-md-6 form-group">
										<input className="form-control"
											   type="text"
											   name="buildingName"
											   value={customerBranchAddress.buildingName}
											   onChange={(e) => this.props.onChangeCustomerBranchAddress(e)}/>
									</div>
								</div>

							</div>

							<div className="col-md-12 row form-row">

								<div className="col-md-6 row">
									<div className="col-md-6">
										<label className="col-form-label">Bina No </label>
									</div>
									<div className="col-md-6 form-group">
										<input className="form-control"
											   type="text"
											   name="buildingNo"
											   value={customerBranchAddress.buildingNo}
											   onChange={(e) => this.props.onChangeCustomerBranchAddress(e)}/>
									</div>
								</div>

								<div className="col-md-6 row">
									<div className="col-md-6 ">
										<label className="col-form-label">Daire No </label>
									</div>
									<div className="col-md-6 form-group">
										<input className="form-control"
											   type="text"
											   name="doorNo"
											   value={customerBranchAddress.doorNo}
											   onChange={(e) => this.props.onChangeCustomerBranchAddress(e)}/>
									</div>
								</div>

								<div className="col-md-6 row">
									<div className="col-md-6">
										<label className="col-form-label">Posta Kodu </label>
									</div>
									<div className="col-md-6 form-group">
										<input className="form-control"
											   type="text"
											   name="postalCode"
											   value={customerBranchAddress.postalCode}
											   onChange={(e) => this.props.onChangeCustomerBranchAddress(e)}/>
									</div>
								</div>
							</div>
						</div>
					</ModalBody>
					<ModalFooter>
						<nav className="col-sm-12 row">
							<div className="col-sm-2">
								<button type="button" data-toggle="tooltip" data-placement="bottom"
										style={{background: 'rgb(158, 158, 158)', padding: '11px 45px 11px 20px'}}
										rel="tooltip" className="TekBtn vazgec"
										onClick={() => this.cancelCurrentBranch()}>ptal
								</button>
							</div>
							<div className="col-sm-8"></div>
							<div className="col-sm-2">
								<button type="button" data-toggle="tooltip" data-placement="bottom"
										style={{background: 'linear-gradient(rgba(159, 208, 55, 0.6), #9fd037)', padding: '11px 45px 11px 20px'}}
										rel="tooltip" className="TekBtn kaydet"
										onClick={() => this.props.addBranchToCustomer()}>Onayla
								</button>
							</div>
						</nav>
					</ModalFooter>
				</Modal>

			</div>
		)
	}
}

export default CustomerBranchInfo;