import React, {useCallback, useEffect, useState} from "react";
import DropTarget from "./DropTarget";
import {DraggablePanel} from "./DraggablePanel";
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import EditModal from "./EditModal";
import {createDefaultOptionByType, generateModalBody, generateSectionBody, onChange, uploadFile} from "./functions";
import Modal from "react-bootstrap/Modal";
import PreviewModal from "./PreviewModal";
import DisplayFileModal from "./DisplayFileModal";
import {convertJson} from "../../../common/FormSchemaJsonConverter";
import {requestK} from "../../../helper/APIUtils";
import Swal from "sweetalert2";
import {showAxiosError} from "../../../helper/helper";
import Alert from "react-s-alert";

function alertValidation(items) {
  Alert.error(items.properties.sections[0].name + " bos birakilamaz.", {
    position: 'top-right',
    effect: 'stackslide',
    timeout: 5000
  });
  return false;
}

export default function DropList(props) {
  const [state, setState] = useState({
    items: [],
    image: null,
    file: null,
    isEditModalOpen: false,
    isSelectOptionModalOpen: false,
    selectedDropdownItem: {},
    isPreviewModalOpen: false,
    selectedItem: {item: null, index: null},
    alert: null,
    isDisplayFileModalOpen: false,
    loading: false,
    err: true
  });

  const [saved, setSave] = useState(true);
  const [edit, setEditable] = useState(false);

  useEffect(()=> {
    let items = state.items;
    if(props.items) {
      items.push(props.items);
      setState(prevState => {
        return {...prevState, items}
      });
    }
  },[props.items]);

  const resetEditor = () => {
    setState({...state,items: []});
  };

  const itemDropped = item => {
    let droppedItem = JSON.parse(item);
    let items = state.items;
    droppedItem.id = Math.random();
    items.push(droppedItem);
    setState({...state, items})
  };

  function removePanel(item) {
    let temp = [...state.items];
    temp = temp.filter((current) => {
      return current.id !== item.id;
    });
    setState({...state, items: temp})
    //props.removeItem(item);
  }

  function openEditModal(item, index) {
    let selectedItem = state.selectedItem;
    selectedItem.item = item;
    selectedItem.index = index;
    let isEditModalOpen = true;
    setState({...state, selectedItem, isEditModalOpen})
  }

  function hideEditModal() {
    setState({...state, selectedItem: {item: null, index: null}, isEditModalOpen: false})
  }

  function hideSelectEditModal() {
    setState({...state, selectedDropdownItem: {}, isSelectOptionModalOpen: false})
  }

  function addDefaultOption(selectedItem) {
    let newOption = createDefaultOptionByType(selectedItem);
    let newItems = [...state.items];
    newItems[selectedItem.index].properties.sections.reduce(section => {
      return section.type === newOption.sectionType
    }).elements.push(newOption.newElement);

    setState(prevState => {
      return {...prevState, items: newItems}
    });
  }

  function moveItemDown(itemIndex) {
    let temp = [...state.items];
    [temp[itemIndex], temp[itemIndex + 1]] = [temp[itemIndex + 1], temp[itemIndex]];
    setState(prevState => {
      return {...prevState, items: temp}
    })
  }

  function moveItemUp(itemIndex) {
    let temp = [...state.items];
    [temp[itemIndex], temp[itemIndex - 1]] = [temp[itemIndex - 1], temp[itemIndex]];
    setState(prevState => {
      return {...prevState, items: temp}
    })
  }

  // function moveItemTop(itemIndex) {
  //   let temp = [...state.items];
  //   temp.splice(itemIndex, 0, temp.splice(itemIndex, 1)[0]);
  //   setState(prevState => {
  //     return {...prevState, items: temp}
  //   })
  // }

  function createModalBody() {
    return generateModalBody(state.selectedItem, addDefaultOption);
  }

  useEffect(() => {
    let items = '';
    setTimeout(() => setState(prevState => {
      return {...prevState, alert: null}
    }), 6000)

    if (props.editedForm) {
      items = JSON.parse(props.editedJson);
      setState({...state, items});
    }

  }, [state.alert]);

  function displaySelectedFile(obj) {
    setState(prevState => {
      return {...prevState, selectedFile: obj, isDisplayFileModalOpen: true}
    })
  }

  function editFunctions(type) {
    switch (type) {
      case 'image':
      case 'file':
        return (
          {
            addFile: (itemIndex, sectionIndex, elementIndex, data) => {
              let file = uploadFile(data);
              let reader = new FileReader();
              reader.readAsDataURL(file);

              reader.onloadend = () => {
                let temp = [...state.items];

                if (isFileTypeValid(type, file.type)) {

                  if (type === 'image') {
                    temp[itemIndex].properties.sections[sectionIndex].elements[elementIndex].image = reader.result;

                  } else {
                    temp[itemIndex].properties.sections[sectionIndex].elements[elementIndex].file = reader.result;

                    let newElement = null;
                    if (!temp[itemIndex].properties.sections[sectionIndex].elements[elementIndex].content) {
                      let newId = Math.random().toString(32);
                      newElement = {
                        id: newId,
                        type: 'file',
                        label: null,
                        replied: false,
                        active: false,
                        content: null,
                        file: null
                      };
                    }
                    temp[itemIndex].properties.sections[sectionIndex].elements[elementIndex].content = file.name;

                    if (newElement) {
                      temp[itemIndex].properties.sections[sectionIndex].elements.push(newElement);
                    }
                  }

                  setState(prevState => {
                    return {...prevState, items: temp}
                  })

                } else {
                  let message = (
                    <div>
                      <p style={{color: 'white'}}>
                        Dosya format uygun deil! Eklenecek dosya aadaki
                        formatlardan birine sahip olmaldr.
                        <ul>
                          <li>jpg / jpeg</li>
                          <li>png</li>
                          {type === 'file' && <li>txt</li>}
                          {type === 'file' && <li>pdf</li>}
                          {type === 'file' && <li>doc / docx</li>}
                          {type === 'file' && <li>xls / xlsx</li>}
                        </ul>
                      </p>
                    </div>
                  );
                  let alert = {message: message};
                  setState(prevState => {
                    return {...prevState, alert}
                  })
                }

              };
            },

            removeFile: (itemIndex, sectionIndex, elementIndex,elementId) => {
              let temp = [...state.items];

              if (type === 'image') {
                //temp[itemIndex].properties.sections[sectionIndex].elements[elementIndex].image = null;
                temp[itemIndex].properties.sections[sectionIndex].elements = temp[itemIndex].properties.sections[sectionIndex].elements.filter((element) => {
                  return element.id !== elementId;
                });
              } else {
                temp[itemIndex].properties.sections[sectionIndex].elements[elementIndex].file = null;
                temp[itemIndex].properties.sections[sectionIndex].elements[elementIndex].content = null;
              }

              setState(prevState => {
                return {...prevState, items: temp}
              })
            },

            displayFile: (itemIndex, sectionIndex, elementIndex) => {
              let element = state.items[itemIndex].properties.sections[sectionIndex].elements[elementIndex];
              let objToDisplay = {};
              if (type === 'image') {
                objToDisplay.type = 'img';
                objToDisplay.data = element.image;
              } else {
                objToDisplay.type = 'file';
                objToDisplay.name = element.content;
                objToDisplay.data = element.file;
              }
              displaySelectedFile(objToDisplay);
            }
          }
        );
      case 'radio-button':
        return (
          {
            onChangeRadioState: (itemIndex, sectionIndex, elementIndex, data) => {
              let newState = onChange(data) === 'on';
              let temp = [...state.items];

              temp[itemIndex].properties.sections[sectionIndex].elements.map((element, index) => {
                index === elementIndex ? element.replied = newState : element.replied = !newState;
              });
              setState(prevState => {
                return {...prevState, items: temp}
              })
            }
          }
        );
      case 'checkbox':
        return (
          {
            onChangeCheckboxState: (itemIndex, sectionIndex, elementIndex, data) => {
              let temp = [...state.items];
              temp[itemIndex].properties.sections[sectionIndex].elements[elementIndex].replied =
                !temp[itemIndex].properties.sections[sectionIndex].elements[elementIndex].replied;
              setState(prevState => {
                return {...prevState, items: temp}
              })
            }
          }
        );
      case 'label':
        return (
          {
            onChangeLabel: (itemIndex, sectionIndex, elementIndex, data) => {
              let temp = [...state.items];
              temp[itemIndex].properties.sections[sectionIndex].elements[elementIndex].label = onChange(data);
              setState(prevState => {
                return {...prevState, items: temp}
              })
            }
          }
        );
      case 'description':
        return (
          {
            onChangeLabel: (itemIndex, sectionIndex, elementIndex, data) => {
              let temp = [...state.items];
              temp[itemIndex].properties.sections[sectionIndex].elements[elementIndex].description = onChange(data);
              setState(prevState => {
                return {...prevState, items: temp}
              })
            }
          }
        );
      case 'text-area':
      case 'text-input':
      case 'number-input':
        return (
          {
            onChangeText: (itemIndex, sectionIndex, elementIndex, data) => {
              let temp = [...state.items];
              temp[itemIndex].properties.sections[sectionIndex].elements[elementIndex].content = onChange(data);
              setState(prevState => {
                return {...prevState, items: temp}
              })
            }
          }
        );
      case 'text-input2':
        return (
          {
            onChangeText2: (itemIndex, sectionIndex, data) => {
              let temp = [...state.items];
              temp[itemIndex].properties.sections[sectionIndex].name = onChange(data);
              setState(prevState => {
                return {...prevState, items: temp}
              })
            }
          }
        );
      case 'link':
        return (
          {
            onChangeText2: (itemIndex, sectionIndex, data) => {
              let temp = [...state.items];
              temp[itemIndex].properties.sections[sectionIndex].name = onChange(data);
              setState(prevState => {
                return {...prevState, items: temp}
              })
            }
          }
        );
      case 'dropdown':
        return (
          {
            onEditDropdown: (itemIndex, sectionIndex, elementIndex, data) => {
              let selectedDropdownItem = {};
              selectedDropdownItem.itemIndex = itemIndex;
              selectedDropdownItem.sectionIndex = sectionIndex;
              selectedDropdownItem.elementIndex = elementIndex;
              selectedDropdownItem.data = data;

              let isSelectOptionModalOpen = true;
              setState({...state, selectedDropdownItem, isSelectOptionModalOpen})
            },
            onSelectOption: (itemIndex, sectionIndex, elementIndex, data) => {
              let temp = [...state.items];
              temp[itemIndex].properties.sections[sectionIndex].elements[elementIndex].content = onChange(data);
              setState(prevState => {
                return {...prevState, items: temp}
              })
            }
          }
        );
      case 'time':
        return {
          onChangeTime: (itemIndex, sectionIndex, elementIndex, e) => {
            let temp = [...state.items];
            temp[itemIndex].properties.sections[sectionIndex].elements[elementIndex].time = e.target.value;
            setState(prevState => {
              return {...prevState, items: temp};
            });
          }
        };
      default:
        return (
          {
            onDeleteElement: (itemIndex, sectionIndex, elementId) => {
              let temp = [...state.items];
              temp[itemIndex].properties.sections[sectionIndex].elements = temp[itemIndex].properties.sections[sectionIndex].elements.filter((element) => {
                return element.id !== elementId;
              });
              setState(prevState => {
                return {...prevState, items: temp}
              })
            }
          }
        )
    }
  }

  function openSelectOptionModal() {
    let selectedItem = {...state.selectedDropdownItem};
    let temp = [...state.items];
    let options = temp[selectedItem.itemIndex].properties.sections[selectedItem.sectionIndex].elements[selectedItem.elementIndex].options;

    options.length === 0 && options.push({id: 0, label: '', value: ''});

    function onChangeOption(e, index) {
      e.target.value = e.target.value.replace(/\s+/g, ' ');
      options[index].label = e.target.value;
      options[index].value = e.target.value;

      setState(prevState => {
        return {...prevState, items: temp}
      })
    }

    function addNewOption() {
      let option = {id: options.length, label: '', value: ''};
      options.push(option);
      setState(prevState => {
        return {...prevState, items: temp}
      })
    }

    function onSubmitNewOptions() {
      let selectedItem = {...state.selectedDropdownItem};
      let temp = [...state.items];
      let options = temp[selectedItem.itemIndex].properties.sections[selectedItem.sectionIndex].elements[selectedItem.elementIndex].options;

      temp[selectedItem.itemIndex].properties.sections[selectedItem.sectionIndex].elements[selectedItem.elementIndex].options = options.filter(option => {
        return option.value;
      });

      setState(prevState => {
        return {...prevState, items: temp, selectedDropdownItem: {}, isSelectOptionModalOpen: false};
      })

    }

    return (
      <Modal show={state.isSelectOptionModalOpen} onHide={hideSelectEditModal}>
        <Modal.Header closeButton
                      className="justify-content-center"
                      style={{padding:'20px 0',fontSize:'1.3em'}}
        >Seenek Oluturun</Modal.Header>
        <Modal.Body>
          <div>
            {options.length > 0 &&
            options.map((option, index) => {
              return (
                <div key={option.id} className='new-option-div'>
                  <label className='new-option-label'>{index + 1}</label>
                  <input style={{width: '80%'}} type='text' value={option.value}
                         onChange={(e) => onChangeOption(e, index)}/>
                  {(index === options.length - 1) &&
                  <FontAwesomeIcon style={{margin: '0 5px'}} icon='plus-square'
                                   color='#72a0c7' size='2x' onClick={() => addNewOption()}/>}
                </div>
              )
            })
            }
          </div>
        </Modal.Body>
        <Modal.Footer>
          <button className="saveButton" onClick={() => onSubmitNewOptions()}>Onayla</button>
        </Modal.Footer>
      </Modal>
    )
  }

  function hidePreviewModal() {
    setState({...state, isPreviewModalOpen: false})
  }

  function hideDisplayFileModal() {
    setState({...state, isDisplayFileModalOpen: false})
  }

  function onSubmit(personalInfo, surveyAnswerGroups, answerWay) {
    let form = [...state.items];
    let result = convertJson(form);
    let validity = true;
    form.map((items) => {
      if (items.properties.sections[0].type === 'checkbox-panel') {
        items.properties.sections[0].elements.map((item) => {
          if (item.content === null || item.content === "") {
            validity = alertValidation(items);
          }
        })
      }
      else if (items.properties.sections[0].type === 'dropdown-panel') {
        if (items.properties.sections[0].elements.length > 1) {
          if (items.properties.sections[0].elements[0].content === null || items.properties.sections[0].elements[0].content === "") {
            validity = alertValidation(items);
          }
          else if (items.properties.sections[0].elements[1].label === null || items.properties.sections[0].elements[1].label === "") {
            validity = alertValidation(items);

            if (items.properties.sections[0].elements[1].options.length >= 0) {
              validity = alertValidation(items);
            }
          }
          else if (items.properties.sections[0].elements[1].label !== null || items.properties.sections[0].elements[1].label !== "") {
            if (items.properties.sections[0].elements[1].options.length <= 0) {
              validity = alertValidation(items);
            }
            else if (items.properties.sections[0].elements[1].options.length > 0) {
              if (items.properties.sections[0].elements[1].content === null || items.properties.sections[0].elements[1].content === "") {
                validity = alertValidation(items);
              }
            }
          }
        }
        else if (items.properties.sections[0].elements.length === 1) {
          if (items.properties.sections[0].elements[0].type === 'text-area') {
            if (items.properties.sections[0].elements[0].content === null || items.properties.sections[0].elements[0].content === "") {
              validity = alertValidation(items);
            }
          }
          else if (items.properties.sections[0].elements[0].type === 'dropdown') {
            if (items.properties.sections[0].elements[0].label === null || items.properties.sections[0].elements[0].label === "") {
              validity = alertValidation(items);
              if (items.properties.sections[0].elements[0].options.length < 0) {
                validity = alertValidation(items);
              }
            }
            else if (items.properties.sections[0].elements[0].label !== null || items.properties.sections[0].elements[0].label !== "") {
              if (items.properties.sections[0].elements[0].options.length <= 0) {
                validity = alertValidation(items);
              }
              else if (items.properties.sections[0].elements[0].options.length > 0) {
                if (items.properties.sections[0].elements[0].content === null || items.properties.sections[0].elements[0].content === "") {
                  validity = alertValidation(items);
                }
              }
            }
          }
        }
      }
      else if(items.properties.sections[0].type === 'heading-panel'){
        items.properties.sections[0].elements.map((item) => {
          if (item.content === null || item.content === "") {
            validity = alertValidation(items);
          }
        })
      }
    });
    if (!validity) {
      return;
    }
    let survey = props.selectedSurveyForm !== undefined ? props.selectedSurveyForm : {};
    survey['bulkJson'] = JSON.stringify(form);
    survey['jsonContent'] = result[1];
    survey['name'] = result[0];
    survey['state'] = 'AKTF';
    survey['personalInfo'] = personalInfo;
    survey['surveyAnswerGroups'] = surveyAnswerGroups;
    survey['answerWay'] = answerWay !== undefined ? answerWay : null;

    let params = {
      url: `/survey/saveSurveyForm`,
      method: "post",
      data: survey
    };
    setState({...state, loading: true});
    requestK(params)
        .then(function (response) {
          let message = response.data;
          setState({...state, loading: false});
          setSave(true);
          Swal({
            title: 'Baarl ! ',
            text: ("Kaydetme ilemi baaryla gerekletirildi."),
            showCancelButton: true,
            html: "Kaydetme ilemi baaryla gerekletirildi." +
                '</br></br><span style="color:red;font-size:20px">*</span>Kaydettiiniz formu "Form ablonlar" sayfasndan  bir ablona balayarak gnderime hazr hale getirebilirsiniz.',
            type: 'success',
            cancelButtonText: "Kapat",
            confirmButtonText: 'Form ablonlarna Git',
          }).then((result) => {
            if (result.dismiss === 'cancel' || result.dismiss === 'overlay') {
              props.history.push(`/${props.peykName}/formbuilderpage`);
            } else {
              props.history.push(`/${props.peykName}/formtemplate`);
              document.getElementById('formbuilderpageNavigator').classList.remove('active');
              document.getElementById('formtemplateNavigator').classList.add('active');
            }
          });
      }).catch(function (error) {
      showAxiosError(error);
      setState({...state, loading: false});
    });
  }


  function isFileTypeValid(elementType, fileType) {
    if (elementType === 'file') {
      switch (fileType) {
        case 'image/png':
        case 'image/jpeg':
        case 'text/plain':
        case 'application/pdf':
        case 'application/xlsx':
        case 'application/vnd.ms-excel':
        case 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet':
        case 'application/msword':
        case 'application/vnd.openxmlformats-officedocument.wordprocessingml.document':
          return true;
        default :
          return false;
      }
    } else {
      switch (fileType) {
        case 'image/png':
        case 'image/jpeg':
          return true;
        default :
          return false;
      }
    }
  }

  function setRequired(section, itemIndex, sectionIndex, elementIndex, data) {
    let temp = [...state.items];
    temp[itemIndex].properties.sections[sectionIndex].required =
      !temp[itemIndex].properties.sections[sectionIndex].required;
    setState(prevState => {
      return {...prevState, items: temp}
    })
  }

  function setRequiredElements(itemIndex, sectionIndex, elementIndex, data) {
    let temp = [...state.items];
    temp[itemIndex].properties.sections[sectionIndex].elements[elementIndex].required =
      !temp[itemIndex].properties.sections[sectionIndex].elements[elementIndex].required;
    setState(prevState => {
      return {...prevState, items: temp}
    })
  }


  return (
    <DropTarget onItemDropped={itemDropped}
                onSubmitForm={onSubmit}
                dropEffect="link"
      // previewForm={previewForm}
                alert={state.alert}
                resetEditor={resetEditor}
                loading={state.loading}
                answerWay={props.selectedSurveyForm && props.selectedSurveyForm.answerWay}
                surveyAnswerGroups={props.selectedSurveyForm && props.selectedSurveyForm.surveyAnswerGroups}
                personalInfo={props.selectedSurveyForm && props.selectedSurveyForm.personalInfo}>
      <div className="drag-drop-container">
        {state.items.map((item, itemIndex) => (
          <div key={item.id} className="item">
            <DraggablePanel>
              {state.items.length > 1 &&
              <div className='swap-items-div'>
                {/*{(itemIndex !== 0 && itemIndex !== 1) && state.items.length > 2 &&*/}
                {/*<FontAwesomeIcon icon='angle-double-up' color='#72a0c7'*/}
                {/*				 style={{margin: '0 5px'}} size='lg'*/}
                {/*				 onClick={() => moveItemTop(itemIndex)}/>*/}
                {/*}*/}
                {itemIndex !== 0 &&
                <FontAwesomeIcon icon='angle-up' color='#72a0c7' style={{margin: '0 5px'}}
                                 size='lg' onClick={() => moveItemUp(itemIndex)}/>
                }
                {itemIndex !== (state.items.length - 1) &&
                <FontAwesomeIcon icon='angle-down' color='#72a0c7' style={{margin: '0 5px'}}
                                 size='lg' onClick={() => moveItemDown(itemIndex)}/>
                }
              </div>
              }
              <div className='dropdown-menu-div'>
                <input className='dropdown-menu-checkbox'
                       id={'dropdown-menu-checkbox' + item.id} type="checkbox" name="menu"/>
                <label htmlFor={'dropdown-menu-checkbox' + item.id}>
                  <FontAwesomeIcon icon='ellipsis-v' color='#72a0c7' size='lg'/>
                </label>
                <ul className="submenu">
                  <li>
                    <button onClick={() => removePanel(item)} style={{cursor: 'pointer'}}>Kaldr
                    </button>
                  </li>
                  {/*link - file - dropdown - image - date - text-area - text-input - checkbox - number-input - file-o - heading*/}
                  {(item.name === 'checkbox' || item.name === 'text-input' || item.name === 'date' || item.name === 'dropdown' || item.name === 'image') &&
                  <li>
                    <button onClick={() => openEditModal(item, itemIndex)}
                            style={{cursor: 'pointer'}}>Dzenle
                    </button>
                  </li>}
                </ul>
              </div>

              {/*<div className='draggable-panel-header'>*/}
              {/*	{item.properties.title}*/}
              {/*</div>*/}

              {item.properties.sections.map((section, sectionIndex) =>
                generateSectionBody(section, sectionIndex, itemIndex, editFunctions, setRequired, setRequiredElements))}
            </DraggablePanel>
          </div>
        ))}

        {state.isEditModalOpen &&
        <EditModal show={state.isEditModalOpen} selectedItem={state.selectedItem} hide={hideEditModal}>
          {createModalBody()}
        </EditModal>}

        {state.isSelectOptionModalOpen && openSelectOptionModal()}

        {state.isPreviewModalOpen &&
        <PreviewModal show={state.isPreviewModalOpen} hide={hidePreviewModal} items={state.items}/>}

        {state.isDisplayFileModalOpen &&
        <DisplayFileModal show={state.isDisplayFileModalOpen} hide={hideDisplayFileModal}
                          file={state.selectedFile}/>}
      </div>
    </DropTarget>
  );
}