export function deepCopyObject(oldObject) {
    return JSON.parse(JSON.stringify(oldObject));
}

export function checkGrant(self, grant) {
    if (!self.props.hasGrant(grant)) {
        if (grant && Array.isArray(grant)) {
            showAuthError(grant[0]);
        } else {
            showAuthError(grant);
        }
        return false;
    } else {
        return true;
    }
}

export function create_UUID() {
    let dt, uuid;
    dt = new Date().getTime();
    uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
        let r = (dt + Math.random() * 16) % 16 | 0;
        dt = Math.floor(dt / 16);
        return (c === 'x' ? r : (r & 0x3 | 0x8)).toString(16);
    });
    return uuid;
}

export function checkGrantToDisplay(self, grant) {
    return self.props.hasGrant(grant);
}

export function checkGrantForFetchResponse(self, responseData) {
    if (responseData === "") {
        showAuthError();
        self.props.history.push({pathname: `/${self.props.peykName}/hr-dashboard`});
    }
}

export function showAuthError(grant = undefined) {
    showErrorMsg(grant ? ("Yetkiniz bulunmamaktadr. : " + grant) : "Yetkiniz bulunmamaktadr.", 8000);
}

export function showErrorMsg(message, timeout = 5000) {
    Alert.error(message, {
        position: 'top-right',
        effect: 'stackslide',
        timeout: timeout
    });
}

export function logout() {
    let params = {
        url: '/authentication/logout',
        method: "post"
    };
    requestK(params)
        .then(function () {
            window.location = "/login";
            sessionStorage.removeItem(ACCESS_TOKEN);
        }).catch(function () {
        window.location = "/login";
        sessionStorage.removeItem(ACCESS_TOKEN);
    });
}

export function showAxiosError(error) {
    if (error.response) {
        let response = error.response;
        if (typeof response.data === 'string') {
            if (response.data.toString().includes("Internal Server Error")) {
                logout();
            }else if (response.data === "Veritaban btnlk ihlali") {
            } else if (response.data === "") {
                Alert.error("leminiz uan gerekletirilemiyor...", {
                    position: 'top-right',
                    effect: 'stackslide',
                    timeout: 5000
                });
            } else if (!!response.statusText && response.statusText === "Gateway Timeout") {
                Alert.error("lem sorgusu zaman amna urad. Ltfen daha sonra tekrar deneyiniz.", {
                    position: 'top-right',
                    effect: 'stackslide',
                    timeout: 5000
                });
            } else if (error.response.status === 401) {
                Alert.error('Yeniden giri yapmay deneyin.', {
                    position: 'top-right',
                    effect: 'stackslide',
                    timeout: 7000,
                    html:false
                });
            } else {
                Alert.error(response.data, {
                    position: 'top-right',
                    effect: 'stackslide',
                    timeout: 5000
                });
            }
        } else if (response.data instanceof ArrayBuffer) {
            let decodedText = new TextDecoder("utf-8").decode(response.data);
            if (!!decodedText && decodedText.includes("\"")) {
                try {
                    if (!!JSON.parse(decodedText) && !!JSON.parse(decodedText).message) {
                        Alert.error(JSON.parse(decodedText).message, {
                            position: 'top-right',
                            effect: 'stackslide',
                            timeout: 5000
                        });
                    }
                } catch (err) {
                    console.log("Grntleme ilemi");
                }
            } else if (error.message && error.message.toString().toLowerCase().includes("network error")) {
                Alert.error("lem cevab zaman amna urad. Tekrar kontrol ediniz.", {
                    position: 'top-right',
                    effect: 'stackslide',
                    timeout: 10000
                });
            } else if (error.request) {
                console.log(error.request);
                Alert.error("u an ileminiz gerekletirilemiyor, ltfen daha sonra tekrar deneyiniz.", {
                    position: 'top-right',
                    effect: 'stackslide',
                    timeout: 5000
                });
            } else if (error.Error) {
                console.log(error.Error);
                Alert.error("u an ileminiz gerekletirilemiyor, ltfen daha sonra tekrar deneyiniz.", {
                    position: 'top-right',
                    effect: 'stackslide',
                    timeout: 5000
                });
            } else if (axios.isCancel(error)) {
                if (!!error.message) {
                    console.log('cancelled:', error.message);
                }
                Alert.error("u an ileminiz gerekletirilemiyor, ltfen daha sonra tekrar deneyiniz.", {
                    position: 'top-right',
                    effect: 'stackslide',
                    timeout: 5000
                });
            } else {
                console.log('Error', error);
                Alert.error("u an ileminiz gerekletirilemiyor, ltfen daha sonra tekrar deneyiniz.", {
                    position: 'top-right',
                    effect: 'stackslide',
                    timeout: 5000
                });
            }
        } else if (error.request) {
            console.log(error.request);
        } else if (error.Error) {
            Alert.error(error.Error, {
                position: 'top-right',
                effect: 'stackslide',
                timeout: 5000
            });
        } else if (axios.isCancel(error)) {
            if (!!error.message)
                console.log('cancelled:', error.message);
        } else {
            console.log('Error', error);
            Alert.error(error.message, {
                position: 'top-right',
                effect: 'stackslide',
                timeout: 5000
            });
        }
    }
}

