export function monthName(monthNumber) {
    switch (monthNumber) {
        case 1 :
            return "Ocak";
        case 2 :
            return "ubat";
        case 3 :
            return "Mart";
        case 4 :
            return "Nisan";
        case 5 :
            return "Mays";
        case 6 :
            return "Haziran";
        case 7 :
            return "Temmuz";
        case 8 :
            return "Austos";
        case 9 :
            return "Eyll";
        case 10 :
            return "Ekim";
        case 11 :
            return "Kasm";
        case 12 :
            return "Aralk";
        default:
            return "Geersiz deer."
    }
}

export function humanReadableByteCount(bytes, si) {
    let thresh = si ? 1000 : 1024;
    if (Math.abs(bytes) < thresh) {
        return bytes + ' B';
    }
    let units = si
        ? ['kB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB']
        : ['KiB', 'MiB', 'GiB', 'TiB', 'PiB', 'EiB', 'ZiB', 'YiB'];
    let u = -1;
    do {
        bytes /= thresh;
        ++u;
    } while (Math.abs(bytes) >= thresh && u < units.length - 1);
    return bytes.toFixed(1) + ' ' + units[u];
}

export function customFieldsBooleanControl(value){
    if (value !== undefined && ( value.toString().toLowerCase() === "false" || value.toString().toLowerCase() === "hayr")){
        return false;
    } else if (value !== undefined && ( value.toString().toLowerCase() === "true" || value.toString().toLowerCase() === "evet"))
        return true;
}

