import React, {Component} from 'react';
import {Col, Input} from "reactstrap";
import ExternalTextArea from "./ExternalTextArea";


class ExpandableTextArea extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modal: false,
      answer: "",
      totalLetters: 0,
      maxTextLength: 6000
    };
    this.handleChange = this.handleChange.bind(this);
  }


  handleChange(e, type) {
    let self = this;
    let value = e.target.value;
    if (value && value.length === 1) {
      value = value.split(" ").join("");
      self.setState({answer: value, totalLetters: value.trim().length});
    } else if( value!=undefined && value.length <= this.state.maxTextLength) {
      self.setState({answer: value, totalLetters: value.trim().length});
    }
    this.props.onChange(value);
  }

  bigTextArea = () => {
    this.setState({modal: true})
  };

  closeModal = () => {
    this.setState({modal: false})
  };

  render() {
    return (
      <>
        {this.state.show}
        <Col xs='12' sm='12' md='12' lg='8' xl='8'
             style={{padding: '2% 0 0 5%', textAlign: 'center', position: "relative"}}>
          <label style={{fontWeight: 'bold'}}>{this.props.title}</label>
          <button
            onClick={() => this.bigTextArea()}
            style={{
              backgroundColor: "transparent",
              border: "none", fontSize: "15px",
              position: "absolute", right: "3%",
              cursor: "pointer"
            }}
            data-toggle="toggle"
            title="Byk Ekran">
            <i className="fas fa-expand"/>
          </button>
        </Col>

        <Col xs='12' sm='12' md='12' lg='8' xl='8' style={{position: 'relative'}}>
          <Input type="textarea" rows={5}
                 style={{
                   height: '200px',
                   border: '1px solid #E3E3E3',
                   marginTop: '2px',
                   padding: '3px 3px',
                 }}
                 value={this.state.answer}
                 onChange={(e) => this.handleChange(e, "answer")}
          />
          <div id='showTotalNumber'style={{position: 'relative'}}>
            <p style={{position: 'absolute', right: 0, fontSize: '10px'}}>{this.state.totalLetters}/{this.state.maxTextLength}</p>
          </div>
        </Col>

        <ExternalTextArea modal={this.state.modal} closeModal={this.closeModal} value={this.state.answer}
                          handleChange={this.handleChange} totalLetters={this.state.totalLetters} maxTextLength={this.state.maxTextLength}/>

      </>

    );
  }
}

export default ExpandableTextArea;