import React from 'react';
import ReactDOM from 'react-dom';
import {BrowserRouter as Router} from 'react-router-dom';
import Master from "./layout/Master";
import {API_BASE_URL, REACT_PATH, VirtualHost} from "./constants"
import {requestK} from "./helper/APIUtils";
import './fontawesome';

import axios from 'axios';

axios.defaults.baseURL = API_BASE_URL;

requestK({url: `/virtual/conf`})
  .then(response => {
    VirtualHost.conf = response.data;
    ReactDOM.render(
      <Router basename={REACT_PATH} keySize={36}>
        <Master />
      </Router>,
      document.getElementById('react'));
    });
