import React, {Component} from 'react';
import 'react-table/react-table.css'
import {Col, FormGroup, Label, Modal, ModalBody, ModalHeader, Row} from "reactstrap";
import {requestK} from "../../../helper/APIUtils";
import Table from "../../../components/Table/Table";
import {approveStatusData, downloadApproveDocument} from "./PeykHelper";
import TextOverFlowTooltip from "../../../components/TextOverFlowTooltip";
import moment from "moment/moment";
import {showAxiosError} from "../../../helper/helper";
import {Button} from "reactstrap";

class StateDetailStatement extends Component {

    constructor(props) {
        super(props);
        this.state = {
            displayed: false,
            content: [],
            page: 0,
            size: 5,
            loading: true,
            approveAnswer: false,
            isReferencedUser: true,
            isAnswerModalOpen: false,
        };
        this.getDocumentFlowHistory = this.getDocumentFlowHistory.bind(this);
    }

    componentDidMount() {
        this.getDocumentFlowHistory(this.props.detail);
    }

    getDocumentFlowHistory(document) {
        let self = this;
        let formData = new FormData();
        formData.append("documentId", document.id);
        formData.append("documentType", this.props.documentType);
        let params = {
            url: `/approval/documentFlowHistory`,
            method: "post",
            data: formData
        };
        self.setState({loading: true});
        requestK(params)
            .then(function (response) {
                self.setState({
                    content: response.data,
                    total: response.data.length,
                    page: 0,
                    size: 5,
                    loading: false,
                    approverUserId: response.data[0].approverUserId
                });
            }).catch(function (error) {
            self.setState({loading: false});
        });
    }

    render() {
        let employee = this.props.detail && this.props.detail.employee;
        let document = this.props.detail;
        let approvable = this.state.content && this.state.content.length && this.state.content[this.state.content.length - 1];


        return (
            <Modal isOpen={this.props.detail} size="xl" toggle={e => this.props.toggle(e)}>
                <ModalHeader className="justify-content-center" toggle={e => this.props.toggle(e)}>
                    {(employee ? (employee.name + " " + employee.surname + " ") : "") + "Gnderi Detaylar"}
                </ModalHeader>
                <ModalBody>
                    <Row className="FormRenk" style={{marginLeft: '0px', paddingLeft: '0px', margin: window.innerWidth < 321 ? '50px 20px 50px 20px' : '20px 20px 0px 20px'}}>
                        <Col sm={12} md={8}>
                            {document && document.employee && document.employee.tckn &&
                                <Col sm="12">
                                    <FormGroup row>
                                        <Label sm={4}>Tckn: </Label>
                                        <Col sm={8}>
                                            <label className="form-control-label">
                                                {document.employee.tckn}
                                            </label>
                                        </Col>
                                    </FormGroup>
                                </Col>}
                            {document && document.createdAt &&
                                <Col sm="12">
                                    <FormGroup row>
                                        <Label sm={4}>Oluturulma Tarihi: </Label>
                                        <Col sm={8}>
                                            <label className="form-control-label">
                                                {moment(document.createdAt.toString().replace("T", " ")).format("DD-MM-YYYY HH:mm:ss")}
                                            </label>
                                        </Col>
                                    </FormGroup>
                                </Col>}
                            {document && document.pageCount !== undefined && document.pageCount !== null &&
                                <Col sm="12">
                                    <FormGroup row>
                                        <Label sm={4}>Sayfa Says: </Label>
                                        <Col sm={8}>
                                            <label className="form-control-label">
                                                {document.pageCount}
                                            </label>
                                        </Col>
                                    </FormGroup>
                                </Col>}
                            {document && !!document.error &&
                                <Col sm="12">
                                    <FormGroup row>
                                        <Label sm={4} style={{color: 'red'}}>Hata Durumu: </Label>
                                        <Col sm={8}>
                                            <label className="form-control-label" style={{color: 'red'}}>
                                                {document.error}
                                            </label>
                                        </Col>
                                    </FormGroup>
                                </Col>}
                            <Col sm="12">
                                <FormGroup row>
                                    <Label sm={12} md={4}>Gnderi Durumu: </Label>
                                    <Col sm={12} md={8}>
                                        <label className="form-control-label">
                                            {(document && (
                                                    document.state === "CONTENT_LOADED" ||
                                                    document.state === "PDF_CREATED" ||
                                                    document.state === "PDF_CERTIFICATE_LOADED" ||
                                                    document.state === "PDF_CERTIFICATE_PROCESSED" ||
                                                    document.state === "PDF_DEFERRED_SIGNED"
                                                )) &&
                                                <span className="text-info">PDF oluturuldu</span>}
                                            {document && document.state === "PDF_SIGNED" &&
                                                <span className="text-info">PDF imzaland ve gnderiliyor</span>}
                                            {document && document.state === "INSERTED_TO_FLOW" &&
                                                <span className="text-warning">Gnderildi ve onay bekliyor</span>}
                                            {document && document.state === "SENT_ERROR" &&
                                                <span className="text-danger">Gnderilemedi</span>}
                                            {document && document.state === "OBJECTION" &&
                                                <span className="text-danger">Red Edildi</span>}
                                            {document && document.state === "FLOW_TIMEOUT" &&
                                                <span className="text-danger">Zaman Amna Urad</span>}
                                            {document && document.state === "SENT_CANCELED" &&
                                                <span className="text-danger">Gnderim ptal Edildi</span>}
                                            {document && document.state === "APPROVED_BY_EMPLOYEE" &&
                                                <span className="text-success">Onayland</span>}
                                            {document && document.state === "ANSWERED_BY_EMPLOYEE" &&
                                                <span className="text-success">Cevapland</span>}
                                            {document && document.state === "CONDITIONALLY_APPROVED_BY_EMPLOYEE" &&
                                                <span className="text-success">artl onayland</span>}
                                            {document && document.state === "CONFIRMED_BY_COMPANY" &&
                                                <span className="text-success">Firma Tarafndan, Onaylanm Kabul Edilmitir</span>}
                                        </label>
                                    </Col>
                                </FormGroup>
                            </Col>
                            {(document && (
                                    document.state === "APPROVED_BY_EMPLOYEE" ||
                                    document.state === "ANSWERED_BY_EMPLOYEE" ||
                                    document.state === "CONDITIONALLY_APPROVED_BY_EMPLOYEE"
                                ) && !!approvable) &&
                                <Col sm="12">
                                    <FormGroup row>
                                        <Label sm={4}>Onay Tarihi: </Label>
                                        <Col sm={8}>
                                            <label className="form-control-label">
                                                {approvable && !!approvable.approveTime &&
                                                    <span>{moment(approvable.approveTime.toString().replace("T", " ")).format("DD-MM-YYYY HH:mm:ss")}</span>}
                                            </label>
                                        </Col>
                                    </FormGroup>
                                </Col>}
                        </Col>
                    </Row>
                    {(this.state.loading === true || (this.state.content && this.state.content.length > 0)) &&
                        <Row style={{padding: '0px 0px 0px 0px', margin: '10px 15px 0px 0px', marginLeft: '20px'}}>
                            <div style={{overflowX: "auto"}}>
                                {this.getFlowTable()}
                            </div>
                        </Row>}
                </ModalBody>
            </Modal>
        );
    }

    getFlowTable() {
        let columns = [
            {
                Header: "Sra",
                accessor: "index"
            },
            {
                Header: "Fotoraf",
                accessor: "photoBase64",
                Cell: row => (
                    <div>
                        {row.photoBase64 &&
                            <span>
              <img className="simple-text logo-mini" style={{width: '40px'}}
                   src={atob(row.photoBase64)} alt=""/>
            </span>}
                        {!row.photoBase64 && row.department !== "Ynetici Hesab" &&
                            <span>
              <img className="simple-text logo-mini" style={{width: '40px'}}
                   src={process.env.REACT_APP_CONTEXT_NAME + "/image/unisex-avatar.png"} alt=""/>
            </span>}
                    </div>
                )
            },
            {
                Header:"Ad",
                accessor: "name",
                Cell: row => (
                    <div style={{margin: 0}}><TextOverFlowTooltip text={row.name} maxLength={15}/></div>
                )
            },
            {
                Header: "Soyad",
                accessor: "surname",
                Cell: row => (
                    <div style={{margin: 0}}><TextOverFlowTooltip text={row.surname} maxLength={15}/></div>
                )
            },
            {
                Header:"Departman",
                accessor: "department",
                Cell: row => (
                    <div style={{margin: 0}}><TextOverFlowTooltip text={row.department} maxLength={15}/></div>
                )
            },
            {
                Header: "Grev",
                accessor: "jobTitle",
                Cell: row => (
                    <div style={{margin: 0}}><TextOverFlowTooltip text={row.jobTitle} maxLength={15}/></div>
                )
            },
            {
                Header: "Durum",
                accessor: "status",
                Cell: row => (
                    <div>
            <span
                className={"text-" +
                    approveStatusData(row.status, row.answer && row.answer.length > 0).style}>
                {approveStatusData(row.status, row.answer && row.answer.length > 0).value}
            </span>
                    </div>
                )
            },
            {
                Header: "Gnderilme Tarihi",
                accessor: "createdTime",
                Cell: row => (
                    <div>
                        {row.createdTime &&
                            <span>{moment(row.createdTime.toString().replace("T", " ")).format("DD-MM-YYYY HH:mm:ss")}</span>}
                    </div>
                )
            },
            {
                Header: "Onay/Cevap Tarihi",
                accessor: "approveTime",
                Cell: row => (
                    <div>
                        {row.approveTime &&
                            <span>{moment(row.approveTime.toString().replace("T", " ")).format("DD-MM-YYYY HH:mm:ss")}</span>}
                    </div>
                )
            }
        ];

        return <Table columns={columns}
                      content={this.state.content}
                      loading={this.state.loading}
                      loadingText="Kaytlar getiriliyor..."
                      total={this.state.total}
                      page={this.state.page}
                      size={this.state.size}
                      options={["5", "10"]}/>;
    }
}

export default StateDetailStatement;