import React from "react";
import Drag from "./Drag";
import * as comps from './draggableComponents';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCalendarAlt, faCaretSquareDown, faCheck, faClock, faDotCircle, faFile, faHeading, faICursor, faImages, faLink, faParagraph, faPhoneSquareAlt, faSortNumericDown, faTags } from '@fortawesome/free-solid-svg-icons';

const iconMapping = {
	'heading': faHeading,
	'tags': faTags,
	'check': faCheck,
	'i-cursor': faICursor,
	'paragraph': faParagraph,
	'calendar-alt': faCalendarAlt,
	'clock': faClock,
	'images': faImages,
	'caret-square-down': faCaretSquareDown,
	'file': faFile,
	'link': faLink,
	'dot-circle': faDotCircle,
	'phone-square-alt': faPhoneSquareAlt,
	'numeric': faSortNumericDown,
};

function DragList(props) {
	return (
		<div style={{minHeight: '100vh'}}>
			{comps.list.map(item => (
				<Drag key={item.name} dataItem={JSON.stringify(item)} dropEffect="link" {...props}>
					<div className="draggable-item">
						<FontAwesomeIcon icon={iconMapping[item.properties.icon]} size='2x' color='#5aa9ec'/>
						<p>{item.properties.title}</p>
					</div>
				</Drag>
			))}
		</div>
	);
}

export default React.memo(DragList);