import React, {Component} from 'react';
import Table from "../../../components/Table/Table";
import 'react-table/react-table.css'
import {CardBody, Nav} from 'reactstrap';
import moment from 'moment';
import 'moment/locale/tr';
import DetailedSearch from "../../../components/DetailedSearch/DetailedSearch";
import {checkGrantForFetchResponse, create_UUID, showAxiosError} from "../../../helper/helper";
import StickButton from "../../../components/StickButton";
import {requestK} from "../../../helper/APIUtils";
import {
    correctStateQuery,
    departmentDataForStatement,
    getJobTitles,
    renewListForPrivileges,
    stateData,
    statusTypeFilterData
} from "../common/PeykHelper";
import TextOverFlowTooltip from "../../../components/TextOverFlowTooltip";
import "../../../helper/explorer_support";
import Alert from "react-s-alert";
import ApproveDocumentViewer from "../approval/helper/ApproveDocumentViewer";
import DocumentState from "../common/DocumentState";
import StateDetail from "../common/StateDetail";
import SweetAlert from "react-bootstrap-sweetalert";
import DropdownCheck from "../../../components/Table/DropdownCheck";
import NavBarKS from "../../../components/NavBarKS";
import base64_arraybuffer from "base64-arraybuffer";
import StateDetailStatement from "../common/StateDetailStatement";
const dropdownCheckRef = React.createRef();
const detailedSearchRef = React.createRef();
class BordroStatement extends Component {

    constructor(props) {
        super(props);

        this.state = {
            selection: [],
            selectedAll: false,
            content: [],
            totalIdList: [],
            total: 0,
            page: 0,
            size: 10,
            departmentList: [],
            loading: false,
            manual: false,
            showOnlyMissing: false,
            showOnlySents: false,
            isKepOwner: false,
            flagForHomePage: true,
            width: window.innerWidth,
            flag: 0,
            deleteSelection: [],
        };
        this.fetchData = this.fetchData.bind(this);
        this.handleSearch = this.handleSearch.bind(this);
        this.downloadMedia = this.downloadMedia.bind(this);
        this.openBordroSession = this.openBordroSession.bind(this);
        this.isSelected = this.isSelected.bind(this);
        this.toggleCheckboxChange = this.toggleCheckboxChange.bind(this);
        this.downloadContent = this.downloadContent.bind(this);
    }

    componentWillMount() {
        this.setState({isKepOwner: this.props.isKepOwner, jobTitlesData: getJobTitles()});
        this.readDepartmentAll();
    }

    componentDidMount() {
        if (this.props.location && this.props.location.widgetType) {
            if (this.props.location.widgetType === "unsigned") {
                this.handleSearch("state=PDF_CREATED&state=MIME_DEFERRED_SIGNED");
            } else if (this.props.location.widgetType === "unsent") {
                this.handleSearch("state=MIME_SIGNED");
            } else if (this.props.location.widgetType === "sent") {
                this.handleSearch("state>MIME_SIGNED");
            }
        }
    }

    readDepartmentAll = () => {
        let self = this;
        let params = {
            url: '/department/getDepartmentByEmployee',
            method: "get",
        };
        requestK(params)
            .then(function (response) {
                let departmentList = response.data;
                self.setState({departmentList});
            }).catch(function (error) {
            showAxiosError(error);
        });
    };

    handleSearch(searchQuery) {
        this.setState({
            searchQuery,
            searching: true
        }, () => {
            this.fetchData(this.state.accessor, this.state.direction, 0, this.state.size);
            this.setState({tableVisible: true});
        });
    }


    fetchData(accessor, direction, page, size) {
        let self = this;
        let sortStr = accessor !== undefined ? ('&sort=' + accessor + ',' + direction) : '&sort=id,desc';
        let searchQuery = correctStateQuery(!self.state.searchQuery ? '' : '&' + self.state.searchQuery);

            searchQuery = this.filterForCurrentTerm(self, searchQuery);
            let url = '/bordro/readBordroAllStatement?';
            if (dropdownCheckRef.current?.state?.selectionTag === 'selectAll') {
                url += '&bringAllBordroIds=true'
            } else {
                url += '&bringAllBordroIds=false'
            }

            let params = {
                url: url + '&showOnlyMissing=' + self.state.showOnlyMissing + '&showOnlySents=' + self.state.showOnlySents +
                    '&page=' + page + '&size=' + size + searchQuery + sortStr,
                method: "get",
            };
            self.setState({loading: true, searching: true, manual: self.manual === true ? false : self.manual});
            self.props.reload(true);
            requestK(params)
                .then(function (response) {
                    checkGrantForFetchResponse(self, response.data);
                    let total = (response.data && response.data.page) ? response.data.page.totalElements : 0;
                    let content = (response.data && response.data.page) ? response.data.page.content : [];
                    let totalIdList = response.data ? response.data.selectedIdList : [];
                    self.setState({
                        page: page,
                        size: size,
                        total,
                        content,
                        totalIdList,
                        loading: false,
                        searching: false,
                        flagForHomePage: true
                    });
                    if (dropdownCheckRef.current?.state?.selectionTag === 'selectAll') {
                        self.setState({selection: totalIdList});
                        if (self.state?.selectedAll === false) {
                            self.setState({selectedAll: true});
                            dropdownCheckRef.current.setState({
                                alert: (
                                    <SweetAlert info
                                                confirmBtnText="Tamam"
                                                confirmBtnBsStyle="success"
                                                title="Tm Seildi"
                                                onConfirm={() => dropdownCheckRef.current.setState({alert: null})}
                                    >
                                        {totalIdList.length + " adet seim yapld."}
                                    </SweetAlert>
                                )
                            });
                        }
                    } else {
                        self.setState({selectedAll: false})
                    }

                    self.props.reload(false);
                })
                .catch(function (error) {
                    self.setState({loading: false, searching: false});
                    self.props.reload(false);
                    showAxiosError(error);
                });
    }

    filterForCurrentTerm = (self, searchQuery) => {
        if (this.props.monthly && this.state.flagForHomePage) {
            if (this.props.location) {
                if (this.props.location.selectedTerm) {
                    let selectedTerm = this.props.location.selectedTerm;
                    self.setState({flagForHomePage: false});
                    let start = selectedTerm + '-01';
                    let end = selectedTerm + '-31';
                    searchQuery = `&term=${start} 00:00:00&term=${end} 23:59:59`;
                    Alert.success("Seilen dnemde oluturulan bordrolar listelenmektedir.", {
                        position: 'top-right',
                        effect: 'stackslide',
                        timeout: 5000
                    });
                }
            }
        }
        return searchQuery;
    };

    arrangeDataSet = (type) => {
        let self = this;
        let dataSet = {};
        dataSet.sheetName = "Bordro_Ariv_Raporu"
        dataSet.uuid = create_UUID();
        dataSet.list = self.state.selection
        dataSet.type = type;
        return dataSet;
    }

    downloadMedia(type) {
        let self = this;
        let data = self.arrangeDataSet(type);
        data.source = "bordrostatement";
        let params = {
            url: `/bordro/downloadReport`,
            method: "post",
            data: data
        };
        requestK(params)
            .then(() => {
                Alert.success("ndirme talebiniz ileme alnd. 'Bekleyen ndirmeler'den' takip edebilirsiniz.", {
                    position: 'top-right',
                    effect: 'stackslide',
                    timeout: 8000
                });
            }).catch(function (error) {
            showAxiosError(error);
        });
    }

    openBordroSession(bordroId, index) {
        let self = this;
        let params = {
            url: `/bordro/readBordroSessionIdByBordroId`,
            method: "post",
            data: bordroId
        };
        self.setState({disabledOpenSessionBtn: true, selectedIndex: index});
        requestK(params)
            .then(function (response) {
                self.setState({disabledOpenSessionBtn: false});
                self.props.history.push(`/${self.props.peykName}/bordroimport/${response.data}`)
            }).catch(function (error) {
            showAxiosError(error);
            self.setState({disabledOpenSessionBtn: false});
        });
    }

    isSelected(id) {
        return (this.state.selection.indexOf(id) !== -1);
    }

    toggleCheckboxChange(e, row, id) {
        let self = this;
        let checked = e.target.checked;
        let items = self.state.content;
        let selection = self.state.selection;
        let deleteSelection = self.state.deleteSelection;
        if (checked === false && dropdownCheckRef.current?.state?.selectionTag === "selectAll") {
            dropdownCheckRef.current.setState({selectionTag: "await",})
        }
        if (row === "all") {
            let selection = [];
            if (checked) {
                items.forEach(item => {
                    selection.push(item[id]);
                    if (DocumentState[item.state].value < 300) {
                        deleteSelection.push(item.bordroSession[id]);
                    }
                });
            }
            self.setState({selection: selection, deleteSelection: deleteSelection, selectedAll: checked});
        } else {
            let index = selection.indexOf(row[id]);
            let deleteIndex = deleteSelection.indexOf(row.bordroSession[id]);
            if (deleteIndex >= 0) {
                deleteSelection = [
                    ...deleteSelection.slice(0, index),
                    ...deleteSelection.slice(index + 1)
                ];
            }
            if (index >= 0) {
                selection = [
                    ...selection.slice(0, index),
                    ...selection.slice(index + 1)
                ];
            } else {
                selection.push(row[id]);
                if (DocumentState[row.state].value < 300) {
                    deleteSelection.push(row.bordroSession[id]);
                }
            }
            self.setState({
                deleteSelection: deleteSelection,
                selection: selection,
                selectedAll: ((selection.length > 0) ? (selection.length === self.state.content.length) : false)
            });
        }
    }


    downloadContent(row, index) {
        let self = this;
        let params = {
            url: `/bordro/previewBordroContent`,
            responseType: 'arraybuffer',
            method: "post",
            data: row.id
        };
        self.setState({disabledDownloadBtn: true, loadingType: "downlaod", selectedIndex: index});
        requestK(params)
            .then(function (response) {
                let blob = new Blob([response.data], {type: response.headers["content-type"]});
                var link = document.createElement('a');
                link.href = window.URL.createObjectURL(blob);
                let randomNum = Math.floor(Math.random() * 100000);
                let createdAt = !!row.createdAt ? ("_" + row.createdAt.toString().substring(0, row.createdAt.indexOf("T"))) : "";
                let fileName = "Bordro" +
                    (row.parsedTckn ? ("_" + row.parsedTckn + "_") : "_") +
                    row.term.split("-")[0] + "_" +
                    row.term.split("-")[1] + "_" + createdAt + "_" + randomNum + ".pdf";
                link.download = fileName;
                document.body.appendChild(link);
                if (row.timeStamp != null && row.timeStamp != "") {
                    let formTimeStamp = new Blob([base64_arraybuffer.decode(row.timeStamp)], {type: 'application/octet-stream'});
                    let linkTs = document.createElement('a');
                    linkTs.href = window.URL.createObjectURL(formTimeStamp);
                    linkTs.download = fileName + ".zd";
                    document.body.appendChild(linkTs);
                    linkTs.click();
                }
                link.click();
                self.setState({disabledDownloadBtn: false, loadingType: null});
                self.setState({loading: false});
            }).catch(function (error) {
            showAxiosError(error);
            self.setState({disabledDownloadBtn: false, title: null, loadingType: null});
        });
    }

    renderMainTable() {
        let pageIdList = [];
        if (this.state.content && this.state.content instanceof Array) {
            this.state.content.map(e => {
                pageIdList.push(e.id);
            });
        }
        let columnList = [
            {
                Header: (this.state.content &&
                    <DropdownCheck
                        ref={dropdownCheckRef}
                        selectionIdList={this.state.selection}
                        totalIdList={this.state.totalIdList}
                        pageIdList={pageIdList}
                        onChange={e => {
                            if (dropdownCheckRef.current?.state?.selectionTag === 'unselectAll') {
                                this.setState({selection: [], selectedAll: false});
                            } else if (dropdownCheckRef.current?.state?.selectionTag === 'selectAll') {
                                dropdownCheckRef.current.setState({alert: null});
                                detailedSearchRef.current.handleSearch();
                            } else {
                                this.setState({selection: e})
                            }
                        }}/>),
                accessor: "id",
                selectType: "checkbox",
                sortable: false,
                Cell: (row, index) => (this.state.content &&
                    <input type="checkbox" checked={this.isSelected(row.id)}
                           onChange={(e) => this.toggleCheckboxChange(e, row, "id", index)}/>)
            },
            {
                Header: "Dnem",
                accessor: "term",
                Cell: row => (
                    <div>
                        {!!row.term && moment(row.term, 'YYYY/MM/DD').format('YYYY/MM')}
                    </div>
                )
            },
            {
                Header: "Lokasyon",
                accessor: "location",
                Cell: row => (
                    <div style={{margin: 0}}><TextOverFlowTooltip text={row.location} maxLength={15}/></div>
                )
            },
            {
                Header: "TCKN",
                accessor: "parsedTckn",
                Cell: row => (
                    <div>
                        {(row.employee && row.employee.name) && <span className="text-success">{row.parsedTckn}</span>}
                        {!(row.employee && row.employee.name) && <span className="text-danger">{row.parsedTckn}</span>}
                    </div>
                )
            },
            {
                Header: "Ad",
                accessor: "employee_name",
                Cell: row => (
                    <div>
                        {row.employee && row.employee.name}
                    </div>
                )
            },
            {
                Header: "Soyad",
                accessor: "employee_surname",
                Cell: row => (
                    <div>
                        {row.employee && row.employee.surname}
                    </div>
                )
            },
            {
                Header: "Departman",
                accessor: "employee_department",
                Cell: row => (
                    <div style={{margin: 0}}>{row.employee && row.employee.department &&
                        <TextOverFlowTooltip text={row.employee.department.name} maxLength={15}/>}</div>
                )
            },
            {
                Header: "Durumu",
                accessor: "state",
                Cell: row => (
                    <div>
                        {row.error && <span className="text-danger">{row.error}</span>}
                        {!row.error &&
                            <span
                                className={"text-" + DocumentState[row.state].messageType}>{DocumentState[row.state].message}</span>}
                    </div>
                )
            },
            {
                Header: "lemler",
                accessor: "",
                sortable: false,
                Cell: (row, index) => (
                    <div className="btn-group" style={{padding: 0, margin: 0}}>
                        <div className="dropdown" style={{padding: 0, margin: 0}}>
                            <button className="MenuBtn2 listt btn btn-default"
                                    style={{WebkitTextFillColor: 'white'}}
                                    type="button" data-toggle="dropdown">Senz
                            </button>
                            <div className="dropdown-menu" aria-labelledby={"dropdownMenuButton_" + row.id}>

                                {row.employee && row.employee.name &&
                                    <a className="dropdown-item"
                                       onClick={() => this.setState({stateDetail: row})}>
                                        <i className="fa fa-info-circle"/>Durum Detay
                                    </a>}
                            </div>
                        </div>
                    </div>
                )
            }
        ];
        let columns = renewListForPrivileges(this, columnList, "accessor", this.state.isKepOwner);
        if (this.props.match && this.props.match.params.id) {
            let newList = [];
            columns.forEach(item => {
                if (!item["accessor"].includes("department") && !item["accessor"].includes("jobTitle")) {
                    newList.push(item);
                }
            });
            columns = newList;
        }

        return <Table columns={columns}
                      content={this.state.content}
                      loading={this.state.loading}
                      loadingText="Kaytlar getiriliyor..."
                      total={this.state.total}
                      page={this.state.page}
                      size={this.state.size}
                      manual={this.state.manual}
                      options={["5", "10", "20", "50", "100"]}
                      fetchData={this.fetchData}/>
    }


    render() {

        const fieldList = [
            {
                label:  "Departman",
                accessor: "employee_department_name",
                type: "select",
                operator: "=",
                placeholder: "Seiniz",
                selectData: departmentDataForStatement(this.state.departmentList),
            },
            {
                label: "alan TCKN",
                accessor: "parsedTckn",
                type: "numericText",
                operator: "=",
            },
            {
                label: "Dnem Tarihi (Balang)",
                accessor: "term",
                type: "month",
                operator: ">=",
            },
            {
                label: "Dnem Tarihi (Biti)",
                accessor: "max_term",
                type: "month",
                operator: "<=",
            },
            {
                label:  "alan Ad",
                accessor: "employee_name",
                type: "text",
                operator: "=",
            },
            {
                label:  "alan Soyad",
                accessor: "employee_surname",
                type: "text",
                operator: "=",
            },
            {
                label: "Durum",
                accessor: "state",
                type: "select",
                operator: "=",
                selectData: stateData(this.state.isKepOwner)
            },

            {
                label: "Lokasyon Bilgisi",
                accessor: "location",
                type: "text",
                operator: "=",
            },

            {
                label: "alan Durumu",
                accessor: "employee_statusType",
                type: "select",
                operator: "=",
                selectData: statusTypeFilterData()
            }
        ];
        let fields = renewListForPrivileges(this, fieldList, "accessor", this.state.isKepOwner);
        if (this.props.match && this.props.match.params.id) {
            let newList = [];
            fields.forEach(item => {
                if (!item["accessor"].includes("employee_") && !item["accessor"].includes("parsedTckn")) {
                    newList.push(item);
                }
            });
            fields = newList;
        }


        return (
            <div className="row baseBackground">
            <Nav className="topbar" style={{paddingLeft: '14px', height: '53px'}}>
                <NavBarKS offsetWidth={this.props.offsetWidth} innerWidth={window.innerWidth}
                          btnDisabled={this.state.btnDisabled}
                          selection={this.state.selection} name='archiveInvoiceList'>
                    {<StickButton position="leftSide" className="zocial Excel" style={{fontSize: 'x-small'}}
                                  label="Rapor Excel ndir"
                                  title="Seilen cret Bordrolarn Raporunu Excel Olarak ndir"
                                  disabled={this.state.loading || !(this.state.selection && this.state.selection.length > 0)}
                                  onClick={() => this.state.selection && this.state.selection.length > 0 && this.downloadMedia("excel")}/>}
                </NavBarKS>
            </Nav>

                {this.state.alert &&
                    <div className="row satirBasi">
                        {this.state.alert}
                    </div>
                }
                <div className="row satirBasi">
                    <div className="col-md-12">
                        <div className="card">
                            <div className="card-header">
                                <h4 className="card-title">cret Bordro Raporlar</h4>
                            </div>
                            <div className="card-body">
                                <div className="">
                                    <div className="" style={{marginTop: '10px', marginBottom: '0px'}}>
                                        <DetailedSearch ref={detailedSearchRef}
                                                        fields={fields}
                                                        loading={this.state.searching}
                                                        onSearch={this.handleSearch}/>
                                    </div>
                                </div>
                            </div>

                            {this.state.tableVisible && (
                                <>
                                    <CardBody>
                                        <div className="row" style={{ marginLeft: '10px', color: 'red' }}>
                                            <span>{this.state.selection.length !== 0 ? `Seilen satr says ${this.state.selection.length}` : ''}</span>
                                        </div>
                                        {this.renderMainTable()}
                                    </CardBody>
                                </>
                            )}
                        </div>
                    </div>
                </div>
                {this.state.mediaModal &&
                    <ApproveDocumentViewer url={this.state.url}
                                           modal={this.state.mediaModal}
                                           closeModal={this.setMediaViewer}
                    />}
                {this.state.stateDetail &&
                    <StateDetailStatement detail={this.state.stateDetail} documentType={"Bordro"}
                                 toggle={() => this.setState({stateDetail: undefined})}
                    />}

            </div>
        );
    }
}

export default BordroStatement;