import React, {useEffect, useState} from "react";
import DragList from "./gear/DragList";
import DropList from "./gear/DropList";
import Joyride from 'react-joyride';

function CustomFormEditor(props) {
    const [run, setRun] = useState(false);
    const [locale, setLocale] = useState({
            back: 'Geri',
            close: 'Kapat',
            last: 'Son',
            next: 'leri',
            skip: 'Atla',
        }
    );
    const [steps, setSteps] = useState([
            {
                target: '.drag-list',
                content: 'Sa tarafta bulunan srkle brak panelinden dilediiniz nesneleri form alanna srkleyerek formunuzu oluturabilirsiniz.',
                placement: "auto",
                disableBeacon: 'true'
            },
            {
                target: '.drop-list div:nth-child(1) label',
                content: 'Oluturduunuz form bir "zlk formu" ise "zlk formu mu ?" seeneini iaretleyebilirsiniz.',
                placement: "auto",
                disableBeacon: 'true'
            },
            {
                target: '.drop-list div:nth-child(1) #answerWays',
                content: 'Cevap ekli seenei "zlk formlar iin geerlidir. Belgeyi, burada seilen kiiler grebilecek ve ilem yapabilecektir. "K Yetkilisi" seilirse belge onay aka tabi olmaz.',
                placement: "auto",
                disableBeacon: 'true'
            },
            {
                target: '.drop-list div:nth-child(1) #group',
                content: 'Belgeye ilem yetkisi olacak grup seimi yaplr.',
                placement: "auto",
                disableBeacon: 'true'
            },
            {
                target: '.drop-list div:nth-child(1) button',
                content: 'Formunuzu oluturduysanz "Kaydet" butonuna tklayarak taslaklara kaydedebilirsiniz.',
                placement: "auto",
                disableBeacon: 'true'
            },
        ],
    );

    const activateJoyride = () => {
        setRun(!run)
    }

    const callback = (data) => {
        if (data.action === 'close' || data.action === 'skip' || data.type === 'tour:end') {
            setRun(false)
        }
    }

    const [items,setItems] = useState(null);    //this array holds only one item !

    const addItem = (item) => {
        item.id=Math.random();
        setItems(item);
    }

    return (
        <div>
            <Joyride
                run={run}
                steps={steps}
                locale={locale}
                callback={callback}
                continuous={true}
                showProgress={true}
                showSkipButton={true}
                disableScrollParentFix={true}
                disableScrolling={true}
                styles={{
                    options: {
                        zIndex: 999999999999999,
                        primaryColor: "#50a8e2"   // "#fc9636"
                    }
                }}
            />

            <div className="row" style={{margin: 0, padding: 0}}>
                <div className="row w-100" style={{minHeight: "50px"}}>
                    <button className="helpButton" style={{position: "absolute", right: "90px", marginTop: "10px"}}
                            onClick={() => {
                                activateJoyride()
                            }}>
                        <span className="tooltipText">Size yardmc olabilirim</span>
                        <i className="far fa-question-circle helpButtonIcon"
                           style={{color: "#5B8DAE", fontSize: "35px"}}/></button>
                </div>
                <div className="row w-100">
                    <div className="col-9 drag-drop-list-container">
                        <div className='drop-list'>
                            {props.location.editedForm &&
                            <DropList
                                editedJson={props.location.selectedSurveyForm.bulkJson}
                                editedForm={props.location.editedForm}
                                selectedSurveyForm={props.location.selectedSurveyForm}
                                {...props}
                                items={items}
                            />}
                            {!props.location.editedForm &&
                            <DropList {...props} items={items}/>}
                        </div>
                    </div>
                    <div className="col-3 drag-drop-list-container">
                        <div className='drag-list'>
                            <DragList addItem={addItem}/>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default CustomFormEditor;