import React, { Component } from 'react';
import { FormGroup, Label, Input, Button, Nav } from 'reactstrap';
import { requestK } from '../../../helper/APIUtils';
import Alert from "react-s-alert";

class MobileUpdatePage extends Component {
    state = {
        platforms: [],
        selectedPlatform: '',
        currentVersion: '',
        newVersion: '',
        previousVersion: '',
    };

    componentDidMount() {
        this.fetchPlatforms();
    }

    fetchPlatforms = async () => {
        try {
            const response = await requestK({ url: '/application/platforms', method: 'get' });
            this.setState({ platforms: response.data });

            if (response.data.length > 0) {
                this.setState({ selectedPlatform: response.data[0] }, () => {
                    this.fetchCurrentVersion();
                });
            }
        } catch (error) {
            Alert.error("Platformlar alma hatas:", error,{
                position: 'top-right', effect: 'stackslide', timeout: 5000
            });
        }
    };

    fetchCurrentVersion = async () => {
        const { selectedPlatform } = this.state;
        if (!selectedPlatform) return;

        try {
            const response = await requestK({
                url: `/application/version/${selectedPlatform}`,
                method: 'get',
            });
            this.setState({ currentVersion: response.data });
        } catch (error) {
            Alert.error("Mevcut versiyonu alma hatas:", error,{
                position: 'top-right', effect: 'stackslide', timeout: 5000
            });
        }
    };

    handlePlatformChange = (e) => {
        this.setState({ selectedPlatform: e.target.value }, () => {
            this.fetchCurrentVersion();
        });
    };

    handleVersionChange = (e) => {
        const newVersion = e.target.value;
        const currentVersion = this.state.currentVersion;
        const regex = /^[0-9.]*$/;

        if (regex.test(newVersion)) {
            this.setState({ newVersion, previousVersion: currentVersion });
        } else {
            // Display an alert for invalid input
            Alert.error("Versiyon sadece rakam ve nokta ierebilir.", {
                position: 'top-right', effect: 'stackslide', timeout: 5000
            });
        }
    };

    updateVersion = async () => {
        const { selectedPlatform, newVersion } = this.state;
        if (!selectedPlatform || !newVersion) {
            Alert.info("Ltfen versiyon alann bo brakmaynz.", {
                position: 'top-right', effect: 'stackslide', timeout: 5000
            });
            return;
        }
        try {
            await requestK({
                url: `/application/updateVersion/${selectedPlatform}`,
                method: 'post',
                data: { version: newVersion },
            });
            Alert.info("Versiyon gncellendi!", {
                position: 'top-right', effect: 'stackslide', timeout: 5000
            });
            this.fetchCurrentVersion();
            this.setState({ newVersion: '' });
        } catch (error) {
            Alert.error("Versiyon gncelleme baarsz oldu. Ltfen tekrar deneyin.", {
                position: 'top-right', effect: 'stackslide', timeout: 5000
            });
        }
    };

    render() {
        const { platforms, selectedPlatform, currentVersion, newVersion } = this.state;

        return (
            <div id="container">
                <div className="row">
                    <div className="col-md-6 col-sm-12">
                        <div className="row baseBackground" style={{ minHeight: '290px'}}>
                            <Nav className="topbar" style={{ paddingLeft: '20px', height: '53px' }}>
                            </Nav>
                            <div className="col-md-12">
                                <div className="card-header">
                                    <h4 className="card-title" style={{ marginTop: '75px' }}>Versiyon gncelleme ilemi</h4>
                                </div>
                                <FormGroup>
                                    <Input style={{ width: '300px', marginLeft: '30px' }}
                                           type="select"
                                           name="select"
                                           id="platformSelect"
                                           onChange={this.handlePlatformChange}
                                           value={selectedPlatform}>

                                        {platforms.map((platform, index) => (
                                            <option key={index} value={platform}>
                                                {platform}
                                            </option>
                                        ))}
                                    </Input>
                                </FormGroup>
                            </div>
                        </div>
                    </div>
                    <div className="col-md-6 col-sm-12">
                        <div className="row baseBackground" style={{ minHeight: '290px'}}>
                            <div className="col-md-12">
                                <div className="card-header">
                                    <h4 className="card-title" style={{ marginTop: '60px' }}> </h4>
                                </div>
                            </div>
                            <div className="col-md-6">
                                <Label for="versionInput" style={{ marginLeft: '10px' }}>Mevcut Versiyon:</Label>
                                <div style={{ background: '#f0f0f0', padding: '2px', border: '1px solid #ccc', marginLeft: '10px' }}>
                                    {currentVersion}
                                </div>
                            </div>
                            <div className="col-md-6">
                                <FormGroup>
                                    <Label for="versionInput" style={{ marginTop: '20px', marginLeft: '10px' }}>Yeni Versiyon Giriniz:</Label>
                                    <Input style={{ marginLeft: '10px' }}
                                           type="text"
                                           name="version"
                                           id="versionInput"
                                           onChange={this.handleVersionChange}
                                           value={newVersion}
                                    />
                                </FormGroup>
                            </div>
                            <div className="col-md-12">
                                <div className="d-flex justify-content-end">
                                    <Button color={"info"} className='launcherBtn' onClick={this.updateVersion}>
                                        Versiyon Gncelle
                                    </Button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default MobileUpdatePage;
