import React, {Component} from "react";
import NavBarKS from "../../../components/NavBarKS";
import StickButton from "../../../components/StickButton";
import {Button, Col, Form, FormGroup, Input, Label, Modal, ModalBody, ModalFooter, ModalHeader} from "reactstrap";
import SweetAlert from "react-bootstrap-sweetalert";
import Select from "../../../local_node_modules/react-select";
import {getEmployees} from "../common/PeykHelper";
import {requestK} from "../../../helper/APIUtils";
import {showAxiosError} from "../../../helper/helper";
import {validateField} from "../../../common/validation";
import {isValidObject} from "../../../common/componentValidation";
import Alert from "react-s-alert";
import Swal from "sweetalert2";
import Table from "../../../components/Table/Table";
import MiniButton from "../../../components/MiniButton";
import moment from "moment";

class TransferAuthority extends Component {
    constructor(props) {
        super(props);
        this.state = {
            alert: null,
            loading: false,
            displayCreateUpdateDialog: false,
            roles: [],
            organizations: [],
            transferAuthorityDto: {},
            hasTransferedAuthorityDto: "",
            checkedRoles: [],
            checkedOrganizations: [],
            manual: false,
            content: [],
            total: 0,
            searching: false,
            searchQuery: undefined,
            page: 0,
            size: 10,
            preview: false
        };
        this.createClicked = this.createClicked.bind(this);
        this.saveClicked = this.saveClicked.bind(this);
        this.selectPerson = this.selectPerson.bind(this);
        this.getPersonalRoleList = this.getPersonalRoleList.bind(this);
        this.getPersonalOrganizationList = this.getPersonalOrganizationList.bind(this);
        this.editHandleDateChange = this.editHandleDateChange.bind(this);
        this.approveDocumentAuthorityDateChange = this.approveDocumentAuthorityDateChange.bind(this);
        this.getTransferedAuthority = this.getTransferedAuthority.bind(this);
        this.deleteTransferedAuthority = this.deleteTransferedAuthority.bind(this);
        this.preview = this.preview.bind(this);
        this.hasTransfereedAuthority = this.hasTransfereedAuthority.bind(this);
    }

    componentDidMount() {
        let self = this;
        self.getTransferedAuthority();
        self.hasTransfereedAuthority();
        self.getPersonalRoleList();
        self.getPersonalOrganizationList();
    }

    hasTransfereedAuthority() {
        let self = this;
        let params = {
            url: '/transfer-authority/hasTransfereeAuthority',
            method: "post"
        };
        requestK(params)
            .then(function (response) {
                self.setState({hasTransferedAuthorityDto: response.data});
            }).catch(function (error) {
            showAxiosError(error);
        });
    }

    preview(row) {
        let self = this;
        let checkedRoles = new Array(self.state.roles.length).fill(false);
        let checkedOrganizations = new Array(self.state.organizations.length).fill(false);
        let selectedRoleIds = row["transferAuthorityRoles"].map(transferAuthorityRole => transferAuthorityRole.roleId);
        let selectedOrganizationIds = row["transferAuthorityOrganizations"].map(transferAuthorityOrganization => transferAuthorityOrganization.organizationId);
        self.state.roles.forEach((role, index) => {
            if (selectedRoleIds.includes(role.id)) {
                checkedRoles[index] = true
            }
        });
        self.state.organizations.forEach((organization, index) => {
            if (selectedOrganizationIds.includes(organization.id)) {
                checkedOrganizations[index] = true
            }
        });
        self.setState({
            transferAuthorityDto: row,
            displayCreateUpdateDialog: true,
            checkedRoles,
            checkedOrganizations
        });
    }

    deleteTransferedAuthority(row) {
        let self = this;
        Swal({
            title: 'Uyar! ',
            text: ("Vekil yetkilendirmesini silmek istiyor musunuz?"),
            cancelButtonText: 'Hayr',
            cancelButtonColor: '#d33',
            showCancelButton: true,
            confirmButtonText: 'Evet'
        }).then((result) => {
            if (result.dismiss != "cancel") {
                let params = {
                    url: '/transfer-authority/getBackTransferedAuthority',
                    method: "post",
                    data: row
                };
                self.setState({loading: true});
                requestK(params)
                    .then(function (response) {
                        let checkedRoles = new Array(self.state.roles.length).fill(false);
                        let checkedOrganizations = new Array(self.state.organizations.length).fill(false);
                        self.setState({loading: false, checkedRoles, checkedOrganizations, transferAuthorityDto: {}});
                        self.getTransferedAuthority();
                        Swal({
                            title: 'Baarl ! ',
                            text: ("Silme ilemi baaryla gerekletirildi."),
                            type: 'success',
                            confirmButtonText: 'Tamam'
                        }).then(() => {
                        });
                    }).catch(function (error) {
                    self.setState({loading: false});
                    showAxiosError(error);
                });
            }
        });

    }

    getTransferedAuthority() {
        let self = this;
        let params = {
            url: '/transfer-authority/getTransferedAuthority',
            method: "post"
        };
        requestK(params)
            .then(function (response) {
                self.setState({content: response.data});
            }).catch(function (error) {
            showAxiosError(error);
        });
    }

    approveDocumentAuthorityDateChange(e) {
        let self = this;
        let {checked} = e.target;
        let transferAuthorityDto = self.state.transferAuthorityDto;
        transferAuthorityDto["approveDocumentAuthority"] = checked;
        self.setState({transferAuthorityDto});
    }

    editHandleDateChange(event) {
        let self = this;
        let {name, value} = event.target;
        let transferAuthorityDto = self.state.transferAuthorityDto;
        if (name === "start_date") {
            transferAuthorityDto["startTransferDate"] = value;
        } else if (name === "finish_date") {
            transferAuthorityDto["finishTransferDate"] = value;
        }
        self.setState({transferAuthorityDto});
    };


    getPersonalRoleList() {
        let self = this;
        let params = {
            url: '/role/personalRoleListByLoginUser',
            method: "post",
            data: self.props.currentUser.id
        };
        requestK(params)
            .then(function (response) {
                let checkedRoles = new Array(response.data.length).fill(false);
                self.setState({roles: response.data, checkedRoles});
            })
            .catch(function (error) {
                showAxiosError(error);
            });
    }

    getPersonalOrganizationList() {
        let self = this;
        let params = {
            url: '/employee/getEmployeeHasOrganizations',
            method: "post",
        };
        requestK(params)
            .then(function (response) {
                let checkedOrganizations = new Array(response.data.length).fill(false);
                self.setState({organizations: response.data, checkedOrganizations});
            })
            .catch(function (error) {
                showAxiosError(error);
            });
    }

    selectPerson() {
        let self = this;
        self.setState({
            alert: (
                <SweetAlert info
                            confirmBtnText="ptal"
                            confirmBtnBsStyle="warning"
                            title="Kii Seiniz"
                            style={{height: '450px'}}
                            onConfirm={() => {
                                self.setState({alert: null});
                            }}>
                    <Select.Async multi={false}
                                  ignoreAccents={false}
                                  onChange={(e) => self.onChangePerson(e)}
                                  isLoading={true}
                                  autoload={false}
                                  cache={false}
                                  clearable={false}
                                  filterOptions={(options) => options}
                                  noResultsText="Kayt bulunamad."
                                  searchPromptText="Arama iin en az 2 karakter giriniz."
                                  placeholder="sim veya soyisim ile ara..."
                                  loadingPlaceholder="Ykleniyor..."
                                  loadOptions={getEmployees}/>
                </SweetAlert>
            )
        });
    }

    onChangePerson(e) {
        let self = this;
        let transferAuthorityDto = self.state.transferAuthorityDto;
        transferAuthorityDto["transfereeUserId"] = e.value;
        transferAuthorityDto["transfereeName"] = e.label;
        self.setState({alert: null, transferAuthorityDto});
    }

    createClicked() {
        let self = this;
        if (self.state.hasTransferedAuthorityDto != "") {
            Swal({
                title: 'Uyar ! ',
                text: (self.state.hasTransferedAuthorityDto.transferorName + " tarafndan tarafnza vekil olarak yetkilendirme yaplm. Bu yetkilendirme kaldrlmadan baka bir alana vekil olarak yetkilendirme yapamazsnz!"),
                type: 'warn',
                confirmButtonText: 'Tamam'
            }).then(() => {
                return;
            });
            return;
        }
        if (self.state.content.length > 0) {
            Swal({
                title: 'Uyar ! ',
                text: ("Daha nce yetkilendirme yaplm. nceki yetkilendirmeyi silmeden yeni yetkilendirme yapamazsnz!"),
                type: 'warn',
                confirmButtonText: 'Tamam'
            }).then(() => {
                self.getTransferedAuthority();
            });
        } else {
            self.setState({
                displayCreateUpdateDialog: true
            });
        }
    };

    handleOnChangeRoles = (position) => {
        let self = this;
        let checkedRoles = self.state.checkedRoles.map((item, index) =>
            index === position ? !item : item
        );
        let transferAuthorityDto = self.state.transferAuthorityDto;
        let selectedRoles = [];
        checkedRoles.forEach((value, index) => {
            if (value === true) {
                selectedRoles.push({roleId: self.state.roles[index]["id"]});
            }
        });
        transferAuthorityDto["transferAuthorityRoles"] = selectedRoles;
        self.setState({checkedRoles, transferAuthorityDto});
    };

    handleOnChangeOrganizations = (position) => {
        let self = this;
        let checkedOrganizations = self.state.checkedOrganizations.map((item, index) =>
            index === position ? !item : item
        );
        let transferAuthorityDto = self.state.transferAuthorityDto;
        let selectedOrganizations = [];
        checkedOrganizations.forEach((value, index) => {
            if (value === true) {
                selectedOrganizations.push({organizationId: self.state.organizations[index]["id"]});
            }
        });
        transferAuthorityDto["transferAuthorityOrganizations"] = selectedOrganizations;
        self.setState({checkedOrganizations, transferAuthorityDto});
    };

    isValidTransferAuthority(transferAuthorityDto) {
        let error = {};
        let errorMessage = "";
        error.transfereeUserId = validateField(undefined, "1,255", transferAuthorityDto.transfereeUserId);
        if (!error.transfereeUserId.valid) {
            errorMessage = errorMessage + " <strong>Yetkilendirilecek " + error.transfereeUserId.message + "</strong><br/>";
        }
        error.finishTransferDate = validateField(undefined, "1,255", transferAuthorityDto.finishTransferDate);
        if (!error.finishTransferDate.valid) {
            errorMessage = errorMessage + " <strong>Yetkinin Geri Alnaca Tarihi" + error.finishTransferDate.message + "</strong><br/>";
        }
        let valid = isValidObject(error);
        return {error: error, valid: valid, errorMessage: errorMessage};
    }

    saveClicked() {
        let self = this;
        let transferAuthorityDto = self.state.transferAuthorityDto;
        transferAuthorityDto['transferorUserId'] = self.props.currentUser.id;
        self.setState({transferAuthorityDto});
        let result = self.isValidTransferAuthority(transferAuthorityDto);
        let valid = result.valid;
        if (valid) {
            let params = {
                url: `/transfer-authority/realizeTransferAuthority`,
                method: "post",
                data: transferAuthorityDto
            };
            requestK(params)
                .then(function (response) {
                    self.setState({
                        transferAuthorityDto: {},
                        displayCreateUpdateDialog: false,
                        checkedRoles: [],
                        checkedOrganizations: []
                    });
                    Swal({
                        title: 'Baarl ! ',
                        text: ("Kaydetme ilemi baaryla gerekletirildi."),
                        type: 'success',
                        confirmButtonText: 'Tamam'
                    }).then(() => {
                        self.getTransferedAuthority();
                    });
                })
                .catch(function (error) {
                    showAxiosError(error);
                });
        } else {
            Alert.error(result.errorMessage, {
                position: 'top-right',
                effect: 'stackslide',
                timeout: 8000
            });
        }
    }

    renderCreateUpdate() {
        return <Modal isOpen={this.state.displayCreateUpdateDialog} size="lg"
                      toggle={() => this.setState({displayCreateUpdateDialog: false})}>
            <ModalHeader className="justify-content-center"
                         toggle={() => this.setState({displayCreateUpdateDialog: false})}>
                Vekil Yetkilendirme
            </ModalHeader>
            <ModalBody>
                <div>
                    <Form>
                        <FormGroup row style={{display: "flex", alignItems: "center"}}>
                            <Label sm={4} for="person"> Yetkilendirilecek Personel:*</Label>
                            <Col sm={5}>
                                <Input type="text" disabled="true" name="person"
                                       value={this.state.transferAuthorityDto?.["transfereeName"]}/>
                            </Col>
                            <Col sm={3}>
                                <Button onClick={this.selectPerson}>Se</Button>
                            </Col>
                        </FormGroup>
                        <FormGroup row>
                            <Label sm={4} for="role">Rol Yetkisi:</Label>
                            <Col sm={8}>
                                <div className='checkBoxList' style={{padding: "5px"}}>
                                    {this.state.roles.map(({id, roleName}, index) => {
                                        return (
                                            <div id={`roles-${index}`} style={{display: "flex", alignItems: "center"}}>
                                                <input
                                                    className='checkBox-inline-block'
                                                    type="checkbox"
                                                    id={`custom-role-checkbox-${index}`}
                                                    onChange={() => this.handleOnChangeRoles(index)}
                                                    name={roleName}
                                                    value={id}
                                                    checked={this.state.checkedRoles[index]}/>
                                                <span style={{marginLeft: "5px", textTransform: "capitalize"}}
                                                      htmlFor={`custom-role-checkbox-${index}`}>{roleName}</span>
                                            </div>
                                        );
                                    })}
                                </div>
                            </Col>
                        </FormGroup>
                        <FormGroup row>
                            <Label sm={4} for="organization">Organizasyon Yetkisi:</Label>
                            <Col sm={8}>
                                <div className='checkBoxList' style={{padding: "5px"}}>
                                    {this.state.organizations.map(({id, name}, index) => {
                                        return (
                                            <div id={`roles-${index}`} style={{display: "flex", alignItems: "center"}}>
                                                <input
                                                    type="checkbox"
                                                    id={`custom-organization-checkbox-${index}`}
                                                    onChange={() => this.handleOnChangeOrganizations(index)}
                                                    name={name}
                                                    value={id}
                                                    checked={this.state.checkedOrganizations[index]}
                                                />
                                                <span style={{marginLeft: "5px", textTransform: "capitalize"}}
                                                      htmlFor={`custom-organization-checkbox-${index}`}>{name}</span>
                                            </div>
                                        );
                                    })}
                                </div>
                            </Col>
                        </FormGroup>
                        <FormGroup row style={{alignItems: "center"}}>
                            <Label sm={4} for="approve">Dkman Onaylama:</Label>
                            <Col sm={8}>
                                <Input type="checkbox" name="approve"
                                       checked={this.state.transferAuthorityDto?.["approveDocumentAuthority"]}
                                       onChange={(e) => this.approveDocumentAuthorityDateChange(e)}
                                       style={{marginLeft: "5px"}}/>
                            </Col>
                        </FormGroup>
                        <FormGroup row style={{alignItems: "center"}}>
                            <Label sm={4} for="finish_date">Yetkinin Geri Alnaca Tarihi:</Label>
                            <Col sm={8} style={{alignItems: "center"}}>
                                <Input type="datetime-local" name="finish_date"
                                       value={this.state.transferAuthorityDto?.["finishTransferDate"]}
                                       onChange={(e) => this.editHandleDateChange(e)}
                                       style={{marginLeft: "5px"}}/>
                            </Col>
                        </FormGroup>
                    </Form>
                </div>
            </ModalBody>
            <ModalFooter>
                <Button title="ptal" style={{paddingLeft: '16px', width: '100px'}} color="secondary"
                        onClick={() => this.setState({displayCreateUpdateDialog: false})}>
                    ptal
                </Button>

                <Button title="Kaydet" style={{paddingLeft: '16px', width: '100px'}} color="primary"
                        onClick={this.saveClicked}>
                    Kaydet
                </Button>
            </ModalFooter>
        </Modal>
    }

    render() {
        const columns = [
            {
                Header: "Yetkiyi Veren",
                accessor: "transferorName",
                sortable: false,
            },
            {
                Header: "Yetkiyi Alan",
                accessor: "transfereeName",
                sortable: false,
            },
            {
                Header: "Dokuman Onaylama Yetkisi",
                accessor: "approveDocumentAuthority",
                sortable: false,
                Cell: (item) => (
                    <div style={{textAlign: "center"}}>
                        <input type="checkbox"
                               checked={item.approveDocumentAuthority !== null ? item.approveDocumentAuthority : false}
                               disabled="disabled"/>
                    </div>
                )
            },
            {
                Header: "Yetkinin Verildii Tarihi ",
                accessor: "startTransferDate",
                sortable: false,
                Cell: (item) => (<div
                    style={{textAlign: "left"}}>{moment(item.startTransferDate).isValid() ? moment(item.startTransferDate).format("DD-MM-YYYY HH:mm:ss") : ''}</div>)
            },
            {
                Header: "Yetkinin Geri Alnaca Tarihi ",
                accessor: "finishTransferDate",
                sortable: false,
                Cell: (item) => (<div
                    style={{textAlign: "left"}}>{moment(item.finishTransferDate).isValid() ? moment(item.finishTransferDate).format("DD-MM-YYYY HH:mm:ss") : ''}
                </div>)
            },
            {
                Header: "lemler",
                accessor: "",
                sortable: false,
                width: "10%",
                Cell: row => (
                    <div>
                        <MiniButton title="Dzenle" faIcon="fa-edit" //onClick={() => this.editClicked(row)}
                                    color="success" onClick={() => this.preview(row)}/>
                        <MiniButton title="Sil" faIcon="fa-trash" color="danger"
                                    onClick={() => this.deleteTransferedAuthority(row)}
                        />
                    </div>)
            }

        ];
        return (
            <div className="row card">
                <NavBarKS offsetWidth={this.props.offsetWidth} innerWidth={window.innerWidth}>
                    <StickButton position="leftSide" className="zocial Yeni" style={{fontSize: 'x-small'}}
                                 label="Yeni Olutur"
                                 onClick={this.createClicked}/>
                </NavBarKS>
                <div className="col-sm-12 satirBasi">
                    <div className="card-header">
                        <h4 className="card-title">Vekil Yetkilendirme</h4>
                    </div>
                    <div className="card-body">
                        <Table columns={columns}
                               content={this.state.content}
                               loading={this.state.loading}
                               total={this.state.total}
                               page={this.state.page}
                               size={this.state.size}
                               paginationEnabled={false}
                        />
                    </div>
                </div>
                {this.state.alert}
                {this.renderCreateUpdate()}
            </div>)
    }
}

export default TransferAuthority;