import React, {Component} from 'react';
import QuickAccessCardBody from "./QuickAccessCardBody";
import QuickAccessCardHeader from "./QuickAccessCardHeader";

class QuickAccessCard extends Component {

	constructor(props) {
		super(props);

		this.state = {};

		this.defineHeaderStyle = this.defineHeaderStyle.bind(this);
		this.defineBodyStyle = this.defineBodyStyle.bind(this);
	}

	componentWillReceiveProps(nextProps, prevState) {
		if ((nextProps.idList !== prevState.idList) && nextProps.idList.length > 0) {
			this.defineNewHeight(nextProps);
		}
	}

	defineNewHeight(nextProps) {
		let specialHeight = 35;
		if (nextProps.idList) {
			nextProps.idList.map(row => {
				specialHeight = specialHeight + document.getElementById(row).offsetHeight;
			});
		}

		this.setState({
			specialHeight
		})
	}

	defineHeaderStyle() {
		let headerStyle = {
				textAlign: 'center',
				color: '#ececec',
				textTransform: 'uppercase',
				fontFamily: 'Montserrat',
				fontSize: 'x-large'
			}
		;

		if (this.props.title) {
			headerStyle.height = '15%';
		}

		if (this.props.type === 'chart') {
			headerStyle.color = 'black';
			headerStyle.fontSize = 'large'
		}

		if (this.props.headerBg) {
			headerStyle.background = this.props.headerBg;
		}
		else {
			headerStyle.background = '#72b243'
		}

		if (this.props.type === 'chart') {
			headerStyle.background = 'white'
			headerStyle.paddingTop = '5px'
		}

		return headerStyle;
	}

	defineBodyStyle() {
		let bodyStyle = {
			textAlign: 'center'
		};

		if (this.props.customHeight) {
			bodyStyle.height = this.props.customHeight;
		} else {
			if (this.props.title) {
				bodyStyle.height = '70%';
			} else {
				if (this.state.specialHeight) {
					bodyStyle.height = this.state.specialHeight;
				} else if (this.props.specificHeight) {
					bodyStyle.height = this.props.specificHeight;
				} else {
					bodyStyle.height = '100%';
				}
			}
		}

		if (this.props.bodyBg) {
			bodyStyle.background = this.props.bodyBg;
		}

		return bodyStyle;
	}


	prepareBodyContent() {
		return this.props.children
	}

	render() {
		return (
			<div className='qa-card' style={{height: this.props.height}}>
				{this.props.title &&
				<QuickAccessCardHeader title={this.props.title} defineHeaderStyle={this.defineHeaderStyle}/>
				}

				<QuickAccessCardBody defineBodyStyle={this.defineBodyStyle} content={this.prepareBodyContent()}/>
			</div>
		)
	}
}

export default QuickAccessCard;