import React, {Component} from 'react';
import {UncontrolledTooltip} from "reactstrap";
import {validateComponent, validateField} from "../../../common/validation";
import Select from "../../../local_node_modules/react-select";
import {deepCopyObject} from "../../../helper/helper";

class CustomerCommercialInfo extends Component {
	constructor(props) {
		super(props);

		this.state = {
			error: {},
			filteredTaxOffices: []
		};

		this.validateField = this.validateField.bind(this);
		this.validateClass = this.validateClass.bind(this);
		this.validateMessage = this.validateMessage.bind(this);
		this.validateComponent = this.validateComponent.bind(this);
		this.onCityChange = this.onCityChange.bind(this);
	}

	componentDidMount() {
		let city = this.props.customer.city;
		let filteredTaxOffices = this.props.taxOffices.filter(to => to.city === city);
		this.setState({
			city,
			filteredTaxOffices,
		})
	}

	validateField(e) {
		let id = e.target.id;
		let rule = e.target.dataset.vdata;
		let boundary = e.target.dataset.vlength && e.target.dataset.vlength;
		let value = e.target.value;

		let result = validateField(rule, boundary, value);
		let error = this.state.error;

		error = Object.assign(error, {[id]: result});

		this.setState({error});
	}

	validateClass(id) {
		let error = this.state.error;
		return (error[id] === undefined ? "" : (error[id].valid ? " has-success" : " has-danger"));
	}

	validateMessage(id) {
		let error = this.state.error;
		return (error[id] === undefined ? '' : (error[id].message));
	}

	validateComponent() {
		let result = validateComponent(this);
		this.setState({
			error: result.error
		});
		return result
	}

	onCityChange(e) {
		let taxOffices = this.props.taxOffices;
		let filteredTaxOffices = taxOffices.filter(to => to.city === e.value);
		this.props.onChangeCustomerCity(e.value);
		this.setState({city: e.value, filteredTaxOffices})
	}

	selectStyle(item) {
		let error = deepCopyObject(this.state.error);
		if (error[item] === undefined || error[item].valid) {
			return null;
		} else {
			return {backgroundColor: '#f8f8f8', borderColor: '#ffcfcf'}
		}
	}

	render() {
		return (
			<div className="col-sm-12">
				<div className="col-sm-12 " style={{marginTop: '-15px'}}>
					<div className=" row">
						<div className="col-sm-6 row">
							<div className="col-sm-4">
								<label className="col-form-label">Ver. Dairesi *</label>
							</div>
							<div className="col-sm-3">
								<div className={"form-group" + this.validateClass('tTaxOffice')} style={{wordWrap: 'normal'}}>
									<Select
										clearable={false}
										className="select-control"
										name="taxOffice"
										id="tTaxOffice"
										onBlur={() => this.validateField(
											{
												target: {
													id: "tTaxOffice",
													dataset: {vdata: "required", vlength: "1,45"},
													value: this.state.city
												}
											})}
										inputProps={{vdata: "required", vlength: "1,45"}}
										ref="tTaxOffice"
										value={this.state.city}
										placeholder="Seiniz..."
										style={this.selectStyle('tTaxOffice')}
										onChange={(e) => this.onCityChange(e)}
										options={this.props.cities.map(city => ({
											value: city.cityName,
											label: city.cityName
										}))}
									/>
									{this.validateMessage('tTaxOffice') !== "" &&
									<UncontrolledTooltip placement="right" target="tTaxOffice" delay={0}>
										{this.validateMessage("tTaxOffice")}
									</UncontrolledTooltip>}
								</div>
							</div>

							<div className="col-sm-5" style={{maxWidth: "none"}}>
								<div className={"form-group" + this.validateClass('tTaxOffice')}>
									<Select
										clearable={false}
										className="select-control"
										name="taxOffice"
										id="tTaxOffice"
										onBlur={() => this.validateField(
											{
												target: {
													id: "tTaxOffice",
													dataset: {vdata: "required", vlength: "1,255"},
													value: this.props.customer.taxOffice
												}
											})}
										inputProps={{vdata: "required", vlength: "1,255"}}
										ref="tTaxOffice"
										value={this.props.customer.taxOffice}
										placeholder="Seiniz..."
										style={this.selectStyle('tTaxOffice')}
										onChange={(e) => this.props.onTaxOfficeChange(e)}
										options={this.state.filteredTaxOffices.map(to => ({
											value: to.vdName,
											label: to.vdName
										}))}
									/>
									{this.validateMessage('tTaxOffice') !== "" &&
									<UncontrolledTooltip placement="right" target="tTaxOffice" delay={0}>
										{this.validateMessage("tTaxOffice")}
									</UncontrolledTooltip>}
								</div>
							</div>
						</div>


						<div className="col-sm-6 row">
							<div className="col-sm-4">
								<label className="col-form-label">Mersis No</label>
							</div>
							<div className="col-sm-8" style={{maxWidth: "none"}}>
								<div className="form-group">
									<input className="form-control"
										   maxLength="16"
										   type="text"
										   name="mersisNo"
										   id="tMersisNo"
										   value={this.props.customer.mersisNo}
										   onChange={(e) => this.props.onChangeCustomer(e)}/>
								</div>
							</div>
						</div>
					</div>
				</div>

				<div className="col-sm-12 ">
					<div className=" row">

						<div className="col-sm-6 row">
							<div className="col-sm-4">
								<label className="col-form-label">Oda Kayt Numaras</label>
							</div>
							<div className="col-sm-8 form-group">
								<input className="form-control"
									   type="text"
									   name="ticaretSicilNo"
									   value={this.props.customer.ticaretSicilNo}
									   onChange={(e) => this.props.onChangeCustomer(e)}/>
							</div>
						</div>

						<div className="col-sm-6 row">
							<div className="col-sm-4">
								<label className="col-form-label">Kaytl Olduu Oda</label>
							</div>
							<div className="col-sm-8">
								<div className="form-group">
									<input className="form-control"
										   type="text"
										   name="commerceChamber"
										   id="tCommerceChamber"
										   value={this.props.customer.commerceChamber}
										   onChange={(e) => this.props.onChangeCustomer(e)}/>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		)
	}
}

export default CustomerCommercialInfo;