import React, {Component} from 'react';
import {Button, Card, CardBody, Col, Row} from 'reactstrap';
import 'react-table/react-table.css'
import SweetAlert from 'react-bootstrap-sweetalert';
import Notify from 'react-notification-alert';
import 'react-datetime/css/react-datetime.css'
import 'moment/locale/tr'
import Axios from 'axios'
import HsmErrorAlert from '../../../../common/hsm/HsmErrorAlert'
import HsmCertificateViewer from '../../common/HsmCertificateViewer';
import ReactLoading from 'react-loading';
import {requestK} from "../../../../helper/APIUtils";
import {showAxiosError} from "../../../../helper/helper";

class SignApproval extends Component {

  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      completed: false,
      signedDocumentCount: 0,
      progressLength: 1,
      pageState: 0,
      timer: 1,
      timerStarted: false,
      certError: true,
      disableButton: false
    };

    this.signDoc = this.signDoc.bind(this);
    this.writeSignedDoc = this.writeSignedDoc.bind(this);
    this.updatePdfCertificates = this.updatePdfCertificates.bind(this);

    this.stateMap = {
      0: {
        label: "PDF imzalama iin hazr. mzalaynz",
      },
      20: {
        label: "Sertifika sahibi PDF'e yazlyor. Ltfen, sayfay terk etmeyiniz.",
        wait: true
      },
      30: {
        label: "PDF imzalanyor. Ltfen, sayfay terk etmeyiniz.",
      },
      40: {
        label: "PDF imzalanamad. Bilgileri kontrol ediniz.",
        danger: true
      }
    };
  }

  componentDidMount() {
    this.axios = Axios.create({
      baseURL: "http://127.0.0.1:9999"
    });
  }

  componentDidUpdate() {
    if (this.state.timer === 1 && !this.state.timerStarted &&
      this.state.completed && this.state.signedDocumentCount === this.state.progressLength) {
      setInterval(() => {
        this.setState(prevState => ({timer: prevState.timer - 1}))
      }, 1000);
      this.setState({timerStarted: true});
    } else if (this.state.timer === 0) {
      this.props.completed();
    }
  }

  sign = () => {
    this.updatePdfCertificates();
  };

  updatePdfCertificates() {
    let self = this;
    let params = {
      url: `/signer/createOnePdfSignedInfoList`,
      method: "post",
      data: {
        documentId: self.props.selectedDoc.documentId,
        documentType: self.props.selectedDoc.documentType,
        hsmType: self.state.pdfCertificate.hsmType,
        certificate: self.state.pdfCertificate.value,
        certificateKeyId: self.state.pdfCertificate.keyId
      },
      timeout: 9600000
    };
    self.setState({pageState: 20, alert: null});
    requestK(params)
      .then(function (response) {
        self.setState({signedInfoList: response.data});
        self.signDoc();
      }).catch(function (error) {
      showAxiosError(error);
      self.setState({pageState: 40, disableButton: false});
    });
  }

  signDoc() {
    let approvalId = this.props.selectedDoc.id;
    let p = Promise.resolve([]);
    let signedDocument = [];

    this.setState({
      progressLength: this.state.signedInfoList.length,
      loading: true,
      pageState: 30
    });
    this.state.signedInfoList.forEach((element, index) => {
      p = p.then((response) => {
        this.setState({docName: element.docName});
        return this.axios.post("/signature/signedinfo?hsmtype=" + element.hsmType, {
          base64: true,
          pin: this.state.pin,
          signedInfo: element.signedInfo,
          certificate: {
            keyId: element.certificateKeyId
          }
        }).then((response) => {
          signedDocument.push({
            id: element.id,
            documentType: element.documentType,
            signature: response.data.signedDocument,
            approvalId: approvalId,
            pdfCertificate: JSON.stringify(this.state.pdfCertificate)
          });
          this.writeSignedDoc(signedDocument);
          signedDocument = [];
        });
      });
    });

    p.then((result) => {
      this.setState({
        loading: false,
        completed: true
      });
    }).catch(error => {
      this.setState({
        loading: false,
        completed: false,
        pageState: 0,
        disableButton: false,
        alert: <HsmErrorAlert error={error} onConfirm={() => this.setState({alert: null})}/>
      });
    });
  }

  writeSignedDoc(signedDocument) {
    let self = this;
    let params = {
      url: `/signer/writeOnePdfSignature`,
      method: "post",
      data: {documents: signedDocument, answer: self.props.answer},
      timeout: 9600000
    };
    requestK(params)
      .then(function (response) {
        self.setState((prevState, props) => {
          return {signedDocumentCount: prevState.signedDocumentCount + 1}
        });
      }).catch(function (error) {
      showAxiosError(error);
      self.setState({pageState: 40});
    });
  }

  renderSignButton() {
    return (
        <Button title="mzala ve onayla"
              disabled={!this.state.pin || this.state.disableButton || this.props.disabled}
              style={{
                paddingTop: '0',
                paddingBottom: '0',
                // width: '150px',
                height: '36px',
                marginTop: '9px',
                alignment: 'center',
                backgroundColor: '#57578d',
                position: 'absolute',
                right: '4em'
              }}
              onClick={() => {
                if (this.state.pdfCertificate && this.state.pdfCertificate.hsmType &&
                  this.state.pdfCertificate.value && this.state.pdfCertificate.keyId)
                  this.setState({
                    alert: (
                      <SweetAlert showCancel warning
                                  confirmBtnText="Evet"
                                  cancelBtnText="Hayr"
                                  confirmBtnBsStyle="success"
                                  cancelBtnBsStyle="default"
                                  title="Emin misiniz?"
                                  onConfirm={this.sign}
                                  onCancel={() => this.setState({alert: null})}
                      >
                        Dokman imzalanp Tbitak zaman damgas ile mhrlenecektir.
                      </SweetAlert>
                    )
                  });
                else
                  this.setState({
                    alert: <HsmErrorAlert error={this.state.certError}
                                          onConfirm={() => this.setState({alert: null})}/>
                  });
              }}>
                  <span style={{
                    position: 'relative',
                    left: '-22px',
                    display: 'inline-block',
                    padding: '11px 12px',
                    height: '36px',
                    background: 'rgba(0,0,0,0.15)',
                    borderRadius: '3px 0 0 3px'
                  }}>
                      <i className="fa fa-check"/>
                  </span>
        mzala ve onayla
      </Button>
    );
  }

  renderState() {
    let stateMap = this.stateMap[this.state.pageState];

    return <div className="row" style={{marginBottom: "15px"}}>
      <Col sm='12' md='6'>
        <div style={{display: "inline-block", marginRight: "8px"}}>
          {stateMap.wait && <ReactLoading type="balls" color="#2c2c2c" height={24} width={24}/>}
        </div>
        <div style={{
          display: "inline-block", marginRight: "8px", position: "relative", top: "-6px",
          color: stateMap.danger === true ? "trueColor" : "falseColor"
        }}>
          {stateMap.label}
        </div>
      </Col>
      <Col sm='12' md='6'>
        <div className='explanationOfSigning' style={{lineHeight: '1.2'}}>
          <i className="fas fa-exclamation-triangle" id="exclamationIcon" style={{paddingRight: '10px'}}/>
          <span style={{opacity: '0.9', fontSize: '12px', fontWeight: '500'}}>mzalama sresince ltfen ekran kapatmaynz ve bilgisayarnzn ekran koruyucusunun kapal olduundan emin olun.</span>
        </div>
      </Col>
    </div>
  }

  render() {
    let isAnswerableAndSignOnly = this.props.selectedDoc && this.props.operationType === "SIGN" && !!this.props.isReferencedUser && !!this.props.selectedDoc.answerable;

    return (
      <Row className="baseBackground" style={{margin: '0'}}>
        <Notify ref="notify"/>
        {this.state.alert}
        <div className="row satirBasi">
          <Col md="12">
            <Card className="card">
              <CardBody style={{position: 'relative', top: '2em'}}>
                {this.renderState()}
              </CardBody>
              <CardBody>
                {this.props.selectedDoc &&
                    <HsmCertificateViewer value={this.state.pdfCertificate}
                                          onChange={(val) => this.setState({pdfCertificate: val})}
                                          pinValueChange={(val) => this.setState({pin: val})}
                                          certificateError={(val) => this.setState({certError: val})}
                                          pdfName={this.state.docName ? this.state.docName : ""}
                                          progressLength={this.state.progressLength}
                                          signedDocumentCount={this.state.signedDocumentCount}
                                          displayCertificateMenu={this.state.pageState === 0 || this.state.pageState === 40}
                                          userId={this.props.currentUser && this.props.currentUser.id}/>}
              </CardBody>
              {this.props.selectedDoc.operationType === "SIGN" && this.props.selectedDoc && this.props.selectedDoc.documentType !== "BORDRO" && this.props.isReferencedUser !== undefined && !this.props.isReferencedUser && this.props.selectedDoc.answerIsRequired &&
                  <CardBody style={{position: 'relative', height: '7em', textAlign: 'right', right: '4em'}}>
                    {this.props.renderOpenAnswerBtn()}
                  </CardBody>}
              <CardBody style={{position: 'relative', height: isAnswerableAndSignOnly ? '13em' : '7em'}}>
                {isAnswerableAndSignOnly && <div style={{height: '36px'}}>{this.props.renderAnswerTextArea()}</div>}
                {this.renderSignButton()}
              </CardBody>
              {this.props.selectedDoc && this.props.operationType === "SIGN" && this.props.isReferencedUser !== undefined && !this.props.isReferencedUser &&
                  <CardBody style={{position: 'relative', height: '7em', textAlign: 'right', right: '4em'}}>
                    {this.props.renderDeclineBtn()}
                  </CardBody>}
            </Card>
          </Col>
        </div>
      </Row>

    );
  }

}

export default SignApproval;
