import React, {Component} from 'react';
import {deepCopyObject, showAxiosError} from "../../../helper/helper";
import Alert from 'react-s-alert';
import {requestK} from "../../../helper/APIUtils";
import {Modal} from "react-bootstrap";
import {Col, Row} from "reactstrap";
import SweetAlert from 'react-bootstrap-sweetalert';
import {ACCESS_TOKEN} from "../../../constants";

class ChangePassword extends Component {

    constructor(props) {
        super(props);
        this.state = {alert: null};
        this.handlePasswordChange = this.handlePasswordChange.bind(this);
        this.isEmployee = this.isEmployee.bind(this);
        this.onlyInputNumber = this.onlyInputNumber.bind(this);
    }

    isEmployee() {
        return this.props.currentUser && this.props.currentUser.employeeId;
    }

    confirmPasswordUpdate() {
        let passwordPayload = deepCopyObject(this.props.passwordPayload);
        // if(this.props.currentUser.employeeId!==null &&
        // 	this.props.currentUser.employeeId!==""){

        if (!this.props.policyCheck()) {
            this.setState({
                alert: (<SweetAlert warning
                                    title="Uyar!"
                                    confirmBtnText="Tamam"
                                    confirmBtnBsStyle="warning"
                                    onConfirm={() => {
                                        this.setState({alert: null})
                                    }}>
                    ifreniz ifre politikasna uygun deildir!
                </SweetAlert>)
            });
            this.props.resetPasswordPolicyStatus();
        } else {
            if (passwordPayload.currentPassword || this.props.impersonate) {
                if (passwordPayload.newPassword && passwordPayload.newPasswordCopy) {
                    if (passwordPayload.newPassword === passwordPayload.newPasswordCopy) {
                        this.handlePasswordChange();
                    } else {
                        Alert.error('Uyumsuz ifre', {
                            position: 'top-right',
                            effect: 'stackslide',
                            timeout: 5000
                        });
                    }
                } else {
                    Alert.error('Yeni ifre Giriniz', {
                        position: 'top-right',
                        effect: 'stackslide',
                        timeout: 5000
                    });
                }
            } else {
                Alert.error('Mevcut ifrenizi Giriniz', {
                    position: 'top-right',
                    effect: 'stackslide',
                    timeout: 5000
                });
            }
        }
    }

    handlePasswordChange() {
        let self = this;
        let data = deepCopyObject(self.props.passwordPayload);
        if (self.props.impersonate) {
            data.impersonate = deepCopyObject(self.props.impersonate);
        }
        let params = {
            url: `/user/updatePassword`,
            data: data,
            method: 'post'
        };
        requestK(params).then((response) => {
            sessionStorage.setItem(ACCESS_TOKEN, response.data.accessToken);
            this.setState({
                alert: (<SweetAlert success
                                    title="ifreniz Baaryla Gncellendi"
                                    confirmBtnText="Tamam"
                                    confirmBtnBsStyle="success"
                                    onConfirm={() => {
                                        this.setState({alert: null}, () => this.props.closePasswordModal());
                                        this.props.logOut();
                                    }}>
                        Eski ifrenizi, varsa dier harici yazlmlarda da gncellemeyi unutmaynz.
                    </SweetAlert>)
            });
        }).catch(error => {
            showAxiosError(error);
            {
                this.props.resetPasswordPolicyStatus()
            }
        });
    }

    showRelatedPassword(target) {
        let nextState = document.getElementById(target).getAttribute('type') === 'password' ? 'text' : 'password';
        document.getElementById(target).setAttribute('type', nextState);
    }

    onlyInputNumber = (event) => {
        if (this.props.currentUser.employeeId !== null &&
            this.props.currentUser.employeeId !== "") {
            let ch = String.fromCharCode(event.which);
            if (!(/[0-9]/.test(ch))) {
                event.preventDefault();
                event.target.setCustomValidity('ifrenizde harf bulunmamaldr!! Sadece rakamlardan olumaldr.');
                event.target.reportValidity();
            }
            if (event.target.value.length > 7) {
                event.preventDefault();
                event.target.setCustomValidity('ifreniz en fazla 8 rakamdan oluabilir!');
                event.target.reportValidity();
            }
        }
    }

    render() {
        return (
            <div>

                {this.state.alert && this.state.alert}

                <Modal show={this.props.isOpen}
                       animation={false}
                       toggle={() => this.props.closePasswordModal()}
                       modalTransition={{timeout: 700}}
                       dialogClassName="modal-90w"
                       backdropTransition={{timeout: 1300}}
                       aria-labelledby="example-custom-modal-styling-title"
                       backdrop="static">
                    <Modal.Header>ifre Deitir</Modal.Header>
                    <Modal.Body>
                        {!this.props.impersonate &&
                        <div style={{overflow: 'hidden'}}>
                            <Row>
                                <Col xs='12' sm='12' md='4' lg='4'>
                                    <label>Mevcut ifre</label>
                                </Col>

                                <Col xs='12' sm='12' md='8' lg='8'>
                                    {/*<div className={"form-group" + this.validateClass('tCurrentPassword')}>*/}
                                    <input className="form-control"
                                           type="password"
                                           name="currentPassword"
                                           id="tCurrentPassword"
                                           data-vlength="1,255"
                                           data-vdata="required"
                                        // onBlur={this.validateField}
                                           ref="tCurrentPassword"
                                           value={this.props.passwordPayload.currentPassword && this.props.passwordPayload.currentPassword}
                                           onChange={(e) => this.props.onChangePasswordPayload(e)}/>
                                    {/*{this.validateMessage('tCurrentPassword') !== "" &&*/}
                                    {/*<UncontrolledTooltip placement="right" target="tCurrentPassword" delay={0}>*/}
                                    {/*{this.validateMessage("tCurrentPassword")}*/}
                                    {/*</UncontrolledTooltip>}*/}
                                </Col>
                            </Row>
                            <i className="far fa-eye" onClick={() => this.showRelatedPassword('tCurrentPassword')}
                               style={{float: 'right', position: 'relative', marginRight: '5px', bottom: '24px'}}/>
                        </div>}

                        <div style={{overflow: 'hidden'}}>
                            <Row>
                                <Col xs='12' sm='12' md='4' lg='4'>
                                    <label className='col-form-label'>Yeni ifre</label>
                                </Col>

                                <Col xs='12' sm='12' md='8' lg='8'>
                                    {/*<div className={"form-group" + this.validateClass('tNewPassword')}>*/}
                                    <input className="form-control"
                                           type="password"
                                           name="newPassword"
                                           id="tNewPassword"
                                           data-vlength="1,255"
                                           data-vdata="required"
                                        // onBlur={this.validateField}
                                           ref="tNewPassword"
                                        // onKeyPress={this.onlyInputNumber}
                                           value={this.props.passwordPayload.newPassword && this.props.passwordPayload.newPassword}
                                           onChange={(e) => this.props.onChangePasswordPayload(e)}/>
                                    {/*{this.validateMessage('tNewPassword') !== "" &&*/}
                                    {/*<UncontrolledTooltip placement="right" target="tNewPassword" delay={0}>*/}
                                    {/*{this.validateMessage("tNewPassword")}*/}
                                    {/*</UncontrolledTooltip>}*/}
                                </Col>
                            </Row>
                            <i className="far fa-eye" onClick={() => this.showRelatedPassword('tNewPassword')}
                               style={{float: 'right', position: 'relative', marginRight: '5px', bottom: '24px'}}/>
                        </div>

                        <div style={{overflow: 'hidden'}}>
                            <Row>
                                <Col xs='12' sm='12' md='4' lg='4'>
                                    <label className='col-form-label'>Yeni ifre(Tekrar)</label>
                                </Col>

                                <Col xs='12' sm='12' md='8' lg='8'>
                                    {/*<div className={"form-group" + this.validateClass('tNewPasswordCopy')}>*/}
                                    <input className="form-control"
                                           type="password"
                                           name="newPasswordCopy"
                                           id="tNewPasswordCopy"
                                           data-vlength="1,255"
                                           data-vdata="required"
                                        // onBlur={this.validateField}
                                        //    onKeyPress={this.onlyInputNumber}
                                           ref="tNewPasswordCopy"
                                           value={this.props.passwordPayload.newPasswordCopy && this.props.passwordPayload.newPasswordCopy}
                                           onChange={(e) => this.props.onChangePasswordPayload(e)}/>
                                    {/*{this.validateMessage('tNewPasswordCopy') !== "" &&*/}
                                    {/*<UncontrolledTooltip placement="right" target="tNewPasswordCopy" delay={0}>*/}
                                    {/*{this.validateMessage("tNewPasswordCopy")}*/}
                                    {/*</UncontrolledTooltip>}*/}
                                </Col>
                            </Row>
                            <i className="far fa-eye" onClick={() => this.showRelatedPassword('tNewPasswordCopy')}
                               style={{float: 'right', position: 'relative', marginRight: '5px', bottom: '24px'}}/>
                        </div>

                    </Modal.Body>
                    <Modal.Footer>
                        <Col xs='6' sm='6' md='6' lg='6' style={{textAlign: 'center'}}>
                            <button type="button"
                                    style={{background: 'rgb(158, 158, 158)', padding: '11px 45px 11px 20px'}}
                                    className="TekBtn vazgec"
                                    onClick={() => this.props.closePasswordModal()}>ptal
                            </button>
                        </Col>

                        <Col xs='6' sm='6' md='6' lg='6' style={{textAlign: 'center'}}>
                            <button type="button"
                                    style={{
                                        background: 'linear-gradient(rgba(159, 208, 55, 0.6), #9fd037)',
                                        padding: '11px 45px 11px 20px'
                                    }}
                                    className="TekBtn kaydet"
                                    onClick={() => this.confirmPasswordUpdate()}>Onayla
                            </button>
                        </Col>
                    </Modal.Footer>
                </Modal>
            </div>
        )
    }
}

export default ChangePassword;