import React, {Component} from 'react';
import {VirtualHost} from "../constants"
import {requestP} from "../helper/APIUtils";

class Footer extends Component {

  constructor(props) {
    super(props);
    this.state = {
      version: ''
    };
  }

  componentDidMount() {
    requestP({url: "/info/version", method: "get"})
      .then(response => this.setState({version: response.data}))
      .catch(error=> this.setState({version: "0.1"}));
  }

  render(){
		return(
            <footer className="footer">
                <div className="container-fluid">
                    <div className="copyright">
                      {VirtualHost.conf.footer} - {this.state.version}
                    </div>
                </div>
            </footer>
		);
	}
}

export default Footer;