import React, {Component} from "react";
import {Modal, ModalBody, ModalHeader} from "reactstrap";
import DocViewer, {
    BMPRenderer,
    HTMLRenderer,
    JPGRenderer,
    MSGRenderer,
    PNGRenderer,
    TIFFRenderer,
    TXTRenderer,
} from "react-doc-viewer";
import {PDFProvider} from "react-doc-viewer/build/plugins/pdf/state";
import PDFPages from "react-doc-viewer/build/plugins/pdf/components/pages/PDFPages";
import {DocRenderer} from "react-doc-viewer";
import {Container} from "react-bootstrap";
import styled from "styled-components";
import PDFControls from "react-doc-viewer/build/plugins/pdf/components/PDFControls";
import Notify from "react-notification-alert";
import SurveyAttachment from "../surveyAttachment/SurveyAttachment";
const PDFRenderer: DocRenderer = ({mainState}) => {
    mainState.currentDocument.uri = "Document";
    return (
        <PDFProvider mainState={mainState}>
            <Container id="pdf-renderer" data-testid="pdf-renderer">
                <PDFControls/>
                <PDFPages/>
            </Container>
        </PDFProvider>
    );
};
PDFRenderer.fileTypes = ["pdf", "application/pdf"];
PDFRenderer.weight = 0;

const MSDocRenderer: DocRenderer = ({mainState: {currentDocument}}) => {
    let arr = currentDocument.uri.split(',');
    let name = arr[0].split(';')[1].replace("name=", "");
    return (
        <ContainerMSDOC id="msdoc-renderer">
            <p>Bu bir Microsoft Ofis dosyasdr. Dosya ieriini grntlemek istiyorsanz dosyay aadaki linkten
                indirip Microsoft Office uygulamas ile anz.</p>
            <p><a href={currentDocument.uri} download={name}>ndir</a></p>
        </ContainerMSDOC>
    );

};


const MSDocFTMaps = {
    doc: ["doc", "application/msword"],
    docx: [
        "docx",
        "application/vnd.openxmlformats-officedocument.wordprocessingml.document",
    ],
    xls: ["xls", "application/vnd.ms-excel"],
    xlsx: [
        "xlsx",
        "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
    ],
    ppt: ["ppt", "application/vnd.ms-powerpoint"],
    pptx: [
        "pptx",
        "application/vnd.openxmlformats-officedocument.presentationml.presentation",
    ],
};

MSDocRenderer.fileTypes = [
    ...MSDocFTMaps.doc,
    ...MSDocFTMaps.docx,
    ...MSDocFTMaps.xls,
    ...MSDocFTMaps.xlsx,
    ...MSDocFTMaps.ppt,
    ...MSDocFTMaps.pptx,
];
MSDocRenderer.weight = 0;
MSDocRenderer.fileLoader = ({ fileLoaderComplete }) => fileLoaderComplete();
const ContainerMSDOC = styled.div`
  width: 100%;
  height: 100%;
`;

class DocumentViewer extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <div size="lg">
                <Modal isOpen={true} size="lg">
                    <ModalHeader
                        toggle={() => this.props.closeViewer()}>Dokman Grntleyici
                    </ModalHeader>
                    <ModalBody style={{overflow: 'visible'}}>
                        <div className="card">
                            <div className="card-body" style={{margin: '0px 16px 16px 16px',height:'700px'}}>
                                <DocViewer
                                    documents={[this.props.attachment.includes("|")?{uri:"data:text/plain;base64,RG9rw7xtYW4gw6dva2x1IG9sZHXEn3UgacOnaW4gZ8O2csO8bnTDvGxlbmVtaXlvci4="}:{uri:this.props.attachment}]}
                                    pluginRenderers={[PDFRenderer, BMPRenderer,
                                        HTMLRenderer,
                                        JPGRenderer,
                                        MSDocRenderer,
                                        MSGRenderer,
                                        PNGRenderer,
                                        TIFFRenderer,
                                        TXTRenderer]} config={{
                                    header: {
                                        disableHeader: true,
                                        disableFileName: true
                                    }
                                }}
                                    theme={{
                                        primary: "#5296d8",
                                        secondary: "#ffffff",
                                        tertiary: "#5296d899",
                                        text_primary: "#ffffff",
                                        text_secondary: "#5296d8",
                                        text_tertiary: "#00000099",
                                        disableThemeScrollbar: false
                                    }}
                                />
                            </div>
                        </div>
                    </ModalBody>
                </Modal>
            </div>
        )
    }
}

export default DocumentViewer;