import React, {Component, PropTypes} from 'react'
import {Modal} from "react-bootstrap";

class MediaViewer extends Component {
	constructor(props) {
		super(props);
		this.state = {
			changeSize: false
		};
		this.isOpen = this.isOpen.bind(this);
		this.closeModal = this.closeModal.bind(this);
		this.openModal = this.openModal.bind(this);
		this.resize = this.resize.bind(this);

	}

	isOpen() {
		this.props.listener();
	}

	openModal() {
		this.setState({modal: true});
	}

	closeModal() {
		this.setState({modal: false});
	}

	resize() {
		if (this.state.changeSize == false) {
			this.setState({changeSize: true});
		}
		else {
			this.setState({changeSize: false});
		}

	}

	render() {

		return (

			<div>
				<Modal show={this.props.modal}
					   animation={false}
					   toggle={this.isOpen}
					   modalTransition={{timeout: 700}}
					   dialogClassName="modal-90w"
					   size={this.state.changeSize ? "xxl" : "xl"}
					   backdropTransition={{timeout: 1300}}
					   aria-labelledby="example-custom-modal-styling-title"
					   backdrop="static">
					<Modal.Header>
						<Modal.Title id="example-custom-modal-styling-title">
							<div className=".col-12 .col-md-8">
								<div className=".col-6 .col-md-4" style={{top: "0"}}>
									<p style={{display: "inline", fontWeight: 700}}>{this.props.title || ""}</p>

									<button type="button"
											style={this.state.changeSize ? {zIndex: "1400", left: "95%", top: "3%"} : {zIndex: "1400", left: "92%", top: "4%"}}
											onClick={this.resize}
											className="close pull-right"
											title={this.state.changeSize ? "Daralt" : "Genilet"}
											aria-label="Daralt">
										<span aria-hidden="true"><i className={this.state.changeSize ? "fas fa-compress" : "fas fa-expand"}
																	style={{fontSize: "47%"}}/></span>
									</button>
									<button type="button"
											style={this.state.changeSize ? {zIndex: "1400", left: "97%", top: "4%"} : {zIndex: "1400", left: "95%", top: "5%"}}
											onClick={this.isOpen}
											className="close pull-right"
											title="Kapat"
											aria-label="Kapat">
										<span aria-hidden="true"></span>
									</button>
								</div>
							</div>
						</Modal.Title>

					</Modal.Header>
					<Modal.Body style={this.state.changeSize ? {padding: "0", left: "1px"} : {padding: "0", height: "611px"}}>
						<div className="col-sm-12" style={{marginTop: "2px", marginBottom: "2px", top: 0, left: 0}}>
							{this.props.url &&
							<iframe src={this.props.url} style={{top: 0, left: 0, width: "100%", height: (this.state.changeSize ? "677px" : "600px")}}/>}
							{!this.props.url && "Ykleniyor..."}
						</div>

					</Modal.Body>
				</Modal>
			</div>
		);
	}

}

export default MediaViewer;