import React, {Component} from 'react';
import 'react-table/react-table.css';
import {Nav} from 'reactstrap';
import 'moment/locale/tr';
import {renewListForPrivileges,} from "../common/PeykHelper";
import {requestK} from "../../../helper/APIUtils";
import {showAxiosError} from "../../../helper/helper";
import Table from "../../../components/Table/Table";
import Switch from "react-switch";
import DetailedSearch from "../../../components/DetailedSearch/DetailedSearch";

class SmsCreditAllCustomerPage extends Component {

    constructor(props) {
        super(props);
        this.state = {
            alert: null,
            loading: false,
            valid: {},
            manual: false,
            list: [],
            total: 0,
            searching: false,
            searchQuery: undefined,
            page: 0,
            size: 10,
            isKepOwner: false,
            options: [],
            types: [],
            isLoading: false,
            lowCredit: true,
            content: [],
        };
        this.fetchData = this.fetchData.bind(this);
        this.getTrStyles = this.getTrStyles.bind(this);
        this.clearChild = this.clearChild.bind(this);
        this.onChangeIsCreditTransfer = this.onChangeIsCreditTransfer.bind(this);
        this.handleSearch = this.handleSearch.bind(this);
    }

    componentWillMount() {
    }

    componentDidMount() {

    }

    clearChild() {
        let self = this;
        self.child.current.clear();
    }

    fetchData(accessor, direction, page, size) {
        let self = this;
        let sortStr = accessor !== undefined ? ('&sort=' + accessor + ',' + direction) : '&sort=customerName,asc';
        let searchQuery = !self.state.searchQuery ? '' : '&' + self.state.searchQuery;
        let params = {
            url: '/credit/getCreditAllCustomers?lowCredit=' + self.state.lowCredit + '&page=' + page + '&size=' + size + searchQuery + sortStr,
            method: "get",
        };
        self.setState({loading: true, searching: true, manual: self.manual === true ? false : self.manual});
        requestK(params)
            .then(function (response) {
            self.setState({
                page: page,
                size: size,
                total: response.data.totalElements,
                loading: false,
                searching: false,
                content: response.data.content,
            });
        }).catch(function (error) {
            self.setState({loading: false, searching: false});
            showAxiosError(error);
        });
    }

    onChangeIsCreditTransfer(e) {
        const isChecked = e;
        this.setState({ lowCredit: isChecked }, () => this.fetchData(this.state.accessor, this.state.direction, 0, this.state.size));
    }

    getTrStyles(item) {
        return (item.statusType && item.statusType !== 'ACTIVE') ? {
            backgroundColor: 'lightgray',
            webkitTextFillColor: 'darkgray'
        } : {};
    }

    hasGrant = (grant) => {
        return this.props.hasGrant(grant);
    };

    handleSearch(searchQuery) {
        this.setState({
            searchQuery,
            searching: true,
            // selection: [],
        }, () => this.fetchData(this.state.accessor, this.state.direction, 0, this.state.size))
    }
    renderTable() {
        let columnList = [
            {
                Header: "Mteri Ad",
                accessor: "customerName",
                sortable: false,
            },
            {
                Header: "Gncel Bakiye",
                accessor: "remain",
                sortable: false,

            },
            {
                Header: "Yllk Yklenen Kredi",
                accessor: "creditPredefined",
                sortable: false,

            },
            {
                Header: "Tanmlanan",
                accessor: "predefined",
                sortable: false
            },
            {
                Header: "Satn Alnan",
                accessor: "purchased",
                sortable: false
            },
            {
                Header: "Kredi Tolerans",
                accessor: "creditTolerance",
                sortable: false
            },
        ];
        let columns = renewListForPrivileges(this, columnList, "accessor", this.state.isKepOwner);
        return <Table columns={columns}
                      content={this.state.content}
                      loading={this.state.loading}
                      loadingText="Kaytlar getiriliyor..."
                      total={this.state.total}
                      page={this.state.page}
                      size={this.state.size}
                      manual={this.state.manual}
                      options={["5", "10", "20", "50", "100"]}
                      getTrStyles={this.getTrStyles}
                      fetchData={this.fetchData}/>
    }

    render() {
        const fieldList = [
            {
                label: "Mteri Ad",
                accessor: "customerName",
                type: "text",
                operator: "=",
            },
            {
                label: "Gncel Bakiye",
                accessor: "remain",
                type: "numericText",
                operator: "=",
            },
            {
                label: "Yllk Yklenen Kredi",
                accessor: "creditPredefined",
                type: "numericText",
                operator: "=",
            },
            {
                label: "Tanmlanan",
                accessor: "predefined",
                type: "numericText",
                operator: "=",
            },
            {
                label: "Satn Alnan",
                accessor: "purchased",
                type: "numericText",
                operator: "=",
            },
            {
                label: "Kredi Tolerans",
                accessor: "creditTolerance",
                type: "numericText",
                operator: "=",
            },
            {
                label: "Kredisi Dk Olan Firmalar Gster",
                accessor: "isLowCredit",
                type: "boolean",
                operator: "=",
            }
    ];
        return <div className="row baseBackground">
            <Nav className="topbar" style={{paddingLeft: '14px', height: '53px'}}>
                <p style={{marginTop: '18px'}}>FRMA SMS KONTR ADET BLGS</p>
            </Nav>
            <div className="row">
                <div className="col-sm-12">
                    <div className="col-sm-12 satirBasi">
                        <div className="card-header">
                            <div className="col-sm-4">
                                <h4>Kredisi Dk Olan Firmalar Gster </h4>
                                <Switch
                                    checked={this.state.lowCredit}
                                    onChange={(e) => this.onChangeIsCreditTransfer(e)}
                                />
                            </div>
                        </div>
                        <br></br>
                        {/*<div className="card-body">*/}
                        {/*    <DetailedSearch fields={fieldList}*/}
                        {/*                    loading={this.state.searching}*/}
                        {/*                    onSearch={this.handleSearch}/>*/}
                        {/*</div>*/}
                        <div className="card-body">
                            {this.renderTable()}
                        </div>
                    </div>
                </div>
            </div>
            {this.state.alert}
        </div>
    }
}

export default SmsCreditAllCustomerPage;
