import React, {Component} from 'react';
import {Modal, ModalBody, ModalHeader, Nav} from 'reactstrap';
import {requestK} from "../../../helper/APIUtils";
import {create_UUID, showAxiosError} from "../../../helper/helper";
import Table from "../../../components/Table/Table";
import Alert from "react-s-alert";
import DetailedSearch from "../../../components/DetailedSearch/DetailedSearch";
import SweetAlert from "react-bootstrap-sweetalert";
import Tree from "rc-tree";
import ReactLoading from "react-loading";
import NavBarKS from "../../../components/NavBarKS";
import StickButton from "../../../components/StickButton";
import {departmentData, getDocumentState, personalInfoStatus, statusTypeFilterData} from "../common/PeykHelper";
import MiniButton from "../../../components/MiniButton";
import DropdownCheck from "../../../components/Table/DropdownCheck";
import TextOverFlowTooltip from "../../../components/TextOverFlowTooltip";
import "./contextmenu.css";
import SurveyAttachmentModal from "../surveyAttachment/SurveyAttachmentModal";
import {RiCloseFill, RiDeleteBin6Line} from "react-icons/ri";
import {ContextMenu, ContextMenuTrigger, MenuItem} from "react-contextmenu";
import Swal from "sweetalert2";
import SurveyFormHR from "../surveys/SurveyFormForHR";
import ApproveDocumentViewer from "../approval/helper/ApproveDocumentViewer";

const icon = <img alt={"icon"}
                  src="data:image/png;base64,"/>

export default class PersonalInfos extends Component {

    statesEnum = {
        SAVED: 'KAYDEDLD',
        INAPPROVABLE: 'ONAYDA BEKLYOR',
        APPROVED: 'ONAYLANDI',
        REJECTED: 'REDDEDLD',
        REFORMATION: 'YENDEN DZENLE'
    };

    constructor(props) {
        super(props);
        this.state = {
            selectedNode: null,
            isFilesLoading: false,
            isFileLoading: false,
            isLoading:false,
            anyFileSelected: false,
            isKepOwner: false,
            searching: false,
            searchQuery: undefined,
            content: [],
            total: 0,
            page: 0,
            size: 10,
            loading: false,
            manual: false,
            formModal: false,
            selectedEmployee: null,
            employeeFiles: [],
            selectedPerInfoForm: null,
            selectedPerInfoFormResponse: null,
            selectedAttachment: "",
            menuIsOpen: false,
            viewer: false,
            attachments: [],
            selectedRowNum: -1,
            defaultSelectedKeys: [],
            isIadeButtonAktif: false,
            personalInfoOptions: [],
            formOptions: [],
            selection: [],
            selectedAll: false,
            totalIdList: [],
            departmentList: [],
            openingModal: false,
            openModal: false,
            surveyNamesList: [],
            selectedKey: "",
            displayDocuments: false,
            openDocumentViewer: false,
            employeeName: null,
            selectedRowColor: "#FFFFFF",
            openFormModal: false
        };
        this.fetchData = this.fetchData.bind(this);
        this.onFocus = this.onFocus.bind(this);
        this.handleSelectedAttachmentChange = this.handleSelectedAttachmentChange.bind(this);
        this.cancelShowFormModal = this.cancelShowFormModal.bind(this);
        this.onClickTable = this.onClickTable.bind(this);
        this.getPersonelFiles = this.getPersonelFiles.bind(this);
        this.downloadMedia = this.downloadMedia.bind(this);
        this.getPersonalInfos = this.getPersonalInfos.bind(this);
        this.uploadRow = this.uploadRow.bind(this);
        this.getPersonelFilesById = this.getPersonelFilesById.bind(this);
        this.openFormModal = this.openFormModal.bind(this);
        this.closeFormModal = this.closeFormModal.bind(this);
    }

    dataURLtoFile(dataurl, filename) {

        let arr = dataurl.split(','), mime = arr[0].match(/:(.*?);/)[1], bstr = atob(arr[1]), n = bstr.length,
            u8arr = new Uint8Array(n);

        while (n--) {
            u8arr[n] = bstr.charCodeAt(n);
        }

        return new File([u8arr], filename, {type: mime});
    }

    componentDidMount() {
        this.totalOptions();
        this.readDepartmentAll();
    }

    openFormModal(item, rowNum) {
        this.setState({
            openFormModal: true,
            selectedEmployee: item,
            selectedRowNum: rowNum,
            selectedRowColor: "#39C0ED"
        });
    }

    closeFormModal() {
        this.setState({openFormModal: false});
    }

    totalOptions = async () => {
        await this.getPersonalInfos();
        await this.getFormTemplateAll();

    }

    onFocus = () => {
        this.setState({menuIsOpen: true});
    };

    onClickTable(row, rowNum) {
        let self = this;
        let employeeName = `${row.name} ${row.surname} `;
        if (!self.state.isFilesLoading) {
            self.setState({
                selectedAttachment: null,
                isIadeButtonAktif: false,
                displayDocuments: true,
                employeeName,
                selectedRowNum: rowNum,
                selectedRowColor: "#00B74A"
            });
            self.getPersonelFiles(row, rowNum);
        }
    }

    getPersonelFiles = (row, rowNum) => {
        this.setState({isFilesLoading: true, employeeFiles: []});
        requestK({
            url: `/personnel-info/getPersonalInfoFileTree`, method: "post", data: row.id
        }).then((response) => {
            this.addIcons(response.data);
            this.setState({
                isFilesLoading: false,
                employeeFiles: [response.data],
                selectedEmployee: row,
            });
        }).catch(() => {
            this.setState({isFilesLoading: false, employeeFiles: []});
            showAxiosError({message: "u an ileminiz gerekletirilemiyor."});
        });
    };

    getPersonelFilesById = () => {
        let self = this;
        self.setState({isFilesLoading: true, employeeFiles: []});
        requestK({
            url: `/personnel-info/getPersonalInfoFileTree`, method: "post", data: self.state.selectedEmployee.id
        }).then((response) => {
            self.addIcons(response.data);
            self.setState({
                isFilesLoading: false, employeeFiles: [response.data],
            });
        }).catch(() => {
            self.setState({isFilesLoading: false, employeeFiles: []});
            showAxiosError({message: "u an ileminiz gerekletirilemiyor."});
        });
    };

    readDepartmentAll = () => {
        let self = this;
        let params = {
            url: '/department/readDepartment', method: "get",
        };
        requestK(params)
            .then(function (response) {
                let departmentList = response.data;
                self.setState({departmentList});
            }).catch(function (error) {
            showAxiosError(error);
        });
    };

    addIcons = (node) => {
        if (node.children.length === 0 && node.isAttachment) {
            if (node.attachmentName !== "" && !node.key.includes("form")) {
                node.title = (<span>
              {node.title + " "}<i className="fas fa-check" style={{color: "green"}}/>
          </span>)
            } else if (node.attachmentName !== "" && node.key.includes("form")) {
                node.title = (<span>
              {node.title + "-" + getDocumentState(node.state)}
          </span>)
            }
        } else {
            if (node.icon === 'icon') {
                node.icon = icon;
            }
            node.children.forEach(n => this.addIcons(n))
        }
    }

    handleSelectedAttachmentChange = (selectedAttachment) => {
        let self = this;
        self.setState({selectedAttachment: selectedAttachment, menuIsOpen: false});
    };

    componentWillMount() {
        this.setState({isKepOwner: this.props.isKepOwner});
    }

    cancelShowFormModal() {
        this.setState({
            formModal: false
        })
    }

    resendPersonalInfo(id, comment) {
        let self = this;
        if (comment.length === 0 || comment === '') {
            Alert.error('Aklama girmeden gnderim yapamazsnz!', {
                position: 'top-right', effect: 'stackslide', timeout: 5000
            });
        } else {
            let idList = [];
            idList.push(parseInt(id));
            let params = {
                url: `/survey/resendPersonalInfo`, method: "post", data: {
                    recordId: idList, comment: comment
                }
            };
            self.setState({loading: true, disableSendBtn: true, alert: null});
            requestK(params)
                .then(function (response) {
                    if (response.data.result === true) {
                        Alert.success(response.data.message, {
                            position: 'top-right',
                            effect: 'stackslide',
                            timeout: 2000
                        });
                    } else if (response.data.result === false) {
                        Alert.error(response.data.message, {
                            position: 'top-right',
                            effect: 'stackslide',
                            timeout: 2000
                        });
                    }
                    self.onClickTable(self.state.selectedEmployee, self.state.selectedRowNum);
                    self.setState({loading: false, disableSendBtn: false, manual: true});
                })
                .catch(function () {
                    showAxiosError({message: "u an ileminiz gerekletirilemiyor."});
                    self.setState({disableSendBtn: false, loading: false});
                });
        }
    }

    fetchData(accessor, direction, page, size) {
        let self = this;
        let sortStr = accessor !== undefined ? ('&sort=' + accessor + ',' + direction) : '&sort=id,desc';
        let searchQuery = !self.state.searchQuery ? '' : '&' + self.state.searchQuery;
        let params = {
            url: '/survey/readPersonalList?page=' + page + '&size=' + size + searchQuery + ((searchQuery !== '' && searchQuery.toString().includes("statusTypeFile")) ? '' : '&statusTypeFile=') + ((searchQuery !== '' && searchQuery.toString().includes("personalInfos")) ? '' : '&personalInfos=') + sortStr,
            method: "get"
        };
        self.setState({
            loading: true,
            searching: true,
            manual: self.manual === true ? false : self.manual,
            selectedEmployee: null,
            employeeFiles: []
        });
        self.props.reload(true);
        requestK(params)
            .then(function (response) {
                self.setState({
                    page: page,
                    size: size,
                    total: (response.data && response.data.page) ? response.data.page.totalElements : 0,
                    content: (response.data && response.data.page) ? response.data.page.content : [],
                    totalIdList: response.data ? response.data.selectedIdList : [],
                    loading: false,
                    searching: false
                });
                self.props.reload(false);
            })
            .catch(function (error) {
                self.setState({loading: false, searching: false});
                self.props.reload(false);
                showAxiosError(error);
            });
    }

    handleSearch = (searchQuery) => {
        this.setState({
            searchQuery, searching: true, selection: [],
        }, () => {
            if (this.state.searchQuery && !this.state.searchQuery.toString().includes("personalInfos") && this.state.searchQuery.toString().includes("statusTypeFile") && this.state.searchQuery !== "statusTypeFile=") {
                Alert.warning('ltfen, sorgu iin nce bir zlk dosyas seiniz.', {
                    position: 'top-right', effect: 'stackslide', timeout: 5000
                });
            } else {
                this.fetchData(this.state.accessor, this.state.direction, 0, this.state.size)
            }
        })
    };

    setMediaViewer = () => {
        this.setState({url: null, mediaModal: false});
    }

    arrangeDataSet = () => {
        let dataSet = {};
        dataSet.sheetName = "zlk_Raporu"
        dataSet.uuid = create_UUID();
        dataSet.type = "excel";
        return dataSet;
    }

    downloadMedia() {
        let self = this;
        self.setState({isFilesLoading: true});
        let data = self.arrangeDataSet();
        let params = {
            url: `/survey/downloadReport`, method: "post", data: data
        };
        requestK(params)
            .then(function () {
                self.setState({isFilesLoading: false});
                Alert.success("ndirme talebiniz ileme alnd. 'Bekleyen ndirmeler'den' takip edebilirsiniz.", {
                    position: 'top-right', effect: 'stackslide', timeout: 8000
                });
            }).catch(function (error) {
            self.setState({isFilesLoading: false});
            showAxiosError(error);
        });
    }

    async getPersonalInfos() {
        return new Promise(resolve => {
            let self = this;
            let params = {
                url: `/survey/getPersonalInfosTemplate`, method: "post"
            };
            requestK(params)
                .then(function (response) {
                    let optionItems = [];
                    response.data.map((personalInfo) => {
                        if (personalInfo.personalInfo === true) {
                            optionItems.push({value: personalInfo.id + "-1", label: personalInfo.name + " - zlk"});
                        } else {
                            optionItems.push({value: personalInfo.id + "-1", label: personalInfo.name + " - Talep"});
                        }

                    });
                    self.setState({personalInfoOptions: optionItems});
                    resolve(true);
                }).catch(function (error) {
                showAxiosError(error);
            });
        })
    }

    async getFormTemplateAll() {
        let self = this;
        let option = self.state.personalInfoOptions;
        let params = {
            url: `/formtemplate/getFormTemplateAll`, method: "post"
        };
        requestK(params)
            .then(function (response) {

                response.data.map((form) => {
                    option.push({value: form.id + "-0", label: form.formName});
                });
                self.setState({formOptions: option});
            }).catch(function (error) {
            showAxiosError(error);
        });
    }

    arrangeDataSetForSelected = (list) => {
        let dataSet = {};
        dataSet.sheetName = "ozluk_dosyalari"
        dataSet.uuid = create_UUID();
        dataSet.list = list;
        dataSet.type = "zip";
        return dataSet;

    }

    downloadSelected() {
        this.showAlertBeforeDownload(this.state.selection);
    }

    downloadRow(row, rowNum) {
        let self = this;
        let list = [];
        list.push(row);
        self.setState({selectedRowNum: rowNum, selectedRowColor: "#FFA900"}, () => self.showAlertBeforeDownload(list));
    }

    uploadRow(item, rowNum) {
        let self = this;
        if (!self.state.isLoading) {
            self.setState({selectedEmployee: item, selectedRowNum: rowNum, selectedRowColor: "#F93154"});
            self.getPersonalInfoNotAttachedFileList(item.employeeUserId);
        }
    }

    getPersonalInfoNotAttachedFileList(userId) {
        let self = this;
        let params = {
            url: `/survey/getPersonalInfoNotAttachedFileList`, method: "post", data: userId
        };
        self.setState({openingModal: true, isLoading:true});
        requestK(params)
            .then(function (response) {
                self.setState({surveyNamesList: response.data, openModal: !self.state.openModal, openingModal: false, isLoading:false})
            }).catch(function (error) {
            self.setState({openingModal: false, isLoading:false});
            showAxiosError(error);
        });
    }

    setModal = () => {
        let self = this;
        self.setState({openModal: !self.state.openModal});
        if (!self.state.openModal) {
            self.setState({selectedAttachment: ""});
        }
    }
    showAlertBeforeDownload = (list) => {
        this.setState({
            alert: (<SweetAlert
                    showCancel
                    warning
                    confirmBtnText="Tamam"
                    cancelBtnText="Vazge"
                    confirmBtnBsStyle="success"
                    cancelBtnBsStyle="default"
                    title="Uyar"
                    onConfirm={() => this.downloadEmployeeDocument(list)}
                    onCancel={() => this.setState({alert: null})}
                >
                    <label
                        style={{fontSize: '15px'}}>Sadece onaylanm
                        statdeki dokmanlar
                        indirilecektir.</label>
                </SweetAlert>

            )
        });
    }
    downloadEmployeeDocument = (list) => {
        let self = this;
        let data = self.arrangeDataSetForSelected(list);
        let params = {
            url: `/personnel-info/downloadAllContent`, responseType: 'arraybuffer', method: "post", data: data
        };
        self.setState({disableDownload: true, alert: false});
        requestK(params)
            .then(function () {
                Alert.success("ndirme talebiniz ileme alnd. 'Bekleyen ndirmeler'den' takip edebilirsiniz.", {
                    position: 'top-right', effect: 'stackslide', timeout: 8000
                });
                self.setState({disableDownload: false});
            }).catch(function (error) {
            showAxiosError(error);
        });
    }

    toggleCheckboxChange = (e, row, id) => {
        let self = this;
        let checked = e.target.checked;
        let items = self.state.content;
        let selection = self.state.selection;
        if (row === "all") {
            let selection = [];
            if (checked) {
                items.forEach(item => {
                    selection.push(item[id]);
                });
            }
            self.setState({selection: selection, selectedAll: checked});
        } else {
            let index = selection.indexOf(row[id]);
            if (index >= 0) {
                selection = [...selection.slice(0, index), ...selection.slice(index + 1)];
            } else {
                selection.push(row[id]);
            }
            self.setState({
                selection: selection,
                selectedAll: ((selection.length > 0) ? (selection.length === self.state.content.length) : false)
            });
        }
    }

    isSelected = (id) => {
        return (this.state.selection.indexOf(id) !== -1);
    }

    deleteAttachment = () => {
        let self = this;
        if (self.state.selectedNode?.attachmentName && self.state.selectedNode?.createdBy !== self.props.currentUser.id) {
            Swal({
                title: 'Uyar! ',
                text: ("Sadece kendi eklediiniz dosyalar silebilirsiniz.!"),
                confirmButtonText: 'Tamam'
            }).then(() => {
            });
            return;
        }
        Swal({
            title: 'Uyar! ',
            text: (self.state.selectedNode.attachmentName + " isimli dokman silmek istiyor musunuz?"),
            cancelButtonText: 'Hayr',
            cancelButtonColor: '#d33',
            showCancelButton: true,
            confirmButtonText: 'Evet'
        }).then((result) => {
            if (result.value === true) {
                self.setState({isFilesLoading: true,selectedAttachment: ""});
                let params = {
                    url: `/survey/deleteAttachment`, method: "post", data: {
                        'surveyResponseId': self.state.selectedNode.surveyResponseId,
                        'attachmentName': self.state.selectedNode.attachmentName
                    }
                }
                requestK(params)
                    .then(function () {
                        Swal({
                            title: 'Baarl ! ',
                            text: ("Silme ilemi baaryla gerekletirildi."),
                            type: 'success',
                            confirmButtonText: 'Tamam'
                        }).then(() => {
                            if (self.state.selectedEmployee !== null) self.getPersonelFilesById();
                        });
                    }).catch(function (error) {
                    self.setState({isFilesLoading: false});
                    showAxiosError(error);
                });
            }
        });
    };
    deleteAllAttachment = () => {
        let self = this;
        if (self.state.selectedNode?.attachmentName && self.state.selectedNode?.createdBy !== self.props.currentUser.id) {
            Swal({
                title: 'Uyar! ',
                text: ("Sadece kendi eklediiniz dosyalar silebilirsiniz.!"),
                confirmButtonText: 'Tamam'
            }).then(() => {
            });
            return;
        }
        Swal({
            title: 'Uyar! ',
            text: (self.state.selectedNode?.attachmentName + " altndaki tm dokmanlar silmek istiyor musunuz?"),
            cancelButtonText: 'Hayr',
            cancelButtonColor: '#d33',
            showCancelButton: true,
            confirmButtonText: 'Evet'
        }).then((result) => {
            if (result.value === true) {
                self.setState({isFilesLoading: true, selectedAttachment: ""});
                let params = {
                    url: `/survey/deleteAllAttachment`, method: "post", data: {
                        'surveyResponseId': self.state.selectedNode.surveyResponseId,
                        'attachmentName': self.state.selectedNode.attachmentName
                    }
                }
                requestK(params)
                    .then(function () {
                        self.setState({isFilesLoading: false});
                        Swal({
                            title: 'Baarl ! ',
                            text: ("Silme ilemi baaryla gerekletirildi."),
                            type: 'success',
                            confirmButtonText: 'Tamam'
                        }).then(() => {
                            self.setState({isFilesLoading: false});
                            if (self.state.selectedEmployee !== null) self.getPersonelFilesById();
                        });
                    }).catch(function (error) {
                    self.setState({isFilesLoading: false});
                    showAxiosError(error);
                });
            }
        });
    };

    render() {
        const fields = [
            {
                label: "Ad",
                accessor: "name",
                type: "text",
                operator: "="
            },
            {
                label: "Soyad",
                accessor: "surname",
                type: "text",
                operator: "="
            },
            {
                label: "TCKN",
                accessor: "tckn",
                type: "numericText",
                operator: "="
            },
            {
                label: "Departman",
                accessor: "department_name",
                type: "select",
                operator: "=",
                selectData: departmentData(this.state.departmentList),
            },
            {
                label: "Gnderim lemleri",
                accessor: "personalInfos",
                type: "select",
                operator: "=",
                selectData: this.state.personalInfoOptions
            },
            {
                label: "Dosya Durumu",
                accessor: "statusTypeFile",
                type: "select",
                operator: "=",
                selectData: personalInfoStatus()
            },
            {
                label: "alan Durumu",
                accessor: "statusType",
                type: "select",
                operator: "=",
                selectData: statusTypeFilterData()
            },
            {
                label: "e Balama Tarihi (Balang)",
                accessor: "min_employmentStartDate", //min_xxx MUST ALWAYS come before the max_xxx, if it exists.
                type: "date",
                operator: ">=",
            },
            {
                label: "e Balama Tarihi (Biti)",
                accessor: "max_employmentStartDate",
                type: "date",
                operator: "<=",
            }
        ];
        let pageIdList = [];
        if (this.state.content && this.state.content instanceof Array) {
            this.state.content.map(e => {
                pageIdList.push(e.id);
            });
        }
        const columns = [

            {
                Header: (this.state.content && <DropdownCheck selectionIdList={this.state.selection}
                                                              totalIdList={this.state.totalIdList}
                                                              pageIdList={pageIdList}
                                                              onChange={e => this.setState({selection: e})}/>),
                accessor: "id",
                selectType: "checkbox",
                sortable: false,
                width: "3%",
                Cell: (row, index) => (this.state.content && <input type="checkbox" checked={this.isSelected(row.id)}
                                                                    onChange={(e) => this.toggleCheckboxChange(e, row, "id", index)}/>)
            }, {
                Header: "Ad", accessor: "name", sortable: false, width: "18%", Cell: (item, i) => (
                    <div style={{
                        textAlign: "left",
                        backgroundColor: this.state.selectedRowNum === i ? this.state.selectedRowColor : ""
                    }}>{item.name}</div>)

            }, {
                Header: "Soyad",
                accessor: "surname",
                sortable: false,
                width: "18%",
                Cell: (item, i) => (
                    <div style={{
                        textAlign: "left",
                        backgroundColor: this.state.selectedRowNum === i ? this.state.selectedRowColor : ""
                    }}>{item.surname}</div>)
            }, {
                Header: "TCKN",
                accessor: "tckn",
                sortable: false,
                width: "13%",
                Cell: (item, i) => (
                    <div style={{
                        textAlign: "left",
                        backgroundColor: this.state.selectedRowNum === i ? this.state.selectedRowColor : ""
                    }}>{item.tckn}</div>)

            }, {
                Header: "Durum",
                accessor: "statusType",
                sortable: false,
                width: "10%",
                Cell: (item, i) => (<div style={{
                    textAlign: "left",
                    backgroundColor: this.state.selectedRowNum === i ? this.state.selectedRowColor : ""
                }}><span>{statusTypeFilterData().map(function (e) {
                    if (e.value === item.statusType) return e.label
                })}</span>
                </div>)
            }, {
                Header: "Departman",
                accessor: "department",
                sortable: false,
                width: "20%",
                Cell: (item, i) => (
                    <div style={{
                        textAlign: "left",
                        backgroundColor: this.state.selectedRowNum === i ? this.state.selectedRowColor : ""
                    }}>{item.department && <TextOverFlowTooltip text={item.department.name} maxLength={12}/>}</div>)
            }, {
                Header: "lemler", accessor: "", sortable: false, width: '18%', Cell: (item, i) => {
                    return (<div className='d-flex' style={{textAlign: 'left', WebkitTextFillColor: 'white'}}>
                        <MiniButton title="Dosyalar Grntle" faIcon="fa-eye"
                                    onClick={() => {
                                        this.onClickTable(item, i)
                                    }}
                                    color="success"/>
                        <MiniButton title="zlk ve Form Dosyalarn ndir" faIcon="fa-download"
                                    disabled={this.state.disableDownload}
                                    onClick={() => {
                                        this.downloadRow(item.id, i)
                                    }}
                                    color="warning"/>
                        <MiniButton title="zlk Dosyas Ykle" faIcon="fa fa-upload"
                                    onClick={() => {
                                        this.uploadRow(item, i)
                                    }}
                                    color="danger"/>
                        <MiniButton title="zlk Formu Doldur" faIcon="fa fa-edit"
                                    onClick={() => {
                                        this.openFormModal(item, i)
                                    }}
                                    color="info"/>
                        {this.state.isLoading && i === this.state.selectedRowNum && (
                            <div
                                className="loader-overlay"
                                style={{
                                    display: 'flex',
                                    justifyContent: 'center',
                                    alignItems: 'center',
                                    position: 'absolute',
                                    top: 0,
                                    left: 0,
                                    right: 0,
                                    bottom: 0,
                                    background: 'rgba(255, 255, 255, 0.5)',
                                    zIndex: 9999
                                }}
                            >
                                <ReactLoading
                                    type="spinningBubbles"
                                    style={{ width: "70px", height: "7f0px", fill: "#888" }}
                                />
                            </div>
                        )}
                    </div>)
                }
            }];
        let comment = "";
        return (<div className="row baseBackground">
                {this.state.alert}
                {this.state.isFileLoading && <ReactLoading type="spinningBubbles" style={{
                    width: "70px",
                    height: "70px",
                    fill: "#888",
                    position: 'fixed',
                    top: '40%',
                    left: '50%',
                    zIndex: 99999
                }}/>}
                <Nav className="topbar" style={{paddingLeft: '14px', height: '53px'}}>
                    <NavBarKS offsetWidth={this.props.offsetWidth} innerWidth={window.innerWidth}
                              btnDisabled={this.state.btnDisabled}
                              selection={this.state.selection} name='archiveInvoiceList'>
                        <StickButton position="leftSide" className="zocial Excel" style={{fontSize: 'x-small'}}
                                     label="Genel Durum Raporu ndir" title="Excel Raporu ndir"
                                     disabled={this.state.loading}
                                     onClick={() => {
                                         this.downloadMedia();
                                     }}/>
                        <StickButton position="leftSide" className="zocial Pdf" style={{fontSize: 'x-small'}}
                                     label="zlk ndir" title="Seilen alanlarn zlk ve Form Dosyalarn ndir"
                                     disabled={this.state.loading || !(this.state.selection && this.state.selection.length > 0) || this.state.disableDownload}
                                     onClick={() => {
                                         this.downloadSelected();
                                     }}/>
                    </NavBarKS>
                </Nav>
                <div className="col-sm-12 satirBasi">
                    <div className="card-header">
                        <div className="row">
                            <div className="col-lg-8 col-md-12">
                                <h4 className="card-title">zlk Dosyalar</h4>
                            </div>
                            <div className="col-lg-4 col-md-12">
                                {this.state.employeeName &&
                                    <h4 style={{
                                        color: 'black',
                                        fontWeight: 'bold',
                                        textAlign: 'center',
                                        backgroundColor: 'lightgrey'
                                    }} className="card-title">{this.state.employeeName.toUpperCase()}</h4>}
                            </div>
                        </div>
                    </div>
                    <div className="card-body">
                        <div className="row" style={{margin: '1px 0 1px 0'}}>
                            <div className="col-lg-8 col-md-12" style={{marginTop: '-9px'}}>
                                <div className='col-sm-12'>
                                    <DetailedSearch fields={fields}
                                                    loading={this.state.searching}
                                                    onSearch={this.handleSearch}/>
                                </div>
                                <div className='col-sm-12'>
                                    <Table
                                        loading={this.state.loading}
                                        loadingText="Kaytlar getiriliyor..."
                                        columns={columns}
                                        content={this.state.content ? this.state.content : []}
                                        total={this.state.total ? this.state.total : 0}
                                        page={this.state.page ? this.state.page : 0}
                                        size={this.state.size ? this.state.size : 0}
                                        options={["10", "20", "50", "100"]}
                                        fetchData={this.fetchData}
                                        manual={this.state.manual}
                                    />
                                </div>
                            </div>
                            <div className="col-lg-4 col-md-12">
                                <div style={{
                                    overflow: 'auto', flex: '1', maxHeight: '600px'
                                }}>
                                    <div style={{margin: '0 20px'}}>
                                        <ContextMenuTrigger id="contextmenu">
                                            <Tree
                                                disabled={this.state.isFilesLoading}
                                                className="myCls"
                                                showLine
                                                selectable={true}
                                                defaultExpandAll={false}
                                                defaultSelectedKeys={this.state.defaultSelectedKeys}
                                                onSelect={(key, info) => {
                                                    if (info.selected === true) this.onSelect(key, info.selectedNodes[0].props)
                                                }}
                                                treeData={this.state.employeeFiles}
                                                onRightClick={(key) => {
                                                    this.rightClickOnTree(key);
                                                }}
                                            />
                                            {this.state.isFilesLoading && <ReactLoading type="spinningBubbles" style={{
                                                width: "70px", height: "70px", margin: "20px auto 0 auto", fill: "#888",
                                            }}/>}
                                        </ContextMenuTrigger>
                                    </div>
                                </div>
                                <div className='d-flex justify-content-center align-items-center'>
                                    {this.state.isIadeButtonAktif && <button type="button" rel="tooltip"
                                                                             className="Btn btn-icon"
                                                                             title="Yeniden Dzenlemeye Gnder"
                                                                             onClick={() => {
                                                                                 comment = "";
                                                                                 this.setState({
                                                                                     alert: (<SweetAlert
                                                                                             showCancel
                                                                                             warning
                                                                                             confirmBtnText="Kaydet"
                                                                                             cancelBtnText="k"
                                                                                             confirmBtnBsStyle="success"
                                                                                             cancelBtnBsStyle="default"
                                                                                             title="Aklama Giriniz"
                                                                                             onConfirm={() => this.resendPersonalInfo(this.state.selectedNode.surveyResponseId, comment)}
                                                                                             onCancel={() => this.setState({alert: null})}
                                                                                         >
                                                                                             <label
                                                                                                 style={{fontSize: '15px'}}>Seilen
                                                                                                 zlk
                                                                                                 Dosyas
                                                                                                 Dzenlenmesi
                                                                                                 iin Tekrar
                                                                                                 gnderilecektir.</label>
                                                                                             <textarea
                                                                                                 cols={4}
                                                                                                 title="Yeniden gnderme talebi aklamas giriniz."
                                                                                                 placeholder='Aklamanz buraya giriniz...'
                                                                                                 onChange={(e) => comment = e.target.value}

                                                                                                 style={{
                                                                                                     borderBottom: '2px solid #007b80',
                                                                                                     fontSize: '12px',
                                                                                                     width: '90%'
                                                                                                 }}
                                                                                             />

                                                                                         </SweetAlert>

                                                                                     )
                                                                                 });
                                                                             }}
                                                                             style={{background: 'linear-gradient(rgb(120, 177, 215), rgb(120, 177, 215))'}}>
                                        <i className="fa fa-edit"/>
                                    </button>}
                                    {this.state.isIadeButtonAktif && <label>
                                        Yeniden Dzenle
                                    </label>}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                {this.state.openModal && <SurveyAttachmentModal openModal={this.state.openModal}
                                                                selectedEmployee={this.state.selectedEmployee}
                                                                getPersonelFilesById={this.getPersonelFilesById}
                                                                setModal={this.setModal}
                                                                surveyNamesList={this.state.surveyNamesList}/>}
                {this.state.mediaModal &&
                    <ApproveDocumentViewer url={this.state.url}
                                           modal={this.state.mediaModal}
                                           closeModal={this.setMediaViewer}
                                           canvas={true}
                    />}
                {this.state.openFormModal &&
                    <Modal isOpen={this.state.openFormModal} size="lg" className="ust">
                        <ModalHeader
                            toggle={this.closeFormModal}>{"zlk Formu" + "-" + this.state.selectedEmployee?.name + " " + this.state.selectedEmployee?.surname}</ModalHeader>
                        <ModalBody>
                            <SurveyFormHR isPersonalInfo={true}
                                          closeFormModal={this.closeFormModal}
                                          employee={this.state.selectedEmployee}
                            />
                        </ModalBody>
                    </Modal>
                }
                {this.state.selectedNode?.isPersonalInfo === true && this.state.selectedNode?.isAttachment === true && this.state.selectedNode?.attachmentName !== "" &&
                    <ContextMenu id="contextmenu">
                        <MenuItem data={{key: this.state.selectedKey}} onClick={this.deleteAttachment}>
                            <RiDeleteBin6Line className="delete"/>
                            <span>Sil</span>
                        </MenuItem>
                        <MenuItem data={{key: this.state.selectedKey}} onClick={this.deleteAllAttachment}>
                            <RiDeleteBin6Line className="delete"/>
                            <span>Tmn Sil</span>
                        </MenuItem>
                        <MenuItem data={{key: this.state.selectedKey}}>
                            <RiCloseFill className="close"/>
                            <span>Kapat</span>
                        </MenuItem>

                    </ContextMenu>}
            </div>
        );
    }

    encodeBase64 = (key) => {
        return new Buffer(key).toString('base64');
    }
    onSelect = (key, node) => {
        let self = this;
        self.setState({
            selectedNode: node,
            isIadeButtonAktif: (node.state === self.statesEnum.APPROVED && node.isPersonalInfo === true),
            selectedAttachment: "",
            selectedKey: key[0]
        });
        self.setState({mediaModal: true});
        requestK({
            url: `/personnel-info/download-doc`,
            responseType: 'arraybuffer',
            method: "post",
            data: self.encodeBase64(key[0])
        }).then((response) => {
            let blob = new Blob([response.data], {type: response.headers["content-type"]});
            let base64Data = "";
            if (blob !== undefined) {
                let reader = new FileReader();
                reader.readAsDataURL(blob);
                reader.onloadend = function () {
                    base64Data = reader.result;
                    self.setState({url: base64Data});
                }
            }
        }).catch((error) => {
            if (error.response && error.response.data) {
                let response = error.response;
                let decodedText = new TextDecoder("utf-8").decode(response.data);
                if (response.data === "Dosya ierii henz oluturulmamtr.") {
                    Alert.warning("Dosya ierii henz oluturulmamtr.", {
                        position: 'top-right', effect: 'stackslide', timeout: 5000
                    });
                } else if (response.data === "Grntlemek istediiniz belge alan tarafndan eklenmemitir.") {
                    Alert.warning("Grntlemek istediiniz belge alan tarafndan eklenmemitir.", {
                        position: 'top-right', effect: 'stackslide', timeout: 5000
                    });
                } else if (decodedText === "Dosya ilemi seiniz.") {
                    Alert.warning("Dosya ilemi seiniz.", {
                        position: 'top-right', effect: 'stackslide', timeout: 5000
                    });
                } else if (decodedText === "Dosya ierii henz oluturulmamtr.") {
                    Alert.warning("Dosya ierii henz oluturulmamtr.", {
                        position: 'top-right', effect: 'stackslide', timeout: 5000
                    });
                } else if (decodedText === "Bu dokman dosyaya eklenmemitir.") {
                    Alert.warning("Bu dokman dosyaya eklenmemitir.", {
                        position: 'top-right', effect: 'stackslide', timeout: 5000
                    });
                } else {
                    showAxiosError(error);
                }
            } else {
                showAxiosError(error);
            }

            self.setState({anyFileSelected: true, mediaModal: false, title: null});
        });

    };

    rightClickOnTree = (key) => {
        this.setState({selectedKey: key.node.props.eventKey, selectedNode: key.node.props});
    }

}
