export function isKepAddressUnique(self) {
    return self.props.sidebarMenus && self.props.sidebarMenus["kepik_uniquekepaddress"] === true;
}

export function isDepartmentMandatory(self) {
    return self.props.sidebarMenus && self.props.sidebarMenus["kepik_department"] === true;
}

export function isFormMandatory(self) {
    return self.props.sidebarMenus && self.props.sidebarMenus["kepik_formtemplate"] === true;
}

export function isBordroMandatory(self) {
    return self.props.sidebarMenus && self.props.sidebarMenus["kepik_bordroimport"] === true;
}

export function renewListForDepartment(self, list, fieldTag) {
    let newList = [];
    if (!isDepartmentMandatory(self)) {
        list.forEach(item => {
            if (!item[fieldTag] ||
                (
                    !item[fieldTag].includes("department") &&
                    !item[fieldTag].includes("jobTitle") &&
                    (!item[fieldTag][0][fieldTag] || !item[fieldTag][0][fieldTag].includes("department"))
                )
            ) {
                newList.push(item);
            }
        });
    } else {
        list.forEach(item => {
            newList.push(item);
        });
    }
    return newList;
}

export function renewListForKepOwnership(list, fieldTag, isKepOwner) {
    let newList = [];
    if (!isKepOwner) {
        list.forEach(item => {
            if (!item[fieldTag].includes("kepAddress")) {
                newList.push(item);
            }
        });
    } else {
        list.forEach(item => {
            if (!item[fieldTag].includes("approv") &&
                !item[fieldTag].includes("email") &&
                !item[fieldTag].includes("telephone") &&
                !item[fieldTag].includes("conditional")) {
                newList.push(item);
            }
        });
    }
    return newList;
}

export function renewListForPrivileges(self, list, fieldTag, isKepOwner) {
    let initList = renewListForDepartment(self, list, fieldTag);
    return renewListForKepOwnership(initList, fieldTag, isKepOwner);
}

