export function getStateValue(state) {
    return typeof state !== 'number' ? DocumentState[state].value : state;
}

export function stateCompare(state, compare, then) {
    if (!state || !then) {
        return null;
    }
    let stateValue = getStateValue(state);
    let comparedValue = getStateValue(then);

    if (state) {
        switch (compare) {
            case "=":
                return stateValue === comparedValue;
            case "<":
                return stateValue < comparedValue;
            case ">":
                return stateValue > comparedValue;
            case "<=":
                return stateValue <= comparedValue;
            case ">=":
                return stateValue >= comparedValue;
            default:
                return stateValue === comparedValue;
        }
    } else {
        return null;
    }
}

