export function getJobTitles() {
    let jobTitlesData = [];
    let params = {
        url: `/job/job-titles`,
        method: "get"
    };
    requestK(params)
        .then(function (response) {
            let jobTitleList = response.data;
            jobTitleList.map((job) => {
                jobTitlesData.push({value: job.title, label: job.title, job: job});
            });
        }).catch(function (error) {
        showAxiosError(error);
    });
    return jobTitlesData;
}


