export function searchTreeChildrenNameList(element, matchingKey, matched = false) {

    if (matchingKey && element.key === matchingKey) {
        matched = true;
    }

    let list = [];
    if (element.children && element.children.length) {
        for (let i = 0; i < element.children.length; i++) {
            matched && list.push(element.children[i].key);
            list.push(...searchTreeChildrenNameList(element.children[i], matchingKey, matched));
        }
    }
    return list;
}

export function searchTreeChildrenIdList(element, matchingKey, matched = false) {

    if (matchingKey && element.key === matchingKey) {
        matched = true;
    }

    let list = [];
    if (element.children && element.children.length) {
        for (let i = 0; i < element.children.length; i++) {
            matched && list.push(element.children[i].key);
            list.push(...searchTreeChildrenIdList(element.children[i], matchingKey, matched));
        }
    }
    return list;
}

export function findChildrenLowestLevel(element, level = 0) {
    if (!element || !element.title)
        return [null, null];

    let currentLevel = level;
    let titleLength = element.title.length * 5 + (currentLevel * 18);
    if (element.children && element.children.length) {
        for (let i = 0; i < element.children.length; i++) {
            let [newLevel, newLength] = findChildrenLowestLevel(element.children[i], level + 1);
            currentLevel = newLevel > currentLevel ? newLevel : currentLevel;
            titleLength = newLength > titleLength ? newLength : titleLength;
        }
    }
    return [currentLevel, titleLength];
}

export function getEmployees(key) {
    if (!key) {
        return Promise.resolve({options: []});
    } else {
        if (key.length >= 2) {
            let params = {
                url: `/employee/searchEmployee?search=${key}&customerId=`,
                method: "get"
            };
            return requestK(params)
                .then(response => {
                    let employeeList = response.data;
                    let options = employeeList.map(employee => ({
                        value: employee.employeeUserId,
                        label: employee.name + " " + employee.surname + " [Tckn: " + employee.tckn + "] " + (employee.department ? "  (" + employee.department.name + " Departman)" : "")
                    }));
                    return {options};
                })
                .catch(function (error) {
                    showAxiosError(error);
                });
        } else {
            return Promise.resolve({options: []});
        }
    }
}

export function getEmployeeIds(key, customerId) {
    if (!key) {
        return Promise.resolve({options: []});
    } else {
        if (key.length >= 2) {
            let params = {
                url: `/employee/searchEmployee?search=${key}&customerId=${customerId}`,
                method: "get"
            };
            return requestK(params)
                .then(response => {
                    let employeeList = response.data;
                    let options = employeeList.map(employee => ({
                        value: employee.id,
                        label: employee.name + " " + employee.surname + " [Tckn: " + employee.tckn + "] " + (employee.department ? "  (" + employee.department.name + " Departman)" : "")
                    }));
                    return {options};
                })
                .catch(function (error) {
                    showAxiosError(error);
                });
        } else {
            return Promise.resolve({options: []});
        }
    }
}


export function addCustomFields(customList, fieldList) {
    customList && customList.map(customObj => {
        let accessorTitle = "cstmfld" + customObj.title;
        accessorTitle = accessorTitle.toString().split(" ").join("");
        if (!customObj.type || customObj.type === "STRING") {
            fieldList.push({
                label: customObj.title,
                accessor: accessorTitle,
                type: "text",
                operator: "=",
            });
        } else if (customObj.type === "INTEGER") {
            fieldList.push({
                label: customObj.title + " (En Dk)",
                accessor: "min_" + accessorTitle,
                type: "numericText",
                operator: ">=",
            });
            fieldList.push({
                label: customObj.title + " (En Yksek)",
                accessor: "max_" + accessorTitle,
                type: "numericText",
                operator: "<=",
            });
        } else if (customObj.type === "DOUBLE") {
            fieldList.push({
                label: customObj.title + " (En Dk)",
                accessor: "min_" + accessorTitle,
                type: "doubleText",
                operator: ">=",
            });
            fieldList.push({
                label: customObj.title + " (En Yksek)",
                accessor: "max_" + accessorTitle,
                type: "doubleText",
                operator: "<=",
            });
        } else if (customObj.type === "DATETIME") {
            fieldList.push({
                label: customObj.title + " (En Dk)",
                accessor: "min_" + accessorTitle,
                type: "datetime",
                operator: ">=",
            });
            fieldList.push({
                label: customObj.title + " (En Yksek)",
                accessor: "max_" + accessorTitle,
                type: "datetime",
                operator: "<=",
            });
        } else if (customObj.type === "BOOLEAN") {
            fieldList.push({
                label: customObj.title,
                accessor: accessorTitle,
                type: "boolean",
                operator: "="
            });
        }
    });
}

export function downloadDocument(self, row, index, type) {
    let params = {
        url: type.toString().toUpperCase() === "BORDRO" ? `/bordro/previewBordroContent` : `/form/previewFormContent`,
        responseType: 'arraybuffer',
        method: "post",
        data: row.id
    };
    self.setState({disabledDownloadBtn: true, loadingType: 'downloadApproveDoc', index: index});
    requestK(params)
        .then(function (response) {
            let blob = new Blob([response.data], {type: response.headers["content-type"]});
            let link = document.createElement('a');
            link.href = window.URL.createObjectURL(blob);
            let createdAt = !!row.createdAt ? ("_" + row.createdAt.toString().substring(0, row.createdAt.indexOf("T"))) : "";
            let employeeName = (row.employee !== undefined && row.employee !== null) ? row.employee.name + "_" + row.employee.surname : "";
            let randomNum = Math.floor(Math.random() * 100000);
            let fileName = (type === "BORDRO" ?
                    "Bordro" +
                    (row.parsedTckn ? ("_" + row.parsedTckn + "_") : "_") +
                    row.term.split("-")[0] + "_" +
                    row.term.split("-")[1] + "_" + createdAt + "_" + randomNum + ".pdf"
                    : "Form_" + employeeName + "_" + createdAt + "_" + randomNum + ".pdf"
            )
            if (row.timeStamp != null && row.timeStamp != "") {
                let formTimeStamp = new Blob([base64_arraybuffer.decode(row.timeStamp)], {type: 'application/octet-stream'});
                let linkTs = document.createElement('a');
                linkTs.href = window.URL.createObjectURL(formTimeStamp);
                linkTs.download = fileName + ".zd";
                document.body.appendChild(linkTs);
                linkTs.click();
            }
            link.download = fileName;
            document.body.appendChild(link);
            link.click();
        }).catch(function (error) {
        showAxiosError(error);
    });
}

export function downloadApproveDocument(self, row, index, type) {
    if (!(self.state.disabledDownloadBtn && index === self.state.selectedIndex)) {
        let formData = new FormData();
        formData.append("documentId", row.id);
        formData.append("documentType", type);
        let params = {
            url: `/approval/downloadApproveDocument`,
            responseType: 'arraybuffer',
            method: "post",
            data: formData
        };
        requestK(params)
            .then(function (response) {
                if (response.data.byteLength === 0) {
                    Alert.info("Bu dokmanda onaylama ilemi yaplmadndan onay belgesi kartlamaz", {
                        position: 'top-right',
                        effect: 'stackslide',
                        timeout: 5000
                    });
                    self.setState({disabledDownloadBtn: false, loadingType: null});
                    return;
                }
                downloadDocument(self, row, index, type);
                let link = document.createElement('a');
                link.href = window.URL.createObjectURL(new Blob([response.data], {type: "application/pdf"}));
                let date = new Date();
                link.download = "Onay_Belgesi_Raporu_" +
                    date.getFullYear() + "-" +
                    date.getMonth() + "-" +
                    date.getDate() + "_" +
                    date.getHours() + "-" +
                    date.getMinutes() + "-" +
                    date.getSeconds() + ".pdf";
                document.body.appendChild(link);
                link.click();
                self.setState({disabledDownloadBtn: false, loadingType: null});
            }).catch(function (error) {
            showAxiosError(error);
            self.setState({disabledDownloadBtn: false, loadingType: null});
        });
    } else {
        Alert.info("leme alnm bir ykleme vardr. Bitince tekrar deneyiniz.", {
            position: 'top-right',
            effect: 'stackslide',
            timeout: 5000
        });
    }
}