import React, {Component} from 'react';
import NavBarKS from "../../../components/NavBarKS";
import {Button, Col, Form, Label} from "reactstrap";
import {requestK} from "../../../helper/APIUtils";
import {showAxiosError} from "../../../helper/helper";
import Alert from "react-s-alert";

const cronList = [
    {id: 1, descriptions: "Otomatik Form lemi", url: `/cron/autoSendForm`},
    {id: 2, descriptions: "alan Oluturma lemi", url: `/cron/createUserFromEmployee`},
    {id: 3, descriptions: "Dorulama Kodu Says lemi", url: `/cron/verficationCode`},
    {id: 4, descriptions: "ndirme stei lemi", url: `/cron/clearRequestsByDate`},
    {id: 5, descriptions: "Kullanc Kayt Bildirimi lemi", url: `/cron/createUserNotification`},
    {id: 6, descriptions: "IVR Durum Kontrol lemi", url: `/cron/runIVRStatusCheck`},
    {
        id: 7,
        descriptions: "SMS Bekleyen Kontrol lemi(10 dk arayla)",
        url: `/cron/runSmsStatusCheckPendingFromProvider`
    },
    {id: 8, descriptions: "SMS Durum Kontrol lemi", url: `/cron/runSmsStatusCheckFromProvider`},
    {id: 9, descriptions: "Mteri Szleme Tarihi Kontrol lemi", url: `/cron/customerContractCron`},
    {id: 10, descriptions: "Portal.properties", url: `/common/reload`},
    {id: 11, descriptions: "Scheduler Conf", url: `/common/reload/notification`},
    {id: 12, descriptions: "Notification Conf", url: `/common/reload/scheduler`},
    {id: 13, descriptions: "RabbitMq Conf", url: `/common/reload/rabbitmq`},
]

class CronLauncher extends Component {

    launcherCron = (id, url, e) => {
        e.preventDefault();
        let params = {
            url: url,
            method: "post"
        };
        requestK(params)
            .then(() => {
                if (id >= 10 && id <= 13) {
                    Alert.success("Yeniden Ykleniyor", {
                        position: 'top-right',
                        effect: 'stackslide',
                        timeout: 5000
                    });
                } else {
                    Alert.success("Cron Balatld", {
                        position: 'top-right',
                        effect: 'stackslide',
                        timeout: 5000
                    });
                }
            })
            .catch((error) => {
                showAxiosError(error);
            });
    }

    renderCron = (id, description, url) => {
        let color = id % 2 === 0 ? "info" : "success";
        let buttonText = id >= 10 && id <= 13 ? "Yeniden Ykle" : "Balat";
        return (
            <div key={id} className="row">
                <Label sm={3}>{description}</Label>
                <Col sm={9}>
                    <Button color={color} className='launcherBtn'
                            onClick={(e) => this.launcherCron(id, url, e)}>
                        {buttonText}
                    </Button>
                </Col>
            </div>
        );
    }

    render() {
        return (
            <div className="row baseBackground">

                <NavBarKS offsetWidth={this.props.offsetWidth} innerWidth={window.innerWidth}>
                </NavBarKS>

                <div className="col-md-12 satirBasi">

                    <div className="card-header ">
                        <h2 className="card-title">Cron lemleri</h2>
                    </div>

                    <div style={{marginTop: '20px'}}>
                        <Form>
                            {cronList.map(cron => (
                                this.renderCron(cron.id, cron.descriptions, cron.url)
                            ))}
                        </Form>
                    </div>
                </div>

            </div>
        );
    }
}

export default CronLauncher;