function readSchemaTitle(textInput) {
  switch (textInput) {
    case "text-input":
      return "metinsatr";
    case "number-input":
      return "say";
    case "text-area":
      return "metinalan";
    case "telephone-input-panel":
      return "telefon";
    case "checkbox-panel":
      return "seimkutular";
    case "radio-button-panel":
      return "tekliseimkutular";
    case "dropdown-panel":
      return "alrliste";
    case "date":
      return "tarih";
    case "time-panel":
      return "saat";
    case "file":
      return "dosya";
    case "image":
      return "dosya";
  }
}
function getBrowserLanguage() {
  let language = navigator.language || navigator.userLanguage || 'en-US';
  return language.toLowerCase().substring(0, 2);
}

const translations = {
  "Selection": {
    "tr": "Seim",
    "en": "Selection"
  },
};

function translate(key) {
  const browserLanguage = getBrowserLanguage();
  return translations[key] && translations[key][browserLanguage] ? translations[key][browserLanguage] : key;
}

function getLastName(mainName, paramNames) {
  for (let i = paramNames.length - 1; i >= 0; i--) {
    if (paramNames[i].toString().startsWith(mainName)) {
      return paramNames[i];
    }
  }
}

function evaluateMainName(name, paramNames) {
  if (!name)
    return "";

  let result = paramNames.map(mainName => {
    if (mainName === name) {
      let lastFound = getLastName(mainName, paramNames);
      if (lastFound.toString().includes("_")) {
        let parts = lastFound.toString().split("_")
        return parts[0] + "_" + (parseInt(parts[1]) + 1)
      } else {
        return name + "_1";
      }
    }
  });

  let filtered = result.filter(function (el) {
    return el != null;
  });

  return filtered.length ? filtered[0] : name;
}

function readName(mainName, indexes, paramNames, textInput) {
  let name = evaluateMainName(mainName, paramNames);
  if (!name || !name.length) {
    if (textInput === "image")
      textInput = "file";
    let found = false;
    indexes && Object.keys(indexes).length && Object.keys(indexes).map(key => {
      if (key === textInput) {
        indexes[key] = parseInt(indexes[key]) + 1;
        found = true;
      }
    });
    if (!found) {
      indexes[textInput] = 0;
    }
    if (indexes[textInput] === 0) {
      name = readSchemaTitle(textInput)
    } else {
      name = readSchemaTitle(textInput) + "_" + indexes[textInput];
    }
  }
  paramNames.push(name);
  return name;
}

function readRequired(schema, required, name) {
  if (required) {
    let required = schema["required"];
    required.push(name);
    schema["required"] = required;
  }
}

function readContent(formData, content, name) {
  if (content) {
    formData[name] = content;
  }
}

export function convertJson(customFormObj) {
  let title = "form";
  let schema = {
    type: "object",
    required: [],
    properties: {}
  };
  let uiSchema = {};
  let formData = {};
  let indexes = {};
  let paramNames = [];
  customFormObj && customFormObj.map(component => {
      let section = component["properties"]["sections"][0];
      let elements = section["elements"];

      switch (section.type) {
        case "heading-panel":
          schema["title"] = elements[0].content;
          title = elements[0].content;
          break;
        case "label-area-panel":
          schema["description"] = elements[0].label;
          break;
        case "text-input-panel":
          elements && elements.map(element => {
            let textInputFieldName = readName(element.label, indexes, paramNames, element.type);
            schema["properties"][textInputFieldName] = {
              type: "string",
              title: element.label,
              description: element.description
            };
            readContent(formData, element.content, textInputFieldName);
            readRequired(schema, element.required, textInputFieldName);
          });
          break;
        case "telephone-input-panel":
          let telephoneInputFieldName = readName(section.name, indexes, paramNames, elements[0].type);
          schema["properties"][telephoneInputFieldName] = {
            type: "string",
            title: elements[0].label,
            description: elements[0].description
          };
          uiSchema [telephoneInputFieldName] = {
            "ui:options": {
              inputType: "tel"}
          };
          readContent(formData, elements[0].content, telephoneInputFieldName);
          readRequired(schema, section.required, telephoneInputFieldName);
          break;
        case "time-panel":
          let timeInputFieldName = readName(section.name, indexes, paramNames, elements[0].type);
          const isRequired = section.hasOwnProperty("required") && section.required === true;
          const defaultValue = isRequired ? "00:00" : "";
          schema["properties"][timeInputFieldName] = {
            type: "string",
            title: elements[0].label,
            description: elements[0].description,
            default: defaultValue
          };
          uiSchema[timeInputFieldName] = {
            "ui:widget": "timeWidget",
            "ui:options": {
              inputType: "time"
            }
          };
          if (!formData.hasOwnProperty(timeInputFieldName) || formData[timeInputFieldName] === null || formData[timeInputFieldName] === undefined) {
            formData[timeInputFieldName] = defaultValue;
          }
          readContent(formData, elements[0].content, timeInputFieldName);
          readRequired(schema, section.required, timeInputFieldName);
          break;
        case "number-input-panel":
          let numberInputFieldName = readName(section.name, indexes, paramNames, elements[0].type);
          schema["properties"][numberInputFieldName] = {
            type: "number",
            title: elements[0].label,
            description: elements[0].description
          };
          readContent(formData, elements[0].content, numberInputFieldName);
          readRequired(schema, section.required, numberInputFieldName);
          break;
        case "text-area-panel":
          let textAreaFieldName = readName(section.name, indexes, paramNames, elements[0].type);
          schema["properties"][textAreaFieldName] = {
            type: "string",
            title: elements[0].label,
            description: elements[0].description
          };
          uiSchema[textAreaFieldName] = {
            "ui:widget": "textarea"
          };
          readContent(formData, elements[0].content, textAreaFieldName);
          readRequired(schema, section.required, textAreaFieldName);
          break;
        case "checkbox-panel":
          let checkboxTitle = "";
          let checkboxEnums = [];
          elements && elements.map(element => {
            if (element.type === "text-area")
              checkboxTitle = element.content;
            else if (element.type === "checkbox")
              checkboxEnums.push(element.content);
          });
          let checkboxFieldName = readName(section.name, indexes, paramNames, section.type);
          schema["properties"][checkboxFieldName] = {
            type: "array",
            title: checkboxTitle,
            items: {
              type: "string",
              enum: checkboxEnums
            },
            uniqueItems: true
          };
          uiSchema[checkboxFieldName] = {
            "ui:widget": "checkboxes"
          };
          readContent(formData, elements[0].content, checkboxFieldName);
          readRequired(schema, section.required, checkboxFieldName);
          break;
        case "radio-button-panel":
          let radiobuttonTitle = "";
          let radioFieldName = readName(section.name, indexes, paramNames, section.type);
          schema["properties"][radioFieldName] = {
            description: elements[0] ? elements[0].label : "",
            type: "object",
            title: radiobuttonTitle,
            properties: {

              Selection: {
                type: "number",
                enum: ["Kabul Ediyorum", "Kabul Etmiyorum"]
              },
            }
          };
          if (schema["properties"][radioFieldName].properties && schema["properties"][radioFieldName].properties.Selection) {
            schema["properties"][radioFieldName].properties.Selection.title = translate("Selection");
          }

          uiSchema[radioFieldName] = {
            radio: {
              "ui:widget": "radio"
            },
            select: {
              "ui:widget": "select"
            },
            Selection: {
              "ui:widget": "radio",
              "ui:options": {
                inline: true
              }
            }
          };
          readContent(formData, elements[0].content, radioFieldName);
          readRequired(schema, section.required, radioFieldName);
          break;
        case "dropdown-panel":
          let dropdownDesc = "";
          let dropdownTitle = "";
          let dropdownLabels = [];
          let dropdownValues = [];
          elements && elements.map(element => {
            if (element.type === "text-area") {
              dropdownDesc = element.content;
            } else if (element.type === "dropdown") {
              dropdownTitle = element.label;
              element.options && element.options.map(option => {
                dropdownLabels.push(option.label);
                dropdownValues.push(option.value);
              });
            }
          });
          let dropdownFieldName = readName(section.name, indexes, paramNames, section.type);
          schema["properties"][dropdownFieldName] = {
            type: "string",
            title: dropdownTitle,
            enum: dropdownValues,
            enumNames: dropdownLabels,
            description: dropdownDesc
          };
          readContent(formData, elements[0].content, dropdownFieldName);
          readRequired(schema, section.required, dropdownFieldName);
          break;
        case "date-panel":
          elements && elements.map(element => {
            let dateFieldName = readName(element.label, indexes, paramNames, element.type);
            schema["properties"][dateFieldName] = {
              type: "string",
              title: element.label ? element.label : "",
              description: element.description
            };
            uiSchema[dateFieldName] = {
              "ui:widget": "date"
            };
            readContent(formData, element.content, dateFieldName);
            readRequired(schema, element.required, dateFieldName);
          });
          break;
        case "file-panel":
          let fileFieldName = readName(section.name, indexes, paramNames, elements[0].type);
          schema["properties"][fileFieldName] = {
            type: "string",
            format: "data-url",
            title: elements[0].label ? elements[0].label : "",
            description: elements[0].description
          };
          uiSchema[fileFieldName] = {
            "ui:widget": "fileChooserWidget",
            "ui:options": {
              accept: ".pdf .xlsx .docx .doc"
            }
          };
          readContent(formData, elements[0].content, fileFieldName);
          readRequired(schema, section.required, fileFieldName);
          break;
        case "image-panel":
          let imageFieldName = readName(section.name, indexes, paramNames, elements[0].type);
          schema["properties"][imageFieldName] = {
            type: "string",
            format: "data-url",
            title: elements[0].label ? elements[0].label : "",
            description: elements[0].description
          };
          uiSchema[imageFieldName] = {
            "ui:widget": "imageFileChooserWidget"
          };
          readContent(formData, elements[0].content, imageFieldName);
          readRequired(schema, section.required, imageFieldName);
          break;
        case "link-panel":
          let linkFieldName = readName(section.name, indexes, paramNames, elements[0].type);
          schema["properties"][linkFieldName] = {
            type: "string",
            title: elements[0].content ? elements[0].content : "",
            default:elements[0].label ? elements[0].label:"",
            description: elements[0].description
          };
          uiSchema[linkFieldName] = {
            "ui:widget": "linkWidget"
          };
          readContent(formData,elements[0].label , linkFieldName);
          readRequired(schema, section.required, linkFieldName);
          break;
      }
    }
  );
  return [title, JSON.stringify({key: "form", name: title, schema: schema, uiSchema: uiSchema, formData: formData})];
}