import React, {Component} from 'react';
import {requestK} from "../../../helper/APIUtils";
import {Col} from 'reactstrap';
import ReCAPTCHA from "react-google-recaptcha";
import {getCaptchaCode} from "../../../helper/helper";

const recaptchaRef = React.createRef();

class DocumentValidate extends Component {
  constructor(props) {
    super(props);
    this.state = {
      captcha: false,
      verificationCode: "",
      errors: {}
    }
    this.documentValidate = this.documentValidate.bind(this);
    this.handleCaptcha = this.handleCaptcha.bind(this);
  }

  componentDidMount() {
    getCaptchaCode(this);
  }

  handleCaptcha = (key) => {
    this.setState({
      captcha: true,
      'g_recaptcha_response': key
    })
  }
  documentValidate = () => {
    let self = this;
    if (self.handleValidation()) {
      requestK({
        url: "/verification/verificationWithCaptcha",
        method: "post",
        data: {
          "verificationCode": self.state.verificationCode,
          "g_recaptcha_response": this.state.g_recaptcha_response
        },
        responseType: 'arraybuffer'
      }).then(response => {
        let blob = new Blob([response.data], {type: response.headers["content-type"]});
        let urlCreator = window.URL || window.webkitURL;
        let url = urlCreator.createObjectURL(blob);
        let a = document.createElement("a");
        document.body.appendChild(a);
        a.style = "display: none";
        a.href = url;
        a.download = self.state.verificationCode + ".pdf";
        a.click();
        this.setState({alert: null});
      }).catch(error => {
        let errors = {};
        errors["verificationCode"] = "Hatal dorulama kodu girdiniz! Belge dorulanamad.";
        this.setState({errors: errors});
      });
      recaptchaRef.current.reset();
    }
  }

  handleValidation() {
    let errors = {};
    let formIsValid = true;
    if (!this.state.verificationCode) {
      formIsValid = false;
      errors["verificationCode"] = "Dorulama kodunu giriniz!";
    }
    if (!this.state.captcha) {
      formIsValid = false;
      errors["captcha"] = "Ltfen yukardaki kutucua tik atnz!";
    }
    this.setState({errors: errors});
    return formIsValid;
  }


  render() {
    return (
      <div className="container" style={{
        height: '100vh',
        position: 'relative',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center'
      }}>
        <Col sm='12'
             style={{position: 'absolute', top: '15%', display: 'flex', flexDirection: 'column', alignItems: 'center'}}>
          <Col sm='12' style={{textAlign: 'center'}}>
            <label style={{fontSize: '20px'}}>
              Elektronik Belge Dorulama
            </label>
          </Col>
          <Col sm='8' style={{background: '#f7f7f9', padding: '5%', border: '1px solid #e4e4e4', borderRadius: '5px'}}>
            <Col style={{display: 'flex', flexDirection: 'column', alignItems: 'center'}}>
              <form role="form" className="form-horizontal">
                <div className="form-group">
                  <div className="col-sm-12">
                    <label htmlFor="dogrulamaKod" className="control-label" style={{fontSize: '16px'}}>Dorulama
                      Kodu:</label>
                  </div>
                  <div className="col-sm-12">
                    <input type="text" name="" id="dogrulamaKod" className="form-control"
                           onChange={data => this.setState({verificationCode: data.target.value})}
                           style={{padding: '6% 4%'}}/>
                    <span style={{color: "red"}}>{this.state.errors["verificationCode"]}</span>
                  </div>
                </div>
                <br/>
                <div className="form-group">
                  <div className="col-md-8">
                    <ReCAPTCHA
                      ref={recaptchaRef}
                      sitekey={this.state.captchaCode}
                      onChange={this.handleCaptcha}
                    />
                    <span style={{color: "red"}}>{this.state.errors["captcha"]}</span>
                  </div>
                </div>
                <div className="form-group">
                  <div className="col-md-12" style={{textAlign: 'center'}}>
                    <input type="button" value="Dorula" className='btn btn-success' onClick={this.documentValidate}/>
                  </div>
                </div>
              </form>
            </Col>
          </Col>
        </Col>
      </div>
    );
  }
}

export default DocumentValidate;
