import React, {Component} from 'react';
import {Modal} from "react-bootstrap";
import {Button, Col, Input, Label, Row, Form, FormGroup} from "reactstrap";


class ExternalTextArea extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  resize = () => {
    this.setState({changeSize: !this.state.changeSize});
  }

  render() {
    let title = "Cevap Metni";
    return (
      <>

        <Modal show={this.props.modal}
               animation={false}
          // toggle={() => this.props.closeModal()}
               modalTransition={{timeout: 700}}
               dialogClassName="modal-90w"
               size={this.state.changeSize ? "xxl" : "xl"}
               backdropTransition={{timeout: 1300}}
               aria-labelledby="example-custom-modal-styling-title"
               backdrop="static">

          <Modal.Header style={{padding: '2% 2% 0 2%'}}>
            <Modal.Title id="example-custom-modal-styling-title"
                         className='col-xs-12 col-sm-10 col-md-10 col-lg-10 col-xl-10'
                         style={{
                           marginBottom: '0',
                           fontSize: '1em',
                           lineHeight: '2em',
                           fontWeight: 'normal'
                         }}>
              <label
                style={{color: 'white', fontFamily: 'Montserrat', fontWeight: 'normal'}}>{title}</label>
            </Modal.Title>
            <div>
              <button
                className={this.state.changeSize ? "fas fa-compress simpleButton" : "fas fa-expand simpleButton"}
                style={{
                  position: 'unset',
                  right: '0',
                  top: '0',
                  outline: '0',
                  background: 'transparent'
                }}
                onClick={() => this.resize()}/>
            </div>
            <div>
              <button className='fas fa-times simpleButton'
                      style={{
                        position: 'unset',
                        right: '0',
                        top: '0',
                        outline: '0',
                        background: 'transparent'
                      }}
                      onClick={() => this.props.closeModal()}/>
            </div>
          </Modal.Header>
          <Modal.Body style={{
            padding: 0,
            position: 'sticky',
            top: 0,
            zIndex: '10000',
            paddingTop: '10px',
            display: 'flex',
            flexDirection: 'row-reverse',
            backgroundColor: '#f5f5f5'
          }}>
            <div style={{
              position: 'relative',
              right: '45px',
            }}>{this.props.totalLetters}/{this.props.maxTextLength}</div>
          </Modal.Body>

          <Modal.Body style={{height: "100vh", paddingTop: 0}}>
            <Col xs="12">
              <textarea name="textarea"
                        style={{
                          border: "1px solid grey",
                          width: "100%",
                          height: "95vh",
                          fontSize: "14px",
                          lineHeight:"1.5"
                        }}
                        value={this.props.value}
                        onChange={(e) => this.props.handleChange(e, "answer")}/>
            </Col>
          </Modal.Body>
        </Modal>
      </>

    );
  }
}

export default ExternalTextArea;