import React, {Component} from 'react';
import {showAxiosError} from "../../../../helper/helper";
import {requestK} from "../../../../helper/APIUtils";
import DefaultModal from "../../../../components/DefaultModal";
import Alert from "react-s-alert";
import {isValidConf} from "../../../../common/componentValidation";
import Select from "../../../../local_node_modules/react-select"
import {Card, CardHeader, Col, FormGroup, Label, TabContent, TabPane} from "reactstrap";
import ReactLoading from 'react-loading';
import SMSConfiguration from "./SMSConfiguration";
import MailConfiguration from "./MailConfiguration";
import HSMConfiguration from "./HSMConfiguration";


class ConfigurationModal extends Component {

    constructor(props) {
        super(props);
        this.state = {
            subservices: [],
            customerSubservices: [],
            subServiceList: [],
            activeTab: null,
        };
        this.saveClicked = this.saveClicked.bind(this);
        this.getValues = this.getValues.bind(this);
        this.controlValues = this.controlValues.bind(this);
        this.onChangeValues = this.onChangeValues.bind(this);
        this.toggle = this.toggle.bind(this);
        this.getActiveTab = this.getActiveTab.bind(this);
        this.findFirst = this.findFirst.bind(this);
        this.changeCustomerSubService = this.changeCustomerSubService.bind(this);
    }

    componentDidMount() {
        let self = this;
        let params = {
            url: `/subservice/getCustomerSubServices`,
            method: "post",
            data: {
                customerId: self.props.customerId
            }
        };
        self.setState({loading: true});
        requestK(params)
            .then(function (response) {
                let data = response.data;
                let {customerSubservices, subservices} = data;
                let subServiceList = customerSubservices.map(cs => cs.subserviceType);
                self.setState({customerSubservices, subservices, subServiceList, loading: false});
            }).catch(function (error) {
            showAxiosError(error);
            self.setState({loading: false});
        });

    }

    saveClicked() {
        let self = this;
        if (self.props.customerId === undefined) {
            let result = isValidConf(self.state.customerSubservices);
            let valid = result.valid;
            if (valid) {
                self.props.saveConfigurationList(self.state.customerSubservices);
            }
            self.props.toggleModal(false);
        } else {
            let result = isValidConf(self.state.customerSubservices);
            let error = result.error;
            let valid = result.valid;

            for (let customerSubservicesKey of self.state.customerSubservices) {
                if (customerSubservicesKey.subserviceType === "HSM") {
                    let config = Array.isArray(customerSubservicesKey.configs) ? JSON.parse(customerSubservicesKey.configs[0]) : JSON.parse(customerSubservicesKey.configs);
                    if ((config === null || config.userName === null || config.userName === "")) {
                        Alert.warning('HSM kullanabilmek iin ltfen kullanc ad giriniz!', {
                            html: true,
                            position: 'top-right',
                            effect: 'stackslide',
                            timeout: 5000
                        });
                        return;
                    }
                }
            }

            if (valid) {
                let self = this;
                let customerSubservices = self.state.customerSubservices.map(cs => {
                    cs.configs = cs.configs ? Array.isArray(cs.configs) ? cs.configs : [cs.configs] : null;
                    return cs;
                });
                let params = {
                    url: `/subservice/saveCustomerSubservices?customerId=${self.props.customerId}`,
                    method: "post",
                    data: customerSubservices
                };
                self.setState({disabledSaveBtn: true});
                requestK(params)
                    .then(function (response) {
                        let message = response.data;
                        self.setState({
                            disabledSaveBtn: false
                        });
                        Alert.success(message, {
                            position: 'top-right',
                            effect: 'stackslide',
                            timeout: 5000
                        });
                        self.props.toggleModal(false);
                    }).catch(function (error) {
                    showAxiosError(error);
                    self.setState({disabledSaveBtn: false});
                });
            } else {
                Alert.error(result.errorMessage, {
                    position: 'top-right',
                    effect: 'stackslide',
                    timeout: 8000
                });
                self.setState({error: error});
            }
        }
    }

    getValues() {
        return this.state.customerSubservices.map(customerConfig => customerConfig.subserviceType);
    }

    controlValues(list) {
        if (list.length > 0) {
            if (list[0].toString() === "E_SIGNATURE")
                list.splice(0, 1);
            else if (list[0].toString() === "KEP") {
                if (list.length === 1)
                    list.push("E_SIGNATURE")
                else
                    list.splice(2, 1);
            } else if (list[0].toString() === "MAIL") {
                if (list.length === 1)
                    list.push("E_SIGNATURE")
                else if (list[list.length - 1].toString() === "KEP")
                    list.splice(list.length - 1, 1);
            } else if ((list[0].toString() === "SMS") || (list[0].toString() === "HSM")) {
                if (list.length === 1)
                    list.push("MAIL", "E_SIGNATURE")
                else if (list[list.length - 1].toString() === "KEP")
                    list.splice(list.length - 1, 1);
            }
        }
        return list;
    }

    onChangeValues(list) {
        let subserviceTypes = list.map(e => e.value);
        let oldCustomerSubservices = this.state.customerSubservices;
        subserviceTypes = this.controlValues(subserviceTypes);
        let customerSubservices = [];
        for (let i = 0; i < oldCustomerSubservices.length; i++) {
            let index = subserviceTypes.indexOf(oldCustomerSubservices[i].subserviceType);
            if (index !== -1) {
                customerSubservices.push(oldCustomerSubservices[i]);
            }
        }
        let existCustomerSubservices = oldCustomerSubservices.map(e => e.subserviceType);
        for (let i = 0; i < subserviceTypes.length; i++) {
            let index = existCustomerSubservices.indexOf(subserviceTypes[i]);
            if (index === -1) {
                customerSubservices.push(
                    {
                        subserviceType: subserviceTypes[i],
                        configs: null
                    }
                );
            }
        }
        this.setState({customerSubservices, subServiceList: subserviceTypes});
    }

    toggle(tab) {
        if (this.state.activeTab !== tab) {
            this.setState({activeTab: tab});
        }
    }

    getActiveTab() {
        let {activeTab, subServiceList} = this.state;
        let index = subServiceList.indexOf(activeTab);
        if (index === -1) {
            if (subServiceList[0] !== 'KEP') {
                return subServiceList[0];
            } else {
                return subServiceList.length > 1 ? subServiceList[1] : null;
            }
        } else {
            return activeTab;
        }
    }

    findFirst(type) {
        let result = this.state.customerSubservices.find((cs) => cs.subserviceType === type);
        return (result && result.configs ? JSON.parse(result.configs) : {});
    }

    changeCustomerSubService(type, data) {
        let customerSubservices = this.state.customerSubservices;
        for (let i = 0; i < customerSubservices.length; i++) {
            let cs = customerSubservices[i];
            if (cs.subserviceType === type) {
                cs.configs = JSON.stringify(data);
                customerSubservices[i] = cs;
                break;
            }
        }
        this.setState({customerSubservices});
    }

    render() {
        let {subServiceList} = this.state;
        let hasSMS = subServiceList.indexOf("SMS") !== -1;
        let hasEmail = subServiceList.indexOf("MAIL") !== -1;
        let hasHsm = subServiceList.indexOf("HSM") !== -1;
        let activeTab = this.getActiveTab();

        return <DefaultModal disabled={this.state.btnDisabled}
                             isOpen={this.props.modal}
                             size="lg"
                             title="Alt Hizmet Seimi"
                             confirmBtnText="Kaydet"
                             cancelBtnText="Kapat"
                             onConfirm={this.saveClicked}
                             onCancel={() => this.props.toggleModal(false)}>
            <div style={{marginTop: '20px'}}>
                {this.state.loading && <ReactLoading type="spinningBubbles" style={{
                    width: "90px",
                    height: "90px",
                    margin: "20px auto 0 auto",
                    fill: "#888"
                }}/>}
                {!this.state.loading &&
                    <Col sm="12">
                        <Card className="">
                            <Col sm="7" style={{padding: 16}}>
                                <FormGroup row>
                                    <Label sm={3}>Alt Hizmetler:</Label>
                                    <Col sm={9}>
                                        <Select
                                            className="select-control"
                                            name="employeeNtfResultList"
                                            id="employeeNtfResultList"
                                            multi={true}
                                            placeholder="Seiniz..."
                                            value={this.getValues()}
                                            onChange={(e) => e && this.onChangeValues(e)}
                                            options={this.state.subservices.map(subserviceType => ({
                                                value: subserviceType,
                                                label: subserviceType
                                            }))}/> </Col>
                                </FormGroup>
                            </Col>
                            <CardHeader>
                                <div className="nav-tabs-navigation">
                                    <div className="nav-tabs-wrapper">
                                        <ul className="nav tabBackground nav-tabs" data-tabs="tabs">
                                            {hasSMS &&
                                                <li className="nav-item">
                                                    <a className={"nav-link " + (activeTab === 'SMS' ? 'active' : '')}
                                                       onClick={() => {
                                                           this.toggle('SMS');
                                                       }}
                                                       data-toggle="tab">SMS</a>
                                                </li>
                                            }
                                            {hasEmail &&
                                                <li className="nav-item">
                                                    <a className={"nav-link " + (activeTab === 'MAIL' ? 'active' : '')}
                                                       onClick={() => {
                                                           this.toggle('MAIL');
                                                       }}
                                                       data-toggle="tab">E-Posta</a>
                                                </li>
                                            }
                                            {hasHsm &&
                                                <li className="nav-item">
                                                    <a className={"nav-link " + (activeTab === 'HSM' ? 'active' : '')}
                                                       onClick={() => {
                                                           this.toggle('HSM');
                                                       }}
                                                       data-toggle="tab">HSM</a>
                                                </li>
                                            }
                                        </ul>
                                    </div>
                                </div>
                            </CardHeader>
                            <TabContent activeTab={activeTab}>
                                {hasSMS &&
                                    <TabPane tabId="SMS">
                                        <SMSConfiguration conf={this.findFirst("SMS")}
                                                          onChange={(conf) => this.changeCustomerSubService("SMS", conf)}/>
                                    </TabPane>
                                }
                                {hasEmail &&
                                    <TabPane tabId="MAIL">
                                        <MailConfiguration conf={this.findFirst("MAIL")}
                                                           onChange={(conf) => this.changeCustomerSubService("MAIL", conf)}/>
                                    </TabPane>
                                }
                                {hasHsm &&
                                    <TabPane tabId="HSM">
                                        <HSMConfiguration conf={this.findFirst("HSM")}
                                                          onChange={(conf) => this.changeCustomerSubService("HSM", conf)}/>
                                    </TabPane>
                                }
                            </TabContent>
                        </Card>
                    </Col>
                }
            </div>
        </DefaultModal>
    }

}

export default ConfigurationModal;
