import React, {Component} from 'react';
import GenericConfiguration from "./GenericConfiguration";

class HSMConfiguration extends Component {
    constructor(props){
        super(props);
        this.state={
            error:{}
        };
    }

    render(){
        const configs = [
            {
                label: "Kullanc Ad :",
                accessor: "userName",
                type: "text"
            }
        ];

        let {error, conf} = this.state;
        return <GenericConfiguration
            configs={configs}
            data={this.props.conf}
            onChange={(conf) => this.props.onChange(conf)}
            error={error}/>;
    }
}

export default HSMConfiguration;