import React, {Component} from 'react';
import {Carousel, CarouselIndicators, CarouselItem, Col, Row} from "reactstrap";
import QuickAccessCard from "../../components/QuickAccessCard";
import {Bar} from 'react-chartjs-2';
import {checkGrantToDisplay, showAxiosError} from "../../helper/helper";
import {requestK} from "../../helper/APIUtils";
import Datetime from "react-datetime";
import 'moment/locale/tr';
import ApproveDocumentViewer from "./approval/helper/ApproveDocumentViewer";

import {
    BORDRO_IMPORT,
    DASHBOARD_IK,
    FORM_SEND,
    FORM_TEMPLATES,
    PERINFO_FORMS_STATE
} from "../../common/authorities/GrantDef";

class HRDashboard extends Component {
    constructor(props) {
        super(props);
        this.state = {
            idList: [],
            horizontalBarData: {
                labels: ['Dalm'],
                datasets: [],
                showPopUp: false,
            },
            companyImage: process.env.REACT_APP_CONTEXT_NAME + "/image/loading.gif",
            peykName: "peyk",
            selectedTerm: Datetime.moment().subtract(0, 'months'),
            counter: 0,
            closeButton: true,
            infoErrorOccurence: false,
            activeIndex: 0,
            items: [],
            image: [],
            url: [],
            selectedUrl: null,
            currentIndex: 0,
            showPopUp: false,
            disabledPreviewBtn: false
        };
        this.getInfos = this.getInfos.bind(this);
        this.getCount = this.getCount.bind(this);
        this.onClickDetail = this.onClickDetail.bind(this);
        this.getCompanyImage = this.getCompanyImage.bind(this);
        this.createStateValue = this.createStateValue.bind(this);
        this.waitingPerInfoFormCount = this.waitingPerInfoFormCount.bind(this);
    }

    onExiting = () => {
        this.animating = true;
    }

    onExited = () => {
        this.animating = false;
    }

    next = () => {
        if (this.animating) return;
        const nextIndex = this.state.activeIndex === this.state.items.length - 1 ? 0 : this.state.activeIndex + 1;
        this.setState({activeIndex: nextIndex});
    }

    previous = () => {
        if (this.animating) return;
        const nextIndex = this.state.activeIndex === 0 ? this.state.items.length - 1 : this.state.activeIndex - 1;
        this.setState({activeIndex: nextIndex});
    }

    goToIndex = (newIndex) => {
        if (this.animating) return;
        this.setState({activeIndex: newIndex});
    }

    carouselDataGenerator = (datasets) => {
        let i, j, temp, newItems = [], index = -1, chunk = 18;
        for (i = 0, j = datasets.length; i < j; i += chunk) {
            index++;
            temp = datasets.slice(i, i + chunk);

            newItems.push({
                labels: ['Dalm'],
                datasets: temp,
                showPopUp: false
            })
        }
        this.setState({
            items: newItems
        })
    }

    hasGrant = (grant) => {
        return this.props.hasGrant(grant);
    };

    rowCreated(rowName) {
        let idList = this.state.idList;

        if (!idList.includes(rowName)) {
            idList.push(rowName);

            this.setState({
                idList
            });
            return rowName;
        }
    }

    waitingPerInfoFormCount() {
        let params = {
            url: '/survey/waitingPerInfoFormCount',
            method: "get",
        };
        requestK(params)
            .then(() => {
            })
            .catch((error) => {
                showAxiosError(error);
            });

    }

    datasetKeyProvider() {
        return Math.random().toString();
    }

    onClickDetail(path) {
        this.props.history.push(path)
    }

    onClickSelectedTermDetail = (path) => {
        this.props.history.push({pathname: path, selectedTerm: this.state.selectedTerm.format('YYYY-MM')});
    };

    isKepOwner() {
        return this.props.currentUser && !!this.props.currentUser.isKepOwner;
    }

    isEmployee() {
        return !(this.hasGrant(DASHBOARD_IK));
    }

    componentDidMount() {
        this.getInfos();
        window.addEventListener('resize', this.calculateSpecificHeight);
        this.getCompanyImage();
        this.getPriorityWaiitings();
        this.getAds();
    }

    componentDidUpdate(prevProps, prevState) {
        if (!this.state.showPopUp && prevState.showPopUp) {
            this.startSlideshow();
        }
        if (prevState.image !== this.state.image) {
            this.startSlideshow();
        }
    }

    getAds = () => {
        let self = this;
        const params = {
            url: '/advertisement/getads',
            method: 'get',
        };

        self.setState({ disabledPreviewBtn: true });

        requestK(params)
            .then(response => {
                const data = response.data;

                const ads = Object.keys(data).reduce((acc, key) => {
                    acc.images.push(data[key].image);
                    acc.urls.push(data[key].url);
                    return acc;
                }, { images: [], urls: [] });

                self.setState({
                    image: ads.images,
                    url: ads.urls,
                    currentIndex: 0,
                    showPopUp: false,
                    disabledPreviewBtn: false,
                });
            })
            .catch(error => {
                showAxiosError(error);
                self.setState({
                    showPopUp: false,
                    disabledPreviewBtn: false,
                });
            });
    };

    handleImageClick = (url) => {
        if (url) {
            this.stopSlideshow();
            this.setState({
                selectedUrl: url,
                showPopUp: true,
            });
        }
    };

    getPriorityWaiitings = () => {
        let self = this;
        let params = {
            url: '/approval/priorityWaitings',
            method: "get",
        };
        requestK(params)
            .then((response) => {
                if (response.data && response.data.length) {
                    let resList = response.data;
                    let res = resList[0];
                    self.setState({showPopUp: true, selectedDocument: res})
                    self.previewPriorityDocument(res);
                } else {
                    self.setState({showPopUp: false});
                }
            })
            .catch(function (error) {
                self.setState({
                    showPopUp: false,
                })
                showAxiosError(error);
            });
    }

    previewPriorityDocument = (document) => {
        let self = this;
        let params = {
            url: `/approval/previewContent`,
            responseType: 'arraybuffer',
            method: "post",
            data: {
                documentType: document.documentType,
                id: document.document.id,
                html: false
            }
        };
        self.setState({disabledPreviewBtn: true, html: false});
        requestK(params)
            .then(function (response) {
                let blob = new Blob([response.data], {type: response.headers["content-type"]});
                let urlCreator = window.URL || window.webkitURL;
                let url = urlCreator.createObjectURL(blob);
                self.setState({url, selected: document, showPopUp: true, timestampDocument: true});
            }).catch(function (error) {
            showAxiosError(error);
            self.setState({showPopUp: false, disabledPreviewBtn: false});
        });
    };

    previewPrioritySignedDocument = () => {
        let self = this;
        const document = self.state.selectedDocument;
        let params = {
            url: `/approval/previewContent`,
            responseType: 'arraybuffer',
            method: "post",
            data: {
                documentType: document.documentType,
                id: document.document.id,
                html: true
            }
        };
        self.setState({disabledPreviewBtn: true});
        requestK(params)
            .then(function (response) {
                let blob = new Blob([response.data], { type: 'text/html' });
                let urlCreator = window.URL || window.webkitURL;
                let url = urlCreator.createObjectURL(blob);
                self.setState({url, selected: document, showPopUp: true, timestampDocument: false, html: true, print: true});
            }).catch(function (error) {
            showAxiosError(error);
            self.setState({showPopUp: false, disabledPreviewBtn: false});
        });
    };

    setMediaViewer = () => {
        this.setState({showPopUp: false, selectedUrl: null, currentIndex: 0, selected: null,});
    };

    refreshTable = () => {
        this.setState({showPopUp: false});
        window.location.reload(false);
    }

    getInfos() {
        let self = this;
        if (self.isKepOwner()) {
            self.getCount("sentBordroK", false);
            self.getCount("sentBordroK", true);
            self.getCount("unsignedBordroK", false);
            self.getCount("unsignedBordroK", true);
            self.getCount("unreadBordroK", false);
            self.getCount("unreadBordroK", true);
            self.getCount("sentFormK", false);
            self.getCount("sentFormK", true);
            self.getCount("unsignedFormK", false);
            self.getCount("unsignedFormK", true);
            self.getCount("unreadFormK", false);
            self.getCount("unreadFormK", true);
            self.setState({peykName: "kepik"});
        } else {
            self.getCount("sentBordro", false);
            self.getCount("sentBordro", true);
            self.getCount("unsignedBordro", false);
            self.getCount("unsignedBordro", true);
            self.getCount("unreadBordro", false);
            self.getCount("unreadBordro", true);
            self.getCount("sentForm", false);
            self.getCount("sentForm", true);
            self.getCount("unsignedForm", false);
            self.getCount("unsignedForm", true);
            self.getCount("unreadForm", false);
            self.getCount("unreadForm", true);
            self.getCount("waitings", false);
            self.getCount("waitingPerInfoFormCount", false);
            self.getCount("waitingPerInfoFormAllCount", false);
            self.setState({peykName: "peyk"});
        }
        self.getCount("groupByFormTypeData", false);
    }

    componentWillUnmount() {
        this.stopSlideshow();
        window.addEventListener('resize', this.calculateSpecificHeight);
    }

    startSlideshow = () => {
        this.intervalId = setInterval(() => {
            this.setState((prevState) => ({
                currentIndex: (prevState.currentIndex + 1) % prevState.image.length
            }));
        }, 5000);
    };

    stopSlideshow = () => {
        if (this.intervalId) {
            clearInterval(this.intervalId);
        }
    };

    createStateValue(response, type, isLastMonth) {
        let self = this;
        let key = type + (isLastMonth ? "M" : "");
        let responseKey = type + (self.isKepOwner() ? "K" : "") + (isLastMonth ? "M" : "");
        return {[key]: (response.data[responseKey])};
    }

    getCount(type, isLastMonth) {
        let self = this;
        let params = {
            url: `/peykhomepage/infos`,
            method: "post",
            data: {
                type: type,
                isLastMonth: isLastMonth,
                selectedTerm: self.state.selectedTerm,
                employeeId: ((self.props.currentUser && self.props.currentUser.employeeId) ? self.props.currentUser.employeeId : null)
            }
        };
        requestK(params)
            .then(response => {
                if (type === "sentBordroK") {
                    self.setState(this.createStateValue(response, "sentBordroCount", isLastMonth));
                } else if (type === "unsignedBordroK") {
                    self.setState(this.createStateValue(response, "unsignedBordroCount", isLastMonth));
                } else if (type === "unreadBordroK") {
                    self.setState(this.createStateValue(response, "unreadBordroCount", isLastMonth));
                } else if (type === "sentFormK") {
                    self.setState(this.createStateValue(response, "sentFormCount", isLastMonth));
                } else if (type === "unsignedFormK") {
                    self.setState(this.createStateValue(response, "unsignedFormCount", isLastMonth));
                } else if (type === "unreadFormK") {
                    self.setState(this.createStateValue(response, "unreadFormCount", isLastMonth));
                } else if (type === "sentBordro") {
                    self.setState(this.createStateValue(response, "sentBordroCount", isLastMonth));
                } else if (type === "unsignedBordro") {
                    self.setState(this.createStateValue(response, "unsignedBordroCount", isLastMonth));
                } else if (type === "unreadBordro") {
                    self.setState(this.createStateValue(response, "unreadBordroCount", isLastMonth));
                } else if (type === "sentForm") {
                    self.setState(this.createStateValue(response, "sentFormCount", isLastMonth));
                } else if (type === "unsignedForm") {
                    self.setState(this.createStateValue(response, "unsignedFormCount", isLastMonth));
                } else if (type === "unreadForm") {
                    self.setState(this.createStateValue(response, "unreadFormCount", isLastMonth));
                } else if (type === "waitings") {
                    self.setState(this.createStateValue(response, "waitings", isLastMonth));
                } else if (type === "waitingPerInfoFormCount") {
                    self.setState({waitingPerInfoFormCount: response.data["waitingPerInfoFormCount"]});
                } else if (type === "waitingPerInfoFormAllCount") {
                    self.setState({waitingPerInfoFormAllCount: response.data["waitingPerInfoFormAllCount"]});
                } else if (type === "groupByFormTypeData") {
                    let result = response.data.groupByFormTypeData;
                    let horizontalBarData = self.state.horizontalBarData;

                    let datasets = [{
                        label: "",
                        data: [0],
                        backgroundColor: 'rgba(0,0,0,0)',
                        borderColor: 'rgba(0,0,0,0)',
                        borderWidth: 0,
                        hoverBackgroundColor: 'rgba(0,0,0,0)',
                        hoverBorderColor: 'rgba(0,0,0,0)',
                    }];
                    let totalCount = 0;
                    for (let i = 0; i < result.length; i++) {
                        let firstColor = Math.floor((Math.random() * 255)) + ',' + Math.floor((Math.random() * 255)) + ',' + Math.floor((Math.random() * 255));
                        datasets.push({
                            label: result[i][0],
                            datasetKeyProvider: self.datasetKeyProvider(),
                            backgroundColor: 'rgba(' + firstColor + ',0.2)',
                            borderColor: 'rgba(' + firstColor + ',1)',
                            borderWidth: 1,
                            data: [result[i][1]]
                        });
                        totalCount = totalCount + result[i][1];
                    }
                    horizontalBarData["datasets"] = datasets;

                    self.setState({horizontalBarData, totalCount});

                    this.carouselDataGenerator(datasets);
                } else {

                }
            }).catch(function (error) {
            if (!self.state.infoErrorOccurence) {
                showAxiosError(error);
                self.setState({infoErrorOccurence: true});
            }
        });
    }

    calculateSpecificHeight() {
        if (window.innerWidth > 991 && document.getElementById('heightGuideDiv')) {
            return document.getElementById('heightGuideDiv').offsetHeight.toString();
        } else {
            return null;
        }
    }

    getCompanyImage() {
        let self = this;
        if (!self.isKepOwner()) {
            let params = {
                url: '/companyinfo/readCompanyImage',
                method: "get"
            };
            requestK(params)
                .then(function (response) {
                    self.setState({companyImage: response.data !== "" ? response.data : "data:image/jpeg;base64,"});
                }).catch(function (error) {
                showAxiosError(error);
            });
        } else {
            self.setState({companyImage: "data:image/png;base64,"});
        }
    }

    render() {
        const { image, currentIndex, url, showPopUp } = this.state;
        const {activeIndex} = this.state;
        const imgStyles = {
            width: '100%',
            height: '100%',
            maxWidth: '100%',
            maxHeight: '100%',
            objectFit: 'contain',
            cursor: 'pointer'
        };
        const slides = this.state.items.map((item) => {

            return (
                <CarouselItem
                    onExiting={this.onExiting}
                    onExited={this.onExited}
                    key={Math.random()}
                >
                    <Bar data={item} height={window.innerWidth < 500 ? 350 : 280}
                         options={{
                             maintainAspectRatio: false,
                             scales: {xAxes: [{maxBarThickness: 40}], yAxes: [{minBarLength: 3}]}
                         }}/>
                </CarouselItem>
            );
        });

        const tinyCardTitleDesign = {
            color: '#ececec',
            fontFamily: 'Montserrat',
            //fontSize: '1.3em',
            paddingLeft: '10px'
        };

        const tinyCardNumberDesign = {
            color: '#ececec',
            fontFamily: 'Montserrat',
            fontSize: '1.3em',
        };

        const tinyCardArrowDesign = {
            textAlign: 'center',
            fontSize: '1.3em',
            color: '#fbb03b',
            padding: '2px 0 0 10px'
        };

        const longCardTitleDesign = {
            fontFamily: 'Montserrat',
            fontSize: '1.4em',
            color: '#8390A4',
            fontWeight: 'bolder',
            padding: '0'
        };

        const longCardInfoStyle = {
            fontFamily: 'Montserrat',
            fontSize: '2.4em',
            fontWeight: 'normal',
            padding: '0'
        };

        const longCardCellStyle = {
            margin: '10px 0 0 0',
            borderRight: 'solid',
            borderWidth: 'thin',
            borderColor: '#cecece'
        };

        let sentOrUnread = !checkGrantToDisplay(this, [DASHBOARD_IK]) ? "Okunmam" : "Gnderilen";
        let unsignedOrCompleted = !checkGrantToDisplay(this, [DASHBOARD_IK]) ? "Tamamlanan" : "mza Bekleyen";
        let unreadOrTotal = !checkGrantToDisplay(this, [DASHBOARD_IK]) ? "Toplam" : "Okunmam";

        let sentOrUnreadBordro = !checkGrantToDisplay(this, [DASHBOARD_IK]) ? (this.state.unreadBordroCountM || 0) : (this.state.sentBordroCountM || 0);
        let unreadOrTotalBordro = !checkGrantToDisplay(this, [DASHBOARD_IK]) ? (this.state.sentBordroCountM || 0) : (this.state.unreadBordroCountM || 0);
        let unsignedOrCompletedBordro = !checkGrantToDisplay(this, [DASHBOARD_IK]) ? (unreadOrTotalBordro - sentOrUnreadBordro) : (this.state.unsignedBordroCountM || 0);
        let sentOrUnreadForm = !checkGrantToDisplay(this, [DASHBOARD_IK]) ? (this.state.unreadFormCountM || 0) : (this.state.sentFormCountM || 0);
        let unreadOrTotalForm = !checkGrantToDisplay(this, [DASHBOARD_IK]) ? (this.state.sentFormCountM || 0) : (this.state.unreadFormCountM || 0);
        let unsignedOrCompletedForm = !checkGrantToDisplay(this, [DASHBOARD_IK]) ? (unreadOrTotalForm - sentOrUnreadForm) : (this.state.unsignedFormCountM || 0);

        const barBordroData = {
            labels: ['Bordro Dalm'],
            datasets: [
                {
                    label: "",
                    data: [0],
                    backgroundColor: 'rgba(0,0,0,0)',
                    borderColor: 'rgba(0,0,0,0)',
                    borderWidth: 0,
                    hoverBackgroundColor: 'rgba(0,0,0,0)',
                    hoverBorderColor: 'rgba(0,0,0,0)',
                },
                {
                    label: sentOrUnread,
                    backgroundColor: 'rgba(226,115,33,0.2)',
                    borderColor: 'rgba(226,115,33,1)',
                    borderWidth: 1,
                    hoverBackgroundColor: 'rgba(226,115,33,0.4)',
                    hoverBorderColor: 'rgba(226,115,33,1)',
                    data: [sentOrUnreadBordro]
                },
                {
                    label: unsignedOrCompleted,
                    backgroundColor: 'rgba(41,171,226,0.2)',
                    borderColor: 'rgba(41,171,226,1)',
                    borderWidth: 1,
                    hoverBackgroundColor: 'rgba(41,171,226,0.4)',
                    hoverBorderColor: 'rgba(41,171,226,1)',
                    data: [unsignedOrCompletedBordro]
                },
                {
                    label: unreadOrTotal,
                    backgroundColor: 'rgba(53,216,204,0.2)',
                    borderColor: 'rgba(53,216,204,1)',
                    borderWidth: 1,
                    hoverBackgroundColor: 'rgba(53,216,204,0.4)',
                    hoverBorderColor: 'rgba(53,216,204,1)',
                    data: [unreadOrTotalBordro]
                }
            ]
        };

        const barFormData = {
            labels: ['Form Dalm'],
            datasets: [
                {
                    label: "",
                    data: [0],
                    backgroundColor: 'rgba(0,0,0,0)',
                    borderColor: 'rgba(0,0,0,0)',
                    borderWidth: 0,
                    hoverBackgroundColor: 'rgba(0,0,0,0)',
                    hoverBorderColor: 'rgba(0,0,0,0)',
                },
                {
                    label: sentOrUnread,
                    backgroundColor: 'rgba(212,20,90,0.2)',
                    borderColor: 'rgba(212,20,90,1)',
                    borderWidth: 1,
                    hoverBackgroundColor: 'rgba(212,20,90,0.4)',
                    hoverBorderColor: 'rgba(212,20,90,1)',
                    data: [sentOrUnreadForm]
                },
                {
                    label: unsignedOrCompleted,
                    backgroundColor: 'rgba(255,147,30,0.2)',
                    borderColor: 'rgba(255,147,30,1)',
                    borderWidth: 1,
                    hoverBackgroundColor: 'rgba(255,147,30,0.4)',
                    hoverBorderColor: 'rgba(255,147,30,1)',
                    data: [unsignedOrCompletedForm]
                },
                {
                    label: unreadOrTotal,
                    backgroundColor: 'rgba(41,171,226,0.2)',
                    borderColor: 'rgba(41,171,226,1)',
                    borderWidth: 1,
                    hoverBackgroundColor: 'rgba(41,171,226,0.4)',
                    hoverBorderColor: 'rgba(41,171,226,1)',
                    data: [unreadOrTotalForm]
                }
            ]
        };
        let companyImage = this.state.companyImage;

        let unreadBordro = (this.state.unreadBordroCount || this.state.unreadBordroCount === 0) ? this.state.unreadBordroCount : "...";
        let totalBordro = (this.state.sentBordroCount || this.state.sentBordroCount === 0) ? this.state.sentBordroCount : "...";
        let completedBordro = (totalBordro >= 0 && unreadBordro >= 0) ? (totalBordro - unreadBordro) : "...";
        let sentBordro = (this.state.sentBordroCount || this.state.sentBordroCount === 0) ? this.state.sentBordroCount : "...";
        let unsignedBordo = (this.state.unsignedBordroCount || this.state.unsignedBordroCount === 0) ? this.state.unsignedBordroCount : "...";

        let unreadForm = (this.state.unreadFormCount || this.state.unreadFormCount === 0) ? this.state.unreadFormCount : "...";
        let totalForm = (this.state.sentFormCount || this.state.sentFormCount === 0) ? this.state.sentFormCount : "...";
        let completedForm = (totalForm >= 0 && unreadForm >= 0) ? (totalForm - unreadForm) : "...";
        let sentForm = (this.state.sentFormCount || this.state.sentFormCount === 0) ? this.state.sentFormCount : "...";
        let unsignedForm = (this.state.unsignedFormCount || this.state.unsignedFormCount === 0) ? this.state.unsignedFormCount : "...";
        let waitings = this.state.waitings ? this.state.waitings : 0;
        let perInfoFormWaiting = this.state.waitingPerInfoFormCount ? this.state.waitingPerInfoFormCount : "";
        let perInfoFormAllWaiting = this.state.waitingPerInfoFormAllCount ? this.state.waitingPerInfoFormAllCount : "";
        return (
            <Row style={{padding: '3% 0 0 0'}}>
                <Col sm='12' md='12' lg='12'>
                    <Row>
                        <Col sm='12' md='12' lg='8' id='heightGuideDiv'>

                            {/*style={{margin: '0 0 15px 0'}}*/}

                            <Row id={this.state.idList.includes('row1') ? 'row1' : this.rowCreated('row1')}
                                 style={{marginBottom: '20px'}}>
                                <Col sm='12' md='3' lg='3'>
                                    <a
                                        onClick={() => this.onClickDetail(!this.hasGrant(DASHBOARD_IK) ? `/${this.state.peykName}/approval` : this.hasGrant(BORDRO_IMPORT) ? `/${this.state.peykName}/bordroimport` : `/${this.state.peykName}/unauthorized`)}>
                                        <div className="quick-access-card-container">
                                            <QuickAccessCard bodyBg='#0071bc'>
                                                <Row
                                                    style={{padding: waitings > 0 ? '25px 15px 0px' : '25px 15px 25px 15px'}}>
                                                    <Col sm='10' className='textAlignHeaders'
                                                         style={{
                                                             color: '#ececec',
                                                             fontFamily: 'Montserrat',
                                                             //fontSize: '1.3em',
                                                             paddingLeft: '10px',
                                                             paddingRight: '5px'
                                                         }}
                                                    >
                                                        {!this.hasGrant(DASHBOARD_IK) &&
                                                            <label style={{margin: '0 0 0 0'}}>Onay
                                                                Bekleyenler&nbsp;&nbsp;

                                                            </label>}
                                                        {this.hasGrant(DASHBOARD_IK) &&
                                                            <label style={{margin: '0 0 0 0'}}>Bordro Gnder</label>}
                                                    </Col>

                                                    <Col sm='2' style={tinyCardArrowDesign}>
                                                        <i className="fas fa-caret-right" style={{marginTop: '0px'}}/>
                                                    </Col>
                                                </Row>
                                                <Row style={tinyCardNumberDesign}>
                                                    {waitings > 0 &&
                                                        <label id='number' style={{
                                                            color: 'crimson',
                                                            fontWeight: 'bold',
                                                            fontSize: '14px',
                                                            verticalAlign: 'sub',
                                                            backgroundColor: 'white',
                                                            margin: '0px auto',
                                                            marginBottom: '5px',
                                                            width: '40px',
                                                            border: '2px solid #ffffff',
                                                            borderRadius: '50%'
                                                        }}>{waitings}</label>}
                                                </Row>
                                            </QuickAccessCard>
                                        </div>
                                    </a>
                                </Col>
                                <Col sm='12' md='3' lg='3'>
                                    <a
                                        onClick={() => this.onClickDetail(!this.hasGrant(DASHBOARD_IK) ? (`/${this.state.peykName}/bordroreports/` + this.props.currentUser.employeeId) : this.hasGrant(FORM_SEND) ? `/${this.state.peykName}/formsend` : `/${this.state.peykName}/unauthorized`)}>
                                        <div className="quick-access-card-container">
                                            <QuickAccessCard bodyBg='#009245'>
                                                <Row
                                                    style={{padding: waitings > 0 ? '25px 15px 30px' : '25px 15px 25px 15px'}}>
                                                    <Col sm='10' className='textAlignHeaders'
                                                         style={tinyCardTitleDesign}>
                                                        <label
                                                            style={{margin: '0 0 0 0'}}>{!this.hasGrant(DASHBOARD_IK) ? "cret Bordrolar" : "Form Gnder"}</label>
                                                    </Col>

                                                    <Col sm='2' style={tinyCardArrowDesign}>
                                                        <i className="fas fa-caret-right"/>
                                                    </Col>
                                                </Row>
                                            </QuickAccessCard>
                                        </div>
                                    </a>
                                </Col>
                                <Col sm='12' md='3' lg='3'>
                                    <a
                                        onClick={() => this.onClickDetail(!this.hasGrant(DASHBOARD_IK) ? (`/${this.state.peykName}/formreports/` + this.props.currentUser.employeeId) : this.hasGrant(FORM_TEMPLATES) ? `/${this.state.peykName}/formcreation` : `/${this.state.peykName}/unauthorized`)}>
                                        <div className="quick-access-card-container">
                                            <QuickAccessCard bodyBg='#404157'>
                                                <Row
                                                    style={{padding: waitings > 0 ? '25px 15px 30px' : '25px 15px 25px 15px'}}>
                                                    <Col sm='10' className='textAlignHeaders'
                                                         style={tinyCardTitleDesign}>
                                                        <label
                                                            style={{margin: '0 0 0 0'}}>{!this.hasGrant(DASHBOARD_IK) ? "Formlar" : "Taslak Hazrla"}</label>
                                                    </Col>
                                                    <Col sm='2' style={tinyCardArrowDesign}>
                                                        <i className="fas fa-caret-right"/>
                                                    </Col>
                                                </Row>
                                            </QuickAccessCard>
                                        </div>
                                    </a>
                                </Col>
                                <Col sm='12' md='3' lg='3'>
                                    <a
                                        onClick={() => this.onClickDetail(!(this.hasGrant(DASHBOARD_IK)) ? (`/${this.state.peykName}/employeePersonalInfo`) : this.hasGrant(PERINFO_FORMS_STATE) ? (`/${this.state.peykName}/personalFormApprovalPage`) : `/${this.state.peykName}/unauthorized`)}>
                                        <div className="quick-access-card-container">
                                            <QuickAccessCard bodyBg='#ca8b26'>
                                                <Row
                                                    style={{padding: (perInfoFormAllWaiting || perInfoFormWaiting) > 0 ? '25px 15px 0px' : '25px 15px 25px 15px'}}>
                                                    <Col sm='10' className='textAlignHeaders'
                                                         style={{
                                                             color: '#ececec',
                                                             fontFamily: 'Montserrat',
                                                             //fontSize: '1.3em',
                                                             paddingLeft: '10px',
                                                             paddingRight: '5px'
                                                         }}>
                                                        <label
                                                            style={{margin: '0 0 0 0'}}>{(this.hasGrant(DASHBOARD_IK) || this.hasGrant(PERINFO_FORMS_STATE)) ? "zlk Form Onay" : "zlk Formlar"}</label>
                                                    </Col>

                                                    <Col sm='2' style={tinyCardArrowDesign}>
                                                        <label
                                                            style={{margin: '0 0 0 0'}}>
                                                            <i className="fas fa-caret-right"/>
                                                        </label>
                                                    </Col>
                                                </Row>
                                                {(((this.hasGrant(DASHBOARD_IK) || this.hasGrant(PERINFO_FORMS_STATE)) && perInfoFormAllWaiting !== "" && perInfoFormAllWaiting >= 0) ||
                                                        (!(this.hasGrant(DASHBOARD_IK) || this.hasGrant(PERINFO_FORMS_STATE)) && (perInfoFormWaiting !== "" && perInfoFormWaiting >= 0))) &&
                                                    <Row style={tinyCardNumberDesign}>
                                                        <label id='number' style={{
                                                            color: 'crimson',
                                                            fontWeight: 'bold',
                                                            fontSize: '14px',
                                                            verticalAlign: 'sub',
                                                            backgroundColor: 'white',
                                                            margin: '0px auto',
                                                            marginBottom: '5px',
                                                            width: '40px',
                                                            border: '2px solid #ffffff',
                                                            borderRadius: '50%'
                                                        }}>{((this.hasGrant(DASHBOARD_IK) || this.hasGrant(PERINFO_FORMS_STATE))) ? ((perInfoFormAllWaiting !== "" && perInfoFormAllWaiting > 0) ? perInfoFormAllWaiting : "") : ((perInfoFormWaiting !== "" && perInfoFormWaiting > 0) ? perInfoFormWaiting : "")}</label>
                                                    </Row>}
                                            </QuickAccessCard>

                                        </div>
                                    </a>
                                </Col>
                            </Row>

                            <Row id={this.state.idList.includes('row2') ? 'row2' : this.rowCreated('row2')}
                                 style={{marginBottom: '20px'}}>

                                <Col sm='12' md='12' lg='12'>
                                    <QuickAccessCard title='BORDRO' headerBg='#663f4e' bodyBg='#f2eeeb'>
                                        <Row style={{padding: '15px 0 15px 0'}}>
                                            <Col sm='12' md='12' lg='4' style={longCardCellStyle}>
                                                <Col sm='12' style={longCardTitleDesign}>
                                                    <labels>{sentOrUnread}</labels>
                                                </Col>
                                                <Col sm='12' style={longCardInfoStyle}>
                                                    <labels>{this.isEmployee() ? (unreadBordro) : (sentBordro)}</labels>
                                                </Col>
                                            </Col>

                                            <Col sm='12' md='12' lg='4' style={longCardCellStyle}>
                                                <Col sm='12' style={longCardTitleDesign}>
                                                    <labels>{unsignedOrCompleted}</labels>
                                                </Col>
                                                <Col sm='12' style={longCardInfoStyle}>
                                                    <labels>{this.isEmployee() ? (completedBordro) : (unsignedBordo)}</labels>
                                                </Col>
                                            </Col>

                                            <Col sm='12' md='12' lg='4' style={{margin: '10px 0 0 0'}}>
                                                <Col sm='12' style={longCardTitleDesign}>
                                                    <labels>{unreadOrTotal}</labels>
                                                </Col>
                                                <Col sm='12' style={longCardInfoStyle}>
                                                    <labels>{this.isEmployee() ? (totalBordro) : (unreadBordro)}</labels>
                                                </Col>
                                            </Col>
                                        </Row>
                                    </QuickAccessCard>
                                </Col>
                            </Row>

                            <Row id={this.state.idList.includes('row3') ? 'row3' : this.rowCreated('row3')}
                                 style={{marginBottom: '20px'}}>

                                <Col sm='12' md='12' lg='12'>
                                    <QuickAccessCard title='FORM' headerBg='#ceac74' bodyBg='#f2eeeb'>
                                        <Row style={{padding: '15px 0 15px 0'}}>
                                            <Col sm='12' md='12' lg='4' style={longCardCellStyle}>
                                                <Col sm='12' style={longCardTitleDesign}>
                                                    <labels>{sentOrUnread}</labels>
                                                </Col>
                                                <Col sm='12' style={longCardInfoStyle}>
                                                    <labels>{this.isEmployee() ? unreadForm : sentForm}</labels>
                                                </Col>
                                            </Col>

                                            <Col sm='12' md='12' lg='4' style={longCardCellStyle}>
                                                <Col sm='12' style={longCardTitleDesign}>
                                                    <labels>{unsignedOrCompleted}</labels>
                                                </Col>
                                                <Col sm='12' style={longCardInfoStyle}>
                                                    <labels>{this.isEmployee() ? completedForm : unsignedForm}</labels>
                                                </Col>
                                            </Col>

                                            <Col sm='12' md='12' lg='4' style={{margin: '10px 0 0 0'}}>
                                                <Col sm='12' style={longCardTitleDesign}>
                                                    <labels>{unreadOrTotal}</labels>
                                                </Col>
                                                <Col sm='12' style={longCardInfoStyle}>
                                                    <labels>{this.isEmployee() ? totalForm : unreadForm}</labels>
                                                </Col>
                                            </Col>
                                        </Row>
                                    </QuickAccessCard>
                                </Col>
                            </Row>
                        </Col>

                        <Col sm='12' md='12' lg='4' style={{ textAlign: 'center', maxHeight: '550px', display: 'flex', flexDirection: 'column', alignItems: 'center', gap: '10px' }}>
                            <QuickAccessCard bodyBg='transparent' idList={this.state.idList} customHeight="200px" style={{ flex: '1 0 auto' }}>
                                <img className="simple-text logo-mini" style={{ height: 'inherit', objectFit: 'contain' }}
                                     src={companyImage} alt={"logo"}/>
                            </QuickAccessCard>

                            <QuickAccessCard bodyBg='transparent' idList={this.state.idList} customHeight="232px" style={{ flex: '1 0 auto' }}>
                                {image.length > 0 && currentIndex !== null && currentIndex < image.length && (
                                    <img
                                        src={image[currentIndex]?.startsWith('http') ? image[currentIndex] : `data:image/png;base64,${image[currentIndex]}`}
                                        style={{ height: 'inherit', objectFit: 'contain' }}
                                        onClick={() => this.handleImageClick(url[currentIndex])}
                                        onLoad={this.handleImageLoad}
                                        alt="Reklam"
                                    />
                                )}
                            </QuickAccessCard>
                        </Col>
                    </Row>

                    <Row>

                        <Col sm='12' md='12' lg='8' id="bildirim-dagilimi">
                            <QuickAccessCard bodyBg='transparent' height='100%' title='BLDRM DAILIMI' type='chart'>
                                <Row style={{padding: '3% 0 0 1%'}}>
                                    <Col sm='12'>
                                        <Row>

                                            <Col sm="1" style={{
                                                display: "flex",
                                                justifyContent: "center",
                                                alignItems: "center"
                                            }}>
                                                <span style={{cursor: "pointer"}} onClick={this.previous}><i
                                                    className="fas fa-chevron-left"></i></span>
                                            </Col>

                                            <Col sm='10' id="hr-carousel-container">
                                                <Carousel
                                                    activeIndex={activeIndex}
                                                    next={this.next}
                                                    previous={this.previous}
                                                >
                                                    {slides}
                                                </Carousel>

                                                <CarouselIndicators items={this.state.items} activeIndex={activeIndex}
                                                                    onClickHandler={this.goToIndex}/>

                                            </Col>

                                            <Col sm="1" style={{
                                                display: "flex",
                                                justifyContent: "center",
                                                alignItems: "center"
                                            }}>
                                                <span style={{cursor: "pointer"}} onClick={this.next}><i
                                                    className="fas fa-chevron-right"></i></span>
                                            </Col>
                                        </Row>
                                    </Col>
                                </Row>

                                <Row style={{padding: '0 0 23px 0'}}>

                                    <Col sm='12' style={{padding: '0', fontWeight: 'bold', color: 'lightslategrey'}}>
                                        <label>Toplam: {(this.state.totalCount || this.state.totalCount === 0) ? this.state.totalCount : "..."} Adet</label>
                                    </Col>
                                </Row>
                            </QuickAccessCard>
                        </Col>

                        <Col sm='12' md='12' lg='4'>
                            <QuickAccessCard bodyBg='transparent' title='AYLIK BLDRMLER' type='chart'>
                                <Row style={{padding: '2% 5% 2%'}}>
                                    <div className="col-sm-4">
                                        Ay :
                                    </div>
                                    <div className="col-sm-4">
                                        <Datetime timeFormat={false} dateFormat="MM-YYYY" closeOnSelect={true}
                                                  inputProps={{placeholder: 'AY-YIL'}}
                                                  value={this.state.selectedTerm}
                                                  renderMonth={this.props.renderMonth}
                                                  onChange={(e) => {
                                                      this.setState({selectedTerm: e}, this.getInfos);
                                                  }}/>
                                    </div>
                                </Row>
                                <Row style={{padding: '2% 5% 2%'}}>
                                    <Col sm='6'>
                                        <Row>
                                            <Col sm='12'>
                                                <Bar
                                                    data={barBordroData}
                                                    height={280}
                                                    options={{
                                                        maintainAspectRatio: false
                                                    }}
                                                />
                                            </Col>
                                        </Row>

                                        <Row style={{padding: '15% 40% 0 40%'}}>
                                            <a
                                                onClick={() => this.onClickSelectedTermDetail(this.isEmployee() ?
                                                    `/${this.state.peykName}/bordroreportsMonthly/` + this.props.currentUser.employeeId :
                                                    `/${this.state.peykName}/bordroreportsMonthly`)}>
                                                <Col sm='10'
                                                     style={{
                                                         display: 'inline',
                                                         padding: '0',
                                                         fontWeight: 'bold',
                                                         color: 'lightslategrey'
                                                     }}>
                                                    <label>Bordro</label>
                                                </Col>

                                                <Col sm='2'
                                                     style={{display: 'inline', color: '#FBB03B', paddingLeft: '10px'}}>
                                                    <i className="fas fa-caret-right"/>
                                                </Col>
                                            </a>
                                        </Row>
                                    </Col>

                                    <Col sm='6'>
                                        <Row>
                                            <Col sm='12'>
                                                <Bar
                                                    data={barFormData}
                                                    height={280}
                                                    options={{
                                                        maintainAspectRatio: false
                                                    }}
                                                />
                                            </Col>
                                        </Row>

                                        <Row style={{padding: '15% 40% 0 40%'}}>
                                            <a
                                                onClick={() => this.onClickSelectedTermDetail(this.isEmployee() ?
                                                    `/${this.state.peykName}/formreportsMonthly/` + this.props.currentUser.employeeId :
                                                    `/${this.state.peykName}/formreportsMonthly`)}>
                                                <Col sm='10'
                                                     style={{
                                                         display: 'inline',
                                                         padding: '0',
                                                         fontWeight: 'bold',
                                                         color: 'lightslategrey'
                                                     }}>
                                                    <label style={{width: '37px'}}>Form</label>
                                                </Col>

                                                <Col sm='2'
                                                     style={{display: 'inline', color: '#FBB03B', paddingLeft: '10px'}}>
                                                    <i className="fas fa-caret-right"/>
                                                </Col>
                                            </a>
                                        </Row>
                                    </Col>
                                </Row>

                            </QuickAccessCard>
                        </Col>
                    </Row>
                </Col>
                {(this.state.showPopUp && (this.state.selected || this.state.selectedUrl )) &&
                    <ApproveDocumentViewer
                        url={this.state.url}
                        selectedUrl={this.state.selectedUrl}
                        closeModal={this.setMediaViewer}
                        modal={this.state.showPopUp}
                        pdfData={this.state.pdfData}
                        refresh={this.refreshTable}
                        selected={this.state.selected}
                        closeButton={this.state.selectedUrl ? true : this.state.closeButton}
                        isDeclineAllowed={this.state.selected?.declineAllowed ?? false}
                        html={this.state.html}
                        timestampDocument={this.state.timestampDocument}
                        previewPrioritySignedDocument={this.previewPrioritySignedDocument}
                        print={this.state.print}
                    />
                }
            </Row>
        );
    }
}

export default HRDashboard;