    this.onChangeCustomerSmsCredit = this.onChangeCustomerSmsCredit.bind(this);
    this.onCustomerTypeChange = this.onCustomerTypeChange.bind(this);
    this.onCustomerStatusChange = this.onCustomerStatusChange.bind(this);
    this.onChangeCustomerBranchAddress = this.onChangeCustomerBranchAddress.bind(this);
    this.onCityChange = this.onCityChange.bind(this);
    this.onCitySubdivisionChange = this.onCitySubdivisionChange.bind(this);
    this.getSubdivisionsByCityForCustomer = this.getSubdivisionsByCityForCustomer.bind(this);
    this.getSubdivisionsByCityForBranch = this.getSubdivisionsByCityForBranch.bind(this);
