  fetchValidDataFromChild(data) {
    this.setState({
      childValidInfo: data.valid
    })
  }

  checkForDateConflicts = () => {
    let final = deepCopyObject(this.state.customer);

    for (let i = 0; i < final.customerContractList.length; i++) {
      for(let j = 0; j < final.customerContractList.length; j++){
        const initContract = final.customerContractList[j];
        const finalContract = final.customerContractList[i];
        if (initContract.recordId !== finalContract.recordId) {
          const existingContractStartDate = moment(initContract.contractStartDate, 'YYYY-MM-DD');
          const existingContractEndDate = moment(initContract.contractFinishDate, 'YYYY-MM-DD');
          const newContractStartDate = moment(finalContract.contractStartDate, 'YYYY-MM-DD');
          const newContractEndDate = moment(finalContract.contractFinishDate, 'YYYY-MM-DD');
          if (
              (existingContractStartDate.isSameOrAfter(newContractStartDate) &&
                  existingContractStartDate.isSameOrBefore(newContractEndDate) ) ||
              (existingContractEndDate.isSameOrAfter(newContractStartDate) &&
                  existingContractEndDate.isSameOrBefore(newContractEndDate))
          ) {
            Alert.error('Szleme tarihleri akyor. Ltfen uygun tarihleri sein.', {
              position: 'top-right',
              effect: 'stackslide',
              timeout: 5000
            });
            return true;
          }
        }
      }
    }
    return false;
  };
  checkMandatoryFields = () => {
    let final = deepCopyObject(this.state.customer);
    const mandatoryFields = [
      { field: 'creditPredefined', message: 'Yllk Yklenecek SMS Kontr bo braklamaz!' },
      { field: 'storagePredefined', message: 'zlk Dosya Boyutu(MB) bo braklamaz!' },
      { field: 'employeePredefined', message: 'Personel Says bo braklamaz!' }
    ];

    let hasEmptyField = false;

    for (let i = 0; i < final.customerContractList.length; i++) {
      const finalContract = final.customerContractList[i];

      for (const field of mandatoryFields) {
        if (!finalContract[field.field]) {
          Alert.error(`${field.message}`, {
            position: 'top-right',
            effect: 'stackslide',
            timeout: 5000
          });
          hasEmptyField = true;
        }
      }
    }
    return hasEmptyField;
  }



  checkBeforeSubmit(info) {
    let init = deepCopyObject(this.state.initState);
    let final = deepCopyObject(this.state.customer);
    if (!(_.isEqual(init, final))) {
      let error = {};



      let generalValidity = this.generalInfoChild.validateComponent();
      let generalInfoError = this.generalInfoChild.state.error;
      for (let key in generalInfoError) {

        error[key] = generalInfoError[key]
      }
      if(this.state.customer.mainCustomerId===null&& this.state.customer.isMainCustomer===false){
        generalValidity.valid=false;
        error.tMainCustomer = {message:"Ana irketi seiniz",valid:false}
      }
      if(this.state.customer.mainCustomerId!==null&& this.state.customer.recordId===this.state.customer.mainCustomerId){
        generalValidity.valid=false;
        error.tMainCustomer = {message:"Ana irket kendisi ile ayn olamaz",valid:false}
      }
      let commercialValidity = this.commercialInfoChild.validateComponent();
      let commercialInfoError = this.commercialInfoChild.state.error;
      for (let key in commercialInfoError) {
        error[key] = commercialInfoError[key]
      }

      let contractValidity = {};

      if (!this.state.customer.customerContractList) {
        contractValidity.valid = true;
      } else {
        contractValidity = this.contractInfoChild.validateComponent();
      }
      let contractInfoError = this.contractInfoChild.state.error;
      for (let key in contractInfoError) {
        error[key] = contractInfoError[key]
      }

      this.setState({
        error
      }, function () {
        if (generalValidity.valid && commercialValidity.valid && contractValidity.valid) {
          if (this.state.customer.customerBranchList === null) {
            this.setState({
              branchConfirmModal: true
            })
          } else {
            this.handleSubmit(info);
          }
        } else {
          Alert.error(this.arrangeErrorMessage(), {
            html: true,
            position: 'top-right',
            effect: 'stackslide',
            timeout: 5000
          })
        }
      });
    } else {
      Alert.warning('Herhangi bir deiiklik yapmadnz !', {
        html: true,
        position: 'top-right',
        effect: 'stackslide',
        timeout: 5000
      })
    }
  }

  arrangeErrorMessage() {
    let errorObject = deepCopyObject(this.state.error);

    let errorArray = [];

    for (let key in errorObject) {
      if (errorObject.hasOwnProperty(key)) {
        let error = {};
        if (!!errorObject[key].message && key !== 'tCity') {
          error.field = this.defineReadableFieldName(key);
          error.message = errorObject[key].message;
          errorArray.push(error);
        }
      }
    }

    this.setState({error: errorObject});

    return errorArray.map(error => '<strong>' + error.field + error.message + '</strong><br/>').join('')
  }

