import React, {Component} from 'react';
import {Button, Label, Modal, ModalBody, ModalHeader} from "reactstrap";
import Form from "react-jsonschema-form";
import {requestK} from "../../../helper/APIUtils";
import {showAxiosError} from "../../../helper/helper";
import Select from "react-select";
import DocumentViewer from "./DocumentViewer";
import Swal from "sweetalert2";
import ImageUpload from "../formbuilder/ImageUpload";
import LinkWidget from "../formbuilder/LinkWidget";
import FileChooserWidget from "../formbuilder/FileChooserWidget";
import PdfViewer from "./PdfViewer/PdfViewer";
import {dataURItoBlob} from "@rjsf/utils";

const imageFileChooserWidget = ImageUpload;
const linkWidget = LinkWidget;
const fileChooserWidget = FileChooserWidget;
const widgets = {imageFileChooserWidget, linkWidget, fileChooserWidget};

class PdfComponent extends Component {

    constructor(props) {
        super(props);
        this.myRef = React.createRef();
        this.state = {
            numPages: null,
            pageNumber: 1,
            scale: 1.2,
            activateZoom: false,
            showAnimationArrows: false,
            page: 1,
            scrollTop: 0,
            disabled: true,
            enableWheel: false,
            surveyFormModal: false,
            surveyFormButton: false,
            surveyForm: null,
            surveyFormResponse: null,
            surveyAttachments: [],
            selectedAttachment: "",
            viewer: false,
            menuIsOpen: false,


        };

        this.onDocumentLoad = this.onDocumentLoad.bind(this);
        this.onClickAttachments = this.onClickAttachments.bind(this);
        this.cancelShowSurveyForm = this.cancelShowSurveyForm.bind(this);
        this.getSurveyFormResponse = this.getSurveyFormResponse.bind(this);
        this.getSurveyForm = this.getSurveyForm.bind(this);
        this.handleSelectedAttachmentChange = this.handleSelectedAttachmentChange.bind(this);
        this.showViewer = this.showViewer.bind(this);
        this.closeViewer = this.closeViewer.bind(this);
        this.onFocus = this.onFocus.bind(this);
    }

    onFocus = e => {
        this.setState({menuIsOpen: true});
    };

    showViewer() {
        let self = this;
        if (self.state.selectedAttachment !== "") {
            self.setState({viewer: true});
        } else {
            Swal.fire({
                    title: 'Uyar ! ',
                    html: 'Grntlemek istediiniz dokman seiniz!',
                    icon: 'warn',
                    confirmButtonText: 'Tamam'
                }
            );
        }
    }

    closeViewer() {
        this.setState({viewer: false});
    }

    componentDidMount() {
        if (this.props && this.props.selected && this.props.selected.documentType !== 'BORDRO')
            this.getSurveyFormResponse();
    }

    handleSelectedAttachmentChange = (selectedAttachment, {action}) => {
        let self = this;
        self.setState({selectedAttachment: selectedAttachment, menuIsOpen: false});

    };

    getSurveyFormResponse() {
        let self = this;
        if (!!self.props.documentId) {
            let params = {
                url: `/survey/getSurveyFormResponseByFormId`,
                method: "post",
                data: self.props.documentId
            };
            requestK(params)
                .then(function (response) {
                    self.setState({surveyFormResponse: response.data});
                    if (self.state.surveyFormResponse !== null && self.state.surveyFormResponse !== "") {
                        let jsObj = JSON.parse(self.state.surveyFormResponse.response == "" ? "{}" : self.state.surveyFormResponse.response);
                        let options = [];
                        Object.keys(jsObj).forEach(function (x) {
                            if (JSON.stringify(jsObj[x]).includes("data:image") || JSON.stringify(jsObj[x]).includes("data:application")) {
                                if (Array.isArray(jsObj[x])) {
                                    jsObj[x].forEach(y => {
                                        let {name} = dataURItoBlob(y);
                                        options.push({
                                            value: y,
                                            label: x + ((name !== "unknown" && name !== undefined) ? "-( " + name + " )" : "")
                                        });
                                    });
                                } else {
                                    let {name} = dataURItoBlob(jsObj[x]);
                                    options.push({
                                        value: jsObj[x],
                                        label: x + ((name !== "unknown" && name !== undefined) ? "-( " + name + " )" : "")
                                    });
                                }
                            }
                        });
                        self.setState({surveyAttachments: options, surveyFormButton: true});
                        self.getSurveyForm();
                    }
                }).catch(function (error) {
                showAxiosError(error);
            });
        }
    }

    getSurveyForm() {
        let self = this;
        let params = {
            url: `/survey/getSurveyForm`,
            method: "post",
            data: self.state.surveyFormResponse.surveyId
        };
        requestK(params)
            .then(function (response) {
                self.setState({surveyForm: response.data, surveyFormButton: true});
                self.props.isSurveyFormTrue();
            }).catch(function (error) {
            showAxiosError(error);
        });
    }

    cancelShowSurveyForm() {
        this.setState({
            surveyFormModal: false
        })
    }

    onDocumentLoad(e) {
        let {numPages} = e;
        this.setState({numPages, flag: true});
    }

    onClickAttachments() {
        this.setState({surveyFormModal: true});
    }

    toggleSetting = type => {
        this.setState(p => ({[type]: !p[type], activateZoom: !this.state.activateZoom}));
    };

    render() {

        const {pageNumber, numPages} = this.state;
        const {scrollTop} = this.state;
        const enableWheel = this.state.enableWheel;
        const disabled = this.state.disabled;
        const {selectedAttachment} = this.state.selectedAttachment;
        return (
            <div>
                <React.Fragment>
                    <PdfViewer
                        url={this.props.url}
                        selectedUrl = {this.props.selectedUrl}
                        showCustomButtons={this.state.surveyFormButton}
                        showForm={this.onClickAttachments}
                        showBarcodes={this.props.showBarcodeList}

                    />
                </React.Fragment>
                <Modal isOpen={this.state.surveyFormModal} size="lg" className="ust">
                    <ModalHeader
                        toggle={this.cancelShowSurveyForm}>{(this.state.surveyForm != "" && this.state.surveyForm !== null) ? JSON.parse(this.state.surveyForm.jsonContent)['name'] : ""}
                    </ModalHeader>
                    <ModalBody style={{overflow: 'visible'}}>
                        <div className="card">
                            <div className="card-body" style={{margin: '0px 16px 16px 16px'}}>
                                <Form
                                    schema={(this.state.surveyForm != "" && this.state.surveyForm !== null) ? JSON.parse(this.state.surveyForm.jsonContent)['schema'] : ""}
                                    uiSchema={(this.state.surveyForm != "" && this.state.surveyForm !== null) ? JSON.parse(this.state.surveyForm.jsonContent)['uiSchema'] : ""}
                                    formData={(this.state.surveyFormResponse != "" && this.state.surveyFormResponse != null) ? JSON.parse(this.state.surveyFormResponse.response) : ""}
                                    disabled={true}
                                    children={<br/>}
                                    widgets={widgets}
                                />
                                <div className="container">
                                    <div className="row">
                                        <div className="col-sm-3 row align-items-center">
                                            <Label>Dokman Listesi</Label>
                                        </div>
                                        <div className="col-sm-5 row align-items-center">
                                            <Select
                                                className="col-sm-12"
                                                clearable={false}
                                                value={this.state.selectedAttachment}
                                                placeholder="Seiniz..."
                                                onChange={this.handleSelectedAttachmentChange}
                                                onFocus={this.onFocus}
                                                menuIsOpen={this.state.menuIsOpen}
                                                options={this.state.surveyAttachments}
                                                noOptionsMessage={() => 'Eklenen Dokman Bulunamad!'}
                                            />
                                        </div>
                                        <div className="col-sm-2 row align-items-center">
                                            <Button type="button" onClick={this.showViewer}>Grntle</Button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </ModalBody>
                </Modal>
                {this.state.viewer && <DocumentViewer closeViewer={this.closeViewer}
                                                      attachment={this.state.selectedAttachment['value']}/>}
            </div>
        )
    }
}

export default PdfComponent;
