import {validateField} from "./validation";
import {checkGrantToDisplay} from "../helper/helper";
import {VIEWS_TCKN} from "./authorities/GrantDef";

export function isValidFormTemplate(formTemplate) {
	let error = {};
	let errorMessage = "";

	error.formType = validateField(undefined, "1,255", formTemplate.formType ? formTemplate.formType.category : undefined);
	if (!error.formType.valid) {
		errorMessage = errorMessage + " <strong>Form Tr bo braklamaz</strong><br/>";
	}
	error.formName = validateField(undefined, "1,200", formTemplate.formName);
	if (!error.formName.valid) {
		errorMessage = errorMessage + "<strong>Form Ad "+error.formName.message+"</strong><br/>";
	}
	error.mimeBody = validateField(undefined, "1,20000", formTemplate.mimeBody);
	if (!error.mimeBody.valid) {
		errorMessage = errorMessage + " <strong>E-posta leti ierii bo braklamaz</strong><br/>";
	}
	error.formContent = validateField(undefined, "1,200000000000000", formTemplate.formContent.toString()
		.replace("<div><p style=\"display:block; width:%100; word-wrap:break-word;\"><br/></p></div>",""));
	if (!error.formContent.valid) {
		errorMessage = errorMessage + " <strong>Form ierii bo braklamaz</strong><br/>";
	}
	let valid = isValidObject(error);
	return {error: error, valid: valid, errorMessage: errorMessage};
}

export function isValidFormTemplateImport(formTemplate) {
	let error = {};
	let errorMessage = "";

	error.formType = validateField(undefined, "1,255", formTemplate.formType);
	if (!error.formType.valid) {
		errorMessage = errorMessage + " <strong>Form Tr bo braklamaz</strong><br/>";
	}
	error.formName = validateField(undefined, "1,200", formTemplate.formName);
	if (!error.formName.valid) {
		errorMessage = errorMessage + "<strong>Form Ad "+error.formName.message+"</strong><br/>";
	}
	error.mimeBody = validateField(undefined, "1,20000", formTemplate.mimeBody);
	if (!error.mimeBody.valid) {
		errorMessage = errorMessage + " <strong>E-posta leti ierii bo braklamaz</strong><br/>";
	}
	if (!formTemplate.id) {
		error.file = validateField(undefined, "1,20000", formTemplate.file);
		if (!error.file.valid) {
			errorMessage = errorMessage + " <strong>Form taslak dosyas bo braklamaz</strong><br/>";
		}
	}
	let valid = isValidObject(error);
	return {error: error, valid: valid, errorMessage: errorMessage};
}

