export function isValidMailProperty(mail) {
	let error = {};
	let errorMessage = "";

	error.protocol = validateField(undefined, "1,45", mail.protocol);
	if (!error.protocol.valid) {
		errorMessage = errorMessage + " <strong>Protocol bilgisi " + error.protocol.message + "</strong><br/>";
	}
	error.host = validateField(undefined, "1,45", mail.host);
	if (!error.host.valid) {
		errorMessage = errorMessage + " <strong>Host bilgisi " + error.host.message + "</strong><br/>";
	}
	error.port = validateField("integer", undefined, mail.port);
	if (!error.port.valid) {
		errorMessage = errorMessage + " <strong>Port bilgisi " + error.port.message + "</strong><br/>";
	}
	error.user = validateField("email", "1,255", mail.user);
	if (!error.user.valid) {
		errorMessage = errorMessage + " <strong>E-Posta/Kullanc Ad " + error.user.message + "</strong><br/>";
	}
	error.password = validateField(undefined, "1,45", mail.password);
	if (!error.password.valid) {
		errorMessage = errorMessage + " <strong>ifre " + error.user.message + "</strong><br/>";
	}
	error.encryption = validateField(undefined, "0,45", mail.encryption);
	if (!error.encryption.valid) {
		errorMessage = errorMessage + " <strong>ifreleme Tr " + error.encryption.message + "</strong><br/>";
	}
	error.signatureText = validateField(undefined, "0,255", mail.signatureText);
	if (!error.signatureText.valid) {
		errorMessage = errorMessage + " <strong>mza Metni " + error.signatureText.message + "</strong><br/>";
	}
	let valid = isValidObject(error);
	return {error: error, valid: valid, errorMessage: errorMessage};
}

export function isValidEmployee(self, employee) {
	let error = {};
	let errorMessage = "";

	error.name = validateField(undefined, "1,255", employee.name);
	if (!error.name.valid) {
		errorMessage = errorMessage + " <strong>alan Ad " + error.name.message + "</strong><br/>";
	}
	error.email = validateField("formulaInjection", "0,255", employee.email);
	if (!error.email.valid) {
		errorMessage = errorMessage + " <strong>alan e-Posta " + error.email.message + "</strong><br/>";
	}
	error.integrationCode = validateField("formulaInjection", "0,100", employee.integrationCode);
	if (!error.integrationCode.valid) {
		errorMessage = errorMessage + " <strong>Entegrasyon kodu " + error.integrationCode.message + "</strong><br/>";
	}
	error.jobTitle = validateField("formulaInjection", "0,200", employee.jobTitle);
	if (!error.jobTitle.valid) {
		errorMessage = errorMessage + " <strong>Grev " + error.jobTitle.message + "</strong><br/>";
	}
	error.educationLevel = validateField("formulaInjection", "0,100", employee.educationLevel);
	if (!error.educationLevel.valid) {
		errorMessage = errorMessage + " <strong>Eitim Durumu " + error.educationLevel.message + "</strong><br/>";
	}
	error.employmentStatus = validateField("formulaInjection", "0,100", employee.employmentStatus);
	if (!error.employmentStatus.valid) {
		errorMessage = errorMessage + " <strong>alma ekli " + error.employmentStatus.message + "</strong><br/>";
	}
	error.address = validateField("formulaInjection", "0,255", employee.address);
	if (!error.address.valid) {
		errorMessage = errorMessage + " <strong>Adres " + error.address.message + "</strong><br/>";
	}
	error.cityOfBirth = validateField("formulaInjection", "0,100", employee.cityOfBirth);
	if (!error.cityOfBirth.valid) {
		errorMessage = errorMessage + " <strong>Doum Yeri " + error.cityOfBirth.message + "</strong><br/>";
	}
	error.militaryStatus = validateField("formulaInjection", "0,100", employee.militaryStatus);
	if (!error.militaryStatus.valid) {
		errorMessage = errorMessage + " <strong>Askerlik Durumu " + error.militaryStatus.message + "</strong><br/>";
	}
	error.surname = validateField(undefined, "1,255", employee.surname);
	if (!error.surname.valid) {
		errorMessage = errorMessage + " <strong>alan Soyad " + error.surname.message + "</strong><br/>";
	}

	if (employee.department === null || employee.department.id === null || employee.department.name === null ) {
		error.department = {valid: false, message: 'alan departman bilgisi bo braklamaz.'};
		errorMessage = errorMessage + " <strong>alan departman bilgisi bo braklamaz.</strong><br/>";
	}

	if ((employee.parentUserId !== null && employee.secondParentUserId !== null) && (employee.parentUserId === employee.secondParentUserId)) {
		error.manager = {valid: false, message: 'alann iki yneticiside ayn kii olamaz.'};
		errorMessage = errorMessage + " <strong>alann iki yneticiside ayn kii olamaz.</strong><br/>";
	}

	if ((employee.parentUserId !== null && employee.thirdParentUserId !== null) && (employee.parentUserId === employee.thirdParentUserId)) {
		error.manager = {valid: false, message: 'alann iki yneticiside ayn kii olamaz.'};
		errorMessage = errorMessage + " <strong>alann iki yneticiside ayn kii olamaz.</strong><br/>";
	}

	if ((employee.secondParentUserId !== null && employee.thirdParentUserId !== null) && (employee.secondParentUserId === employee.thirdParentUserId)) {
		error.manager = {valid: false, message: 'alann iki yneticiside ayn kii olamaz.'};
		errorMessage = errorMessage + " <strong>alann iki yneticiside ayn kii olamaz.</strong><br/>";
	}
	error.tckn = validateField(undefined, "11,11", employee.tckn);
	if (!error.tckn.valid) {
		errorMessage = errorMessage + " <strong>alan TCKN " + error.tckn.message + "</strong><br/>";
	}

	if (checkGrantToDisplay(self, VIEWS_TCKN) && !validateTckn(employee.tckn)) {
		error.tckn.valid = false;
		error.tckn.message = "TCKN format uygun deil."
		errorMessage = errorMessage + " <strong>TCKN format uygun deil</strong><br/>";
	}

	let checkEmail = false;
	let checkKep = false;
	for (let i = 0; i < employee.employeeNotificationTypeList.length; i++) {
		let employeeNotification = employee.employeeNotificationTypeList[i];
		if (employeeNotification.ntfType.shortCode === "EMAIL") {
			checkEmail = true;
		} else if (employeeNotification.ntfType.shortCode === "KEP") {
			checkKep = true;
		}
	}
	if (employee.employeeNotificationTypeList.length === 0) {
		error.employeeNotificationTypeList = {valid: false, message: 'alann bildirim tr bo braklamaz.'};
		errorMessage = errorMessage + " <strong>alann bildirim tr bo braklamaz.</strong><br/>";
	} else if (!checkKep) {
		error.telephone = validateField("number", "10,10", employee.telephone);
		if (!error.telephone.valid) {
			errorMessage = errorMessage + " <strong>alan telefon " + error.telephone.message + "</strong><br/>";
		}
	}

	if (checkEmail) {
		error.email = validateField("email", "3,255", employee.email);
		if (!error.email.valid) {
			errorMessage = errorMessage + " <strong>alan e-Posta " + error.email.message + "</strong><br/>";
		}
		error.email = validateField("formulaInjection", "3,255", employee.email);
		if (!error.email.valid) {
			errorMessage = errorMessage + " <strong>alan e-Posta " + error.email.message + "</strong><br/>";
		}
	}

	//todo:Her halkarda zorunlu yaplm baklmal
	if (checkKep) {
		error.kepAddress = validateField("email", "3,255", employee.kepAddress);
		if (!error.kepAddress.valid) {
			errorMessage = errorMessage + " <strong>alan Kep Adresi  " + error.kepAddress.message + "</strong><br/>";
		}
	}

	let valid = isValidObject(error);
	return {error: error, valid: valid, errorMessage: errorMessage};
}

export function validateTckn(tckn) {
	let totalX = 0;
	let totalY = 0;
	let totalZ = 0;

	for (let i = 0; i < 9; i += 2) {
		totalX += Number(tckn.substr(i, 1));
	}
	for (let i = 1; i < 8; i += 2) {
		totalY += Number(tckn.substr(i, 1));
	}
	for (let i = 0; i < 10; i++) {
		totalZ += Number(tckn.substr(i, 1));
	}

	let checkMode = ((totalX * 7) - totalY) < 0 ? ((totalX * 7) - totalY) + 100 : ((totalX * 7) - totalY);
	let result1 = (checkMode % 10) === Number(tckn.substr(9, 1));
	let result2 = (totalZ % 10) === Number(tckn.substr(10, 1));

	return result1 && result2;
}

export function isValidObject(error) {
	let valid = true;
	for (let key in error) {
		let tError = error[key];
		if (!tError.valid) {
			valid = false;
			break;
		}
	}
	return valid;
}