import React, {Component} from 'react';
import 'react-table/react-table.css';
import {Button, Col, Form, FormGroup, Input, Label, Modal, ModalBody, ModalFooter, ModalHeader, Nav} from 'reactstrap';
import 'moment/locale/tr';
import {
    renewListForPrivileges,
} from "../common/PeykHelper";
import {requestK} from "../../../helper/APIUtils";
import {showAxiosError} from "../../../helper/helper";
import Table from "../../../components/Table/Table";
import StickButton from "../../../components/StickButton";
import MiniButton from "../../../components/MiniButton";
import SweetAlertExt from "../../../components/SweetAlertExt";
import Alert from "react-s-alert";
import moment from "moment";
import Swal from "sweetalert2";
import {CREDIT, SALE_PLAN_ADMIN} from "../../../common/authorities/GrantDef";

class SmsInformationPage extends Component {

    constructor(props) {
        super(props);
        this.state = {
            alert: null,
            loading: false,
            showModal: false,
            displayDetailDialog: false,
            valid: {},
            manual: false,
            list: [],
            totalIdList: [],
            total: 0,
            searching: false,
            searchQuery: undefined,
            page: 0,
            size: 10,
            selected: {
                name: null,
                credit: null,
                price: null,
                explanation: null,
                unit: "Adet",
            },
            selection: [],
            selectedAll: false,
            isKepOwner: false,
            options: [],
            types: [],
            isLoading: false,
            pageSms:0,
            sizeSms: 10,
            totalSms: 0,
            listSms:[],
            loadingSms: false,
            searchingSms: false,
            manualSms: false,
            content: [],
            contentSms: [],
        };
        this.fetchData = this.fetchData.bind(this);
        this.fetchDataPurchaseHistory = this.fetchDataPurchaseHistory.bind(this);
        this.getTrStyles = this.getTrStyles.bind(this);
        this.openDetailDialog = this.openDetailDialog.bind(this);
        this.isSelected = this.isSelected.bind(this);
        this.createClicked = this.createClicked.bind(this);
        this.closeDetailDialog = this.closeDetailDialog.bind(this);
        this.clearChild = this.clearChild.bind(this);
        this.clear = this.clear.bind(this);
        this.child = React.createRef();
        this.saveSmsClicked = this.saveSmsClicked.bind(this);
        this.deleteSmsClicked = this.deleteSmsClicked.bind(this);
        this.updateSms = this.updateSms.bind(this);
        this.purchaseCredit = this.purchaseCredit.bind(this);
        this.editHandleInputChange = this.editHandleInputChange.bind(this);

    }

    componentWillMount() {
    }

    saveSmsClicked() {
        let self = this;
        let data = self.state.selected;
        data.unit = "Adet";
        let params = {
            url: `/saleplan/addSalePlan`,
            method: "post",
            data: data
        };
        if (data.credit === undefined || data.explanation === undefined || data.name === undefined || data.price === undefined || data.credit === "" || data.explanation === "" || data.name === "" || data.price === "") {
            Alert.warning("Bo Alan Brakmamalsnz!", {
                position: 'top-right',
                effect: 'stackslide',
                timeout: 5000
            });
            return;
        }
        if (data.price === "0" || data.credit === "0") {
            Alert.warning("Kredi veya Fiyat 0 olmamal!", {
                position: 'top-right',
                effect: 'stackslide',
                timeout: 5000
            });
            return;
        }
        self.setState({disabledSaveBtn: true});
        requestK(params)
            .then(function (response) {
                self.setState({
                    displayDetailDialog: false,
                    disabledSaveBtn: false
                });
                Swal({
                    title: 'Baarl ! ',
                    text: ("lem baaryla gerekletirilidi."),
                    type: 'success',
                    confirmButtonText: 'Tamam'
                }).then(() => {
                    self.fetchData(undefined, undefined, self.state.pageSms, self.state.sizeSms);
                });
            }).catch(function (error) {
            showAxiosError(error);
            self.setState({disabledSaveBtn: false});
        });
    }

    deleteSmsClicked(row) {

            let self = this;
            let params = {
                url: `/saleplan/deleteSalePlan`,
                method: "post",
                data: row
            };
            self.setState({disabledDeleteBtn: true});
            requestK(params)
                .then(function (response) {
                    self.setState({
                        selected: null,
                        displayDetailDialog: false,
                        manual: true,
                        alert: null,
                        disabledDeleteBtn: false
                    });
                    Swal({
                        title: 'Baarl ! ',
                        text: ("Silme ilemi baaryla gerekletirildi."),
                        type: 'success',
                        confirmButtonText: 'Tamam'
                    }).then(() => {
                       // self.fetchData();
                        self.fetchData(undefined,undefined,self.state.pageSms,self.state.sizeSms);
                    });
                }).catch(function (error) {
                showAxiosError(error);
                self.setState({loading: false, disabledDeleteBtn: false});
            });
    }


    componentDidMount() {
        this.fetchGetCredit();
    }

    clearChild() {
        let self = this;
        self.child.current.clear();
    }

    fetchData(accessor, direction, page, size) {
        let self = this;
        let sortStr = accessor !== undefined ? ('&sort=' + accessor + ',' + direction) : '&sort=id,desc';
        let searchQuery = !self.state.searchQuery ? '' : '&' + self.state.searchQuery;
        let params = {
            url: '/saleplan/readSalePlanAll?page=' + page + '&size=' + size + searchQuery + sortStr,
            method: "get",
        };
        self.setState({loadingSms: true, searchingSms: true, manualSms: self.manualSms === true ? false : self.manualSms});
        self.props.reload(true);
        requestK(params)
            .then(function (response) {
            self.setState({
                pageSms: page,
                sizeSms: size,
                totalSms: response.data.totalElements,
                listSms: (response.data) ? response.data : [],
                loadingSms: false,
                searchingSms: false,
                contentSms: response.data.content,
            });
                self.props.reload(false);
        }).catch(function (error) {
            self.setState({loadingSms: false, searching: false});
            self.props.reload(false);
            showAxiosError(error);
        });
    }

    clear() {
        this.setState({
            pageSms: 0,
            totalSms: 0,
            sizeSms: 10,
            listSms: [],
        })

    }

    fetchGetCredit() {
        let self = this;
        let params = {
            url: '/credit/getCredit',
            method: "get",
        };
        let initStateData = null;
        requestK(params)
            .then(function (response) {
                initStateData = response.data;
                self.setState({
                    remain: response.data.remain,
                    purchased: response.data.purchased,
                    predefined: response.data.predefined,
                });
            }).catch(function (error) {
            showAxiosError(error);
        });
    }

    purchaseCredit(row) {
        let self = this;
        let params = {
            url: `/credit/purchaseCredit`,
            method: "post",
            data: row
        };
        let initStateData = null;
        requestK(params)
            .then(function (response) {
                initStateData = response.data;
                self.setState({
                    paymentUrl: response.data.paymentUrl,
                });
                Swal({
                    title: 'Bilgilendirme ! ',
                    text: ("deme sayfasna ynlendiriliyor. Ltfen Bekleyiniz..."),
                    timer: 4000,
                    showConfirmButton: false,
                }).then(() => {
                    window.location.href = response.data.paymentUrl;
                });
            }).catch(function (error) {
            showAxiosError(error);
            self.setState({loading: false, disabledDeleteBtn: false});
        });
    }

    fetchDataPurchaseHistory(accessor, direction, page, size) {
        let self = this;
        let sortStr = accessor !== undefined ? ('&sort=' + accessor + ',' + direction) : '&sort=id,desc';
        let searchQuery = !self.state.searchQuery ? '' : '&' + self.state.searchQuery;
        let params = {
            url: '/credit/findCreditLog?page=' + page + '&size=' + size + searchQuery + sortStr,
            method: "get",
        };
        self.setState({loading: true, searching: true, manual: self.manual === true ? false : self.manual});
        requestK(params).then(function (response) {
            self.setState({
                page: page,
                size: size,
                total: response.data.totalElements,
                loading: false,
                searching: false,
                content: response.data.content,
            });
        }).catch(function (error) {
            self.setState({loading: false, searching: false});
            showAxiosError(error);
        });
    }

    openDetailDialog(row) {
        let clone = Object.assign({}, row);
        delete clone["_$visited"];
        this.setState({
            selected: clone,
            valid: {},
            displayDetailDialog: true
        });
    }

    closeDetailDialog() {
        this.setState({
            selected: undefined,
            valid: {},
            displayDetailDialog: false
        });
    }

    isSelected(id) {
        return (this.state.selection.indexOf(id) !== -1);
    }

    getTrStyles(item) {
        return (item.statusType && item.statusType !== 'ACTIVE') ? {
            backgroundColor: 'lightgray',
            webkitTextFillColor: 'darkgray'
        } : {};
    }

    createClicked() {
        this.newData = true;
        this.setState({
            selected: {},
            valid: {},
            displayDetailDialog: true,
        });
    }

    updateSms(row) {
        let clone = Object.assign({}, row);
        delete clone["_$visited"];
        this.setState({
            selected: clone,
            valid: {},
            displayDetailDialog: true
        });
    }

    activateJoyride() {
        if (this.state.joyrideActive === false) {
            this.setState({flag: 0});
            this.setState({joyrideActive: true})
            if (this.state.width < 1000) {
                this.setState({flag: 1});
            }
        }
    }

    editHandleInputChange(event) {
        let self = this;
        const target = event.target;
        let value = target.value;
        let name = target.name;
        let selected = self.state.selected;
        selected[name] = value;
        self.setState({selected});
    }

    hasGrant = (grant) => {
        return this.props.hasGrant(grant);
    };

    renderCreateUpdateSms() {
        return <Modal isOpen={this.state.displayDetailDialog} size="xl"
                      toggle={() => this.setState({displayDetailDialog: false})}>
            <ModalHeader className="justify-content-center"
                         toggle={() => this.setState({displayDetailDialog: false})}>
                Yeni Paket Olutur
            </ModalHeader>
            <ModalBody>
                {this.state.selected && <div>
                    <Form>
                        <FormGroup row>
                            <Label sm={4} for="name">Ad: *</Label>
                            <Col sm={4}>
                                <Input type="text"
                                       name="name"
                                       id="tname"
                                       value={this.state.selected.name}
                                       onChange={this.editHandleInputChange}
                                />
                            </Col>
                        </FormGroup>
                        <FormGroup row>
                            <Label sm={4} for="credit">Kontr Adeti: *</Label>
                            <Col sm={4}>
                                <Input type="number"
                                       name="credit"
                                       id="tkontorcredit"
                                       min="1"
                                       style={{textAlign: "left"}}
                                       value={this.state.selected.credit}
                                       onChange={this.editHandleInputChange}
                                       maxLength={8}
                                       onKeyPress={(e) => {
                                           const allowedChars = /^[0-9]+$/;
                                           const key = String.fromCharCode(e.which);

                                           // stenmeyen karakterleri kontrol ediyoruz
                                           if (!allowedChars.test(key)) {
                                               e.preventDefault();
                                           }

                                           // Maksimum karakter snrn kontrol ediyoruz
                                           if (e.target.value.length >= 8) {
                                               e.preventDefault();
                                           }
                                       }}
                                />
                            </Col>
                        </FormGroup>
                        <FormGroup row>
                            <Label sm={4} for="price">Kontr Fiyat: *</Label>
                            <Col sm={4}>
                                <Input type="number"
                                       name="price"
                                       id="tkontorprice"
                                       min="1"
                                       style={{textAlign: "left"}}
                                       value={this.state.selected.price}
                                       onChange={this.editHandleInputChange}
                                       onKeyPress={(e) => {
                                           const allowedChars = /^[0-9]+$/;
                                           const key = String.fromCharCode(e.which);

                                           // stenmeyen karakterleri kontrol ediyoruz
                                           if (!allowedChars.test(key)) {
                                               e.preventDefault();
                                           }

                                           // Maksimum karakter snrn kontrol ediyoruz
                                           if (e.target.value.length >= 8) {
                                               e.preventDefault();
                                           }
                                       }}
                                />
                            </Col>
                        </FormGroup>
                        <FormGroup row>
                            <Label sm={4} for="explanation">Aklama: *</Label>
                            <Col sm={4}>
                                <Input type="text"
                                       name="explanation"
                                       id="texplanation"
                                       value={this.state.selected.explanation}
                                       onChange={this.editHandleInputChange}/>
                            </Col>
                        </FormGroup>
                    </Form>
                </div>}
            </ModalBody>
            <ModalFooter>
                <Button title="ptal" style={{paddingLeft: '16px', width: '100px'}} color="secondary"
                        onClick={() => this.setState({displayDetailDialog: false})}>
                    ptal
                </Button>
                <Button title="Kaydet" style={{paddingLeft: '16px', width: '100px'}} color="primary"
                        disabled={this.state.disabledSaveBtn}
                        onClick={this.saveSmsClicked}>
                    Kaydet
                </Button>
            </ModalFooter>
        </Modal>
    }

    renderMainTable() {

        let columnFieldList = [
            {
                Header: "Ad",
                accessor: "name",
                sortable: false
            },
            {
                Header: "Kontr",
                accessor: "credit",
                sortable: false
            },
            {
                Header: "Adet",
                accessor: "unit",
                sortable: false
            },
            {
                Header: "Fiyat",
                accessor: "price",
                sortable: false,
            },
            {
                Header: "Aklama",
                accessor: "explanation",
                sortable: false,
            },
            {
                Header: "lemler",
                accessor: "",
                sortable: false,
                width: "auto",
                Cell: row => (
                    <div>
                        {this.hasGrant(CREDIT) &&
                        <button type="button" className="select-button-sms" onClick={() => this.purchaseCredit(row)}>Satn Al</button>}
                        {this.props.isSuperAdmin && this.hasGrant(SALE_PLAN_ADMIN) &&
                            <MiniButton title="Dzenle" faIcon="fa-edit" onClick={() => this.updateSms(row)}
                                        color="success"/>}
                        {this.props.isSuperAdmin && this.hasGrant(SALE_PLAN_ADMIN) &&
                            <MiniButton title="Sil" faIcon="fa-trash" color="danger"
                                        onClick={() => {
                                            this.setState({
                                                alert: (
                                                    <SweetAlertExt showCancel warning
                                                                   confirmBtnText="Evet"
                                                                   cancelBtnText="Hayr"
                                                                   confirmBtnBsStyle="danger"
                                                                   cancelBtnBsStyle="default"
                                                                   disabled={this.state.disabledDeleteBtn}
                                                                   title="Sms paketi silinecek. Emin misiniz?"
                                                                   onConfirm={() => this.setState({alert: null}, () => {
                                                                       this.deleteSmsClicked(row);
                                                                   })}
                                                                   onCancel={() => this.setState({alert: null})}
                                                    >
                                                    </SweetAlertExt>
                                                )
                                            });
                                        }}/>}
                    </div>)
            }
        ];
        let columns = renewListForPrivileges(this, columnFieldList, "accessor", this.state.isKepOwner);
        return <Table columns={columns}
                      content={this.state.contentSms}
                      loading={this.state.loadingSms}
                      loadingText="Kaytlar getiriliyor..."
                      total={this.state.totalSms}
                      page={this.state.pageSms}
                      size={this.state.sizeSms}
                      manual={this.state.manualSms}
                      options={["5", "10", "20", "50", "100"]}
                      getTrStyles={this.getTrStyles}
                      fetchData={this.fetchData}/>
    }

    renderTable() {
        let columnList = [
            {
                Header: "Oluturulan Tarih",
                accessor: "createdAt",
                sortable: false,
                Cell: row => (
                    <div style={{margin: 0}}>
                        {moment(row.createdAt).isValid() ? moment(row.createdAt).format("DD-MM-YYYY HH:mm:ss") : ''}
                    </div>
                )
            },
            {
                Header: "Kredi",
                accessor: "credit",
                sortable: false,

            },
            {
                Header: "Kredi Tr",
                accessor: "creditType",
                sortable: false,
            },
            {
                Header: "Aklama",
                accessor: "explanation",
                sortable: false
            }
        ];
        let columns = renewListForPrivileges(this, columnList, "accessor", this.state.isKepOwner);
        return <Table columns={columns}
                      content={this.state.content}
                      loading={this.state.loading}
                      loadingText="Kaytlar getiriliyor..."
                      total={this.state.total}
                      page={this.state.page}
                      size={this.state.size}
                      manual={this.state.manual}
                      options={["5", "10", "20", "50", "100"]}
                      getTrStyles={this.getTrStyles}
                      fetchData={this.fetchDataPurchaseHistory}/>
    }

    render() {
        return <div className="row baseBackground">
            <Nav className="topbar" style={{paddingLeft: '14px', height: '53px'}}>
                <p style={{marginTop: '18px'}}>SMS KONTR BLGLENDRME</p>
            </Nav>
            <div className="row">
                <div className="col-sm-12">
                    <div className="col-sm-12 satirBasi">
                        <div className="card-header">
                            {(this.hasGrant(CREDIT)) && this.props.isSuperAdmin === false &&
                                <h4 className="card-title" style={{ fontWeight: 'bold', color: 'red' }}>Kalan Sms Kontr Miktar: {this.state.remain} Adet</h4>}
                            {/*{this.props.isSuperAdmin === false &&*/}
                            {/*<h4 className="card-title">Satn Alnan: {this.state.purchased} Adet</h4>}*/}
                            {/*{this.props.isSuperAdmin === false &&*/}
                            {/*<h4 className="card-title">Tanmlanan: {this.state.predefined} Adet</h4>}*/}
                            {this.props.isSuperAdmin &&
                                <StickButton position="leftSide" className="zocial Yeni" style={{fontSize: 'x-small'}}
                                             label="Yeni Paket Olutur"
                                             onClick={this.createClicked}
                                />}
                            <h4 className="card-title" style={{ fontWeight: 'bold'}}>SMS KONTR PAKETLER </h4>
                        </div>
                        <br></br>
                        <div className="card-body">
                            {this.renderMainTable()}
                        </div>
                    </div>
                </div>
                {(this.hasGrant(CREDIT)) && <div className="col-sm-12">
                    <div className="col-sm-12 satirBasi">
                        <div className="card-header">
                            <h4 className="card-title" style={{ fontWeight: 'bold'}}>SATIN ALMA GEM </h4>
                        </div>
                        <div className="card-body">
                            {this.renderTable()}
                        </div>
                    </div>
                </div>}
            </div>
            {this.state.alert}
            {this.renderCreateUpdateSms()}
        </div>
    }
}

export default SmsInformationPage;
