import React, {Component} from 'react';
import DefaultModal from "../../../../components/DefaultModal";
import {Col, Label, Row} from "reactstrap";
import {requestK} from "../../../../helper/APIUtils";
import Alert from "react-s-alert";
import {showAxiosError} from "../../../../helper/helper";
import Dropzone from "react-dropzone";
import TextOverFlowTooltip from "../../../../components/TextOverFlowTooltip";
import MiniButton from "../../../../components/MiniButton";
import SweetAlertExt from "../../../../components/SweetAlertExt";

class DownloadableFileModal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      btnDisabled: false,
      currentFile: {},
      index: 0,
      realIndex: 0,
      process: false,
      disableZone: false,
      fileList: [],
      existFileList: [],
      fileUrl: '',
      verificationCode: null,
      loading: false
    };
    this.onDrop = this.onDrop.bind(this);
    this.onDropRejected = this.onDropRejected.bind(this);
  }

  componentDidMount() {
    this.getData();
  }

  saveFile = () => {
    let self = this;
    let fileList = self.state.fileList;
    let document = fileList[0];
    let formData = new FormData();
    formData.append("content", document.content);
    formData.append("name", document.name);
    let params = {
      url: `/formsession/uploadDownloadableFile`,
      method: "post",
      data: formData
    };
    fileList[0] = {
      status: "processing",
      statusText: "Gnderim balatld.",
      name: document.name,
      size: document.size,
      content: document.content,
    };
    requestK(params)
      .then(function (response) {
        fileList[0] = {
          status: "success",
          statusText: "Gnderim baarl.",
          name: document.name,
          size: document.size,
          content: document.content,
        };
        Alert.success("ndirilebilir Dosya Kaydedildi.", {
          position: 'top-right',
          effect: 'stackslide',
          timeout: 5000
        });
        self.setState({showDownloadableFileModal: false});
        self.setState({fileList: []}, () => self.getData());
      }).catch(function (error) {
      fileList[0] = {
        status: "error",
        statusText: error.data,
        name: document.name,
        size: document.size,
        content: document.content,
      };
      showAxiosError(error);
      self.setState({disabledSaveBtn: false});
    });

  }

  getData = () => {
    let self = this;
    let params = {
      url: `/formsession/readDownloadableFiles`,
      method: "get",
    };
    this.setState({loading: true});
    requestK(params)
      .then(function (response) {
        self.setState({
          existFileList: response.data.files,
          fileUrl: response.data.baseUrl,
          loading: false
        });
      }).catch(function (error) {
      showAxiosError(error);
      self.setState({disabledSaveBtn: false});
    });
  }

  deleteRow = (row) => {
    let self = this;
    let params = {
      url: `/formsession/deleteFile`,
      method: "post",
      data: row
    };
    this.setState({loading : true})
    requestK(params)
      .then(function (response) {
        self.setState({
          alert: null,
          loading: false,
          existFileList: []
        })
        let message = response.data;
        if (message === "Dosya silindi.") {
          Alert.success(message, {
            position: 'top-right',
            effect: 'stackslide',
            timeout: 5000
          });
          self.getData();
        } else {
          Alert.error(message, {
            position: 'top-right',
            effect: 'stackslide',
            timeout: 5000
          });
        }
      })
      .catch(function (error) {
        showAxiosError(error);
      });
  }

  onDrop(acceptedFiles, rejectedFiles) {
    let self = this;
    if (!!acceptedFiles) {
      self.setState({processing: true, uploadBtnDisabled: true, fileList: []});
      let fileList = [];
      for (let i = 0; i < acceptedFiles.length; i++) {
        fileList.push({content: acceptedFiles[i], name: acceptedFiles[i].name, size: acceptedFiles[i].size});
      }
      self.setState({processing: false, uploadBtnDisabled: false, fileList: fileList, index: 0, realIndex: 0});
    }
  }

  onDropRejected(rejectedFiles) {
    if (!!rejectedFiles) {
      rejectedFiles.forEach((file) => {
        if (file.size > 104857600) {
          Alert.error("Dosya boyutu 100 MB dan fazla, kontrol ediniz.", {
            position: 'top-right',
            effect: 'stackslide',
            timeout: 5000
          });
        } else {
          Alert.error("Dosya yklenemedi.", {
            position: 'top-right',
            effect: 'stackslide',
            timeout: 5000
          });
        }
      })
    }
  }


  getStatusRender(status) {
    let className = (status === "processing" ? "spinner fa-spin" : (status === "success" ? "check" : (status === "error" ? "close" : "pause")));
    let color = (status === "processing" ? "dark" : (status === "success" ? "green" : (status === "error" ? "red" : "dark")));
    return (<i className={"fas fa-" + className} style={{color: color, paddingRight: "4px"}}></i>);
  }

  render() {
    return (
      <DefaultModal
        isOpen={this.props.modal}
        title="ndirilebilir Dosya Ykleme"
        size="lg"
        modalBodyStyle={{padding: "0", minHeight: "500px"}}
        disabled={this.state.btnDisabled && this.state.fileList[0] === null}
        confirmBtnText="Dosya(lar) Ykle"
        cancelBtnText="Kapat"
        onConfirm={(e) => this.saveFile()}
        onCancel={(e) => this.props.toggleModal(false, false)}>
        <Row>
          <Col sm="12" style={{paddingTop: "15px", paddingRight: "32px", paddingLeft: "32px"}}>

            <Dropzone
              // accept=".pdf, .zip, application/x-zip-compressed, application/zip, multipart/x-zip, .xlsx, image/*"
              minSize={0}
              maxSize={104857600}
              activeClassName="dropzoneActiveClass"
              className="FormRenk col-sm-12"
              multiple={false}
              disabled={this.state.btnDisabled}
              onDrop={this.onDrop}
              onDropRejected={this.onDropRejected}>
              <div className="dropzoneInnerArea">
                ndirilebilir dosyanz ykleyiniz.
              </div>
            </Dropzone>
          </Col>
        </Row>
        <Row>
          <Col sm="12" style={{
            paddingTop: "0px",
            paddingRight: "15px",
            paddingLeft: "15px",
            maxHeight: '500px',
            overflow: "auto"
          }}>
            <table className="" style={{textAlign: 'center', paddingBottom: "5px"}}>
              <thead>
              <tr style={{backgroundColor: '#ebedf0'}}>
                <th width="5%">#</th>
                <th>Dosya Ad</th>
                <th>Durum</th>
              </tr>
              </thead>
              <tbody>
              {this.state.fileList && this.state.fileList.map((document, index) =>
                <tr key={index + "_" + document.name + "_" + document.size}>
                  <td>{index + 1}</td>
                  <td style={{margin: "0px", textAlign: "left"}}>{<TextOverFlowTooltip
                    text={document.name || "-"}
                    maxLength={30}/>}</td>
                  <td
                    style={{margin: "0px"}}>{this.getStatusRender(document.status)}{(document.statusText || "Gnderim bekliyor")}</td>
                </tr>
              )}
              {!this.state.processing && (this.state.fileList && this.state.fileList.length === 0) &&
              <tr>
                <td colSpan="8">Kayt bulunamad</td>
              </tr>
              }
              {this.state.processing &&
              <tr>
                <td colSpan="8">Dokman ileniyor...</td>
              </tr>
              }
              </tbody>
            </table>
          </Col>
          <Col sm="12">
            <p style={{float: "right"}}>Toplam
              Adet: {this.state.fileList ? this.state.fileList.length : 0}</p>
          </Col>
        </Row>
        <Row>
          <Label style={{
            paddingTop: "30px",
            paddingRight: "15px",
            paddingLeft: "15px",
            maxHeight: '500px',
            overflow: "auto"
          }}>Kaytl ndirilebilir Dosyalar</Label>
          <Col sm="12" style={{
            paddingTop: "0px",
            paddingRight: "15px",
            paddingLeft: "15px",
            maxHeight: '500px',
            overflow: "auto"
          }}>
            <table className="" style={{textAlign: 'center', paddingBottom: "5px"}}>
              <thead>
              <tr style={{backgroundColor: '#ebedf0'}}>
                <th width="5%">#</th>
                <th>Dosya Ad</th>
                <th>Eriim Adresi</th>
                <th>lem</th>
              </tr>
              </thead>
              <tbody>
              {this.state.existFileList && this.state.existFileList.map((document, index) =>
                <tr key={index + "_" + document.name + "_" + document.size}>
                  <td>{index + 1}</td>
                  <td style={{margin: "0px", textAlign: "left"}}>{<TextOverFlowTooltip
                    text={document.loadName || "-"}
                    maxLength={30}/>}</td>
                  <td
                    style={{margin: "0px"}}>{this.state.fileUrl + "/ik-platform/api/companyinfo/readDownloadableFiles/" + document.verificationCode}</td>
                  <td
                    style={{margin: "0px"}}><MiniButton title="Sil" faIcon="fa-trash" color="danger"
                                                        onClick={() => {
                                                          this.setState({
                                                            alert: (
                                                              <SweetAlertExt showCancel warning
                                                                             confirmBtnText="Evet"
                                                                             cancelBtnText="Hayr"
                                                                             confirmBtnBsStyle="danger"
                                                                             cancelBtnBsStyle="default"
                                                                             title="Emin misiniz?"
                                                                             onConfirm={() => this.deleteRow(document.id)}
                                                                             onCancel={() => this.setState({alert: null})}

                                                              >
                                                                {document.loadName + " " + " adl dosya silinecek."}
                                                              </SweetAlertExt>
                                                            )
                                                          });
                                                        }}/></td>
                </tr>
              )}
              {!this.state.processing && (this.state.existFileList && this.state.existFileList.length === 0) &&
              <tr>
                <td colSpan="8">Kayt bulunamad</td>
              </tr>
              }
              </tbody>
            </table>
          </Col>
          <Col sm="12">
            <p style={{float: "right"}}>Toplam
              Adet: {this.state.existFileList ? this.state.existFileList.length : 0}</p>
          </Col>
          {this.state.alert}
        </Row>

      </DefaultModal>
    );
  }

}

export default DownloadableFileModal;