import React, {Component} from 'react';
import moment from "moment/moment";
import 'moment/locale/tr';

import Select from "../../local_node_modules/react-select"
import '../../local_node_modules/react-select/dist/react-select.css';

import Datetime from "react-datetime/DateTime";
import 'react-datetime/css/react-datetime.css';


class SearchField extends Component {
    constructor(props) {
        super(props);

        this.state = {
            label: undefined,
            accessor: undefined,
            operator: undefined,
            type: undefined,
            value: '',
            selectData: []
        };

        this.createComponent = this.createComponent.bind(this);
        this.dateComponent = this.dateComponent.bind(this);
        this.dateTimeComponent = this.dateTimeComponent.bind(this);
        this.selectComponent = this.selectComponent.bind(this);
        this.textComponent = this.textComponent.bind(this);
        this.numericTextComponent = this.numericTextComponent.bind(this);
        this.doubleTextComponent = this.doubleTextComponent.bind(this);
        this.dateComponent = this.dateComponent.bind(this);

        this.onChangeText = this.onChangeText.bind(this);
        this.onChangeNumericText = this.onChangeNumericText.bind(this);
        this.onChangeDoubleText = this.onChangeDoubleText.bind(this);
        this.onChangeDate = this.onChangeDate.bind(this);
        this.onChangeSelect = this.onChangeSelect.bind(this);
        this.onChangeCheckBox = this.onChangeCheckBox.bind(this);

    }

    componentDidMount() {
        if (this.props.value)
            this.setState({value: this.props.value});
    }

    dateComponent() {
        let dateValue = this.props.value;
        return (
            <Datetime locale="tr"
                      className="datepicker"
                      input={true}
                      closeOnSelect={true}
                      value={moment(dateValue, "YYYY-MM-DD", true).isValid() ? moment(dateValue).format("DD-MM-YYYY") : dateValue}
                      dateFormat="DD-MM-YYYY"
                      timeFormat={false}
                      onChange={(e) => this.onChangeDate(e, "date", this.props.accessor)}
                      inputProps={{readOnly: true}}/>
        )
    }

    dateTimeComponent() {
        let dateValue = this.props.value;
        return (
            <Datetime locale="tr"
                      className="datepicker"
                      input={true}
                      closeOnSelect={true}
                      value={moment(dateValue, "YYYY-MM-DD", true).isValid() ? moment(dateValue).format("DD-MM-YYYY HH:mm:ss") : dateValue}
                      dateFormat="DD-MM-YYYY"
                      timeFormat={false}
                      onChange={(e) => this.onChangeDate(e, "datetime", this.props.accessor)}
                      inputProps={{readOnly: true}}/>

        )
    }

    monthComponent() {
        let dateValue = this.props.value;
        return (
            <Datetime locale="tr"
                      className="datepicker"
                      input={true}
                      closeOnSelect={true}
                      value={moment(dateValue, "YYYY-MM-DD", true).isValid() ? moment(dateValue).format("YYYY/MM") : dateValue}
                      dateFormat="MM/YYYY"
                      timeFormat={false}
                      onChange={(e) => this.onChangeDate(e, "month", this.props.accessor)}
                      inputProps={{readOnly: true}}/>
        )
    }

    optionClicked(optionsList) {
        this.setState({multiSelect: optionsList});
    }

    selectedBadgeClicked(optionsList) {
        this.setState({multiSelect: optionsList});
    }

    selectComponent() {
        return (
            <Select
                // className='selectpicker'
                type='select'
                // multiple
                value={this.props.value}
                clearable={false}
                noResultsText="Veri yok"
                clearValueText="Temizle"
                placeholder="Seiniz..."
                onChange={(e) => this.onChangeSelect(e, this.props.accessor)}
                options={this.props.selectData}
                // options= {this.props.selectData.map((data) => ({
                //   value: data
                // }))}
                // {this.props.selectData.map((data) => <option value={data.value}>{data.label}</option>)}</Select>
            />
        )
    }

    textComponent(handleKeyPress) {
        return (
            <input className="form-control"
                   type="text"
                   onKeyPress={handleKeyPress}
                   value={this.props.value}
                   onChange={(e) => this.onChangeText(e, this.props.accessor)}/>
        )
    }

    checkBoxComponent() {
        return (
            <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                <input
                    className="form-control"
                    type="checkbox"
                    checked={this.props.value}
                    style={{ width: '20px'}}
                    onChange={(e) => this.onChangeCheckBox(e, this.props.accessor)}
                />
            </div>
        );
    }

    numericTextComponent(handleKeyPress) {
        return (
            <input className="form-control"
                   type="text"
                   pattern="[0-9]*"
                   value={this.props.value}
                   onKeyPress={handleKeyPress}
                   onChange={(e) => this.onChangeNumericText(e, this.props.accessor)}/>
        )
    }

    doubleTextComponent(handleKeyPress) {
        return (
            <input className="form-control"
                   type="text"
                   pattern="\d+(\.\d*)?"
                   value={this.props.value}
                   onKeyPress={handleKeyPress}
                   onChange={(e) => this.onChangeDoubleText(e, this.props.accessor)}/>
        )
    }

    /********** EVENT HANDLERS ****/

    onChangeSelect(e, fieldName) {

        if (!e) return;

        this.setState({value: e.value});

        this.props.listener(fieldName, e.value)

    }

    onChangeText(e, fieldName) {

        if (!e) return;

        this.setState({value: e.target.value});

        this.props.listener(fieldName, e.target.value)

    }

    onChangeCheckBox(e, fieldName) {

        this.setState({value: e.target.checked});

        this.props.listener(fieldName, e.target.checked)

    }

    onChangeNumericText(e, fieldName) {

        if (!e) return;
        if (!e.target.validity.valid) {
            return;
        }

        let value = e.target.value;

        this.setState({value: value});

        this.props.listener(fieldName, value)

    }

    onChangeDoubleText(e, fieldName) {

        if (!e) return;
        if (!e.target.validity.valid) {
            return;
        }

        let value = e.target.value;

        this.setState({value: value});

        this.props.listener(fieldName, value)

    }

    onChangeDate(momentObj, type, fieldValue) {
        let value;
        if (!(typeof momentObj === "string")) {

            if (type === "date") {
                value = momentObj.format("YYYY-MM-DD");
                this.setState({value});
            } else if (type === "datetime") {
                value = momentObj.format("YYYY-MM-DD");
                this.setState({value})
            } else if (type === "month") {
                value = momentObj.format("YYYY-MM-DD");
                this.setState({value});
            }
        } else {
            value = momentObj;
            if (type === "datetime" && moment(value, "DD-MM-YYYY", true).isValid()) {
                value = moment(value, "DD-MM-YYYY", true).format("YYYY-MM-DD");
            }
            this.setState({value})
        }

        this.props.listener(fieldValue, value)
    }


    render() {

        return (

            <div className="col-sm-6 row">
                <div className="col-sm-5" style={{marginBottom: '5px', marginLeft: '-5px'}}>
                    {
                        this.props.label
                    }
                </div>

                <div className="col-sm-7" style={{marginBottom: '5px'}}>

                    {
                        this.props.type &&
                        this.createComponent(this.props.type, this.props.operator)
                    }
                </div>
            </div>

        )
    }


    /**** COMPONENT HANDLER ***/


    createComponent(type, operator) {
        if (type === "select") {
            return this.selectComponent();
        } else if (type === "date") {
            return this.dateComponent();
        } else if (type === "datetime") {
            return this.dateTimeComponent();
        } else if (type === "month") {
            return this.monthComponent();
        } else if (type === "text") {
            return this.textComponent(this.props.onKeyPress);
        } else if (type === "numericText") {
            return this.numericTextComponent(this.props.onKeyPress);
        } else if (type === "doubleText") {
            return this.doubleTextComponent(this.props.onKeyPress);
        } else if (type === "boolean") {
            return this.checkBoxComponent();
        } else {
            return null;
        }
    }


}


export default SearchField;