import React from 'react';
import Swal from "sweetalert2";
import {Col, Row} from "reactstrap";


class ImageUpload extends React.Component {
    constructor(props) {
        super(props);
        this.state = {file:"", imagePreviewUrl: props.value !== undefined ? [props.value[0]] : [], rotation: 0};
        this.rotate = this.rotate.bind(this);
        this.rotateleft = this.rotateleft.bind(this);
        this.rotateImage = this.rotateImage.bind(this);
        this.delete = this.delete.bind(this);
    }

    _handleImageChange(e) {
        let self = this;
        e.preventDefault();
        let reader = new FileReader();
        let file = e.target.files[0];
        if (file != undefined && file.size > 8 * 1024 * 1024) {
            Swal({
                title: 'Uyar ! ',
                text: "Dosya Boyutu 8MB'dan byk olamaz",
                confirmButtonText: 'Tamam'
            }).then((result) => {
                //self.fetchData
            });
            return;
        }
        if (file != undefined && (file.type != "image/jpeg" && file.type != "image/jpg")) {
            Swal({
                title: 'Uyar ! ',
                text: "Dosya jpg yada jpeg formatnda olmaldr.",
                confirmButtonText: 'Tamam'
            }).then((result) => {
                //self.fetchData
            });
            return;
        }
        if (file.name.includes(",")) {
            Swal({
                title: 'Uyar ! ',
                text: "Dosya isminde ',' bulunamaz.",
                confirmButtonText: 'Tamam'
            }).then((result) => {
                //self.fetchData
            });
            return;
        }
        reader.onloadend = () => {
            self.setState({
                file: file,
                imagePreviewUrl: [reader.result],
                rotatedImage: [reader.result],
                rotation: 0
            });
            self.props.onChange(self.state.rotatedImage[0]);
        }
        if (file != undefined)
            reader.readAsDataURL(file);
        e.target.value = "";
    }

    rotateImage(imageBase64, rotation) {
        let img = new Image();
        img.src = imageBase64;
        img.onload = () => {
            let canvas = document.createElement("canvas");
            canvas.width = img.width;
            canvas.height = img.height;
            let ctx = canvas.getContext("2d");
            ctx.translate(canvas.width / 2, canvas.height / 2);
            ctx.rotate(rotation * (Math.PI / 180));
            ctx.drawImage(img, -canvas.width / 2, -canvas.height / 2);
            this.setState({rotatedImage: [canvas.toDataURL("image/jpeg", "image/jpg")]});
            this.props.onChange(this.state.rotatedImage[0]);
        };
    }

    rotate() {
        let newRotation = this.state.rotation + 90;
        if (newRotation >= 360) {
            newRotation = -360;
        }
        this.setState({
            rotation: newRotation,
        })
        this.rotateImage(this.state.imagePreviewUrl[0], newRotation);
    }

    rotateleft() {
        let newRotation = this.state.rotation - 90;
        if (newRotation >= 360) {
            newRotation = -360;
        }

        this.setState({
            rotation: newRotation,
        })
        this.rotateImage(this.state.imagePreviewUrl[0], newRotation);
    }

    delete() {
        this.setState({imagePreviewUrl: [], rotatedImage: [], newRotation: 0, file:""});
        this.props.onChange(undefined);
    }

    render() {
        let {imagePreviewUrl, rotation} = this.state;
        return (

            <div className="previewComponent" style={{border: "1px solid"}}>
                <table>
                    <tbody>
                    <tr>
                        <td rowSpan="5" width="200" height="200" style={{border: "1px black solid"}}><img
                            id="uploadedImage"
                            style={{transform: `rotate(${rotation}deg)`, objectFit: "containg"}}
                            src={imagePreviewUrl[0] !== undefined ? imagePreviewUrl[0] : (this.props.value !== undefined ? this.props.value[0] : "")}/>
                        </td>
                        <td>
                            <Row>
                                <Col>
                                    <button disabled={this.props.disabled}>{" Dosya Se "}
                                        <input type={"file"} onChange={(e) => this._handleImageChange(e)}
                                               accept="image/jpeg, imagejpg"/>
                                    </button>
                                </Col>
                                <Col>
                                    <label style={{
                                        textOverflow: "ellipsis",
                                        overflow: "hidden",
                                        whiteSpace: "nowrap",
                                        width: "120px",
                                        marginLeft: "10px"
                                    }}>{this.state.file.name}</label>
                                </Col>
                            </Row>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <button disabled={this.props.disabled} onClick={this.rotate} type="button"><i
                                className="fas fa-arrow-alt-circle-right"></i>{" Saa Dndr"}</button>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <button disabled={this.props.disabled} onClick={this.rotateleft} type="button"><i
                                className="fas fa-arrow-alt-circle-left"></i>{" Sola Dndr"}</button>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <button disabled={this.props.disabled} onClick={this.delete} type="button"><i
                                className="far fa-trash-alt"></i>{" Temizle "}</button>
                        </td>
                    </tr>
                    <tr>
                        <td><p style={{color: "red", fontSize: "80%"}}>Not: Resim ters grnyor ise yukardaki yn
                            tular ile saa veya sola dndrerek
                            dzeltiniz. </p></td>
                    </tr>
                    </tbody>
                </table>
            </div>
        )
    }
}

export default ImageUpload;