import React, {Component} from 'react';
import {Modal} from "react-bootstrap";
import {Col, Row} from "reactstrap";
import {requestK} from "../../../../helper/APIUtils";
import {showAxiosError} from "../../../../helper/helper";
import TextOverFlowTooltip from "../../../../components/TextOverFlowTooltip";
import Alert from "react-s-alert";
import moment from "moment";

class VaccinationCardModal extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showVaccinatinCardModal: this.props.showVaccinatinCardModal,
            vaccinationCardInfo: this.props.vaccinationCardInfo
        }
    }

    confirmVaccinationInfo = () => {
        let self = this;
        let params = {
            url: `/employeevaccination/saveVaccinationCard`,
            method: "post",
            data: {
                employeeId: self.props.employeeId,
                vaccinationCardPyld: self.state.vaccinationCardInfo
            }
        };
        requestK(params)
            .then(function (response) {
                self.setState({alert: null}, () => self.props.toggleModal(false));
                Alert.success("A Kart/PCR testi Bilgileri Kaydedildi.", {
                    position: 'top-right',
                    effect: 'stackslide',
                    timeout: 5000
                });
            }).catch(function (error) {
            showAxiosError(error);
            Alert.error("u an ileminiz gerekletirilemiyor. Ltfen daha sonra tekrar deneyiniz.", {
                position: 'top-right',
                effect: 'stackslide',
                timeout: 5000
            });
        });
    }

    deleteVaccinationInfo = (employeeId) => {
        let self = this;
        let params = {
            url: `/employeevaccination/deleteVaccinationCard`,
            method: "post",
            data: employeeId,
        };
        requestK(params)
            .then(function (response) {
                self.setState({}, () => self.props.isVaccinationcard())
                self.setState({alert: null}, () => self.props.toggleModal(false))
            }).catch(function (error) {
            showAxiosError(error);
            Alert.error("u an ileminiz gerekletirilemiyor. Ltfen daha sonra tekrar deneyiniz.", {
                position: 'top-right',
                effect: 'stackslide',
                timeout: 5000
            });
        });
    }

    getValue = (field) => {
        let flag = this.props.create;
        if (field === "name") {
            return this.state.vaccinationCardInfo.personInfoPyld ? this.state.vaccinationCardInfo.personInfoPyld.name : "";
        } else if (field === "surname") {
            return this.state.vaccinationCardInfo.personInfoPyld ? this.state.vaccinationCardInfo.personInfoPyld.surname : "";
        } else if (field === "birthOfDay") {
            return this.state.vaccinationCardInfo.birthOfDay ? moment(this.state.vaccinationCardInfo.birthOfDay).isValid() ? moment(this.state.vaccinationCardInfo.birthOfDay).format("DD-MM-YYYY") : '' : "";
        } else if (field === "version") {
            return this.state.vaccinationCardInfo.version ? this.state.vaccinationCardInfo.version : "";
        }

        if (flag && this.state.vaccinationCardInfo.vaccinationInfoPyld != null ) {
            if (field === "dt" && flag) {
                return this.state.vaccinationCardInfo.vaccinationInfoPyld.dt ? moment(this.state.vaccinationCardInfo.vaccinationInfoPyld.dt).isValid() ? moment(this.state.vaccinationCardInfo.vaccinationInfoPyld.dt).format("DD-MM-YYYY") : '' : "";

            } else if (field === "cardType") {
                return "A Kart";
            } else if (field === "ma" && flag) {
                return this.state.vaccinationCardInfo.vaccinationInfoPyld.ma ? this.state.vaccinationCardInfo.vaccinationInfoPyld.ma : "";
            } else if (field === "dn" && flag) {
                return this.state.vaccinationCardInfo.vaccinationInfoPyld.dn ? this.state.vaccinationCardInfo.vaccinationInfoPyld.dn : "";
            } else if (field === "sd" && flag) {
                return this.state.vaccinationCardInfo.vaccinationInfoPyld.sd ? this.state.vaccinationCardInfo.vaccinationInfoPyld.sd : "";
            } else if (field === "ci" && flag) {
                return this.state.vaccinationCardInfo.vaccinationInfoPyld.ci ? this.state.vaccinationCardInfo.vaccinationInfoPyld.ci : "";
            } else if (field === "is" && flag) {
                return this.state.vaccinationCardInfo.vaccinationInfoPyld.is ? this.state.vaccinationCardInfo.vaccinationInfoPyld.is : "";
            } else if (field === "co" && flag) {
                return this.state.vaccinationCardInfo.vaccinationInfoPyld.co ? this.state.vaccinationCardInfo.vaccinationInfoPyld.co : "";
            }
        } else if (flag && this.state.vaccinationCardInfo.pcrInfoPyld != null){
            if (field === "ma" && flag) {
                return this.state.vaccinationCardInfo.pcrInfoPyld.ma ? this.state.vaccinationCardInfo.pcrInfoPyld.ma : "";
            } else if (field === "cardType") {
                return "PCR Testi";
            } else if (field === "ci" && flag) {
                return this.state.vaccinationCardInfo.pcrInfoPyld.ci ? this.state.vaccinationCardInfo.pcrInfoPyld.ci : "";
            } else if (field === "is" && flag) {
                return this.state.vaccinationCardInfo.pcrInfoPyld.is ? this.state.vaccinationCardInfo.pcrInfoPyld.is : "";
            } else if (field === "co" && flag) {
                return this.state.vaccinationCardInfo.pcrInfoPyld.co ? this.state.vaccinationCardInfo.pcrInfoPyld.co : "";
            } else if (field === "sc" && flag) {
                return this.state.vaccinationCardInfo.pcrInfoPyld.sc ? moment(this.state.vaccinationCardInfo.pcrInfoPyld.sc).isValid() ? moment(this.state.vaccinationCardInfo.pcrInfoPyld.sc).format("DD-MM-YYYY HH:mm:ss") : '' : "";
            } else if (field === "dr" && flag) {
                return this.state.vaccinationCardInfo.pcrInfoPyld.dr ? moment(this.state.vaccinationCardInfo.pcrInfoPyld.dr).isValid() ? moment(this.state.vaccinationCardInfo.pcrInfoPyld.dr ).format("DD-MM-YYYY HH:mm:ss") : '' : "";
            } else if (field === "tc" && flag) {
                return this.state.vaccinationCardInfo.pcrInfoPyld.tc ? this.state.vaccinationCardInfo.pcrInfoPyld.tc : "";
            }
        }

        if (!flag ) {
            if (field === "dt" && !flag) {
                return this.state.vaccinationCardInfo ? this.state.vaccinationCardInfo.vaccinationDate : "";
            } else if (field === "cardType") {
                return this.state.vaccinationCardInfo.cardType === "VACCINATION" ? "A Kart" : "PCR Testi";
            } else if (field === "ma" && !flag) {
                return this.state.vaccinationCardInfo ? this.state.vaccinationCardInfo.manufacturer : "";
            } else if (field === "dn" && !flag) {
                return this.state.vaccinationCardInfo ? this.state.vaccinationCardInfo.doseNumber : "";
            } else if (field === "sd" && !flag) {
                return this.state.vaccinationCardInfo ? this.state.vaccinationCardInfo.totalDoses : "";
            } else if (field === "ci" && !flag) {
                return this.state.vaccinationCardInfo ? this.state.vaccinationCardInfo.certificateId : "";
            } else if (field === "is" && !flag) {
                return this.state.vaccinationCardInfo ? this.state.vaccinationCardInfo.certificateIssuer : "";
            } else if (field === "co" && !flag) {
                return this.state.vaccinationCardInfo ? this.state.vaccinationCardInfo.country : "";
            } else if (field === "sc" && !flag) {
                return this.state.vaccinationCardInfo ? this.state.vaccinationCardInfo.samplingDate : "";
            } else if (field === "dr" && !flag) {
                return this.state.vaccinationCardInfo ? this.state.vaccinationCardInfo.resultDate : "";
            } else if (field === "tc" && !flag) {
                return this.state.vaccinationCardInfo ? this.state.vaccinationCardInfo.testingAgency : "";
            }
        }
    }

    renderInfo(label, field){
        return (
            <Row className='containerRows'>
                <Col xs='6' sm='6' md='6' lg='6' xl='6' style={{margin: '0', padding: '0 0 0 2%'}}>
                    <label style={{fontWeight: 'bold'}}>{label + ": "}</label>
                </Col>

                <Col xs='6' sm='6' md='6' lg='6' xl='6' style={{margin: '0', padding: '0'}}>
                    {typeof this.getValue(field) === "boolean" ? <input type="checkbox" checked={this.getValue(field)}/>:
                        <TextOverFlowTooltip
                            maxLength={25}
                            controlled
                            autohide={false}
                            style={{
                                wordBreak: 'break-word',
                                color: 'darkgray',
                            }}
                            text={
                                this.state.vaccinationCardInfo &&
                                this.getValue(field) ? (this.getValue(field)) : "Belirtilmemi..."
                            }
                        />}
                </Col>
            </Row>
        )
    }

    render() {
        let title = this.props.create ? "AB A Kart/PCR Testi Bilgileri" : "Son Eklenen AB A Kart/PCR Testi Bilgileri";
        return (
            <Modal show={this.props.modal}
                   animation={false} size={"xl"}
                   toggle={() => {
                       this.setState({}, () => this.props.toggleModal(false, false))
                   }}
                   modalTransition={{timeout: 700}}
                   dialogClassName="modal-90w"
                   backdropTransition={{timeout: 1300}}
                   aria-labelledby="example-custom-modal-styling-title"
                   backdrop="static">
                <Modal.Header>
                    <Modal.Title>
                        <label style={{color: 'white', fontFamily: 'Montserrat', fontWeight: 'normal'}}>{title}</label>
                    </Modal.Title>
                    <div>
                        <button className='fas fa-times simpleButton'
                                style={{
                                    position: 'unset',
                                    right: '0',
                                    top: '0',
                                    outline: '0',
                                    background: 'transparent'
                                }}
                                onClick={(e) => this.props.toggleModal(false, false)}/>
                    </div>
                </Modal.Header>
                <Modal.Body>
                    {this.props.create && this.state.vaccinationCardInfo.vaccinationInfoPyld !== null &&
                    <Row className="FormRenk" style={{padding: '2% 0 2% 0', margin: '0 4% 2% 4%'}}>
                        <Col xs='12' sm='12' md='12' lg='12'>
                            <Row>
                                <Col xs='12' sm='12' md='12' lg='7' xl='7' className='containerFrame'>
                                    {this.renderInfo("sim", "name")}
                                    {this.renderInfo("Soyisim", "surname")}
                                    {this.renderInfo("Belge Tr", "cardType")}
                                    {this.renderInfo("Doum tarihi", "birthOfDay")}
                                    {this.renderInfo("Version", "version")}
                                    {this.renderInfo("A Tarihi", "dt")}
                                    {this.renderInfo("Doz Numaras", "dn")}
                                    {this.renderInfo("Toplam Doz", "sd")}
                                    {this.renderInfo("A reticisi", "ma")}
                                    {this.renderInfo("Sertifika ID", "ci")}
                                    {this.renderInfo("Sertifika Veren", "is")}
                                    {this.renderInfo("lke", "co")}
                                </Col>
                            </Row>
                        </Col>
                    </Row>}
                    {!this.props.create && this.state.vaccinationCardInfo.cardType === "VACCINATION" &&
                    <Row className="FormRenk" style={{padding: '2% 0 2% 0', margin: '0 4% 2% 4%'}}>
                        <Col xs='12' sm='12' md='12' lg='12'>
                            <Row>
                                <Col xs='12' sm='12' md='12' lg='7' xl='7' className='containerFrame'>
                                    {this.renderInfo("Belge Tr", "cardType")}
                                    {this.renderInfo("Alanma Tarihi", "dt")}
                                    {this.renderInfo("Doz Numaras", "dn")}
                                    {this.renderInfo("Toplam Doz", "sd")}
                                    {this.renderInfo("A reticisi", "ma")}
                                    {this.renderInfo("Sertifika ID", "ci")}
                                    {this.renderInfo("Sertifika Veren", "is")}
                                    {this.renderInfo("lke", "co")}
                                </Col>
                            </Row>
                        </Col>
                    </Row>}
                    {this.props.create && this.state.vaccinationCardInfo.pcrInfoPyld !== null &&
                    <Row className="FormRenk" style={{padding: '2% 0 2% 0', margin: '0 4% 2% 4%'}}>
                        <Col xs='12' sm='12' md='12' lg='12'>
                            <Row>
                                <Col xs='12' sm='12' md='12' lg='7' xl='7' className='containerFrame'>
                                    {this.renderInfo("sim", "name")}
                                    {this.renderInfo("Soyisim", "surname")}
                                    {this.renderInfo("Belge Tr", "cardType")}
                                    {this.renderInfo("Doum tarihi", "birthOfDay")}
                                    {this.renderInfo("Version", "version")}
                                    {this.renderInfo("rnek Alma Tarihi", "sc")}
                                    {this.renderInfo("Sonu Tarihi", "dr")}
                                    {this.renderInfo("Test Kuruluu", "tc")}
                                    {this.renderInfo("reticisi", "ma")}
                                    {this.renderInfo("Sertifika ID", "ci")}
                                    {this.renderInfo("Sertifika Veren", "is")}
                                    {this.renderInfo("lke", "co")}
                                </Col>
                            </Row>
                        </Col>
                    </Row>}
                    {!this.props.create && this.state.vaccinationCardInfo.cardType === "PCR" &&
                    <Row className="FormRenk" style={{padding: '2% 0 2% 0', margin: '0 4% 2% 4%'}}>
                        <Col xs='12' sm='12' md='12' lg='12'>
                            <Row>
                                <Col xs='12' sm='12' md='12' lg='7' xl='7' className='containerFrame'>
                                    {this.renderInfo("Belge Tr", "cardType")}
                                    {this.renderInfo("rnek Alma Tarihi", "sc")}
                                    {this.renderInfo("Sonu Tarihi", "dr")}
                                    {this.renderInfo("Test Kuruluu", "tc")}
                                    {this.renderInfo("reticisi", "ma")}
                                    {this.renderInfo("Sertifika ID", "ci")}
                                    {this.renderInfo("Sertifika Veren", "is")}
                                    {this.renderInfo("lke", "co")}
                                </Col>
                            </Row>
                        </Col>
                    </Row>}
                </Modal.Body>
                <Modal.Footer>
                    <Col xs='6' sm='6' md='6' lg='6' style={{textAlign: 'center'}}>
                        <button type="button"
                                style={{background: 'rgb(158, 158, 158)', padding: '11px 45px 11px 20px'}}
                                className="TekBtn vazgec"
                                onClick={() => this.setState({}, () => this.props.toggleModal(false))}>{'Kapat'}
                        </button>
                    </Col>
                </Modal.Footer>
                {this.state.alert}
            </Modal>
        );
    }
}

export default VaccinationCardModal;