import React, {Component} from 'react';
import {Card, CardBody, CardHeader, CardTitle, Col, Container, Input, Nav, Row} from 'reactstrap';
import 'react-table/react-table.css'
import SweetAlert from 'react-bootstrap-sweetalert';
import Notify from 'react-notification-alert';
import 'react-datetime/css/react-datetime.css'
import 'moment/locale/tr'
import StickButton from "../../../components/StickButton";
import {requestK} from "../../../helper/APIUtils";
import {showAxiosError} from "../../../helper/helper";
import Alert from "react-s-alert";
import TextOverFlowTooltip from "../../../components/TextOverFlowTooltip";
import {renewListForPrivileges} from "../common/PeykHelper";
import Table from "../../../components/Table/Table";
import DocumentState from "../common/DocumentState";
import DropdownCheck from "../../../components/Table/DropdownCheck";

class ResendFormMimePage extends Component {

    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            manual: false,
            content: [],
            total: 0,
            searching: false,
            searchQuery: undefined,
            page: 0,
            size: 10,
            selection: [],
            approvalUserId: null,
            employeeUserId: null,
            timeOut: false,
        };
        this.notifyMimeResend = React.createRef();

        this.fetchData = this.fetchData.bind(this);
        this.resendMimes = this.resendMimes.bind(this);
        this.showErrorMessage = this.showErrorMessage.bind(this);
    }

    showErrorMessage(error) {
        this.notifyMimeResend.current.notificationAlert({
            place: 'tr',
            message: 'Hata: ' + error.errorMessage,
            type: 'danger',
            autoDismiss: 10,
        });
    }

    componentDidMount() {
        if (!this.props || !this.props.location || !this.props.location.formSession)
            this.props.history.push({pathname: `/${this.props.peykName}/formsessionlist`});
        else {
            if(!this.state.timeOut) {
                this.readMimeInfo();
            }
        }

    }
    getEmployeeId = async () => {
        let self = this;
        let employeeId = this.state.content[0].employee.id;
        let params = {
            url: `/employee/getEmployeeId/${employeeId}`,
            method: "post"
        };

        try {
            let response = await requestK(params);
            self.setState({
                employeeUserId: response.data
            });
        } catch (error) {
            showAxiosError(error);
            self.setState({ loading: false });
        }
    };

    getApprovalUserId = async () => {
        let self = this;
        let approvalId = this.state.content[0].id;
        let params = {
            url: `/approval/getApprovalUserId/${approvalId}`,
            method: "post"
        };

        try {
            let response = await requestK(params);
            self.setState({
                approvalUserId: response.data
            });
        } catch (error) {
            showAxiosError(error);
            self.setState({ loading: false });
        }
    };

    readMimeInfo = () => {
        let self = this;
        let params = {
            url: '/formsession/readMimeInfo',
            method: "post",
            data: self.props.location.formSession.id

        };

        self.setState({loading: true});
        requestK(params)
            .then(function (response) {
                self.setState({
                    mimeTitle: response.data.mimeTitle,
                    mimeBody: response.data.mimeBody,
                    loading: false,

                });

                if (self.props.location.isKepOwner) {
                    self.setState({kepAccount: response.data.account});
                }
            })
            .catch(function (error) {
                showAxiosError(error);
                self.setState({loading: false});
            });
    };

    fetchData(accessor, direction, page, size) {
        let self = this;
        let sortStr = accessor !== undefined ? ('&sort=' + accessor + ',' + direction) : '';
        let params = {
            url: '/formsession/readResendFormNtfEmployeeList?page=' + page + '&size=' + size + sortStr,
            method: "post",
            data: self.props.location.formSession.id

        };
        self.setState({loading: true, manual: self.manual === true ? false : self.manual});
        requestK(params)
            .then(async function (response) {
                self.setState({
                    page: page,
                    size: size,
                    total: (response.data && response.data.page) ? response.data.page.totalElements : 0,
                    content: (response.data && response.data.page) ? response.data.page.content : [],
                    totalIdList: response.data ? response.data.selectedIdList : [],
                    loading: false
                });
                if(response.data.page.content[0].state === 'FLOW_TIMEOUT'){
                    self.setState({ timeOut:true});
                    await self.getEmployeeId();
                    await self.getApprovalUserId();
                    if (self.state.approvalUserId === self.state.employeeUserId) {
                        self.readFlowTimeoutTemplate();
                    } else {
                        self.readTimeoutTemplate();
                    }
                }
            }).catch(function (error) {
            self.setState({loading: false});
            showAxiosError(error);
        });

    }
    readFlowTimeoutTemplate = () => {
        let self = this;
        let params = {
            url: `/signer/readFlowTimeoutTemplate`,
            method: "get"
        };
        requestK(params)
            .then(function (response) {
                if (response.data) {
                    self.setState({mimeBody: response.data})
                }
            }).catch(function (error) {
            showAxiosError(error);
        });
    };
    readTimeoutTemplate = () => {
        let self = this;
        let params = {
            url: `/signer/readTimeoutTemplate`,
            method: "get"
        };
        requestK(params)
            .then(function (response) {
                if (response.data) {
                    self.setState({mimeBody: response.data})
                }
            }).catch(function (error) {
            showAxiosError(error);
        });
    };
    updateMimeInfo(){
        let self = this;
        let params = {
            url: `/signer/writeFormMimeInfo`,
            method: "post",
            data: {
                formSessionId: self.props.location.formSession.id,
                kepAccountId: self.state.kepAccountId && parseInt(self.state.kepAccountId),
                title: self.state.mimeTitle,
                body: self.state.mimeBody
            }
        };
        requestK(params)
            .then(function (response) {
                self.resendMimes();
            }).catch(function (error) {
            showAxiosError(error);
        });
    }

    resendMimes() {
        let self = this;
        let params = {
            url: `/formsession/resendMime`,
            method: "post",
            data: {
                id: self.props.location.formSession.id,
                recordIds: self.state.selection
            }
        };
        Alert.warning("Uyar gnderme ilemi balatlyor...", {
            position: 'top-right',
            effect: 'stackslide',
            timeout: 4000
        });
        self.setState({disableSendBtn: true, loading: true});
        requestK(params)
            .then(function (response) {
                Alert.success(response.data, {
                    position: 'top-right',
                    effect: 'stackslide',
                    timeout: 2000
                });
                self.setState({disableSendBtn: false, loading: false});
                self.props.history.push({pathname: `/${self.props.peykName}/formsession/` + self.props.location.formSession.id});
            })
            .catch(function (error) {
                self.setState({disableSendBtn: false, loading: false});
                showAxiosError(error);
            });
    }
    handleChangeTitle = (event) => {
        this.setState({ mimeTitle: event.target.value });
    }

    handleChangeBody = (event) => {
        this.setState({ mimeBody: event.target.value });
    }

    renderNavButtons() {
        return (
            <Nav className="topbar" style={{paddingLeft: '14px', height: '53px'}}>
                <StickButton position="leftSide" className="zocial GeriDon" style={{fontSize: 'small'}}
                             label="Geri" title="nceki Sayfaya Git"
                             onClick={e => this.props.history.push({
                                 pathname: `/${this.props.peykName}/formsession/` + this.props.location.formSession.id,
                                 selectedIdList: this.props.location.selectedIdList
                             })}/>
                <StickButton position="leftSide" className="zocial Gonder" style={{fontSize: 'small'}}
                             label="Uyar Gnder" title="Gnderilmi letilere Uyar Gnder"
                             onClick={() => {
                                 {
                                     if (!this.state.selection || this.state.selection.length === 0) {
                                         this.setState({
                                             alert: (
                                                 <SweetAlert info
                                                             confirmBtnText="Tamam"
                                                             confirmBtnBsStyle="warning"
                                                             title="UYARI"
                                                             onConfirm={() => {
                                                                 this.setState({alert: null});
                                                             }}
                                                 >
                                                     Alclarna uyar gnderilmesi istenen formlar seiniz.
                                                 </SweetAlert>
                                             )
                                         });
                                     } else {
                                         this.setState({
                                             alert: (
                                                 <SweetAlert showCancel warning
                                                             confirmBtnText="Evet"
                                                             cancelBtnText="Hayr"
                                                             confirmBtnBsStyle="danger"
                                                             cancelBtnBsStyle="default"
                                                             title="Emin misiniz?"
                                                             onConfirm={() => {
                                                                 this.setState({alert: null});
                                                                 this.updateMimeInfo()
                                                             }}
                                                             onCancel={() => this.setState({alert: null})}
                                                 >
                                                     Uyar iletileri gnderilecek.
                                                 </SweetAlert>
                                             )
                                         });
                                     }
                                 }
                             }}/>
            </Nav>
        );
    }

    renderMimeInputs() {

        return <Container>
            <div className="FormRenk">
                {this.props.location.isKepOwner &&
                    <Row sm={6}>
                        <Col sm={2} style={{top: '7px'}}>
                            Kep Hesab
                        </Col>
                        <Col sm={6}>
                            <Input type="text" style={{marginTop: '2px'}}
                                   value={this.state.kepAccount}
                                   disable={true}/>
                        </Col>
                    </Row>}
                <Row sm={6}>
                    <Col sm={2} style={{top: '5px'}}>
                        {this.props.location.isKepOwner ? "KEP leti Konusu" : "E-Posta leti Konusu"}
                    </Col>
                    <Col sm={6}>
                        <Input type="text" style={{marginTop: '2px'}}
                               value={this.state.mimeTitle}
                               onChange={this.handleChangeTitle}/>
                    </Col>
                </Row>
                <Row sm={6}>
                    <Col sm={2} style={{top: '25px'}}>
                        {this.props.location.isKepOwner ? "KEP leti erii" : "E-Posta leti erii"}
                    </Col>
                    <Col sm={6}>
                        <Input type="textarea" rows={5}
                               style={{height: '200px', border: '1px solid #E3E3E3', marginTop: '2px'}}
                               value={this.state.mimeBody}
                               disabled={this.state.content[0]?.state === 'FLOW_TIMEOUT'}
                               onChange={this.handleChangeBody}/>
                        {this.state.content[0]?.state === 'FLOW_TIMEOUT' &&
                            <span><span style={{ color: 'red' }}>NOT:</span> E-Posta leti eriini Bildirim Tasarm Ekranndan deitirebilirsiniz.</span>
                        }
                    </Col>
                </Row>
            </div>
        </Container>
    }

    toggleCheckboxChange = (e, row, id) => {
        let self = this;
        let checked = e.target.checked;
        let items = self.state.content;
        let selection = self.state.selection;
        if (row === "all") {
            let selection = [];
            if (checked) {
                items.forEach(item => {
                    selection.push(item[id]);
                });
            }
            self.setState({selection: selection, selectedAll: checked});
        } else {
            let index = selection.indexOf(row[id]);
            if (index >= 0) {
                selection = [
                    ...selection.slice(0, index),
                    ...selection.slice(index + 1)
                ];
            } else {
                selection.push(row[id]);
            }
            self.setState({
                selection: selection,
                selectedAll: ((selection.length > 0) ? (selection.length === self.state.content.length) : false)
            });
        }
    };

    isSelected = (id) => {
        return (this.state.selection && this.state.selection.indexOf(id) !== -1);
    };

    renderFormList() {
        let pageIdList = [];
        if (this.state.content && this.state.content instanceof Array) {
            this.state.content.map(e => {
                if (!e.employee || !e.employee.name || e.employee.statusType === "ACTIVE") {
                    pageIdList.push(e.id);
                }
            });
        }

        let columnFieldList = [
            {
                Header: (
                    <DropdownCheck selectionIdList={this.state.selection}
                                   totalIdList={this.state.totalIdList}
                                   pageIdList={pageIdList}
                                   onChange={e => this.setState({selection: e})}/>
                ),
                accessor: "id",
                selectType: "checkbox",
                sortable: false,
                width: '40px',
                Cell: (row, index) => (
                    <input type="checkbox" checked={this.isSelected(row.id)}
                           disabled={row.employee && row.employee.statusType && row.employee.statusType !== 'ACTIVE'}
                           onChange={(e) => this.toggleCheckboxChange(e, row, "id", index)}/>)
            },
            {
                accessor: [
                    {Header: "Ad", accessor: "name"},
                    {Header: "Soyad", accessor: "surname"}
                ],
                Cell: row => (
                    <div style={{margin: 0}}>
                        {row.employee && row.employee.name}

                        <br/>
                        {row.employee && row.employee.surname}
                    </div>
                )
            },
            {
                accessor: [
                    {Header: "Departman", accessor: "department"},
                    {Header: "Grev", accessor: "jobTitle"}
                ],
                Cell: row => (
                    <div style={{margin: 0}}>
                        {row.employee && row.employee.department &&
                            <TextOverFlowTooltip text={row.employee.department.name} maxLength={12}/>}
                        {row.employee && row.employee.department && <br/>}
                        <TextOverFlowTooltip text={row.employee && row.employee.jobTitle} maxLength={12}/>
                    </div>
                )
            },
            {
                Header: "TCKN",
                accessor: "tckn",
                Cell: row => (
                    <div style={{margin: 0}}>
                        {row.employee && <span className="text-success">{row.employee.tckn}</span>}
                    </div>
                )
            },
            {
                Header: "Uyar Says",
                accessor: "warningCount",
                Cell: row => (
                    <div style={{margin: 0}}>
                        {row.warningCount}
                    </div>
                )
            },
            {
                Header: "Form Durumu",
                accessor: "state",
                width: '200px',
                Cell: row => (
                    <div>
                        {row.state &&
                            <span
                                className={"text-" + DocumentState[row.state].messageType}>{DocumentState[row.state].message}</span>}
                        {row.state && DocumentState[row.state].value === 601 && <br/>}
                        {row.state && DocumentState[row.state].value === 601 &&
                            <span className={"text-" + DocumentState[row.state].messageType}>
                            {"Ltfen, Onay ak ablonunuz ile seilen alan bilgilerinin uyumluluunu kontrol ediniz."}
                        </span>}
                    </div>
                )
            }
        ];

        let columns = renewListForPrivileges(this, columnFieldList, "accessor", this.props.location.isKepOwner);

        return <Table columns={columns}
                      content={this.state.content}
                      loading={this.state.loading}
                      loadingText="Kaytlar getiriliyor..."
                      total={this.state.total}
                      page={this.state.page}
                      size={this.state.size}
                      manual={this.state.manual}
                      options={["5", "10", "20", "50", "100"]}
                      fetchData={this.fetchData}/>;

    }

    render() {
        return (
            <Row className="baseBackground">
                <Notify ref={this.notifyMimeResend} className="modalAlert"/>
                {this.renderNavButtons()}
                {this.state.alert &&
                    <div className="row satirBasi">
                        <Notify ref={this.notify}/>
                        {this.state.alert}
                    </div>}
                <div className="row satirBasi">
                    <Col md="12">
                        <Card className="card">
                            <CardHeader>
                                <CardTitle tag="h4">
                                    Uyar Gnder
                                </CardTitle>
                            </CardHeader>
                            <CardBody>
                                {this.props.location.formSession && this.renderMimeInputs()}
                            </CardBody>
                            <CardBody>
                                {this.props.location.formSession && this.renderFormList()}
                            </CardBody>
                        </Card>
                    </Col>
                </div>
            </Row>
        );
    }
}

export default ResendFormMimePage;
