import React, {Component} from 'react';
import './OngoingProcesses.css'
import {Dropdown, DropdownItem, DropdownMenu, DropdownToggle, Progress} from "reactstrap";
import {Client} from '@stomp/stompjs';


class OngoingProcesses extends Component {

  messages = [];
  QUEUE_PREFIX = process.env.NODE_ENV === 'development' ? "springboot-":"wildfly-";

  constructor(props) {
    super(props);
    this.state = {
      dropdownOpen: false,
      processes: new Map(),
      hasNewMessage: false
    };
  };

  componentDidMount() {
  }

  onMessage = (message) => {
    let {processes} = this.state;
    this.messages.push(message);
    let body = JSON.parse(message.body);
    processes.set(body.id, body);
    this.setState({processes, hasNewMessage: true});
  };

  toggle = () => {
    this.messages.forEach(message => message.ack());
    this.messages = [];
    this.setState(prevState => ({
      dropdownOpen: !prevState.dropdownOpen,
      hasNewMessage: false
    }));
  };

  renderOngoingProcesses = () => {
    if (this.state.processes.size === 0) {
      return (
        <DropdownItem>
          <i className={"fa fa-check"} style={{color: "green"}}/>
          Mevcut ilerleyen isleminiz bulunmamaktadir.
        </DropdownItem>
      );
    } else {
      return (
        Array.from(this.state.processes).map((process, index) => this.renderSingleProcess(process[1], index))
      );
    }
  };

  renderSingleProcess = (process, index) => {
    if (process.status === "COMPLETED") {
      return (
        <DropdownItem key={index}>
          <div>
            <i className={"fa fa-check"} style={{color: "green"}}/>
            {process.id} tamamlandi.
          </div>
        </DropdownItem>

      );
    }
    else {
      return (
        <DropdownItem key={index}>
          <Progress value={process.count * 10}>{"%" + process.count * 10}</Progress>
        </DropdownItem>
      );
    }
  };

  render() {

    return (
      <li className="nav-item">
        <Dropdown isOpen={this.state.dropdownOpen} toggle={this.toggle}>
          <DropdownToggle caret style={{
            color: window.innerWidth < 992 ? "white" : '#525252',
            background: 'transparent',
            border: 'none',
            outline: 'none',
            width: "40px",
            padding: "11px",
            margin: "3px"
          }}>
            <i className={"fa fa-hourglass-half" + (this.state.hasNewMessage ? " fa-spin" : "")}/>
          </DropdownToggle>
          <DropdownMenu right>
            {this.renderOngoingProcesses()}
          </DropdownMenu>
        </Dropdown>
      </li>

    );
  }
}


export default OngoingProcesses;



