    renderApprove() {
        if (this.props.vaccinationCard && this.props.vaccinationCard.length > 0) {
            this.setState({
                closeButton: true,
                alert: (
                    <SweetAlert showCancel success
                                confirmBtnText="Evet"
                                cancelBtnText="Hayr"
                                confirmBtnBsStyle="success"
                                cancelBtnBsStyle="default"
                                title=""
                                disabled={this.state.disabledApproveBtn}
                                onConfirm={() => this.approveVaccinationCard()}
                                onCancel={() => this.setState({alert: null})}
                    >
                        Ekte bilgileri yer alan a kart/PCR testi ilgili alan iin onaylanarak
                        kaydedilecek,
                        Dokman onaylanacak ve onaynz Tbitak zaman damgas ile
                        mhrlenerek saklanacaktr.
                    </SweetAlert>
                )
            });
        } else {
            this.setState({
                closeButton: true,
                alert: (
                    <SweetAlert showCancel success
                                confirmBtnText="Evet"
                                cancelBtnText="Hayr"
                                confirmBtnBsStyle="success"
                                cancelBtnBsStyle="default"
                                title=""
                                disabled={this.state.disabledApproveBtn}
                                onConfirm={() => this.state.approveAnswer && !this.state.isReferencedUser ? this.confirmApprove() : this.approve()}
                                onCancel={() => this.setState({alert: null})}
                    >
                        {this.props.documentType === 'KVKK' ? 'Devam ettiiniz takdirde, dokman Tbitak zaman damgas ile mhrlenerek saklanacaktr.' : 'Dokman onaylanacak ve onaynz Tbitak zaman damgas ile mhrlenerek saklanacaktr.'}
                    </SweetAlert>
                )
            });
        }
    }
