import React, {useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';
import {useForm} from 'react-hook-form';
import {Button} from '@material-ui/core';
import FileBase64 from "./FileBase64";
import SurveyAttachment from "./SurveyAttachment";
import {requestK} from "../../../helper/APIUtils";
import {showAxiosError} from "../../../helper/helper";
import Swal from "sweetalert2";
import ReactLoading from "react-loading";
import SweetAlert from "react-bootstrap-sweetalert";

const useStyles = makeStyles((theme) => ({
    modal: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    },
    modalWidth: {
        width:'1000px',
        backgroundColor: theme.palette.background.paper,
        border: '2px solid #000',
        boxShadow: theme.shadows[5],
        // padding: theme.spacing(2, 4, 3),
    },
}));

const SurveyAttachmentModal = ({openModal, setModal, selectedEmployee, surveyNamesList, getPersonelFilesById}) => {

    const classes = useStyles();
    const [open, setOpen] = useState(openModal);
    const [alert, setAlert] = useState(false);
    const [employee, setEmployee] = useState(selectedEmployee);
    const [files, setFiles] = useState([]);
    const [surveyNames, setSurveyNames] = useState(surveyNamesList);
    const [saving, setSaving] = useState(false);
    const [selectedRecordError, setSelectedRecordError] = useState(false);
    const {formState: {errors}, handleSubmit} = useForm();

    const updateSurveyNamesList = (param) => {
        setSurveyNames(param);
    }

    const handleOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setSelectedRecordError(false);
        setModal();
        setOpen(false);
        setFiles([]);
    };

    const saveSurvey = (addToSurvey) => {
        setSelectedRecordError(false);
        let selectedSurveys = [];
        for (const surveyName of surveyNames) {
            for (const attachment of surveyName.attachmentList) {
                if (surveyName.surveyId !== -1 && attachment.isAttached) {
                    selectedSurveys.push(surveyName);
                    break;
                }
            }
        }
        if (selectedSurveys.length === 0) {
            setSelectedRecordError(true);
            return;
        }
        let data = {};
        data.personalInfoAttachments = selectedSurveys;
        data.addToSurvey = addToSurvey;

        let params = {
            url: `/survey/savePersonalInfoNotAttachedFiles`,
            method: "post",
            data: data
        };
        setSaving(true);
        requestK(params)
            .then(function (response) {
                getPersonelFilesById();
                handleClose();
                setSaving(false);
                Swal({
                    title: 'Baarl ! ',
                    text: ("Kaydetme ilemi baaryla gerekletirildi."),
                    type: 'success',
                    confirmButtonText: 'Tamam'
                }).then(() => {

                });

            }).catch(function (error) {
            handleClose();
            setSaving(false);
            if (error.response.data.includes("yklenirken hata meydana gelmitir!")) {
                Swal({
                    title: 'Hata!',
                    text: error.response.data,
                    type: 'error',
                    confirmButtonText: 'Tamam'
                });
            }else{
                showAxiosError(error);
            }

        });
    }

    const onSubmit = () => {
        setAlert(true);
    }

    const getFiles = async (files) => {
        surveyNamesList.forEach(surveyNames => surveyNames.attachmentList.forEach(attachment => {
            if (attachment.isAttached === true) {
                attachment.isAttached = false;
                attachment.base64Value = null
            }
        }));
        setSurveyNames(surveyNamesList);
        await setFiles(files);
    }

    return (
        <div>
            <SweetAlert
                warning
                showCancel
                closeOnClickOutside={false}
                focusConfirmBtn={true}
                show={alert}
                confirmBtnBsStyle="info"
                confirmBtnText="EKLE"
                cancelBtnBsStyle={"warning"}
                cancelBtnText={"DETR"}
                title="Dikkat!"
                onConfirm={() => {
                    saveSurvey(true);
                    setAlert(false);
                }}
                onCancel={() => {
                    saveSurvey(false);
                    setAlert(false);
                }}
            >
                Yklediiniz dokman/dokmanlar nceki dokmann stne eklemek iin EKLE,
                nceki dokmann/dokmanlarn yerine kaydetmek iin DETR butonuna tklaynz !!
            </SweetAlert>
            <Modal
                aria-labelledby="transition-modal-title"
                aria-describedby="transition-modal-description"
                className={classes.modal}
                open={open}
                onClose={handleClose}
                closeAfterTransition
                BackdropComponent={Backdrop}
                BackdropProps={{
                    timeout: 500,
                }}
            >
                <Fade in={open}>
                    <div
                        className=  {classes.modalWidth}
                        style={{overflow:"auto", maxHeight:"80%"}}>
                        <div className='d-flex justify-content-start px-3' style={{background: '#e9ecef'}}>
                            <h3>{employee.name + " " + employee.surname.toUpperCase()}</h3></div>
                        <div className='p-3 ' >
                            <form>
                                <div className='mb-4 mt-2'>
                                    <FileBase64 multiple={true} onDone={getFiles}>
                                        Dosya Se
                                    </FileBase64>
                                    {selectedRecordError && <label> Ltfen Dosya Seiniz!</label>}

                                </div>
                                {(files.length > 0) &&
                                    files.map((file, index) => (
                                        <SurveyAttachment file={file} surveyNamesList={surveyNames}
                                                          updateSurveyNamesList={updateSurveyNamesList}/>
                                    ))
                                }
                                {saving && <ReactLoading type="spinningBubbles" style={{
                                    width: "60px",
                                    height: "60px",
                                    margin: "20px auto 0 auto",
                                    fill: "#888",
                                    left: '50%',
                                    top: '50%',
                                    color: 'black'
                                }}/>}
                            </form>
                            {<div className='d-flex justify-content-end mt-5'>
                                <div>
                                    <Button
                                        variant="contained"
                                        className='mr-2'
                                        disabled={saving}
                                        onClick={handleClose}
                                        type="submit">Vazge</Button>
                                    <Button
                                        variant="contained"
                                        color="secondary"
                                        type="submit"
                                        disabled={saving}
                                        onClick={() => handleSubmit(onSubmit)()}>Kaydet</Button>
                                </div>
                            </div>
                            }
                        </div>
                    </div>
                </Fade>
            </Modal>
        </div>
    );
}

export default SurveyAttachmentModal;