import React, {Component} from 'react';
import {validateComponent, validateField} from "../../../common/validation";
import Table from "../../../components/Table/Table";
import {deepCopyObject, showAxiosError} from "../../../helper/helper";
import moment from "moment/moment";
import TextOverFlowTooltip from "../../../components/TextOverFlowTooltip";
import {requestK} from "../../../helper/APIUtils";


class CustomerSmsPurchaseReport extends Component {
    constructor(props) {
        super(props);
        this.notifyRef = React.createRef();
        this.state = {
            error: {},
            purchaseList: [],
            page: 0,
            size: 10,
            content: [],
            alert: null,
            loading: false,
            showModal: false,
            displayDetailDialog: false,
            valid: {},
            manual: false,
            list: [],
            totalIdList: [],
            total: 0,
            searching: false,
            searchQuery: undefined,
        };

        this.validateMessage = this.validateMessage.bind(this);
        this.validateComponent = this.validateComponent.bind(this);
        this.isChanged = this.isChanged.bind(this);
        this.arrangeData = this.arrangeData.bind(this);
        this.fetchDataPurchase = this.fetchDataPurchase.bind(this);
        this.getTrStyles = this.getTrStyles.bind(this);
    }

    componentDidMount() {
        let purchaseList = this.props.purchaseList;
        this.setState({purchaseList});
    }

    componentWillReceiveProps(nextProps) {
        this.setState({purchaseList: nextProps.purchaseList});
    }


    validateField(e) {
        let id = e.target.id;
        let rule = e.target.dataset.vdata;
        let boundary = e.target.dataset.vlength;
        let value = e.target.value;
        let result = validateField(rule, boundary, value);
        let error = this.state.error;
        error = Object.assign(error, {[id]: result});
        this.setState({error});
    }

    validateClass(id) {
        let error = this.state.error;
        return (error[id] === undefined ? "" : (error[id].valid ? " has-success" : " has-danger"));
    }

    validateMessage(id) {
        let error = this.state.error;
        return (error[id] === undefined ? '' : (error[id].message));
    }

    validateComponent() {
        let result = validateComponent(this);
        this.setState({error: result.error});
        return result
    }


    isChanged(initState, input) {
        if (JSON.stringify(initState) === JSON.stringify(input)) {
            return false
        } else {
            return true
        }
    }

    arrangeData(page, size) {
        this.setState({
            page: page,
            size: size,
            loading: false
        });
    }

    getTrStyles(item) {
        return (item.statusType && item.statusType !== 'ACTIVE') ? {
            backgroundColor: 'lightgray',
            webkitTextFillColor: 'darkgray'
        } : {};
    }

    fetchDataPurchase(accessor, direction, page, size) {
        let self = this;
        if(!this.props.customer) return;
        let sortStr = accessor !== undefined ? ('&sort=' + accessor + ',' + direction) : '&sort=id,desc';
        let searchQuery = !self.state.searchQuery ? '' : '&' + self.state.searchQuery;
        let params = {
            url: '/credit/customerPurchaseAdmin?page=' + page + '&size=' + size + '&customerId=' + this.props.customer + searchQuery + sortStr,
            method: "get",
        };
        self.setState({loading: true, searching: true, manual: self.manual === true ? false : self.manual});
        requestK(params).then(function (response) {

            console.log(response)
            self.setState({
                page: page,
                size: size,
                total: response.data.totalElements,
                loading: false,
                searching: false,
                content: response.data.content,
            });
        }).catch(function (error) {
            self.setState({loading: false, searching: false});
            showAxiosError(error);
        });
    }

    selectStyle(item) {
        let error = deepCopyObject(this.state.error);
        if (error[item] === undefined || error[item].valid) {
            return null;
        } else {
            return {backgroundColor: '#f8f8f8', borderColor: '#ffcfcf'}
        }
    }

    render() {
        const columns = [
            {
                Header: "Oluturulan Tarih",
                accessor: "createdAt",
                sortable: false,
                Cell: row => (
                    <div style={{margin: 0}}>
                        {moment(row.createdAt).isValid() ? moment(row.createdAt).format("DD-MM-YYYY HH:mm:ss") : ''}
                    </div>
                )
            },
            {
                Header: "Satn Alnan Tarih",
                accessor: "purchasedAt",
                sortable: false,
                Cell: row => (
                    <div style={{margin: 0}}>
                        {moment(row.purchasedAt).isValid() ? moment(row.purchasedAt).format("DD-MM-YYYY HH:mm:ss") : ''}
                    </div>
                )
            },
            {
                Header: "Kredi",
                accessor: 'credit',
                sortable: false
            },
            {
                Header: "Kredi Tr",
                accessor: 'creditType',
                sortable: false
            },
            {
                Header: "Fiyat",
                accessor: "price",
                sortable: false,
            },
            {
                Header: "Satn Alan",
                accessor: "firstName",
                sortable: false,
                Cell: row => (<div style={{margin: 0}}><TextOverFlowTooltip text={row.firstName + " " + row.lastName} maxLength={30}/></div>)
            },
            {
                Header: "Aklama",
                accessor: 'explanation',
                sortable: false
            },
            {
                Header: "Referans Kodu",
                accessor: "referenceCode",
                sortable: false,
            },
            {
                Header: "Durumu",
                accessor: "status",
                sortable: false,
            },
        ];
        return (
            <div>
                <div className="card-body">
                    <Table columns={columns}
                           content={this.state.content}
                           loading={this.state.loading}
                           loadingText="Kaytlar getiriliyor..."
                           total={this.state.total}
                           page={this.state.page}
                           size={this.state.size}
                           manual={this.state.manual}
                           options={["5", "10", "20", "50", "100"]}
                           getTrStyles={this.getTrStyles}
                           fetchData={this.fetchDataPurchase}/>
                </div>
            </div>
        )
    }
}

export default CustomerSmsPurchaseReport;