import React, {Component} from 'react';
import {Button, Col, Form, FormGroup, Input, Label, Modal, ModalBody, ModalFooter, ModalHeader} from 'reactstrap';
import Table from '../../../components/Table/Table';
import StickButton from "../../../components/StickButton";
import MiniButton from "../../../components/MiniButton";
import SweetAlertExt from "../../../components/SweetAlertExt";
import validator from "validator";
import {checkGrant, checkGrantForFetchResponse, checkGrantToDisplay, showAxiosError} from "../../../helper/helper";
import {requestK} from "../../../helper/APIUtils";
import Alert from "react-s-alert";
import moment from "moment/moment";
import Select from "../../../local_node_modules/react-select";
import SweetAlert from "react-bootstrap-sweetalert";
import {EMPLOYEES_CUSTOMS} from "../../../common/authorities/GrantDef";

class EmployeeCustomFieldsPage extends Component {

    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            manual: false,
            content: [],
            total: 0,
            searching: false,
            searchQuery: undefined,
            page: 0,
            size: 5,
            searchKey: "",
            disableNewBtn: false,
            titleLimit: 40
        };

        this.editValidateDef = {
            title: v => !validator.isEmpty(v),
            value: v => !validator.isEmpty(v)
        };
    }

    componentDidMount() {
        let self = this;
        let params = {
            url: `/employeecustom/readTitleLimit`,
            method: "get"
        };
        self.setState({disableNewBtn: true});
        requestK(params)
            .then(function (response) {
                self.setState({titleLimit: response.data, disableNewBtn: false});
            })
            .catch(function () {
                self.setState({disableNewBtn: false});
            });
    }

    handleSearch = (searchQuery) => {
        this.setState({searchQuery, searching: true},
            () => this.fetchData(this.state.accessor, this.state.direction, 0, this.state.size));
    };

    searchWithKey = (e) => {
        this.setState({searchKey: e.target.value, manual: true});
    };

    fetchData = (accessor, direction, page, size) => {
        let self = this;
        let sortStr = accessor !== undefined ? ('&sort=' + accessor + ',' + direction) : '';
        let searchQuery = !self.state.searchQuery ? '' : '&' + self.state.searchQuery;
        let params = {
            url: '/employeecustom/readTitlePageAll?page=' + page + '&size=' + size + searchQuery + sortStr,
            method: "get",
        };
        self.setState({loading: true, searching: true, manual: self.manual === true ? false : self.manual});
        self.props.reload(true);
        requestK(params)
            .then(function (response) {
                checkGrantForFetchResponse(self, response.data);
                self.setState({
                    page: page,
                    size: size,
                    total: response.data.totalElements,
                    content: response.data.content,
                    loading: false,
                    searching: false
                });
                self.props.reload(false);
            }).catch(function (error) {
            self.setState({loading: false, searching: false});
            self.props.reload(false);
            showAxiosError(error);
        });
    };

    createClicked = () => {
        this.setState({
            selected: {title: "", activeForFilter: 0},
            valid: {},
            displayCreateUpdateDialog: true
        });
    };

    editClicked = (row) => {
        let clone = Object.assign({}, row);
        delete clone["_$visited"];
        this.setState({
            selected: clone,
            valid: {},
            displayCreateUpdateDialog: true
        });
    };

    deleteClicked = (row) => {
        let self = this;
        let params = {
            url: `/employeecustom/deleteEmployeeTitle`,
            method: "post",
            data: row.id
        };
        self.setState({disabledDeleteBtn: true, alert: null});
        requestK(params)
            .then(function (response) {
                let result = response.data;
                self.setState({
                    selected: null,
                    alert: null
                });
                if (result) {
                    self.setState({
                        disabledDeleteBtn: false,
                        manual: true
                    });
                    Alert.success("Balk silindi.", {
                        position: 'top-right',
                        effect: 'stackslide',
                        timeout: 5000
                    });
                } else {
                    self.setState({
                        alert: (
                            <SweetAlert showCancel warning
                                        confirmBtnText="Tmn Sil"
                                        cancelBtnText="ptal"
                                        confirmBtnBsStyle="error"
                                        cancelBtnBsStyle="default"
                                        title="Dikkat"
                                        onConfirm={() => self.deleteForce(row.id)}
                                        onCancel={() => self.setState({alert: null})}>
                                Baz alanlarda bu balk iin veri bulunmaktadr. Bu sebeple balk silinemedi.
                                Tmn Sil ile tm
                                alanlarnzn verilerini silerek silinme ilemini tamamlayabilirsiniz.
                            </SweetAlert>
                        )
                    });
                }
            }).catch(function (error) {
            showAxiosError(error);
            self.setState({disabledDeleteBtn: false});
        });
    };

    saveClicked = () => {
        if (checkGrant(this, EMPLOYEES_CUSTOMS)) {
            let customTitle = Object.assign({}, this.state.selected);
            let self = this;
            let params = {
                url: `/employeecustom/saveEmployeeTitle`,
                method: "post",
                data: customTitle
            };
            self.setState({disabledSaveBtn: true});
            requestK(params)
                .then(function (response) {
                    let saved = response.data;
                    self.setState({
                        selected: null,
                        displayCreateUpdateDialog: false,
                        manual: true,
                        disabledSaveBtn: false
                    });
                    if (saved) {
                        Alert.success("Kaydedildi.", {
                            position: 'top-right',
                            effect: 'stackslide',
                            timeout: 5000
                        });
                    } else {
                        Alert.error("Bu balktan daha nce kaydedilmi.", {
                            position: 'top-right',
                            effect: 'stackslide',
                            timeout: 5000
                        });
                    }
                }).catch(function (error) {
                showAxiosError(error);
                self.setState({disabledSaveBtn: false});
            });
        }
    };

    editHandleInputChange = (event) => {
        const target = event.target;
        let value = target.type === 'checkbox' ? target.checked : target.value;
        let name = target.name;

        if (!(name === "title" && value.toString().startsWith(" "))) {
            let valid = this.editValidateDef[name] && this.editValidateDef[name](value);
            this.setState((prevState, props) => {
                prevState.selected[name] = value;
                prevState.valid[name] = valid;
                return {
                    selected: prevState.selected,
                    valid: prevState.valid
                };
            });
        }
    };

    editClassName = (name) => {
        let valid = this.state.valid[name];
        if (typeof valid !== "boolean")
            return undefined;
        else {
            return {
                valid: valid,
                invalid: !valid
            };
        }
    };

    renderCreateUpdateModal() {
        return <Modal isOpen={this.state.displayCreateUpdateDialog} size="md"
                      toggle={() => this.setState({displayCreateUpdateDialog: false})}>
            <ModalHeader className="justify-content-center"
                         toggle={() => this.setState({displayCreateUpdateDialog: false})}>
                alan Ek Bilgisi in Yeni Balk Ekle
            </ModalHeader>
            <ModalBody>
                {this.state.selected && <div>
                    <Form>
                        <FormGroup row>
                            <Label sm={4} for="name">Yeni Bilgi Bal: </Label>
                            <Col sm={8}>
                                <Input type="text" name="title" value={this.state.selected.title}
                                       onChange={this.editHandleInputChange} {...this.editClassName("title")}/>
                            </Col>
                        </FormGroup>
                        <FormGroup row>
                            <Label sm={4} for="name">Veri Tipi: </Label>
                            <Col sm={8}>
                                <Select
                                    placeholder="Seiniz..."
                                    value={this.state.selected.type}
                                    clearable={false}
                                    noResultsText={"Veri yok"}
                                    options={[
                                        {value: "STRING", label: 'METN VE SAYI'},
                                        {value: "INTEGER", label: 'SAYI'},
                                        {value: "DOUBLE", label: 'ONDALIKLI SAYI'},
                                        {value: "DATETIME", label: 'TARIH SAAT'},
                                        {value: "BOOLEAN", label: 'MANTIKSAL'},
                                    ]}
                                    onChange={(e) => {
                                        this.editHandleInputChange({
                                            target: {
                                                name: 'type',
                                                value: e.value
                                            }, e
                                        });
                                    }}/>
                            </Col>
                        </FormGroup>
                        <FormGroup row>
                            <Label sm={4} for="name">Arama Yapabilme: </Label>
                            <Col sm={8}>
                                <Select
                                    placeholder="Seiniz..."
                                    value={this.state.selected.activeForFilter ? this.state.selected.activeForFilter : false}
                                    clearable={false}
                                    noResultsText={"Veri yok"}
                                    options={[
                                        {value: false, label: 'KAPALI'},
                                        {value: true, label: 'AIK'}
                                    ]}
                                    onChange={(e) => {
                                        this.editHandleInputChange({
                                            target: {
                                                name: 'activeForFilter',
                                                value: (e && e.value) ? e.value : false
                                            }, e
                                        });
                                    }}/>
                            </Col>
                        </FormGroup>
                    </Form>
                </div>}
            </ModalBody>
            <ModalFooter>
                <Button title="ptal" style={{paddingLeft: '16px', width: '100px'}} color="secondary"
                        onClick={() => this.setState({displayCreateUpdateDialog: false})}>
                    ptal
                </Button>
                <Button title="Kaydet" style={{paddingLeft: '16px', width: '100px'}} color="primary"
                        disabled={this.state.disabledSaveBtn || !(this.state.selected && this.state.selected.title && this.state.selected.type)}
                        onClick={this.saveClicked}>
                    Kaydet
                </Button>
            </ModalFooter>
        </Modal>
    }

    render() {
        let columns = [
            {
                Header: "Yeni Eklenen Balk Ad",
                accessor: "title",
                sortable: false,
            },
            {
                Header: "Veri Tipi",
                accessor: "type",
                sortable: false,
                Cell: row => (
                    <div style={{margin: 0}}>
                        {row.type === "STRING" && <span className="text-info">METN VE SAYI</span>}
                        {row.type === "INTEGER" && <span className="text-info">SAYI</span>}
                        {row.type === "DOUBLE" && <span className="text-info">ONDALIKLI SAYI</span>}
                        {row.type === "DATETIME" && <span className="text-info">TARH SAAT</span>}
                        {row.type === "BOOLEAN" && <span className="text-info">MANTIKSAL</span>}
                        {!row.type && <span className="text-info">METN VE SAYI</span>}
                    </div>
                )
            },
            {
                Header: "Arama Yapabilme",
                accessor: "activeForFilter",
                sortable: false,
                Cell: row => (
                    <div style={{margin: 0}}>
                        {row.activeForFilter && <span className="text-success">AIK</span>}
                        {!row.activeForFilter && <span>KAPALI</span>}
                    </div>
                )
            },
            {
                Header: "Oluturulma Zaman",
                accessor: "createdAt",
                sortable: false,
                Cell: row => (
                    <div style={{margin: 0}}>
                        {(row.createdAt && moment(row.createdAt).isValid()) ? moment(row.createdAt).format("DD-MM-YYYY HH:mm:ss") : ''}
                    </div>
                )
            },
            {
                Header: "lemler",
                accessor: "",
                sortable: false,
                width: "10%",
                Cell: row => (
                    <div style={{textAlign: 'left'}}>
                        {checkGrantToDisplay(this, EMPLOYEES_CUSTOMS) &&
                            <MiniButton title="Dzenle" faIcon="fa-edit" onClick={() => this.editClicked(row)}
                                        color="success"/>}
                        {checkGrantToDisplay(this, EMPLOYEES_CUSTOMS) &&
                            <MiniButton title="Sil" faIcon="fa-trash" color="danger"
                                        disabled={this.state.disabledDeleteBtn}
                                        onClick={() => {
                                            this.setState({
                                                alert: (
                                                    <SweetAlertExt showCancel warning
                                                                   confirmBtnText="Evet"
                                                                   cancelBtnText="Hayr"
                                                                   confirmBtnBsStyle="danger"
                                                                   cancelBtnBsStyle="default"
                                                                   title="Emin misiniz?"
                                                                   onConfirm={() => this.deleteClicked(row)}
                                                                   onCancel={() => this.setState({alert: null})}
                                                                   disabled={this.state.disabledDeleteBtn}
                                                    >
                                                        {row.title + " bal silinecek."}
                                                    </SweetAlertExt>
                                                )
                                            });
                                        }}/>}
                    </div>
                )
            },
        ];

        return (
            <div className="row baseBackground">
                {this.renderCreateUpdateModal()}
                <div className="col-sm-12" style={{marginTop: '10px'}}>
                    {!!this.props.onModal &&
                        <div>
                            <StickButton position="leftSide" className="zocial Yeni" style={{fontSize: 'x-small'}}
                                         label="Yeni Balk Ekle" title="alan Bilgilerine Yeni Balk Ekle"
                                         disabled={this.state.disableNewBtn}
                                         onClick={() => {
                                             if (this.state.total === 0 || this.state.total < this.state.titleLimit) {
                                                 this.createClicked();
                                             } else {
                                                 this.setState({
                                                     alert: (
                                                         <SweetAlert warning
                                                                     confirmBtnText="Tamam"
                                                                     confirmBtnBsStyle="warning"
                                                                     title="Dikkat"
                                                                     onConfirm={() => this.setState({alert: null})}>
                                                             Yeni balk ekleme limitine ulalmtr.
                                                         </SweetAlert>
                                                     )
                                                 });
                                             }
                                         }}/>
                        </div>
                    }
                    <div className="card-header ">
                        <h4 className="card-title">alan Ek Bilgileri zelletirme</h4>
                        <Label>alan listesine yeni bir kolon bal ekleyebilirsiniz. "Arama Yapabilme" alanndan,
                            eklediiniz
                            baln gelimi aramaya dahil edilmesini salayabilirsiniz.</Label>
                    </div>
                    <div className="card-body">
                        <Table columns={columns}
                               content={this.state.content}
                               loading={this.state.loading}
                               total={this.state.total}
                               page={this.state.page}
                               manual={this.state.manual}
                               size={this.state.size}
                               options={["5", "10", "20", "50", "100"]}
                               fetchData={this.fetchData}/>
                    </div>
                </div>
                {this.state.alert}
            </div>
        );
    }

    deleteForce = (id) => {
        let self = this;
        self.setState({alert: null});
        let params = {
            url: `/employeecustom/deleteEmployeeTitleForce`,
            method: "post",
            data: id
        };
        requestK(params)
            .then(function (response) {
                let result = response.data;
                self.setState({
                    manual: true,
                    disabledDeleteBtn: false
                });
                if (result) {
                    Alert.success("Balk silindi.", {
                        position: 'top-right',
                        effect: 'stackslide',
                        timeout: 5000
                    });
                } else {
                    Alert.error("Balk silinemedi.", {
                        position: 'top-right',
                        effect: 'stackslide',
                        timeout: 5000
                    });
                }
            }).catch(function (error) {
            showAxiosError(error);
            self.setState({disabledDeleteBtn: false});
        });
    };
}

export default EmployeeCustomFieldsPage;