import React, {Component} from 'react';
import {Button, Col, Row} from 'reactstrap';
import {requestK} from "../../../../helper/APIUtils";
import {
    checkGrant,
    checkGrantForFetchResponse,
    checkGrantToDisplay,
    customFieldsBooleanControl,
    showAxiosError
} from "../../../../helper/helper";
import moment from "moment";
import {employmentContractData, genderData, statusReasonData, statusTypeFilterData} from "../../common/PeykHelper";
import {Modal} from "react-bootstrap";
import TextOverFlowTooltip from "../../../../components/TextOverFlowTooltip";
import NotifyKS from "../../../../components/NotifyKS";
import Select from "../../../../local_node_modules/react-select";
import Avatar from 'react-avatar-edit';
import Alert from 'react-s-alert';
import {EMPLOYEES, SEE_SALARY_DATA, VIEW_MAIL, VIEW_TEL, VIEWS_TCKN} from "../../../../common/authorities/GrantDef";
import VaccinationCardModal from "../../approval/helper/VaccinationCardModal";

class EmployeeDetails extends Component {

    constructor(props) {
        super(props);

        this.state = {
            employee: undefined,
            bordroCount: 0,
            formCount: 0,
            getBordroCount: true,
            getFormCount: true,
            showUpload: false,
            initPreview: "data:image/png;base64,",
            imageSize: 1,
            currentImage: null,
            capitals: null,
            isActualImage: false,
            mode: 'display',
            notify: null,
            customFields: {},
            preview: null,
            defineImage: true,
            isVaccinationcard: false,
            showVaccinatinCardModal: false,
            vaccinationCardInfo: {},
            windowSize: window.matchMedia('(max-width: 600px)'),
            downloadPdf: false
            // setProfileImage: false,
        };

        this.init = this.init.bind(this);
        this.getType = this.getType.bind(this);
        this.getValue = this.getValue.bind(this);
        this.getFormCount = this.getFormCount.bind(this);
        this.getBordroCount = this.getBordroCount.bind(this);
        this.submitAvatar = this.submitAvatar.bind(this);
        this.decideAvatarDiv = this.decideAvatarDiv.bind(this);
        this.closeDetailDialog = this.closeDetailDialog.bind(this);
    }

    static getTitle(label) {
        if (!label) {
            return "";
        }
        return label + "lar";
    }

    componentWillMount() {
        if (!!this.props.selectedId) {
            this.readCustomFields();
        }
    }

    componentDidMount() {
        this.init();
        this.fetchEmployee();
        this.setState({preview: this.state.initPreview});
        this.decideAvatarDiv();
        this.getSurveyList();
        window.addEventListener('resize', this.decideAvatarDiv);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.decideAvatarDiv);
    }

    readCustomFields = () => {
        let self = this;
        let params = {
            url: `/employeecustom/readCustomFields`,
            method: "post",
            data: self.props.selectedId
        };
        requestK(params)
            .then(function (response) {
                self.setState({customFields: response.data});
            })
            .catch(function (error) {
            });
    };

    fetchEmployee() {
        let id = this.props.selectedId;
        let self = this;
        let params = {
            url: '/employee/getEmployeeById',
            method: "post",
            data: id
        };
        self.setState({loading: true});
        requestK(params)
            .then(function (response) {
                checkGrantForFetchResponse(self, response.data);
                let image = response.data.photo;
                self.setState({
                    employee: EmployeeDetails.getTextOfDepartment(response.data),
                    currentImage: image,
                    actualImage: image,
                    loading: false
                })
            })
            .catch(function (error) {
                showAxiosError(error);
                self.setState({loading: false})
            });
    }

    static getTextOfDepartment(data) {
        let employee = data;
        employee.department = data.department ? data.department.name : "";
        employee.expenditureDepartment = data.expenditureDepartment ? data.expenditureDepartment.name : "";
        return employee;
    }

    init() {
        if (this.props.isBordroOwner && this.state.getBordroCount) {
            this.getBordroCount(this.props.selectedId);
            this.setState({getBordroCount: false});
        }
        if (this.props.isFormOwner && this.state.getFormCount) {
            this.getFormCount(this.props.selectedId);
            this.setState({getFormCount: false});
        }
        this.isExistVaccinationcard(this.props.selectedId);
        this.getVaccinationcardInfo(this.props.selectedId);
    }

    getVaccinationcardInfo = (employeeId) => {
        let self = this;
        let params = {
            url: `/employeevaccination/readContent`,
            method: "post",
            data: employeeId,
        };
        requestK(params)
            .then(function (response) {
                self.setState({vaccinationCardInfo: response.data})
            }).catch(function (error) {
            showAxiosError(error);
        });
    }
    isExistVaccinationcard = (employeeId) => {
        if (employeeId) {
            let self = this;
            let params = {
                url: '/employee/isExistVaccinationcard',
                method: "post",
                data: employeeId
            };
            requestK(params)
                .then(function (response) {
                    self.setState({isVaccinationcard: response.data});
                })
                .catch(function (error) {
                    showAxiosError(error);
                });
        }
    }

    getBordroCount(employeeId) {
        if (employeeId) {
            let self = this;
            let params = {
                url: '/bordro/readBordroCountByEmployee',
                method: "post",
                data: employeeId
            };
            requestK(params)
                .then(function (response) {
                    self.setState({bordroCount: response.data});
                })
                .catch(function (error) {
                    showAxiosError(error);
                });
        }
    }

    getFormCount(employeeId) {
        if (employeeId) {
            let self = this;
            let params = {
                url: '/form/readFormCountByEmployee',
                method: "post",
                data: employeeId
            };
            requestK(params)
                .then(function (response) {
                    self.setState({formCount: response.data});
                })
                .catch(function (error) {
                    showAxiosError(error);
                });
        }
    }

    getSurveyList = () => {
        let self = this;
        let params = {
            url: '/survey/getSurveyFormsForEmployeeDetails',
            method: "post",
            data: "0"
        };
        requestK(params)
            .then(function (response) {
                let optionItems = [];
                response.data.map((form) => {
                    optionItems.push({value: form.id, label: form.name, form: form});
                });
                self.setState({options: optionItems});
            }).catch(function (error) {
            showAxiosError(error);
        });
    };

    downloadPersonalCard = (e) => {
        e.preventDefault();
        let self = this;
        let params = {
            url: '/employee/downloadProfileCard',
            responseType: 'arraybuffer',
            method: "post",
            data: this.props.selectedId
        };
        self.setState({downloadPdf: true})
        requestK(params)
            .then(function (response) {
                let link = document.createElement('a');
                link.href = window.URL.createObjectURL(new Blob([response.data], {type: "application/pdf"}));
                let date = new Date();
                link.download = "Personel_Karti" +
                    date.getFullYear() + "-" +
                    date.getMonth() + "-" +
                    date.getDate() + "_" +
                    date.getHours() + "-" +
                    date.getMinutes() + "-" +
                    date.getSeconds() + ".pdf";
                document.body.appendChild(link);
                link.click();
                self.setState({downloadPdf: false})
            })
            .catch(function (error) {
                showAxiosError(error);
                self.setState({loading: false, downloadPdf: false})
            });
    }

    renderDocument(label, countField, path) {
        return (
            <Row style={{margin: '0'}}>
                <Col xs='4' sm='4' md='4' lg='4' xl='4' style={{margin: '0', padding: '0'}}>
                    <label style={{fontWeight: 'bold'}}>{label + " Says: "}</label>
                </Col>

                <Col xs='2' sm='2' md='2' lg='2' xl='2' style={{margin: '0', padding: '0'}}>
                    <label>{this.state[countField] ? this.state[countField] : 0}</label>
                </Col>

                <Col xs='6' sm='6' md='6' lg='6' xl='6' style={{margin: '0', padding: '0'}}>
                    {this.renderDocButton(label, countField, path)}
                </Col>
            </Row>
        )
    }

    renderVaccinationcard = () => {
        return (
            <Row style={{margin: '0'}}>
                <Col xs='6' sm='6' md='4' lg='6' xl='6' style={{margin: '0', padding: '0'}}>
                    <label style={{fontWeight: 'bold'}}>A Kart/PCR testi</label>
                </Col>

                <Col xs='6' sm='6' md='6' lg='6' xl='6' style={{margin: '0', padding: '0'}}>
                    <Button title={"A Kart Grntle"} color="warning"
                            disabled={(!(this.state.isVaccinationcard) || this.state.loading)}
                            style={{
                                width: '150px',
                                height: '36px',
                                marginTop: '-7px',
                                marginLeft: '-5px',
                                padding: '0 5% 0 0'
                            }}
                            onClick={() => {
                                this.setState({showVaccinatinCardModal: true})
                            }}>
            <span
                style={{
                    position: 'relative',
                    left: '-14px',
                    padding: '11px 10px',
                    height: '36px',
                    background: 'rgba(0,0,0,0.15)',
                    borderRadius: '3px 0 0 3px'
                }}>
                <i className="fa fa-eye"/>
            </span>
                        {"Bilgileri Gster"}
                    </Button>
                </Col>
            </Row>
        )
    }

    renderDocButton(label, field, path) {
        let left = '-7px';
        if (label === "Form") {
            left = '-11px'
        }
        return (
            <Button title={"Kiiye Ait Tm " + EmployeeDetails.getTitle(label) + " Grntle"} color="warning"
                    disabled={(!(this.state[field] && this.state[field] > 0) || this.state.loading)}
                    style={{
                        width: '150px',
                        height: '36px',
                        marginTop: '-7px',
                        marginLeft: '-5px',
                        padding: '0 5% 0 0'
                    }}
                    onClick={() => {
                        this.props.propsHistory.push(path)
                    }}>
            <span
                style={{
                    position: 'relative',
                    left: left,
                    padding: '11px 10px',
                    height: '36px',
                    background: 'rgba(0,0,0,0.15)',
                    borderRadius: '3px 0 0 3px'
                }}>
                <i className="fa fa-eye"/>
            </span>
                {EmployeeDetails.getTitle(label) + " Gster"}
            </Button>
        );
    }

    renderSurveyDocument() {
        return (checkGrantToDisplay(this, EMPLOYEES) &&
            <Row style={{margin: '0'}}>
                <Col xs='4' sm='4' md='4' lg='4' xl='4' style={{margin: '0', padding: '0'}}>
                    <label style={{fontWeight: 'bold'}}>{"Talep Formlar: "}</label>
                </Col>

                <Col xs='8' sm='8' md='8' lg='8' xl='8' style={{margin: '0', padding: '0'}}>
                    <Select value={this.state.selectedSurveyForm}
                            clearable={false}
                            noResultsText="Tanmlanm form bulunamad"
                            clearValueText="Temizle"
                            placeholder="Seiniz..."
                            onChange={this.handleSurveyChange}
                            options={this.state.options}/>
                </Col>
            </Row>
        )
    }

    handleSurveyChange = e => {
        this.setState({selectedSurveyForm: e});
        this.props.propsHistory.push({pathname: `/peyk/surveyForms/${e.value}/${this.state.employee.id}`});
    };

    renderSalaryDetail = (label, field) => {
        if (this.hasGrant(SEE_SALARY_DATA)) {
            return (
                <Row className='containerRows'>
                    <Col xs='6' sm='6' md='6' lg='6' xl='6' style={{margin: '0', padding: '0 0 0 2%'}}>
                        <label style={{fontWeight: 'bold'}}>{label + ": "}</label>
                    </Col>

                    <Col xs='6' sm='6' md='6' lg='6' xl='6' style={{margin: '0', padding: '0'}}>
                        {
                            <TextOverFlowTooltip
                                maxLength={20}
                                controlled
                                autohide={false}
                                style={{wordBreak: 'break-word'}}
                                text={this.state.employee && this.getValue(field) && field === "salary" ? this.getValue(field) + " " : "Belirtilmemi..."}
                            />}
                    </Col>
                </Row>
            )
        } else {
            return (
                <Row className='containerRows'>
                    <Col xs='6' sm='6' md='6' lg='6' xl='6' style={{margin: '0', padding: '0 0 0 2%'}}>
                        <label style={{fontWeight: 'bold'}}>{label + ": "}</label>
                    </Col>

                    <Col xs='6' sm='6' md='6' lg='6' xl='6' style={{margin: '0', padding: '0'}}>
                        {
                            <TextOverFlowTooltip
                                maxLength={20}
                                controlled
                                autohide={false}
                                style={{wordBreak: 'break-word'}}
                                text={this.state.employee && this.getValue(field) && field === "salary" ? "********" + " " : "********" + " "}
                            />}
                    </Col>
                </Row>
            )
        }

    }

    renderDetail(label, field) {
        return (
            <Row className='containerRows'>
                <Col xs='6' sm='6' md='6' lg='6' xl='6' style={{margin: '0', padding: '0 0 0 2%'}}>
                    <label style={{fontWeight: 'bold'}}>{label + ": "}</label>
                </Col>

                <Col xs='6' sm='6' md='6' lg='6' xl='6' style={{margin: '0', padding: '0'}}>
                    {typeof this.getValue(field) === "boolean" ?
                        <input type="checkbox" checked={this.getValue(field)}/> :
                        <TextOverFlowTooltip
                            maxLength={20}
                            controlled
                            autohide={false}
                            style={{
                                wordBreak: 'break-word',
                                color:
                                    (this.state.employee && !this.getValue(field)) ?
                                        'darkgray' :
                                        (
                                            field === "statusType" ?
                                                (
                                                    this.getValue(field) === "ACTIVE" ?
                                                        'green' :
                                                        'red'
                                                ) :
                                                ''
                                        )
                            }}

                            text={
                                this.state.employee &&
                                this.getValue(field) ?
                                    (
                                        (typeof this.getValue(field) !== "number" && isNaN(this.getValue(field)) && moment(this.getValue(field)).isValid()) ?
                                            moment(this.getValue(field)).format("DD-MM-YYYY HH:mm:ss") :
                                            (
                                                field === "salary" ?
                                                    this.getValue(field) + " " :
                                                    (
                                                        field === "statusType" ?
                                                            this.getType(this.getValue(field), statusTypeFilterData()) :
                                                            (
                                                                field === "employmentContract" ?
                                                                    this.getType(this.getValue(field), employmentContractData()) === "Tanmlanmam" ? "Belirtilmemi..." : this.getType(this.getValue(field), employmentContractData()) :
                                                                    (
                                                                        field === "reason" ?
                                                                            this.getType(this.getValue(field), statusReasonData()) :
                                                                            (
                                                                                field === "gender" ?
                                                                                    this.getType(this.getValue(field), genderData()) :
                                                                                    this.getValue(field)
                                                                            )
                                                                    )
                                                            )
                                                    )
                                            )
                                    ) :
                                    "Belirtilmemi..."
                            }
                        />}
                </Col>
            </Row>
        )
    }

    renderCustomDetail(label, value, customTitle) {
        return (
            <Row className='containerRows'>
                <Col xs='6' sm='6' md='6' lg='6' xl='6' style={{margin: '0', padding: '0 0 0 2%'}}>
                    <label style={{fontWeight: 'bold'}}>{label + ": "}</label>
                </Col>

                <Col xs='6' sm='6' md='6' lg='6' xl='6' style={{margin: '0', padding: '0'}}>
                    {customTitle.type === "BOOLEAN" ?
                        <input type="checkbox" checked={customFieldsBooleanControl(value)}/> :
                        <TextOverFlowTooltip
                            maxLength={25}
                            controlled
                            autohide={false}
                            style={{
                                wordBreak: 'break-word',
                                color: (this.state.employee && !value) ? 'darkgray' : ''
                            }}
                            text={
                                this.state.customFields && this.props.titleList && !!this.props.titleList.length &&
                                value ?
                                    // ((typeof value !== "number" && moment(value).isValid()) ? moment(value).format("DD-MM-YYYY") :
                                    value :
                                    // ) :
                                    "Belirtilmemi..."
                            }/>
                    }
                </Col>
            </Row>
        )
    }

    getValue(field) {
        if (field === "manager") {
            return this.state.employee.manager ? this.state.employee.manager.label : "";
        } else if (field === "secondManager") {
            return this.state.employee.secondManager ? this.state.employee.secondManager.label : "";
        } else if (field === "thirdManager") {
            return this.state.employee.thirdManager ? this.state.employee.thirdManager.label : "";
        } else if (field === "tckn") {
            let tcknValue = this.state.employee[field];
            if (tcknValue.includes("*")) {
                let str;
                let str1 = this.state.employee[field].substring(0, 3);
                let str2 = this.state.employee[field].substring(10, 11);
                str = str1 + '-------' + str2;
                return str;
            } else {
                return this.state.employee[field];
            }
        } else if (field === "telephone") {
            let phoneValue = this.state.employee[field];
            if (phoneValue.includes("*")) {
                let str;
                let str1 = this.state.employee[field].substring(0, 3);
                let str2 = this.state.employee[field].substring(9, 10);
                str = str1 + '-------' + str2;
                return str;
            } else {
                return this.state.employee[field];
            }
        } else if (field === "salary") {
            if (this.state.employee[field] === null || this.state.employee[field] === undefined || this.state.employee[field] === "") {
                return this.state.employee[field];
            } else {
                let salaryValue = (this.state.employee[field]).toLocaleString(['ban', 'id'], {
                    minimumFractionDigits: 1,
                    maximumFractionDigits: 2
                });
                return salaryValue;
            }
        }
        return this.state.employee[field];
    }

    getType(input, list) {
        let ret = "";
        let tempList = list;
        tempList &&
        tempList.length > 0 &&
        tempList.map(e => {
            if (e.value === input) {
                ret = e.label;
            }
        });
        return ret;
    }

    resizeImage(e) {
        this.setState({
            imageSize: e.target.value
        })
    }

    defineProfileImage() {

        this.setState({loading: true});

        let gender = this.state.employee.gender;

        if (!gender || gender === 'UNDECLARED') {

            let nameCap = this.state.employee.name.charAt(0).toUpperCase();
            let surnameCap = this.state.employee.surname.charAt(0).toUpperCase();

            let capitals = nameCap + ' ' + surnameCap;

            this.setState({
                capitals,
                isActualImage: false,
                currentImage: process.env.REACT_APP_CONTEXT_NAME + "/image/" + "unknown-avatar.png",
                loading: false
            });

            return null;
        }

        if (gender === 'MALE') {
            this.setState({
                currentImage: process.env.REACT_APP_CONTEXT_NAME + "/image/" + "unknown-avatar-m.png",
                isActualImage: false,
                loading: false
            });

            return null;
        }

        if (gender === 'FEMALE') {
            this.setState({
                currentImage: process.env.REACT_APP_CONTEXT_NAME + "/image/" + "unknown-avatar-f.png",
                isActualImage: false,
                loading: false
            });

            return null;
        }
    }

    submitAvatar() {
        let self = this;

        let photo = self.stringToByteArray();
        let payload = {};
        payload.id = self.state.employee.id;
        payload.photo = photo;
        let croppedImage = self.state.preview;
        self.setState({loading: true});
        let params = {
            url: '/employee/saveAvatarPic',
            method: "post",
            data: payload
        };
        requestK(params)
            .then(() => {
                self.setState({
                    loading: false,
                    currentImage: croppedImage,
                    actualImage: croppedImage,
                    // setProfileImage: false,
                    imageSize: 1,
                    mode: 'display',
                });
                Alert.success('Resim baaryla eklendi', {
                    position: 'top-right',
                    effect: 'stackslide',
                    timeout: 5000
                });
                self.setState({setProfileImage: false})
            })
            .catch(function (error) {
                showAxiosError(error);
                self.setState({
                    loading: false,
                    mode: 'display'

                })
            });
    }

    onCrop = (preview) => {
        this.setState({
            preview,
            currentImage: preview
        })
    }

    onClose = () => {
        this.setState({preview: null, currentImage: null, capitals: null});
    }

    renderAvatarSection() {
        return (
            <div style={{padding: '0', margin: '0'}}>
                <div className='avatarContainer'>
                    {!this.state.setProfileImage &&
                        <img src={this.state.currentImage ? this.state.currentImage : this.defineProfileImage()}
                             style={{width: '286px', height: '295px'}} alt={'profileImage'}/>}

                    {this.state.setProfileImage &&
                        <React.Fragment>
                            <Avatar
                                width={window.innerWidth < 321 ? 235 : 286}
                                imageWidth={window.innerWidth < 321 ? 200 : 286}
                                height={window.innerWidth < 321 ? 235 : 295}
                                imageHeight={295}
                                onCrop={this.onCrop}
                                onClose={this.onClose}
                                label='Resim Ekle'
                                // onBeforeFileLoad={this.onBeforeFileLoad}
                                src={this.state.currentImage}
                            />
                            <button onClick={() => this.submitAvatar()}
                                    style={{
                                        border: 'none',
                                        background: 'transparent',
                                        color: 'green',
                                        fontSize: '12px',
                                        fontWeight: '500',
                                        cursor: 'pointer'
                                    }}
                            >Kaydet
                            </button>
                        </React.Fragment>
                    }
                    <React.Fragment>
            <span onClick={() => this.setState({currentImage: null, setProfileImage: true, capitals: null})}
                  data-toggle="title"
                  title='Resmi Ekle'
                  style={{cursor: 'pointer', padding: '0 1em', color: 'blue'}}
            >
            <i className="far fa-edit"/>
          </span>
                    </React.Fragment>
                    {this.state.capitals &&
                        <div className='overlayCapitals' style={{height: '80%'}}>
                            <label>{this.state.capitals}</label>
                        </div>}

                    {this.state.loading &&
                        <div className="grid-loading">
                            <div/>
                            <div/>
                            <div/>
                            <div/>
                            <div/>
                            <div/>
                            <div/>
                            <div/>
                            <div/>
                        </div>}

                </div>
                {
                    this.state.isActualImage && this.state.mode === 'edit' && !this.state.loading &&
                    <Row style={{margin: '0'}}>
                        <Col xs='4' sm='4' md='4' lg='4' xl='4'
                             style={{margin: '0', padding: '0 5px 0 0', textAlign: 'right'}}>
                            <label style={{fontWeight: 'bold'}}>Boyut:</label>
                        </Col>

                        <Col xs='8' sm='8' md='8' lg='8' xl='8' style={{margin: '0', padding: '0 40px 0 0'}}>
                            <input type='range' step={0.01} min={1} max={2} value={this.state.imageSize}
                                   onChange={(e) => this.resizeImage(e)}/>
                        </Col>
                    </Row>
                }
            </div>
        )
    }

    decideAvatarDiv() {

        let currentWidth = window.innerWidth;

        if (currentWidth < 992) {
            this.setState({
                avatarDiv: 'top'
            });
            return null;
        } else {
            this.setState({
                avatarDiv: 'side'
            });
            return null;
        }
    }

    forceCloseDetailDialog() {
        this.props.closeDetailDialog();
    }

    stringToByteArray() {
        let imageStr = this.state.preview;
        let bytes = [];
        for (let i = 0; i < imageStr.length; i++) {
            bytes.push(imageStr.charCodeAt(i));
        }
        return bytes;
    }

    saveAndCloseDetailDialog() {
        let self = this;

        let payload = {};
        payload.id = self.state.employee.id;
        payload.photo = this.stringToByteArray();

        self.setState({loading: true});

        let params = {
            url: '/employee/saveAvatarPic',
            method: "post",
            data: payload
        };
        requestK(params)
            .then(() => {
                self.setState({loading: false});
                self.forceCloseDetailDialog();
            })
            .catch(function (error) {
                showAxiosError(error);
                self.setState({loading: false})
            });
    }

    closeDetailDialog() {
        if (this.state.isActualImage && this.state.mode === 'edit') {
            this.setState({
                notify: {
                    type: 'error',
                    message: 'Yeni resim eklendi fakat kaydedilmedi ! Devam ederseniz deiiklikleriniz kaybolacak',
                    html:
                        <Row>
                            <Col xs='6' style={{textAlign: 'center'}}>
                                <button className='notifyButton red' disabled={this.state.loading}
                                        onClick={() => this.forceCloseDetailDialog()}>Devam
                                    Et
                                </button>
                            </Col>

                            <Col xs='6' style={{textAlign: 'center'}}>
                                <button className='notifyButton blue' disabled={this.state.loading}
                                        onClick={() => {
                                            if (checkGrant(this, EMPLOYEES)) {
                                                this.saveAndCloseDetailDialog();
                                            }
                                        }
                                        }>Resmi Kaydet ve Kapat
                                </button>
                            </Col>
                        </Row>,
                    autohide: false
                }
            })
        } else {
            this.forceCloseDetailDialog();
        }
    }

    hasGrant = (grant) => {
        return this.props.hasGrant(grant);
    };

    render() {
        let employee = this.state.employee;

        return (
            <Modal show={this.props.isOpen}
                   size="lg"
                   animation={false}
                   modalTransition={{timeout: 700}}
                   dialogClassName="modal-90w"
                   backdropTransition={{timeout: 1300}}
                   aria-labelledby="example-custom-modal-styling-title"
                   backdrop="static">
                <Modal.Header className='modalHeader'>
                    <Modal.Title id="example-custom-modal-styling-title"
                                 className='col-xs-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 modalTitle'>

                        <Col xs='2' sm='2' md='2' lg='2' xl='2' style={{padding: '0'}}/>

                        <Col xs='8' sm='8' md='8' lg='8' xl='8' style={{padding: '0'}}>
                            <label className='modalLabel'>
                                {employee && (employee.name + " " + employee.surname)}
                            </label>
                        </Col>

                        <Col xs='2' sm='2' md='2' lg='2' xl='2' style={{padding: '0 15px 0 0', textAlign: 'right'}}>
                            <button disabled={this.state.loading}
                                    className='fas fa-times simpleButton modalHeaderButton'
                                    onClick={() => this.closeDetailDialog()}/>
                        </Col>
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body className='modalBody'>

                    {this.state.notify &&
                        <Row>
                            <NotifyKS
                                type={this.state.notify.type}
                                message={this.state.notify.message}
                                html={this.state.notify.html}
                                timeout={this.state.notify.timeout}
                                autohide={this.state.notify.autohide}
                            />
                        </Row>}

                    {this.state.employee ?
                        <Row className='modalBodyMainRow'>

                            {(this.state.avatarDiv && this.state.avatarDiv === 'top') &&
                                <Col xs='12' sm='12' md='12' className='containerFrame'>
                                    <Col id='avatarContainer' xs='12' sm='12' md='12' lg='12' xl='12'
                                         style={{padding: '0', margin: '0', textAlign: 'center'}}>
                                        {this.renderAvatarSection()}
                                    </Col>

                                    <Col xs='12' sm='12' md='12' lg='12' xl='12'
                                         style={{padding: '0 5px 0 6px', margin: '40px 0 0 0'}}>
                                        {this.props.isBordroOwner && checkGrantToDisplay(this, EMPLOYEES) && this.renderDocument("Bordro", "bordroCount", `/peyk/bordroreports/${employee.id}`)}
                                        {this.props.isFormOwner && checkGrantToDisplay(this, EMPLOYEES) && this.renderDocument("Form", "formCount", `/peyk/formreports/${employee.id}`)}
                                        {!this.props.readOnly && this.state.options && !!this.state.options.length && this.renderSurveyDocument()}
                                    </Col>
                                </Col>}

                            <Col xs='12' sm='12' md='12' lg='7' xl='7' className='containerFrame'>
                                {this.renderDetail("Tckn", "tckn")}
                                {this.props.isDepartmentOwner && this.renderDetail("Departman", "department")}
                                {this.props.isDepartmentOwner && this.renderDetail("Grevi", "jobTitle")}
                                {this.props.isDepartmentOwner && this.renderDetail(this.state.windowSize.matches ? "Ba. Old. Ynetici" : "Bal Olduu Ynetici", "manager")}
                                {this.props.isDepartmentOwner && this.renderDetail(this.state.windowSize.matches ? "Ba. Old. 2. Ynetici" : "Bal Olduu 2. Ynetici", "secondManager")}
                                {this.props.isDepartmentOwner && this.renderDetail(this.state.windowSize.matches ? "Ba. Old. 3. Ynetici" : "Bal Olduu 3. Ynetici", "thirdManager")}
                                {this.props.isKepOwner && this.renderDetail("Kep Adresi", "kepAddress")}
                                {!this.props.isKepOwner && this.renderDetail("e-Posta Adresi", "email")}
                                {!this.props.isKepOwner && this.renderDetail("Telefon Numaras", "telephone")}
                                {this.renderDetail("Durum", "statusType")}
                                {(employee && employee.statusType === 'PASSIVE') && this.renderDetail("Pasif Durum Gerekesi", "reason")}
                                {(employee && employee.statusType === 'PASSIVE') && this.renderDetail("Pasif Durum Biti Tarihi", "passiveEndDate")}
                                {this.renderDetail("e Balama Tarihi", "employmentStartDate")}
                                {this.renderDetail("alma ekli", "employmentStatus")}
                                {this.renderDetail("Yllk zin Hakk", "remainingAnnualVacation")}
                                {this.renderDetail("Mazeret zin Hakk", "remainingExcuseVacation")}
                                {this.renderDetail("Cinsiyet", "gender")}
                                {this.renderDetail("Adres", "address")}
                                {this.renderDetail("Doum Tarihi", "dateOfBirth")}
                                {this.renderDetail("Doum Yeri", "cityOfBirth")}
                                {this.renderDetail("Eitim Durumu", "educationLevel")}
                                {this.renderDetail("Askerlik Durumu", "militaryStatus")}
                                {this.renderDetail("Entegrasyon Kodu", "integrationCode")}
                                {this.renderDetail(" Szlemesi Tr", "employmentContract")}
                                {this.renderSalaryDetail("cret", "salary")}
                                {this.renderDetail("VIP", "vip")}
                                {this.renderDetail("Masraf Yeri", "expenditureDepartment")}
                                {this.props.titleList && this.props.titleList.length > 0 && this.props.titleList.map(customTitle => {
                                    return this.renderCustomDetail(customTitle.title, this.state.customFields[customTitle.title], customTitle);
                                })}
                                {this.renderDetail("Son Gncelleme Tarihi", "updatedAt")}
                            </Col>

                            {(this.state.avatarDiv && this.state.avatarDiv === 'side') &&
                                <Col lg='5' xl='5' className='containerFrame'>
                                    <Col id='avatarContainer' xs='12' sm='12' md='12' lg='12' xl='12'
                                         style={{padding: '0', margin: '0', textAlign: 'center'}}>
                                        {this.renderAvatarSection()}
                                    </Col>

                                    <Col xs='12' sm='12' md='12' lg='12' xl='12'
                                         style={{padding: '0 5px 0 6px', margin: '40px 0 0 0'}}>
                                        {checkGrantToDisplay(this, [EMPLOYEES]) && this.renderVaccinationcard()}
                                        {this.props.isBordroOwner && checkGrantToDisplay(this, [EMPLOYEES]) && this.renderDocument("Bordro", "bordroCount", `/peyk/bordroreports/${employee.id}`)}
                                        {this.props.isFormOwner && checkGrantToDisplay(this, [EMPLOYEES]) && this.renderDocument("Form", "formCount", `/peyk/formreports/${employee.id}`)}
                                        {!this.props.readOnly && checkGrantToDisplay(this, [EMPLOYEES]) && this.state.options && !!this.state.options.length && this.renderSurveyDocument()}
                                        {checkGrantToDisplay(this, EMPLOYEES) && checkGrantToDisplay(this, VIEWS_TCKN) && checkGrantToDisplay(this, VIEW_TEL) && checkGrantToDisplay(this, VIEW_MAIL) &&
                                            <Row style={{marginTop: '20%', marginLeft: '20%'}}>
                                                <Button color="primary" onClick={this.downloadPersonalCard}
                                                        disabled={this.state.downloadPdf}>
                                                    <span>Personel Kart ndir&nbsp;&nbsp;</span>
                                                    <i className="fa fa-download"/>
                                                </Button>
                                            </Row>}
                                    </Col>
                                </Col>}
                        </Row>

                        :

                        <Row style={{padding: '5%'}}>
                            <div className="ripple-loading" style={{left: '45%'}}>
                                <div style={{background: '#72b243'}}/>
                                <div style={{background: '#2587c8'}}/>
                            </div>
                        </Row>}
                </Modal.Body>
                {this.state.showVaccinatinCardModal &&
                    <VaccinationCardModal
                        showVaccinatinCardModal={this.state.showVaccinatinCardModal}
                        vaccinationCardInfo={this.state.vaccinationCardInfo}
                        employeeId={this.props.selectedId}
                        create={false}
                        toggleModal={(flag) => this.setState({showVaccinatinCardModal: flag})}
                        modal={this.state.showVaccinatinCardModal}
                        isVaccinationcard={() => this.setState({isVaccinationcard: false})}/>}
            </Modal>
        );
    }
}

export default EmployeeDetails;
