import React, {Component} from "react";
import Select from 'react-select';
import {dataURItoBlob} from "@rjsf/utils";
import PdfViewer from "../approval/PdfViewer/PdfViewer";
import MiniButton from "../../../components/MiniButton";

class SurveyAttachment extends Component {

    constructor(props) {
        super(props);
        this.state = {
            selectedSurvey: null,
            selectedAttachment: null,
            options: props.surveyNamesList.map(d => ({'value': d.surveyId, 'label': d.surveyName})),
            attachmentOptions: [],
            selectedFilename: dataURItoBlob(this.props.file.base64).name,
            displaySurveyAttachmentFileModal: false,
            selectedFile: {base64: ""},
            selectedFileType: "image"
        };
        this.changeSelectOptionHandler = this.changeSelectOptionHandler.bind(this);
        this.changeSelectOptionAttachmentHandler = this.changeSelectOptionAttachmentHandler.bind(this);
    }

    componentDidUpdate() {
        if (this.props.file !== null &&
            this.props.file.base64 !== null
        ) {
            let newFileName = dataURItoBlob(this.props.file.base64).name;
            if (newFileName !== this.state.selectedFilename) {
                this.setState({
                    selectedSurvey: null,
                    selectedAttachment: null,
                    selectedFilename: newFileName,
                    selectedFile: null,
                    displaySurveyAttachmentFileModal : false
                });
            }
        }
    }
    changeSelectOptionHandler = (event) => {
        let self = this;
        let selectedSurvey = self.props.surveyNamesList.find(survey => survey.surveyId === event.value);
        let attachmentOptions = selectedSurvey !== null ? [...new Set(selectedSurvey.attachmentList.map(attachment => attachment.name))] : [];
        attachmentOptions = attachmentOptions.map(name => ({"value": name, "label": name}));
        self.setState({selectedSurvey, attachmentOptions});
    }

    componentDidMount() {
        let self = this;
        let isFind = false;
        self.props.surveyNamesList.forEach(survey => {
            survey.attachmentList.forEach(attachment => {
                if (attachment.name.trim().toUpperCase() === self.props.file.name.split(".", 1)[0].trim().toUpperCase() && attachment.isAttached !== true && isFind === false) {
                    attachment.isAttached = true;
                    attachment.base64Value = this.props.file.base64;
                    let attachmentOptions = [...new Set(survey.attachmentList.map(attachment => attachment.name))];
                    attachmentOptions = attachmentOptions.map(name => ({"value": name, "label": name}));
                    self.setState({selectedSurvey: survey, selectedAttachment: attachment, attachmentOptions});
                    isFind = true;
                }
            })
        });
        if (self.state.selectedSurvey !== null) {
            let attachmentOptions = self.state.selectedSurvey !== null ? [...new Set(self.state.selectedSurvey.attachmentList.map(attachment => attachment.name))] : [];
            attachmentOptions = attachmentOptions.map(name => ({"value": name, "label": name}));
            self.setState({attachmentOptions});
        }
    }


    changeSelectOptionAttachmentHandler = (event) => {
        let self = this;
        let selectedSurvey = self.state.selectedSurvey;
        let selectedAttachment = self.state.selectedAttachment;
        if (selectedAttachment !== null) {
            selectedSurvey.attachmentList.forEach(attachment => {
                if (selectedAttachment !== null &&
                    attachment.base64Value !== null &&
                    selectedAttachment.base64Value !== null &&
                    dataURItoBlob(attachment.base64Value).name !== null &&
                    dataURItoBlob(selectedAttachment.base64Value).name !== null &&
                    attachment.name === selectedAttachment.name &&
                    (dataURItoBlob(attachment.base64Value).name === dataURItoBlob(selectedAttachment.base64Value).name) &&
                    selectedAttachment.isAttached === true) {
                    attachment.base64Value = null;
                    attachment.isAttached = false;
                }
            });
        }
        let newAttachment = {};
        newAttachment.name = event.value;
        newAttachment.base64Value = self.props.file?.base64;
        newAttachment.isAttached = true;
        selectedAttachment = newAttachment;
        selectedSurvey.attachmentList.push(selectedAttachment);
        self.setState({selectedSurvey, selectedAttachment});
    }

    showFile = (file) => {
        let selectedFileType;
        if (file.base64.includes("application/pdf"))
            selectedFileType = "pdf";
        else
            selectedFileType = "image"
        this.setState({
            selectedFile: file,
            selectedFileType,
            displaySurveyAttachmentFileModal: !this.state.displaySurveyAttachmentFileModal
        });
    }

    render() {
        return (
            <div>
                <div className='row mb-3'>
                    <div className='col-12 col-md-4 d-flex align-items-center pr-3'>
                        <MiniButton style={{ marginRight: "10px" }}
                                    title="Grntle" faIcon="fa-eye"
                                    onClick={() => this.showFile(this.props.file)}
                                    color="success"/>
                        <div style={{ wordWrap: 'break-word', maxWidth: '200px' }}>
                        <span className='m-0'
                              style={{ fontSize: '0.8em' }}
                              title={this.props.file.name}>
                            <b>{this.props.file.name}</b>
                        </span>
                        </div>
                    </div>
                    <div className='col-12 col-md-4 px-3'>
                        <Select
                            placeholder={"Seiniz.."}
                            value={this.state.selectedSurvey !== null && this.state.selectedSurvey !== undefined ? this.state.options.find(survey => survey.value === this.state.selectedSurvey.surveyId) : ""}
                            onChange={this.changeSelectOptionHandler}
                            options={this.state.options}
                        />
                    </div>
                    <div style={{marginLeft: 'auto'}} className='col-12 col-md-4 px-3'>
                        {<Select
                            placeholder={"Seiniz.."}
                            value={this.state.selectedAttachment !== null ? this.state.attachmentOptions.find(attachment => attachment.value === this.state.selectedAttachment?.name) : ""}
                            onChange={this.changeSelectOptionAttachmentHandler}
                            options={this.state.attachmentOptions}
                        />
                        }
                    </div>
                </div>
                <div>
                    {this.state.displaySurveyAttachmentFileModal && this.state.selectedFileType === "image" && this.state.selectedFile !== null &&
                        <img src={this.state.selectedFile.base64}
                             alt={this.state.selectedFile.name}/>
                    }
                    {this.state.displaySurveyAttachmentFileModal && this.state.selectedFileType === "pdf" && this.state.selectedFile !== null &&
                        <PdfViewer
                            url={this.state.selectedFile.base64}
                            scale={0.7}
                        />
                    }
                </div>
                <br/>
            </div>
        );
    };
}

export default SurveyAttachment;