import React, {Component} from 'react';
import {requestK} from "../../../../helper/APIUtils";
import {showAxiosError} from "../../../../helper/helper";
import Alert from "react-s-alert";
import moment from "moment";
import {personalInfoStatus, renewListForDepartment} from "../../common/PeykHelper";
import Table from "../../../../components/Table/Table";
import DetailedSearch from "../../../../components/DetailedSearch/DetailedSearch";

class ArchivePersonalInfo extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selection: [],
      selectedAll: false,
      content: [],
      total: 0,
      page: 0,
      size: 10,
      loading: false,
      manual: false,
      isConditionAllowed: false,
      stop: null,
      searching: false,
      getPersonalInfos: [],
      searchQuery: undefined,

    };
    this.fetchData = this.fetchData.bind(this);
  }

  componentDidMount() {
    this.getPersonalInfos();
  }

  fetchData(accessor, direction, page, size) {
    let self = this;
    if (accessor === "date") {
      accessor = undefined;
      direction = undefined;
    }
    let sortStr = accessor !== undefined ? ('&sort=' + accessor + ',' + direction) : '';
    let searchQuery = !self.state.searchQuery ? '' : '&' + self.state.searchQuery;
    let params;
    params = {
      url: '/survey/readPersonalInfo?page=' + page + '&size=' + size + searchQuery +
        ((searchQuery !== '' && searchQuery.includes("statusTypeFile")) ? '' : '&statusTypeFile=') +
        ((searchQuery !== '' && searchQuery.includes("personalInfos")) ? '' : '&personalInfos=')
        + sortStr,
      method: "get",
    };
    self.setState({loading: true, manual: self.manual === true ? false : self.manual, searching: true});
    requestK(params)
      .then(function (response) {
        self.setState({
          page: page,
          size: size,
          total: response.data.totalElements,
          content: response.data.content,
          loading: false,
          selectedAll: false,
          selection: [],
          searching: false
        });
        self.setState({loading: false});
      })
      .catch(function (error) {
        self.setState({loading: false});
        showAxiosError(error);
      });
  }

  handleSearch = (searchQuery) => {
    this.setState({
      searchQuery,
      searching: true,
      selection: [],
    }, () => {
      if (this.state.searchQuery && !this.state.searchQuery.toString().includes("personalInfos") && this.state.searchQuery.toString().includes("statusTypeFile")) {
        Alert.warning('Ltfen, sorgu iin nce bir zlk dosyas seiniz.', {
          position: 'top-right',
          effect: 'stackslide',
          timeout: 5000
        });
      } else {
        this.fetchData(this.state.accessor, this.state.direction, 0, this.state.size)
      }
    })
  };

  getPersonalInfos = () => {
    let self = this;
    let params = {
      url: `/survey/getPersonalInfosTemplate`,
      method: "post"
    };
    requestK(params)
      .then(function (response) {
        let optionItems = [];
        response.data.map((personalInfo) => {
          optionItems.push({value: personalInfo.id, label: personalInfo.name});
        });
        self.setState({personalInfoOptions: optionItems});
      }).catch(function (error) {
      showAxiosError(error);
    });
  }

  renderTable() {
    let columnExist = [
      {
        Header: "Dokman",
        accessor: "surveyName"
      },
      {
        Header: "Oluturulma Tarihi",
        accessor: "document_createdAt",
        sortable: false,
        Cell: row => (
          <div>
            {row.date && moment(row.date.toString().replace("T", " ")).format("DD-MM-YYYY HH:mm:ss")}
          </div>
        )
      },
      {
        Header: "Ad",
        accessor: "name",
        sortable: false,
        Cell: row => (<div>{row.user.firstName}</div>)
      },
      {
        Header: "Soyad",
        accessor: "surname",
        sortable: false,
        Cell: row => (<div>{row.user.lastName}</div>)
      },
      {
        Header: "TCKN",
        accessor: "tckn",
        sortable: false,
        Cell: row => (
          <div>
            {row.user.username}
          </div>
        )
      },
      {
        Header: "Onay Durumu",
        accessor: "state",
        Cell: row => (
          <div>
            {row.state}
          </div>
        )
      },
    ];
    let columns = renewListForDepartment(this, columnExist, "accessor");

    return <Table columns={columns}
                  content={this.state.content}
                  loading={this.state.loading}
                  loadingText="Kaytlar getiriliyor..."
                  total={this.state.total}
                  page={this.state.page}
                  size={this.state.size}
                  manual={this.state.manual}
                  options={["5", "10", "20", "50", "100"]}
                  fetchData={this.fetchData}/>
  }

  render() {
    const fields = [
      {
        label: "Ad",
        accessor: "user_firstName",
        type: "text",
        operator: "="
      },
      {
        label: "Soyad",
        accessor: "user_lastName",
        type: "text",
        operator: "="
      },
      {
        label: "TCKN",
        accessor: "user_username",
        type: "numericText",
        operator: "="
      },
      {
        label: "zlk Dosyalar",
        accessor: "personalInfos",
        type: "select",
        operator: "=",
        selectData: this.state.personalInfoOptions
      },
      {
        label: "Durum",
        accessor: "statusTypeFile",
        type: "select",
        operator: "=",
        selectData: personalInfoStatus()
      }
    ];
    return (

      <div>
        {
        <DetailedSearch fields={fields}
                        loading={this.state.searching}
                        onSearch={this.handleSearch}/>
        }
        <div>
          {this.renderTable()}
        </div>
      </div>
    );
  }
}

export default ArchivePersonalInfo;