import React, {Component} from 'react';
import {encrypt, login, requestK} from "./helper/APIUtils";
import {ACCESS_TOKEN, KEP_IK_API_BASE_URL} from "./constants";
import {validateField} from "./common/validation";
import {Button, Col, Row} from "reactstrap";
import ReCAPTCHA from "react-google-recaptcha";
import Alert from "react-s-alert";
import {getCaptchaCode, getFailedLoginCountParam, showAxiosError} from "./helper/helper";
import {PublicClientApplication} from "@azure/msal-browser";
import  {configLocal,configTest,configDemo,configProd,configPreProd} from "./AzureConfig";

let config = null;
if (window.location.href.startsWith("http://localhost:3000"))
    config = configLocal;
else if (window.location.href.startsWith("https://peyktest.kolaysoft.com.tr"))
    config = configTest;
else if (window.location.href.startsWith("https://peykdemo.kolaysoft.com.tr"))
    config = configDemo;
else if (window.location.href.startsWith("https://peykdestek.kolaysoft.com.tr"))
    config = configPreProd;    
else if (window.location.href.startsWith("https://peyk.kolaysoft.com.tr"))
    config = configProd;

const recaptchaRef = React.createRef();

class Login extends Component {

    constructor(props) {
        super(props);
        this.state = {
            error: {},
            errorMessage: '',
            errorType: "",
            username: '',
            password: '',
            processing: false,
            passwordHandle: false,
            captcha: false,
            isCaptchaActive: false,
            captchaErrors: {},
            azureAuthentication:false
        };

        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleNewPassword = this.handleNewPassword.bind(this);
        this.validateField = this.validateField.bind(this);
        this.onChange = this.onChange.bind(this);
        this.handleKeyPress = this.handleKeyPress.bind(this);
        this.validateForm = this.validateForm.bind(this);
        this.handleCaptcha = this.handleCaptcha.bind(this);
        this.handleValidationCaptcha = this.handleValidationCaptcha.bind(this);
        this.loginWithAzure = this.loginWithAzure.bind(this);
        this.publicClientApplication = new PublicClientApplication({
            auth: {
                clientId: config != null ? config.appId : "",
                redirectUri: config != null ? config.redirectUri : "",
                authority: config != null ? config.authority : ""
            },
            cache: {
                cacheLocation: "sessionStorage",
                storeAuthStateInCookie: false
            }
        });
        getFailedLoginCountParam(this);
    }

    async loginWithAzure() {
        let self = this;
        self.publicClientApplication.loginPopup({
            scopes: config != null ? config.scopes : "", prompt: "select_account", redirectUri: config.redirectUri
        }).then(function (response) {
            sessionStorage.setItem(ACCESS_TOKEN, response.accessToken);
            self.setState(() => self.props.onLogin());
            self.setState({azureAuthentication: true});
            requestK({
                url: "/auth/signinWithAzure", method: 'post'
            });
        }).catch(function (error) {
            self.setState({azureAuthentication: false});
            showAxiosError(error);
        });
    }

    logout() {
        let self = this;
        self.publicClientApplication.logoutPopup().then(r => self.setState({azureAuthentication: false}));
    }

    handleCaptcha = (key) => {
        this.setState({
            captcha: true,
            g_recaptcha_response: key
        })
    }

    handleValidationCaptcha() {
        let errors = {};
        let captchaIsValid = true;
        if (!this.state.captcha) {
            captchaIsValid = false;
            errors["captcha"] = "Ltfen gvenlik dorulamasn tamamlaynz!";
        }
        this.setState({captchaErrors: errors});
        return captchaIsValid;
    }

    componentDidMount() {
        getCaptchaCode(this);

    }

    async componentWillMount() {
        await getFailedLoginCountParam(this);
    }

    onChange(e) {
        this.setState({[e.target.id]: e.target.value, errorMessage: "", errorType: ""})
    }

    validateField(e) {
        return;
        let id = e.target.id;
        let rule = e.target.dataset.vdata;
        let boundary = e.target.dataset.vlength;
        let result = validateField(rule, boundary, e.target.value);
        let error = this.state.error;
        error = Object.assign(error, {[id]: result});
        this.setState({error});
        this.props.errorListener(error);
    }

    validateClass(id) {
        let error = this.state.error;
        return (error[id] === undefined ? "" : (error[id].valid ? " has-success" : " has-danger"));
    }

    validateMessage(id) {
        let error = this.state.error;
        return (error[id] === undefined ? '' : (error[id].message));
    }

    validateForm() {
        if (!!this.state.username && !!this.state.password) {
            if (this.state.isCaptchaActive) {
                if (this.handleValidationCaptcha()) {
                    return true;
                } else {
                    return false;
                }
            }
            return true;
        } else {
            let userMsg = "";
            let passMsg = "";
            if (!this.state.username) {
                userMsg = "Kullanc ad "
            }
            if (!this.state.password) {
                passMsg = "ifre "
            }
            if (userMsg && passMsg)
                this.setState({errorMessage: userMsg + " ve " + passMsg + " girmediniz", errorType: "warning"});
            else
                this.setState({errorMessage: userMsg + passMsg + " girmediniz", errorType: "warning"});

            return false;
        }
    }

    handleKeyPress(event) {

        if (event.key === 'Enter') {
            this.handleSubmit();
        }
    }

    handleSubmit() {
        let self = this;

        if (!this.validateForm()) return;

        const loginRequest =
            {
                'username': encrypt(this.state.username),
                'password': encrypt(this.state.password),
                'captcha': this.state.g_recaptcha_response
            };
        self.setState({processing: true})
        sessionStorage.removeItem(ACCESS_TOKEN);
        login(loginRequest)
            .then(response => {
                if (self.state.isCaptchaActive)
                    recaptchaRef.current.reset();
                if (response.data.message === 'TWO_PHASE_AUTH') {
                    self.setState({
                        processing: false,
                        errorType: "",
                        isCaptchaActive: false,
                        captcha: false
                    });
                    this.props.history.push({
                        pathname: `/loginWithVerCode`,
                        state: {
                            username: this.state.username,
                            password: this.state.password,
                            durationTime: response.data.duration
                        }
                    });
                } else if (response.data.message === 'TWO_PHASE_AUTH_EXIST') {
                    Alert.warning("Telefonunuza dorulama kodu zaten gnderilmi. Mevcut dorulama kodunun sresi dolunca yenileme istei gnderebilirsiniz.", {
                        position: 'top-right',
                        effect: 'stackslide',
                        timeout: 5000
                    });
                    this.props.history.push({
                        pathname: `/loginWithVerCode`,
                        state: {
                            username: this.state.username,
                            password: this.state.password,
                            durationTime: response.data.duration
                        }
                    });
                } else {
                    sessionStorage.setItem(ACCESS_TOKEN, response.data.accessToken);
                    self.setState({
                        processing: false,
                        errorType: "",
                        isCaptchaActive: false,
                        captcha: false
                    }, () => self.props.onLogin());
                }
            }).catch(error => {
            showAxiosError(error);
            let response = error.response;
            if (!response) {
                Alert.error("Bir sorun olutu.", {
                    position: 'top-right',
                    effect: 'stackslide',
                    timeout: 5000
                });
            } else {
                let status = response.status !== undefined ? response.status : '';
                let errorMessage = "";
                let isCaptchaActive = response.data['captcha'];
                if (!!status && status >= 400 && status !== 500) {
                    errorMessage = response.data['message'];
                } else {
                    errorMessage = error.message ? error.message : 'Doru bilgilerle tekrar deneyin.';
                }
                self.setState({
                    errorMessage,
                    processing: false,
                    errorType: "danger",
                    isCaptchaActive: isCaptchaActive,
                    captcha: false
                });

                if (self.state.isCaptchaActive)
                    recaptchaRef.current.reset();
            }
        });

    }

    handleNewPassword() {
        this.props.history.push("/passwordRecovery");
    }

    render() {
        let failedLoginCountParam = this.state.failedLoginCountParam === 0 ? true : false;
        return (
            <Row
                style={{
                    // height: '-webkit-fill-available',
                    height: '100vh',
                    backgroundImage: 'url(' + KEP_IK_API_BASE_URL + "/virtual/resource/loginBackground.png" + ')'
                }}>
                <Col sm='12' md='12' lg='12' className='login-panel-container'>
                    <Row className='login-panel'>
                        <Col xs='12' sm='12' md='4' lg='4' style={{textAlign: 'center', padding: '0 3% 3% 3%'}}>
                            <img src={KEP_IK_API_BASE_URL + "/virtual/resource/login.png"} alt="now-logo"/>
                        </Col>

                        <Col xs='12' sm='12' md='8' lg='8' style={{padding: '2% 2% 3% 2%'}}>
                            <Col sm='12' md='12' lg='12' className="no-border  input-group input-group-lg">
								<span className="input-group-addon">
									<i style={{fontSize: '15px', color: 'white'}}
                                       className="now-ui-icons users_circle-08"/>
								</span>

                                <input placeholder="T.C. Kimlik No"
                                       autoComplete="off"
                                       autoFocus
                                       id="username"
                                       name="username"
                                       style={{
                                           borderLeft: '1px solid #c3cdd4',
                                           background: '#b5bdc6',
                                           color: 'white',
                                           fontSize: '14px',
                                           fontWeight: 'bold'
                                       }}
                                       onKeyPress={this.handleKeyPress}
                                       onChange={this.onChange}
                                       value={this.state.username}
                                       className="loginText form-control"
                                       type="text"/>
                            </Col>

                            <Col sm='12' md='12' lg='12' className="no-border  input-group input-group-lg">
								<span className="input-group-addon">
									<i style={{fontSize: '15px', color: 'white'}} className="fa fa-asterisk"
                                       aria-hidden="true"/>
								</span>

                                <input placeholder="ifre"
                                       autoComplete="off"
                                       id="password"
                                       name="password"
                                       style={{
                                           borderLeft: '1px solid #c3cdd4',
                                           background: '#b5bdc6',
                                           color: 'white',
                                           fontSize: '14px',
                                           fontWeight: 'bold'
                                       }}
                                       onKeyPress={this.handleKeyPress}
                                       onChange={this.onChange}
                                       value={this.state.password}
                                       className="loginText form-control"
                                       type="password"/>
                            </Col>

                            {(failedLoginCountParam || this.state.isCaptchaActive) &&
                                <Col sm='12' md='12' lg='12' className="no-border  input-group input-group-lg" style={{
                                    textAlign: 'center',
                                    padding: '0',
                                    margin: '4% 0 0 0'
                                }}>

                                    <ReCAPTCHA
                                        ref={recaptchaRef}
                                        sitekey={this.state.captchaCode}
                                        onChange={this.handleCaptcha}
                                        className="loginText form-control"
                                    />

                                </Col>}
                            {this.state.captchaErrors["captcha"] &&
                                <Col sm='12' md='12' lg='12'
                                     style={{textAlign: 'center', padding: '0', margin: '4% 0 0 0'}}>
                                    <label
                                        className='errorMessage'>{this.state.captchaErrors["captcha"]}</label>
                                </Col>}
                            {this.state.errorMessage &&
                                <Col sm='12' md='12' lg='12'
                                     style={{textAlign: 'center', padding: '0', margin: '4% 0 0 0'}}>
                                    <label className='errorMessage'>{this.state.errorMessage}</label>
                                </Col>}

                        </Col>

                        <Col xs='12' sm='12' md='12' lg='12' className='login-button-container'>
                            <Button color="primary" className='login-button'
                                    onClick={this.handleSubmit}>
                                {!this.state.processing ? 'GR' : <i className="fa fa-spinner fa-spin"/>}
                            </Button>
                        </Col>
                        {!this.state.processing &&
                            <Col xs='12' sm='12' md='12' lg='12' className='login-button-container'>
                                <Button color="secondary" className='login-button'
                                        onClick={this.handleNewPassword}>
                                    ifre Olutur / ifremi Unuttum
                                </Button>
                            </Col>}

                        {config != null &&
                            <Col xs='12' sm='12' md='12' lg='12' className='login-button-container'>
                                <div className="loginSeperator">
                                    <span></span>
                                    <label className="loginSeperatorText">VEYA</label>
                                    <span></span>
                                </div>
                                <Button color="primary" className='login-button'
                                        onClick={() => this.loginWithAzure()}>
                                    <i style={{float: 'left', fontSize: '1.4em'}} className="fab fa-windows"/>
                                    Microsoft Azure ile Giri
                                </Button>
                            </Col>}
                    </Row>
                </Col>
            </Row>
        );
    }
}

export default Login;
