import React, {Component} from "react";
import {Accordion, AccordionItem, AccordionItemBody, AccordionItemTitle} from "react-accessible-accordion";
import Table from "../../../components/Table/Table";
import {requestK} from "../../../helper/APIUtils";
import {showAxiosError} from "../../../helper/helper";
import Alert from "react-s-alert";
import MediaViewer from "../../../components/MediaViewer";
import SweetAlertExt from "../../../components/SweetAlertExt";
import MiniButton from "../../../components/MiniButton";
import moment from "moment";
import SweetAlert from 'react-bootstrap-sweetalert';
import Datetime from "react-datetime/DateTime";
import {Col, Input, Label, UncontrolledTooltip} from "reactstrap";
import {validateComponent, validateField} from "../../../common/validation";
import Select from "../../../local_node_modules/react-select"

class CustomerContractAccordion extends Component {

	constructor(props) {
		super(props);

		this.state = {
			page: 0,
			aPage: 0,
			size: 10,
			aSize: 10,
			manual: false,
			aManual: false,
			url: null,
			mediaModal: false,
			serviceList: [],
			customerContractList: [],
			alert: null,
			resendAlert: null,
			contractRenewalAlert: null,
			error: {},
			resendingServices: {},
			resendingSuccess: {},
		};

		this.arrangeData = this.arrangeData.bind(this);
		this.handleShow = this.handleShow.bind(this);
		this.setMediaViewer = this.setMediaViewer.bind(this);
	}

	validateField(e) {
		let id = e.target.id;
		let rule = e.target.dataset.vdata;
		let boundary = e.target.dataset.vlength && e.target.dataset.vlength;
		let value = e.target.value;

		let result = validateField(rule, boundary, value);
		let error = this.state.error;

		error = Object.assign(error, {[id]: result});

		this.setState({error});
	}

	validateClass(id) {
		let error = this.state.error;
		return (error[id] === undefined ? "" : (error[id].valid ? " has-success" : " has-danger"));
	}

	validateMessage(id) {
		let error = this.state.error;
		return (error[id] === undefined ? '' : (error[id].message));
	}

	validateComponent() {
		let result = validateComponent(this);
		this.setState({
			error: result.error
		});
		return result
	}

	checkDateValidity(current, name) {
		let copyCurrent = current.clone();
		if (name === 'contractStartDate') {
			return copyCurrent.isAfter(Datetime.moment().subtract(1, 'days'));
		}
		if (name === 'contractFinishDate') {
			return copyCurrent.isAfter(this.state.selectedStartDate);
		}
	}

	arrangeData(page, size) {
		this.setState({
			page: page,
			size: size,
			loading: false
		});
	}

	handleShow(row) {
		let self = this;

		let formData = new FormData();
		formData.append("recordId", row.recordId);

		let params = {
			url: `/signature/contract/getContract`,
			responseType: 'arraybuffer',
			method: "post",
			data: formData
		};
		self.setState({mediaModal: true, title: "Szleme PDF"});

		requestK(params)
			.then(function (response) {
				let blob = new Blob([response.data], {type: response.headers["content-type"]});
				let urlCreator = window.URL || window.webkitURL;
				let url = urlCreator.createObjectURL(blob);
				self.setState({url: url});

			}).catch(function (error) {
			showAxiosError(error);
			self.setState({mediaModal: false, title: null});
		});
	}

	setMediaViewer() {
		this.setState({url: null, mediaModal: false});
	}

	handleEditContractDates(contract, event, type) {
		let contractId = contract.recordId;
		if (type === 'contractStartDate') {
			if (!(typeof event === "string")) {
				let newStartDate = event.format("YYYY-MM-DD");
				let newFinishDate = event.clone().add(1, 'years').format('YYYY-MM-DD');
				let automaticExtension = contract.automaticExtension;
				let creditPredefined = contract.creditPredefined;
				let storagePredefined = contract.storagePredefined;
				let employeePredefined = contract.employeePredefined;

				this.props.updateContractDates(contractId, newStartDate, newFinishDate,automaticExtension,creditPredefined,storagePredefined,employeePredefined);
			}
		}
		if (type === 'contractFinishDate') {
			if (!(typeof event === "string")) {
				let newFinishDate = event.format("YYYY-MM-DD");
				let automaticExtension = contract.automaticExtension;
				let creditPredefined = contract.creditPredefined;
				let storagePredefined = contract.storagePredefined;
				let employeePredefined = contract.employeePredefined;
				this.props.updateContractDates(contractId, null, newFinishDate,automaticExtension,creditPredefined,storagePredefined,employeePredefined);
			}
		}
		if(type === 'automaticExtension'){
			let automaticExtension = !contract.automaticExtension;
			this.setState({
				data: {
					...this.state.data,
					automaticExtension
				}
			})
			let currentFinishDate = contract.contractFinishDate;
			let creditPredefined = contract.creditPredefined;
			let storagePredefined = contract.storagePredefined;
			let employeePredefined = contract.employeePredefined;
			this.props.updateContractDates(contractId, null, currentFinishDate, automaticExtension,creditPredefined,storagePredefined,employeePredefined);
		}
		if (type === 'creditPredefined') {
			const inputText = event.target.value;
			const regex = /^[1-9][0-9]*$/;

			if (regex.test(inputText) || inputText === '' ) {
				let currentFinishDate = contract.contractFinishDate;
				let automaticExtension = contract.automaticExtension;
				let storagePredefined = contract.storagePredefined;
				let employeePredefined = contract.employeePredefined;
				let creditPredefined = inputText;
				this.props.updateContractDates(contractId, null, currentFinishDate, automaticExtension, creditPredefined,storagePredefined,employeePredefined);
			}
		}

		if (type === 'storagePredefined') {
			const inputText = event.target.value;
			const regex = /^[1-9][0-9]*$/;

			if (regex.test(inputText) || inputText === '') {
				let currentFinishDate = contract.contractFinishDate;
				let automaticExtension = contract.automaticExtension;
				let creditPredefined = contract.creditPredefined;
				let employeePredefined = contract.employeePredefined;
				let storagePredefined = inputText;
				this.props.updateContractDates(contractId, null, currentFinishDate, automaticExtension, creditPredefined,storagePredefined,employeePredefined);

			}
		}
		if (type === 'employeePredefined') {
			const inputText = event.target.value;
			const regex = /^[1-9][0-9]*$/;

			if (regex.test(inputText) || inputText === '') {
				let currentFinishDate = contract.contractFinishDate;
				let automaticExtension = contract.automaticExtension;
				let storagePredefined = contract.storagePredefined;
				let creditPredefined = contract.creditPredefined;
				let employeePredefined = inputText;
				this.props.updateContractDates(contractId, null, currentFinishDate, automaticExtension, creditPredefined,storagePredefined,employeePredefined);
			}
		}

	}

	updateContractDates(data) {
		let self = this;
		let params = {
			url: `/customer/updateContractDates`,
			method: 'post',
			data: data
		};
		requestK(params)
			.then(() => {
				Alert.success('Szleme Gncellendi', {
					position: 'top-right',
					effect: 'stackslide',
					timeout: 5000
				});
				self.props.completeUpdate();
				self.setState({alert: null})
			})
			.catch(function (error) {
				showAxiosError(error);
			});
	}

	cancelContract(data) {
		let self = this;
		let params = {
			url: `/customer/cancelContract`,
			method: 'post',
			data: data.recordId
		};
		requestK(params)
			.then(() => {
				this.props.fetchUpdatedContractList();
				Alert.success('szleme sonlandrld', {
					position: 'top-right',
					effect: 'stackslide',
					timeout: 5000
				});
			}).then(() => {
			self.setState({alert: null});
		})
		.catch(function (error) {
			showAxiosError(error);
		});
	}
	terminateContract(data) {
		let self = this;
		let params = {
			url: `/customer/deleteContract/${data.recordId}`,
			method: 'post',
		};

		requestK(params)
			.then(() => {
				this.props.fetchUpdatedContractList();
				Alert.success('Szleme silindi.', {
					position: 'top-right',
					effect: 'stackslide',
					timeout: 5000
				});
			})
			.then(() => {
				self.setState({ alert: null });
			})
			.catch(function (error) {
				showAxiosError(error);
			});
	}


	checkMandatoryField = (data) => {
		const mandatoryFields = [
			{ field: 'creditPredefined', message: 'Yllk Yklenecek SMS Kontr bo braklamaz!' },
			{ field: 'storagePredefined', message: 'zlk Dosya Boyutu(MB) bo braklamaz!' },
			{ field: 'employeePredefined', message: 'Personel Says bo braklamaz!' }
		];

		let hasEmptyField = false;

		for (const field of mandatoryFields) {
			if (!data[field.field]) {
				Alert.error(`${field.message}`, {
					position: 'top-right',
					effect: 'stackslide',
					timeout: 5000
				});
				hasEmptyField = true;
			}
		}
		return hasEmptyField;
	}


	checkForDateConflict = (newContractData) => {
		const existingContracts = this.props.customerContractList;
		const newContractStartDate = moment(newContractData.contractStartDate, 'YYYY-MM-DD');
		const newContractEndDate = moment(newContractData.contractFinishDate, 'YYYY-MM-DD');

		for (let i = 0; i < existingContracts.length; i++) {
			const existingContract = existingContracts[i];
			if (existingContract.recordId !== newContractData.recordId) {
				const existingContractStartDate = moment(existingContract.contractStartDate, 'YYYY-MM-DD');
				const existingContractEndDate = moment(existingContract.contractFinishDate, 'YYYY-MM-DD');
				if (
					(existingContractStartDate.isSameOrAfter(newContractStartDate) &&
					existingContractStartDate.isSameOrBefore(newContractEndDate) ) ||
					(existingContractEndDate.isSameOrAfter(newContractStartDate) &&
						existingContractEndDate.isSameOrBefore(newContractEndDate))
				) {
					Alert.error('Szleme tarihleri akyor. Ltfen uygun tarihleri sein.', {
						position: 'top-right',
						effect: 'stackslide',
						timeout: 5000
					});
					return true;
				}
			}
		}
		return false;
	};
	compareDates = (data) => {
		const currentDate1 = moment().format('YYYY-MM-DD');
		const currentDate=moment(currentDate1,'YYYY-MM-DD')

		const contractStartDate = moment(data.contractStartDate, 'YYYY-MM-DD');
		const contractFinishDate = moment(data.contractFinishDate, 'YYYY-MM-DD');
		if (
			(contractStartDate.isSameOrBefore(currentDate) &&
				currentDate.isSameOrBefore(contractFinishDate))
		) {
			return false;
		}else{
			Alert.warning('Sadece geerli szleme sonlandrlabilir!', {
				position: 'top-right',
				effect: 'stackslide',
				timeout: 5000
			});
			return true;
		}
	};

	validateContractDeletion = (data) => {
		const currentDate1 = moment().format('YYYY-MM-DD');
		const currentDate=moment(currentDate1,'YYYY-MM-DD')

		const contractStartDate = moment(data.contractStartDate, 'YYYY-MM-DD');
		const contractFinishDate = moment(data.contractFinishDate, 'YYYY-MM-DD');
		if (
			(currentDate.isBefore(contractStartDate) &&
				currentDate.isBefore(contractFinishDate))
		) {
			return false;
		}else{
			Alert.warning('Sadece szleme tarihinden byk olan szlemeler silinebilir!', {
				position: 'top-right',
				effect: 'stackslide',
				timeout: 5000
			});
			return true;
		}
	};

	showHideFakeElementsForCancellation(id) {
		document.getElementById('fake-cancel-warning-' + id).style["display"] = "unset";
		document.getElementById('accordion-item-title-' + id).style["background"] = "#fffb5fad";
		document.getElementById('accordion-item-title-' + id).style["color"] = "#A1A1A1";
		document.getElementById('accordion-item-title-' + id).style["font-size"] = "10px";
		document.getElementById('accordion-body-' + id).style["background"] = "#fffb5fad";
		document.getElementById('cancel-contract-button-' + id).style["display"] = "none";
	}

	warnContractCancellation(data) {
		this.setState({
			alert: (

				<SweetAlert showCancel
							warning
							title="Uyar"
							confirmBtnText="Evet"
							cancelBtnText="Hayr"
							confirmBtnBsStyle="danger"
							cancelBtnBsStyle="default"
							onConfirm={() => this.cancelContract(data)}
							onCancel={() => this.setState({alert: null})}
				>
					<div>
						<p>Aada bilgileri verilmi mteriye ait szleme bugn itibariyle sonlandirilacaktr. Devam etmek istiyor musunuz?</p>
						<ul>
							<li>
								Szleme Tarihi : {data.contractStartDate} - {data.contractFinishDate}
							</li>
							<li>
								Hizmetler : {this.previewServices(data)}
							</li>
						</ul>
					</div>
				</SweetAlert>
			)
		})
	}
	showContractDeletionWarning(data) {
		this.setState({
			alert: (

				<SweetAlert showCancel
							warning
							title="Uyar"
							confirmBtnText="Evet"
							cancelBtnText="Hayr"
							confirmBtnBsStyle="danger"
							cancelBtnBsStyle="default"
							onConfirm={() => this.terminateContract(data)}
							onCancel={() => this.setState({alert: null})}
				>
					<div>
						<p>Aada bilgileri verilmi mteriye ait szleme silinecektir. Devam etmek istiyor musunuz?</p>
						<ul>
							<li>
								Szleme Tarihi : {data.contractStartDate} - {data.contractFinishDate}
							</li>
							<li>
								Hizmetler : {this.previewServices(data)}
							</li>
						</ul>
					</div>
				</SweetAlert>
			)
		})
	}

	defineBody(data) {

		const signatureStateOptions = [
			{ value: 'INITIALIZED', label: 'Balatld' },
			{ value: 'SIGNED', label: 'mzaland' },
			{ value: 'COUNTER_SIGNED', label: 'Kar mzaland' },
			{ value: 'VALIDATED', label: 'Onayland' }
		];

		let serviceList = [];
		let copyCcs = {};
		copyCcs.ublCode = 'PEYK';
		serviceList.push(copyCcs);

		const serviceColumns = [
			{
				Header: "Hizmet",
				accessor: "ublCode",
				sortable: false
			},
			{
				Header: "cret",
				accessor: "price",
				sortable: false,
				Cell: row => (
					<div>
						{(this.props.customerContract && (this.props.customerContract.signatureState !== 'VALIDATED' || (this.props.isEntegrator && this.props.currentUser.adminUser))) &&
						<span><input value={row.price} onChange={(e) => this.props.onChangeInvalidatedContractPrice(e, row)}/><label></label></span>}
						{(this.props.customerContract && (this.props.customerContract.signatureState === 'VALIDATED' && !this.props.isEntegrator)) &&
						<label>{row.price !== null ? row.price + " " : "-"}</label>}
					</div>
				)
			},
			{
				Header: "Aklama",
				accessor: "explanation",
				sortable: false,
				Cell: row => (
					<div>
						{(this.props.customerContract && (this.props.customerContract.signatureState !== 'VALIDATED' || (this.props.isEntegrator && this.props.currentUser.adminUser))) &&
						<span><input value={row.explanation} onChange={(e) => this.props.onChangeInvalidatedContractExplanation(e, row)}/></span>}
						{(this.props.customerContract && (this.props.customerContract.signatureState === 'VALIDATED' && !this.props.isEntegrator)) &&
						<label>{row.explanation !== null ? row.explanation : "-"}</label>}
					</div>
				)
			}
		];

		return (
			<div>
				<div className="col-sm-12 row">
					{(this.props.customerContract &&  (this.props.isEntegrator || this.props.isDistributor)) &&
					<Col id={'cancel-contract-button-' + data.recordId} xs='12'
						 style={{
							 margin: '-10px 0 10px 0',
							 borderRadius: '5px',
							 border: '1px solid #dadada',
							 background: '#ff070712'
						 }}>
						<button onClick={() =>{
							const hasCompareDates = this.compareDates(data);
							if (!hasCompareDates) {
								this.warnContractCancellation(data)
							}
							}} className='btn' style={{background: '#b81717'}
						}>
							Szlemeyi Sonlandr
						</button>
						<button onClick={() =>{
							const hasValidateContract = this.validateContractDeletion(data);
							if (!hasValidateContract) {
								this.showContractDeletionWarning(data)
							}
						}} className='btn' style={{background: '#b81717'}
						}>
							Szlemeyi Sil
						</button>
					</Col>}

					{(this.props.isEntegrator && this.props.isAdminUser) ?
						<div className='col-sm-10 row'
							 style={{borderStyle: 'solid', borderWidth: '1px', borderRadius: '3px', borderColor: '#dadada', margin: '0'}}>
							<div className="col-sm-4">
								<label className="col-sm-12 col-form-label">Szleme Balang Tarihi *</label>
								<div className="col-sm-12">
									<div className={"form-group" + this.validateClass("tContractStartDate")}>
										<Datetime locale="tr"
												  className="datepicker"
												  input={true}
												  inputProps={{
													  id: 'tContractStartDate',
													  dataset: {
														  vdata: "date",
														  vlength: "1,255",
														  value: data.contractStartDate
													  }
												  }}
												  onBlur={(momentObj) => this.validateField({
													  target: {
														  id: "tContractStartDate",
														  dataset: {'vdata': "required", 'vlength': "1,255"},
														  value: (!(typeof momentObj === "string") ? momentObj.format("DD-MM-YYYY") : momentObj)
													  }
												  })}
												  closeOnSelect={false}
												  value={moment(data.contractStartDate).format('DD-MM-YYYY')}
												  dateFormat="DD-MM-YYYY"
												  ref="tContractStartDate"
												  isValidDate={(current) => this.checkDateValidity(current, 'contractStartDate')}
												  timeFormat={false}
												  onChange={(e) => this.handleEditContractDates(data, e, 'contractStartDate')}/>
										{this.validateMessage("tContractStartDate") !== "" &&
										<UncontrolledTooltip placement="right" target={"tContractStartDate"} delay={0}>
											{this.validateMessage("tContractStartDate")}
										</UncontrolledTooltip>}
									</div>
								</div>
							</div>

							<div className="col-sm-4">
								<label className="col-sm-12 col-form-label">Szleme Biti Tarihi *</label>
								<div className="col-sm-12">
									<div className={"form-group" + this.validateClass("tContractFinishDate")}>
										<Datetime locale="tr"
												  className="datepicker"
												  input={true}
												  inputProps={{
													  id: 'tContractFinishDate',
													  dataset: {
														  vdata: "date",
														  vlength: "1,255",
														  value: data.contractFinishDate
													  }
												  }}
												  onBlur={(momentObj) => this.validateField({
													  target: {
														  id: "tContractFinishDate",
														  dataset: {'vdata': "required", 'vlength': "1,255"},
														  value: (!(typeof momentObj === "string") ? momentObj.format("DD-MM-YYYY") : momentObj)
													  }
												  })}
												  closeOnSelect={false}
												  value={moment(data.contractFinishDate).format('DD-MM-YYYY')}
												  dateFormat="DD-MM-YYYY"
												  ref="tContractFinishDate"
												  isValidDate={(current) => this.checkDateValidity(current, 'contractFinishDate')}
												  timeFormat={false}
												  onChange={(e) => this.handleEditContractDates(data, e, 'contractFinishDate')}/>
										{this.validateMessage("tContractFinishDate") !== "" &&
										<UncontrolledTooltip placement="right" target={"tContractFinishDate"} delay={0}>
											{this.validateMessage("tContractFinishDate")}
										</UncontrolledTooltip>}
									</div>
								</div>
							</div>

							<div className="col-sm-2" style={{padding: '0'}}>
								<label className="col-sm-12" style={{padding: '0'}}>Otomatik Uzatma</label>
								<Label className="col-sm-12" check style={{paddingLeft: '65px'}}>
									<Input type="checkbox"   checked={data.automaticExtension}
										   onChange={(e) => this.handleEditContractDates(data, e, 'automaticExtension')}/>
								</Label>
							</div>
							<div className="col-sm-3"style={{ marginTop: '30px' }}>
								<label className="col-sm-12 form-col-label">Yllk Yklenecek SMS Kontr *</label>
								<div className="col-sm-12">
									<div className={"form-group" + this.validateClass('tCreditPredefined')}>
										<input className="form-control"
											   type="text"
											   name="creditPredefined"
											   ref="tCreditPredefined"
											   value={data.creditPredefined}
											   onBlur={(e) => this.validateField({
												   target: {
													   id: "tCreditPredefined",
													   dataset: {'vdata': "required", 'vlength': "1,350"},
													   value: e.target.value
												   }
											   })}
											   onChange={(e) => this.handleEditContractDates(data, e, 'creditPredefined')}/>
										{this.validateMessage('tCreditPredefined') !== "" &&
											<UncontrolledTooltip placement="right" target="tCreditPredefined" delay={0}>
												{this.validateMessage("tCreditPredefined")}
											</UncontrolledTooltip>}
									</div>
								</div>
							</div>
							<div className="col-sm-3"style={{ marginTop: '30px' }}>
								<label className="col-sm-12 form-col-label">zlk Dosya Boyutu(MB) *</label>
								<div className="col-sm-12">
									<div className={"form-group" + this.validateClass('tStoragePredefined')}>
										<input className="form-control"
											   type="text"
											   name="storagePredefined"
											   ref="tStoragePredefined"
											   value={data.storagePredefined}
											   onBlur={(e) => this.validateField({
												   target: {
													   id: "tStoragePredefined",
													   dataset: {'vdata': "required", 'vlength': "1,350"},
													   value: e.target.value
												   }
											   })}
											   onChange={(e) => this.handleEditContractDates(data, e, 'storagePredefined')}/>
										{this.validateMessage('tStoragePredefined') !== "" &&
											<UncontrolledTooltip placement="right" target="tStoragePredefined" delay={0}>
												{this.validateMessage("tStoragePredefined")}
											</UncontrolledTooltip>}
									</div>
								</div>
							</div>
							<div className="col-sm-3"style={{ marginTop: '30px' }}>
								<label className="col-sm-12 form-col-label">Personel Says *</label>
								<div className="col-sm-12">
									<div className={"form-group" + this.validateClass('tEmployeePredefined')}>
										<input className="form-control"
											   type="text"
											   name="employeePredefined"
											   ref="tEmployeePredefined"
											   value={data.employeePredefined}
											   onBlur={(e) => this.validateField({
												   target: {
													   id: "tEmployeePredefined",
													   dataset: {'vdata': "required", 'vlength': "1,350"},
													   value: e.target.value
												   }
											   })}
											   onChange={(e) => this.handleEditContractDates(data, e, 'employeePredefined')}/>
										{this.validateMessage('tEmployeePredefined') !== "" &&
											<UncontrolledTooltip placement="right" target="tEmployeePredefined" delay={0}>
												{this.validateMessage("tEmployeePredefined")}
											</UncontrolledTooltip>}
									</div>
								</div>
							</div>

							{(this.props.isEntegrator || this.props.isDistributor) && (this.props.currentStartDate !== null || this.props.currentFinishDate !== null) && data.recordId === this.props.currentContractId &&
							<div className='col-sm-2'style={{ marginLeft: '80px', marginTop: '-15px' }}>
								<button
									type="button"
									title="Deiiklikleri Kaydet"
									style={{ background: '#1db954' }}
									className="btn btn-md btn-icon"
									onClick={() => {
										const hasDateConflict = this.checkForDateConflict(data);
										const hasMandatoryField = this.checkMandatoryField(data);
										if (!hasDateConflict) {
											if(!hasMandatoryField) {
												this.updateContractDates(data);
											}
										}
									}}
								>
									<i className="fas fa-check" />
								</button>
							</div>}
						</div>

						:
						<div className='col-sm-10 row'
							 style={{margin: '0'}}>
							<div className="col-sm-5">
								<div className="col-sm-12">
									<label className="col-form-label">Szleme Balang Tarihi: </label>
								</div>
								<div className="col-sm-12">
									<label className="col-form-label">{data.contractStartDate}</label>
								</div>
							</div>

							<div className="col-sm-5">
								<div className="col-sm-12">
									<label className="col-form-label">Szleme Biti Tarihi: </label>
								</div>
								<div className="col-sm-12">
									<label className="col-form-label">{data.contractFinishDate}</label>
								</div>
							</div>

							<div className="col-sm-2">
								<label className="col-sm-12 col-form-label">Otomatik Uzatma</label>
								<div className="col-sm-12">
									<label className="col-form-label">{data.automaticExtension}</label>
								</div>
							</div>

						</div>}


					<div className="col-sm-2 row" style={{margin: '0'}}>
						<div className="col-sm-8">
							<label className="col-form-label">Szleme PDF: </label>
						</div>
						<div className="col-sm-4">
							<button type="button"
									title="Szleme Grntle"
									style={{marginTop: "0px", backgroundColor: '#ec3333;'}}
									className="btn btn-md btn-icon"
									onClick={() => this.handleShow(data)}>
								<i className="fa fa fa-file-pdf-o"/>
							</button>
						</div>
						<label className="col-sm-12 col-form-label">Szleme imza durumu:</label>
						<div className="col-sm-12 form-group" >
							<Select
								clearable={false}
								className="select-control"
								name="signatureState"
								id="tSignatureState"
								value={data.signatureState}
								placeholder={ "Seiniz..."}
								onChange={(e) => this.signatureStateAlert(data,e)}
								options={signatureStateOptions}

							/>
						</div>
					</div>
				</div>
				<hr/>

				<h6 style={{marginTop: '30px', marginBottom: '10px'}}>Szlemeye Dahl Hzmetler</h6>
				<div key={this.state.serviceKey} className="col-sm-12">
					<Table
						columns={serviceColumns}
						content={serviceList}
						page={this.state.page}
						size={this.state.size}
						total={this.state.serviceList.length}
						paginationEnabled={false}
					/>
				</div>
			</div>
		)
	}

	previewServices(contract) {
		let servicesString;
		if (contract) {
			servicesString = 'K Platformu';
		} else {
			servicesString = '-';
		}
		return servicesString;
	}

	selectServiceName(serviceId) {
		if(serviceId === 7){
			return "PEYK";
		}else{
			return "";
		}
	}

	setSelectedContract(contract) {
		this.props.setSelectedContract(contract)
	}

	rateTypeAlert(data) {
		let messageText = data.rateType === 1 ? 'Kontrl' : 'Fatural';
		this.setState({
			alert: (
				<SweetAlertExt showCancel warning
							   confirmBtnText="Evet"
							   cancelBtnText="Hayr"
							   confirmBtnBsStyle="danger"
							   cancelBtnBsStyle="default"
							   title="Emin misiniz?"
							   onConfirm={() => this.switchRateType(data)}
							   onCancel={() => this.setState({alert: null})}
				>
					{'Szleme tr ' + messageText + " olarak deitirilecek."}
				</SweetAlertExt>
			)
		});
	}

	signatureStateAlert(data,e) {
		data.signatureState=e.value;
		let messageText = e.label;
		this.setState({
			alert: (
				<SweetAlertExt showCancel warning
							   confirmBtnText="Evet"
							   cancelBtnText="Hayr"
							   confirmBtnBsStyle="danger"
							   cancelBtnBsStyle="default"
							   title="Emin misiniz?"
							   onConfirm={() => this.switchSignatureState(data)}
							   onCancel={() => this.setState({alert: null})}
				>
					{'mza durumu ' + messageText + " olarak deitirilecek."}
				</SweetAlertExt>
			)
		});
	}

	switchSignatureState(data) {
		let self = this;
		let params = {
			url: `/customer/switchSignatureState`,
			method: 'post',
			data: data
		};
		requestK(params)
			.then(() => {
				Alert.success('mza durumu Deitirildi', {
					position: 'top-right',
					effect: 'stackslide',
					timeout: 5000
				});
				self.setState({
					alert: null
				});
			}).catch(function (error) {
			self.setState({alert: null});
			showAxiosError(error);
		});
	}
	switchRateType(data) {
		let self = this;
		let params = {
			url: `/customer/switchRateType`,
			method: 'post',
			data: data
		};
		requestK(params)
			.then(() => {
				Alert.success('Szleme Tr Deitirildi', {
					position: 'top-right',
					effect: 'stackslide',
					timeout: 5000
				});
				self.setState({
					alert: null
				}, () => {
					this.manipulateContractRateType(data)
				});
			}).catch(function (error) {
			self.setState({alert: null});
			showAxiosError(error);
		});
	}

	manipulateContractRateType(data) {
		this.props.manipulateContractRateType(data);
	}

	manipulateContractFinishDate(data) {
		this.props.manipulateContractFinishDate(data);
	}

	renewContract(currentContract) {
		let self = this;

		let params = {
			url: `/customer/renewContract`,
			method: 'post',
			data: currentContract
		};
		requestK(params)
			.then(() => {
				Alert.success('Szleme Sresi Bir Yl Uzatld', {
					position: 'top-right',
					effect: 'stackslide',
					timeout: 5000
				});
				self.setState({contractRenewalAlert: null}
					, () => self.props.redefineExpiringContracts(currentContract)
					//, () => this.manipulateContractRateType(data)
				);
				self.manipulateContractFinishDate(currentContract)
			}).catch(function (error) {
			self.setState({alert: null});
			showAxiosError(error);
		});
	}

	warnUserAboutRenewal(currentContract) {

		let newFinishDate = moment(currentContract.contractFinishDate).add(1, 'YEARS').format('DD-MM-YYYY');
		this.setState({
			contractRenewalAlert: (
				<SweetAlert showCancel warning
							confirmBtnText="Evet"
							cancelBtnText="Hayr"
							confirmBtnBsStyle="danger"
							cancelBtnBsStyle="default"
							title="Onaylyor musunuz ?"
							onConfirm={() => this.renewContract(currentContract)}
							onCancel={() => this.setState({contractRenewalAlert: null})}
				>
					{moment(currentContract.contractFinishDate).format('DD-MM-YYYY') + ' olan szleme bitii, ' + newFinishDate + " tarihine kadar uzatlacak."}
				</SweetAlert>
			)
		});
	}

	render() {
		let contract = this.props.customerContract;
		let warning = false;
		let terminated = false;
		let pendingTermination = false;
		let pendingActivation = false;
		let layout = {};

		if (contract.recordId) {
			if (this.props.expiringContracts) {
				this.props.expiringContracts.map(ec => {
					if (ec.recordId === contract.recordId) {
						warning = true;
						layout.border = '#dc0000';
						layout.borderStyle = 'double'
					}
				});
			}
		}

		return (
			<div>
				{this.state.alert}
				{this.state.resendAlert}
				{this.state.contractRenewalAlert}
				<Accordion>
					<AccordionItem onClick={() => this.setSelectedContract(contract)}>
						<AccordionItemTitle id={'accordion-item-title-' + contract.recordId} style={layout}>
							<div className='col-sm-12 row'>

								{terminated &&
								<Col id={'cancelled-contract-warning-' + contract.recordId} xs='12'>
									<p style={{fontSize: '15px', padding: '0 0 0 15px', fontStyle: 'italic', color: 'red'}}>
										Szleme ptal Edilmi
									</p>
								</Col>}

								{pendingTermination &&
								<Col xs='12'>
									<p style={{fontSize: '15px', padding: '0 0 0 15px', fontStyle: 'italic', color: '#ff9e00'}}>
										ptal Talebi Gnderildi, mza Bekleniyor
									</p>
								</Col>}

								<Col xs='12' id={'fake-cancel-warning-' + contract.recordId} style={{display: 'none'}}>
									<p style={{fontSize: '15px', padding: '0 0 0 15px', fontStyle: 'italic', color: '#ff9e00'}}>
										ptal Talebi Gnderildi, mza Bekleniyor
									</p>
								</Col>

								{pendingActivation &&
								<Col xs='12'>
									<p style={{fontSize: '15px', padding: '0 0 0 15px', fontStyle: 'italic', color: '#2bb738'}}>
										Aktivasyon Talebi Gnderildi, mza Bekleniyor
									</p>
								</Col>}

								<Col xs='12' id={'fake-activate-warning-' + contract.recordId} style={{display: 'none'}}>
									<p style={{fontSize: '15px', padding: '0 0 0 15px', fontStyle: 'italic', color: '#2bb738'}}>
										Aktivasyon Talebi Gnderildi, mza Bekleniyor
									</p>
								</Col>

								<div className='accordion__arrow'/>
								<div className='col-sm-5'>
									<div className='col-sm-12'>
										<label>Szleme Tarihi</label>
									</div>
									<div className='col-sm-12' style={{maxWidth: 'none'}}>
										<h6 style={{color: warning && '#dc0000'}}>({moment(contract.contractStartDate).format('DD-MM-YYYY')}) -
											({moment(contract.contractFinishDate).format('DD-MM-YYYY')})</h6>
									</div>
								</div>
								<div className='col-sm-4'>
									<div className='col-sm-12'>
										<label>Hizmetler</label>
									</div>
									<div className='col-sm-12' style={{maxWidth: 'none'}}>
										<label style={{fontWeight: 'bold'}}>{this.previewServices(contract)}</label>
									</div>
								</div>

								<div className='col-sm-3'>
									<div className='col-sm-12'>
										<label>Szleme Tr</label>
									</div>
									<div className='col-sm-12 row' style={{maxWidth: 'none'}}>
										<div className='col-sm-5'>
											<label
												style={{fontWeight: 'bold'}}>{contract.rateType === 1 ? 'Fatural' : (contract.rateType === 2 ? 'Kontrl' : '')}</label>
										</div>
										{((this.props.currentUser.entegrator || this.props.isDistributor) && this.props.currentUser.adminUser && contract.rateType !== null && !terminated) &&
										<div className='col-sm-7'>
											<MiniButton title='Szleme Trn Deitir' faIcon='fa-exchange-alt' style={{bottom: '8px'}}
														onClick={() => this.rateTypeAlert(contract)}/>
										</div>}
									</div>
								</div>
							</div>

							{warning &&
							<div className='col-sm-12 row'>
								<div className='col-sm-6 row' style={{paddingLeft: '45px'}}>
									<div className='col-sm-1' style={{padding: '0px', textAlign: 'right'}}>
										<MiniButton faIcon="fa-angle-up" style={{background: '#dc0000', borderRadius: '20px'}}
													onClick={() => this.warnUserAboutRenewal(contract)}/>
									</div>
									<div className='col-sm-5' style={{padding: '3px 0 0 5px'}}>
										<label style={{color: '#dc0000', verticalAlign: 'sub'}}>Szlemeyi 1(bir) yl uzat</label>
									</div>
								</div>
							</div>
							}


						</AccordionItemTitle>
						<AccordionItemBody id={'accordion-body-' + contract.recordId} className='accordion-body'
										   style={{background: terminated ? '#cd6e6e61' : (pendingTermination ? '#fffb5fad' : (pendingActivation ? '#7fff5fad' : ''))}}>
							<div style={{margin: '0 0 40px 0'}}>
								{this.defineBody(contract)}
							</div>
						</AccordionItemBody>
					</AccordionItem>
				</Accordion>
				{this.state.mediaModal &&
				<MediaViewer modal={this.state.mediaModal}
							 url={this.state.url}
							 title={this.state.title}
							 listener={this.setMediaViewer}
				/>
				}

			</div>
		)
	}
}

export default CustomerContractAccordion;