import React, {Component} from "react";
import {Col, Nav} from "reactstrap";
import StickButton from "../../../components/StickButton";
import {requestK} from "../../../helper/APIUtils";
import DetailedSearch from "../../../components/DetailedSearch/DetailedSearch";
import Table from "../../../components/Table/Table";
import {
    formSendTypeData,
    formTypeData,
    getJobTitles,
    renewListForPrivileges, stateCompare,
    stateData,
    statusTypeFilterData
} from "../common/PeykHelper";
import DropdownCheck from "../../../components/Table/DropdownCheck";
import TextOverFlowTooltip from "../../../components/TextOverFlowTooltip";
import moment from "moment";
import DocumentState from "../common/DocumentState";
import {checkGrantToDisplay, showAxiosError} from "../../../helper/helper";
import ApproveDocumentViewer from "../approval/helper/ApproveDocumentViewer";
import StateDetail from "../common/StateDetail";
import Alert from "react-s-alert";
import HsmErrorAlert from "../../../common/hsm/HsmErrorAlert";
import SweetAlert from "react-bootstrap-sweetalert";
import {
    FORM_SEND,
    HEALTH_FORM_SEND,
    HSM_AUTHORITY,
    ISG_FORM_SEND, KVKK_FORM_SEND,
    ZIMMET_FORM_SEND
} from "../../../common/authorities/GrantDef";
import Axios from "axios";
import HsmCertificateViewer from "../common/HsmCertificateViewer";
import ReactLoading from "react-loading";


class MultiSessionFormsPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selection: this.props.location.selections != undefined ? this.props.location.selections : [],
            content: [],
            totalIdList: [],
            pageNumber: 0,
            pageSize: 10,
            isKepOwner: false,
            loading: false,
            progressLength: 1,
            total: 0,
            searching: false,
            page: 0,
            size: 10,
            reloadCalled: false,
            manual: false,
            searchKey: "",
            formState:false,
            pageStatus: 0,
            completed: false,
            signedDocumentCount: 0,
            certError: true,
            disableButton: false,
            timerStarted: false,
            timer: 1,
            signError: false,
            isHsmOwner: true,

        }
        this.searchWithKey = this.searchWithKey.bind(this);
        this.handleSearch = this.handleSearch.bind(this);
        this.sign = this.sign.bind(this);
        this.hsmSign = this.hsmSign.bind(this);
        this.mimeSign = this.mimeSign.bind(this);
        this.kepPdfSign = this.kepPdfSign.bind(this);
        this.isHsmOwner = this.isHsmOwner.bind(this);
        this.isKepOwner = this.isKepOwner.bind(this);
        this.mailPdfSign = this.mailPdfSign.bind(this);
        this.manualPdfSign = this.manualPdfSign.bind(this);
        this.fetchData = this.fetchData.bind(this);
        this.isSelected = this.isSelected.bind(this);
        this.toggleCheckboxChange = this.toggleCheckboxChange.bind(this);
        this.previewContent = this.previewContent.bind(this);
        this.setMediaViewer = this.setMediaViewer.bind(this);
        this.checkCustomerSmsCreditAndSentMultiSession = this.checkCustomerSmsCreditAndSentMultiSession.bind(this);
        this.checkCustomerSmsCreditAndSentMultiSessionManual = this.checkCustomerSmsCreditAndSentMultiSessionManual.bind(this);
        this.signDocuments = this.signDocuments.bind(this);
        this.updateMimeInfo = this.updateMimeInfo.bind(this);
        this.signPdf = this.signPdf.bind(this);
        this.writeSignedForm = this.writeSignedForm.bind(this);
        this.updatePdfCertificates = this.updatePdfCertificates.bind(this);
        this.deleteRow = this.deleteRow.bind(this);
        this.stateMap = {
            0: {
                label: "",
            },
            10: {
                label: "letiler oluturuluyor. Ltfen bekleyiniz.",
                wait: true
            },
            20: {
                label: "Sertifika sahibi formlara yazlyor. Ltfen, sayfay terk etmeyiniz.",
                wait: true
            },
            30: {
                label: "Pdf dosyalar imzalanyor. Ltfen bekleyiniz",
            },
            // mzalanamad ise;
            40: {
                label: "Pdf dosyalar imzalanamad. Ltfen bilgileri kontrol ediniz.",
                danger: true
            },
            50: {
                label: "Pdf dosyalar imzaland."
            },
            60: {
                label: ""
            },
        };
    }

    checkCustomerSmsCreditAndSentMultiSession() {
        let self = this;
        let params = {
            url: `/form/checkCustomerSmsCreditForMultiSession`,
            method: "post",
            data: self.state.selection
        };
        requestK(params)
            .then(function (response) {
                if (self.isNotEminEczanesi()) {
                    self.sign()
                } else {
                    self.manualPdfSign()
                }
            }).catch(function (error) {
            self.setState({alert:null});
            showAxiosError(error);
        });
    }

    checkCustomerSmsCreditAndSentMultiSessionManual() {
        let self = this;
        let params = {
            url: `/form/checkCustomerSmsCreditForMultiSession`,
            method: "post",
            data: self.state.selection
        };
        requestK(params)
            .then(function (response) {
                self.manualPdfSign()
            }).catch(function (error) {
            showAxiosError(error);
        });
    }
    isNotEminEczanesi = () => {
        return !!this.props.currentUser && !!this.props.currentUser.vknTckn && this.props.currentUser.vknTckn !== "3470304270";
    }

    toggleCheckboxChange(e, row, id) {
        let self = this;
        let checked = e.target.checked;
        let items = self.state.content;
        let selection = self.state.selection;
        if (row === "all") {
            let selection = [];
            if (checked) {
                items.forEach(item => {
                    selection.push(item[id]);
                });
            }
            self.setState({selection: selection, selectedAll: checked});
        } else {
            let index = selection.indexOf(row[id]);
            if (index >= 0) {
                selection = [
                    ...selection.slice(0, index),
                    ...selection.slice(index + 1)
                ];
            } else {
                selection.push(row[id]);
            }
            self.setState({
                selection: selection,
                selectedAll: ((selection.length > 0) ? (selection.length === self.state.content.length) : false)
            });
        }
    }

    isSelected(id) {
        return (this.state.selection.indexOf(id) !== -1);
    }
    deleteRow(item) {
        let self = this;
        let params = {
            url: `/form/deleteForm`,
            method: "post",
            data: item.id
        };
        self.setState({disabledDeleteBtn: true});
        requestK(params)
            .then(function () {
                self.setState({
                    manual: true,
                    alert: null,
                    disabledDeleteBtn: false
                });
                Alert.success("Form silindi.", {
                    position: 'top-right',
                    effect: 'stackslide',
                    timeout: 5000
                });
                self.props.reload(true, "sidebar")
            }).catch(function (error) {
            showAxiosError(error);
            self.setState({disabledDeleteBtn: false});
        });
    }

    handleSearch(searchQuery) {
        this.setState({searchQuery, searching: true, page: 0},
            () => this.fetchData(this.state.accessor, this.state.direction, 0, this.state.size))
    }

    searchWithKey(e) {
        this.setState({searchKey: e.target.value, manual: true});
    }

    fetchData(accessor, direction, page, size) {
        let self = this;
        let sortStr = accessor !== undefined ? ('&sort=' + accessor + ',' + direction) : '&sort=id,desc';
        let searchQuery = !self.state.searchQuery ? '' : '&' + self.state.searchQuery;
        let url = '/form/readAllFormForMultiFormSessions?';
        if (this.props.match && this.props.match.params.id) {
            url += 'employee_id=' + self.props.match.params.id
        } else {
            url += 'employee_id=' + "";
        }
        let selectedFormSessions = self.props.location.selectedFormSessions || [];
        let params = {
            url: url + ((searchQuery !== '' && searchQuery.includes("formSendType")) ? '' : '&formSendType=')
                + '&page=' + page + '&size=' + size + searchQuery + sortStr,
            method: "post",
            data: selectedFormSessions
        };
        self.setState({loading: true, searching: true, manual: self.manual === true ? false : self.manual});
        self.props.reload(true);
        requestK(params)
            .then(function (response) {
                let total = (response.data && response.data.page) ? response.data.page.totalElements : 0;
                let content = (response.data && response.data.page) ? response.data.page.content : [];
                let totalIdList = response.data ? response.data.selectedIdList : [];
                let subFormSessionIdList = response.data ? response.data.formSessionIdList : [];
                let selection = self.state.selection.filter(formId => totalIdList.includes(formId));
                self.setState({
                    page: page,
                    size: size,
                    total,
                    content,
                    totalIdList,
                    loading: false,
                    searching: false,
                    flagForHomePage: true,
                    selection,
                    subFormSessionIdList,
                });
                self.props.reload(false);
            }).catch(function (error) {
            self.setState({loading: false, searching: false});
            self.props.reload(false);
            showAxiosError(error);
        });
    }

    componentWillMount() {
        this.setState({isKepOwner: this.props.isKepOwner, jobTitlesData: getJobTitles()});
    }

    componentDidMount() {
        let self = this;
        self.axios = Axios.create({
            baseURL: "http://127.0.0.1:9999"
        });
        window.history.pushState(null, null, document.URL);

        let params = {
            url: `/formtype/readFormTypeAllWithSablonsuz`,
            method: "get"
        };
        requestK(params)
            .then(function (response) {
                let formTypeList = response.data;
                self.setState({formTypeList});
            }).catch(function (error) {
            showAxiosError(error);
        });
        self.isHsmOwner();
        self.isKepOwner();
    }

    componentDidUpdate() {
        if (this.state.completed && this.state.signError) {
            this.setState({pageState: 40});
        }else if (this.state.timer === 1 && !this.state.timerStarted &&
            this.state.completed && this.state.signedDocumentCount === this.state.progressLength) {
            setInterval(() => {
                this.setState(prevState => ({timer: prevState.timer - 1}))
            }, 4000);
            this.setState({timerStarted: true});
        } else if (this.state.timer === 0) {
            this.setState({backToTheFuture: true});
            if (!this.state.reloadCalled) {
                this.props.reload(true, "sidebar");
                this.setState({ reloadCalled: true });
            }
        }
        if (this.state.backToTheFuture) {
            if (!this.state.reloadCalled) {
                this.props.reload(true, "sidebar");
                this.setState({ reloadCalled: true });
            }
        }
    }


    sign() {
        if (this.state.isKepOwner) {
            this.mimeSign();
        } else {
            if (this.state.isHsmOwner) {
                this.hsmSign();
            } else {
                this.mailPdfSign();
            }
        }
    }

    mimeSign() {
        let sendList = this.state.selection;
        if (sendList && sendList.length > 0) {
            this.props.history.push({
                pathname: `/${this.props.peykName}/formmimesign`,
                formSessionId: this.state.formSessionId,
                kepAccountList: this.state.kepAccountList,
                mimeTitle: this.state.mimeTitle,
                mimeBody: this.state.mimeBody
            });
        } else {
            this.showAlreadySentMessage();
        }
    }
    updateMimeInfo() {
        let self = this;
        let params = {
            url: `/signer/writeMultiSessionFormsMimeInfo`,
            method: "post",
            data: {
                formIds: this.state.selection,
                kepAccountId: self.state.kepAccountId && parseInt(self.state.kepAccountId),
            },
            timeout: 9600000
        };
        self.setState({pageStatus: 10, alert: null, disableButton: true});
        requestK(params)
            .then(function (response) {
                self.signDocuments()
            })
            .catch(function (error) {
                showAxiosError(error);
                self.setState({pageStatus: 40, disableButton: false});
            });
    }

    signDocuments() {
        this.setState({
            progressLength: this.state.selection.length,
            loading: true,
            pageStatus: 30
        });
        this.timerStatus(true);
        requestK({
            url: `/signer/hsm-sign-form-list`,
            method: "post",
            data: this.state.selection
        }).then((response) => {
            Alert.success(response.data);
            this.stateMap[60].label = response.data;
            this.setState({
                completed: true,
                pageStatus: 60
            });
            setInterval(() => {
                this.setState({backToTheFuture: true, loading: false})
            }, 4000);
        }).catch(error => {
            this.setState({
                loading: false,
                completed: true,
                pageStatus: 40,
                disableButton: false,
                alert: <HsmErrorAlert error={error} onConfirm={() => this.setState({alert: null})}/>
            });
            this.timerStatus(false);
        });
    }

    updateMailInfo() {
        let self = this;
        let params = {
            url: `/signer/writeMultiSessionFormsMimeInfo`,
            method: "post",
            data: {
                formIds: this.state.selection,
                kepAccountId: self.state.kepAccountId && parseInt(self.state.kepAccountId),
            }
        };
        self.setState({pageStatus: 10, alert: null, disableButton: true});
        requestK(params)
            .then(function (response) {
                self.updatePdfCertificates();
            }).catch(function (error) {
            showAxiosError(error);
            self.setState({pageStatus: 40, disableButton: false});
        });
    }

    timerStatus(status){
        this.props.onTimerStatus(status);
    };

    updatePdfCertificates() {
        let self = this;
        let params = {
            url: `/signer/createMultiSessionFormPdfSignedInfoList`,
            method: "post",
            data: {
                formSessionIds: self.state.subFormSessionIdList,
                hsmType: self.state.pdfCertificate.hsmType,
                certificate: self.state.pdfCertificate.value,
                certificateKeyId: self.state.pdfCertificate.keyId,
                formIdList: self.state.selection,
                pdfCertificate:  JSON.stringify(self.state.pdfCertificate)
            },
            timeout: 9600000
        };
        this.setState({pageStatus: 20, alert: null});
        this.timerStatus(true);
        requestK(params)
            .then(function (response) {
                self.setState({signedInfoList: response.data});
                self.signPdf();
            }).catch(function (error) {
            showAxiosError(error);
            self.setState({pageStatus: 40, disableButton: false});
            this.timerStatus(false);
        });
    }

    async signPdf() {
        this.timerStatus(true);
        let signedDocument = [];

        this.setState({
            progressLength: this.state.signedInfoList.length,
            pageStatus: 30
        });
        try {
            for (const element of this.state.signedInfoList) {
                this.setState({pdfName: element.pdfName});
                const response = await this.axios.post("/signature/signedinfo?hsmtype=" + element.hsmType, {
                    base64: true,
                    pin: this.state.pin,
                    signedInfo: element.signedInfo,
                    certificate: {
                        keyId: element.certificateKeyId
                    }
                });
                signedDocument.push({
                    id: element.id,
                    signature: response.data.signedDocument
                });
                await this.writeSignedForm(signedDocument);
                signedDocument = [];
                this.setState({
                    loading: false,
                    completed: true
                });

                this.timerStatus(false);
            }
        } catch (error) {
            if (error.response && error.response.status === 401) {
                this.setState({
                    alert: (
                        <SweetAlert info
                                    confirmBtnText="Tamam"
                                    confirmBtnBsStyle="warning"
                                    title="UYARI"
                                    onConfirm={() => {
                                        this.setState({alert: null});
                                        window.location = "/accounting/login";
                                    }}
                        >
                            Peyk sunucularna eriim esnasindaki bir hatadan dolayi mza sreci tamamlanamadi. Ltfen tekrar login olup kalan imzalama islemine devam ediniz.
                        </SweetAlert>
                    )
                });
                return;
            }else{
                this.setState({
                    loading: false,
                    completed: false,
                    pageState: 0,
                    disableButton: false,
                    alert: <HsmErrorAlert error={error} onConfirm={() => this.setState({alert: null})}/>
                });
                this.timerStatus(false);
            }
        }
    }

    async writeSignedForm(signedDocument) {
        this.timerStatus(true);
        try {
            let params = {
                url: `/signer/writeFormPdfSignature`,
                method: "post",
                data: signedDocument,
                timeout: 9600000
            };
            await requestK(params);
            this.setState((prevState) => ({
                signedDocumentCount: prevState.signedDocumentCount + 1
            }));
        } catch (error) {
            if (error.response && error.response.status === 401) {
                throw error;
            } else {
                showAxiosError(error);
                this.setState({pageState: 40, disableButton: false});
                this.timerStatus(false);
            }
        }
    }

    hsmSign() {
        let sendList = this.state.selection;
        if (sendList && sendList.length > 0) {
            this.updateMimeInfo();
        } else {
            this.showAlreadySentMessage();
        }
    }

    kepPdfSign() {
        this.props.history.push({
            pathname: `/${this.props.peykName}/formsign`,
            formSessionId: this.state.formSessionId,
            selectedIdList: this.state.selection
        });
    }

    mailPdfSign() {
        let sendList = this.state.selection;
        if (sendList && sendList.length > 0) {
            this.updateMailInfo()
        } else {
            this.showAlreadySentMessage();
        }
    }

    manualPdfSign() {
        let sendList = this.state.selection;
        if (sendList && sendList.length > 0) {
            this.props.history.push({
                pathname: `/${this.props.peykName}/multiSessionFormsign/mail`,
                formSessionIds: this.state.subFormSessionIdList,
                formIds: this.state.selection,
                manual:true
            });
        } else {
            this.showAlreadySentMessage();
        }
    }

    isKepOwner() {
        this.setState({isKepOwner: this.props.isKepOwner});
    }

    isHsmOwner() {
        requestK({url: 'signer/hsm/hasAccess', method: "get"})
            .then(response => this.setState({isHsmOwner: response.data}))
            .catch(() => this.setState({isHsmOwner: false}));
    }

    previewContent(row) {
        let self = this;
        let params = {
            url: `/form/previewFormContent`,
            responseType: 'arraybuffer',
            method: "post",
            data: row.id
        };
        self.setState({mediaModal: true});
        requestK(params)
            .then(function (response) {
                let blob = new Blob([response.data], {type: response.headers["content-type"]});
                let base64Data = "";
                if (blob != undefined) {
                    let reader = new FileReader();
                    reader.readAsDataURL(blob);
                    reader.onloadend = function () {
                        base64Data = reader.result;
                        self.setState({url: base64Data});
                    }
                }
            }).catch(function (error) {
            showAxiosError(error);
            self.setState({mediaModal: false, title: null});
        });
    }

    setMediaViewer() {
        this.setState({url: null, mediaModal: false});
    }

    renderState() {
        let stateMap = this.stateMap[this.state.pageStatus];

        return <div className="row" style={{marginBottom: "15px"}}>
            <Col sm='12' md='6'>
                <div style={{display: "inline-block", marginRight: "8px"}}>
                    {stateMap.wait && <ReactLoading type="balls" color="#2c2c2c" height={24} width={24}/>}
                </div>
                <div style={{
                    display: "inline-block", marginRight: "8px", position: "relative", top: "-6px",
                    color: stateMap.danger === true ? "trueColor" : "falseColor"
                }}>
                    {stateMap.label}
                </div>
            </Col>
            <Col sm='12' md='6'>
                { !this.state.isHsmOwner && this.state.subFormSessionIdList && this.state.subFormSessionIdList.length>0 && !this.state.reloadCalled &&
                <div className='explanationOfSigning' style={{lineHeight: '1.2',marginBottom: '10px'}}>
                    <i className="fas fa-exclamation-triangle" id="exclamationIcon" style={{paddingRight: '10px'}}/>
                    <span style={{opacity: '0.9', fontSize: '12px', fontWeight: '500'}}>mzalama sresince ltfen ekran kapatmaynz ve bilgisayarnzn ekran koruyucusunun kapal olduundan emin olun.</span>
                </div>}
                { !this.state.isHsmOwner && this.state.subFormSessionIdList && this.state.subFormSessionIdList.length>0 && !this.state.reloadCalled &&
                    <div className='explanationOfSigning' style={{lineHeight: '1.2'}}>
                        <i className="fas fa-exclamation-triangle" id="exclamationIcon" style={{paddingRight: '10px'}}/>
                        <span style={{opacity: '0.9', fontSize: '12px', fontWeight: '500'}}> e-imzann USB'ye takl olduunu,
                        browser helper yazlmnn altn, imzalama iin kullanlan porta (9999) eriim engeli oluturan gvenlik yazlmlarnn pasif olduunu kontrol ediniz.</span>
                    </div>}
            </Col>
        </div>
    }

    render() {
        let pageIdList = [];
        if (this.state.content && this.state.content instanceof Array) {
            this.state.content.map(e => {
                pageIdList.push(e.id);
            });
        }
        const fieldList = [

            {
                label: "alan Ad",
                accessor: "employee_name",
                type: "text",
                operator: "=",
            },
            {
                label: "alan TCKN",
                accessor: "employee_tckn",
                type: "numericText",
                operator: "=",
            },
            {
                label: "alan Soyad",
                accessor: "employee_surname",
                type: "text",
                operator: "=",
            },
            {
                label: "Durum",
                accessor: "state",
                type: "select",
                operator: "=",
                selectData: stateData(this.state.isKepOwner)
            },
            {
                label: "alan KEP Adresi",
                accessor: "employee_kepAddress",
                type: "text",
                operator: "=",
            },
            {
                label: "alan Departman",
                accessor: "employee_department_name",
                type: "text",
                operator: "=",
            },
            {
                label: "alan Durumu",
                accessor: "employee_statusType",
                type: "select",
                operator: "=",
                selectData: statusTypeFilterData()
            },
            {
                label: "alan Grevi",
                accessor: "employee_jobTitle",
                type: "select",
                operator: "=",
                selectData: this.state.jobTitlesData
            },
            {
                label: "Form Tr",
                accessor: "formTemplate_formType",
                type: "select",
                operator: "=",
                selectData: formTypeData(this.state.formTypeList)
            },
            {
                label: "Form Gnderim Tr",
                accessor: "submissionType",
                type: "select",
                operator: "=",
                selectData: formSendTypeData()
            },
            {
                label: "Form Ad",
                accessor: "formTemplate_formName",
                type: "text",
                operator: "=",
            },
            {
                label: "ablon erii",
                accessor: "formTemplate_formContent",
                type: "text",
                operator: "=",
            },
            {
                label: "Oluturulma Tarihi (Balang)",
                accessor: "min_createdAt",
                type: "datetime",
                operator: ">=",
            },
            {
                label: "Oluturulma Tarihi (Biti)",
                accessor: "max_createdAt",
                type: "datetime",
                operator: "<=",
            },
            {
                label: "Sayfa Says",
                accessor: "pageCount",
                type: "numericText",
                operator: "=",
            },
            {
                label: "Uyar Says",
                accessor: "warningCount",
                type: "numericText",
                operator: "=",
            },
        ];
        let columnList = [
            {
                Header: (this.state.content &&
                    <DropdownCheck selectionIdList={this.state.selection}
                                   totalIdList={this.state.totalIdList}
                                   pageIdList={pageIdList}
                                   onChange={e => this.setState({selection: e})}/>),
                accessor: "id",
                selectType: "checkbox",
                sortable: false,
                Cell: (row, index) => (this.state.content &&
                    <div style={{textAlign: "center"}}>
                        <input type="checkbox" checked={this.isSelected(row.id)}
                               disabled={row.state === "SENT_ERROR"}
                               onChange={(e) => this.toggleCheckboxChange(e, row, "id", index)}/></div>)
            },

            {
                Header: "Form Tr",
                accessor: "formTemplate_formType_category",
                Cell: row => (<div>{row.formType && row.formType.category}</div>)
            },
            {
                Header: "Form Ad",
                accessor: "formName",
                sortable: false
            },
            {
                Header: "Ad",
                accessor: "employee_name",
                Cell: row => (<div>{row.employee && <span>{row.employee.name}</span>}</div>)
            },
            {
                Header: "Soyad",
                accessor: "employee_surname",
                Cell: row => (<div>{row.employee && <span>{row.employee.surname}</span>}</div>)
            },
            {
                Header: "Departman",
                accessor: "employee_department",
                Cell: row => (
                    <div style={{margin: 0}}>{row.employee && row.employee.department &&
                        <TextOverFlowTooltip text={row.employee.department.name} maxLength={15}/>}</div>
                )
            },
            {
                Header: "Grev",
                accessor: "employee_jobTitle",
                Cell: row => (<div style={{margin: 0}}>{row.employee &&
                        <TextOverFlowTooltip text={row.employee.jobTitle} maxLength={15}/>}</div>
                )
            },
            {
                Header: "TCKN",
                accessor: "employee_tckn",
                Cell: row => (<div>{row.employee && <span>{row.employee.tckn}</span>}</div>)
            },
            {
                Header: "KEP Adresi",
                accessor: "employee_kepAddress",
                Cell: row => (
                    <div style={{margin: 0}}>{row.employee &&
                        <TextOverFlowTooltip text={row.employee.kepAddress} maxLength={20}/>}</div>
                )
            },
            {
                Header: "Oluturulma Tarihi",
                accessor: "createdAt",
                Cell: row => (
                    <div style={{margin: 0}}>
                        {moment(row.createdAt).isValid() ? moment(row.createdAt).format("DD-MM-YYYY HH:mm:ss") : ''}
                    </div>
                )
            },
            {
                Header: "Onay Tipi",
                accessor: "formTemplate_isAnswerable",
                Cell: row => (
                    <div style={{ margin: 0 }}>
            <span className="isAnswerable">
                {(
                    (row.conditionalApproveAllowed && row.isAnswerable && !row.declineAllowed) ? <span style={{ color: '#08c21d' }}>Cevap Zorunlu</span> :
                        (row.conditionalApproveAllowed && row.isAnswerable && row.declineAllowed) ? <span style={{ color: '#08c21d' }}>Cevap Zorunlu - Reddedilebilir</span> :
                            (!row.conditionalApproveAllowed && row.isAnswerable && !row.declineAllowed) ? <span style={{ color: '#08c21d' }}>Cevap Zorunlu</span> :
                                (!row.conditionalApproveAllowed && row.isAnswerable && row.declineAllowed) ? <span style={{ color: '#08c21d' }}>Cevap Zorunlu - Reddedilebilir</span> :
                                    (row.conditionalApproveAllowed && !row.isAnswerable && !row.declineAllowed) ? <span style={{ color: '#007acc' }}>artl Onaylanabilir</span> :
                                        (row.conditionalApproveAllowed && !row.isAnswerable && row.declineAllowed) ? <span style={{ color: '#007acc' }}>artl Onaylanabilir - Reddedilebilir</span> :
                                            (!row.conditionalApproveAllowed && !row.isAnswerable && row.declineAllowed) ? <span style={{ color: '#408f57' }}>Reddedilebilir</span> :
                                                null
                )}
            </span>
                    </div>
                )
            },
            {
                Header: "Gnderim Tr",
                accessor: "submissionType",
                Cell: row => (<div style={{margin: 0}}>
                    <span className="formSendType">
                      {row.submissionType === "AUTOMATIC" ? "Otomatik" : (row.submissionType === "PRIORITY" ? "ncelikli" : "Normal")}
                    </span>
                </div>)
            },
            {
                Header: "Uyar Says",
                accessor: "warningCount",
                Cell: row => (
                    <div style={{margin: 0}}>
                        {row.warningCount}
                    </div>
                )
            },
            {
                Header: "Durumu",
                accessor: "state",
                Cell: row => (
                    <div>
                        {!!row.error && <span
                            className="text-danger">{row.error.toString().includes("lem gerekletirilemedi.") ? "lem gerekletirilemedi." : row.error}</span>}
                        {!row.error && row.state &&
                            <span
                                className={"text-" + DocumentState[row.state].messageType}>{DocumentState[row.state].message}</span>}
                    </div>
                )
            },
            {
                Header: "lemler",
                accessor: "",
                sortable: false,
                width: "105px",
                Cell: (row, index) => (
                    <div className="btn-group" style={{padding: 0, margin: 0}}>
                        <div className="dropdown" style={{padding: 0, margin: 0}}>
                            <button className="MenuBtn2 listt btn btn-default"
                                    style={{WebkitTextFillColor: 'white'}}
                                    type="button" data-toggle="dropdown">Senz
                            </button>
                            <div className="dropdown-menu" aria-labelledby={"dropdownMenuButton_" + row.id}>

                                {!this.state.loading &&
                                    <a className="dropdown-item"
                                       onClick={() => this.previewContent(row)}>
                                        <i className="fa fa-search"/>Form Grntle
                                    </a>}

                                {row.employee && row.employee.name &&
                                    <a className="dropdown-item"
                                       onClick={() => this.setState({stateDetail: row})}>
                                        <i className="fa fa-info-circle"/>Durum Detay
                                    </a>}
                                {stateCompare(row.state, "<", "PDF_SIGNED") &&
                                    <a className="dropdown-item"
                                       onClick={() =>
                                           this.setState({
                                               alert: (
                                                   <SweetAlert showCancel warning
                                                               disabled={this.state.disabledDeleteBtn}
                                                               confirmBtnText="Evet"
                                                               cancelBtnText="Hayr"
                                                               confirmBtnBsStyle="danger"
                                                               cancelBtnBsStyle="default"
                                                               title="Emin misiniz?"
                                                               onConfirm={() => {
                                                                   this.deleteRow(row)
                                                               }}
                                                               onCancel={() => this.setState({alert: null})}>
                                                       Form silinecek.
                                                   </SweetAlert>)
                                           })
                                       }>
                                        <i className="fa fa-close"/>Sil
                                    </a>
                                }
                            </div>
                        </div>
                        {this.state.mediaModal &&
                            <ApproveDocumentViewer url={this.state.url}
                                                   modal={this.state.mediaModal}
                                                   closeModal={this.setMediaViewer}
                                                   canvas={true}
                            />}
                    </div>
                )
            }
        ];
        let fields = renewListForPrivileges(this, fieldList, "accessor", this.state.isKepOwner);
        let columns = renewListForPrivileges(this, columnList, "accessor", this.state.isKepOwner);
        return (<div className="row baseBackground">
            {this.state.alert}
            <Nav className="topbar" style={{paddingLeft: '14px', height: '53px'}}>
                <StickButton position="leftSide" className="zocial Dokuman" style={{fontSize: 'x-small'}}
                             label="Form lemleri" title="Form lemleri"
                             onClick={() => this.props.history.push({
                                 pathname: `/${this.props.peykName}/formsessionlist`})}/>
                {checkGrantToDisplay(this, FORM_SEND) &&
                    <StickButton position="leftSide" className="zocial Gonder" label="Form Seimi"
                                 title="Form Seimi"
                                 onClick={() => this.props.history.push({pathname: `/${this.props.peykName}/formsend`})}/>}
                        {!this.state.send &&
                    <>
                        {((checkGrantToDisplay(this, FORM_SEND) || checkGrantToDisplay(this, HSM_AUTHORITY)) || checkGrantToDisplay(this,ISG_FORM_SEND) || checkGrantToDisplay(this,HEALTH_FORM_SEND) || checkGrantToDisplay(this,ZIMMET_FORM_SEND) || checkGrantToDisplay(this,KVKK_FORM_SEND)) &&
                            <StickButton position="leftSide" className="zocial Onay"
                                         style={{fontSize: 'x-small', backgroundColor: 'forestgreen'}}
                                         label="mzala & Gnder" title="mzala ve Gnder"
                                         disabled={this.state.disabledBtn || (!this.state.isHsmOwner && !this.state.pin)}
                                         onClick={() => {
                                             if(!this.state.isHsmOwner )
                                                 if (this.state.pdfCertificate && this.state.pdfCertificate.hsmType &&
                                                     this.state.pdfCertificate.value && this.state.pdfCertificate.keyId)
                                                     this.setState({
                                                         alert: (
                                                             <SweetAlert showCancel warning
                                                                         confirmBtnText="Evet"
                                                                         cancelBtnText="Hayr"
                                                                         confirmBtnBsStyle="success"
                                                                         cancelBtnBsStyle="default"
                                                                         title="Emin Misiniz?"
                                                                         onConfirm={() => this.checkCustomerSmsCreditAndSentMultiSession()}
                                                                         onCancel={() => this.setState({alert: null})}
                                                             >
                                                                 Dokmanlar imzalanp Tbitak zaman damgas ile mhrlenerek gnderilecektir.
                                                             </SweetAlert>
                                                         )
                                                     });
                                                 else
                                                     this.setState({
                                                         alert: <HsmErrorAlert error={this.state.certError}
                                                                               onConfirm={() => this.setState({alert: null})}/>
                                                     });
                                             else
                                                 this.setState({
                                                     alert: (
                                                         <SweetAlert showCancel warning
                                                                     confirmBtnText="Evet"
                                                                     cancelBtnText="Hayr"
                                                                     confirmBtnBsStyle="success"
                                                                     cancelBtnBsStyle="default"
                                                                     title="Emin Misiniz?"
                                                                     onConfirm={() => this.checkCustomerSmsCreditAndSentMultiSession()}
                                                                     onCancel={() => this.setState({alert: null})}
                                                         >
                                                             Dokmanlar imzalanp Tbitak zaman damgas ile mhrlenerek gnderilecektir.
                                                         </SweetAlert>
                                                     )
                                                 });
                                         }}/>}

                    </>
                }
                <StickButton position="leftSide" className="zocial Yenile" style={{fontSize: 'x-small'}}
                             label="Sonu Yenile" title="Sonu Yenile"
                             disabled={this.state.disabledBtn}
                             onClick={() => {
                                 this.props.reload(true, "sidebar")
                             }}/>

            </Nav>
            <div className="row satirBasi">
                <div className="col-md-12">
                    <div className="card">
                        <div className="card-header">
                            <h4 className="card-title">Gnderilmemi Formlar</h4>
                        </div>
                        <div className="card-body">
                            {this.state.subFormSessionIdList && this.renderState()}
                        </div>
                        <div className="card-body">
                            <div className="">
                                <div className="" style={{marginTop: '10px', marginBottom: '0px'}}>
                                    <DetailedSearch fields={fields}
                                                    loading={this.state.searching}
                                                    onSearch={this.handleSearch}/>
                                </div>
                            </div>
                        </div>
                        <div className="card-body">
                        </div>
                        <div className="card-body">
                            <div className="row" style={{marginLeft: '10px', color: 'red'}}>
                                <div className="row" style={{marginLeft: '10px', color: 'red'}}>
                                    <span>
                                       {(this.state.selection.length !== 0 ? "Seilen satr says " + this.state.selection.length : "")}
                                    </span>
                            </div>
                            </div>

                            <Table columns={columns}
                                   content={this.state.content}
                                   loading={this.state.loading}
                                   total={this.state.total}
                                   page={this.state.page}
                                   manual={this.state.manual}
                                   size={this.state.size}
                                   options={["5", "10", "20", "50", "100"]}
                                   fetchData={this.fetchData}
                            />
                        </div>
                        <div className="card-body">
                            {!this.state.isHsmOwner && this.state.subFormSessionIdList && this.state.subFormSessionIdList.length>0 && !this.state.reloadCalled &&
                            <HsmCertificateViewer
                                manual={this.state.manual != undefined ? this.state.manual : false}
                                value={this.state.pdfCertificate}
                                onChange={(val) => this.setState({pdfCertificate: val})}
                                pinValueChange={(val) => this.setState({pin: val})}
                                certificateError={(val) => this.setState({certError: val})}
                                pdfName={this.state.pdfName ? this.state.pdfName : ""}
                                progressLength={this.state.progressLength}
                                signedDocumentCount={this.state.signedDocumentCount}
                                displayCertificateMenu={this.state.pageStatus === 0 || this.state.pageStatus === 40}
                                userId={this.props.currentUser && this.props.currentUser.id}/>}
                        </div>
                    </div>
                </div>
            </div>
            {this.state.stateDetail &&
                <StateDetail detail={this.state.stateDetail}
                             documentType={this.state.stateDetail.formType ? this.state.stateDetail.formType.category : ""}
                             toggle={() => this.setState({stateDetail: undefined})}
                />}
        </div>);

    }
}

export default MultiSessionFormsPage;