export function createDefaultOptionByType(selectedItem) {
  console.log("createDefaultOptionByType1- ", selectedItem)
  let newId = Math.random().toString(32);
  switch (selectedItem.item.name) {
    case 'radio' :
      return {
        sectionType: "radio-button-panel",
        newElement: {id: newId, type: 'radio-button', label: null, replied: false, active: false, content: null}
      };
    case 'checkbox' :
      return {
        sectionType: "checkbox-panel",
        newElement: {id: newId, type: 'checkbox', label: null, replied: false, active: false, content: null}
      };
    case 'text-input' :
      return {
        sectionType: "text-input-panel",
        newElement: {id: newId, type: 'text-input', label: null, replied: false, active: false, content: null}
      };
    case 'date' :
      return {
        sectionType: "date-panel",
        newElement: {id: newId, type: 'date', label: null, replied: false, active: false, content: null}
      };
    case 'time' :
      return {
        sectionType: "time-panel",
        newElement: {id: newId, type: 'time', label: null, replied: false, active: false, content: null}
      };
    case 'image' :
      return {
        sectionType: "image-panel",
        newElement: { id: newId, type: 'image', label: null, replied: false, active: false, content: null, image: null}
      };
    case 'dropdown' :
      return {
        sectionType: "image-panel",
        newElement: { id: newId, type: 'dropdown', label: null, replied: false, active: false, content: '', options: []}
      };
    case 'file' :
      return {
        sectionType: "file-panel",
        newElement: { id: newId, type: 'file', label: null, replied: false, active: false, content: null, file: null}
      };
  }
}

export function generateModalBody(selectedItem, addDefaultOption) {
  switch (selectedItem.item.name) {
    case 'radio':
    case 'checkbox':
      return (
        <div className='edit-modal-row'>
          <FontAwesomeIcon className='font-awesome-button' icon='plus-square' color='#72a0c7' size='2x'
                           onClick={() => addDefaultOption(selectedItem)}/>
          <label className='edit-modal-label'>Seenek Ekle</label>
        </div>
      );
    case 'text-input':
      return (
        <div className='edit-modal-row'>
          <FontAwesomeIcon className='font-awesome-button' icon='plus-square' color='#72a0c7' size='2x'
                           onClick={() => addDefaultOption(selectedItem)}/>
          <label className='edit-modal-label'>Metin Satr Ekle</label>
        </div>
      );
    case 'date':
      return (
        <div className='edit-modal-row'>
          <FontAwesomeIcon className='font-awesome-button' icon='plus-square' color='#72a0c7' size='2x'
                           onClick={() => addDefaultOption(selectedItem)}/>
          <label className='edit-modal-label'>Yeni Tarih Bilgisi Ekle</label>
        </div>
      );
    case 'image':
      return (
        <div className='edit-modal-row'>
          <FontAwesomeIcon className='font-awesome-button' icon='plus-square' color='#72a0c7' size='2x'
                           onClick={() => addDefaultOption(selectedItem)}/>
          <label className='edit-modal-label'>Yeni Fotoraf Ekle</label>
        </div>
      );
    case 'dropdown':
      return (
        <div className='edit-modal-row'>
          <FontAwesomeIcon className='font-awesome-button' icon='plus-square' color='#72a0c7' size='2x'
                           onClick={() => addDefaultOption(selectedItem)}/>
          <label className='edit-modal-label'>Yeni Seim Mens Ekle</label>
        </div>
      );
  }
}

export function generateTelephoneElements(section, sectionIndex, element, elementIndex, itemIndex, editFunctions, setRequiredElements) {
  return (
      <div key={element.id} className='text-input-element'>
        <div style={{display: 'flex', marginBottom: '5px'}}>
          {section.saved ?
              <div style={{width: '80%'}}>
                <input className='text-input-label-init' placeholder='Metin Bal'
                       onChange={(e) => editFunctions('label').onChangeLabel(itemIndex, sectionIndex, elementIndex, e)}/>
              </div>
              :
              <div style={{width: '75%'}}>
                <div style={{width: '100%'}} className='mb-2'>
                  <input className='text-input-label-init'
                         placeholder='Aklama'
                         value={element.description}
                         onChange={(e) => editFunctions('description').onChangeLabel(itemIndex, sectionIndex, elementIndex, e)}/>
                </div>
                <div style={{width: '80%'}}>
                  <input className='text-input-label-init'
                         placeholder='Metin Bal'
                         value={element.label}
                         onChange={(e) => editFunctions('label').onChangeLabel(itemIndex, sectionIndex, elementIndex, e)}/>
                </div>
              </div>
          }
        </div>
        <input type='text' className='text-input'
               value={element.content}
               onChange={(e) => editFunctions(element.type).onChangeText(itemIndex, sectionIndex, elementIndex, e)}/>
      </div>
  )
}

export function generateRadioButtonElements(section, sectionIndex, element, elementIndex, itemIndex, editFunctions, setRequired) {

  const labels = {
    1: "Kabul Ediyorum",
    2: "Kabul Etmiyorum"
  };
  const labelStyle = {
    display: 'block',
    marginTop: '9px',
  };

  const showLabelInput = elementIndex === 0;

  return (
      <div key={element.id}>
        {showLabelInput && <div key={`${element.id}-label`} className='label-area-element'>
          <input className='label-init'
                 placeholder='Metni Buraya Girebilirsiniz'
                 value={element.label}
                 onChange={(e) => editFunctions('label').onChangeLabel(itemIndex, sectionIndex, elementIndex, e)}/>
        </div>}


        {element.type === 'radio-button' &&
            <div className='radio-button-element'>
              <input type='radio' className='radio-button' checked={element.replied}
                     onChange={(e) => editFunctions(element.type).onChangeRadioState(itemIndex, sectionIndex, elementIndex, e)}/>
              <label htmlFor={`radio-${element.id}`} style={labelStyle}>{labels[element.id]} </label>
            </div>}
      </div>
  )
}

export function generateCheckBoxElements(section, sectionIndex, element, elementIndex, itemIndex, editFunctions, setRequired) {
  return (
    //ToDo: AKTIF DEGILSE KISMI EKLENECEK
    <div key={element.id}>
      {element.type === 'text-area' &&
      <div style={{padding: '5px 10px'}} className='text-area-element'>

        {/*ToDo: ADMIN SEVIYESINDE KULLANICININ KAYDEDILEN ALANI DEGISTIREBILMESI ICIN BIR KONTROL DAHA EKLENMELI... AYRICA*/}
        {/*'SECTION' FORMU DOLDURAN TARAFINDAN CEVAPLANDIYSA ADMIN'IN MUDAHELE EDEMEMESI ICIN BIR KONTROL DAHA EKLENMELI*/}
        {section.saved ?
          <textarea style={{width: '100%', minHeight: '75px'}}
                    placeholder='Metni Buraya Girebilirsiniz'
                    onChange={(e) => editFunctions(element.type).onChangeText(itemIndex, sectionIndex, elementIndex, e)}/>
          :
          // <p>{element.content}</p>
          <div style={{width: '100%'}}>
            <span style={{cursor: 'pointer', position: 'absolute', top: '1.6em', left: '11em'}}
                  onClick={(e) => setRequired(itemIndex, sectionIndex, elementIndex, e)}
                  data-toggle='title'
                  title='"Zorunlu Alan" olarak iaretle"'
            >
              {!element.required &&
              <i className="far fa-star"/>}
              {element.required &&
              <i className="fas fa-star" style={{color: 'red'}}/>}
            </span>
            <textarea style={{width: '100%', minHeight: '75px'}}
                      placeholder='Metni Buraya Girebilirsiniz'
                      value={element.content}
                      onChange={(e) => editFunctions(element.type).onChangeText(itemIndex, sectionIndex, elementIndex, e)}/>
          </div>
        }
      </div>}

      {element.type === 'checkbox' &&
      <div className='checkbox-element'>
        <input type='checkbox' className='checkbox' checked={element.replied}
               onChange={(e) => editFunctions(element.type).onChangeCheckboxState(itemIndex, sectionIndex, elementIndex, e)}/>
        {section.saved ?
          <p>{element.content ? element.content : <label>Bu alan bo braklm</label>}</p>
          :
          <input className='checkbox-input' type='text'
                 placeholder='Metni Buraya Girebilirsiniz'
                 value={element.content}
                 onChange={(e) => editFunctions('text-input').onChangeText(itemIndex, sectionIndex, elementIndex, e)}/>
        }
        {!section.saved &&
        <div style={{padding: '7px 5px'}}>
          <FontAwesomeIcon icon='trash-alt' color='#72a0c7'
                           onClick={() => editFunctions().onDeleteElement(itemIndex, sectionIndex, element.id)}/>
        </div>}

      </div>}
    </div>
  )
}

