import React, {Component} from 'react';
import DefaultModal from "../../../../components/DefaultModal";
import {Col, Form, FormGroup, Input, Label, Row} from "reactstrap";
import Select from "../../../../local_node_modules/react-select"
import {requestK} from "../../../../helper/APIUtils";
import Alert from "react-s-alert";
import {showAxiosError} from "../../../../helper/helper";

class PasswordPolicyScreen extends Component {

    constructor(props) {
        super(props);
        this.state = {
            isChecked: false,
            upperCase: false,
            lowerCase: false,
            oneNumber: false,
            oneSymbol: false,
            maxLength: 12,
            minLength: 6,
            passwordHistoryScanns: 3,
            replacementFrequency: 90,
            radioLoading: false
        };
        this.handleChange = this.handleChange.bind(this);
    }

    componentDidMount() {
      this.fetchData();
    }

    fetchData = () => {
      let self = this;
      let params = {
        url: `/companyinfo/getExistPasswordPolicy`,
        method: "post",
      };
      requestK(params)
        .then(function (response) {
          if (response.data !== null && response.data !== "") {
            self.setState({
              oneNumber: response.data.oneNumber,
              oneSymbol: response.data.oneSymbol,
              upperCase: response.data.upperCase,
              lowerCase: response.data.lowerCase,
              maxLength: response.data.maxLength,
              minLength: response.data.minLength,
              passwordHistoryScanns: response.data.passwordHistoryScanns,
              replacementFrequency: response.data.replacementFrequency
            });
          }
        }).catch(function (error) {
        showAxiosError(error);
      });
    }

  handleChange(e) {
        let {name, value} = e.target;

        if (name === 'maxPasswordlength') {
            if(value > 12 || value < 7) {
                this.setState({maxLength: 12})
            }else{
                this.setState({maxLength: value})
            }
        }else if (name === 'minPasswordLength') {
            if(value < 6 || value > 11) {
                this.setState({minLength: 6})
            }else{
                this.setState({minLength: value})
            }
        }else if (name === 'replacementFrequency') {
            if (value>0 && value<1001){
                this.setState({replacementFrequency: value});
            }else if(value===''){
                this.setState({replacementFrequency: ''});
            }else if (value<1){
                this.setState({replacementFrequency: 1});
            }else if(value>1000){
                this.setState({replacementFrequency: 1000});
            }
        }else if (name === 'passwordHistoryScanns') {
            if (value>0 && value<6){
                this.setState({passwordHistoryScanns: value});
            }else if(value===''){
                this.setState({passwordHistoryScanns: ''});
            }else if (value<1){
                this.setState({passwordHistoryScanns: 1});
            }else if (value>5){
                this.setState({passwordHistoryScanns: 5});
            }
        }

    }

    savePasswordPolicy = () => {
        let self = this;

        let obj = {
            maxLength: self.state.maxLength,
            minLength: self.state.minLength,
            passwordHistoryScanns: self.state.passwordHistoryScanns,
            replacementFrequency: self.state.replacementFrequency,
            oneNumber: self.state.oneNumber,
            oneSymbol: self.state.oneSymbol,
            upperCase: self.state.upperCase,
            lowerCase: self.state.lowerCase,
        };
        let params = {
            url: `/companyinfo/saveCompanyPasswordPolicy`,
            method: "post",
            data: JSON.stringify(obj)
        };
        requestK(params)
            .then(function (response) {
                Alert.success("ifre Politikas Oluturuldu", {
                    position: 'top-right',
                    effect: 'stackslide',
                    timeout: 5000
                });
                self.props.toggleModal(false, true);
            }).catch(function (error) {
            showAxiosError(error);
            self.setState({disabledSaveBtn: false});
        });
    }


    render() {

        return <DefaultModal disabled={this.state.btnDisabled}
                             isOpen={this.props.modal}
                             size="lg"
                             title="ifre Politikas Ekran"
                             confirmBtnText="Kaydet"
                             cancelBtnText="Kapat"
                             onConfirm={() => {this.savePasswordPolicy() }}
                             onCancel={() => this.props.toggleModal(false, true)}>
            <div style={{marginTop: '20px'}}>
                <Form>
                    <div className="FormRenk" style={{marginBottom: '10px', paddingLeft: '20px', paddingRight: '20px'}}>
                       <Row>
                            <Col sm={12}>
                                <FormGroup row className="PasswordType">
                                    <Label sm={12} style={{fontSize: '14px'}}>ifre karakter yapsn oluturunuz</Label>
                                    <Col sm={12}>
                                        <Row>
                                            <Col sm={1}>
                                                <Input type="checkbox" name="lowerCase"
                                                       style={{marginLeft: '10px', marginTop: '10px'}}
                                                       checked={this.state.lowerCase}
                                                       onChange={(e) => {
                                                           this.setState({
                                                               lowerCase: e.target.checked,
                                                           });
                                                       }}/>
                                            </Col>
                                            <Col sm={11} style={{textAlign: 'left'}}><label>En az bir kk harf gerekli</label> </Col>

                                        </Row>

                                        <Row>
                                            <Col sm={1}>
                                                <Input type="checkbox" name="upperCase"
                                                       style={{marginLeft: '10px', marginTop: '10px'}}
                                                       checked={this.state.upperCase}
                                                       onChange={(e) => {
                                                           this.setState({
                                                               upperCase: e.target.checked,
                                                           });
                                                       }}/>
                                            </Col>
                                            <Col sm={11} style={{textAlign: 'left'}}><label>En az bir byk harf gerekli</label> </Col>

                                        </Row>

                                        <Row>
                                            <Col sm={1}>
                                                <Input type="checkbox" name="oneNumber"
                                                       style={{marginLeft: '10px', marginTop: '10px'}}
                                                       checked={this.state.oneNumber}
                                                       onChange={(e) => {
                                                           this.setState({
                                                               oneNumber: e.target.checked,
                                                           });
                                                       }}/>
                                            </Col>
                                            <Col sm={11} style={{textAlign: 'left'}}><label>En az bir say gerekli</label> </Col>

                                        </Row>
                                        <Row>
                                            <Col sm={1}>
                                                <Input type="checkbox" name="oneSymbol"
                                                       style={{marginLeft: '10px', marginTop: '10px'}}
                                                       checked={this.state.oneSymbol}
                                                       onChange={(e) => {
                                                           this.setState({
                                                               oneSymbol: e.target.checked,
                                                           });
                                                       }}/>
                                            </Col>
                                            <Col sm={11} style={{textAlign: 'left'}}><label>En az bir zel karakter gerekli</label> </Col>

                                        </Row>

                                    </Col>
                                </FormGroup>
                            </Col>
                        </Row>
                        <Label>&nbsp;&nbsp;</Label>
                        <Row>
                            <Col sm={12}>
                                <FormGroup row className="PasswordType">
                                    <Label sm={12} style={{fontSize: '14px'}}>ifre uzunluunu belirleyiniz</Label>
                                    <Col sm={12}>
                                        Min. <input type="number" min="6" max={this.state.maxLength}
                                                    value={this.state.minLength}
                                                    onChange={e => {
                                                        this.handleChange({
                                                            target: {
                                                                name: 'minPasswordLength',
                                                                value: (e && e.target && e.target.value) ? e.target.value : ''
                                                            }
                                                        });
                                                    }} /> Karakter
                                       Max. <input type="number" min={this.state.minLength} max="12"
                                                   value={this.state.maxLength}
                                                   onChange={e=> {
                                                        this.handleChange({
                                                            target: {
                                                                name: 'maxPasswordlength',
                                                                value: (e && e.target && e.target.value) ? e.target.value : ''
                                                            }
                                                        });
                                                    }} />Karakter
                                    </Col>
                                </FormGroup>
                            </Col>
                        </Row>
                        <Label>&nbsp;&nbsp;</Label>
                        <Row>
                            <Col sm={12}>
                                <FormGroup row className="passwordHistoryScanns">
                                    <Col sm={12}>
                                        Son kullanlan <input sm={4} type="number" name="passwordChangePeriod"
                                                              min={1} max={5}
                                                              value={this.state.passwordHistoryScanns}
                                                              onChange={e => {
                                                                  this.handleChange({
                                                                      target: {
                                                                          name: 'passwordHistoryScanns',
                                                                          value: (e && e.target && e.target.value) ? e.target.value : ''
                                                                      }
                                                                  });
                                                              }}/> ifreden farkl olmas art eklensin <b>*</b>
                                    </Col>
                                </FormGroup>
                            </Col>
                        </Row>
                        <Label>&nbsp;&nbsp;</Label>
                        <Row>
                            <Col sm={12}>
                                <FormGroup row className="PasswordType">
                                    <Label sm={12} style={{fontSize: '14px'}}>Zorunlu ifre deitirme skl belirleyiniz <b>*</b></Label>
                                    <Col sm={12}>
                                        <input type="number" name="replacementFrequency"
                                               min={1} max={1000}
                                               value={this.state.replacementFrequency}
                                               onChange={e=> {
                                                   this.handleChange({
                                                       target: {
                                                           name: 'replacementFrequency',
                                                           value: (e && e.target && e.target.value) ? e.target.value : ''
                                                       }
                                                   });
                                               }}/> Gn
                                    </Col>
                                </FormGroup>
                            </Col>
                        </Row>
                        <Label>&nbsp;&nbsp;</Label>
                        <Label><b>**</b> Son kullanlan ifre kontroln ve ifre deitirme skln kullanmak istemiyorsanz bo brakabilrsiniz.</Label>
                    </div>
                </Form>
                {this.state.alert}
            </div>
        </DefaultModal>
    }

}

export default PasswordPolicyScreen;
