import React, {Component} from 'react';
import {Dropdown, DropdownItem, DropdownMenu, DropdownToggle, Input} from "reactstrap";
import SweetAlert from "react-bootstrap-sweetalert";

class DropdownCheck extends Component {

  constructor(props) {
    super(props);
    this.state = {
      selection: [],
      selectionTag: "",
      selectionIdList: [],
      dropdownOpen: false,
      selectionNumber: 0,
    };
    this.toggle = this.toggle.bind(this);
    this.changeSelectionList = this.changeSelectionList.bind(this);
    this.readSelectionTag = this.readSelectionTag.bind(this);
  }

  onChange = (value) => {
    this.setState({selectionTag: value});
    this.props.onChange(this.changeSelectionList(value));
  }

  getNumber = () => {
    let totalList = this.props.totalIdList;

    if (totalList && totalList.length) {
      this.setState({
        alert: (
          <SweetAlert info
                      confirmBtnText="Tamam"
                      confirmBtnBsStyle="success"
                      title="Seim yaplmasn istediiniz sayy giriniz"
                      onConfirm={() => {
                        this.setState({alert: null});
                        this.onChange("specifiedNumber");
                      }}
          >
            <div>
              <Input type="text"
                     name="selectionNumber"
                     id="selectionNumber"
                // value={this.state.selectionNumber}
                     onChange={(e) => {
                       this.handleChangeInteger(e)
                     }}/>
            </div>
          </SweetAlert>
        )
      });
    }

  }

  handleChangeInteger = (e) => {
    let {name, value} = e.target;
    var intFormat = /^[0-9]+$/;
    if (value.length > 0 && !value.match(intFormat)) {
      return;
    }
    this.setState({selectionNumber: value});
  };


  changeSelectionList(selectionTag) {
    let selection = [];
    let selectionList = this.props.selectionIdList;
    let pageList = this.props.pageIdList;
    let totalList = this.props.totalIdList;

    switch (selectionTag) {
      case "selectAll":
        if(this.props.isConditional  || this.props.isDeclinable)
          selection = [...new Set([...selectionList, ...totalList])];
        else
          selection = totalList;
        if (totalList && totalList.length) {
          this.setState({
            alert: (
              <SweetAlert info
                          confirmBtnText="Tamam"
                          confirmBtnBsStyle="success"
                          title="Tm Seildi"
                          onConfirm={() => this.setState({alert: null})}
              >
                {totalList.length + " adet seim yapld."}
              </SweetAlert>
            )
          });
        }
        break;
      case "unselectAll":
        if(this.props.isConditional || this.props.isDeclinable)
          selection = selectionList.filter(id => !totalList.includes(id));
        else
          selection = [];
        break;
      case "selectPage":
        if(this.props.isConditional  || this.props.isDeclinable)
          pageList = pageList.filter(id => totalList.includes(id));
        pageList.map(e => {
          if (!selectionList.includes(e)) {
            selectionList.push(e);
          }
        });
        selection = selectionList;
        if (pageList && pageList.length) {
          this.setState({
            alert: (
              <SweetAlert info
                          confirmBtnText="Tamam"
                          confirmBtnBsStyle="success"
                          title="Sayfadakiler Seildi"
                          onConfirm={() => this.setState({alert: null})}
              >
                {pageList.length + " adet seim yapld."}
              </SweetAlert>
            )
          });
        }
        break;
      case "unselectPage":
        if(this.props.isConditional  || this.props.isDeclinable)
          pageList = pageList.filter(id => totalList.includes(id));
        let tempList = [];
        selectionList.map(e => {
          if (!pageList.includes(e)) {
            tempList.push(e);
          }
        });
        selection = tempList;
        break;
      case "specifiedNumber":
        let size = this.state.selectionNumber;
        let tempListTwo = [];
        if(this.props.isConditional || this.props.isDeclinable)
           tempListTwo = [...new Set(selectionList)];
        if (totalList && totalList.length >= size ) {
          totalList.slice(totalList.length - size, totalList.length).map(e => {
            if (!tempListTwo.includes(e)) {
              tempListTwo.push(e);
            }
          });
          selection = tempListTwo;
          break;
        } else if (totalList && totalList.length < size) {
          selection = totalList;
          break;
        }

      default:
        break;
    }

    this.setState({selection: selection});
    return selection;
  }

  readSelectionTag() {
    let selectionTagResult = "";
    let selectionIdList = this.props.selectionIdList;
    let totalIdList = this.props.totalIdList;
    if (totalIdList instanceof Array && totalIdList.length > 0) {
      if (selectionIdList instanceof Array && selectionIdList.length === totalIdList.length) {
        selectionTagResult = "selectAll";
      } else {
        let pageList = this.props.pageIdList;
        selectionTagResult = "selectPage";
        if (pageList instanceof Array) {
          pageList.map(e => {
            if (!selectionIdList.includes(e)) {
              selectionTagResult = "";
            }
          });
        } else {
          if (!selectionIdList.includes(pageList)) {
            selectionTagResult = "";
          }
        }
      }
    }
    return selectionTagResult;
  }

  toggle() {
    this.setState(prevState => ({
      dropdownOpen: !prevState.dropdownOpen
    }));
  }

  render() {
    let selection = [];
      // (this.state.selection.length || this.props.selectionIdList.length) ?
      //   this.readSelectionTag() :
      //   this.state.selectionTag;

    return (
      <Dropdown isOpen={this.state.dropdownOpen} toggle={this.toggle}>
        <DropdownToggle caret style={{
          background: "none", color: "black", border: "1px solid #a9a9a9",
          padding: "0 0 0 0", margin: "0 0 0 0", width: "30px"
        }}>
          <input type="checkbox" defaultChecked={selection === "selectAll" || selection === "selectPage"}
                 style={{verticalAlign: "sub"}}/>
        </DropdownToggle>
        <DropdownMenu aria-labelledby={"dropdownMenuButton"}
                      style={{margin: "10px 0px 0px 97px", minWidth: "auto"}}>
          <DropdownItem onClick={() => this.onChange("selectPage")}>
            <i className={selection === "selectPage" && "fa fa-check"}/>
            Sayfadakileri Se
          </DropdownItem>
          <DropdownItem onClick={() => this.onChange("unselectPage")}>
            <i className={selection === "unselectPage" && "fa fa-check"}/>
            Sayfadakileri Kaldr
          </DropdownItem>
          <DropdownItem onClick={() => this.onChange("selectAll")}>
            <i className={selection === "selectAll" && "fa fa-check"}/>
            Tm Kaytlar Se
          </DropdownItem>
          <DropdownItem onClick={() => this.onChange("unselectAll")}>
            <i className={selection === "unselectAll" && "fa fa-check"}/>
            Tm Kaytlar Kaldr
          </DropdownItem>
          <DropdownItem onClick={() => this.getNumber()}>
            <i className={selection === "specifiedNumber" && "fa fa-check"}/>
            ... Adet Se
          </DropdownItem>
        </DropdownMenu>
        {this.state.alert}
      </Dropdown>
    );
  }
}

export default DropdownCheck;