import React, {Component} from 'react';
import {requestK} from "../../../helper/APIUtils";
import {showAxiosError} from "../../../helper/helper";
import Alert from "react-s-alert";
import SweetAlert from 'react-bootstrap-sweetalert';

class SendMail extends Component {

	constructor(props) {
		super(props);

		this.state = {
			style: 'fas fa-at',
			alert: null
		}
	}

	handleSendMail(content) {

		const confirm = () => {
			this.setState({style: 'fas fa-spinner fa-spin'})
			let self = this;
			let params = {
				url: `/user/sendNewMail`,
				method: 'post',
				data: content.recordId
			};
			requestK(params).then(function (response) {
				Alert.warning('steiniz sraya alnmtr.', {
					position: 'top-right',
					effect: 'stackslide',
					offset: 100,
					timeout: 5000
				});
				self.setState({style: 'fas fa-at', alert: null});
			}).catch(function (error) {
				self.setState({style: 'fas fa-at', alert: null})
				showAxiosError(error);
			});
		};

		this.setState({
			alert: (
				<SweetAlert showCancel warning
							confirmBtnText="Onayla"
							cancelBtnText="ptal"
							confirmBtnBsStyle="danger"
							cancelBtnBsStyle="default"
							title="Emin misiniz?"
							onConfirm={() => confirm()}
							onCancel={() => this.setState({alert: null})}
				>
					{'Giri bilgileri ' + content.email + ' adresine yeniden gnderilecek.'}
				</SweetAlert>
			)
		});
	}

	render() {
		return (
			<span>
				{this.state.alert}
				<button type="button"
						onClick={() => this.handleSendMail(this.props.item)}
						data-toggle="tooltip" data-placement="bottom" title="Tekrar e-Posta Gnder"
						style={{background: 'rgb(255, 180, 88)'}}
						rel="tooltip" className="Btn btn-sm btn-icon">
					<i class={this.state.style}></i>
				</button>
			</span>
		)
	}
}

export default SendMail;