import React, {Component} from 'react';
import {requestK} from "../../../../helper/APIUtils";
import {Col, FormGroup, Label, Modal, ModalBody, ModalHeader, Row} from "reactstrap";
import TextOverFlowTooltip from "../../../../components/TextOverFlowTooltip";
import Table from "../../../../components/Table/Table";
import moment from "moment/moment";
import Alert from "react-s-alert";

class ViewAnswerModal extends Component {

  constructor(props) {
    super(props);
    this.state = {
      displayed: false,
      content: [],
      page: 0,
      size: 5,
      loading: true
    };
  this.getDocumentAnswer = this.getDocumentAnswer.bind(this);
  }

  componentDidMount() {
    this.getDocumentAnswer(this.props.detail)
  }

  getDocumentAnswer(document) {
    let self = this;
    let formData = new FormData();
    formData.append("documentId", document.id);
    formData.append("documentType", this.props.documentType);
    let params = {
      url: `/approval/documentAnswerHistory`,
      method: "post",
      data: formData
    };
    self.setState({loading: true});
    requestK(params)
      .then(function (response) {
        if (response.data.length === 0) {
          Alert.warning("Cevap metni bulunamad.", {
            position: 'top-right',
            effect: 'stackslide',
            timeout: 5000
          });
          self.setState({loading: false})
          this.props.toggle(false);
        }else {
          self.setState({
            content: response.data,
            total: response.data.length,
            page: 0,
            size: 5,
            loading: false
          });
        }
      }).catch(function (error) {
        self.setState({loading: false})
    })

  }

  render() {
    let employee = this.props.detail && this.props.detail.employee;
    return (
      <Modal isOpen={this.props.detail} size="lg" toggle={e => this.props.toggle(e)}>
        <ModalHeader className="justify-content-center" toggle={e => this.props.toggle(e)}>
          {(employee ? (employee.name + " " + employee.surname + " ") : "") + "Cevap Metinleri"}
        </ModalHeader>
        <ModalBody>
          {(this.state.loading === true || (this.state.content && this.state.content.length > 0)) &&
          <Row style={{padding: '0px 0px', margin: '10px 15px 0px 20px'}}>
            {this.getAnswerTable()}
          </Row>}
        </ModalBody>
      </Modal>
    );
  }

  getAnswerTable() {
    let columns = [
      {
        Header: (<span style={{fontSize: '12px'}}>{"Sra"}</span>),
        accessor: "index"
      },
      {
        Header: (<span style={{fontSize: '12px', maxLength: '5px'}}>{"Ad"}</span>),
        accessor: "name"
      },
      {
        Header: (<span style={{fontSize: '12px', maxLength: '5px'}}>{"Soyad"}</span>),
        accessor: "surname"
      },
      {
        Header: (<span style={{fontSize: '12px'}}>{"Cevap Metni"}</span>),
        accessor: "answer",
        Cell: row => (
          <div style={{margin: 0, height: '380px', overflow: 'auto'}}>{row.answer}</div>
        )
      }
    ];

    return <Table columns={columns}
                  content={this.state.content}
                  loading={this.state.loading}
                  loadingText="Kaytlar getiriliyor..."
                  total={this.state.total}
                  page={this.state.page}
                  size={this.state.size}
                  options={["5", "10"]}/>;
  }
}

export default ViewAnswerModal;