import React, {Component} from 'react';
import {Col, Input, Nav, NavItem, NavLink, Row, TabContent, TabPane, UncontrolledTooltip} from "reactstrap";
import {validateComponent, validateField} from "../../../common/validation";
import Table from "../../../components/Table/Table";
import {requestK} from "../../../helper/APIUtils";
import {deepCopyObject, showAxiosError} from "../../../helper/helper";
import Select from "../../../local_node_modules/react-select"
import Alert from "react-s-alert";
import classnames from "classnames";
import emailMask from "text-mask-addons/dist/emailMask";
import MaskedInput from "react-text-mask";

class UserForm extends Component {

  constructor(props) {
    super(props);

        this.state = {
            error: {},
            mode: 'create',
            key: Math.random().toString(36),
            customerBranchList: [],
            residueBranchMap: {},
            branchMap: {},
            page: 0,
            size: 10,
            manual: false,
            loading: false,
            authRoles: [],
            selectedRole: null,
            activeTab: '1',
            employeeList: [],
            customerVknTckn: null,
            customerId: null,
            selectedCustomerVknTckn: null,
            systemDefinedRoleList: [],
            selectedUserRole: null,
            pairedEmployee: null,
            user: {
                admin: true,
                firstName: null,
                lastName: null,
                username: null,
                email: null,
                phoneNumber: null,
                roleId: null,
                branchList: [],
                kepSignatory: 1
            }
        };

    this.validateClass = this.validateClass.bind(this);
    this.validateComponent = this.validateComponent.bind(this);
    this.validateField = this.validateField.bind(this);
    this.validateMessage = this.validateMessage.bind(this);
    this.fetchBranchList = this.fetchBranchList.bind(this);
    this.toggleSelectedRow = this.toggleSelectedRow.bind(this);
    this.saveUser = this.saveUser.bind(this);
    this.fetchRoleList = this.fetchRoleList.bind(this);
    this.onRoleChange = this.onRoleChange.bind(this);
    this.clearForm = this.clearForm.bind(this);
    this.cancelProcess = this.cancelProcess.bind(this);
    this.onCheckAdmin = this.onCheckAdmin.bind(this);
    this.onKepSignatoryChange = this.onKepSignatoryChange.bind(this);
    this.toggle = this.toggle.bind(this);
    this.getEmployeeName = this.getEmployeeName.bind(this);
    this.onChangeSelectedCustomer = this.onChangeSelectedCustomer.bind(this);
    this.fetchBranchListForCEdit = this.fetchBranchListForCEdit.bind(this);
    this.onChangeEmployee = this.onChangeEmployee.bind(this);
    this.getSystemRoles = this.getSystemRoles.bind(this);
  }

  componentDidMount() {
    this.fetchRoleList();
    if (this.props.mode === 'edit') {
      let customerId = this.props.customerId ? this.props.customerId : null;
      this.getSystemRoles(customerId);
      let selectedUserRole = this.props.selectedUser.roles.length > 0 ? this.props.selectedUser.roles[0].id : "";
      this.setState({customerId, selectedUserRole});
      this.getEmployeeName();
    }

    if (this.props.selectedUser) {
      let selectedUser = deepCopyObject(this.props.selectedUser);
      let customerVknTckn = this.props.customerVknTckn ? this.props.customerVknTckn : null;
      this.setState({
        mode: this.props.mode,
        selectedRole: selectedUser.roleId && selectedUser.roleId.recordId,
        branchMap: this.props.branchMap,
        user: selectedUser,
        //selectedUserRole: this.props.selectedUser.userRole.roleId,
        customerVknTckn
      })
    }

  }

  getEmployeeName() {
    let self = this;
    if (self.props.selectedUser && self.props.selectedUser.pairedEmployeeId) {
      let params = {
        url: `/employee/readEmployeeName`,
        data: self.props.selectedUser.pairedEmployeeId,
        method: 'post'
      };
      requestK(params)
        .then(function (response) {
          let value = self.props.selectedUser.pairedEmployeeId;
          let label = response.data;
          self.setState({
            pairedEmployee: {
              value,
              label
            }
          });
        })
        .catch(function (error) {
          showAxiosError(error);
        });
    }
  };

  validateField(e) {
    let id = e.target.id;

    let rule = e.target.dataset.vdata;
    let boundary = e.target.dataset.vlength;
    let value = e.target.value;

    let result = validateField(rule, boundary, value);
    let error = this.state.error;

    error = Object.assign(error, {[id]: result});

    this.setState({error});
  }

  validateClass(id) {
    let error = this.state.error;
    return (error[id] === undefined ? "" : (error[id].valid ? " has-success" : " has-danger"));
  }

  validateMessage(id) {
    let error = this.state.error;
    return (error[id] === undefined ? '' : (error[id].message));
  }

  validateComponent() {
    let result = validateComponent(this);
    this.setState({
      error: result.error
    });
    return result
  }

  fetchBranchList() {
    let self = this;
    let params = {
      url: `/user/fetchCustomerBranches`,
      method: 'get'
    };
    requestK(params).then(function (response) {
      let customerBranchList = response.data;
      if (self.state.mode === 'create') {
        customerBranchList.map(cb => {
          cb.selected = false;
        });
        self.setState({
            customerBranchList: response.data
          }
        )
      } else {
        customerBranchList.map(cb => {
          cb.selected = Object.keys(self.state.branchMap).includes(cb.recordId.toString());
        });
        self.setState({
            customerBranchList: response.data
          }
        )
      }
    })
      .catch(function (error) {
        showAxiosError(error);
      });
  }

  fetchRoleList() {
    let authRoles = [];
    let newRole1 = {};
    newRole1.recordId = 1;
    newRole1.roleName = "ADMIN KULLANICI";
    authRoles.push(newRole1);
    let newRole2 = {};
    newRole2.recordId = 2;
    newRole2.roleName = "NORMAL KULLANICI";
    authRoles.push(newRole2);
    this.setState({authRoles})
  }

  onChangeUser(e) {
    this.setState({
      user: {
        ...this.state.user,
        [e.target.name]: e.target.value
      }
    })
  }

  onChangeUserPhoneNumber(e) {
    let {name, value} = e.target;
    if (name === "phoneNumber" && value && value.length > 0) {
      value = value.split(" ").join("");
      value = value.replace(/[{()}]/g, '').replace(/-/g, "").replace(/_/g, "");
      if (value[0] === "0") {
        value = value.substring(1, value.length);
      }
    }
    if (name === "phoneNumber" && value.length > 10) {
      value = value.substring(0, 10);
    }
    this.setState({
      user: {
        ...this.state.user,
        [name]: value
      }
    })
  }

    onChangeOtp(name, value) {
        if (value && name === "otpEnabled")
            value = true;
        if (!value && name === "otpEnabled")
            value = false;
        this.setState({
            user: {
                ...this.state.user,
                [name]: value
            }
        })
    }

  onChangeRole(e) {
    this.setState({selectedUserRole: e.value})
  }

  onChangeEmployee(e) {
    let {user} = this.state;
    let employee;
    this.setState({});
    user.pairedEmployeeId = e && e.value;
    employee = this.getEmployee(user.pairedEmployeeId);
    if (user.pairedEmployeeId) {
      user.firstName = employee.name;
      user.lastName = employee.surname;
      user.email = employee.email;
    }
    this.setState({user, pairedEmployee: e});
  }

  toggleSelectedRow(row) {
    let user = deepCopyObject(this.state.user);
    let customerBranchList = deepCopyObject(this.state.customerBranchList);
    let branchMap = deepCopyObject(this.state.branchMap);
    let residueBranchMap = deepCopyObject(this.state.residueBranchMap);
    let userBranchList = user.branchList;

    // UNSELECT ISLEMLERI
    if (row.selected) {

      // SECILI SATIR BRANCHMAP'TE VAR - YANI ONCEDEN KAYITLI BIR BRANCH...
      // userBranchList'TEN KALDIRIP, RESIDUEBRANCHMAP'E EKLEYIP, BRANCHMAP'TEN SILIYORUZ
      if (Object.keys(branchMap).includes(row.recordId.toString())) {

        userBranchList.map(ub => {
          if (ub.recordId === branchMap[row.recordId].recordId) {
            userBranchList.splice(userBranchList.indexOf(ub), 1)
          }
        });

        residueBranchMap[row.recordId] = branchMap[row.recordId];
        delete branchMap[row.recordId];

        // SECILI SATIR BRANCHMAP'TE YOK - YANI EDIT ISLEMI BASLADIKTAN SONRA YENI EKLENMIS BIR BRANCH
      } else {

        userBranchList.map(ub => {
          if (ub.branch && ub.branch === row.recordId) {
            userBranchList.splice(userBranchList.indexOf(ub), 1);

            customerBranchList.map(cb => {
              if (cb.recordId === row.recordId) {
                cb.selected = false;
              }
            })
          }
        })
      }

      // customerBranchList'te secimi kaldirilan branch'i guncelle
      customerBranchList.map(cb => {
        if (cb.recordId === row.recordId) {
          cb.selected = false;
        }
      });

      // unselect islemi sonucunda ilgili objeleri set et
      this.setState({
        customerBranchList,
        residueBranchMap,
        branchMap,
        user
      });

      // SELECT ISLEMLERI
    } else {

      // RESIDUEBRANCHMAP DOLU
      if (Object.keys(residueBranchMap).length) {
        let found = false;
        //rb direk KEY'i veriyor
        for (let rb in residueBranchMap) {

          if (row.recordId.toString() === rb) {
            found = true;
            userBranchList.push(residueBranchMap[rb])
            branchMap[rb] = residueBranchMap[rb]

            customerBranchList.map(cb => {
              if (cb.recordId.toString() === rb) {
                cb.selected = true;
              }
            });
            delete residueBranchMap[rb];
          }

        }

        // residueBranchMap dolu fakat secilen row icinde degil
        if (!found) {
          let newBranch = {};
          newBranch.branch = row.recordId;
          userBranchList.push(newBranch);
          customerBranchList.map(cb => {
            if (cb.recordId === row.recordId) {
              cb.selected = true;
            }
          })
        }

        //RESIDUEBRANCHMAP BOS
      } else {
        let newBranch = {};
        newBranch.branch = row.recordId;
        userBranchList.push(newBranch);

        customerBranchList.map(cb => {
          if (cb.recordId === row.recordId) {
            cb.selected = true;
          }
        })
      }

      // select islemi sonucunda ilgili objeleri set et
      this.setState({
        customerBranchList,
        residueBranchMap,
        branchMap,
        user
      })
    }
  }

  saveUser() {

    let validity = this.validateComponent();

    if (validity.valid && this.state.user.branchList.length > 0) {

      let user = deepCopyObject(this.state.user);
      user.admin = true;
      if (this.state.selectedUserRole == null) {
        Alert.error('Ltfen bir rol seiniz.', {
          position: 'top-right',
          effect: 'stackslide',
          timeout: 5000
        });
        return;
      }
      if(this.state.selectedUserRole!=null) {
        let roles = [{id: this.state.selectedUserRole}];
        user.roles = roles;
      }
      let customerVknTckn = deepCopyObject(this.state.customerVknTckn);
      let createUserPayload = {
        user,
        customerVknTckn
      };

      let regex = new RegExp(/[]/g);
      if (user.username.match(regex) !== null) {
        Alert.error('Kullanc ad trke karakter ieremez.', {
          position: 'top-right',
          effect: 'stackslide',
          timeout: 5000
        });
      } else {
        this.props.saveUser(createUserPayload);
        this.clearForm();
        this.cancelProcess();
      }
    } else {
      let user = deepCopyObject(this.state.user);

      if (user.branchList.length === 0) {
        Alert.error('Kullanc iin ube seiniz.', {
          position: 'top-right',
          effect: 'stackslide',
          timeout: 5000
        });
      } else {
        Alert.error('Hatal alanlar var.', {
          position: 'top-right',
          effect: 'stackslide',
          timeout: 5000
        });
      }
    }
  }

  onRoleChange(e) {
    this.setState({
      selectedRole: e.value,
      user: {
        ...this.state.user,
        roleId: e.value
      }
    })
  }

  clearForm() {
    this.setState({
      key: Math.random().toString(36),
      selectedRole: null,
      user: {
        firstName: null,
        lastName: null,
        username: null,
        email: null,
        roleId: null,
        branchList: []
      }
    })
  }

  cancelProcess() {
    this.props.cancelProcess();
  }

  onUsernameBlur(e) {
    this.validateField(e);
    if (!!this.state.user.username) {
      let payload = {
        user: deepCopyObject(this.state.user)
      };
      if (this.props.module === 'userList') {
        this.checkIfUsernameExists(payload);
      } else if (this.props.module === 'cUserList') {
        payload.customerVknTckn = deepCopyObject(this.state.customerVknTckn)
        this.checkIfUsernameExists(payload);
      }
    }
  }

  checkIfUsernameExists(value) {
    let self = this;
    const data = value;
    let params = {
      url: `/user/findByUsernameAndCustomer`,
      data: data,
      method: 'post'
    };
    requestK(params).then(function (response) {
      if (response.data && response.data.recordId !== value.user.recordId) {
        let tUserName = {
          valid: false,
          message: "Bu Kullanc Ad Zaten Mevcut"
        };
        let error = deepCopyObject(self.state.error);
        error.tUserName = tUserName;
        self.setState({
          error
        });
        Alert.error('Bu Kullanc Ad Zaten Mevcut', {
          html: true,
          position: 'top-right',
          effect: 'stackslide',
          timeout: 5000
        })
      }
    })
      .catch(function (error) {
        showAxiosError(error);
      });
  }

  onCheckAdmin() {
    let admin = !deepCopyObject(this.state.user.admin);
    if (admin) {
      let error = deepCopyObject(this.state.error);
      if (error['tRole']) {
        delete error['tRole']
      }
      this.setState({
        error,
        selectedRole: null,
        user: {
          ...this.state.user,
          roleId: 1,
          admin
        }
      })
    } else {
      this.setState({
        selectedRole: null,
        user: {
          ...this.state.user,
          roleId: null,
          admin
        }
      })
    }
  }

  onKepSignatoryChange(e) {
    this.setState({
      user: {
        ...this.state.user,
        kepSignatory: e.target.checked ? 1 : 0
      }
    })
  }

  toggle(tab) {
    this.setState({activeTab: tab});
  }

  async onChangeSelectedCustomer(e) {
    await this.fetchBranchListForC(e);
    this.setState({
      customerVknTckn: e.value,
      customerId: e.recordId,
      user: {
        admin: true,
        firstName: null,
        lastName: null,
        username: null,
        email: null,
        roleId: 1,
        branchList: []
      }
    })
    this.getSystemRoles(e.recordId);
  }

  async fetchBranchListForC(e) {
    let self = this;
    let data = e.recordId;
    let params = {
      url: `/user/fetchCustomerBranchesForC`,
      method: 'post',
      data: data
    };
    let customerBranchList = null;
    await requestK(params).then(function (response) {
      let customerBranchListResp = response.data;
      customerBranchListResp.map(cb => {
        cb.selected = false;
      });
      customerBranchList = customerBranchListResp;
      return customerBranchList;
    }).catch(function (error) {
      showAxiosError(error);
    });
    self.setState({
      customerBranchList
    })
  }

  fetchBranchListForCEdit() {
    let self = this;
    let params = {
      url: `/user/fetchCustomerBranchesForCEdit`,
      method: 'post',
      data: self.props.customerVknTckn
    };
    requestK(params).then(function (response) {
      let customerBranchList = response.data;

      customerBranchList.map(cb => {
        cb.selected = Object.keys(self.state.branchMap).includes(cb.recordId.toString());
      });
      self.setState({customerBranchList: response.data});
    }).catch(function (error) {
      showAxiosError(error);
    });
  }

  selectStyle(item) {
    let error = deepCopyObject(this.state.error);
    if (error[item] === undefined || error[item].valid) {
      return null;
    } else {
      return {backgroundColor: '#f8f8f8', borderColor: '#ffcfcf'}
    }
  }

  getEmployee(id) {
    let {employeeList} = this.state;

    for (let i = 0; i < employeeList.length; i++) {
      if (employeeList[i].id === id) {
        return employeeList[i];
      }
    }
  }

  getEmployeeIds(key, customerId) {
    if (!key) {
      return Promise.resolve({options: []});
    } else {
      if (key.length >= 2) {
        let params = {
          url: `/employee/searchEmployee?search=${key}&customerId=${customerId}`,
          method: "get"
        };
        return requestK(params)
          .then(response => {
            let employeeList = response.data;
            this.setState({employeeList});
            let options = employeeList.map(employee => ({
              value: employee.id,
              label: employee.name + " " + employee.surname + " [Tckn: " + employee.tckn + "] " + (employee.department ? "  (" + employee.department.name + " Departman)" : "")
            }));
            return {options};
          })
          .catch(function (error) {
            showAxiosError(error);
          });
      } else {
        return Promise.resolve({options: []});
      }
    }
  }

  getSystemRoles(customerId) {
    let self = this;
    let params = {
      url: `/user/systmDefinedRoles`,
      method: "post",
      data: customerId
    };
    return requestK(params)
      .then(response => {
        let systemDefinedRoleList = response.data;

        let options = systemDefinedRoleList.map(role => ({
          value: role.id,
          label: role.roleName
        }));
        self.setState({systemDefinedRoleList: options});
      })
      .catch(function (error) {
        showAxiosError(error);
      });


  }

  render() {

    const {customerBranchList} = this.state;

    const columns = [
      {
        Header: "",
        accessor: "recordId",
        sortable: false,
        Cell: (row) => (
          <input type="checkbox" checked={row.selected} onChange={(e) => this.toggleSelectedRow(row)}/>)
      },
      {
        Header: "Adres Ad",
        accessor: "branchName"
      },
      {
        Header: "le",
        accessor: "citySubdivisionName"
      },
      {
        Header: "ehir",
        accessor: "city",
      },
      {
        Header: "Telefon",
        accessor: "phone"
      },
      {
        Header: "E-posta",
        accessor: "email"
      },
      {
        Header: "Web Sitesi",
        accessor: "webSite"
      }
    ];

    return (
      <div key={this.state.key}>
        <div className='card-header'>
          <h4
            className="card-title">{this.state.mode === 'create' ? 'Yeni Kullanc Olutur' : 'Kullanc Dzenle'}</h4>
        </div>
        <div className="card-body">
          <div className="FormRenk col-sm-12">

            {(this.props.module === 'cUserList') &&
            <div className='col-sm-12 row' style={{margin: '0 0 40px -16px'}}>
              <div className="col-sm-2">
                <label className='col-form-label'>Mteri *</label>
              </div>
              <div className='col-sm-10' style={{margin: '0 0 0 -4px', padding: '0 70px 0 35px'}}>
                <Select
                  className="basic-single"
                  classNamePrefix="select"
                  disabled={this.state.mode === 'edit'}
                  placeholder="Mteri Vkn/Tckn'si veya unvan ile arama yapabilirsiniz"
                  isClearable={true}
                  isSearchable={true}
                  name="customer"
                  options={this.props.customers && this.props.customers}
                  onChange={(e) => this.onChangeSelectedCustomer(e)}
                  value={this.state.customerVknTckn}
                  noResultsText={"Mteri bulunamad"}
                />
              </div>
            </div>}

            <div className='col-sm-12 row'>
              <div className='col-sm-6 row'>
                <div className="col-sm-5">
                  <label>Ad *</label>
                </div>
                <div className="col-sm-7">
                  <div className={"form-group" + this.validateClass('tFirstName')}>
                    <input className="form-control"
                           type="text"
                           name="firstName"
                           id="tFirstName"
                           data-vlength="1,255"
                           onBlur={this.validateField}
                           ref="tFirstName"
                           value={this.state.user.firstName}
                           onChange={(e) => this.onChangeUser(e)}/>
                    {this.validateMessage('tFirstName') !== "" &&
                    <UncontrolledTooltip placement="right" target="tFirstName" delay={0}>
                      {this.validateMessage("tFirstName")}
                    </UncontrolledTooltip>}
                  </div>
                </div>
              </div>
              <div className='col-sm-6 row'>
                <div className="col-sm-5">
                  <label>Soyad *</label>
                </div>
                <div className="col-sm-7">
                  <div className={"form-group" + this.validateClass('tLastName')}>
                    <input className="form-control"
                           type="text"
                           name="lastName"
                           id="tLastName"
                           data-vlength="1,255"
                           onBlur={this.validateField}
                           ref="tLastName"
                           value={this.state.user.lastName}
                           onChange={(e) => this.onChangeUser(e)}/>
                    {this.validateMessage('tLastName') !== "" &&
                    <UncontrolledTooltip placement="right" target="tLastName" delay={0}>
                      {this.validateMessage("tLastName")}
                    </UncontrolledTooltip>}
                  </div>
                </div>
              </div>
            </div>

            <div className='col-sm-12 row'>
              <div className='col-sm-6 row'>
                <div className="col-sm-5">
                  <label>Kullanc Ad *</label>
                </div>
                <div className="col-sm-7">
                  <div className={"form-group" + this.validateClass('tUserName')}>
                    <input className="form-control"
                           disabled={!!this.state.user.employeeId}
                           type="text"
                           name="username"
                           id="tUserName"
                           data-vlength="1,20"
                           onBlur={(e) => this.onUsernameBlur(e)}
                           ref="tUserName"
                           value={this.state.user.username}
                           onChange={(e) => this.onChangeUser(e)}/>
                    {this.validateMessage('tUserName') !== "" &&
                    <UncontrolledTooltip placement="right" target="tUserName" delay={0}>
                      {this.validateMessage("tUserName")}
                    </UncontrolledTooltip>}
                  </div>
                </div>
              </div>

              <div className='col-sm-6 row'>
                <div className="col-sm-5">
                  <label>e-Posta *</label>
                </div>
                <div className="col-sm-7">
                  <div className={"form-group" + this.validateClass('tEmail')}>
                    <MaskedInput className="form-control"
                                 type="text"
                                 mask={emailMask}
                                 name="email"
                                 inputProps={{vdata: "email", vlength: '1,255'}}
                                 id="tEmail"
                                 onBlur={() => this.validateField({
                                   target: {
                                     id: "tEmail",
                                     dataset: {vdata: "email", vlength: '1,255'},
                                     value: this.state.user.email
                                   }
                                 })}
                                 placeholder="_________@______.___"
                                 ref="tEmail"
                                 value={this.state.user.email}
                                 onChange={(e) => this.onChangeUser(e)}/>
                    {this.validateMessage('tEmail') !== "" &&
                    <UncontrolledTooltip placement="right" target="tEmail" delay={0}>
                      {this.validateMessage("tEmail")}
                    </UncontrolledTooltip>}
                  </div>
                </div>
              </div>

                <div className='col-sm-6 row'>
                    <div className="col-sm-5">
                        <label>Telefon Numaras *</label>
                    </div>
                    <div className="col-sm-7">
                        <div className={"form-group" + this.validateClass('phoneNumber')}>
                            <MaskedInput className="form-control"
                                         mask={['0', '(', /[1-9]/, /\d/, /\d/, ')', ' ', /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/]}
                                         placeholder="(5xx) xxx-xxxx"
                                         type="text"
                                         id="phoneNumber"
                                         ref="phoneNumber"
                                         inputProps={{vdata:"phone", vlength:'10,10'}}
                                         onBlur={() => this.validateField({
                                             target: {
                                                 id: "phoneNumber",
                                                 dataset: {vdata: 'phone', vlength:'10,10'},
                                                 value: this.state.user.phoneNumber
                                             }
                                         })}
                                         name="phoneNumber"
                                         value={this.state.user.phoneNumber}
                                         onChange={(e) => this.onChangeUserPhoneNumber(e)}/>
                            {this.validateMessage('tSorumluCepTel') !== "" &&
                            <UncontrolledTooltip placement="right" target="phoneNumber" delay={0}>
                                {this.validateMessage("phoneNumber")}
                            </UncontrolledTooltip>}
                        </div>
                    </div>
                </div>

              {/*<div className='col-sm-6 row'>
                <div className="col-sm-5">
                  <label>OTP Kullanm :</label>
                </div>
                <div className="col-sm-7">
                  <div className={"form-group"}>
                    <Input type="checkbox" name="otpEnabled"
                           style={{marginLeft: '10px', marginTop: '10px'}}
                           checked={this.state.user.otpEnabled || ''}
                           onChange={(e) => this.onChangeOtp("otpEnabled", e.target.checked)}/>
                  </div>
                </div>
              </div>*/
              }

            </div>
            {this.state.customerVknTckn &&
            <div className='col-sm-12 row' style={{marginTop: '20px'}}>
              <div className='col-sm-12 row'>
                <div className='col-sm-3'>
                  <label className="col-form-label">Eleecek alan </label>
                </div>
                <div className='col-sm-9'>
                  <div className={"form-group"}>
                    <Select.Async multi={false}
                                  ignoreAccents={false}
                                  value={this.state.pairedEmployee}
                                  onChange={(e) => this.onChangeEmployee(e)}
                                  isLoading={true}
                                  autoload={false}
                                  cache={false}
                                  clearable={true}
                                  filterOptions={(options, filter, currentValues) => options}
                                  noResultsText="Kayt bulunamad."
                                  searchPromptText="Arama iin en az 2 karakter giriniz."
                                  placeholder="sim veya soyisim ile ara..."
                                  loadingPlaceholder="Ykleniyor..."
                                  clearValueText="Seimi kaldr"
                                  loadOptions={(e) => this.getEmployeeIds(e, this.state.customerId)}/>
                  </div>
                </div>
              </div>
            </div>}
            {this.state.customerVknTckn &&
            <div className='col-sm-12 row' style={{marginTop: '20px'}}>
              <div className='col-sm-12 row'>
                <div className='col-sm-3'>
                  <label className="col-form-label">Rol </label>
                </div>
                <div className='col-sm-9'>
                  <div className={"form-group"}>
                    <Select multi={false}
                            ignoreAccents={false}
                            value={this.state.selectedUserRole}
                            onChange={(e) => this.onChangeRole(e)}
                            clearable={false}
                            noResultsText="Kayt bulunamad."
                            placeholder="Rol ismi ile ara..."
                            loadingPlaceholder="Ykleniyor..."
                            clearValueText="Seimi kaldr"
                            options={this.state.systemDefinedRoleList}/>
                  </div>
                </div>
              </div>
            </div>}
          </div>
        </div>
        <div className="col-sm-12">
          <Nav pills className="nav-pills-primary ">
            <NavItem>
              <NavLink
                className={classnames({active: this.state.activeTab === '1'})}
                onClick={() => {
                  this.toggle('1');
                }}
              >
                ube Bilgileri
              </NavLink>
            </NavItem>
          </Nav>
        </div>

        <div className="col-sm-12">
          <TabContent activeTab={this.state.activeTab}>
            <TabPane tabId="1">
              <Row>
                <Col sm="12">
                  <div className='card-header'>
                    <h6 className='card-title'
                        style={{marginBottom: '5px', marginTop: '20px'}}>Kullanc n
                      ube(ler) senz</h6>
                  </div>
                  <div className='col-sm-12'>
                    <Table columns={columns}
                           loading={this.state.loading}
                           loadingText="Kaytlar getiriliyor..."
                           content={customerBranchList}
                           total={customerBranchList.length}
                           page={this.state.page}
                           manual={this.state.manual}
                           size={this.state.size}
                           options={["5", "10", "20", "50"]}
                           fetchData={this.props.module === 'userList' ? this.fetchBranchList : (this.props.mode === 'edit' ? this.fetchBranchListForCEdit : null)}
                    />
                  </div>
                </Col>
              </Row>
            </TabPane>
          </TabContent>
        </div>

        <div className="modal-footer">
          <div className='col-sm-12 row'>
            <div className='col-sm-2'>
              <button type="button" data-toggle="tooltip" data-placement="bottom"
                      style={{background: '#9e9e9e', padding: '10px 54px 10px 23px'}}
                      rel="tooltip" className="TekBtn vazgec "
                      onClick={this.cancelProcess}>
                ptal
              </button>
            </div>
            <div className="col-sm-8"></div>
            <div className='col-sm-2'>
              <button type="button" data-toggle="tooltip" data-placement="bottom"
                      style={{
                        background: 'linear-gradient(rgba(159, 208, 55, 0.6), #9fd037)',
                        float: 'right', padding: '10px 54px 10px 23px'
                      }}
                      rel="tooltip" className="TekBtn kaydet"
                      onClick={() => this.saveUser()}>{this.state.mode === 'create' ? 'Onayla' : 'Kaydet'}
              </button>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default UserForm;