import React, {Component} from 'react';
import SweetAlert from 'react-bootstrap-sweetalert';
import {Button} from 'reactstrap';
import {requestK} from "../../helper/APIUtils";
import {showAxiosError} from "../../helper/helper";


class HsmErrorAlert extends Component {

  constructor(props) {
    super(props);
  }

  renderNotWorking() {
    return (
      <div className="container">

        <div className="row">
          <div className="col-sm-12" style={{fontSize : window.innerWidth < 768 ? '13px' : 'initial'}}>
            Yardmc uygulamaya ulalamad. Uygulamay kurduktan sonra masastndeki bu
            (<img style={{height: '30px'}} src={process.env.REACT_APP_CONTEXT_NAME + "/image/bhelper.png"}/>)
            ikona tklayarak altrnz ve imzalama ileminize devam ediniz.
          </div>
        </div>

        <div className="row">
          <div className="col-sm-2"/>

          <div className="col-sm-8" style={{paddingTop: window.innerWidth < 768 ? '10px' :'30px'}}>
            <Button style={{margin: "0 0 0.3em 0", lineHeight: "0.8em"}}
                    onClick={this.downloadBrowserHelper}>ndir</Button>
          </div>

          <div className="col-sm-2"/>
        </div>
        {/*<div className="row">*/}
        {/*<div className="col-sm-8">*/}
        {/*Windows x64*/}
        {/*</div>*/}
        {/*<div className="col-sm-4">*/}
        {/*<a href={generateURL(`${API_BASE_URL}/signature/helper/browserHelper/download?os=win32_x64`)}>*/}
        {/*<Button style={{margin: "0 0 0.3em 0", lineHeight: "0.8em"}}>ndir</Button>*/}
        {/*</a>*/}
        {/*</div>*/}
        {/*</div>*/}
      </div>
    )
  }

  renderErrorContent() {
    let error = this.props.error;

    return <span>{error.response.data.error.errorMessage}</span>
  }

  render() {

    let error = this.props.error;

    return error && <SweetAlert danger
                                confirmBtnText="Tamam"
                                confirmBtnBsStyle="danger"
                                title="Hata"
                                onConfirm={this.props.onConfirm}>

      {!error.response && this.renderNotWorking()}
      {error.response && this.renderErrorContent()}

    </SweetAlert>
  }

  downloadBrowserHelper = () => {
    requestK({
      url: `/signature/helper/browserHelper/download?os=win32_x86`,
      responseType: 'arraybuffer',
      method: "post",
    }).then(response => {
      let blob = new Blob([response.data], {type: response.headers["content-type"]});
      const link = document.createElement('a');
      link.href = window.URL.createObjectURL(blob);
      let contentDisposition = response.headers["content-disposition"];
      let fileName = `BrowserHelper.msi`;
      if(contentDisposition!=null && contentDisposition.includes("filename")){
        fileName = contentDisposition.split("filename=")[1]
      }
      link.download = fileName;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    }).catch(error => {
      showAxiosError(error);
    });
  };
}

export default HsmErrorAlert;
