import { ACCESS_TOKEN, KEP_IK_API_BASE_URL, PUBLIC_BASE_URL, SERVLET_PATH } from '../constants';
import * as axios from "axios";

import jwtDecode from "jwt-decode";
import forge from "node-forge";
import Swal from "sweetalert2";

export function getIdToken() {
    const idToken = sessionStorage.getItem(ACCESS_TOKEN);
    if (!idToken) {
        console.log("ERROR: no id token found!");
    }
    return idToken;
}

export function isTokenExpired() {
    let token = getIdToken();
    if (token) {
        let decoded = jwtDecode(token);
        let current_time = Date.now() / 1000;
        return decoded.exp < current_time;
    } else {
        return true;
    }
}

export function isAuthenticated() {
    if (isTokenExpired()) {
        sessionStorage.clear();
        localStorage.clear();
        return false;
    }
    return true;
}

export function requestK(options, headers) {
    let defaultHeaders = {
        'Content-Type': 'application/json;charset=UTF-8',
        'Source': 'WebApp'
    };

    defaultHeaders = Object.assign(defaultHeaders, headers);

    if (sessionStorage.getItem(ACCESS_TOKEN) && !isAuthenticated()) {
        window.location = SERVLET_PATH + "/login";
    }

    if (sessionStorage.getItem(ACCESS_TOKEN)) {
        defaultHeaders['Authorization'] = 'Bearer ' + sessionStorage.getItem(ACCESS_TOKEN);
    }

    const defaults = {headers: defaultHeaders, baseURL: KEP_IK_API_BASE_URL};
    options = Object.assign({}, defaults, options);

    return axios(options).catch(function (error) {
        if (error.response.status === 401 && sessionStorage.getItem(ACCESS_TOKEN) !== null) {
            sessionStorage.removeItem(ACCESS_TOKEN);
            Swal.fire({
                title: 'Uyar!',
                text: "Ltfen ilem yapmak iin tekrar giri yapnz!",
                icon: 'warning',
                confirmButtonColor: '#3085d6',
                confirmButtonText: 'Tamam'
            }).then((result) => {
                window.location = "/accounting/login";
            });
        } else {
            throw error;
        }
    });

}

export function requestP(options, headers) {
    let defaultHeaders = {
        'Content-Type': 'application/json;charset=UTF-8'
    };

    defaultHeaders = Object.assign(defaultHeaders, headers);

    const defaults = {headers: defaultHeaders, baseURL: PUBLIC_BASE_URL};
    options = Object.assign({}, defaults, options);

    return axios(options);
}

export function login(loginRequest) {
    return requestK({
        url: "/auth/signin",
        method: 'post',
        data: loginRequest
    });
}

export function getCurrentUser() {
    if (!sessionStorage.getItem(ACCESS_TOKEN)) {
        return Promise.reject("No access token set.");
    }

    return requestK({
        url: "/user/me",
        method: 'get'
    });

}

const pub_key = `-----BEGIN PUBLIC KEY-----\n${process.env.REACT_APP_PUBLIC_KEY}\n-----END PUBLIC KEY-----`;


export function encrypt(data) {
    const publicKey = forge.pki.publicKeyFromPem(pub_key);
    const encrypted = publicKey.encrypt(data, "RSAES-PKCS1-V1_5");
    return forge.util.encode64(encrypted);
}
