import React, {Component} from 'react';
import GenericConfiguration from "./GenericConfiguration";

class MailConfiguration extends Component {
    constructor(props){
        super(props);
        this.state={
            error:{}
        };
    }

    render(){
        const configs = [
            {
                label: "Username :",
                accessor: "userName",
                type: "text"
            },
            {
                label: "Password :",
                accessor: "password",
                type: "text"
            },
            {
                label: "Protocol :",
                accessor: "protocol",
                type: "text"
            },
            {
                label: "Host :",
                accessor: "host",
                type: "text"
            },
            {
                label: "Port :",
                accessor: "port",
                type: "text"
            },
            {
                label: "Encrytion :",
                accessor: "encryption",
                type: "text"
            }
        ];

        let {error, conf} = this.state;
        return <GenericConfiguration
            configs={configs}
            data={this.props.conf}
            onChange={(conf) => this.props.onChange(conf)}
            error={error}/>;
    }
}

export default MailConfiguration;