import React, {Component} from 'react';
import {Col, FormGroup, Label} from "reactstrap";
import DefaultModal from "./DefaultModal";
import {requestK} from "../helper/APIUtils";
import {ACCESS_TOKEN} from "../constants";
import {showAxiosError} from "../helper/helper";
import Alert from "react-s-alert";


class SwitchCompanyModal extends Component {

  constructor(props) {
    super(props);
    this.state = {};

    this.handleChange = this.handleChange.bind(this);
  }

  impersonate = () => {
    let recordId = this.state.recordId;
    if (recordId) {
      this.setState({disabledLoginBtn: true});
      let self = this;
      let params = {
        url: '/user/switchUser',
        method: "post",
        data: {
          recordId: parseInt(recordId)
        }
      };
      requestK(params)
        .then(function (response) {
          let data = response.data;
          sessionStorage.setItem(ACCESS_TOKEN, data.accessToken);
          self.props.onLogin();
          self.setState({disabledLoginBtn: false});
        }).catch(function (error) {
        showAxiosError(error);
        self.setState({disabledLoginBtn: false});
      });
    } else {
      Alert.error("Firma seimi yapmalsnz.", {
        position: 'top-right',
        effect: 'stackslide',
        timeout: 1500
      });
    }
  };

  handleChange (event) {
    // let switchCompany = document.getElementById('switchCompany').value;
    this.setState({company: event.target.label, recordId: event.target.value})
  }

  render() {
    return (
      <DefaultModal title="Bal olunan firma listesi"
                    size="md"
                    disabled={this.state.disabledLoginBtn}
                    isOpen={this.props.isOpen}
                    confirmBtnText="Giri Yap"
                    cancelBtnText="Kapat"
                    onConfirm={this.impersonate}
                    onCancel={() => {
                      this.props.close();
                      this.setState({
                        disabledLoginBtn: false,
                        company: null,
                        recordId: null
                      });
                    }}
      >
        <FormGroup row>
          <Label sm={4} for="batchEnabled">Firma: </Label>
          <Col sm={8}>
            <select
              type='select'
              id= 'switchCompany'
              clearable={false}
              value={this.state.company}
              placeholder="Firma Seiniz..."
              onChange={this.handleChange}
              style={{width: '100%', height: '30px', fontSize: '12px'}}
              //onChange={(e) => (e && e.value) && this.setState({company: e, recordId: e.value})}
              //options={this.props.companyList}
            >
              <option selected='selected' disabled hidden>Firma Seiniz...</option>
              {this.props.companyList.map((comp) => <option value={comp.value}>{comp.label}</option> )}
            </select>
          </Col>
        </FormGroup>
      </DefaultModal>
    )
  }
}

export default SwitchCompanyModal;