import React, {Component} from 'react';
import * as content from '../constants/exportcontent'
import {deepCopyObject} from "../helper/helper";
import Datetime from "react-datetime";
import moment from "moment";

class ExcelCustomizer extends Component {

  constructor(props) {
    super(props);

    this.state = {content: [], customData: []};
  }

  componentDidMount() {
    let modifiedContent = null;
    if (this.props.type === 'customer') {
      modifiedContent = content.customerContent;
    }

    if (modifiedContent) {
      modifiedContent.map(mc => mc.selected = true);
      this.setState({
        content: modifiedContent,
        customData: modifiedContent
      })
    }
  }

  handleSelect(data) {
    let content = deepCopyObject(this.state.content);
    let customData = deepCopyObject(this.state.customData);
    let column = {};

    content.find(function (obj) {
      return obj.name === data.name
    }).selected = !content.find(function (obj) {
      return obj.name === data.name
    }).selected;

    column.name = data.name;
    column.accessor = data.accessor;
    column.type = data.type;
    let obj = customData.find(function (obj) {
      return obj.name === data.name;
    });
    if (!obj) {
      customData.push(column);
      this.setState({customData, content});
    } else {
      let index = customData.indexOf(obj);
      customData.splice(index, 1);
      this.setState({customData, content})
    }
  }

  selectAll() {
    this.props.handleSelectAllDocuments();
  }

  onChangeDate(e, field) {
    this.props.onChangeDate(e, field)
  }

  defineTarget(type) {
    let target = {};
    if (type === 'customer') {
      target.targetName = '';
      target.targetId = 'vknTckn';
      target.targetTitle = 'formattedName';
    }
    return target;
  }

  defineValue(type) {
    let value;
    if (type === 'customer') {
      value = this.props.vknTckn;
    }
    return value;
  }

  render() {
    const documentType = this.props.type;
    return (
      <div>

        <div className='col-sm-12 row'>
          {documentType !== 'customer' &&
          <div className='col-sm-6' style={{padding: '0', borderRightStyle: 'inset', borderRightWidth: 'thin'}}>

            <div className="card-header">
              <h4 className="card-title">Kolonlar</h4>
            </div>

            <ul>
              {this.state.content.map(content =>
                <li>
                  <input type="checkbox"
                         checked={content.selected}
                         onClick={() => this.handleSelect({
                           name: content.name,
                           accessor: content.accessor,
                           type: content.type
                         })}
                         style={{marginRight: '10px'}}/>
                  {content.name}
                </li>
              )}
            </ul>
          </div>}

          <div className={documentType === 'customer' ? 'col-sm-12' : 'col-sm-6'} style={{padding: '0'}}>

            <div className="card-header">
            </div>

            <div className='col-sm-12'>
              <input type='checkbox' checked={this.props.isAll} onClick={() => this.selectAll()}/>
              <label className='col-form-label' style={{
                margin: '0 0 5px 5px',
                fontWeight: 'bold'
              }}>{documentType === 'customer' ? "Tmn Excel'e Aktar" : "Tm Belgeleri Excel'e Aktar"}</label>
            </div>

            {documentType !== 'customer' &&
            <div className='col-sm-12 row' style={{padding: '0', margin: '10px 0 0 0'}}>
              <div className='col-sm-12' style={{padding: '0 0 0 20px', margin: '0 0 5px 0'}}>
                <label className='col-form-label' style={{fontWeight: 'bold'}}>Tarih Aralna Gre Aktar: </label>
              </div>

              <div className='col-sm-12 row' style={{margin: '0px'}}>
                <div className='col-sm-6' style={{padding: '0 0 0 10px'}}>
                  <label className='col-form-label'>Balang</label>
                  <Datetime locale="tr"
                            className="datepicker"
                            input={true}
                            inputProps={{
                              disabled: this.props.isAll,
                              id: 'tStartDate',
                            }}
                            closeOnSelect={true}
                            value={moment(this.props.startDate, "YYYY-MM-DD", true).isValid() ? moment(this.props.startDate).format("DD-MM-YYYY") : this.props.startDate}
                            dateFormat="DD-MM-YYYY"
                            ref="tStartDate"
                            timeFormat={false}
                            onChange={(e) => this.onChangeDate(e, 'startDate')}/>
                </div>
                <div className='col-sm-6' style={{padding: '0 0 0 10px'}}>
                  <label className='col-form-label'>Biti</label>
                  <Datetime locale="tr"
                            className="datepicker"
                            input={true}
                            inputProps={{
                              disabled: this.props.isAll,
                              id: 'tFinishDate',
                            }}
                            closeOnSelect={true}
                            value={moment(this.props.finishDate, "YYYY-MM-DD", true).isValid() ? moment(this.props.finishDate).format("DD-MM-YYYY") : this.props.finishDate}
                            dateFormat="DD-MM-YYYY"
                            ref="tFinishDate"
                            timeFormat={false}
                            onChange={(e) => this.onChangeDate(e, 'finishDate')}/>
                </div>
              </div>
            </div>}

            <div className='col-sm-12 row' style={{padding: '0', margin: '10px 0 0 0'}}>
              <div className='col-sm-12' style={{margin: '0 0 15px 0'}}>
                <h4 className='card-header'>zelletir</h4>
              </div>

              <div className='col-sm-12 row' style={{padding: '0px', margin: '0 0 15px 15px'}}>
                <div className='col-sm-4' style={{padding: '0px'}}>
                  <label className='col-form-label'>{this.defineTarget(this.props.type).targetName} VKN/TCKN</label>
                </div>
                <div className='col-sm-7' style={{padding: '0 0 0 0', margin: '0 0 0 15px'}}>
                  <input className="form-control"
                         type="text"
                         disabled={documentType === 'customer' && this.props.isAll}
                         value={this.defineValue(this.props.type)}
                         onChange={(e) => this.props.filterResults(e, this.defineTarget(this.props.type).targetId)}/>
                </div>
              </div>

              <div className='col-sm-12 row' style={{padding: '0px', margin: '0 0 15px 15px'}}>
                <div className='col-sm-4'>
                  <label className='col-form-label'>{this.defineTarget(this.props.type).targetName} Unvan</label>
                </div>
                <div className='col-sm-7' style={{padding: '0 0 0 0', margin: '0 0 0 15px'}}>
                  <input className="form-control"
                         type="text"
                         disabled={documentType === 'customer' && this.props.isAll}
                         value={this.defineValue(this.props.value)}
                         onChange={(e) => this.props.filterResults(e, this.defineTarget(this.props.type).targetTitle)}/>
                </div>
              </div>

              {documentType === 'customer' &&
              <div className='col-sm-12 row' style={{padding: '0px', margin: '0 0 15px 15px'}}>
                <div className='col-sm-4'>
                  <label className='col-form-label'>Ad</label>
                </div>
                <div className='col-sm-7' style={{padding: '0 0 0 0', margin: '0 0 0 15px'}}>
                  <input className="form-control"
                         type="text"
                         disabled={this.props.isAll}
                         value={this.props.givenName}
                         onChange={(e) => this.props.filterResults(e, 'givenName')}/>
                </div>
              </div>}

              {documentType === 'customer' &&
              <div className='col-sm-12 row' style={{padding: '0px', margin: '0 0 15px 15px'}}>
                <div className='col-sm-4'>
                  <label className='col-form-label'>Soyad</label>
                </div>
                <div className='col-sm-7' style={{padding: '0 0 0 0', margin: '0 0 0 15px'}}>
                  <input className="form-control"
                         type="text"
                         disabled={this.props.isAll}
                         value={this.props.familyName}
                         onChange={(e) => this.props.filterResults(e, 'familyName')}/>
                </div>
              </div>}
            </div>
          </div>
        </div>

        <div className='col-sm-12'>
          <button type="button" data-toggle="tooltip" data-placement="bottom"
                  style={{
                    background: 'linear-gradient(rgba(159, 208, 55, 0.6), #9fd037)',
                    float: 'right',
                    margin: '20px 15px 30px 0'
                  }}
                  rel="tooltip" className="TekBtn ekle btn-icon"
                  onClick={() => this.props.confirmCustomHeaders(this.state.customData)}>Onayla
          </button>
        </div>
      </div>
    )
  }
}

export default ExcelCustomizer;