import React, {Component} from 'react';
import {Modal, ModalBody, ModalFooter, ModalHeader} from "reactstrap";
import Table from "./Table/Table";
import {requestK} from "../helper/APIUtils";
import {create_UUID, showAxiosError} from "../helper/helper";
import moment from "moment";
import TextOverFlowTooltip from "./TextOverFlowTooltip";
import Alert from "react-s-alert";

class DownloadRequestModal extends Component {

    constructor(props) {
        super(props);

        this.state = {
            loading: false,
            searching: false,
            total: 0,
            page: 0,
            size: 10
        };

        this.fetchDownloadRequests = this.fetchDownloadRequests.bind(this);
    }

    fetchDownloadRequests(accessor, direction, page, size) {
        let self = this;
        let sortStr = !!accessor ? ('&sort=' + accessor + ',' + direction) : '&sort=id,desc';
        let params = {
            url: '/download-request/downloadRequests?page=' + page + '&size=' + size + sortStr,
            method: "get",
        };
        self.setState({loading: true, searching: true});
        requestK(params).then(function (response) {
            self.setState({
                downloadRequests: response.data.content,
                page: page,
                size: size,
                total: response.data.totalElements,
                loading: false,
                searching: false
            })
        }).catch(function (error) {
            self.setState({buttonStyle: null, loading: false, searching: false});
            showAxiosError(error);
        });
    }

    downloadFile(row) {
        let uuid = row.uuid;
        let fileName = row.fileName;

        let params = {
            url: '/download-request/downloadFile',
            responseType: 'arraybuffer',
            method: "post",
            data: uuid
        };

        requestK(params).then(function (response) {
            if (row.documentType === "excel") {
                let blob = new Blob([response.data], {type: response.headers["content-type"]});
                let urlCreator = window.URL || window.webkitURL;
                let url = urlCreator.createObjectURL(blob);
                let link = document.createElement("a");
                document.body.appendChild(link);
                link.style = "display: none";
                link.href = url;
                link.download = fileName + ".xlsx";
                link.click();
            } else if (row.documentType === "pdf") {
                let link = document.createElement('a');
                link.href = window.URL.createObjectURL(new Blob([response.data], {type: "application/pdf"}));
                link.download = fileName + ".pdf";
                document.body.appendChild(link);
                link.click();
            } else if (row.documentType === "zip") {
                let link = document.createElement('a');
                link.href = window.URL.createObjectURL(new Blob([response.data], {type: "application/zip"}));
                link.download = fileName + ".zip";
                document.body.appendChild(link);
                link.click();
            }
        }).catch(function (error) {
            showAxiosError(error);
        });
    }

    render() {

        const Selection = {
            type: "number",
            enum: {
                DOCUMENT_PREPARING: 0,
                DOCUMENT_DOWNLOADABLE: 1,
                DOCUMENT_DOWNLOADABLE_PREVIOUSLY_DOWNLOADED: 2,
                EMPLOYEES_LOADING: 3,
                EMPLOYEES_NOT_ADDED: 4,
                EMPLOYEES_SUCCESSFULLY_LOADED: 5,
                EMPLOYEES_NOT_ADDED_PREVIOUSLY_DOWNLOADED: 6,
                EMPLOYEE_LOADING_TIMED_OUT: 7,
                PROCESS_DELAY_NOTIFICATION_SENT: 8
            }
        };

        const columns = [
            {
                Header: "Bekleyen Dkman",
                accessor: "fileName",
                sortable: false,
                Cell: row => (
                    <div>
                        {row.fileName ? row.fileName : "-"}
                    </div>
                )
            },
            {
                Header: "Belge Tr",
                accessor: "documentType",
                sortable: false,
                Cell: row => (
                    <div>
                        {row.documentType ? row.documentType : "-"}
                    </div>
                )
            },
            {
                Header: "Balama Tarihi",
                accessor: "createdAt",
                sortable: false,
                Cell: row => (
                    <div>
                        {row.createdAt ? moment(row.createdAt.toString().replace("T", " ")).format("DD-MM-YYYY HH:mm:ss") : "-"}
                    </div>
                )
            },
            {
                Header: "Biti Tarihi",
                accessor: "completedAt",
                sortable: false,
                Cell: row => (
                    <div>
                        {row.completedAt ? moment(row.completedAt.toString().replace("T", " ")).format("DD-MM-YYYY HH:mm:ss") : "-"}
                    </div>
                )
            },
            {
                Header: "Hata Durumu",
                accessor: "errorMessage",
                sortable: false,
                Cell: row => (
                    <div>
                        {row.cancelled && row.cancelled === 1 ?
                            <label style={{color: "red"}}><TextOverFlowTooltip
                                text={"Hata  " + row.errorMessage} maxLength={6}/></label> : row.cancelled === 2 ?
                                <label style={{color: "orange"}}><TextOverFlowTooltip
                                    text={"Uyar " + row.errorMessage} maxLength={6}/></label> : ""}
                    </div>
                )
            },
            {
                Header: "Durum",
                accessor: '',
                sortable: false,
                Cell: row => (
                    row.cancelled == null ?
                        <label>{(row.state === Selection.enum.DOCUMENT_PREPARING ? 'Belge Hazrlanyor' :
                            (row.state === Selection.enum.DOCUMENT_DOWNLOADABLE ? 'Belge ndirilebilir' :
                                (row.state === Selection.enum.DOCUMENT_DOWNLOADABLE_PREVIOUSLY_DOWNLOADED ? 'Belge ndirilebilir / (Daha nce indirildi)' :
                                    (row.state === Selection.enum.EMPLOYEES_LOADING ? 'leminiz devam ediyor.' :
                                        (row.state === Selection.enum.EMPLOYEES_NOT_ADDED ? 'Eklenemeyen alanlar var!' :
                                            (row.state === Selection.enum.EMPLOYEES_SUCCESSFULLY_LOADED ? 'leminiz baarl bir ekilde gerekleti.' :
                                                (row.state === Selection.enum.EMPLOYEES_NOT_ADDED_PREVIOUSLY_DOWNLOADED ? 'Eklenemeyen alanlar var! / (Daha nce indirildi.)' :
                                                    (row.state === Selection.enum.EMPLOYEE_LOADING_TIMED_OUT ? 'leminiz devam ediyor...' :
                                                        (row.state === Selection.enum.PROCESS_DELAY_NOTIFICATION_SENT? 'leminiz devam ediyor..' :
                                                        '')))))))))}</label> :
                        <label>Belge indirilemedi.</label>
                )
            },
            {
                Header: "lemler",
                accessor: '',
                sortable: false,
                Cell: row => (
                    <div style={{textAlign: "center"}}>
                        {(row.state === Selection.enum.DOCUMENT_DOWNLOADABLE || row.state === Selection.enum.DOCUMENT_DOWNLOADABLE_PREVIOUSLY_DOWNLOADED || row.state === Selection.enum.EMPLOYEES_NOT_ADDED || row.state === Selection.enum.EMPLOYEES_NOT_ADDED_PREVIOUSLY_DOWNLOADED) &&
                        <button type="button"
                                data-toggle="tooltip" data-placement="bottom" title="ndir"
                                style={{background: 'rgba(70, 255, 150, 1), rgb(22, 224, 38))'}}
                                rel="tooltip" className="Btn btn-icon" onClick={() => this.downloadFile(row)}>
                            <i className="BtnIcon fa fa-download"/>
                        </button>}
                    </div>
                )
            }
        ];

        return (
            <Modal isOpen={this.props.isOpen} size='xl'>
                <ModalHeader toggle={this.props.close}>Bekleyen ndirmeleriniz</ModalHeader>
                <ModalBody>
                    <Table
                        columns={columns}
                        content={this.state.downloadRequests ? this.state.downloadRequests : []}
                        total={this.state.total}
                        page={this.state.page}
                        size={this.state.size}
                        loading={this.state.loading}
                        options={['5', '10', '20']}
                        fetchData={this.fetchDownloadRequests}/>
                </ModalBody>
                <ModalFooter/>
            </Modal>
        )
    }
}

export default DownloadRequestModal;