import React, {Component} from 'react';
import {Col, FormGroup, Input, Label, Row, UncontrolledTooltip} from "reactstrap";

class GenericConfiguration extends Component {
    constructor(props){
        super(props);
        this.state={
            data:{}
        };
        this.validateClass   = this.validateClass.bind(this);
        this.validateMessage = this.validateMessage.bind(this);
        this.onChange        = this.onChange.bind(this);
    }

    componentDidMount(){
        let {data} = this.props;
        this.setState({data});
    }

    validateClass(id) {
        let error = this.props.error;
        return (error[id] === undefined ? "" : (error[id].valid ? " has-success" : " has-danger"));
    }

    validateMessage(id) {
        let error = this.props.error;
        return (error[id] === undefined ? "" : (error[id].message));
    }

    onChange(e){
        let {name,value}=e;
        let data = this.state.data;
        data[name]=value;
        this.setState({data});
        this.props.onChange(data);
    }

    render(){

        return<Row style={{padding:16, minHeight:'160px'}}>
            {this.props.configs.map((config, index) =>
                <Col sm="6" key={index}>
                    <FormGroup row  className={this.validateClass(config.accessor)}>
                        <Label sm={4}>{config.label}</Label>
                        <Col sm={8}>
                            <Input type={config.type} value={this.state.data[config.accessor] || ''} onChange={(e) => this.onChange({name: config.accessor, value: e.target.value})} />
                            {this.validateMessage(config.accessor) !== "" &&
                            <UncontrolledTooltip placement="right"
                                                 target="model"
                                                 delay={0}>
                                {this.validateMessage(config.accessor)}
                            </UncontrolledTooltip>}
                        </Col>
                    </FormGroup>
                </Col>
            )}
        </Row>;
    }
}

export default GenericConfiguration;