import React, {Component} from 'react';
import {Button} from "reactstrap";

class SquareButton extends Component {

	constructor(props) {
		super(props);

		this.state = {};
	}

	render() {

		let cloneProps = Object.assign({}, this.props);

		if (cloneProps.title) {
			cloneProps.toggle = "tooltip";
			cloneProps.placement = "top";
			cloneProps.rel = "tooltip";
		}

		return (
			<div>
				<Button {...cloneProps} disabled={this.props.selection && (this.props.selection.length === 0 || this.props.btnDisabled)}
						style={this.props.style && this.props.style} className={this.props.icon ? ('BtnZkn ' + this.props.icon) : 'BtnZkn'} title={this.props.title}>
					{this.props.img &&
					<img className='simple-text logo-mini' style={{width:'17px'}}
						 src={process.env.REACT_APP_CONTEXT_NAME + "/image/" + this.props.img}
					/>}
				</Button>
			</div>
		)
	}
}

export default SquareButton;