import React, {Component} from 'react'
import {requestK} from "../../helper/APIUtils";
import {deepCopyObject, showAxiosError} from "../../helper/helper";
import UserGeneralInfo from "./component/UserGeneralInfo";
import UserBranchInfo from "./component/UserBranchInfo";
import Alert from 'react-s-alert';
import {Modal, ModalBody, ModalFooter, ModalHeader} from "reactstrap";
import Table from "../../components/Table/Table";
import ChangePassword from "./component/ChangePassword";
import SweetAlert from 'react-bootstrap-sweetalert';

class UserProfile extends Component {

	constructor(props) {
		super(props);

		this.state = {
			activeBranchModal: false,
			passwordModal: false,
			passwordPayload: {
				currentPassword: "",
				newPassword: "",
				newPasswordCopy: ""
			},
			alert: null,
			isPasswordPolicyExist: null,
			upperCase: null,
			lowerCase: null,
			oneNumber: null,
			oneSymbol: null,
			maxLength: null,
			minLength: null,
			passwordHistoryScann: null,
			replacementFrequency: null,
			isUpperCase: false,
			isLowerCase: false,
			isOneNumber: false,
			isOneSymbol: false,
			isMaxLength: null,
			isMinLength: null,
			isPasswordHistoryScann: null,
			isReplacementFrequency: null,
		};

		this.onChangeGeneralInfo = this.onChangeGeneralInfo.bind(this);
		this.handleSubmit = this.handleSubmit.bind(this);
		this.openActiveBranchModal = this.openActiveBranchModal.bind(this);
		this.closeActiveBranchModal = this.closeActiveBranchModal.bind(this);
		this.handleChangeUserBranches = this.handleChangeUserBranches.bind(this);
		this.closePasswordModal = this.closePasswordModal.bind(this);
		this.onChangePasswordPayload = this.onChangePasswordPayload.bind(this);
		this.policyCheck=this.policyCheck.bind(this);
		this.resetPasswordPolicyStatus=this.resetPasswordPolicyStatus.bind(this);
		this.openPreWarningForPasswordChange = this.openPreWarningForPasswordChange.bind(this);
	}

	componentDidMount() {
		this.fetchCompleteUser();
		this.fetchRelatedCustomer();
		this.getExistPasswordPolicy();
	}

	hasGrant = (grant) => {
		return this.props.hasGrant(grant);
	};

	getExistPasswordPolicy (){
		let self = this;
		let params = {
			url: `/companyinfo/getExistPasswordPolicy`,
			method: "post",
		};
		requestK(params)
			.then(function (response) {
				if(response.data !== null && response.data !==""){
					self.setState({
						isPasswordPolicyExist: true,
						oneNumber: response.data.oneNumber,
						oneSymbol: response.data.oneSymbol,
						upperCase: response.data.upperCase,
						lowerCase: response.data.lowerCase,
						maxLength: response.data.maxLength,
						minLength: response.data.minLength,
						passwordHistoryScann: response.data.passwordHistoryScanns,
						replacementFrequency: response.data.replacementFrequency
					});
				}
			}).catch(function (error) {
			showAxiosError(error);
		});
	}

	fetchCompleteUser() {
		let self = this;
		let params = {
			url: `/user/fetchCompleteUser`,
			method: 'post'
		};
		let initStateData = null;
		requestK(params)
			.then(function (response) {
				initStateData = response.data;
				self.setState({completeUser: response.data})
			})
			.then(() => self.fetchReadableUserBranches())
			.then(() => self.prepareInitStates(initStateData))
			.catch(function (error) {
				showAxiosError(error);
			});
	}

	prepareInitStates(data) {
		let generalInfoInitState = {};
		generalInfoInitState.firstName = data.firstName;
		generalInfoInitState.lastName = data.lastName;
		generalInfoInitState.username = data.username;
		generalInfoInitState.email = data.email;
		this.setState({generalInfoInitState})
	}

	fetchRelatedCustomer() {
		let self = this;
		let params = {
			url: `/user/fetchRelatedCustomer/${this.props.currentUser.customerId}`,
			method: 'post'
		};
		requestK(params)
			.then(function (response) {
				self.setState({currentCustomer: response.data})
			}).catch(function (error) {
			showAxiosError(error);
		});
	}

	fetchReadableUserBranches() {
		let self = this;
		let data = self.state.completeUser.branchList;
		let params = {
			url: `/user/fetchReadableUserBranches`,
			method: 'post',
			data: data
		};
		requestK(params)
			.then(function (response) {
				self.prepareBranchesToShow(response.data);
			}).catch(function (error) {
			showAxiosError(error);
		});
	}

	prepareBranchesToShow(data) {
		let activeBranchToShow = null;
		let activeBranchOtherInfo = {};

		data.map(b => {
			if (b.active) {
				activeBranchToShow = (b.branchName ? b.branchName : '') + ': ' + (b.street ? b.street : '') + ' '
					+ (b.buildingName ? b.buildingName : '') + ' ' + (b.buildingNo ? b.buildingNo : '') + ' '
					+ (b.doorNo ? b.doorNo : '') + ' ' + (b.postalCode ? b.postalCode : '') + ' '
					+ (b.citySubdivisionName ? b.citySubdivisionName : '') + '-' + (b.city ? b.city : '') + ' / '
					+ (b.country ? b.country : '');
			}

			activeBranchOtherInfo.activeBranchEmail = b.email;
			activeBranchOtherInfo.activeBranchPhone = b.phone;
			activeBranchOtherInfo.activeBranchFax = b.fax;
		});

		let steadyStateOfBranches = JSON.stringify(data);

		this.setState({
			steadyStateOfBranches,
			userBranches: data,
			activeBranchToShow,
			activeBranchOtherInfo
		})
	}

	onConfirmAlert() {
		this.setState({
			alert: null
		})
	}

	onChangeGeneralInfo(e) {
		let completeUser = deepCopyObject(this.state.completeUser);

		if (e.target.name === 'username') {
			let username = e.target.value;
			let regex = new RegExp(/[A-Z!@#$%^&*(),.?"':;{}|<>\\/]/g);

			if (username.match(regex) !== null) {
				this.setState({
					alert: (
						<SweetAlert warning
									title="Uyar"
									confirmBtnText="Tamam"
									confirmBtnBsStyle="danger"
									onConfirm={() => this.onConfirmAlert()}>
							<p>Kullanc Ad:</p>
							<ul>
								<li>Harf ve/veya rakamlardan olumaldr.</li>
								<li>Yalnzca kk harf bulundurabilir.</li>
								<li>Trke karakter ieremez.</li>
								<li>- veya _ dnda zel karakter ieremez.</li>
							</ul>
						</SweetAlert>
					)
				})
			} else {
				completeUser[e.target.name] = e.target.value;
			}
		} else {
			completeUser[e.target.name] = e.target.value;
		}

		this.setState({
			completeUser
		});
	}

	handleSubmit() {
		let self = this;
		let data = deepCopyObject(self.state.completeUser);
		let params = {
			url: `/user/updateProfile`,
			data: data,
			method: 'post'
		};
		self.setState({disabledBtn: true})
		requestK(params).then(() => {
				self.props.loadCurrentUser('Kaydedildi.');
			})
			.catch(function (error) {
				showAxiosError(error);
			});
	}

	openActiveBranchModal() {
		this.setState({activeBranchModal: true})
	}

	closeActiveBranchModal() {
		this.setState({activeBranchModal: false})
	}

	selectAsActiveBranch(data) {
		let newActiveBranchId = data.recordId;
		let userBranchesToShow = deepCopyObject(this.state.userBranches);
		let user = deepCopyObject(this.state.completeUser);
		let userBranches = user.branchList;

		userBranchesToShow.map(ubts => {
			if (ubts.active && ubts.recordId !== newActiveBranchId) {
				ubts.active = false;
			}
			if (ubts.recordId === newActiveBranchId) {
				ubts.active = true;
			}
		});

		userBranches.map(ub => {
			if (ub.active && ub.recordId !== newActiveBranchId) {
				ub.active = false;
			}
			if (ub.recordId === newActiveBranchId) {
				ub.active = true;
			}
		});

		let newStateOfBranches = JSON.stringify(userBranchesToShow);

		this.setState({
			completeUser: user,
			newStateOfBranches,
			userBranches: userBranchesToShow
		})
	}

	handleChangeUserBranches() {
		this.handleSubmit();
		this.prepareBranchesToShow(this.state.userBranches);
		this.setState({
			activeBranchModal: false
		})
	}

	onConfirmPreWarning() {
		this.setState({
			alert: null,
			passwordModal: true
		})
	}

	openPreWarningForPasswordChange() {
		// if(this.isEmployee()){
			if(this.state.isPasswordPolicyExist){
				this.setState({
					alert: (
						<SweetAlert warning
									title="Dikkat"
									confirmBtnText="Devam Et"
									confirmBtnBsStyle="warning"
									onConfirm={() => this.onConfirmPreWarning()}>
							ifreniz {this.state.oneNumber ? 'bir rakam' : ''} {this.state.lowerCase ? 'bir kk harf' : ''} {this.state.upperCase ? 'bir byk harf' : '' }  {this.state.oneSymbol ? 'bir zel karakter' : ''} {this.state.oneNumber || this.state.lowerCase || this.state.upperCase || this.state.oneSymbol ? ' iermelidir  ve ' : ''} {this.state.minLength}- {this.state.maxLength} aras sayda karakterden olumaldr.
						</SweetAlert>)
				});
			}else{
				this.setState({
					alert: (
						<SweetAlert warning
									title="Dikkat"
									confirmBtnText="Tamam"
									confirmBtnBsStyle="danger"
									onConfirm={() => this.onConfirmAlert()}>
							ifre politikanz oluturulmam. Ltfen insan kaynaklar ile grnz.
						</SweetAlert>)
				});
			}
	}

	closePasswordModal() {
		this.setState({
			passwordModal: false,
			passwordPayload: {
				currentPassword: null,
				newPassword: null,
				newPasswordCopy: null
			}
		})
	}

	onChangePasswordPayload(e) {
		let passwordPayload = deepCopyObject(this.state.passwordPayload);
		passwordPayload[e.target.name] = e.target.value;
		this.setState({
				passwordPayload
			})
	}

	isEmployee() {
		return this.props.currentUser && this.props.currentUser.employeeId;
	}

	render() {

		let userBranches = [];
		if (this.state.userBranches) {
			userBranches = deepCopyObject(this.state.userBranches);
		}

		const columns = [
			{
				Header: "Aktif",
				accessor: "recordId",
				sortable: false,
				Cell: (row) => (<input type="radio" checked={row.active} onChange={() => this.selectAsActiveBranch(row)}/>)
			},
			{
				Header: "Adres Ad",
				accessor: "branchName"
			},
			{
				Header: "le",
				accessor: "citySubdivisionName"
			},
			{
				Header: "ehir",
				accessor: "city",
			},
			{
				Header: "Telefon",
				accessor: "phone"
			},
			{
				Header: "E-posta",
				accessor: "email"
			},
			{
				Header: "Web Sitesi",
				accessor: "webSite"
			}
		];
		return (
			<div>

				{this.state.alert && this.state.alert}

				<div className='card-header'>
					<h4 className="card-title">Kullanc Profili</h4>
				</div>

				<div className="card-body">
					<div className="FormRenk col-sm-12" style={{paddingLeft: '0', paddingTop: '0'}}>
						<UserGeneralInfo completeUser={this.state.completeUser}
										 currentCustomer={this.state.currentCustomer}
										 onChangeGeneralInfo={this.onChangeGeneralInfo}
										 handleSubmit={this.handleSubmit}
										 loading={this.state.disabledBtn}
										 openPreWarningForPasswordChange={this.openPreWarningForPasswordChange}
										 initState={this.state.generalInfoInitState}
										 isEmployee={this.props.isEmployee}
										 history={this.props.history}
										 impersonate={this.props.impersonate}
										 hasGrant={this.hasGrant}/>

					</div>
				</div>
				{!this.isEmployee() &&
				<div className="card-body">
					<div className="FormRenk col-sm-12" style={{paddingLeft: '0', paddingTop: '0'}}>

						<UserBranchInfo activeBranchToShow={this.state.activeBranchToShow}
										activeBranchOtherInfo={this.state.activeBranchOtherInfo}
										userBranches={this.state.userBranches}
										openActiveBranchModal={this.openActiveBranchModal}/>

					</div>
				</div>
				}
				<ChangePassword isOpen={this.state.passwordModal}
								currentUser={this.props.currentUser}
								logOut={this.props.logOut}
								closePasswordModal={this.closePasswordModal}
								passwordPayload={this.state.passwordPayload}
								onChangePasswordPayload={this.onChangePasswordPayload}
								policyCheck={this.policyCheck}
								resetPasswordPolicyStatus={this.resetPasswordPolicyStatus}
								impersonate={this.props.impersonate}/>

				<Modal isOpen={this.state.activeBranchModal} size="lg">
					<ModalHeader toggle={() => this.closeActiveBranchModal()}>ube Dzenle</ModalHeader>
					<ModalBody>
						<div className='col-sm-12'>
							<Table columns={columns}
								   loading={this.state.loading}
								   loadingText="Kaytlar getiriliyor..."
								   content={userBranches}
								   total={userBranches.length}
								   page={this.state.page}
								   manual={this.state.manual}
								   size={this.state.size}
								   options={["5", "10", "20", "50"]}
								   fetchData={this.fetchBranchList}
								   paginationEnabled={false}
							/>
						</div>
					</ModalBody>
					<ModalFooter>
						<button disabled={!this.state.newStateOfBranches || this.state.steadyStateOfBranches === this.state.newStateOfBranches}
								onClick={this.handleChangeUserBranches}>Onayla
						</button>
					</ModalFooter>
				</Modal>
			</div>
		)
	}

	policyCheck = () => {
		let status =this.updatePasswordPolicyStatus(this.state.passwordPayload.newPassword);
		if((this.state.upperCase && status.isUpperCase !== true)){
			Alert.error("ifrenizde Byk harf gerekmektedir.", {
				position: 'top-right',
				effect: 'stackslide',
				html: true,
				timeout: 5000
			});
		}
		if((this.state.lowerCase && status.isLowerCase !== true)){
			Alert.error("ifrenizde Kk harf gerekmektedir.", {
				position: 'top-right',
				effect: 'stackslide',
				html: true,
				timeout: 5000
			});
		}
		if((this.state.oneNumber && status.isOneNumber !== true)) {
			Alert.error("ifrenizde Rakam gerekmektedir.", {
				position: 'top-right',
				effect: 'stackslide',
				html: true,
				timeout: 5000
			});
		}
		if((this.state.oneSymbol && status.isOneSymbol !== true)) {
			Alert.error("ifrenizde Sembol gerekmektedir.", {
				position: 'top-right',
				effect: 'stackslide',
				html: true,
				timeout: 5000
			});
		}
		if((this.state.passwordPayload.newPassword.length > this.state.maxLength) || (this.state.passwordPayload.newPassword.length < this.state.minLength) ) {
			Alert.error("ifrenizin karakter saysn kontrol ediniz.", {
				position: 'top-right',
				effect: 'stackslide',
				html: true,
				timeout: 5000
			});
		}
		if((this.state.passwordPayload.newPassword !== this.state.passwordPayload.newPasswordCopy) ) {
			Alert.error("ifreniz tekrar ile uyumsuz.", {
				position: 'top-right',
				effect: 'stackslide',
				html: true,
				timeout: 5000
			});
		}
		return (this.state.passwordPayload.newPassword === this.state.passwordPayload.newPasswordCopy
			&& this.state.passwordPayload.newPassword.length >= this.state.minLength
			&& this.state.passwordPayload.newPassword.length <= this.state.maxLength
			&& ((this.state.upperCase && status.isUpperCase) || (!this.state.upperCase && !status.isUpperCase))
			&& ((this.state.lowerCase && status.isLowerCase) || (!this.state.lowerCase && !status.isLowerCase))
			&& ((this.state.oneNumber && status.isOneNumber) || (!this.state.oneNumber && !status.isOneNumber))
			&& ((this.state.oneSymbol && status.isOneSymbol) || (!this.state.oneSymbol && !status.isOneSymbol))
		)
	};

	updatePasswordPolicyStatus = (password) => {
		let isUpperCase, isLowerCase, isOneNumber, isOneSymbol = false;
		for (let i = 0; i < password.length; i++) {
			let ch = password.substring(i, i + 1);

			if (this.state.upperCase && !this.state.isUpperCase && (/[A-Z]/.test(ch))) { // Byk harf art var ve daha nce hi Byk harf gelmedi
				isUpperCase = true;
				this.setState({isUpperCase, validate: false})
			} else if (this.state.lowerCase && !this.state.isLowerCase && (/[a-z]/.test(ch))) {
				isLowerCase = true;
				this.setState({isLowerCase, validate: false})
			} else if (this.state.oneNumber && !this.state.isOneNumber && (/[0-9]/.test(ch))) {
				isOneNumber = true;
				this.setState({isOneNumber, validate: false})
			} else if (this.state.oneSymbol && !this.state.isOneSymbol && (/[-!$%^&*()_+|~=`{}\[\]:";'<>?,.\/]/.test(ch))) {
				isOneSymbol = true;
				this.setState({isOneSymbol, validate: false})
			}

		}
		return {isUpperCase, isLowerCase, isOneNumber, isOneSymbol};
	};

	resetPasswordPolicyStatus = () => {
		this.setState(
			{
				isUpperCase: false,
				isLowerCase: false,
				isOneNumber: false,
				isOneSymbol: false,
				isMaxLength: null,
				isMinLength: null,
				passwordPayload: {
					currentPassword: "",
					newPassword: "",
					newPasswordCopy: ""
				},
			}
		)
	};

}

export default UserProfile;