import React, {Component} from 'react'
import {checkGrantToDisplay, showAxiosError} from "../../helper/helper";
import {requestK} from "../../helper/APIUtils";
import NavBarKS from "../../components/NavBarKS";
import Table from "../../components/Table/Table";
import TextOverFlowTooltip from "../../components/TextOverFlowTooltip";
import {Modal, ModalBody, ModalHeader, Row} from "reactstrap";
import UserForm from "./component/UserForm";
import Alert from "react-s-alert";
import SquareButtonX from "../../components/SquareButtonX";
import SendMail from "./component/SendMail";
import {ACCESS_TOKEN} from "../../constants";
import DetailedSearch from "../../components/DetailedSearch/DetailedSearch";
import SweetAlert from 'react-bootstrap-sweetalert';
import NotifyKS from "../../components/NotifyKS";
import {HEALTH_FORM_SEND, ISG_FORM_SEND, USER_OPERATIONS, ZIMMET_FORM_SEND, KVKK_FORM_SEND} from "../../common/authorities/GrantDef";
import axios from "axios/index";

class CollectiveUserList extends Component {

  constructor(props) {
    super(props);

    this.state = {
      selectedUser: null,
      customerVknTckn: null,
      aliasList: null,
      addUserModal: false,
      customerList: [{}],
      selectedCustomer: null,
      customerId: null,
      users: [],
      loading: false,
      searching: false,
      searchQuery: undefined,
      total: 0,
      page: 0,
      size: 10,
      manual: false,
      alert: null,
      notify: null
    };

    this.fetchCollectiveUserList = this.fetchCollectiveUserList.bind(this);
    this.cancelUserCreation = this.cancelUserCreation.bind(this);
    this.saveUser = this.saveUser.bind(this);
    this.handleSearch = this.handleSearch.bind(this);
  }

  componentDidMount() {
    this.fetchCustomers();
  }

    fetchCollectiveUserList(accessor, direction, page, size) {
        if (this._source) {
            this._source.cancel();
        }
        let self = this;
        this._source = axios.CancelToken.source();
        let sortStr = !!accessor ? ('&sort=' + accessor + ',' + direction) : '&sort=recordId,desc';
        let searchQuery = !self.state.searchQuery ? '' : '&' + self.state.searchQuery;
        let params = {
            url: '/user/fetchCollectiveUsers?page=' + page + '&size=' + size + sortStr + searchQuery,
            method: "get",
            cancelToken: self._source.token,
        };
        self.setState({ loading: true, searching: true });
        requestK(params)
            .then(function (response) {
                let users = response.data.content;
                self.setState({
                    page: page,
                    size: size,
                    total: response.data.totalElements,
                    users,
                    loading: false,
                    searching: false
                });
            }).catch(function (error) {
            self.setState({ loading: false, searching: false });
            showAxiosError(error);
        });
    }


    async fetchUserByRecordId(item) {
    let self = this;
    let data = item.recordId;
    let customerId;
    let params = {
      url: `/user/fetchUserByRecordId`,
      data: data,
      method: 'post'
    };
    let content = {};
    await requestK(params).then(function (response) {
      content.selectedUser = response.data.user;
      content.userBranchMap = response.data.branchMap;
      customerId = response.data.customerId;
      self.setState({customerId});
      return content;
    }).catch(function (error) {
      showAxiosError(error);
    });
    self.setState({
      mode: 'edit',
      selectedUser: content.selectedUser,
      userBranchMap: content.userBranchMap,
      addUserModal: true,
      customerVknTckn: item.customer.vknTckn
    })
  }

  async handleEdit(item) {
    await this.fetchUserByRecordId(item);
    this.setState({customerVknTckn: item.customer.vknTckn});
  }

  saveUser(content) {
    let self = this;
    let data = content;


    let params = {
      url: `/user/createUser`,
      data: data,
      method: 'post'
    };
    requestK(params).then(() => {
      Alert.success('Kullanc baaryla kaydedildi.', {
        position: 'top-right',
        effect: 'stackslide',
        timeout: 5000
      });

    }).catch(function (error) {
      showAxiosError(error);
    });
  }

  cancelUserCreation() {
    this.setState({
      addUserModal: false,
      selectedUser: null,
    })
  }

  createUserForCustomer() {
    this.setState({
      addUserModal: true,
      mode: 'create'
    });
  }

    async fetchCustomers() {
        let self = this;
        let params = {
            url: `/customer/fetchByCreatedBy`,
            method: 'post'
        };
        let customers = [];
        await requestK(params).then(function (response) {
            if (response.data) {
                response.data.map(c => {
                    let customer = {};
                    customer.value = c.vknTckn;
                    customer.label = c.vknTckn + (c.formattedName !== null ? ' , ' + c.formattedName :
                        c.givenName !== null ? (c.familyName !== null ? ' , ' + c.givenName + ' ' + c.familyName : ' , ' + c.givenName) :
                            c.familyName !== null ? ' ' + c.familyName : "");
                    customer.recordId = c.recordId
                    customers.push(customer);
                })
            }
            return customers;
        }).catch(function (error) {
            showAxiosError(error);
        });
        self.setState({customers})
    }

  impersonate(recordId) {
    if (recordId) {
      this.setState({disabledLoginBtn: true});
      let self = this;
      let params = {
        url: '/user/impersonate',
        method: "post",
        data: {
          recordId: recordId
        }
      };
      requestK(params)
        .then(function (response) {
          let data = response.data;
          sessionStorage.setItem(ACCESS_TOKEN, data.accessToken);
          self.props.onLogin();
          self.props.onImpersonate(true);
          self.setState({disabledLoginBtn: false});
        }).catch(function (error) {
        showAxiosError(error);
        self.setState({disabledLoginBtn: false});
      });
    } else {
      Alert.error("Kullanc seimi yapmalsnz.", {
        position: 'top-right',
        effect: 'stackslide',
        timeout: 1500
      });
    }
  }

  handleSearch(searchQuery) {
    this.setState({
      searchQuery,
      searching: true
    }, () => this.fetchCollectiveUserList(this.state.accessor, this.state.direction, 0, this.state.size))
  }

  handleDelete(item) {
    let self = this;

    let params = {
      url: `/user/deleteCUser`,
      data: item,
      method: 'post'
    };

    let currentPage = this.state.page;

    const deleteUser = () =>

      requestK(params).then(function (response) {
        self.setState({
          notify: {
            type: 'success',
            message: 'Kullanc baaryla silindi',
          },
          alert: null,
          manual: true,
          page: currentPage
        })
      }).catch(function (error) {
        self.setState({
          notify: {
            type: 'warning',
            message: 'Kullanc silme ilemi baarsz',
          },
          alert: null
        });
        showAxiosError(error);
      });


    this.setState({
      alert: (
        <SweetAlert showCancel warning
                    disabled={this.state.loading}
                    confirmBtnText="Evet"
                    cancelBtnText="Hayr"
                    confirmBtnBsStyle="danger"
                    cancelBtnBsStyle="default"
                    title="Emin misiniz?"
                    onConfirm={() => {
                      deleteUser(item)
                    }}
                    onCancel={() => this.setState({alert: null, notify: null})}>
          {item.username} kullancs silinecek..! Onaylyor musunuz ?
        </SweetAlert>
      )
    })
  }
	isSuperAdmin() {
		return this.props.isSuperAdmin;
	}

  render() {

      const {users} = this.state;

      const columns = [
          {
              Header: "Kullanc Ad",
              accessor: "username"
          },
          {
              Header: "Ad",
              accessor: 'firstName',
              Cell: row => (row.firstName && <TextOverFlowTooltip text={row.firstName} maxLength={20}/>)
          },
          {
              Header: "Soyad",
              accessor: "lastName",
              Cell: row => (row.lastName && <TextOverFlowTooltip text={row.lastName} maxLength={20}/>)
          },
          {
              Header: "Unvan",
              accessor: "customerId_formattedName",
              Cell: row => (<TextOverFlowTooltip text={row.customer && row.customer.formattedName} maxLength={20}/>)
          },
          {
              Header: "VKN/TCKN",
              accessor: "customerId_vknTckn",
              Cell: row => (<TextOverFlowTooltip text={row.customer && row.customer.vknTckn} maxLength={20}/>)
          },
          {
              Header: "Rol",
              accessor: "",
              Cell: row => (<label>{row.employeeId ? "ALIAN" : row.userRole ? row.userRole.roleName : ""}</label>)
          },
          {
              Header: "lemler",
              accessor: "",
              Cell: row => (
                  <div>
                      {!row.employeeId && checkGrantToDisplay(this, USER_OPERATIONS) &&
                      <button type="button" onClick={() => this.handleEdit(row)} rel="tooltip"
                              className="Btn btn-icon" title="Dzenle"
                              style={{background: 'linear-gradient(rgb(120, 177, 215), rgb(120, 177, 215))'}}>
                          <i className="fa fa-edit"/>
                      </button>}

                      {!row.employeeId && checkGrantToDisplay(this, USER_OPERATIONS) &&
                      <SendMail item={row} currentUser={this.props.currentUser}/>}

                      {!(this.props.hasOnlyPeyk && !(this.props.currentUser && this.props.currentUser.username === '111111444')) && !row.employeeId && checkGrantToDisplay(this, USER_OPERATIONS) &&
                      <button type="button" onClick={() => this.impersonate(row.recordId)} rel="tooltip"
                              className="Btn btn-icon" title="Kullanc Portalna Git"
                              style={{background: 'rgb(52, 216, 103)'}}>
                          <i className="fa fa-desktop"/>
                      </button>}

                      {!(this.props.hasOnlyPeyk && !(this.props.currentUser && this.props.currentUser.username === '111111444')) && !row.employeeId && checkGrantToDisplay(this, USER_OPERATIONS) &&
                      <button type="button"
                              onClick={() => this.handleDelete(row)}
                              data-toggle="tooltip" data-placement="bottom" title="Kullancy Sil"
                              style={{background: '#cf5555'}}
                              rel="tooltip" className="Btn btn-sm btn-icon">
                          <i class="fas fa-trash"/>
                      </button>}
                      {!(
                          !row.employeeId &&
                          ((!(checkGrantToDisplay(this, ISG_FORM_SEND))&&!(checkGrantToDisplay(this, HEALTH_FORM_SEND))&&!(checkGrantToDisplay(this, ZIMMET_FORM_SEND)&&!this.hasGrant(KVKK_FORM_SEND))) ||
                              !(this.props.hasOnlyPeyk && !(this.props.currentUser && this.props.currentUser.username === '111111444')))
                      ) && "-"}
                  </div>
              )
          }
      ];

      const fields = [
          {
              label: "Kullanc Ad",
              accessor: "username",
              type: "text",
              operator: "=",
          },
          {
              label: "Ad",
              accessor: "firstName",
              type: "text",
              operator: "=",
          },
          {
              label: "Soyad",
              accessor: "lastName",
              type: "text",
              operator: "=",
          },
          {
              label: "Unvan",
              accessor: "customerId_formattedName",
              type: "text",
              operator: "=",
          },
          {
              label: "Vkn/Tckn",
              accessor: "customerId_vknTckn",
              type: "text",
              operator: "="
          }
      ];

      return (
          <div className="row baseBackground">

              {this.state.alert && this.state.alert}

              <NavBarKS offsetWidth={this.props.offsetWidth} innerWidth={window.innerWidth}>
                  {this.props.isSuperAdmin && checkGrantToDisplay(this, USER_OPERATIONS) &&
                  <SquareButtonX position="leftSide" img="add-user.png" onClick={() => this.createUserForCustomer()}
                                 label="Yeni Olutur"/>}
              </NavBarKS>

              <div className="col-md-12 satirBasi">

                  {this.state.notify &&
                  <Row>
                      <NotifyKS
                          type={this.state.notify.type}
                          message={this.state.notify.message}
                          html={this.state.notify.html}
                          timeout={this.state.notify.timeout}
                          autohide={this.state.notify.autohide}
                      />
                  </Row>}

                  <div className="card-header ">
                      <h4 className="card-title">Tm Kullanclar</h4>
                  </div>
                  <div className="card-body">
                      <div className=" ">
                          <div className="" style={{marginTop: '10px', marginBottom: '10px'}}>
                              <DetailedSearch fields={fields}
                                              loading={this.state.searching}
                                              onSearch={this.handleSearch}/>
                          </div>
                      </div>
                  </div>
                  <div className="card-body">
                      <Table
                          loading={this.state.loading}
                          loadingText="Kaytlar getiriliyor..."
                          content={users}
                          total={this.state.total}
                          page={this.state.page}
                          manual={this.state.manual}
                          size={this.state.size}
                          options={["5", "10", "20", "50", "100"]}
                          columns={columns}
                          fetchData={this.fetchCollectiveUserList}
                      />
                  </div>
              </div>

              <Modal isOpen={this.state.addUserModal} size="lg" className="ust">
                  <ModalHeader toggle={this.cancelUserCreation}>Kullanc Dzenle</ModalHeader>
                  <ModalBody>
                      <UserForm saveUser={this.saveUser} customerId={this.state.customerId}
                                cancelProcess={this.cancelUserCreation} selectedUser={this.state.selectedUser}
                                mode={this.state.mode}
                                branchMap={this.state.userBranchMap}
                                customerVknTckn={this.state.customerVknTckn}
                                customers={this.state.customers} module={this.props.name}
                                isKepOwner={this.props.currentUser.isKepOwner}/>
                  </ModalBody>
              </Modal>

          </div>
      )
  }
}

export default CollectiveUserList;