import React, {Component} from 'react';
import {requestK} from "../helper/APIUtils";
import {deepCopyObject, showAxiosError} from "../helper/helper";
import {Button, Modal, ModalBody, ModalFooter, ModalHeader} from "reactstrap";
import ExcelCustomizer from "./ExcelCustomizer";
import Alert from "react-s-alert";

class ExcelExporter extends Component {

  constructor(props) {
    super(props);
    window.excelExportComponent = this;

    this.state = {
      exportSelected: true,
      isCustomExportEnabled: true,
      buttonStyle: null,
      customizerModal: false,
      isAll: false,
      isDate: false,
      startDate: null,
      finishDate: null,
      filterMap: [],
      isArchive: 0 ,
      sourceId: '',
      sourceTitle: '',
      destinationId: '',
      destinationTitle: '',
      responseCode: '',
      documentProfile: '',
      invoiceTypeCode: '',
      cancelled: '',
      noDocumentError: false,
      vknTckn: '',
      formattedName: '',
      givenName: '',
      familyName: ''
    };

    this.exportXls = this.exportXls.bind(this);
    this.prepareSheetName = this.prepareSheetName.bind(this);
    this.openCustomizerModal = this.openCustomizerModal.bind(this);
    this.closeCustomizerModal = this.closeCustomizerModal.bind(this);
    this.confirmCustomHeaders = this.confirmCustomHeaders.bind(this);
    this.clearCustomHeaders = this.clearCustomHeaders.bind(this);
    this.handleSelectAll = this.handleSelectAll.bind(this);
    this.onChangeDate = this.onChangeDate.bind(this);
    this.filterResults = this.filterResults.bind(this);
    this.resetConditions = this.resetConditions.bind(this);
    this.exportXlsAll = this.exportXlsAll.bind(this);
  }

  componentDidMount() {
    if (!this.props.customExport) {
      this.setState({isCustomExportEnabled: this.props.customExport});
    }
    this.setState({filterMap: new Map()});
    if (this.props.disableSelect) {
      this.setState({exportSelected: false});
    }
  }

  componentWillReceiveProps(nextProps, nextContext) {
    if (nextProps.customHeaders && nextProps.customHeaders.length !== 0) {
      this.exportXls();
    }
  }

  resetConditions() {
    this.setState({
      isAll: false,
      isDate: false,
      startDate: null,
      finishDate: null,
      filterMap: [],
      isArchive: 0,
      sourceId: '',
      sourceTitle: '',
      destinationId: '',
      destinationTitle: '',
      responseCode: '',
      documentProfile: '',
      invoiceTypeCode: '',
      cancelled: '',
      noDocumentError: false,
      vknTckn: '',
      formattedName: '',
      givenName: '',
      familyName: ''
    })
  }

  arrangeDataSet(type) {
    let dataSet = {};

    // dataSet.content = this.props.data;

    let selection = this.props.selection;
    let content = this.props.content;
    let selectedContent = [];

    if (type !== 'custom')
      content.map(con => {
        selection.map(sel => {
          if (con.recordId === sel) {
            selectedContent.push(con)
          }
        })
      });

    dataSet.content = selectedContent;


    if (!this.state.customHeaders) {
      dataSet.headers = this.props.defaultHeaders;
    } else {
      dataSet.headers = this.state.customHeaders;
    }

    dataSet.sheetName = this.props.sheetName;

    let queryPayload = {};

    if (this.state.isAll) {
      queryPayload.startDate = null;
      queryPayload.finishDate = null;
      queryPayload.completeFetch = true;
    } else {
      queryPayload.startDate = deepCopyObject(this.state.startDate);
      queryPayload.finishDate = deepCopyObject(this.state.finishDate);
      queryPayload.completeFetch = false;
    }

    if (this.props.docType) {
      if (this.props.docType === 'SERBESTMESLEKMAKBUZ') {
        queryPayload.docType = 'sVoucher';
      } else {
        queryPayload.docType = 'mVoucher';
      }
    }


    let filterMap = {};

    filterMap.isArchive = this.state.isArchive;
    filterMap.sourceId = this.state.sourceId;
    filterMap.sourceTitle = this.state.sourceTitle;
    filterMap.destinationId = this.state.destinationId;
    filterMap.destinationTitle = this.state.destinationTitle;
    filterMap.responseCode = this.state.responseCode;
    filterMap.documentProfile = this.state.documentProfile;
    filterMap.invoiceTypeCode = this.state.invoiceTypeCode;
    filterMap.cancelled = this.state.cancelled;
    filterMap.vknTckn = this.state.vknTckn;
    filterMap.formattedName = this.state.formattedName;
    filterMap.givenName = this.state.givenName;
    filterMap.familyName = this.state.familyName;

    queryPayload.filterMap = filterMap;

    dataSet.xlsQueryPayload = queryPayload;
    dataSet.uuid = this.create_UUID();
    dataSet.year = this.props.year;

    if (this.props.type === 'outbox' || this.props.type === 'eArchive') {
      dataSet.stateCodeMap = this.props.stateCodes;
    }

    return dataSet;
  }

  filterResults(e, field) {
    this.setState({
      [field]: e.target ? e.target.value : e.value
    });
  }

  create_UUID() {
    let dt = new Date().getTime();
    let uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
      let r = (dt + Math.random() * 16) % 16 | 0;
      dt = Math.floor(dt / 16);
      return (c == 'x' ? r : (r & 0x3 | 0x8)).toString(16);
    });
    return uuid;
  }

  async prepareSheetName() {
    let selection = deepCopyObject(this.props.selection);
    let isAllDocumentsSelected = deepCopyObject(this.state.isAll);
    let payload = [];
    let sheetName = null;

    if ((selection && selection.length > 0) && !isAllDocumentsSelected) {
      if (selection.length === 1) {
        payload.push(selection[0]);
        payload.push(null);
      } else {
        payload.push(selection[0]);
        payload.push(selection[selection.length - 1]);
      }

      let self = this;
      let customSheetNameUrl = (self.props.year !== undefined) ? `${self.props.customSheetNameUrl}?year=${self.props.year}` : self.props.customerSheetNameUrl;
      let params = {
        url: customSheetNameUrl,
        method: "post",
        data: payload
      };
      self.setState({manual: self.manual === true ? false : self.manual});
      await requestK(params).then(function (response) {
        let result = response.data;
        if (result[1] === null) {
          sheetName = self.props.sheetName + '-' + result[0].documentId + ' - (' + result[0].documentIssueDate + ')';
        } else {
          sheetName = self.props.sheetName + '-' + result[1].documentId + ' - ' + result[0].documentId + ' - (' +
            result[1].documentIssueDate + ' - ' + result[0].documentIssueDate + ')';
        }
      }).catch(function (error) {
        showAxiosError(error);
      });
    }

    if (isAllDocumentsSelected) {
      sheetName = 'Tm ' + this.props.sheetName
    }

    if (!isAllDocumentsSelected && (!selection || selection.length === 0)) {
      sheetName = '(' + deepCopyObject(this.state.startDate) + ')' + ' - ' + '(' + deepCopyObject(this.state.finishDate) + ')' + ' Aralndaki Tm ' +
        this.props.sheetName;
    }

    return sheetName;
  }

  async exportXls() {
    this.setState({buttonStyle: 'fas fa-spinner fa-spin'});
    let self = this;
    let data = self.arrangeDataSet('');
    let params = {
      url: self.props.url,
      method: "post",
      responseType: 'arraybuffer',
      data: data,
    };
    self.setState({manual: self.manual === true ? false : self.manual});
    let download = await self.prepareSheetName();
    await requestK(params).then(function (response) {
      let blob = new Blob([response.data], {type: response.headers["content-type"]});
      let urlCreator = window.URL || window.webkitURL;
      let url = urlCreator.createObjectURL(blob);
      let a = document.createElement("a");
      document.body.appendChild(a);
      a.style = "display: none";
      a.href = url;
      // a.download = self.props.sheetName + ".xls";
      a.download = download;
      a.click();
      self.setState({buttonStyle: null});
    }).catch(function (error) {
      self.setState({buttonStyle: null});
      showAxiosError(error);
    });
  }

  async exportCustomXls() {
    this.setState({buttonStyle: 'fas fa-spinner fa-spin'});
    let self = this;
    let data = self.arrangeDataSet('custom');
    let params = {
      url: self.props.url,
      method: "post",
      data: data,
    };
    self.setState({manual: self.manual === true ? false : self.manual});
    await requestK(params).then(function (response) {
      self.setState({buttonStyle: null, noDocumentError: false});
    }).catch(function (error) {
      self.setState({buttonStyle: null, noDocumentError: true});
      showAxiosError(error);
    });
  }


  async exportXlsAll() {
    this.setState({buttonStyle: 'fas fa-spinner fa-spin'});

    let dataSet = {};
    dataSet.headers = this.props.defaultHeaders;
    dataSet.sheetName = this.props.sheetName;

    let self = this;
    let params = {
      url: self.props.url,
      method: "post",
      responseType: 'arraybuffer',
      data: dataSet,
    };
    self.setState({manual: self.manual === true ? false : self.manual});
    let download = self.props.sheetName + '_' + this.getCurrentLocalDate();
    await requestK(params).then(function (response) {
      let blob = new Blob([response.data], {type: response.headers["content-type"]});
      let urlCreator = window.URL || window.webkitURL;
      let url = urlCreator.createObjectURL(blob);
      let a = document.createElement("a");
      document.body.appendChild(a);
      a.style = "display: none";
      a.href = url;
      // a.download = self.props.sheetName + ".xls";
      a.download = download;
      a.click();
      self.setState({buttonStyle: null, noDocumentError: false});
    }).catch(function (error) {
      self.setState({buttonStyle: null, noDocumentError: true});
      showAxiosError(error);
    });
  }

  getCurrentLocalDate() {
    let today = new Date();
    let dd = String(today.getDate()).padStart(2, '0');
    let mm = String(today.getMonth() + 1).padStart(2, '0');
    let yyyy = today.getFullYear();

    today = dd + '/' + mm + '/' + yyyy;
    return today;
  }

  openCustomizerModal() {
    this.setState({customizerModal: true, isAll: true});
  }

  closeCustomizerModal() {
    this.setState({
      customizerModal: false,
    }, () => this.resetConditions())
  }

  async confirmCustomHeaders(data) {
    let isAll = deepCopyObject(this.state.isAll);
    let startDate = deepCopyObject(this.state.startDate);
    let finishDate = deepCopyObject(this.state.finishDate);

    if (!isAll && (!startDate || !finishDate) && this.props.type !== 'customer') {
      Alert.error('Geerli bir tarih aral giriniz', {
        html: true,
        position: 'top-right',
        effect: 'stackslide',
        timeout: 5000
      })
    } else {
      await this.setState({customHeaders: data, noDocumentError: false}, () => this.exportCustomXls());
      this.closeCustomizerModal();
      await this.sleep(2000);
      if (!this.state.noDocumentError) {
        this.props.triggerDownloadRequestTooltip();
      }
    }
  }

  sleep(ms) {
    return new Promise(resolve => setTimeout(resolve, ms));
  }

  clearCustomHeaders() {
    this.setState({customHeaders: []})
  }

  handleSelectAll() {
    let isAll = !deepCopyObject(this.state.isAll);
    this.setState({
      isAll,
      startDate: null,
      finishDate: null
    })
  }

  onChangeDate(e, field) {
    if (!(typeof e === "string")) {
      let date = e.clone().format("YYYY-MM-DD");
      if (field === 'startDate') {
        this.setState({
          startDate: date
        });
      } else {
        this.setState({
          finishDate: date
        })
      }
    }
  }

  clearSelection() {
    this.props.clearSelection();
  }

  render() {

    return (
      <div style={{display: '-webkit-box'}}>

        {(this.props.selection && this.state.exportSelected) &&
        <Button style={{marginLeft: '3px', paddingBottom: '6px'}}
                className="BtnZkn"
                type="button"
                data-toggle="tooltip"
                data-placement="top"
                rel="tooltip" title="Excel'e Aktar"
                disabled={this.props.selection && (this.props.selection.length === 0 || this.props.btnDisabled)}
                onClick={this.exportXls}>
          {this.state.buttonStyle &&
          <i class={this.state.buttonStyle}/>}
          {!this.state.buttonStyle &&
          <img className=" simple-text logo-mini" style={{width: '17px'}}
               src={process.env.REACT_APP_CONTEXT_NAME + "/image/excel-transfer.png"}
          />}
        </Button>}


        {(this.props.completeFetch && this.props.completeFetch) &&
        <Button style={{marginLeft: '3px', paddingBottom: '6px'}}
                className="BtnZkn"
                type="button"
                data-toggle="tooltip"
                data-placement="top"
                rel="tooltip" title="Excel'e Aktar"
                onClick={this.exportXlsAll}>
          {this.state.buttonStyle &&
          <i class={this.state.buttonStyle}/>}
          {!this.state.buttonStyle &&
          <img className=" simple-text logo-mini" style={{width: '17px'}}
               src={process.env.REACT_APP_CONTEXT_NAME + "/image/excel-transfer.png"}
          />}
        </Button>}


        {this.state.isCustomExportEnabled === true &&
        <Button style={{marginLeft: '3px'}}
                className="BtnZkn"
                type="button"
                data-toggle="tooltip"
                data-placement="top"
                rel="tooltip" title="Dzenle ve Excel'e Aktar"
                onClick={() => this.openCustomizerModal()}>
          {this.state.buttonStyle &&
          <i class={this.state.buttonStyle}/>}
          {!this.state.buttonStyle &&
          <img className=" simple-text logo-mini" style={{width: '17px'}}
               src={process.env.REACT_APP_CONTEXT_NAME + "/image/excel-custom-transfer.png"}
          />}
        </Button>
        }

        <Modal isOpen={this.state.customizerModal} size={this.props.type === 'customer' ? 'sm' : 'lg'}>
          <ModalHeader toggle={this.closeCustomizerModal}>Excel zelletir</ModalHeader>
          <ModalBody style={{padding: '20px 0 0 20px'}}>
            <ExcelCustomizer type={this.props.type} confirmCustomHeaders={this.confirmCustomHeaders} handleSelectAllDocuments={this.handleSelectAll}
                             isAll={this.state.isAll} onChangeDate={this.onChangeDate} startDate={this.state.startDate}
                             finishDate={this.state.finishDate} filterResults={this.filterResults} isArchive={this.state.isArchive}
                             sourceId={this.state.sourceId} sourceTitle={this.state.sourceTitle} destinationId={this.state.destinationId}
                             destinationTitle={this.state.destinationTitle} responseCode={this.state.responseCode} vknTckn={this.state.vknTckn}
                             formattedName={this.state.formattedName} familyName={this.state.familyName} givenName={this.state.givenName}
                             documentProfile={this.state.documentProfile} invoiceTypeCode={this.state.invoiceTypeCode}
                             cancelled={this.state.cancelled}/>
          </ModalBody>
        </Modal>
      </div>
    )
  }
}

export default ExcelExporter;