import React, {Component} from 'react';
import {requestK} from "../../helper/APIUtils";
import {showAxiosError} from "../../helper/helper";
import Table from "../../components/Table/Table";
import {Modal, ModalBody, ModalHeader, Row} from "reactstrap";
import UserForm from "./component/UserForm";
import Alert from "react-s-alert";
import NavBarKS from "../../components/NavBarKS";
import SquareButtonX from "../../components/SquareButtonX";
import SendMail from "./component/SendMail";
import DetailedSearch from "../../components/DetailedSearch/DetailedSearch";
import SweetAlert from 'react-bootstrap-sweetalert';
import NotifyKS from "../../components/NotifyKS";

class UserList extends Component {

	constructor(props) {
		super(props);

		this.state = {
			selectedUser: null,
			branches: null,
			users: [],
			loading: false,
			searching: false,
			searchQuery: undefined,
			total: 0,
			page: 0,
			size: 10,
			manual: false,
			addUserModal: false,
			alert: null,
			notify: null
		};

		this.listUsers = this.listUsers.bind(this);
		this.cancelUserCreation = this.cancelUserCreation.bind(this);
		this.saveUser = this.saveUser.bind(this);
		this.handleSearch = this.handleSearch.bind(this);
	}

	listUsers(accessor, direction, page, size) {
		let self = this;
		let sortStr = !!accessor ? ('&sort=' + accessor + ',' + direction) : '&sort=recordId,desc';
		let searchQuery = !self.state.searchQuery ? '' : '&' + self.state.searchQuery;
		let params = {
			url: '/user/fetchUsers?page=' + page + '&size=' + size + sortStr + searchQuery,
			method: "get",
		};
		self.setState({loading: true, manual: self.manual === true ? false : self.manual, searching: true});
		self.props.reload(true);
		requestK(params)
			.then(function (response) {
				let users = response.data.content;
				self.setState({
					page: page,
					size: size,
					total: response.data.totalElements,
					users,
					loading: false,
					searching: false
				});
				self.props.reload(false);
			}).catch(function (error) {
			self.setState({loading: false, searching: false});
			self.props.reload(false);
			showAxiosError(error);
		});
	}

	saveUser(content) {
		let self = this;
		let data = content;
		let params = {
			url: `/user/createUser`,
			data: data,
			method: 'post'
		};
		requestK(params).then(() => {
			Alert.success('Kullanc baaryla kaydedildi.', {
				position: 'top-right',
				effect: 'stackslide',
				timeout: 5000
			});
			self.setState({
				addUserModal: false,
				selectedUser: null,
				branches: null,
				manual: true
			})
		}).catch(function (error) {
			showAxiosError(error);
		});
	}

	cancelUserCreation() {
		this.setState({
			addUserModal: false,
			selectedUser: null,
			branches: null
		})
	}

	handleEdit(item) {
		let self = this;
		let data = item.recordId;
		let params = {
			url: `/user/fetchUserByRecordId`,
			data: data,
			method: 'post'
		};
		requestK(params).then(function (response) {
			self.setState({
				mode: 'edit',
				selectedUser: response.data.user,
				userBranchMap: response.data.branchMap,
				addUserModal: true
			})
		}).catch(function (error) {
			showAxiosError(error);
		});
	}

	handleSearch(searchQuery) {
		this.setState({
			searchQuery,
			searching: true
		}, () => this.listUsers(this.state.accessor, this.state.direction, 0, this.state.size))
	}

	handleDelete(item) {
		let self = this;
		let params = {
			url: `/user/deleteUser`,
			data: item,
			method: 'post'
		};
		const deleteUser = () =>
			requestK(params).then(function (response) {
				self.setState({
					notify: {
						type: 'success',
						message: 'Kullanc baaryla silindi',
					},
					alert: null
				})
			}).catch(function (error) {
				self.setState({
					notify: {
						type: 'warning',
						message: 'Kullanc silme ilemi baarsz',
					},
					alert: null
				});
				showAxiosError(error);
			});

		this.setState({
			alert: (
				<SweetAlert showCancel warning
							disabled={this.state.loading}
							confirmBtnText="Evet"
							cancelBtnText="Hayr"
							confirmBtnBsStyle="danger"
							cancelBtnBsStyle="default"
							title="Emin misiniz?"
							onConfirm={() => {
								deleteUser(item)
							}}
							onCancel={() => this.setState({alert: null, notify: null})}>
					{item.username} kullancs silinecek..! Onaylyor musunuz ?
				</SweetAlert>
			)
		})
	}

	render() {
		const {users} = this.state;
		const columns = [
			{
				Header: "Ad",
				accessor: "firstName",
			},
			{
				Header: "Soyad",
				accessor: "lastName",
			},
			{
				Header: "Kullanc Ad",
				accessor: "username",
			},
			{
				Header: "e-Posta",
				accessor: "email",
			},
			{
				Header: "Rol",
				accessor: "",
				Cell: item => (
					<label>{item.roleId && item.roleId.roleName}</label>
				)
			},
			{
				Header: "lemler",
				accessor: "",
				sortable: false,
				Cell: item => (
					<div>
						<button type="button"
								onClick={() => this.handleEdit(item)}
								data-toggle="tooltip" data-placement="bottom" title="Dzenle"
								style={{background: 'linear-gradient(rgb(120, 177, 215), rgb(120, 177, 215))'}}
								rel="tooltip" className="Btn btn-sm btn-icon">
							<i className="fa fa-edit"/>
						</button>

						<SendMail item={item} currentUser={this.props.currentUser}/>

						<button type="button"
								onClick={() => this.handleDelete(item)}
								data-toggle="tooltip" data-placement="bottom" title="Kullancy Sil"
								style={{background: '#cf5555'}}
								rel="tooltip" className="Btn btn-sm btn-icon">
							<i class="fas fa-trash"/>
						</button>
					</div>
				)
			}
		];

		const fields = [
			{
				label: "Ad",
				accessor: "firstName",
				type: "text",
				operator: "=",
			},
			{
				label: "Soyad",
				accessor: "lastName",
				type: "text",
				operator: "=",
			},
			{
				label: "Kullanc Ad",
				accessor: "username",
				type: "text",
				operator: "=",
			}
		];
		return (
			<div className="row baseBackground">

				{this.state.alert && this.state.alert}

				<NavBarKS offsetWidth={this.props.offsetWidth} innerWidth={window.innerWidth}>
					<SquareButtonX position="leftSide" img="add-user.png" onClick={() => this.setState({addUserModal: true, mode: 'create'})}
								   label="Yeni Olutur"/>
				</NavBarKS>

				<div className="col-md-12 satirBasi">

					{this.state.notify &&
					<Row>
						<NotifyKS
							type={this.state.notify.type}
							message={this.state.notify.message}
							html={this.state.notify.html}
							timeout={this.state.notify.timeout}
							autohide={this.state.notify.autohide}
						/>
					</Row>}

					<div className="card-header ">
						<h4 className="card-title">Kullanc Listesi</h4>
					</div>
					<div className="card-body">
						<div className=" ">
							<div className="" style={{marginTop: '10px', marginBottom: '10px'}}>
								<DetailedSearch fields={fields}
												loading={this.state.searching}
												onSearch={this.handleSearch}/>
							</div>
						</div>
					</div>
					<div className="card-body">
						<Table
							loading={this.state.loading}
							loadingText="Kaytlar getiriliyor..."
							content={users}
							total={this.state.total}
							page={this.state.page}
							manual={this.state.manual}
							size={this.state.size}
							options={["5", "10", "20", "50", "100"]}
							columns={columns}
							fetchData={this.listUsers}
						/>
					</div>
				</div>

				<Modal isOpen={this.state.addUserModal} size="lg" className="ust">
					<ModalHeader toggle={this.cancelUserCreation}>Kullanc Dzenle</ModalHeader>
					<ModalBody>
						<UserForm saveUser={this.saveUser}
								  cancelProcess={this.cancelUserCreation}
								  selectedUser={this.state.selectedUser}
								  mode={this.state.mode}

								  branchMap={this.state.userBranchMap}
								  module={this.props.name}
								  isKepOwner={this.props.currentUser.isKepOwner}/>
					</ModalBody>
				</Modal>

			</div>
		)
	}
}

export default UserList;