  componentDidMount() {
  }

  getPasswordPolicy = (username, url) => {
    let self = this;
    let errorMsg = "ifre Politikanz oluturulmam. Ltfen insan kaynaklar ile grnz.";
    let params = {
      url: `/passwordCreation/getPassword`,
      method: "post",
      data: {
      username: username
    }
    };
    requestK(params)
        .then(function (response) {
          if (response.data !== null && response.data !== "") {
            self.setState({processing: false});
            self.props.history.push({pathname: url, policy: response.data})
          } else {
            self.errorMessage(errorMsg);
            self.setState({processing: false});
            setTimeout(self.props.history.push("/login"), 3000);
          }
        }).catch(function () {
      self.setState({processing: false});
      self.errorMessage(errorMsg);
    })
  };

  errorMessage(message) {
    Alert.error(message, {
      position: 'top-right',
      effect: 'stackslide',
      timeout: 5000
    });
  }
  successMessage(message) {
    Alert.success(message, {
      position: 'top-right',
      effect: 'stackslide',
      timeout: 5000
    });
  }

  editHandleInputChange(event) {
    const target = event.target;
    let value = target.value;
    let name = target.name;

    this.setState((prevState) => {
      prevState.selected[name] = value;
      return {
        selected: prevState.selected,
      };
    });
  }

  // confirm() {
  //
  // let res =this.state.isExistPasswordPolicy
