  userCheck() {
      let username = this.state.selected.username;
      let notification = this.state.selected.notificationInfo;

      if (username === null || username === undefined || username.length === 0) {
        Alert.error("Ltfen kullanc adnz giriniz.", {
          position: 'top-right',
          effect: 'stackslide',
          timeout: 5000
        });
      } else if (notification === null || notification === undefined || notification.length === 0) {
        Alert.error("Ltfen iletiim bilgisini giriniz.", {
          position: 'top-right',
          effect: 'stackslide',
          timeout: 5000
        });
      }
    else if (notification.charAt(0)==='0' && (!notification.includes("@"))){
      Alert.error("Ltfen Telefon Numaranz Banda Sfr Olmadan Giriniz!", {
        position: 'top-right',
        effect: 'stackslide',
        timeout: 5000
      });
    }
      else {

        this.setState({processing: true});
        let self = this;
        let params = {
          url: `/employee/userCheck`,
          method: "post",
          data: {
            username: username,
            notificationInfo: notification
          }
        };
        requestK(params)
            .then(function (response) {
              if (!response.data) {
                self.errorMessage("Eleme salanamad, ltfen daha sonra tekrar deneyiniz!");
                self.setState({processing: false});
                setTimeout(self.props.history.push("/login"), 3000);
              } else {
                self.getPasswordPolicy(username, response.data);
              }
            }).catch(function (error) {
          self.setState({processing: false});
          Alert.error(error.response.data, {
            position: 'top-right',
            effect: 'stackslide',
            timeout: 10000
          });
        });
      }
  }

  loginScreen() {
    this.props.history.push("/login");
  }

  activateJoyride() {
    if (this.state.joyrideActive === false) {
      this.setState({flag: 0});
      this.setState({joyrideActive: true});
      if (this.state.width < 1000) {
        this.setState({flag: 1});
      }
    }
  }

  callback = (data) => {
    if (data.action === 'close' && data.type === 'step:after') {
      this.setState({shouldRun: false});
      this.setState({flag: 0});
    }
    if (data.action === 'stop' || data.action === 'reset' || data.action === 'skip') {
      this.setState({shouldRun: true});
      this.setState({joyrideActive: false});
      this.setState({flag: 0});
      this.activateJoyride();
    }
  };

  keyPress = (e) => {
    if (e.keyCode === 13) {
      this.userCheck();
    }
  };

