  hasValidPhoneOrMail = () => {
    let phoneValidatable = false;
    let mailValidatable = false;
    if (this.state.selected.phone.length === 10) {
      phoneValidatable = true;
    }
    if (this.state.selected.email.length > 3) {
      mailValidatable = true;
    }
    return phoneValidatable || mailValidatable;
  };

  sendLinkChannelsText = () => {
    let phoneValidatable = false;
    let mailValidatable = false;
    if (this.state.phoneCheck) {
      phoneValidatable = true;
    }
    if (this.state.emailCheck) {
      mailValidatable = true;
    }
    if (phoneValidatable && mailValidatable) {
      return `letiim Bilgisi Dorula`;  // return `e-Posta/SMS Link Yolla`;
    } else if (phoneValidatable) {
      return `letiim Bilgisi Dorula`;  // return `SMS Link Yolla`;
    } else if (mailValidatable) {
      return `letiim Bilgisi Dorula`;  // return `e-Posta Link Yolla`;
    } else {
      return "Telefon Numaras veya e-Posta Adresi Girilmeli";
    }
  };

