import React, {Component} from 'react';
import NavBarKS from "../../../components/NavBarKS";
import SquareButtonX from "../../../components/SquareButtonX";
import {requestK} from "../../../helper/APIUtils";
import {checkGrantForFetchResponse, showAxiosError} from "../../../helper/helper";
import DetailedSearch from "../../../components/DetailedSearch/DetailedSearch";
import Table from "../../../components/Table/Table";
import ReactLoadingTop from "../../../components/ReactLoadingTop";
import axios from 'axios';
import moment from "moment";
import SurveyForm from "./SurveyForm";
import TextOverFlowTooltip from "../../../components/TextOverFlowTooltip";

class SurveyLists extends Component {
    statesEnum = {
        SAVED: 'KAYDEDLD',
        INAPPROVABLE: 'ONAYDA BEKLYOR',
        APPROVED: 'ONAYLANDI',
        REJECTED: 'REDDEDLD',
        WAITING: "GNDERLMEY BEKLYOR",
        REFORMATION: "YENDEN DZENLE"
    };

    constructor(props) {
        super(props);
        this.state = {
            surveys: [],
            modal: false,
            loading: false,
            searching: false,
            searchQuery: undefined,
            total: 0,
            page: 0,
            size: 10,
            manual: false,
            selectedResponse: null,
            isPersonalInfo: this.props.isPersonalInfo,
            isCurrentUserFlag: false,
            createDialog: false
        };
        this.fetchData = this.fetchData.bind(this);
        this.createNewSurvey = this.createNewSurvey.bind(this);
        this.cancelNewSurvey = this.cancelNewSurvey.bind(this);
        this.handleSearch = this.handleSearch.bind(this);
        this.handleEdit = this.handleEdit.bind(this);
    }

    componentDidMount() {
        let self = this;
        if (self.props.match && self.props.match.params && self.props.match.params.id) {
            if (self.props.match.params.id && self.props.currentUser.employeeId === self.props.match.params.id) {
                self.setState({isCurrentUserFlag: false});
            } else if (self.props.match.params.id && self.props.currentUser.employeeId !== self.props.match.params.id) {
                self.setState({isCurrentUserFlag: true});
            }
        }
    }

    handleEdit(item) {
        this.setState({
            createDialog: true,
            selectedResponse: item
        });
    }

    createNewSurvey() {
        this.setState({
            createDialog: true,
            selectedResponse: null
        });
    }

    cancelNewSurvey() {
        let self = this;
        self.fetchData(self.state.accessor,self.state.direction,self.state.page,self.state.size);
        self.setState({
            createDialog: false,
            selectedResponse: null
        })
    }

    fetchData(accessor, direction, page, size) {
        if (this._source)
            this._source.cancel();
        let self = this;
        this._source = axios.CancelToken.source();
        let sortStr = !!accessor ? ('&sort=' + accessor + ',' + direction) : '&sort=responseId,desc';
        let searchQuery = !self.state.searchQuery ? '' : '&' + self.state.searchQuery;
        searchQuery = searchQuery + '&personalInfo=' + self.state.isPersonalInfo;
        let url = '/survey/fetch?page=' + page + '&size=' + size + sortStr + searchQuery;
        if (self.props.match && self.props.match.params.type && self.props.match.params.id) {
            url = '/survey/fetchByType?page=' + page + '&size=' + size + sortStr + searchQuery +
                '&typeId=' + self.props.match.params.type + '&employeeId=' + self.props.match.params.id;
        }
        let params = {
            url: url,
            method: "get",
            cancelToken: self._source.token,

        };
        self.setState({loading: true, searching: true, manual: self.manual === true ? false : self.manual});
        self.props.reload(true);
        requestK(params)
            .then(function (response) {
                checkGrantForFetchResponse(self, response.data);
                self.setState({
                    page: page,
                    size: size,
                    total: response.data.totalElements,
                    surveys: response.data.content,
                    loading: false,
                    searching: false
                });
                self.props.reload(false);
            }).catch(function (error) {
            self.props.reload(false);
            self.setState({loading: false, searching: false});
            showAxiosError(error);
        });
    }

    handleSearch(searchQuery) {
        this.setState({
            searchQuery,
            searching: true
        }, () => this.fetchData(this.state.accessor, this.state.direction, 0, this.state.size))
    }

    render() {

        const columns = [
            {
                Header: this.state.isPersonalInfo ? "Form No" : "Talep No",
                accessor: "responseId",
            },
            {
                Header: this.state.isPersonalInfo ? "Form Ad" : "Talep Ad",
                accessor: "surveyName",
                Cell: row => (<div style={{margin: 0}}><TextOverFlowTooltip text={row.surveyName} maxLength={60}/></div>)
            },
            {
                Header: this.state.isPersonalInfo ? "Form Tarihi" : "Talep Tarihi",
                accessor: "date",
                Cell: row => (
                    <div style={{margin: 0}}>
                        {moment(row.date).isValid() ? moment(row.date).format("DD-MM-YYYY HH:mm:ss") : ''}
                    </div>
                )
            },
            {
                Header: "Durumu",
                accessor: "state",
                Cell: row => (
                    <div>
                        {row.state === this.statesEnum.APPROVED ?
                            <span className="text-success">{this.statesEnum.APPROVED}</span> :
                            row.state === this.statesEnum.INAPPROVABLE ?
                                <span className="text-warning">{this.statesEnum.INAPPROVABLE}</span> :
                                row.state === this.statesEnum.WAITING ?
                                    <span className="text-warning">{this.statesEnum.WAITING}</span> :
                                    row.state === this.statesEnum.REFORMATION ?
                                        <span className="text-danger">{this.statesEnum.REFORMATION}</span> :
                                        row.state === this.statesEnum.REJECTED ?
                                            <span className="text-danger">{this.statesEnum.REJECTED}</span> :
                                            row.state === this.statesEnum.SAVED ?
                                                <span className="text-muted">{this.statesEnum.SAVED}</span> : ''
                        }
                    </div>
                )
            },
            {
                Header: "lemler",
                Cell: row => (
                    <div>
                        {!this.state.isCurrentUserFlag &&
                            <button type="button" onClick={() => this.handleEdit(row)} rel="tooltip"
                                    className="Btn btn-icon" title="Dzenle"
                                    style={{background: 'linear-gradient(rgb(120, 177, 215), rgb(120, 177, 215))'}}>
                                <i className="fa fa-edit"/>
                            </button>}
                    </div>
                )
            }
        ];
        const fields = [
            {
                label: this.state.isPersonalInfo ? "Form No" : "TalepNo",
                accessor: "responseId",
                type: "numericText",
                operator: "="
            }
        ];
        return (
            <div className="row card">
                <NavBarKS offsetWidth={this.props.offsetWidth} innerWidth={window.innerWidth}>
                    {!this.state.isPersonalInfo && !this.state.isCurrentUserFlag &&
                        <SquareButtonX position="leftSide" img="add-user.png"
                                       onClick={() => this.setState({createDialog: true})}
                                       label="Yeni Talep"/>}
                </NavBarKS>
                <div className="col-sm-12 satirBasi">
                    <div className="">
                        <div className="card-header ">
                            <h4 className="card-title">{this.state.isPersonalInfo ? "zlk Form Listesi" : "Talep Form Listesi"}</h4>
                        </div>
                        <div className="card-body">
                            <div className=" ">
                                <div className="" style={{marginTop: '10px', marginBottom: '10px'}}>
                                    <DetailedSearch fields={fields}
                                                    loading={this.state.searching}
                                                    onSearch={this.handleSearch}/>
                                </div>
                            </div>
                        </div>
                        <div className="card-body">
                            <Table
                                columns={columns}
                                content={this.state.surveys}
                                total={this.state.total}
                                page={this.state.page}
                                size={this.state.size}
                                loading={this.state.loading}
                                options={["5", "10", "20", "50", "100"]}
                                fetchData={this.fetchData}
                                manual={this.state.manual}
                            />
                        </div>
                    </div>
                    {this.state.getCustomerLoading && <ReactLoadingTop style={{width: '90px', height: '90px'}}/>}
                </div>
                {this.state.createDialog &&
                    <SurveyForm
                        isPersonalInfo={this.state.isPersonalInfo}
                        cancelProcess={this.cancelNewSurvey}
                        createDialog={this.state.createDialog}
                        currentUser={this.props.currentUser}
                        isEmployee={this.props.isEmployee}
                        selectedResponse={this.state.selectedResponse}
                    />
                }
            </div>
        )
    }
}

export default SurveyLists;
