import React, {Component} from 'react';
import Swal from 'sweetalert2';
import {Col, FormGroup, Label, Row} from "reactstrap";
import {checkGrantToDisplay, selectServiceData, showAxiosError} from "../../helper/helper";
import {requestK} from "../../helper/APIUtils";
import Table from "../../components/Table/Table";
import DetailedSearch from "../../components/DetailedSearch/DetailedSearch";
import DefaultModal from "../../components/DefaultModal";
import Select from "../../local_node_modules/react-select"
import Alert from "react-s-alert";
import {ACCESS_TOKEN} from "../../constants/index";
import ReactLoadingTop from "../../components/ReactLoadingTop";
import NavBarKS from "../../components/NavBarKS";
import TextOverFlowTooltip from "../../components/TextOverFlowTooltip";
import ExcelExporter from "../../components/ExcelExporter";
import * as headerContent from "../../constants/exportcontent";
import SquareButton from "../../components/SquareButton";
import ConfigurationModal from "../kepik/subservice/component/ConfigurationModal";
import StickButton from "../../components/StickButton";
import axios from 'axios';
import NotifyKS from "../../components/NotifyKS";
import {CUSTOMER_OPERATIONS} from "../../common/authorities/GrantDef";

class CustomerList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      customers: [],
      allWarningMails: [],
      loading: false,
      searching: false,
      searchQuery: undefined,
      total: 0,
      page: 0,
      size: 10,
      selectedCustomer: {},
      disabledLoginBtn: false,
      showLoginModal: false,
      showXMLDownloadRequestModal: false,
      serviceStateModal: false,
      priceAmountMap: {},
      priceAmountMbMap: {},
      users: [],
      manual: false,
      selectedAccountant: null,
      assignedCustomers: [],
      notifyKey: Math.random().toString(36)
    };
    this.fetchData = this.fetchData.bind(this);
    this.getTrStyles = this.getTrStyles.bind(this);
    this.impersonate = this.impersonate.bind(this);
    this.handleCreate = this.handleCreate.bind(this);
    this.handleEdit = this.handleEdit.bind(this);
    this.handleDelete = this.handleDelete.bind(this);
    this.handleSearch = this.handleSearch.bind(this);
    this.handleCustomizeSidebar = this.handleCustomizeSidebar.bind(this);
    this.serviceTypesSelectData = this.serviceTypesSelectData.bind(this);
    this.handleConsole = this.handleConsole.bind(this);
    this.showNotification = this.showNotification.bind(this);
  }

  handleEdit(item) {
    if (this.isSuperAdmin()) {
      this.props.history.push(`/createEditCustomer/${item.recordId}`);
    } else {
      Alert.error("Yetkiniz bulunmamaktadr.", {
        position: 'top-right',
        effect: 'stackslide',
        timeout: 5000
      });
    }
  }

  handleCustomizeSidebar(item) {
    this.props.history.push(`/customizeSidebar/${item.recordId}`);
  }

  handleCreate() {
    this.props.history.push(`/createEditCustomer/0`);
  }

  handleConsole() {
    let params = {
      url: '/common/reload/',
      method: "post"
    };

    requestK(params)
      .then(function (response) {
        Alert.success("Listelerin yenilenme ilemi tamamland. Portal properties yeniden yklendi.", {
          position: 'top-right',
          effect: 'stackslide',
          timeout: 1500
        });
      }).catch(function (error) {
      showAxiosError(error);
    });
  }

  handleDelete(item) {
    let self = this;
    let tItem = JSON.parse(JSON.stringify(item._original.recordId));
    let params = {
      url: '/customer/delete/' + tItem,
      method: "post",
      cancelToken: self._source.token
    };

    requestK(params)
      .then(function (response) {
        self.setState({loading: false});
        Swal({
          title: 'Baarl ! ',
          text: ("Silme ilemi baaryla gerekletirildi."),
          type: 'success',
          confirmButtonText: 'Tamam'
        }).then((result) => {
          self.fetchData();
        })
      }).catch(function (error) {
      showAxiosError(error);
    });
  }

  fetchData(accessor, direction, page, size) {
    if (this._source)
      this._source.cancel();

    let self = this;
    this._source = axios.CancelToken.source();
    let sortStr = !!accessor ? ('&sort=' + accessor + ',' + direction) : '&sort=recordId,desc';
    let propName = this.props.name;
    let searchQuery = !self.state.searchQuery ? '' : '&' + self.state.searchQuery;
    let params = {
      url: '/' + propName + '/fetch?page=' + page + '&size=' + size + sortStr + searchQuery +
          ((searchQuery !== '' && searchQuery.includes("showPassive")) ? '' : '&showPassive=FALSE'),
      method: "get",
      cancelToken: self._source.token,
    };
    self.setState({loading: true, searching: true, manual: self.manual === true ? false : self.manual});
    self.props.reload(true);
    requestK(params)
      .then(function (response) {
        self.setState({
          page: page,
          size: size,
          total: response.data.totalElements,
          customers: response.data.content,
          loading: false,
          searching: false
        });
        self.props.reload(false);
      }).catch(function (error) {
      self.props.reload(false);
      self.setState({loading: false, searching: false});
      showAxiosError(error);
    });
  }

  getTrStyles(item) {
    return (item.isActive !== null && item.isActive !== true) ? {
      backgroundColor: 'lightgray',
      webkitTextFillColor: 'darkgray'
    } : {};
  }

  isSuperAdmin() {
    return this.props.isSuperAdmin;
  }

  getCustomerUser(row) {
    let self = this;
    let params = {
      url: '/customer/getCustomerUser',
      method: "post",
      data: {
        recordId: row.recordId
      }
    };
    this.setState({getCustomerLoading: true});
    requestK(params)
      .then(function (response) {
        let data = response.data;
        let users = data.users;
        let recordId = (users.length === 0 ? null : users[0][5]);
        self.setState({users: data.users, showLoginModal: true, getCustomerLoading: false, recordId: recordId});
      }).catch(function (error) {
      showAxiosError(error);
      self.setState({showLoginModal: false, getCustomerLoading: false});
    });
  }

  impersonate() {
    let recordId = this.state.recordId;
    if (recordId) {
      this.setState({disabledLoginBtn: true});
      let self = this;
      let params = {
        url: '/user/impersonate',
        method: "post",
        data: {
          recordId: recordId
        }
      };
      requestK(params)
        .then(function (response) {
          let data = response.data;
          sessionStorage.setItem(ACCESS_TOKEN, data.accessToken);
          self.props.onLogin();
          self.props.onImpersonate(true);
          self.setState({disabledLoginBtn: false});
        }).catch(function (error) {
        showAxiosError(error);
        self.setState({disabledLoginBtn: false});
      });
    } else {
      Alert.error("Kullanc seimi yapmalsnz.", {
        position: 'top-right',
        effect: 'stackslide',
        timeout: 1500
      });
    }
  }

  handleSearch(searchQuery) {
    this.setState({
      searchQuery,
      searching: true
    }, () => this.fetchData(this.state.accessor, this.state.direction, 0, this.state.size))
  }

  defineServiceStateIcon(row) {
    if (row.customerContractList &&
      row.customerContractList[0] &&
      row.customerContractList[0].signatureState &&
      !row.customerContractList[0].serviceTerminationDate) {
      return <label className="fas fa-check" style={{color: '#9fd037', fontSize: '1.2em', cursor: 'pointer'}}/>;
    } else {
      return <a><label className="fas fa-exclamation" style={{color: '#F2421B', fontSize: '1.2em', cursor: 'pointer'}}/></a>;
    }
  }

  serviceTypesSelectData() {
    let data = selectServiceData();
    data.splice(-1, 1);
    return data;
  }

  showNotification(type, message) {
    this.setState({
      notifyKey: Math.random().toString(36),
      notify: {
        type: type,
        message: message,
      }
    })
  }

  render() {
    const defaultHeaders = headerContent.customerContent;

    const columns = [
      {
        Header: "Unvan",
        accessor: "formattedName",
        Cell: row => (
          <div>
            {row.vknTckn.length === 10 ? <TextOverFlowTooltip text={row.formattedName} maxLength={20}/> :
              <TextOverFlowTooltip text={row.givenName + ' ' + row.familyName} maxLength={20}/>}
          </div>
        )
      },
      {
        Header: "VKN/TCKN",
        accessor: "vknTckn"
      },
      {
        Header: "Kurumsal e-Posta",
        accessor: "corporateMail"
      },
      {
        Header: "Vergi Dairesi",
        accessor: "taxOffice"
      },
      {
        Header: "Bal Olduu Kurulu",
        accessor: "parentCustomer",
        sortable: false,
      },
      {
        Header: "Hizmet Durumu",
        accessor: "",
        sortable: false,
        Cell: row => (
          <div>{this.defineServiceStateIcon(row)}</div>
        )
      },
      {
        Header: "lemler",
        accessor: "",
        sortable: false,
        width: "11%",
        Cell: row => (
          this.isSuperAdmin() ?
            <div className="btn-group">
              <div className="dropdown">
                <button className="MenuBtn2 listt btn btn-default" type="button" data-toggle="dropdown">Senz
                </button>
                <div className="dropdown-menu" aria-labelledby={"dropdownMenuButton_" + row.recordId}>
                  {this.props.isPeyk && <a className="dropdown-item" onClick={() => this.handleEdit(row)}>
                    <i
                      className="fa fa-edit"/> {this.isSuperAdmin() ? "Mteri Bilgilerini Dzenle" : "Bal Kurulu Bilgilerini Dzenle "}
                  </a>}
                  {this.props.isPeyk &&
                  <a className="dropdown-item"
                     onClick={() => this.setState({showConfigurationModal: true, customerId: row.recordId})}>
                    <i className="fas fa-pen-alt"/> Alt Hizmetleri Dzenle
                  </a>}
                  {this.props.isPeyk && <a className="dropdown-item" onClick={() => this.getCustomerUser(row)}>
                    <i className="fa fa-desktop"/> Kullanc Portalna Git
                  </a>
                  }
                </div>
              </div>
            </div>
            : "-"
        )
      }
    ];

    const fields = [
      {
        label: this.isSuperAdmin() ? "Mteri Ad" : "Bal Kurulu Ad",
        accessor: "customerName",
        type: "text",
        operator: "="
      },
      {
        label: "Unvan",
        accessor: "formattedName",
        type: "text",
        operator: "="
      },
      {
        label: "VKN/TCKN",
        accessor: "vknTckn",
        type: "numericText",
        operator: "="
      },
      {
        label: "Ad",
        accessor: "givenName",
        type: "text",
        operator: "="
      },
      {
        label: "Soyad",
        accessor: "familyName",
        type: "text",
        operator: "="
      },
      {
        label: "Pasif Mterileri Gster",
        accessor: "showPassive",
        type: "boolean",
        operator: "=",
      }
    ];

    return (
      <div className="row card">
        <NavBarKS offsetWidth={this.props.offsetWidth} innerWidth={window.innerWidth} name={this.props.name}>

          {!this.props.currentUser.accountant && this.isSuperAdmin() && checkGrantToDisplay(this, CUSTOMER_OPERATIONS) &&
          <StickButton position="leftSide"
                       onClick={this.handleCreate}
                       label="Yeni Olutur"
                       title="Yeni Olutur"
                       className='stickButton plus-circle'
                       icon='plus-circle'/>}

          {!this.props.currentUser.accountant && this.isSuperAdmin() && checkGrantToDisplay(this, CUSTOMER_OPERATIONS) &&
          <ExcelExporter position="leftSide" url={'/customer/exportXls'}
                         sheetName='Mteri_Listesi' title="Excel'e Aktar" customExport={true}
                         defaultHeaders={defaultHeaders} type='customer' url='/customer/exportXls'
                         triggerDownloadRequestTooltip={this.props.triggerDownloadRequestTooltip}
                         icon='excel'/>}
        </NavBarKS>

        {this.props.isEntegrator &&
        <NavBarKS offsetWidth={this.props.offsetWidth} innerWidth={window.innerWidth} name={this.props.name}>

          {!this.props.currentUser.accountant && this.isSuperAdmin() && checkGrantToDisplay(this, CUSTOMER_OPERATIONS) &&
          <StickButton position="leftSide"
                       onClick={this.handleCreate}
                       label="Yeni Olutur"
                       title="Yeni Olutur"
                       className='stickButton plus-circle'
                       icon='plus-circle'/>}

          {!this.props.currentUser.accountant &&
          <SquareButton position="leftSide"
                        title='Yenile'
                        onClick={this.handleConsole}
                        img='refresh_white.png'
                        icon='reload'/>}
        </NavBarKS>
        }

        {this.state.notify &&
        <Row>
          <NotifyKS
            key={this.state.notifyKey}
            type={this.state.notify.type}
            message={this.state.notify.message}
            html={this.state.notify.html}
            timeout={this.state.notify.timeout}
            autohide={this.state.notify.autohide}
          />
        </Row>}

        <div className="col-sm-12 satirBasi">
          <div className="">
            <div className="card-header ">
              <h4 className="card-title">{this.isSuperAdmin() ? "Mteri Listesi" : "Bal Kurulu  Listesi"}</h4>
            </div>
            <div className="card-body">
              <div className=" ">
                <div className="" style={{marginTop: '10px', marginBottom: '10px'}}>
                  <DetailedSearch fields={fields}
                                  loading={this.state.searching}
                                  onSearch={this.handleSearch}/>
                </div>
              </div>
            </div>
            <div className="card-body">
              <Table
                columns={columns}
                content={this.state.customers}
                total={this.state.total}
                page={this.state.page}
                size={this.state.size}
                loading={this.state.loading}
                options={["5", "10", "20", "50", "100"]}
                fetchData={this.fetchData}
                getTrStyles={this.getTrStyles}
                manual={this.state.manual}
              />
            </div>
          </div>
          {this.state.getCustomerLoading && <ReactLoadingTop style={{width: '90px', height: '90px'}}/>}
        </div>

        {this.state.showLoginModal &&
        <DefaultModal title="Mteri Kullanc Listesi"
                      size="md"
                      disabled={this.state.disabledLoginBtn}
                      isOpen={this.state.showLoginModal}
                      confirmBtnText="Giri Yap"
                      cancelBtnText="Kapat"
                      onConfirm={this.impersonate}
                      onCancel={(e) => this.setState({
                        showLoginModal: false,
                        disabledLoginBtn: false,
                        recordId: null
                      })}
        >

          <FormGroup row>
            <Label sm={4} for="batchEnabled">Kullanc: *</Label>
            <Col sm={8}>
              <Select
                clearable={false}
                value={this.state.recordId}
                placeholder="Kullanc Seiniz..."
                onChange={(e) => (e && e.value) && this.setState({recordId: e.value})}
                options={this.state.users.map(user => ({
                  value: user["recordId"],
                  label: (user["username"] || "") + "-" +
                    (user["firstName"] || "") + " " +
                    (user["lastName"] || "") + (user["isAdmin"] === 1 ? "( Ynetici )" : "")
                }))}
              />
            </Col>
          </FormGroup>
        </DefaultModal>
        }

        {this.state.showConfigurationModal &&
        <ConfigurationModal
          customerId={this.state.customerId}
          modal={this.state.showConfigurationModal}
          toggleModal={(flag) => this.setState({showConfigurationModal: flag})}
        />
        }
      </div>
    );
  }
}

export default CustomerList;