import React, { Component } from 'react';
import {Nav} from 'reactstrap';
import Select from "../../../local_node_modules/react-select";
import {CREDIT, CREDIT_ADMIN} from "../../../common/authorities/GrantDef";
import {requestK} from "../../../helper/APIUtils";
import {showAxiosError} from "../../../helper/helper";
import SweetAlert from "react-bootstrap-sweetalert";
import Alert from "react-s-alert";



class SmsTransferPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: true,
            searching: false,
            options: [],
            firms: [],
            representative: '',
            alert: null,

        };
        this.getCustomersList = this.getCustomersList.bind(this);
        this.handleChangeType = this.handleChangeType.bind(this);
        this.handleChangeCustomer = this.handleChangeCustomer.bind(this);
        this.fetchGetCredit = this.fetchGetCredit.bind(this);
        this.fetchGetCreditPredefined = this.fetchGetCreditPredefined.bind(this);
        this.showCreditTransferWarning = this.showCreditTransferWarning.bind(this);
        this.isFormValid = this.isFormValid.bind(this);
        this.creditTransfer = this.creditTransfer.bind(this);
        this.updateSmsClicked = this.updateSmsClicked.bind(this);
    }
    componentDidMount() {
        if (this.hasGrant(CREDIT) && this.props.isSuperAdmin === false) {
            this.fetchGetCredit(this.props.recordId,'remain');
            this.fetchGetCreditPredefined(this.props.recordId);
            this.handleChangeCustomer(this.props.recordId);
        }else{
            this.getCustomersList();
        }
    }

    hasGrant = (grant) => {
        return this.props.hasGrant(grant);
    };
    handleChangeType = parentCompanySelection => {
        this.setState(
            {
                parentCompanySelection,
                transferSelection: null,
                transferredSelection: null,
                transferRemain: '',
                transferredRemain: '',

            },
            () => {
                this.handleChangeCustomer(parentCompanySelection.value);
                this.fetchGetCredit(parentCompanySelection.value, 'remain');
                this.fetchGetCreditPredefined(parentCompanySelection.value);
            }
        );
    };
    handleTransferFirmSelection = transferSelection => {
        this.setState(
            {
                transferSelection,
            },
            () => {
                if(transferSelection && this.state.transferredSelection){
                    if(transferSelection.value === this.state.transferredSelection.value){
                        this.setState(
                            {
                                transferredSelection: null,
                                transferredRemain: '',
                            })
                    }
                }
                this.fetchGetCredit(transferSelection.value,'transferRemain');
            }
        );
    };
    handleTransferredFirmSelection = transferredSelection => {
        this.setState(
            {
                transferredSelection,
            },
            () => {
                if(this.state.transferSelection && transferredSelection){
                    if(this.state.transferSelection.value === transferredSelection.value){
                        this.setState(
                            {
                                transferSelection: null,
                                transferRemain: '',
                            })
                    }
                }
                this.fetchGetCredit(transferredSelection.value,'transferredRemain');
            }
        );
    };
    getCustomersList() {
        let self = this;
        self.setState({isLoading: true});

        if (this.hasGrant(CREDIT_ADMIN) !== true) {
            if (!this.props.isSuperAdmin ){
                self.setState({
                    options: [],
                    loading: false,
                    searching: false,
                    isLoading: false,
                });
                return;
            }
        }

        let params = {
            url: '/customer/fetchAllCustomersForAdmin',
            method: "get"
        };
        requestK(params)
            .then(function (response) {
                let optionItems = [];
                response.data.map((customer) => {
                    optionItems.push({value: customer.recordId, label: customer.formattedName});
                });

                self.setState({
                    options: optionItems,
                    loading: false,
                    searching: false,
                    isLoading: false,
                });
            }).catch(function (error) {
            self.props.reload(false);
            self.setState({loading: false, searching: false, isLoading: false});
            showAxiosError(error);
        });
    }

    fetchGetCreditPredefined(id) {
        let self = this;
        let params = {
            url: `/customer/getCreditPredefined/${id}`,
            method: "get",
        };
        requestK(params)
            .then(function (response) {
                self.setState({
                    creditPredefined: response.data,
                });
            }).catch(function (error) {
            showAxiosError(error);
        });
    }
    fetchGetCredit(id, targetField) {
        let self = this;
        let params = {
            url: `/credit/getCreditId/${id}`,
            method: "get",
        };
        requestK(params)
            .then(function (response) {
                const newState = {};
                newState[targetField] = response.data.remain;
                self.setState(newState);
            })
            .catch(function (error) {
                showAxiosError(error);
            });
    }

    handleChangeCustomer(mainCustomerId) {
        let self = this;
        self.setState({isLoading: true});

            let params = {
                url: `/customer/customerList/${mainCustomerId}`,
                method: "get"
            };
            requestK(params)
                .then(function (response) {
                    let optionItems = [];
                    response.data.map((customer) => {
                        optionItems.push({value: customer.recordId, label: customer.formattedName});
                    });

                    self.setState({
                        firms: optionItems,
                        loading: false,
                        searching: false,
                        isLoading: false,
                    });
                }).catch(function (error) {
                self.props.reload(false);
                self.setState({loading: false, searching: false, isLoading: false});
                showAxiosError(error);
            });
    }

    isFormValid() {
        const { transferSelection, transferredSelection, representative } = this.state;
        return transferSelection && transferredSelection && representative !== '';
    }
    creditTransfer() {
        let transferCustomerId= this.state.transferSelection.value;
        let transferredCustomerId= this.state.transferredSelection.value;
        let transfer =this.state.transferRemain;
        let transferred =this.state.transferredRemain;

        let self = this;
        self.updateSmsClicked();
        let transferList = [
            {
                customerId: transferCustomerId,
                remain: transfer,
                predefined: -this.state.representative,
            },
            {
                customerId: transferredCustomerId,
                remain: transferred,
                predefined: this.state.representative,
            }
        ];
        let params = {
            url: `/credit/creditTransfer`,
            method: 'post',
            data: transferList
        };
        requestK(params)
            .then(() => {
                Alert.success('Kontr akatarm baarl', {
                    position: 'top-right',
                    effect: 'stackslide',
                    timeout: 5000
                });
            }).then(() => {
            self.setState({
                alert: null,
                transferSelection: null,
                transferredSelection: null,
                transferRemain: '',
                transferredRemain: '',
                representative:'',

            });
        }).catch(function (error) {
                showAxiosError(error);
            });
    }
    updateSmsClicked() {
        const transferLabel = this.state.transferSelection.label;
        const transferredLabel = this.state.transferredSelection.label;
        let creditLogList= [
            {
                predefined: -this.state.representative,
                customerId: this.state.transferSelection.value,
                explanation:  `${transferLabel} Firmasndan ${transferredLabel} Firmasna kontr aktarm yaplmtr`,
            },
            {
                predefined: this.state.representative,
                customerId: this.state.transferredSelection.value,
                explanation:  `${transferredLabel} Firmas ${transferLabel} Firmasndan kontr aktarm almtr.`,
            }
        ];

        const params = {
            url: `/credit/updateCreditLog`,
            method: "post",
            data: creditLogList,
        };
        return requestK(params)
            .then(response => {
                return response;
            })
            .catch(error => {
                showAxiosError(error);
            });
    }


    showCreditTransferWarning() {
        if(this.state.representative <= this.state.transferRemain){
            this.setState({
                alert: (
                    <SweetAlert showCancel
                                warning
                                title="Uyar"
                                confirmBtnText="Evet"
                                cancelBtnText="Hayr"
                                confirmBtnBsStyle="danger"
                                cancelBtnBsStyle="default"
                                onConfirm={() => this.creditTransfer()}
                                onCancel={() => this.setState({alert: null})}
                    >
                        <div>
                            <p>{this.state.transferSelection.label} Firmasndan {this.state.transferredSelection.label} Firmasna kontr aktarm gerekleirilecektir. Onaylyor
                                musunuz?</p>
                        </div>
                    </SweetAlert>
                )
            })
        }else{
            Alert.error(`Aktarm yapacak ${this.state.transferSelection.label} Firmasnn kontr yetersizdir! Uygun miktar giriniz.`, {
                position: 'top-right',
                effect: 'stackslide',
                timeout: 5000
            });
        }

    }

    render() {

        return<div className="row baseBackground"> {this.state.alert}
            <Nav className="topbar" style={{paddingLeft: '14px', height: '53px'}}>
                <p style={{marginTop: '18px'}}>SMS KONTR AKTARIMI</p>
            </Nav>
            <div className="row">
                <div className="col-sm-12">
                    <div className="col-sm-12 satirBasi">
                        <div className="row" style={{marginTop: '100px'}}>
                            {this.props.isSuperAdmin &&
                                <div className='col-sm-4 row' >
                                    <label className="col-sm-12 form-col-label">Ana irket Seiniz:</label>
                                    <div className='col-sm-8'>
                                        <div className="form-group">
                                            <Select
                                                clearable={false}
                                                className="select-control"
                                                name="parentCompany"
                                                id="tParentCompany"
                                                placeholder="Seiniz..."
                                                value={this.state.parentCompanySelection}
                                                onChange={this.handleChangeType}
                                                options={this.state.options}
                                            />
                                        </div>
                                    </div>
                                </div>}
                            {(this.hasGrant(CREDIT)) && this.props.isSuperAdmin === false &&
                                <div className="col-sm-4 row"> <h4 className="card-title" style={{ fontWeight: 'bold',marginLeft: '10px',marginTop:'20px'}}> Yllk Yklenen Kontrnz: {this.state.creditPredefined} Adet</h4></div>}
                            {(this.hasGrant(CREDIT)) && this.props.isSuperAdmin === false &&
                                <div className="col-sm-6 row"> <h4 className="card-title" style={{ fontWeight: 'bold', color: 'red',marginLeft: '10px',marginTop:'20px'}}> Kalan Kontrnz: {this.state.remain} Adet</h4></div>}

                            {this.props.isSuperAdmin &&
                            <div className="col-sm-4 row">
                                <label className="col-sm-12 form-col-label">Yllk Yklenen Kontr</label>
                                <div className="col-sm-8" style={{maxWidth: "none"}}>
                                    <div className={"form-group"}>
                                        <input className="form-control" style={{ background: '#f0f0f0', border: '1px solid #ccc',color: 'black'}}
                                               type="text"
                                               name="totalCredit"
                                               id="tTotalCredit"
                                               disabled={true}
                                               value={this.state.creditPredefined}

                                        />
                                    </div>
                                </div>
                            </div>}
                            {this.props.isSuperAdmin &&
                            <div className="col-sm-4 row">
                                <label className="col-sm-12 form-col-label">Kalan Kontr:</label>
                                <div className="col-sm-8" style={{maxWidth: "none"}}>
                                    <div className={"form-group"}>
                                        <input className="form-control" style={{ background: '#f0f0f0', border: '1px solid #ccc',color: 'black'}}
                                               type="text"
                                               name="remainingCredit"
                                               id="tRemainingCredit"
                                               disabled={true}
                                               value={this.state.remain}

                                        />
                                    </div>
                                </div>
                            </div>}
                            <div className='col-sm-4 row'  style={{marginTop: '100px'}}>
                                <label className="col-sm-12 form-col-label">Aktarm Yapacak Firmay Seiniz:</label>
                                <div className='col-sm-8'>
                                    <div className="form-group">
                                        <Select
                                            clearable={false}
                                            className="select-control"
                                            name="selectedTransfer"
                                            id="tSelectedTransfer"
                                            placeholder="Seiniz..."
                                            value={this.state.transferSelection}
                                            onChange={this.handleTransferFirmSelection}
                                            options={this.state.firms}

                                        />
                                    </div>
                                </div>
                            </div>
                            <div className='col-sm-4 row'  style={{marginTop: '100px'}}>
                                <label className="col-sm-12 form-col-label">Aktarm Yaplacak Firmay Seiniz:</label>
                                <div className='col-sm-8'>
                                    <div className="form-group">
                                        <Select
                                            clearable={false}
                                            className="select-control"
                                            name="tselectedTransferred"
                                            id="tSelectedTransferred"
                                            placeholder="Seiniz..."
                                            value={this.state.transferredSelection}
                                            onChange={this.handleTransferredFirmSelection}
                                            options={this.state.firms}

                                        />
                                    </div>
                                </div>
                            </div>

                            <div className="col-sm-4 row"  style={{marginTop: '100px'}}>
                                <label className="col-sm-12 form-col-label">Aktarlacak Miktar:</label>
                                <div className="col-sm-8" style={{maxWidth: "none"}}>
                                    <div className={"form-group"}>
                                        <input className="form-control"
                                               type="text"
                                               name="representative"
                                               id="tRepresentative"
                                               value={this.state.representative}
                                               onChange={(e) => {
                                                   const inputText = e.target.value;
                                                   const regex = /^[1-9][0-9]*$/;
                                                   if (regex.test(inputText) || inputText === '') {
                                                       this.setState({
                                                           representative: inputText
                                                       });
                                                   }
                                               }}
                                        />
                                    </div>
                                </div>
                            </div>
                            <div className="col-sm-4 row"  style={{marginTop: '30px'}}>
                                <label className="col-sm-12 form-col-label">Kalan Kontr:</label>
                                <div className="col-sm-8" style={{maxWidth: "none"}}>
                                    <div className={"form-group"} >
                                        <input className="form-control" style={{ background: '#f0f0f0', border: '1px solid #ccc',color: 'black'}}
                                               type="text"
                                               name="remainingCredit"
                                               id="tRemainingCredit"
                                               disabled={true}
                                               value={this.state.transferRemain}

                                        />
                                    </div>
                                </div>
                            </div>
                            <div className="col-sm-4 row"  style={{marginTop: '30px'}} >
                                <label className="col-sm-12 form-col-label">Kalan Kontr:</label>
                                <div className="col-sm-8" style={{maxWidth: "none"}}>
                                    <div className={"form-group"}   >
                                        <input className="form-control" style={{ background: '#f0f0f0', border: '1px solid #ccc',color: 'black'}}
                                               type="text"
                                               name="remainingCredit"
                                               id="tRemainingCredit"
                                               disabled={true}
                                               value={this.state.transferredRemain}

                                        />
                                    </div>
                                </div>
                            </div>
                            <div className="col-sm-4 row" style={{ marginLeft: "70px", marginTop: '30px'}}>
                                <div className="col-sm-8" style={{maxWidth: "none"}}>
                                    <div className={"form-group"} >
                                        <button style={{background: '#2CA8FF'}}
                                                className='btn'
                                                disabled={!this.isFormValid()}
                                                onClick={() => {
                                                    this.showCreditTransferWarning();
                                                }}>
                                           Aktar
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    }

}

export default SmsTransferPage;