import React, {Component} from 'react';
import {Button, Col, Form, Row, UncontrolledTooltip} from "reactstrap";
import {ACCESS_TOKEN, KEP_IK_API_BASE_URL, SERVLET_PATH} from "./constants";
import {encrypt, requestK} from "./helper/APIUtils";
import ReCAPTCHA from "react-google-recaptcha";
import {getCaptchaCode, getFailedLoginCountParam, showAxiosError} from "./helper/helper";
import Alert from "react-s-alert";
import Timer from "react-compound-timer";
const recaptchaRef = React.createRef();
class LoginWithVerCode extends Component {
  constructor(props) {
    super(props);
    this.state = {
      username: "",
      password: "",
      verificationCode: "",
      processing: false,
      repeatLogin: false,
      captcha: false,
      isCaptchaActive: false,
      captchaErrors: {},
    };
    this.loginWithVer = this.loginWithVer.bind(this);
    this.validateForm = this.validateForm.bind(this);
    this.handleCaptcha = this.handleCaptcha.bind(this);
    this.handleValidationCaptcha = this.handleValidationCaptcha.bind(this);
    getFailedLoginCountParam(this);
  }
  validateForm() {
    if (!!this.state.verificationCode) {
      if (this.state.isCaptchaActive) {
        if (this.handleValidationCaptcha()) {
          return true;
        } else {
          return false;
        }
      }
      return true;
    }
  }
  handleValidationCaptcha() {
    let errors = {};
    let captchaIsValid = true;
    if (!this.state.captcha) {
      captchaIsValid = false;
      errors["captcha"] = "Ltfen gvenlik dorulamasn tamamlaynz!";
    }
    this.setState({captchaErrors: errors});
    return captchaIsValid;
  }

  handleCaptcha = (key) => {
    this.setState({
      captcha: true,
      g_recaptcha_response: key
    })
  }
  componentDidMount() {
    this.setState({
      username: this.props.location.state.username,
      password: this.props.location.state.password,
    })
    getCaptchaCode(this);
  }

  editHandleInputChange = (event) => {
    const target = event.target;
    let value = target.value;
    value = value.replace(" ", "");
    this.setState({
      verificationCode: value
    });
  };


  loginScreen = () => {
    this.props.history.push("/login");
  };

  onTimerEnd = () => {
    this.props.history.push("/login");
  };

  keyPress = (e) => {
    if (e.keyCode === 13) {
      this.sendCode();
    }
  };

  loginWithVer() {
    const loginRequest =
      {
        'username': encrypt(this.state.username),
        'password': encrypt(this.state.password),
        'verificationCode': this.state.verificationCode
      };
    let self = this;
    if (!this.validateForm()) return;
    self.setState({processing: true})
    let params = {
      url: `/auth/signin/two-step`,
      method: 'post',
      data: loginRequest
    };
    requestK(params)
      .then(function (response) {
        self.setState({
          processing: false
        });
        sessionStorage.setItem(ACCESS_TOKEN, response.data.accessToken);
        self.props.onLogin();
      }).catch(function (error) {
      self.setState({
        processing: false
      });
      let response = error.response;
      let status = response.status !== undefined ? response.status : '';
      let errorMessage = "";
      let isCaptchaActive = response.data['captcha'];
      if (!!status && status >= 400 && status !== 500) {
        errorMessage = response.data['message'];
      } else {
        errorMessage = error.message ? error.message : 'Doru bilgilerle tekrar deneyin.';
      }
      self.setState({
        errorMessage,
        processing: false,
        errorType: "danger",
        isCaptchaActive: isCaptchaActive,
        captcha: false
      });

      if (self.state.isCaptchaActive)
        recaptchaRef.current.reset();
    });

  }

  readFormTypeAll = () => {
    let self = this;
    let params = {
      url: `/formtype/readFormTypeAll`,
      method: "get"
    };
    requestK(params)
      .then(function (response) {
        let formTypeList = response.data;
        self.setState({formTypeList});
      }).catch(function (error) {
      showAxiosError(error);
    });
  };

  render() {
    let failedLoginCountParam = this.state.failedLoginCountParam === 0 ? true : false;
    return (
      <Row
        style={{
          // height: '-webkit-fill-available',
          height: '200vh',
          backgroundImage: 'url(' + KEP_IK_API_BASE_URL + "/virtual/resource/loginBackground.png" + ')'
        }}>
        <Col sm='12' md='12' lg='12' className='login-panel-container'>
          <Row className='login-panel'>
            <Col xs='12' sm='12' md='4' lg='4' style={{textAlign: 'center', padding: '0 3% 3% 3%'}}>
              <img src={KEP_IK_API_BASE_URL + "/virtual/resource/login.png"} alt="now-logo"/>
            </Col>

            <Col xs='12' sm='12' md='8' lg='8' style={{padding: '2% 2% 1% 2%'}}>
              <Col sm='12' md='12' lg='12' className="no-border  input-group input-group-lg">
                <div className="listOfIcons" style={{position: "absolute", right: "4%"}}>
                  <div>
                    <button id="UncontrolledTooltipExample" className="login-button-icon"
                            onClick={this.loginScreen}>
                      <i className="fas fa-user-circle loginIcon" style={{fontSize: "20px"}}/>
                    </button>
                    <UncontrolledTooltip placement="auto" target="UncontrolledTooltipExample">
                      Giri Sayfasna Dn
                    </UncontrolledTooltip>
                  </div>
                </div>
              </Col>



              <Col sm='12' md='12' lg='12' className="no-border  input-group input-group-lg">
                <Timer id={"timer"} initialTime={this.props.location.state.durationTime} direction="backward" checkpoints={
                  [
                    {
                      time: 300,
                      callback: this.onTimerEnd,
                    }
                  ]
                }>
                  Kalan Sre (<Timer.Minutes/> d {" "}<Timer.Seconds/> s)
                </Timer>
              </Col>
              <Col sm='12' md='12' lg='12' className="no-border  input-group input-group-lg">
                <input placeholder="Dorulama Kodu"
                       autoComplete="off"
                       id="verificationCode"
                       name="verificationCode"
                       value={this.state.verificationCode}
                       style={{
                         borderLeft: '1px solid #c3cdd4',
                         background: '#b5bdc6',
                         color: 'white',
                         fontSize: '14px',
                         fontWeight: 'bold'
                       }}
                       onChange={this.editHandleInputChange}
                       onKeyDown={this.keyPress}
                       className="loginText form-control"
                       type="text"/>
              </Col>
              {(failedLoginCountParam || this.state.isCaptchaActive) &&
                  <Col sm='12' md='12' lg='12' className="no-border  input-group input-group-lg" style={{
                    textAlign: 'center',
                    padding: '0',
                    margin: '4% 0 0 0'
                  }}>

                    <ReCAPTCHA
                        ref={recaptchaRef}
                        sitekey={this.state.captchaCode}
                        onChange={this.handleCaptcha}
                        className="loginText form-control"
                    />

                  </Col>}
              {this.state.captchaErrors["captcha"] &&
                  <Col sm='12' md='12' lg='12'
                       style={{textAlign: 'center', padding: '0', margin: '4% 0 0 0'}}>
                    <label
                        className='errorMessage'>{this.state.captchaErrors["captcha"]}</label>
                  </Col>}
              {this.state.errorMessage &&
                  <Col sm='12' md='12' lg='12'
                       style={{textAlign: 'center', padding: '0', margin: '4% 0 0 0'}}>
                    <label className='errorMessage'>{this.state.errorMessage}</label>
                  </Col>}

            </Col>
            <Col xs='12' sm='12' md='12' lg='12' className='login-button-container'>
              <Button color="primary" className='login-button'
                      onClick={this.loginWithVer}>
                {!this.state.processing ? 'GR' : <i className="fa fa-spinner fa-spin"/>}
              </Button>
            </Col>
          </Row>
        </Col>
      </Row>
    );
  }


}

export default LoginWithVerCode;