import React, {useCallback, useEffect, useMemo, useState} from "react"

import {dataURItoBlob} from "@rjsf/utils"
import {Col, Row} from "reactstrap";
import Swal from "sweetalert2";
import {localizeString} from "../../../helper/helper";
function addNameToDataURL(dataURL, name) {
    if (dataURL === null) {
        return null
    }
    return dataURL.replace(";base64", `;name=${encodeURIComponent(name)};base64`)
}

function processFile(file) {
    const {name, size, type} = file
    return new Promise((resolve, reject) => {
        const reader = new window.FileReader()
        reader.onerror = reject
        reader.onload = event => {
            if (typeof event.target?.result === "string") {
                resolve({
                    dataURL: addNameToDataURL(event.target.result, name),
                    name,
                    size,
                    type
                })
            } else {
                resolve({
                    dataURL: null,
                    name,
                    size,
                    type
                })
            }
        }
        reader.readAsDataURL(file)
    })
}

function processFiles(files) {
    return Promise.all(Array.from(files).map(processFile))
}

function FilesInfo({filesInfo}) {
    if (filesInfo.length === 0) {
        return null
    }
    return (
        <ul className="file-info">
            {filesInfo.map((fileInfo, key) => {
                const {name, size, type} = fileInfo
                return (
                    <li key={key}>
                        <strong>{name}</strong> ({type}, {size} bytes)
                    </li>
                )
            })}
        </ul>
    )
}

function extractFileInfo(dataURLs) {
    return dataURLs
        .filter(dataURL => typeof dataURL !== "undefined")
        .map(dataURL => {
            const {blob, name} = dataURItoBlob(dataURL);
            return {
                name: localizeString(name),
                size: blob.size,
                type: blob.type
            }
        })
}

function FileChooserWidget({
                               multiple = true,
                               id,
                               readonly,
                               disabled,
                               onChange,
                               value,
                               autofocus = false
                           }) {
    const [filesInfo, setFilesInfo] = useState([]);
    const extractedFilesInfo = useMemo(
        () => {
            if (value !== undefined) {
                setFilesInfo(Array.isArray(value) ? extractFileInfo(value) : extractFileInfo([value]));
            }
        },
        [value]
    );
    const handleChange = useCallback(
        event => {
            if (!event.target.files || event.target.files.length == 0) {
                return
            }
            processFiles(event.target.files).then(filesInfoEvent => {
                if (filesInfoEvent.filter(file => file.size > 8 * 1024 * 1024).length > 0) {
                    Swal({
                        title: 'Uyar ! ',
                        text: "Dosya Boyutu 8MB'dan byk olamaz!",
                        confirmButtonText: 'Tamam'
                    }).then((result) => {
                        //self.fetchData
                    });
                    return;
                }

                if (filesInfoEvent.filter(file => (file.type !== "image/jpeg" && file.type !== "image/jpg" && file.type !== "application/pdf")).length > 0) {
                    Swal({
                        title: 'Uyar ! ',
                        text: "Sadece JPG ve PDF tr dosyalar desteklenmektedir.",
                        confirmButtonText: 'Tamam'
                    }).then((result) => {
                        //self.fetchData
                    });
                    return;
                }

                if (filesInfoEvent.length > 10) {
                    Swal({
                        title: 'Uyar ! ',
                        text: "Seilen dosya says en fazla 10 adet olabilir!",
                        confirmButtonText: 'Tamam'
                    }).then((result) => {
                        //self.fetchData
                    });
                    return;
                }

                setFilesInfo(filesInfoEvent)
                const newValue = filesInfoEvent.map(fileInfo => fileInfo.dataURL)
                if (multiple) {
                    onChange(newValue)
                } else {
                    onChange(newValue[0])
                }
            })
        },
        [multiple, onChange]
    );

    const cleanSelectedFiles = useCallback(
        () => {
            setFilesInfo([]);
            onChange([]);
        }, [multiple, onChange]
    );


    return (
        <div className="previewComponent">
            <Row>
                <Col>
                    <p>
                        <button disabled={disabled} className='btn btn-info py-1 px-2'
                                style={{fontWeight: 'bold'}}>{"Fotoraf veya Dosya Se "}
                            <input
                                id={id}
                                name={id}
                                type="file"
                                disabled={readonly || disabled}
                                onChange={handleChange}
                                defaultValue=""
                                autoFocus={autofocus}
                                multiple={multiple}
                                accept=".doc, .docx, .pdf, .xlsx, .jpg, .jpeg"
                            />
                        </button>
                        <FilesInfo filesInfo={filesInfo}/>
                    </p>
                </Col>
                <Col>
                    <p>
                        <button disabled={disabled} className='btn btn-danger py-1 px-2'
                                style={{fontWeight: 'bold'}} onClick={cleanSelectedFiles} type="button"><i
                            className="far fa-trash-alt"></i>{" Temizle "}</button>
                    </p>
                </Col>
            </Row>
        </div>
    )
}

export default FileChooserWidget
